import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowDeclareOpaqueType(
  generator: Generator,
  node: t.FlowDeclareOpaqueTypeNode,
) {
  generator.word('declare');
  generator.word('opaque');
  generator.word('type');
  generator.print(node.id, node);
}
