import {JSNodeBase, TSTypeNode} from '../index';
import {createBuilder} from '../utils';

export type TSTypeAnnotationNode = JSNodeBase & {
  type: 'TSTypeAnnotation';
  typeAnnotation: TSTypeNode;
};

export const tsTypeAnnotation = createBuilder<TSTypeAnnotationNode>(
  'TSTypeAnnotation',
  {visitorKeys: ['typeAnnotation']},
);
