import Scope from '../Scope';
import * as t from '@romejs/js-ast';

export default function ClassExpression(
  node: t.ClassExpressionNode | t.ClassDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  const newScope = scope.fork('class', node);
  newScope.evaluate(node.typeParameters);
  return newScope;
}
