import {
  JSNodeBase,
  ClassMemberBase,
  ExpressionNode,
  FlowTypeAnnotationNode,
  PrivateNameNode,
} from '../index';
import {createBuilder} from '../utils';

export type ClassPrivatePropertyNode = JSNodeBase &
  ClassMemberBase & {
    type: 'ClassPrivateProperty';
    key: PrivateNameNode;
    value: ExpressionNode;
    typeAnnotation?: undefined | FlowTypeAnnotationNode;
  };

export const classPrivateProperty = createBuilder<ClassPrivatePropertyNode>(
  'ClassPrivateProperty',
);
