import {isEscaped} from '@romejs/parser-core';

// A tagged template literal helper that will escape all interpolated strings, ensuring only markup works
export function markup(
  strs: TemplateStringsArray,
  ...values: Array<string>
): string {
  let out = '';

  for (let i = 0; i < strs.length; i++) {
    const str = strs[i];
    out += str;

    const interpolated = values[i];
    if (interpolated !== undefined) {
      out += escapeMarkup(interpolated);
    }
  }

  return out;
}

export function escapeMarkup(input: string): string {
  let escaped = '';
  for (let i = 0; i < input.length; i++) {
    const char = input[i];

    if (char === '<' && !isEscaped(i, input)) {
      escaped += '\\<';
    } else if (
      char === '\\' &&
      !isEscaped(i, input) &&
      i === input.length - 1
    ) {
      // When there's a trailing backslash that's unescaped, escape it as it could
      // conflict with the next part
      escaped += '\\\\';
    } else {
      escaped += char;
    }
  }
  return escaped;
}
