import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function MemberExpression(
  generator: Generator,
  node: t.MemberExpressionNode | t.OptionalMemberExpressionNode,
) {
  generator.print(node.object, node);

  if (node.computed !== true && node.property.type === 'MemberExpression') {
    throw new TypeError('Got a MemberExpression for MemberExpression property');
  }

  let computed = node.computed;
  if (
    node.property.type === 'NumericLiteral' &&
    typeof node.property.value === 'number'
  ) {
    computed = true;
  }

  let isOptional = node.type === 'OptionalMemberExpression';

  if (isOptional) {
    generator.token('?.');
  }

  if (computed === true) {
    generator.token('[');
    generator.print(node.property, node);
    generator.token(']');
  } else {
    if (!isOptional) {
      generator.token('.');
    }
    generator.print(node.property, node);
  }
}
