import {MasterRequest} from '@romejs/core';
import {FlagParser} from '@romejs/cli-flags';
import {Consumer} from '@romejs/codec-json';
import {WebServer} from '../web/index';
import {createMasterCommand} from '../../commands';
import {commandCategories} from '../../commands';

const DEFAULT_PORT = 8081;

export default createMasterCommand({
  category: commandCategories.SOURCE_CODE,
  description: 'start a web server',

  parseCLIFlags(p: FlagParser) {
    return {
      port: p.number('port', {
        description: '',
        default: DEFAULT_PORT,
      }),
    };
  },

  async default(req: MasterRequest, flags: Consumer): Promise<void> {
    const {reporter} = req;
    const port = flags.getProperty('port').asNumber(DEFAULT_PORT);

    const web = new WebServer(req);
    web.listen(port);
    reporter.success(`Listening on http://localhost:${String(port)}`);

    req.teardownEvent.subscribe(() => {
      web.close();
    });

    await new Promise(() => {});
  },
});
