import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type ImportNamespaceSpecifierNode = JSNodeBase & {
  type: 'ImportNamespaceSpecifier',
  local: IdentifierNode,
};

export const importNamespaceSpecifier = createBuilder<ImportNamespaceSpecifierNode>(
  'ImportNamespaceSpecifier',
  {visitorKeys: ['local']},
);
