import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type ThrowStatementNode = JSNodeBase & {
  type: 'ThrowStatement',
  argument: ExpressionNode,
};

export const throwStatement = createBuilder<ThrowStatementNode>(
  'ThrowStatement',
  {visitorKeys: ['argument']},
);
