export function humanizeTime(ms: number): string {
  const s = Math.floor(ms / 1000);
  const m = Math.floor(s / 60);
  const h = Math.floor(m / 60);

  let buf = '';
  if (h > 0) {
    buf += `${String(h)}h`;
  }
  if (m > 0) {
    buf += `${String(m % 60)}m`;
  }
  buf += `${String(s % 60)}s`;
  return buf;
}
