import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowTypeofTypeAnnotation(
  generator: Generator,
  node: t.FlowTypeofTypeAnnotationNode,
) {
  generator.word('typeof');
  generator.space();
  generator.print(node.argument, node);
}
