import {Position, SourceLocation} from '@romejs/parser-core';
import {PartialDiagnosticAdvice} from '@romejs/diagnostics';
import {ErrorFrames, ErrorFrame} from './types';
import {isPlainObject} from '@romejs/refinement';

export * from './types';

export class V8Error extends Error {
  constructor(
    msg: string,
    advice?: undefined | PartialDiagnosticAdvice,
    framesToPop?: undefined | number,
  ) {
    super(msg);
    this.__ROME_ERROR_FRAMES__ = undefined;
    this.__ROME_ERROR_ADVICE__ = advice;
    this.__ROME_POP_FRAMES__ = framesToPop;
  }

  __ROME_ERROR_FRAMES__: undefined | ErrorFrames;
  __ROME_ERROR_ADVICE__: undefined | PartialDiagnosticAdvice;
  __ROME_POP_FRAMES__: undefined | number;
}

export function toV8Error(err: Error): V8Error {
  return err as any;
}

export function getFramesToPop(_err: Error): number {
  const err = toV8Error(_err);
  return err.__ROME_POP_FRAMES__ === undefined ? 0 : err.__ROME_POP_FRAMES__;
}

export function getFrame(index: number): ErrorFrame {
  return getFrames(new Error())[index + 1];
}

export function isErrorLike(err: unknown): err is Error {
  return (
    isPlainObject(err) &&
    typeof err.message === 'string' &&
    (typeof err.stack === 'string' || err.stack === undefined)
  );
}

type ErrorLike = {
  name: string;
  message: string;
  stack?: undefined | string;
};

export function toErrorLike(err: unknown): ErrorLike {
  if (err instanceof Error) {
    return err;
  }

  let name = 'Error';
  let message = 'Unknown message';
  let stack = undefined;

  if (isPlainObject(err)) {
    // For actual error-likes, retain the original object
    if (isErrorLike(err)) {
      return err;
    }

    if (typeof err.name === 'string') {
      name = err.name;
    }

    if (typeof err.message === 'string') {
      message = err.message;
    }

    if (typeof err.stack === 'string') {
      stack = err.stack;
    }
  }

  return {
    name,
    message,
    stack,
  };
}

export function getFrames(_err: unknown): ErrorFrames {
  if (!isErrorLike(_err)) {
    return [];
  }

  // $FlowFixMe
  const err = toV8Error(_err);

  // Error.prepareStackTrace is only called when the `stack` property is retrieved
  err.stack;

  const frames =
    err.__ROME_ERROR_FRAMES__ === undefined ? [] : err.__ROME_ERROR_FRAMES__;
  return frames;
}

export function getAdvice(_err: unknown): PartialDiagnosticAdvice {
  if (!isErrorLike(_err)) {
    return [];
  }

  // $FlowFixMe
  const err = toV8Error(_err);
  return err.__ROME_ERROR_ADVICE__ === undefined
    ? []
    : err.__ROME_ERROR_ADVICE__;
}

export function getSourceLocationFromFrame(frame: ErrorFrame): SourceLocation {
  const pos: Position = {
    index: -1,
    line: frame.lineNumber === undefined ? 1 : frame.lineNumber,
    column: frame.columnNumber === undefined ? 0 : frame.columnNumber,
  };

  return {
    filename: frame.filename === undefined ? undefined : frame.filename,
    start: pos,
    end: pos,
  };
}
