import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function CatchClause(generator: Generator, node: t.CatchClauseNode) {
  generator.word('catch');
  generator.space();
  generator.token('(');
  generator.print(node.param, node);
  generator.token(')');
  generator.space();
  generator.print(node.body, node);
}
