import {Position} from '@romejs/parser-core';
import {TokenType} from '../tokenizer/types';
import Parser from '../Parser';
import {types as tt} from '../tokenizer/types';
import * as n from '@romejs/js-ast';
import {
  parseExpressionAtom,
  parseIdentifierName,
  parseMaybeUnary,
  parseIdentifier,
  parseTemplate,
  parseMaybeAssign,
  parseFunctionStatement,
  parseClass,
  parseVarStatement,
  parsePropertyName,
  parseBindingListNonEmpty,
  hasCommaAfterRest,
  parseBlockOrModuleBlockBody,
  parseExpression,
  parseTypeExpressionStatement,
  parseStringLiteral,
  assertVarKind,
} from './index';
import {VariableDeclarationKind, TSMappedTypeBoolean} from '@romejs/js-ast';

type ParsingContext =
  | 'EnumMembers'
  | 'HeritageClauseElement'
  | 'TupleElementTypes'
  | 'TypeMembers'
  | 'TypeParametersOrArguments';

// Doesn't handle 'void' or 'undefined' because those are keywords, not identifiers.
function keywordTypeFromName(value: string): n.TSKeywordType | undefined {
  switch (value) {
    case 'any':
      return 'TSAnyKeyword';

    case 'boolean':
      return 'TSBooleanKeyword';

    case 'bigint':
      return 'TSBigIntKeyword';

    case 'never':
      return 'TSNeverKeyword';

    case 'number':
      return 'TSNumberKeyword';

    case 'object':
      return 'TSObjectKeyword';

    case 'string':
      return 'TSStringKeyword';

    case 'symbol':
      return 'TSSymbolKeyword';

    case 'undefined':
      return 'TSUndefinedKeyword';

    case 'unknown':
      return 'TSUnknownKeyword';

    default:
      return undefined;
  }
}

function tsIsIdentifier(parser: Parser): boolean {
  // TODO: actually a bit more complex in TypeScript, but shouldn't matter.
  // See https://github.com/Microsoft/TypeScript/issues/15008
  return parser.match(tt.name);
}

function tsNextTokenCanFollowModifier(parser: Parser) {
  // Note: TypeScript's implementation is much more complicated because
  // more things are considered modifiers there.
  // This implementation only handles modifiers not handled by @babel/parser itself. And 'static'.
  // TODO: Would be nice to avoid lookahead. Want a hasLineBreakUpNext() method...
  parser.next();
  return (
    !parser.hasPrecedingLineBreak() &&
    !parser.match(tt.parenL) &&
    !parser.match(tt.parenR) &&
    !parser.match(tt.colon) &&
    !parser.match(tt.eq) &&
    !parser.match(tt.question) &&
    !parser.match(tt.bang)
  );
}

/** Parses a modifier matching one the given modifier names. */
export function parseTSModifier<T extends n.TSModifier>(
  parser: Parser,
  allowedModifiers: Array<T>,
): undefined | T {
  if (!parser.match(tt.name)) {
    return undefined;
  }

  // @ts-ignore: We are lying here but we validate it in all the correct places
  const modifier: T = String(parser.state.tokenValue);
  if (
    allowedModifiers.includes(modifier) &&
    tryTSParse(parser, tsNextTokenCanFollowModifier)
  ) {
    return modifier;
  }
}

export function hasTSModifier(
  parser: Parser,
  allowedModifiers: Array<n.TSModifier>,
): boolean {
  return parseTSModifier(parser, allowedModifiers) !== undefined;
}

function tsIsListTerminator(parser: Parser, kind: ParsingContext): boolean {
  switch (kind) {
    case 'EnumMembers':
    case 'TypeMembers':
      return parser.match(tt.braceR);

    case 'HeritageClauseElement':
      return parser.match(tt.braceL);

    case 'TupleElementTypes':
      return parser.match(tt.bracketR);

    case 'TypeParametersOrArguments':
      return parser.isRelational('>');
  }

  throw new Error('Unreachable');
}

function parseTSList<T>(
  parser: Parser,
  kind: ParsingContext,
  parseElement: ParserCallback<T>,
): Array<T> {
  const result: Array<T> = [];
  while (!tsIsListTerminator(parser, kind)) {
    // Skipping 'parseListElement' from the TS source since that's just for error handling.
    result.push(parseElement(parser));
  }
  return result;
}

/**
 * If !expectSuccess, returns undefined instead of failing to parse.
 * If expectSuccess, parseElement should always return a defined value.
 */
function parseTSDelimitedList<T>(
  parser: Parser,
  kind: ParsingContext,
  parseElement: ParserCallback<undefined | T>,
): Array<T> {
  const result = [];

  while (true) {
    if (tsIsListTerminator(parser, kind)) {
      break;
    }

    const element = parseElement(parser);
    if (element == undefined) {
      break;
    }

    result.push(element);

    if (parser.eat(tt.comma)) {
      continue;
    }

    if (tsIsListTerminator(parser, kind)) {
      break;
    }

    // This will fail with an error about a missing comma
    if (parser.expect(tt.comma)) {
      break;
    }
  }

  return result;
}

function parseTSBracketedList<T>(
  parser: Parser,
  kind: ParsingContext,
  parseElement: ParserCallback<undefined | T>,
  bracket: boolean,
  skipFirstToken: boolean,
): Array<T> {
  if (!skipFirstToken) {
    if (bracket) {
      parser.expect(tt.bracketL);
    } else {
      parser.expectRelational('<');
    }
  }

  const result = parseTSDelimitedList(parser, kind, parseElement);

  if (bracket) {
    parser.expect(tt.bracketR);
  } else {
    parser.expectRelational('>');
  }

  return result;
}

function parseTSImportType(parser: Parser): n.TSImportTypeNode {
  const start = parser.getPosition();
  parser.expect(tt._import);
  const openContext = parser.expectOpening(
    tt.parenL,
    tt.parenR,
    'ts import type',
  );

  if (!parser.match(tt.string)) {
    parser.addDiagnostic({
      message: 'Argument in a type import must be a string literal',
    });
  }

  const argument = parseExpressionAtom(parser, 'ts import argument');
  parser.expectClosing(openContext);

  let qualifier;
  if (parser.eat(tt.dot)) {
    qualifier = parseTSEntityName(parser, /* allowReservedWords */ true);
  }

  let typeParameters;
  if (parser.isRelational('<')) {
    typeParameters = parseTSTypeArguments(parser);
  }

  return {
    loc: parser.finishLoc(start),
    type: 'TSImportType',
    argument,
    qualifier,
    typeParameters,
  };
}

function parseTSEntityName(
  parser: Parser,
  allowReservedWords: boolean,
): n.TSEntityNameNode {
  let entity: n.TSQualifiedNameNode | n.TSEntityNameNode = parseIdentifier(
    parser,
  );
  while (parser.eat(tt.dot)) {
    const start: Position = parser.getLoc(entity).start;
    const right = parseIdentifier(parser, allowReservedWords);
    entity = {
      loc: parser.finishLoc(start),
      type: 'TSQualifiedName',
      left: entity,
      right,
    };
  }
  return entity;
}

function parseTSTypeReference(parser: Parser): n.TSTypeReferenceNode {
  const start = parser.getPosition();
  const typeName = parseTSEntityName(parser, /* allowReservedWords */ false);
  let typeParameters;
  if (!parser.hasPrecedingLineBreak() && parser.isRelational('<')) {
    typeParameters = parseTSTypeArguments(parser);
  }
  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeReference',
    typeName,
    typeParameters,
  };
}

function parseTSThisTypePredicate(
  parser: Parser,
  lhs: n.TSThisTypeNode,
): n.TSTypePredicateNode {
  parser.next();
  const start = parser.getLoc(lhs).start;
  const parameterName = lhs;
  const typeAnnotation = parseTSTypeAnnotation(parser, /* eatColon */ false);

  return {
    loc: parser.finishLoc(start),
    type: 'TSTypePredicate',
    parameterName,
    typeAnnotation,
  };
}

function parseTSThisTypeNode(parser: Parser): n.TSThisTypeNode {
  const start = parser.getPosition();
  parser.next();
  return {
    loc: parser.finishLoc(start),
    type: 'TSThisType',
  };
}

function parseTSTypeQuery(parser: Parser): n.TSTypeQueryNode {
  const start = parser.getPosition();
  parser.expect(tt._typeof);
  let exprName;
  if (parser.match(tt._import)) {
    exprName = parseTSImportType(parser);
  } else {
    exprName = parseTSEntityName(parser, /* allowReservedWords */ true);
  }
  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeQuery',
    exprName,
  };
}

function parseTSTypeParameter(parser: Parser): n.TSTypeParameterNode {
  const start = parser.getPosition();
  const name = parseIdentifierName(parser);
  const constraint = tsEatThenParseType(parser, tt._extends);
  const _default = tsEatThenParseType(parser, tt.eq);
  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeParameter',
    name,
    constraint,
    default: _default,
  };
}

function tryParseTSTypeParameters(
  parser: Parser,
): undefined | n.TSTypeParameterDeclarationNode {
  if (parser.isRelational('<')) {
    return parseTSTypeParameters(parser);
  }
}

export function parseTSTypeParameters(
  parser: Parser,
): n.TSTypeParameterDeclarationNode {
  const start = parser.getPosition();

  parser.expectRelational('<');

  const params = parseTSBracketedList(
    parser,
    'TypeParametersOrArguments',
    parseTSTypeParameter,
    /* bracket */ false,
    /* skipFirstToken */ true,
  );

  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeParameterDeclaration',
    params,
  };
}

export function tryTSNextParseConstantContext(
  parser: Parser,
): undefined | n.TSTypeReferenceNode {
  if (parser.lookaheadState().tokenType === tt._const) {
    parser.next();
    return parseTSTypeReference(parser);
  }
}

export function tsCheckLiteralForConstantContext(parser: Parser, node: n.Node) {
  switch (node.type) {
    case 'StringLiteral':
    case 'TemplateLiteral':
    case 'NumericLiteral':
    case 'BooleanLiteral':
    case 'SpreadElement':
    case 'ObjectMethod':
    case 'ObjectExpression':
      break;

    case 'ArrayExpression':
      for (const elem of node.elements) {
        if (elem) {
          tsCheckLiteralForConstantContext(parser, elem);
        }
      }
      break;

    case 'ObjectProperty':
      tsCheckLiteralForConstantContext(
        parser,
        (node as n.ObjectPropertyNode).value,
      );
      break;

    case 'UnaryExpression':
      tsCheckLiteralForConstantContext(parser, node.argument);
      break;

    default:
      parser.addDiagnostic({
        loc: node.loc,
        message: 'Only literal values are allowed in constant contexts',
      });
  }
}

// Note: In TypeScript implementation we must provide `yieldContext` and `awaitContext`,
// but here it's always false, because parser.is only used for types.
function tsFillSignature(
  parser: Parser,
  returnToken: TokenType,
): n.TSSignatureDeclarationBase {
  // Arrow fns *must* have return token (`=>`). Normal functions can omit it.
  const returnTokenRequired = returnToken === tt.arrow;
  const typeParameters = tryParseTSTypeParameters(parser);
  const parameters = parseTSBindingListForSignature(parser);

  let typeAnnotation;
  if (returnTokenRequired) {
    typeAnnotation = parseTSTypeOrTypePredicateAnnotation(parser, returnToken);
  } else if (parser.match(returnToken)) {
    typeAnnotation = parseTSTypeOrTypePredicateAnnotation(parser, returnToken);
  }

  return {
    typeParameters,
    parameters,
    typeAnnotation,
  };
}

function parseTSBindingListForSignature(
  parser: Parser,
): Array<
  | n.IdentifierNode
  | n.RestElementNode
  | n.ObjectPatternNode
  | n.ArrayPatternNode
> {
  const openContext = parser.expectOpening(
    tt.parenL,
    tt.parenR,
    'ts signature parameters',
  );
  const patterns = parseBindingListNonEmpty(parser, openContext);

  for (const pattern of patterns) {
    if (
      pattern.type !== 'Identifier' &&
      pattern.type !== 'RestElement' &&
      pattern.type !== 'ObjectPattern' &&
      pattern.type !== 'ArrayPattern'
    ) {
      parser.addDiagnostic({
        loc: pattern.loc,
        message: `Name in a signature must be an Identifier, ObjectPattern or ArrayPattern, instead got ${
          pattern.type
        }`,
      });
    }
  }

  return patterns;
}

function parseTSTypeMemberSemicolon(parser: Parser): void {
  if (!parser.eat(tt.comma)) {
    parser.semicolon();
  }
}

function parseTSConstructSignatureDeclaration(
  parser: Parser,
): n.TSConstructSignatureDeclarationNode {
  const start = parser.getPosition();
  parser.expect(tt._new);
  const signature = tsFillSignature(parser, tt.colon);
  parser.semicolon();
  return {
    type: 'TSConstructSignatureDeclaration',
    loc: parser.finishLoc(start),
    ...signature,
  };
}

function parseTSCallSignatureDeclaration(
  parser: Parser,
): n.TSCallSignatureDeclarationNode {
  const start = parser.getPosition();
  const signature = tsFillSignature(parser, tt.colon);
  parser.semicolon();
  return {
    type: 'TSCallSignatureDeclaration',
    loc: parser.finishLoc(start),
    ...signature,
  };
}

function tsIsUnambiguouslyIndexSignature(parser: Parser) {
  parser.next(); // Skip '{'
  return parser.eat(tt.name) && parser.match(tt.colon);
}

export function tryTSParseIndexSignature(
  parser: Parser,
  start: Position,
): undefined | n.TSIndexSignatureNode {
  if (
    !(
      parser.match(tt.bracketL) &&
      lookaheadTS(parser, tsIsUnambiguouslyIndexSignature)
    )
  ) {
    return undefined;
  }

  parser.expect(tt.bracketL);

  const idStart = parser.getPosition();
  const id = {
    ...parseIdentifier(parser),
    typeAnnotation: parseTSTypeAnnotation(parser),
    loc: parser.finishLoc(idStart),
  };

  parser.expect(tt.bracketR);
  const parameters = [id];

  const typeAnnotation = tryTSParseTypeAnnotation(parser);

  parser.semicolon();
  return {
    loc: parser.finishLoc(start),
    type: 'TSIndexSignature',
    typeAnnotation,
    parameters,
  };
}

function parseTSPropertyOrMethodSignature(
  parser: Parser,
  start: Position,
  readonly: boolean,
): n.TSPropertySignatureNode | n.TSMethodSignatureNode {
  const name = parsePropertyName(parser);
  const optional = parser.eat(tt.question);

  if (!readonly && (parser.match(tt.parenL) || parser.isRelational('<'))) {
    const signature = tsFillSignature(parser, tt.colon);
    parseTSTypeMemberSemicolon(parser);
    return {
      loc: parser.finishLoc(start),
      type: 'TSMethodSignature',
      optional,
      ...signature,
      ...name,
    };
  } else {
    const typeAnnotation = tryTSParseTypeAnnotation(parser);
    parseTSTypeMemberSemicolon(parser);
    return {
      loc: parser.finishLoc(start),
      type: 'TSPropertySignature',
      optional,
      readonly,
      typeAnnotation,
      ...name,
    };
  }
}

function parseTSTypeMember(parser: Parser): n.TSTypeElementNode {
  if (parser.match(tt.parenL) || parser.isRelational('<')) {
    return parseTSCallSignatureDeclaration(parser);
  }

  if (
    parser.match(tt._new) &&
    lookaheadTS(parser, tsIsStartOfConstructSignature)
  ) {
    return parseTSConstructSignatureDeclaration(parser);
  }

  const start = parser.getPosition();
  const readonly = hasTSModifier(parser, ['readonly']);

  const idx = tryTSParseIndexSignature(parser, start);
  if (idx) {
    return {
      ...idx,
      readonly,
    };
  }

  return parseTSPropertyOrMethodSignature(parser, start, readonly);
}

function tsIsStartOfConstructSignature(parser: Parser) {
  parser.next();
  return parser.match(tt.parenL) || parser.isRelational('<');
}

function parseTSTypeLiteral(parser: Parser): n.TSTypeLiteralNode {
  const start = parser.getPosition();
  const members = parseTSObjectTypeMembers(parser);
  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeLiteral',
    members,
  };
}

function parseTSObjectTypeMembers(parser: Parser): Array<n.TSTypeElementNode> {
  const openContext = parser.expectOpening(
    tt.braceL,
    tt.braceR,
    'ts object type members',
  );
  const members = parseTSList(parser, 'TypeMembers', parseTSTypeMember);
  parser.expectClosing(openContext);
  return members;
}

function tsIsStartOfMappedType(parser: Parser): boolean {
  parser.next();

  if (parser.eat(tt.plusMin)) {
    return parser.isContextual('readonly');
  }

  if (parser.isContextual('readonly')) {
    parser.next();
  }

  if (!parser.match(tt.bracketL)) {
    return false;
  }

  parser.next();

  if (!tsIsIdentifier(parser)) {
    return false;
  }

  parser.next();

  return parser.match(tt._in);
}

function parseTSMappedTypeParameter(parser: Parser): n.TSTypeParameterNode {
  const start = parser.getPosition();
  const name = parseIdentifierName(parser);
  const constraint = tsExpectThenParseType(parser, tt._in);
  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeParameter',
    name,
    constraint,
  };
}

function toPlusMin(val: unknown): '+' | '-' {
  const str = String(val);
  if (str === '+' || str === '-') {
    return str;
  } else {
    throw new Error('Expected +/-');
  }
}

function parseTSMappedType(parser: Parser): n.TSMappedTypeNode {
  const start = parser.getPosition();

  const openContext = parser.expectOpening(
    tt.braceL,
    tt.braceR,
    'ts mapped type',
  );

  let readonly: n.TSMappedTypeBoolean;
  if (parser.match(tt.plusMin)) {
    readonly = toPlusMin(parser.state.tokenValue);
    parser.next();
    parser.expectContextual('readonly');
  } else if (parser.eatContextual('readonly')) {
    readonly = true;
  }

  const paramOpenContext = parser.expectOpening(
    tt.bracketL,
    tt.bracketR,
    'ts mapped type parameter',
  );
  const typeParameter = parseTSMappedTypeParameter(parser);
  parser.expectClosing(paramOpenContext);

  let optional: TSMappedTypeBoolean;
  if (parser.match(tt.plusMin)) {
    optional = toPlusMin(parser.state.tokenValue);
    parser.next();
    parser.expect(tt.question);
  } else if (parser.eat(tt.question)) {
    optional = true;
  }

  const typeAnnotation = tryTSParseType(parser);
  parser.semicolon();
  parser.expectClosing(openContext);

  return {
    loc: parser.finishLoc(start),
    type: 'TSMappedType',
    typeParameter,
    typeAnnotation,
    optional,
    readonly,
  };
}

function parseTSTupleType(parser: Parser): n.TSTupleTypeNode {
  const start = parser.getPosition();
  const elementTypes = parseTSBracketedList(
    parser,
    'TupleElementTypes',
    parseTSTupleElementType,
    /* bracket */ true,
    /* skipFirstToken */ false,
  );

  // Validate the elementTypes to ensure:
  //   No mandatory elements may follow optional elements
  //   If there's a rest element, it must be at the end of the tuple
  let seenOptionalElement = false;
  for (const elementNode of elementTypes) {
    if (elementNode.type === 'TSOptionalType') {
      seenOptionalElement = true;
    } else if (seenOptionalElement && elementNode.type !== 'TSRestType') {
      parser.addDiagnostic({
        loc: elementNode.loc,
        message: 'A required element cannot follow an optional element.',
      });
    }
  }

  return {
    loc: parser.finishLoc(start),
    type: 'TSTupleType',
    elementTypes,
  };
}

function parseTSTupleElementType(parser: Parser): n.TSTypeNode {
  // parses `...TsType[]`
  if (parser.match(tt.ellipsis)) {
    const start = parser.getPosition();
    parser.next(); // skips ellipsis
    const typeAnnotation = parseTSType(parser);
    hasCommaAfterRest(parser);
    return {
      loc: parser.finishLoc(start),
      type: 'TSRestType',
      typeAnnotation,
    };
  }

  const typeAnnotation = parseTSType(parser);

  // Parses `TsType?`
  if (parser.eat(tt.question)) {
    const start = parser.getLoc(typeAnnotation).start;
    return {
      loc: parser.finishLoc(start),
      type: 'TSOptionalType',
      typeAnnotation,
    };
  }

  return typeAnnotation;
}

function parseTSParenthesizedType(parser: Parser): n.TSParenthesizedTypeNode {
  const start = parser.getPosition();
  const openContext = parser.expectOpening(
    tt.parenL,
    tt.parenR,
    'ts parenthesized type',
  );
  const typeAnnotation = parseTSType(parser);
  parser.expectClosing(openContext);
  return {
    loc: parser.finishLoc(start),
    type: 'TSParenthesizedType',
    typeAnnotation,
  };
}

function parseTSFunctionType(parser: Parser): n.TSFunctionTypeNode {
  const start = parser.getPosition();
  const signature = tsFillSignature(parser, tt.arrow);
  return {
    type: 'TSFunctionType',
    loc: parser.finishLoc(start),
    ...signature,
  } as n.TSFunctionTypeNode;
}

function parseTSConstructorType(parser: Parser): n.TSConstructorTypeNode {
  const start = parser.getPosition();
  parser.expect(tt._new);
  const signature = tsFillSignature(parser, tt.arrow);
  return {
    type: 'TSConstructorType',
    loc: parser.finishLoc(start),
    ...signature,
  } as n.TSConstructorTypeNode;
}

function parseTSLiteralTypeNode(parser: Parser): n.TSLiteralTypeNode {
  const start = parser.getPosition();

  const literal = parseExpressionAtom(parser, 'ts literal type');
  if (
    literal.type !== 'StringLiteral' &&
    literal.type !== 'NumericLiteral' &&
    literal.type !== 'BooleanLiteral'
  ) {
    parser.addDiagnostic({
      loc: literal.loc,
      message: 'Not a valid TS literal type',
    });
  }

  return {
    loc: parser.finishLoc(start),
    type: 'TSLiteralType',
    literal,
  };
}

function parseTSTemplateLiteralType(parser: Parser): n.TSTypeNode {
  const start = parser.getPosition();
  const templateNode = parseTemplate(parser, false);
  if (templateNode.expressions.length > 0) {
    parser.addDiagnostic({
      loc: parser.getLoc(templateNode.expressions[0]),
      message: 'Template literal types cannot have any substitution',
    });
  }

  const literal = templateNode;
  return {
    loc: parser.finishLoc(start),
    type: 'TSLiteralType',
    literal,
  };
}

function parseTSNonArrayType(parser: Parser): n.TSTypeNode {
  switch (parser.state.tokenType) {
    case tt.name:
    case tt._void:
    case tt._null: {
      let type;
      if (parser.match(tt._void)) {
        type = 'TSVoidKeyword';
      } else if (parser.match(tt._null)) {
        type = 'TSNullKeyword';
      } else {
        type = keywordTypeFromName(String(parser.state.tokenValue));
      }
      if (type !== undefined && parser.lookaheadState().tokenType !== tt.dot) {
        const start = parser.getPosition();
        parser.next();
        return {
          loc: parser.finishLoc(start),
          type,
        };
      }
      return parseTSTypeReference(parser);
    }

    case tt.string:
    case tt.num:
    case tt._true:
    case tt._false:
      return parseTSLiteralTypeNode(parser);

    case tt.plusMin:
      if (parser.state.tokenValue === '-') {
        const start = parser.getPosition();
        if (parser.lookaheadState().tokenType !== tt.num) {
          throw parser.unexpected();
        }

        const literal = parseMaybeUnary(parser, 'ts literal type');
        return {
          loc: parser.finishLoc(start),
          type: 'TSLiteralType',
          literal,
        };
      }
      break;

    case tt._this: {
      const thisKeyword = parseTSThisTypeNode(parser);
      if (parser.isContextual('is') && !parser.hasPrecedingLineBreak()) {
        return parseTSThisTypePredicate(parser, thisKeyword);
      } else {
        return thisKeyword;
      }
    }

    case tt._typeof:
      return parseTSTypeQuery(parser);

    case tt._import:
      return parseTSImportType(parser);

    case tt.braceL:
      return lookaheadTS(parser, tsIsStartOfMappedType)
        ? parseTSMappedType(parser)
        : parseTSTypeLiteral(parser);

    case tt.bracketL:
      return parseTSTupleType(parser);

    case tt.parenL:
      return parseTSParenthesizedType(parser);

    case tt.backQuote:
      return parseTSTemplateLiteralType(parser);
  }

  parser.addDiagnostic({
    message: 'Unknown TS non array type start',
  });

  return parser.createUnknownNode('ts non array type start');
}

function parseTSArrayTypeOrHigher(parser: Parser): n.TSTypeNode {
  let type = parseTSNonArrayType(parser);
  while (!parser.hasPrecedingLineBreak() && parser.eat(tt.bracketL)) {
    if (parser.match(tt.bracketR)) {
      const start = parser.getLoc(type).start;
      const elementType = type;
      parser.expect(tt.bracketR);
      type = {
        loc: parser.finishLoc(start),
        type: 'TSArrayType',
        elementType,
      };
    } else {
      const start = parser.getLoc(type).start;
      const objectType = type;
      const indexType = parseTSType(parser);
      parser.expect(tt.bracketR);
      type = {
        loc: parser.finishLoc(start),
        type: 'TSIndexedAccessType',
        objectType,
        indexType,
      };
    }
  }
  return type;
}

function parseTSTypeOperator(
  parser: Parser,
  operator: n.TSTypeOperator,
): n.TSTypeOperatorNode {
  const start = parser.getPosition();
  parser.expectContextual(operator);

  const typeAnnotation = parseTSTypeOperatorOrHigher(parser);

  const node: n.TSTypeOperatorNode = {
    loc: parser.finishLoc(start),
    type: 'TSTypeOperator',
    typeAnnotation,
    operator,
  };

  if (operator === 'readonly') {
    tsCheckTypeAnnotationForReadOnly(parser, node);
  }

  return node;
}

function tsCheckTypeAnnotationForReadOnly(parser: Parser, node: n.Node) {
  switch (node.typeAnnotation.type) {
    case 'TSTupleType':
    case 'TSArrayType':
      return undefined;

    default:
      parser.addDiagnostic({
        loc: node.loc,
        message:
          "'readonly' type modifier is only permitted on array and tuple literal types.",
      });
      break;
  }
}

function parseTSInferType(parser: Parser): n.TSInferTypeNode {
  const inferStart = parser.getPosition();
  parser.expectContextual('infer');

  const start = parser.getPosition();
  const typeParameter: n.TSTypeParameterNode = {
    type: 'TSTypeParameter',
    name: parseIdentifierName(parser),
    loc: parser.finishLoc(start),
  };

  return {
    loc: parser.finishLoc(inferStart),
    type: 'TSInferType',
    typeParameter,
  };
}

const TS_TYPE_OPERATORS: Array<n.TSTypeOperator> = [
  'keyof',
  'unique',
  'readonly',
];

function parseTSTypeOperatorOrHigher(parser: Parser): n.TSTypeNode {
  let operator: undefined | n.TSTypeOperator;

  for (const op of TS_TYPE_OPERATORS) {
    if (parser.isContextual(op)) {
      operator = op;
      break;
    }
  }

  if (operator !== undefined) {
    return parseTSTypeOperator(parser, operator);
  } else if (parser.isContextual('infer')) {
    return parseTSInferType(parser);
  } else {
    return parseTSArrayTypeOrHigher(parser);
  }
}

function parseTSUnionOrIntersectionType(
  parser: Parser,
  kind: 'TSUnionType' | 'TSIntersectionType',
  parseConstituentType: ParserCallback<n.TSTypeNode>,
  operator: TokenType,
): n.TSUnionTypeNode | n.TSIntersectionTypeNode {
  parser.eat(operator);
  let type = parseConstituentType(parser);
  if (parser.match(operator)) {
    const types = [type];
    while (parser.eat(operator)) {
      types.push(parseConstituentType(parser));
    }
    const start = parser.getLoc(type).start;
    type = {
      loc: parser.finishLoc(start),
      type: kind,
      types,
    };
  }
  return type;
}

function parseTSIntersectionTypeOrHigher(parser: Parser): n.TSTypeNode {
  return parseTSUnionOrIntersectionType(
    parser,
    'TSIntersectionType',
    parseTSTypeOperatorOrHigher,
    tt.bitwiseAND,
  );
}

function parseTSUnionTypeOrHigher(parser: Parser) {
  return parseTSUnionOrIntersectionType(
    parser,
    'TSUnionType',
    parseTSIntersectionTypeOrHigher,
    tt.bitwiseOR,
  );
}

function tsIsStartOfFunctionType(parser: Parser) {
  if (parser.isRelational('<')) {
    return true;
  }
  return (
    parser.match(tt.parenL) &&
    lookaheadTS(parser, tsIsUnambiguouslyStartOfFunctionType)
  );
}

function tsSkipParameterStart(parser: Parser): boolean {
  if (parser.match(tt.name) || parser.match(tt._this)) {
    parser.next();
    return true;
  }

  if (parser.match(tt.braceL)) {
    let braceStackCounter = 1;
    parser.next();

    while (braceStackCounter > 0) {
      if (parser.match(tt.braceL)) {
        braceStackCounter++;
      } else if (parser.match(tt.braceR)) {
        braceStackCounter--;
      }
      parser.next();
    }
    return true;
  }

  if (parser.match(tt.bracketL)) {
    let braceStackCounter = 1;
    parser.next();

    while (braceStackCounter > 0) {
      if (parser.match(tt.bracketL)) {
        braceStackCounter++;
      } else if (parser.match(tt.bracketR)) {
        braceStackCounter--;
      }
      parser.next();
    }
    return true;
  }

  return false;
}

function tsIsUnambiguouslyStartOfFunctionType(parser: Parser): boolean {
  parser.next();
  if (parser.match(tt.parenR) || parser.match(tt.ellipsis)) {
    // ()
    // (...
    return true;
  }
  if (tsSkipParameterStart(parser)) {
    if (
      parser.match(tt.colon) ||
      parser.match(tt.comma) ||
      parser.match(tt.question) ||
      parser.match(tt.eq)
    ) {
      // (xxx :
      // (xxx ,
      // (xxx ?
      // (xxx =
      return true;
    }
    if (parser.match(tt.parenR)) {
      parser.next();
      if (parser.match(tt.arrow)) {
        // (xxx ) =>
        return true;
      }
    }
  }
  return false;
}

export function parseTSTypeOrTypePredicateAnnotation(
  parser: Parser,
  returnToken: TokenType,
): n.TSTypeAnnotationNode {
  const start = parser.getPosition();
  parser.pushScope('TYPE', true);
  parser.expect(returnToken);

  let typePredicateVariable;
  if (tsIsIdentifier(parser)) {
    typePredicateVariable = tryTSParse(parser, parseTSTypePredicatePrefix);
  }
  if (typePredicateVariable === undefined) {
    parser.popScope('TYPE');
    return parseTSTypeAnnotation(parser, /* eatColon */ false, start);
  }

  const type = parseTSTypeAnnotation(parser, /* eatColon */ false);

  const typePredicateStart = parser.getLoc(typePredicateVariable).start;
  parser.popScope('TYPE');

  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeAnnotation',
    typeAnnotation: {
      loc: parser.finishLoc(typePredicateStart),
      type: 'TSTypePredicate',
      parameterName: typePredicateVariable,
      typeAnnotation: type,
    },
  };
}

function tryTSParseTypeAnnotation(
  parser: Parser,
): undefined | n.TSTypeAnnotationNode {
  return parser.match(tt.colon) ? parseTSTypeAnnotation(parser) : undefined;
}

function tryTSParseType(parser: Parser): undefined | n.TSTypeNode {
  return tsEatThenParseType(parser, tt.colon);
}

function parseTSTypePredicatePrefix(
  parser: Parser,
): undefined | n.IdentifierNode {
  const id = parseIdentifier(parser);
  if (parser.isContextual('is') && !parser.hasPrecedingLineBreak()) {
    parser.next();
    return id;
  }
}

export function parseTSTypeAnnotation(
  parser: Parser,
  eatColon: boolean = true,
  start: Position = parser.getPosition(),
): n.TSTypeAnnotationNode {
  parser.pushScope('TYPE', true);

  if (eatColon) {
    parser.expect(tt.colon);
  }

  const typeAnnotation = parseTSType(parser);
  parser.popScope('TYPE');

  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeAnnotation',
    typeAnnotation,
  };
}

/** Be sure to be in a type context before calling parser. using `tsInType`. */
function parseTSType(parser: Parser): n.TSTypeNode {
  parser.pushScope('TYPE', true);

  const type = parseTSNonConditionalType(parser);
  if (parser.hasPrecedingLineBreak() || !parser.eat(tt._extends)) {
    parser.popScope('TYPE');
    return type;
  }

  const start = parser.getLoc(type).start;
  const checkType = type;
  const extendsType = parseTSNonConditionalType(parser);
  parser.expect(tt.question);
  const trueType = parseTSType(parser);
  parser.expect(tt.colon);
  const falseType = parseTSType(parser);
  parser.popScope('TYPE');
  return {
    loc: parser.finishLoc(start),
    type: 'TSConditionalType',
    checkType,
    extendsType,
    trueType,
    falseType,
  };
}

function parseTSNonConditionalType(parser: Parser): n.TSTypeNode {
  if (tsIsStartOfFunctionType(parser)) {
    return parseTSFunctionType(parser);
  }

  if (parser.match(tt._new)) {
    // As in `new () => Date`
    return parseTSConstructorType(parser);
  }

  return parseTSUnionTypeOrHigher(parser);
}

export function parseTSTypeAssertion(parser: Parser): n.TSTypeAssertionNode {
  const start = parser.getPosition();
  const _const = tryTSNextParseConstantContext(parser);
  const typeAnnotation = _const || tsNextThenParseType(parser);
  parser.expectRelational('>');

  const expression = parseMaybeUnary(parser, 'ts type assertion');
  if (_const) {
    tsCheckLiteralForConstantContext(parser, expression);
  }

  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeAssertion',
    expression,
    typeAnnotation,
  };
}

export function parseTSHeritageClause(
  parser: Parser,
  descriptor: string,
): Array<n.TSExpressionWithTypeArgumentsNode> {
  const originalStart = parser.state.startPos;
  const delimitedList = parseTSDelimitedList(
    parser,
    'HeritageClauseElement',
    parseTSExpressionWithTypeArguments,
  );

  if (!delimitedList.length) {
    parser.addDiagnostic({
      start: originalStart,
      message: `'${descriptor}' list cannot be empty.`,
    });
  }

  return delimitedList;
}

function parseTSExpressionWithTypeArguments(
  parser: Parser,
): n.TSExpressionWithTypeArgumentsNode {
  const start = parser.getPosition();

  // Note: TS uses parseLeftHandSideExpressionOrHigher,
  // then has grammar errors later if it's not an EntityName.
  const expression = parseTSEntityName(parser, /* allowReservedWords */ false);

  let typeParameters;
  if (parser.isRelational('<')) {
    typeParameters = parseTSTypeArguments(parser);
  }

  return {
    loc: parser.finishLoc(start),
    type: 'TSExpressionWithTypeArguments',
    expression,
    typeParameters,
  };
}

export function parseTSInterfaceDeclaration(
  parser: Parser,
  start: Position,
): n.TSInterfaceDeclarationNode {
  parser.pushScope('TYPE', true);
  const id = parseIdentifier(parser);
  const typeParameters = tryParseTSTypeParameters(parser);

  let _extends;
  if (parser.eat(tt._extends)) {
    _extends = parseTSHeritageClause(parser, 'extends');
  }

  const bodyStart = parser.getPosition();
  const bodyItems = parseTSObjectTypeMembers(parser);
  const body: n.TSInterfaceBodyNode = {
    loc: parser.finishLoc(bodyStart),
    type: 'TSInterfaceBody',
    body: bodyItems,
  };

  parser.popScope('TYPE');
  return {
    loc: parser.finishLoc(start),
    type: 'TSInterfaceDeclaration',
    id,
    body,
    typeParameters,
    extends: _extends,
  };
}

export function parseTSTypeAliasDeclaration(
  parser: Parser,
  start: Position,
): n.TSTypeAliasDeclarationNode {
  const id = parseIdentifier(parser);
  const typeParameters = tryParseTSTypeParameters(parser);
  const typeAnnotation = tsExpectThenParseType(parser, tt.eq);
  parser.semicolon();
  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeAliasDeclaration',
    id,
    typeParameters,
    typeAnnotation,
  };
}

function tsInNoContext<T>(parser: Parser, cb: ParserCallback<T>): T {
  const oldContext = parser.state.context;
  parser.state.context = [oldContext[0]];
  const res = cb(parser);
  parser.state.context = oldContext;
  return res;
}

function tsEatThenParseType(
  parser: Parser,
  token: TokenType,
): n.TSTypeNode | undefined {
  if (parser.match(token)) {
    return tsNextThenParseType(parser);
  }
}

function tsExpectThenParseType(parser: Parser, token: TokenType): n.TSTypeNode {
  return tsDoThenParseType(parser, () => {
    parser.expect(token);
  });
}

export function tsNextThenParseType(parser: Parser): n.TSTypeNode {
  return tsDoThenParseType(parser, () => parser.next());
}

function tsDoThenParseType(parser: Parser, cb: () => void): n.TSTypeNode {
  cb();
  return parseTSType(parser);
}

function parseTSEnumMember(parser: Parser): n.TSEnumMemberNode {
  const start = parser.getPosition();
  // Computed property names are grammar errors in an enum, so accept just string literal or identifier.
  const id = parser.match(tt.string)
    ? parseExpressionAtom(parser, 'ts enum member id')
    : parseIdentifier(parser, /* liberal */ true);

  let initializer;
  if (parser.eat(tt.eq)) {
    initializer = parseMaybeAssign(parser, 'ts enum member initializer');
  }

  return {
    loc: parser.finishLoc(start),
    type: 'TSEnumMember',
    initializer,
    id,
  };
}

export function parseTSEnumDeclaration(
  parser: Parser,
  start: Position,
  isConst: boolean,
): n.TSEnumDeclarationNode {
  const id = parseIdentifier(parser);

  const openContext = parser.expectOpening(
    tt.braceL,
    tt.braceR,
    'ts enum declaration',
  );

  const members = parseTSDelimitedList(
    parser,
    'EnumMembers',
    parseTSEnumMember,
  );
  parser.expectClosing(openContext);

  return {
    loc: parser.finishLoc(start),
    type: 'TSEnumDeclaration',
    members,
    id,
    const: isConst,
  };
}

export function parseTSModuleBlock(parser: Parser): n.TSModuleBlockNode {
  const start = parser.getPosition();

  const openContext = parser.expectOpening(
    tt.braceL,
    tt.braceR,
    'ts module block',
  );

  // Inside of a module block is considered 'top-level', meaning it can have imports and exports.
  const {body} = parseBlockOrModuleBlockBody(
    parser,
    /* allowDirectives */ false,
    /* topLevel */ true,
    openContext,
  );
  return {
    loc: parser.finishLoc(start),
    type: 'TSModuleBlock',
    body,
  };
}

export function parseTSModuleOrNamespaceDeclaration(
  parser: Parser,
  start: Position,
): n.TSModuleDeclarationNode {
  const id = parseIdentifier(parser);

  let body;
  if (parser.eat(tt.dot)) {
    body = parseTSModuleOrNamespaceDeclaration(parser, parser.getPosition());
  } else {
    body = parseTSModuleBlock(parser);
  }

  return {
    loc: parser.finishLoc(start),
    type: 'TSModuleDeclaration',
    id,
    body,
  };
}

export function parseTSAmbientExternalModuleDeclaration(
  parser: Parser,
  start: Position,
): n.TSModuleDeclarationNode {
  let global;
  let id;
  if (parser.isContextual('global')) {
    global = true;
    id = parseIdentifier(parser);
  } else if (parser.match(tt.string)) {
    id = parseStringLiteral(parser);
  } else {
    throw parser.unexpected();
  }

  let body;
  if (parser.match(tt.braceL)) {
    body = parseTSModuleBlock(parser);
  } else {
    parser.semicolon();
  }

  return {
    loc: parser.finishLoc(start),
    type: 'TSModuleDeclaration',
    id,
    global,
    body,
  };
}

export function parseTSImportEqualsDeclaration(
  parser: Parser,
  start: Position,
  isExport: boolean = false,
): n.TSImportEqualsDeclarationNode {
  const id = parseIdentifier(parser);
  parser.expect(tt.eq);

  const moduleReference = parseTSModuleReference(parser);
  parser.semicolon();

  return {
    loc: parser.finishLoc(start),
    type: 'TSImportEqualsDeclaration',
    id,
    moduleReference,
  };
}

function tsIsExternalModuleReference(parser: Parser): boolean {
  return (
    parser.isContextual('require') &&
    parser.lookaheadState().tokenType === tt.parenL
  );
}

function parseTSModuleReference(parser: Parser): n.TSModuleReferenceNode {
  return tsIsExternalModuleReference(parser)
    ? parseTSExternalModuleReference(parser)
    : parseTSEntityName(parser, /* allowReservedWords */ false);
}

function parseTSExternalModuleReference(
  parser: Parser,
): n.TSExternalModuleReferenceNode {
  const start = parser.getPosition();
  parser.expectContextual('require');
  const openContext = parser.expectOpening(
    tt.parenL,
    tt.parenR,
    'ts external module reference',
  );

  let expression;
  if (parser.match(tt.string)) {
    expression = parseStringLiteral(parser);
  } else {
    parser.addDiagnostic({
      message: 'Invalid TS external module reference expression',
    });

    // Skip as much of the next expression as we can
    parseExpressionAtom(parser, 'ts external module reference expression');

    // Create a fake string literal
    expression = {
      loc: parser.finishLoc(start),
      type: 'StringLiteral',
      value: '',
    } as n.StringLiteralNode;
  }

  parser.expectClosing(openContext);

  return {
    loc: parser.finishLoc(start),
    type: 'TSExternalModuleReference',
    expression,
  };
}

// Utilities

type ParserCallback<T> = (parser: Parser) => T;

function lookaheadTS<T>(parser: Parser, f: ParserCallback<T>): T {
  const state = parser.cloneState();
  const res = f(parser);
  parser.state = state;
  return res;
}

function tryTSParse<T>(
  parser: Parser,
  f: ParserCallback<undefined | false | T>,
): undefined | T {
  const state = parser.cloneState();
  const result = f(parser);
  if (result === undefined || result === false) {
    parser.state = state;
    return undefined;
  } else {
    return result;
  }
}

export function parseTSDeclare(
  parser: Parser,
  start: Position,
): n.StatementNode {
  let starttype = parser.state.tokenType;
  let kind: undefined | VariableDeclarationKind;
  if (parser.isContextual('let')) {
    starttype = tt._var;
    kind = 'let';
  }

  if (
    starttype === tt._const &&
    parser.match(tt._const) &&
    parser.isLookaheadContextual('enum')
  ) {
    // `const enum = 0;` not allowed because 'enum' is a strict mode reserved word.
    parser.expect(tt._const);
    parser.expectContextual('enum');
    return {
      declare: true,
      ...parseTSEnumDeclaration(parser, start, /* isConst */ true),
    };
  }

  switch (starttype) {
    case tt._function:
      return {
        ...parseFunctionStatement(parser, start),
        declare: true,
      };

    case tt._class:
      return {
        ...parseClass(
          parser,
          start,
          /* isStatement */ true,
          /* optionalId */ false,
        ),
        declare: true,
      };

    case tt._const:
    case tt._var:
      kind =
        kind === undefined
          ? assertVarKind(String(parser.state.tokenValue))
          : kind;
      return {
        declare: true,
        ...parseVarStatement(parser, start, kind),
      };

    case tt.name: {
      const value = String(parser.state.tokenValue);
      if (value === 'global') {
        return {
          declare: true,
          ...parseTSAmbientExternalModuleDeclaration(parser, start),
        };
      } else if (isTSDeclarationStart(parser)) {
        const id = parseIdentifier(parser);
        const decl = parseTypeExpressionStatement(parser, start, id);
        // TODO check decl.tokenType here
        return {...decl, declare: true};
      }
    }
  }

  parser.addDiagnostic({
    message: 'Unknown typescript declare start',
  });
  return parser.createUnknownNode('typescript declare start', start);
}

export function parseTSAbstractClass(
  parser: Parser,
  start: Position,
  optionalId: boolean,
): n.ClassDeclarationNode {
  const node = parseClass(parser, start, /* isStatement */ true, optionalId);
  if (node.type !== 'ClassDeclaration') {
    throw new Error('Passed isStatement above');
  }
  return {
    ...node,
    abstract: true,
  };
}

export function parseTSTypeArguments(
  parser: Parser,
): n.TSTypeParameterInstantiationNode {
  const start = parser.getPosition();
  parser.pushScope('TYPE', true);

  const params = tsInNoContext(parser, () => {
    parser.expectRelational('<');
    return parseTSDelimitedList(
      parser,
      'TypeParametersOrArguments',
      parseTSType,
    );
  });

  // This reads the next token after the `>` too, so do parser.in the enclosing context.
  // But be sure not to parse a regex in the jsx expression `<C<number> />`, so set exprAllowed = false
  parser.state.exprAllowed = false;
  parser.popScope('TYPE');
  parser.expectRelational('>');

  return {
    loc: parser.finishLoc(start),
    type: 'TSTypeParameterInstantiation',
    params,
  };
}

export function isTSDeclarationStart(parser: Parser): boolean {
  if (parser.match(tt.name)) {
    switch (parser.state.tokenValue) {
      case 'abstract':
      case 'declare':
      case 'enum':
      case 'interface':
      case 'module':
      case 'namespace':
      case 'type':
        return true;
    }
  }

  return false;
}

export function parseTSAccessModifier(
  parser: Parser,
): undefined | n.TSAccessibility {
  return parseTSModifier(parser, ['public', 'protected', 'private']);
}

export function isTSAbstractClass(parser: Parser): boolean {
  return (
    parser.isContextual('abstract') &&
    parser.lookaheadState().tokenType === tt._class
  );
}

export function parseTSExport(
  parser: Parser,
  start: Position,
):
  | undefined
  | n.TSNamespaceExportDeclarationNode
  | n.TSExportAssignmentNode
  | n.TSImportEqualsDeclarationNode {
  if (!parser.isSyntaxEnabled('ts')) {
    return undefined;
  }

  if (parser.match(tt._import)) {
    // `export const A =B;`
    parser.expect(tt._import);
    return parseTSImportEqualsDeclaration(parser, start, /* isExport */ true);
  }

  if (parser.eat(tt.eq)) {
    // `export = x;`
    const expression = parseExpression(parser, 'ts export assignment');
    parser.semicolon();
    return {
      loc: parser.finishLoc(start),
      type: 'TSExportAssignment',
      expression,
    };
  }

  if (parser.eatContextual('as')) {
    // `export as namespace A;`
    // See `parseNamespaceExportDeclaration` in TypeScript's own parser
    parser.expectContextual('namespace');
    const id = parseIdentifier(parser);
    parser.semicolon();
    return {
      loc: parser.finishLoc(start),
      type: 'TSNamespaceExportDeclaration',
      id,
    };
  }
}
