import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {FlowTypeAlias} from './FlowTypeAlias';

export function FlowDeclareTypeAlias(
  generator: Generator,
  node: t.FlowDeclareTypeAliasNode,
) {
  generator.word('declare');
  generator.space();
  FlowTypeAlias(generator, node);
}
