import {JSNodeBase, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowArrayTypeAnnotationNode = JSNodeBase & {
  type: 'FlowArrayTypeAnnotation',
  elementType: FlowNode,
};

export const flowArrayTypeAnnotation = createBuilder<FlowArrayTypeAnnotationNode>(
  'FlowArrayTypeAnnotation',
  {visitorKeys: ['elementType']},
);
