import {Master} from '@romejs/core';
import {Stats} from './MemoryFileSystem';
import {WorkerContainer} from './WorkerManager';
import Locker from '../common/utils/Locker';
import invariant from '@romejs/invariant';

export default class FileManager {
  constructor(master: Master) {
    this.master = master;
    this.fileToWorker = new Map();
    this.locker = new Locker();
  }

  master: Master;
  locker: Locker<string>;
  fileToWorker: Map<string, number>;

  getAllOwnedFilenames(): Array<string> {
    return Array.from(this.fileToWorker.keys());
  }

  hasOwner(filename: string): boolean {
    return this.getOwnerId(filename) !== undefined;
  }

  getOwnerId(filename: string): undefined | number {
    return this.fileToWorker.get(filename);
  }

  verifySize(filename: string, stats: Stats) {
    const project = this.master.projectManager.assertProjectExisting(filename);
    const maxSize = project.config.files.maxSize;

    if (stats.size > maxSize) {
      throw new Error(
        `The file ${filename} exceeds the project config max size of ${maxSize} bytes`,
      );
    }
  }

  getOwnerAssert(filename: string): WorkerContainer {
    const {workerManager} = this.master;
    const workerId = this.getOwnerId(filename);
    if (workerId === undefined) {
      throw new Error(`No worker found for ${filename}`);
    }

    const worker = workerManager.getWorkerAssert(workerId);
    invariant(worker.ready, "Worker %s isn't ready", workerId);
    return worker;
  }

  async getOrAssignOwner(filename: string): Promise<WorkerContainer> {
    const {workerManager} = this.master;

    const workerId = this.getOwnerId(filename);
    if (workerId === undefined) {
      return this.assignOwner(filename);
    } else {
      await workerManager.locker.waitLock(workerId);
      return workerManager.getWorkerAssert(workerId);
    }
  }

  async groupFilesByWorker(
    files: Array<string>,
  ): Promise<Array<Array<string>>> {
    const filesByWorker: Map<number, Array<string>> = new Map();

    // Populate our queues
    await Promise.all(
      files.map(async file => {
        const worker = await this.getOrAssignOwner(file);

        let queue = filesByWorker.get(worker.id);
        if (queue === undefined) {
          queue = [];
          filesByWorker.set(worker.id, queue);
        }
        queue.push(file);
      }),
    );

    return Array.from(filesByWorker.values());
  }

  async evict(filename: string) {
    // Find owner
    const workerId = this.getOwnerId(filename);
    if (workerId === undefined) {
      return undefined;
    }

    // Notify the worker to remove it from 'it's cache
    const worker = this.master.workerManager.getWorkerAssert(workerId);
    await worker.bridge.evict.call({
      filename,
    });
  }

  async disown(filename: string) {
    // Find owner
    const workerId = this.getOwnerId(filename);
    if (workerId === undefined) {
      return undefined;
    }

    // Evict file from 'worker cache
    await this.evict(filename);

    // Evict from 'disk cache
    await this.master.cache.evict(filename);

    // Disown it from 'our internal map
    this.fileToWorker.delete(filename);

    // Remove the total size from 'this worker so it'll be assigned next
    const stats = this.master.memoryFs.getFileStatsAssert(filename);
    this.master.workerManager.disown(workerId, stats);
  }

  async onFileChange(
    filename: string,
    oldStats: undefined | Stats,
    newStats: Stats,
  ) {
    const {logger, workerManager} = this.master;

    // Send update to worker owner
    if (this.hasOwner(filename)) {
      // Get the worker
      const workerId = this.getOwnerId(filename);
      if (workerId === undefined) {
        throw new Error(`Expected worker id for ${filename}`);
      }

      // Evict the file from 'cache
      await this.evict(filename);

      // Verify that this file doesn't exceed any size limit
      this.verifySize(filename, newStats);

      // Add on the new size, and remove the old
      if (oldStats === undefined) {
        throw new Error(
          'File already has an owner so expected to have old stats but had none',
        );
      }
      workerManager.disown(workerId, oldStats);
      workerManager.own(workerId, newStats);
    } else if (
      await this.master.projectManager.maybeEvictPossibleConfig(filename)
    ) {
      logger.info(
        `[FileManager] Evicted the project belonging to config %s`,
        filename,
      );
    } else {
      logger.info(`[FileManager] No owner for eviction %s`, filename);
    }
  }

  async assignOwner(filename: string): Promise<WorkerContainer> {
    const {workerManager, logger} = this.master;

    const lock = await this.locker.getLock(filename);

    // We may have waited on the lock and could already have an owner
    if (this.hasOwner(filename)) {
      lock.release();
      return this.getOwnerAssert(filename);
    }

    const worker = await workerManager.getNextWorker(filename);

    // Add ourselves to the file map
    logger.info(
      `[FileManager] File %s assigned to worker %s`,
      filename,
      worker.id,
    );
    this.fileToWorker.set(filename, worker.id);

    // Release and continue
    lock.release();
    return worker;
  }
}
