import {PartialDiagnosticAdvice} from '@romejs/diagnostics';
import {Scope} from '../../scopes';
import T from '../T';
import {orderBySimilarity} from '@romejs/string-utils';
import findClosestStringMatch from '../../utils/findClosestStringMatch';
import E from './E';

export default class UnknownPropE extends E {
  constructor(
    scope: Scope,
    originNode: undefined | Node,
    opts: {
      object: T,
      property: T,
      key: string,
      thisKeys: Array<string>,
      protoKeys: Array<string>,
    },
  ) {
    super(scope, originNode);
    this.thisKeys = opts.thisKeys;
    this.protoKeys = opts.protoKeys;
    this.allProps = [...this.thisKeys, ...this.protoKeys];
    this.key = opts.key;
    this.object = opts.object;
    this.property = opts.property;
  }

  static type = 'UnknownPropE';
  allProps: Array<string>;
  thisKeys: Array<string>;
  protoKeys: Array<string>;
  property: T;
  object: T;
  key: string;

  sortProps(props: Array<string>): Array<string> {
    if (props.length === 0) {
      return props;
    }

    const ratings = orderBySimilarity(this.key, props);
    const sortedProps = ratings.map(prop => prop.target);
    return sortedProps;
  }

  getError() {
    let message: string = `Property '${this.key}' not found in`;

    const suggestion = findClosestStringMatch(this.key, this.allProps);

    let infoMessage = '';
    let infoList = undefined;

    if (suggestion === undefined && this.allProps.length > 0) {
      infoMessage = 'Possible object properties are';

      infoList = [
        ...this.sortProps(this.thisKeys),
        ...this.sortProps(this.protoKeys),
      ];
    } else if (suggestion !== undefined) {
      infoMessage = `Did you mean '${suggestion}'?`;
    } else {
      infoMessage = 'There are no properties.';
    }

    const advice: PartialDiagnosticAdvice = [
      {
        type: 'log',
        category: 'info',
        message: infoMessage,
      },
    ];

    if (infoList !== undefined) {
      advice.push({
        type: 'list',
        list: infoList,
      });
    }

    return {
      message,
      advice,
      lowerTarget: this.property,
      upperTarget: this.object,
    };
  }
}
