import {
  JSNodeBase,
  JSXNamespacedNameNode,
  JSXIdentifierNode,
  JSXMemberExpressionNode,
  JSXAttributeNode,
  JSXSpreadAttributeNode,
  JSXOpeningFragmentNode,
  TSTypeParameterInstantiationNode,
} from '../index';
import {createBuilder} from '../utils';

export type JSXOpeningElementNode = JSNodeBase & {
  type: 'JSXOpeningElement',
  name:
    | JSXIdentifierNode
    | JSXMemberExpressionNode
    | JSXOpeningFragmentNode
    | JSXNamespacedNameNode,
  selfClosing?: undefined | boolean,
  attributes: Array<JSXAttributeNode | JSXSpreadAttributeNode>,
  typeArguments?: undefined | TSTypeParameterInstantiationNode,
};

export const jsxOpeningElement = createBuilder<JSXOpeningElementNode>(
  'JSXOpeningElement',
  {
    visitorKeys: ['name', 'attributes', 'typeArguments'],
  },
);
