import {JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type RegExpLiteralNode = JSNodeBase & {
  type: 'RegExpLiteral';
  pattern: string;
  global?: undefined | boolean;
  multiline?: undefined | boolean;
  sticky?: undefined | boolean;
  insensitive?: undefined | boolean;
  noDotNewline?: undefined | boolean;
  unicode?: undefined | boolean;
};

export const regExpLiteral = createBuilder<RegExpLiteralNode>('RegExpLiteral');
