import {JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type StringLiteralNode = JSNodeBase & {
  type: 'StringLiteral',
  value: string,
};

export const stringLiteral = createBuilder<StringLiteralNode>('StringLiteral');
