import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import FunctionT from '../../types/FunctionT';
import MaybeT from '../../types/MaybeT';

export function FlowFunctionTypeAnnotation(
  node: n.FlowFunctionTypeAnnotationNode,
  scope: Scope,
) {
  const bodyScope = scope.fork();
  if (node.typeParameters) {
    bodyScope.evaluate(node.typeParameters);
  }

  // build param types
  const params = [];
  let rest;
  for (const paramNode of node.params) {
    if (paramNode.type === 'FlowFunctionTypeParamRest') {
      rest = bodyScope.evaluate(paramNode.param.typeAnnotation);
    } else {
      let paramType = bodyScope.evaluate(paramNode.typeAnnotation);
      if (paramNode.optional === true) {
        paramType = new MaybeT(scope, paramNode, paramType);
      }
      params.push(paramType);
    }
  }

  // build return type
  const returns = bodyScope.evaluate(node.returnType);

  // create the function
  return new FunctionT(scope, node, {params, rest, returns, body: undefined});
}
