import {
  JSNodeBase,
  SuperNode,
  ImportNode,
  ExpressionNode,
  SpreadElementNode,
  FlowTypeParameterInstantiationNode,
  TSTypeParameterInstantiationNode,
} from '../index';
import {createBuilder} from '../utils';

export type NewExpressionNode = JSNodeBase & {
  type: 'NewExpression',
  callee: ExpressionNode | SuperNode | ImportNode,
  arguments: Array<ExpressionNode | SpreadElementNode>,
  typeArguments?:
    | undefined
    | FlowTypeParameterInstantiationNode
    | TSTypeParameterInstantiationNode,
  optional?: undefined | boolean,
};

export const newExpression = createBuilder<NewExpressionNode>('NewExpression', {
  visitorKeys: ['callee', 'arguments'],
});
