import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function SwitchCase(generator: Generator, node: t.SwitchCaseNode) {
  if (node.test) {
    generator.word('case');
    generator.space();
    generator.print(node.test, node);
    generator.token(':');
  } else {
    generator.word('default');
    generator.token(':');
  }

  generator.newline();

  if (node.consequent.length) {
    generator.printStatementList(node.consequent, node, {indent: true});
  }
}
