import {DiagnosticsPrinterFlags} from '@romejs/cli-diagnostics';
import {BundlerMode} from './bundler';
import {Platform} from './platform';
import {DEFAULT_PRINTER_FLAGS} from '@romejs/cli-diagnostics';

export const DEFAULT_API_REQUEST_FLAGS: APIRequestFlags = {
  cwd: process.cwd(),
  color: process.stdout.isTTY === true,
  json: false,
  silent: false,
  verbose: false,
  watch: false,
  rage: false,
  ragePath: '',
  logs: false,
  logPath: '',
  logWorkers: false,
  profile: false,
  profilePath: '',
  profileTimeout: 0,
  bundlerMode: undefined,
  resolverPlatform: undefined,
  resolverScale: undefined,
  resolverMocks: false,
  ...DEFAULT_PRINTER_FLAGS,
};

export type APIRequestFlags = DiagnosticsPrinterFlags & {
  cwd: string;
  color: boolean;
  json: boolean;
  silent: boolean;
  verbose: boolean;
  watch: boolean;

  // Bundler flags
  bundlerMode: undefined | BundlerMode;
  resolverPlatform: undefined | Platform;
  resolverScale: undefined | number;
  resolverMocks: boolean;

  // Debugging flags
  rage: boolean;
  ragePath: string;
  logs: boolean;
  logPath: string;
  logWorkers: boolean;
  profile: boolean;
  profilePath: string;
  profileTimeout: number;
};

export type FileReference = {
  project: number;
  uid: string;
  real: string;
};
