import {Node} from '@romejs/js-ast';
import {Path} from '@romejs/js-compiler';
import {
  functionExpression,
  blockStatement,
  stringLiteral,
} from '@romejs/js-ast';
import {template} from '@romejs/js-ast-utils';
import {getOptions} from './_utils';

export default {
  name: 'magicCJSTransform',
  enter(path: Path): Node {
    const {node, scope, context} = path;
    const options = getOptions(context);

    // Update relative requires with their module id
    if (
      node.type === 'CallExpression' &&
      node.callee.type === 'Identifier' &&
      node.callee.name === 'require' &&
      scope.getBinding('require') === undefined
    ) {
      const args = node.arguments;

      // Maybe error?
      if (args.length !== 1) {
        return node;
      }

      const arg = args[0];

      // Maybe error?
      if (arg.type !== 'StringLiteral') {
        return node;
      }

      const source = arg.value;

      if (
        Object.prototype.hasOwnProperty.call(
          options.relativeSourcesToModuleId,
          source,
        )
      ) {
        const resolved = options.relativeSourcesToModuleId[source];
        const sourceNode = stringLiteral.create({
          value: resolved,
        });
        return template.expression`Rome.require(${sourceNode})`;
      }
    }

    // Add module wrapper
    if (node.type === 'Program') {
      const source = stringLiteral.create({
        value: options.moduleId,
      });

      const body = blockStatement.create({
        directives: node.directives,
        body: node.body,
      });

      const factory = {
        ...functionExpression.assert(
          template.expression`(function(module, exports) {})`,
        ),
        body,
      };

      const wrapper = template.statement`Rome.declare(${source}, ${factory})`;

      return {
        ...node,
        directives: [],
        body: [wrapper],
      };
    }

    return node;
  },
};
