import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import StringLiteralT from '../../types/StringLiteralT';

export function FlowStringLiteralTypeAnnotation(
  node: n.FlowStringLiteralTypeAnnotationNode,
  scope: Scope,
) {
  return new StringLiteralT(scope, node, node.value);
}
