import {JSNodeBase, ExpressionNode, StatementNode} from '../index';
import {createBuilder} from '../utils';

export type WithStatementNode = JSNodeBase & {
  type: 'WithStatement',
  object: ExpressionNode,
  body: StatementNode,
};

export const withStatement = createBuilder<WithStatementNode>('WithStatement', {
  visitorKeys: ['object', 'body'],
});
