import {toErrorLike} from '@romejs/v8';
import {
  Diagnostic,
  PartialDiagnosticAdvice,
  PartialDiagnostic,
  PartialDiagnostics,
} from './types';
import {Position} from '@romejs/parser-core';
import {formatAnsi} from '@romejs/string-ansi';
import {errors} from '@romejs/v8';

function normalizeArray<T>(val: undefined | Array<T>): Array<T> {
  if (Array.isArray(val)) {
    return val;
  } else {
    return [];
  }
}

export function mergeDiagnostics(
  cwd: string,
  rootDiag: PartialDiagnostic,
  ...diags: Array<PartialDiagnostic>
): PartialDiagnostic {
  let mergedAdvice: PartialDiagnosticAdvice = [
    ...normalizeArray(rootDiag.advice),
  ];

  for (const diag of diags) {
    mergedAdvice = [
      ...mergedAdvice,
      ...deriveRootAdviceFromDiagnostic(diag, cwd),
      ...normalizeArray(diag.advice),
    ];
  }

  return {
    ...rootDiag,
    advice: mergedAdvice,
  };
}

export function getDiagnosticHeader(opts: {
  filename: undefined | string;
  start: undefined | Position;
  cwd: undefined | string;
}): string {
  const {start, filename} = opts;

  if (filename === undefined) {
    return 'unknown';
  }

  if (start === undefined) {
    return `<filelink filename="${filename}" />`;
  }

  return `<filelink filename="${filename}" line="${start.line}" column="${
    start.column
  }" />`;
}

export function deriveRootAdviceFromDiagnostic(
  diag: PartialDiagnostic | Diagnostic,
  cwd: string,
  skipFrame: boolean = false,
): PartialDiagnosticAdvice {
  const advice: PartialDiagnosticAdvice = [];

  let header = getDiagnosticHeader({
    start: diag.start,
    filename: diag.filename,
    cwd,
  });

  if (diag.category !== undefined) {
    header += ' ' + formatAnsi.bold(diag.category);
  }

  if (diag.fixable === true) {
    header += ' ' + formatAnsi.inverse(' FIXABLE ');
  }

  advice.push({
    type: 'log',
    category: 'none',
    message: header,
  });

  advice.push({
    type: 'log',
    category: 'error',
    message: diag.message,
  });

  if (skipFrame === false) {
    if (diag.start !== undefined && diag.end !== undefined) {
      advice.push({
        type: 'frame',
        code: diag.code,
        filename: diag.filename,
        marker: diag.marker,
        start: diag.start,
        end: diag.end,
      });
    } else if (diag.marker !== undefined) {
      // If we have no start/end, but we do have a marker then output is a log error
      advice.push({
        type: 'log',
        category: 'error',
        message: diag.marker,
      });
    }
  }

  return advice;
}

type DeriveErrorDiagnosticOpts = {
  error: unknown;
  name?: undefined | string;
  filename?: undefined | string;
  firstAdvice?: PartialDiagnosticAdvice;
  lastAdvice?: PartialDiagnosticAdvice;
};

export function deriveDiagnosticFromError(
  opts: DeriveErrorDiagnosticOpts,
): PartialDiagnostic {
  const {error, filename, firstAdvice = [], lastAdvice = []} = opts;
  let {name = 'Error'} = opts;

  let targetFilename: undefined | string = filename;
  let targetCode = undefined;
  let targetLoc = undefined;

  let message: string = '';
  let advice: PartialDiagnosticAdvice = [];

  if (error != null && typeof error === 'object') {
    // Point the target to the closest frame with a filename
    const frames = errors.getFrames(error);
    for (const frame of frames) {
      if (frame.filename === undefined) {
        continue;
      }

      targetFilename = frame.filename;
      targetLoc = errors.getSourceLocationFromFrame(frame);
      break;
    }

    ({name, message} = toErrorLike(error));

    if (name !== undefined && name !== 'Error') {
      message = `${name}: ${message}`;
    }

    advice = [
      ...firstAdvice,
      ...getErrorStackAdvice(error),
      ...errors.getAdvice(error),
      ...lastAdvice,
    ];
  } else {
    message = `Not an error instance: ${String(error)}`;
    advice = [...firstAdvice, ...lastAdvice];
  }

  //
  const diagnostic: PartialDiagnostic = {
    filename: targetFilename,
    start: targetLoc === undefined ? undefined : targetLoc.start,
    end: targetLoc === undefined ? undefined : targetLoc.end,
    code: targetCode,
    category: name,
    message,
    advice,
  };
  return diagnostic;
}

export function getErrorStackAdvice(
  errorLike: unknown,
  title?: string,
): PartialDiagnosticAdvice {
  const error = errors.toErrorLike(errorLike);
  const frames = errors.getFrames(error);
  const {stack} = error;
  const advice: PartialDiagnosticAdvice = [];

  if (frames.length === 0 && stack !== undefined) {
    // Just in case we didn't get the frames for some reason

    if (title !== undefined) {
      advice.push({
        type: 'log',
        category: 'info',
        message: title,
      });
    }

    // Remove the `message` from the `stack`
    let cleanStack = stack;
    let removeMessage = `${error.name}: ${error.message}`;
    if (cleanStack.startsWith(removeMessage)) {
      cleanStack = cleanStack.slice(removeMessage.length);
    }

    advice.push({
      type: 'log',
      category: 'error',
      message: cleanStack,
    });
  } else {
    const adviceFrames = frames.map(frame => {
      const {
        typeName,
        functionName,
        methodName,
        filename,
        lineNumber,
        columnNumber,
        isEval,
        isNative,
        isConstructor,
        isAsync,
      } = frame;

      const prefixes = [];
      if (isAsync) {
        prefixes.push('await');
      }
      if (isEval) {
        prefixes.push('eval');
      }
      if (isConstructor) {
        prefixes.push('new');
      }
      const prefix = prefixes.length === 0 ? undefined : prefixes.join(' ');

      let object = typeName;
      let property = '<anonymous>';
      if (functionName !== undefined) {
        property = functionName;
      }
      if (methodName !== undefined) {
        property = methodName;
      }

      let suffix;
      if (isNative) {
        suffix = 'native';
      }

      return {
        suffix,
        prefix,
        object,
        property,
        filename,
        line: lineNumber,
        column: columnNumber,
      };
    });

    advice.push({
      type: 'stacktrace',
      title,
      frames: adviceFrames,
    });
  }

  return advice;
}

export function addOriginsToDiagnostics(
  origins: Array<string>,
  diagnostics: PartialDiagnostics,
): PartialDiagnostics {
  return diagnostics.map(diag => {
    return addOriginsToDiagnostic(origins, diag);
  });
}

export function addOriginsToDiagnostic(
  origins: Array<string>,
  diag: PartialDiagnostic,
): PartialDiagnostic {
  const newOrigins =
    diag.origins === undefined ? origins : [...origins, ...diag.origins];
  return {
    ...diag,
    origins: newOrigins,
  };
}
