import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type ConditionalExpressionNode = JSNodeBase & {
  type: 'ConditionalExpression',
  test: ExpressionNode,
  alternate: ExpressionNode,
  consequent: ExpressionNode,
};

export const conditionalExpression = createBuilder<ConditionalExpressionNode>(
  'ConditionalExpression',
  {visitorKeys: ['test', 'consequent', 'alternate']},
);
