import {JSNodeBase, IdentifierNode, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowFunctionTypeParamNode = JSNodeBase & {
  type: 'FlowFunctionTypeParam',
  name?: undefined | IdentifierNode,
  typeAnnotation: FlowNode,
  optional?: undefined | boolean,
};

export const flowFunctionTypeParam = createBuilder<FlowFunctionTypeParamNode>(
  'FlowFunctionTypeParam',
  {visitorKeys: ['name', 'typeAnnotation']},
);
