import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowArrayTypeAnnotation(
  generator: Generator,
  node: t.FlowArrayTypeAnnotationNode,
) {
  generator.print(node.elementType, node);
  generator.token('[');
  generator.token(']');
}
