import {Stdout} from './types';
import {writeOnNthLine, clearNthLine, getColumns} from './util';
import {Reporter} from '@romejs/cli-reporter';

const SPINNER_FRAMES = ''.split('');
const SPINNER_INTERVAL = 80;

export default class Spinner {
  constructor(reporter: Reporter, lineNumber: number = 0) {
    this.currentFrame = 0;
    this.prefix = '';
    this.lineNumber = lineNumber;
    this.reporter = reporter;
    this.stdout = reporter.stdout;
    this.text = '';
    this.intervalTimer = undefined;
    this.paused = false;
    this.lastRenderedLine = '';
    this.lastRenderTime = 0;
  }

  reporter: Reporter;
  lastRenderTime: number;
  lastRenderedLine: string;
  stdout: Stdout;
  prefix: string;
  currentFrame: number;
  lineNumber: number;

  text: string;
  intervalTimer: undefined | NodeJS.Timeout;
  paused: boolean;

  setPrefix(prefix: string) {
    this.prefix = prefix;
    this.render();
  }

  setText(text: string) {
    this.text = text;
    this.render();
  }

  start() {
    this.currentFrame = 0;
    this.render();
  }

  resume() {
    this.paused = false;
  }

  pause() {
    this.paused = true;
  }

  clearTimeout() {
    if (this.intervalTimer !== undefined) {
      clearTimeout(this.intervalTimer);
    }
  }

  clear() {
    this.lastRenderedLine = '';
    clearNthLine(this.stdout, this.lineNumber);
  }

  render() {
    // Check if we've already rendered within this timeframe
    const now = Date.now();
    if (this.lastRenderTime + SPINNER_INTERVAL > now) {
      return undefined;
    }

    // Remove the timeout that triggered this render
    this.clearTimeout();

    // Build spinner frame
    let spinnerFrame = SPINNER_FRAMES[this.currentFrame];

    // Build message
    let msg = `${this.prefix}${spinnerFrame} ${this.text}`;

    // Ensure we don't wrap to the next line
    const columns = getColumns(this.stdout);
    msg = msg.slice(0, columns);

    // Cheap bail out
    if (msg === this.lastRenderedLine) {
      this.queue();
      return undefined;
    } else {
      this.lastRenderedLine = msg;
      this.lastRenderTime = now;
    }

    writeOnNthLine(this.stdout, this.lineNumber, msg);

    // Don't update the spinner when paused
    if (this.paused === false) {
      this.currentFrame = ++this.currentFrame % SPINNER_FRAMES.length;
    }

    this.queue();
  }

  queue() {
    this.intervalTimer = setTimeout(
      (): void => this.render(),
      SPINNER_INTERVAL,
    );
  }

  stop() {
    this.pause();
    this.clearTimeout();
    this.clear();
  }
}
