import {
  JSNodeBase,
  PatternBase,
  PatternNode,
  FlowTypeAnnotationNode,
} from '../index';
import {createBuilder} from '../utils';

export type ArrayPatternNode = JSNodeBase &
  PatternBase & {
    type: 'ArrayPattern';
    elements: Array<undefined | PatternNode>;
    typeAnnotation?: undefined | FlowTypeAnnotationNode;
    optional?: undefined | true;
  };

export const arrayPattern = createBuilder<ArrayPatternNode>('ArrayPattern', {
  visitorKeys: ['elements', 'typeAnnotation'],
});
