import {Node} from '@romejs/js-ast';
import {HydrateTypeFactory, HydrateData} from '../Evaluator';
import {SerialTypeFactory} from './T';
import {Scope} from '../scopes';
import {HumanBuilder} from '../Utils';
import ObjT from './ObjT';
import T from './T';

export default class FunctionT extends ObjT {
  constructor(
    scope: Scope,
    originNode: undefined | Node,
    opts: {
      params: Array<T>;
      rest: undefined | T;
      returns: T;
      props?: Array<T>;
      proto?: undefined | T;
      body?: undefined | T;
    },
  ) {
    super(scope, originNode, {
      props: opts.props,
      proto: opts.proto,
      calls: [],
    });
    this.params = opts.params;
    this.rest = opts.rest;
    this.returns = opts.returns;
    this.body = opts.body;
  }

  static type = 'FunctionT';

  params: Array<T>;
  rest: undefined | T;
  returns: T;
  body: undefined | T;

  serialize(addType: SerialTypeFactory): HydrateData {
    return {
      params: this.params.map(type => addType(type)),
      rest: this.rest ? addType(this.rest) : undefined,
      returns: addType(this.returns),
      proto: this.proto === undefined ? null : addType(this.proto),
      body: this.body === undefined ? null : addType(this.body),
      props: this.props.map(type => addType(type)),
    };
  }

  static hydrate(
    scope: Scope,
    originNode: Node,
    data: HydrateData,
    getType: HydrateTypeFactory,
  ): T {
    return new FunctionT(scope, originNode, {
      params: Array(data.params).map(id => getType(id)),
      rest: data.rest === undefined ? undefined : getType(data.rest),
      returns: getType(data.returns),
      props: Array(data.props).map(id => getType(id)),
      proto: data.proto === undefined ? undefined : getType(data.proto),
      body: data.body === undefined ? undefined : getType(data.body),
    });
  }

  humanize(builder: HumanBuilder): string {
    return `(${this.params
      .map(param => builder.humanize(param))
      .join(', ')}) => ${builder.humanize(this.returns)}`;
  }

  reduce(): T {
    // No body, just a type signature
    const {body} = this;
    if (body === undefined) {
      return this;
    }

    // Reduce the body and create a new function
    const reducedBody = this.utils.reduce(body);
    if (reducedBody !== body) {
      return new FunctionT(this.scope, this.originNode, {
        params: this.params,
        rest: this.rest,
        returns: this.returns,
        props: this.props,
        proto: this.proto,
        body: reducedBody,
      });
    }

    // Already been reduced
    return this;
  }
}
