import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type BreakStatementNode = JSNodeBase & {
  type: 'BreakStatement',
  label?: undefined | IdentifierNode,
};

export const breakStatement = createBuilder<BreakStatementNode>(
  'BreakStatement',
  {visitorKeys: ['label']},
);
