import {Node} from '@romejs/js-ast';

export default function isConditional(node: undefined | Node): boolean {
  if (node === undefined) {
    return false;
  }

  switch (node.type) {
    case 'ConditionalExpression':
    case 'IfStatement':
      return true;

    default:
      return false;
  }
}
