import {
  JSNodeBase,
  IdentifierNode,
  StringLiteralNode,
  BlockStatementNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowDeclareModuleNode = JSNodeBase & {
  type: 'FlowDeclareModule',
  id: IdentifierNode | StringLiteralNode,
  body: BlockStatementNode,
  kind?: undefined | 'commonjs' | 'es',
};

export const flowDeclareModule = createBuilder<FlowDeclareModuleNode>(
  'FlowDeclareModule',
  {visitorKeys: ['id', 'body']},
);
