import {JSNodeBase, TSImportTypeNode, TSEntityNameNode} from '../index';
import {createBuilder} from '../utils';

export type TSTypeQueryNode = JSNodeBase & {
  type: 'TSTypeQuery';
  exprName: TSImportTypeNode | TSEntityNameNode;
};

export const tsTypeQuery = createBuilder<TSTypeQueryNode>('TSTypeQuery', {
  visitorKeys: ['exprName'],
});
