import {JSNodeBase, TSTypeNode} from '../index';
import {createBuilder} from '../utils';

export type TSParenthesizedTypeNode = JSNodeBase & {
  type: 'TSParenthesizedType';
  typeAnnotation: TSTypeNode;
};

export const tsParenthesizedType = createBuilder<TSParenthesizedTypeNode>(
  'TSParenthesizedType',
  {visitorKeys: ['typeAnnotation']},
);
