import {MasterQueryResponse} from '@romejs/core';
import {APIRequestFlags} from '../common/types/api';
import {APIRequestOpts} from './APIRequest';
import {Reporter} from '@romejs/cli-reporter';
import {DEFAULT_API_REQUEST_FLAGS} from '../common/types/api';
import APIRequest from './APIRequest';
import Master from '../master/Master';
import {MasterBridge, SOCKET_PATH, CLI_SOCKET_PATH} from '@romejs/core';
import fork from '../common/utils/fork';
import {createBridgeFromLocal, createBridgeFromSocket} from '@romejs/events';
import stream = require('stream');
import tty = require('tty');
import net = require('net');
import fs = require('fs');

const NEW_SERVER_INIT_TIMEOUT = 600000;

type APIOptions = {
  stdout?: stream.Writable;
  stderr?: stream.Writable;
  flags?: Partial<APIRequestFlags>;
};

type BridgeStatus = {
  bridge: MasterBridge;
  dedicated: boolean;
};

export default class API {
  constructor(opts: APIOptions) {
    this.flags = {
      ...DEFAULT_API_REQUEST_FLAGS,
      ...opts.flags,
    };

    this.bridgeStatus = undefined;

    const reporter = new Reporter({
      silent:
        this.flags.silent ||
        opts.stdout === undefined ||
        opts.stderr === undefined,
      verbose: this.flags.verbose,
      stderr: opts.stderr,
      stdout: opts.stdout,
      markupOptions: {
        cwd: this.flags.cwd,
      },
    });
    this.reporter = reporter;
    this.stdout = reporter.stdout;
    this.stderr = reporter.stderr;
  }

  flags: APIRequestFlags;
  reporter: Reporter;
  stdout: stream.Writable;
  stderr: stream.Writable;
  bridgeStatus: undefined | BridgeStatus;

  end() {
    const status = this.bridgeStatus;
    if (status !== undefined) {
      status.bridge.end();
      this.bridgeStatus = undefined;
    }
  }

  query(opts: APIRequestOpts): Promise<MasterQueryResponse> {
    const request = new APIRequest(this, opts);
    return request.init();
  }

  getBridge(): undefined | BridgeStatus {
    return this.bridgeStatus;
  }

  async attachBridge(bridge: MasterBridge, dedicated: boolean) {
    const {stderr, stdout, reporter} = this;

    this.bridgeStatus = {bridge, dedicated};

    bridge.stderr.subscribe(chunk => {
      stderr.write(chunk);
    });

    bridge.stdout.subscribe(chunk => {
      stdout.write(chunk);
    });

    // Listen for resize column events if stdout is a TTY
    if (stdout instanceof tty.WriteStream && stdout.isTTY) {
      function onStdoutResize() {
        bridge.setColumns.call(reporter.getColumns());
      }

      stdout.on('resize', onStdoutResize);

      bridge.endEvent.subscribe(() => {
        stdout.off('resize', onStdoutResize);
      });
    }

    await bridge.handshake();
  }

  async findOrStartMaster(): Promise<MasterBridge> {
    // First check if we already have a bridge connection
    const connected = this.getBridge();
    if (connected !== undefined) {
      return connected.bridge;
    }

    // Then check if there's already a running daemon
    const runningDaemon = await this.tryConnectToExistingDaemon();
    if (runningDaemon) {
      await this.attachBridge(runningDaemon, true);
      return runningDaemon;
    }

    // Otherwise, start a master inside this process
    const master = new Master(false);
    const bridge = createBridgeFromLocal(MasterBridge);
    await master.init();
    await Promise.all([
      master.attachToBridge(bridge),
      this.attachBridge(bridge, false),
    ]);
    return bridge;
  }

  async startDaemon(): Promise<undefined | MasterBridge> {
    const {reporter} = this;

    reporter.info('No running daemon found. Starting one...');
    const proc = fork('master', {
      detached: true,
    });
    proc.unref();

    let exited = false;

    const newDaemon: undefined | MasterBridge = await new Promise(resolve => {
      const timeout = setTimeout(() => {
        reporter.error('Daemon connection timed out');
        cleanup();
        resolve();
      }, NEW_SERVER_INIT_TIMEOUT);

      const socketServer = net.createServer(() => {
        cleanup();
        resolve(this.tryConnectToNewDaemon());
      });

      fs.unlink(CLI_SOCKET_PATH, () => {
        socketServer.listen(CLI_SOCKET_PATH);
      });

      proc.on('close', () => {
        exited = true;
        cleanup();
        resolve();
      });

      function cleanup() {
        clearTimeout(timeout);
        socketServer.close();
      }
    });
    if (newDaemon) {
      await this.attachBridge(newDaemon, true);
      return newDaemon;
    }

    // as a final precaution kill the server
    if (exited) {
      reporter.error('Daemon died while initialising.');
    } else {
      reporter.error('Failed to connect. Killing daemon.');
    }
    proc.kill();

    return undefined;
  }

  async tryConnectToNewDaemon(): Promise<undefined | MasterBridge> {
    const bridge = await this.tryConnectToExistingDaemon();
    if (bridge) {
      this.reporter.success(`Started daemon!`);
      return bridge;
    } else {
      return undefined;
    }
  }

  async tryConnectToExistingDaemon(): Promise<undefined | MasterBridge> {
    const promise: Promise<undefined | net.Socket> = new Promise(
      (resolve, reject) => {
        const socket = net.createConnection(
          {
            path: SOCKET_PATH,
          },
          () => {
            resolve(socket);
          },
        );

        socket.on('error', (err: NodeJS.ErrnoException) => {
          if (err.code === 'ENOENT') {
            resolve();
          } else {
            reject(err);
          }
        });
      },
    );

    const socket = await promise;
    if (socket === undefined) {
      return undefined;
    }

    const client = createBridgeFromSocket(MasterBridge, socket);
    await this.attachBridge(client, true);
    this.reporter.success('Connected to daemon');
    return client;
  }
}
