import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type ExportNamespaceSpecifierNode = JSNodeBase & {
  type: 'ExportNamespaceSpecifier',
  exported: IdentifierNode,
};

export const exportNamespaceSpecifier = createBuilder<ExportNamespaceSpecifierNode>(
  'ExportNamespaceSpecifier',
  {visitorKeys: ['exported']},
);
