import {JSNodeBase, TSTypeNode, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type TSAsExpressionNode = JSNodeBase & {
  type: 'TSAsExpression';
  typeAnnotation: TSTypeNode;
  expression: ExpressionNode;
};

export const tsAsExpression = createBuilder<TSAsExpressionNode>(
  'TSAsExpression',
  {visitorKeys: ['expression', 'typeAnnotation']},
);
