import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import StringLiteralT from '../../types/StringLiteralT';
import GetPropT from '../../types/GetPropT';

export function MemberExpression(node: n.MemberExpressionNode, scope: Scope) {
  let prop;
  if (node.computed === true) {
    prop = scope.evaluate(node.property);
  } else {
    const {property} = node;
    if (property.type === 'Identifier') {
      prop = new StringLiteralT(scope, property, property.name);
    } else {
      throw new Error('Unknown property');
    }
  }
  return new GetPropT(scope, node, scope.evaluate(node.object), prop);
}
