import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {_interfaceish} from './FlowInterfaceDeclaration';

export function FlowDeclareClass(
  generator: Generator,
  node: t.FlowDeclareClassNode,
  parent: t.Node,
) {
  if (parent.type !== 'FlowDeclareExportDeclaration') {
    generator.word('declare');
    generator.space();
  }
  generator.word('class');
  generator.space();
  _interfaceish(generator, node);
}
