import {JSNodeBase, ExpressionNode, SuperNode} from '../index';
import {createBuilder} from '../utils';

export type MemberExpressionNode = JSNodeBase & {
  type: 'MemberExpression';
  object: ExpressionNode | SuperNode;
  property: ExpressionNode;
  computed?: undefined | boolean;
};

export const memberExpression = createBuilder<MemberExpressionNode>(
  'MemberExpression',
  {
    visitorKeys: ['object', 'property'],
  },
);
