import {Profile} from '@romejs/v8';
import {Diagnostics} from '@romejs/diagnostics';
import {APIRequestFlags} from '../types/api';
import {Bridge} from '@romejs/events';

export type MasterQueryRequest = {
  columns: number;
  hasClearScreen: boolean;
  commandFlags: unknown;
  flags: APIRequestFlags;
  args: Array<string>;
  command: string;
  noData: boolean;
};

export type MasterQueryResponse = {
  error?: {
    handled: boolean;
    name: string;
    message: string;
    stack?: undefined | string;
  };
  killed?: boolean;
  diagnostics?: Diagnostics;
  data?: unknown;
};

export default class MasterBridge extends Bridge {
  stdout = this.createEvent<string, void>({name: 'stdout'});
  stderr = this.createEvent<string, void>({name: 'stderr'});
  setColumns = this.createEvent<number, void>({name: 'columns.set'});
  query = this.createEvent<MasterQueryRequest, MasterQueryResponse>({
    name: 'query',
  });
  profilingStart = this.createEvent<void, void>({name: 'profiling.start'});
  profilingStop = this.createEvent<
    void,
    {
      master: Profile;
      workers: Array<Profile>;
    }
  >({name: 'profiling.stop'});
}
