import {JSNodeBase, StatementNode, DirectiveNode} from '../index';
import {createBuilder} from '../utils';

export type BlockStatementNode = JSNodeBase & {
  type: 'BlockStatement',
  body: Array<StatementNode>,
  directives?: undefined | Array<DirectiveNode>,
};

export const blockStatement = createBuilder<BlockStatementNode>(
  'BlockStatement',
  {visitorKeys: ['body', 'directives']},
);
