import {MasterRequest} from '@romejs/core';
import {PartialDiagnostics} from '@romejs/diagnostics';
import {SourceMap} from '@romejs/codec-source-map';
import {FlagParser} from '@romejs/cli-flags';
import {Consumer} from '@romejs/codec-json';
import {catchDiagnosticsAsync} from '@romejs/diagnostics';
import {createMasterCommand} from '../../commands';
import {JS_EXTENSIONS} from '../../worker/extensions';
import {runTests} from '../testing/index';
import {commandCategories} from '../../commands';
import Bundler from '../bundler/Bundler';

import path = require('path');

function isTestFile(filename: string, testFolder: string): boolean {
  const parts = filename.split(path.sep);

  for (const part of parts) {
    // Don't include files/directories that are prefixed with an underscore
    if (part[0] === '_' && part !== testFolder) {
      return false;
    }

    // Don't ever include node_modules
    if (part === 'node_modules') {
      return false;
    }
  }

  // Make sure we're actually a test file
  for (const part of parts) {
    if (part === testFolder) {
      return true;
    }
  }

  return false;
}

export default createMasterCommand({
  category: commandCategories.CODE_QUALITY,
  description: 'run tests',
  hasHeader: true,

  parseCLIFlags(p: FlagParser) {
    return {
      coverage: p.boolean('coverage', {
        description: '',
        default: true,
      }),
      showAllCoverage: p.boolean('show-all-coverage', {
        description: '',
        default: false,
      }),
      updateSnapshots: p.boolean('update-snapshots', {
        description: '',
        default: false,
      }),
    };
  },

  async default(req: MasterRequest, flagConsumer: Consumer): Promise<void> {
    const {master, args, flags, reporter} = req;

    if (args.length === 0) {
      const project = await master.projectManager.assertProject(flags.cwd);
      args.push(project.dir);
    }

    const files: Array<string> = [];

    for (const arg of args) {
      const loc = path.resolve(flags.cwd, arg);
      await master.projectManager.assertProject(loc);
      const matches = master.memoryFs.glob(loc, {extensions: JS_EXTENSIONS});

      for (const filename of matches) {
        const project = master.projectManager.assertProjectExisting(filename);

        if (isTestFile(filename, project.config.testing.folderName)) {
          files.push(filename);
        }
      }
    }

    if (files.length === 0) {
      reporter.warn('No tests ran');
      return undefined;
    }

    let addDiagnostics: PartialDiagnostics = [];

    const tests: Map<
      string,
      {
        code: string;
        sourceMap: SourceMap;
      }
    > = new Map();

    for (const resolvedTestFile of files) {
      const {diagnostics} = await catchDiagnosticsAsync(
        'test: Bundling test file',
        async () => {
          const bundler = new Bundler(
            req,
            req.reporter.fork({
              stderr: undefined,
              stdout: undefined,
            }),
            req.getBundlerConfigFromFlags(),
            {
              ...req.getResolverOptionsFromFlags(),
              mocks: true,
            },
          );

          const {content, map} = await bundler.bundle(resolvedTestFile);
          tests.set(resolvedTestFile, {
            code: content,
            sourceMap: map,
          });
        },
      );

      addDiagnostics = [...addDiagnostics, ...diagnostics];
    }

    //
    await runTests(
      req,
      {
        coverage: flagConsumer.getProperty('coverage').asBoolean(true),
        showAllCoverage: flagConsumer
          .getProperty('showAllCoverage')
          .asBoolean(false),
        updateSnapshots: flagConsumer
          .getProperty('updateSnapshots')
          .asBoolean(false),
        verboseDiagnostics: req.flags.verboseDiagnostics,
      },
      tests,
      addDiagnostics,
    );
  },
});
