import {Comments} from './types';
import {ConsumerMap} from './Consumer';
import {PRIORITIZE_KEYS} from './constants';
import {naturalCompare} from '@romejs/string-utils';
import {isValidWord} from './Parser';
import Consumer from './Consumer';
import {formatNumber} from '@romejs/pretty-format';
import {escapeString} from '@romejs/string-escape';

function joinList(
  open: string,
  close: string,
  indent: string,
  items: Array<string>,
) {
  if (items.length === 0) {
    return open + close;
  }

  if (items.length === 1) {
    // Trim to remove indentation
    const first = items[0].trim();

    // We never want to place a comment in between braces because it will break for line comments
    // and look weird for blocks
    if (first[0] !== '/') {
      return open + first + close;
    }
  }

  return [open, ...items, indent + close].join('\n');
}

function stringifyKey(key: string): string {
  if (isValidWord(key)) {
    // A property key doesn't need quotes if it's a valid word
    return key;
  } else {
    return escapeString(key, {
      ignoreWhitespaceEscapes: true,
      json: true,
    });
  }
}

function stringifyDate(date: Date): string {
  const year = date.getUTCFullYear();
  const month = date.getUTCMonth();
  const day = date.getUTCDate();
  const hour = date.getUTCHours();
  const minute = date.getUTCMinutes();
  const second = date.getUTCSeconds();
  const ms = date.getUTCMilliseconds();

  // Build up the date string
  const dateStr = [year, month, day].join('-');

  // If there's no time then just return the date string as-is
  if (hour === 0 && minute === 0 && second === 0 && ms === 0) {
    return dateStr;
  }

  // Build up an ISO compatible string, but without a redundant Z suffix
  const secondStr = ms === 0 ? second : `${second}.${ms}`;
  const timeStr = [hour, minute, secondStr].join(':');
  return `${dateStr}T${timeStr}`;
}

export function stringifyComments(
  indent: string,
  comments: Comments,
): Array<string> {
  return comments.map(node => {
    if (node.type === 'BlockComment') {
      return `${indent}/*${node.value}*/`;
    } else {
      // node.type === 'LineComment'
      return `${indent}//${node.value}`;
    }
  });
}

function stringifyPrimitives(value: unknown): undefined | string {
  if (value === null) {
    return 'null';
  }

  if (value instanceof RegExp) {
    return `/${value.source}/${value.flags}`;
  }

  if (value instanceof Date) {
    return stringifyDate(value);
  }

  // Coerce primitive objects to their primitive form, as specified in ECMA262 24.5.2.1
  if (
    value instanceof Number ||
    value instanceof String ||
    value instanceof Boolean
  ) {
    value = value.valueOf();
  }

  // Basic primitive types
  switch (typeof value) {
    case 'symbol':
    case 'function':
      return 'null';

    case 'undefined':
      return 'undefined';

    case 'boolean':
      return value ? 'true' : 'false';

    case 'string':
      return escapeString(value, {json: true, ignoreWhitespaceEscapes: true});

    case 'bigint':
      return `${formatNumber(value)}n`;

    case 'number':
      return formatNumber(value);
  }

  return undefined;
}

function sortMapKeys(map: ConsumerMap): Set<string> {
  return new Set(Array.from(map.keys()).sort(naturalCompare));
}

function sortMap(map: ConsumerMap): Map<string, Consumer> {
  const sortedMap: Map<string, Consumer> = new Map();
  const sortedKeys: Set<string> = sortMapKeys(map);

  // Add any prioritized keys so they're before anything alphabetized
  for (const key of PRIORITIZE_KEYS) {
    if (sortedKeys.has(key)) {
      sortedKeys.delete(key);
      sortedMap.set(key, map.getStrict(key));
    }
  }

  // Now add the rest
  for (const key of sortedKeys) {
    sortedMap.set(key, map.getStrict(key));
  }

  return sortedMap;
}

type ConsumerInfo = {
  isTopLevel: boolean;
  level: number;
  stack: Set<unknown>;
};

type ObjectInfo = ConsumerInfo & {
  prevIndent: string;
  nextIndent: string;
};

function stringifyArray(consumer: Consumer, info: ObjectInfo) {
  const {level, prevIndent, nextIndent, stack} = info;

  let buff: Array<string> = [];

  const arr = consumer.asArray();
  for (const consumer of arr) {
    // Add element comments
    const comments = consumer.getComments().outer;
    buff = buff.concat(stringifyComments(nextIndent, comments));

    // Add the actual element line
    const element = stringifyConsumer(consumer, {
      isTopLevel: false,
      level: level + 1,
      stack,
    });
    buff.push(`${nextIndent}${element}`);
  }

  // Add inner comments
  const innerComments = consumer.getComments().inner;
  buff = buff.concat(stringifyComments(nextIndent, innerComments));

  return joinList('[', ']', prevIndent, buff);
}

function stringifyPlainObject(consumer: Consumer, info: ObjectInfo): string {
  const {level, prevIndent, stack, isTopLevel} = info;
  let {nextIndent} = info;

  // Must be an object if we failed all the other conditions
  let buff: Array<string> = [];
  const map = consumer.asMap();

  // Remove function, symbol, and undefined properties
  for (const [key, consumer] of map) {
    const value = consumer.asMixed();

    if (
      typeof value === 'function' ||
      typeof value === 'undefined' ||
      typeof value === 'symbol'
    ) {
      map.delete(key);
    }
  }

  let propLevel = level + 1;

  // We only want to increase the level for properties when we aren't at the top
  if (isTopLevel && level === 0) {
    propLevel = 0;
    nextIndent = '';
  }

  // Build properties
  for (const [key, consumer] of sortMap(map)) {
    // Add property comments
    const comments = consumer.getComments().outer;
    buff = buff.concat(stringifyComments(nextIndent, comments));

    // Add the actual property line
    const propKey = stringifyKey(key);
    const propValue = stringifyConsumer(consumer, {
      isTopLevel: false,
      level: propLevel,
      stack,
    });
    buff.push(`${nextIndent}${propKey}: ${propValue}`);
  }

  // We track this so we know whether we can safely put everything at the top level
  // If we only have comments then there's no way the parser could infer it was originally an object
  const hasProps = buff.length > 0;

  // Add inner comments
  const innerComments = consumer.getComments().inner;
  buff = buff.concat(stringifyComments(nextIndent, innerComments));

  if (level === 0 && isTopLevel) {
    if (hasProps) {
      return buff.join('\n');
    } else if (buff.length > 0) {
      // Otherwise we just have a bunch of comments
      // Indent them correctly and just output it as a normal object
      buff = buff.map(str => {
        return '  ' + str;
      });
    }
  }

  return joinList('{', '}', prevIndent, buff);
}

function stringifyObject(
  consumer: Consumer,
  value: unknown,
  opts: ConsumerInfo,
) {
  const {isTopLevel, level, stack} = opts;

  const info: ObjectInfo = {
    isTopLevel,
    nextIndent: '  '.repeat(level + 1),
    prevIndent: level === 0 ? '' : '  '.repeat(level - 1),
    level,
    stack,
  };

  try {
    stack.add(value);

    if (Array.isArray(value) || value instanceof Set) {
      return stringifyArray(consumer, info);
    }

    return stringifyPlainObject(consumer, info);
  } finally {
    stack.delete(value);
  }
}

export function stringifyConsumer(
  consumer: Consumer,
  opts: ConsumerInfo,
): string {
  const {isTopLevel, level, stack} = opts;
  let value = consumer.asMixed();

  // Support toJSON - code is a bit gnarly here due to TS type refinements
  if (
    typeof value === 'object' &&
    value != null &&
    consumer.hasProperty('toJSON')
  ) {
    const toJSON = consumer.getProperty('toJSON').asMixed();
    if (typeof toJSON === 'function') {
      const consumer2 = new Consumer({
        filename: consumer.filename,
        context: consumer.context,
        value: toJSON.call(value),
        parent: consumer.parent,
        key: consumer.key,
      });
      return stringifyConsumer(consumer2, {isTopLevel, level, stack});
    }
  }

  // Stringify primitives
  const asPrim = stringifyPrimitives(value);
  if (asPrim !== undefined) {
    return asPrim;
  }

  // Check if we're already stringfying this value to prevent recursion
  if (stack.has(value)) {
    throw new TypeError('Recursive');
  }

  return stringifyObject(consumer, value, opts);
}
