import {JSNodeBase, TSTypeNode} from '../index';
import {createBuilder} from '../utils';

export type TSIntersectionTypeNode = JSNodeBase & {
  type: 'TSIntersectionType';
  types: Array<TSTypeNode>;
};

export const tsIntersectionType = createBuilder<TSIntersectionTypeNode>(
  'TSIntersectionType',
  {
    visitorKeys: ['types'],
  },
);
