import {JSNodeBase, PatternBase, PatternNode} from '../index';
import {createBuilder} from '../utils';

export type RestElementNode = JSNodeBase &
  PatternBase & {
    type: 'RestElement';
    argument: PatternNode;
  };

export const restElement = createBuilder<RestElementNode>('RestElement', {
  visitorKeys: ['argument', 'typeAnnotation'],
});
