import {
  JSNodeBase,
  BlockStatementNode,
  CatchClauseNode,
} from '../index';
import {createBuilder} from '../utils';

export type TryStatementNode = JSNodeBase & {
  type: 'TryStatement',
  block: BlockStatementNode,
  handler: undefined | CatchClauseNode,
  finalizer: undefined | BlockStatementNode,
};

export const tryStatement = createBuilder<TryStatementNode>('TryStatement', {
  visitorKeys: ['block', 'handler', 'finalizer'],
});
