import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type MetaPropertyNode = JSNodeBase & {
  type: 'MetaProperty',
  meta: IdentifierNode,
  property: IdentifierNode,
};

export const metaProperty = createBuilder<MetaPropertyNode>('MetaProperty', {
  visitorKeys: ['meta', 'property'],
});
