import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowIntersectionTypeAnnotation(
  generator: Generator,
  node: t.FlowIntersectionTypeAnnotationNode,
) {
  generator.printJoin(node.types, node, {separator: andSeparator});
}

function andSeparator(generator: Generator) {
  generator.space();
  generator.token('&');
  generator.space();
}
