import {MasterRequest} from '@romejs/core';
import {Consumer} from '@romejs/codec-json';
import {commandCategories} from '../../commands';
import {createMasterCommand} from '../../commands';
import test from './test';
import lint from './lint';

export default createMasterCommand({
  category: commandCategories.CODE_QUALITY,
  description: 'install dependencies, run lint and tests',

  async default(req: MasterRequest, flags: Consumer): Promise<void> {
    const {reporter} = req;

    reporter.heading('Installing dependencies');
    // TODO

    reporter.heading('Linting');
    await lint.default(req, flags);

    reporter.heading('Running tests');
    await test.default(req, flags);
  },
});
