import {SourceType, ProgramSyntax} from '@romejs/js-ast';

import path = require('path');

export type UserOptions = {
  filename: string;
  syntax?: Array<ProgramSyntax>;
  sourceType?: SourceType;
  tokens?: boolean;
  allowReturnOutsideFunction?: boolean;
};

export type Options = {
  allowReturnOutsideFunction: boolean;
  syntax: Array<ProgramSyntax>;
  sourceType: SourceType;
  filename: string;
  tokens: boolean;
};

export function deriveSyntaxFromFilename(
  filename: string,
): Array<ProgramSyntax> {
  switch (path.extname(filename)) {
    case '.ts':
      return ['ts'];

    case '.tsx':
      return ['jsx', 'ts'];

    case '.jsx':
      return ['jsx', 'flow'];

    case '.rjs':
      return ['jsx', 'flow', 'ts'];

    default:
      // TODO remove this
      return ['jsx', 'flow'];
  }
}

// Interpret and default an options object
export function normalizeOptions(opts: UserOptions): Options {
  return {
    // I want to kill this option very badly
    allowReturnOutsideFunction: false,

    // Source type ("template", "script" or "module") for different semantics
    sourceType: 'script',

    // Whether we should be tracking tokens when parsing this file
    // NOTE: This is memory-intensive
    tokens: false,

    //
    ...opts,

    // Derive syntax from filename if not specified
    syntax:
      opts.syntax === undefined
        ? deriveSyntaxFromFilename(opts.filename)
        : opts.syntax,
  };
}
