import {
  JSNodeBase,
  JSXOpeningElementNode,
  JSXClosingElementNode,
  JSXTextNode,
  JSXExpressionContainerNode,
  JSXSpreadChildNode,
  JSXFragmentNode,
} from '../index';
import {createBuilder} from '../utils';

export type JSXElementNode = JSNodeBase & {
  type: 'JSXElement';
  openingElement: JSXOpeningElementNode;
  closingElement?: undefined | JSXClosingElementNode;
  children: Array<
    | JSXTextNode
    | JSXExpressionContainerNode
    | JSXSpreadChildNode
    | JSXElementNode
    | JSXFragmentNode
  >;
};

export const jsxElement = createBuilder<JSXElementNode>('JSXElement', {
  visitorKeys: ['openingElement', 'children', 'closingElement'],
});
