import {JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type BigIntLiteralNode = JSNodeBase & {
  type: 'BigIntLiteral',
  value: string,
};

export const bigIntLiteral = createBuilder<BigIntLiteralNode>('BigIntLiteral');
