import {JSNodeBase, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowTypeParameterInstantiationNode = JSNodeBase & {
  type: 'FlowTypeParameterInstantiation',
  params: Array<FlowNode>,
};

export const flowTypeParameterInstantiation = createBuilder<FlowTypeParameterInstantiationNode>(
  'FlowTypeParameterInstantiation',
  {visitorKeys: ['params']},
);
