import {
  JSNodeBase,
  DirectiveNode,
  StatementNode,
  InterpreterDirectiveNode,
  SourceType,
  Comment,
} from '../index';
import {PartialDiagnostics} from '@romejs/diagnostics';
import {createBuilder} from '../utils';

export type ProgramSyntax = 'ts' | 'jsx' | 'flow';

export type ProgramNode = JSNodeBase & {
  type: 'Program';
  directives: Array<DirectiveNode>;
  body: Array<StatementNode>;
  filename: string;
  interpreter: undefined | InterpreterDirectiveNode;
  sourceType: SourceType;
  diagnostics: PartialDiagnostics;
  comments: Array<Comment>;
  syntax: Array<ProgramSyntax>;
};

export const program = createBuilder<ProgramNode>('Program', {
  visitorKeys: ['directives', 'body'],
});
