import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {isStatement} from '@romejs/js-ast-utils';

export function IfStatement(generator: Generator, node: t.IfStatementNode) {
  generator.word('if');
  generator.space();
  generator.token('(');
  generator.print(node.test, node);
  generator.token(')');
  generator.space();

  let needsBlock = false;
  if (node.alternate !== undefined) {
    const lastStatement = getLastStatement(node.consequent);
    if (lastStatement !== undefined && lastStatement.type === 'IfStatement') {
      needsBlock = true;
    }
  }
  if (needsBlock) {
    generator.token('{');
    generator.newline();
    generator.indent();
  }

  generator.print(node.consequent, node);

  if (needsBlock) {
    generator.dedent();
    generator.newline();
    generator.token('}');
  }

  if (node.alternate) {
    if (generator.endsWith('}')) {
      generator.space();
    }
    generator.word('else');
    generator.space();
    generator.print(node.alternate, node);
  }
}

// Recursively get the last statement.
function getLastStatement(statement: t.Node): undefined | t.Node {
  if (isStatement(statement.body)) {
    return getLastStatement(statement.body);
  } else {
    return statement;
  }
}
