import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type LogicalExpressionNode = JSNodeBase & {
  type: 'LogicalExpression',
  operator: LogicalOperator,
  left: ExpressionNode,
  right: ExpressionNode,
};

export type LogicalOperator = '||' | '&&' | '??';

export const logicalExpression = createBuilder<LogicalExpressionNode>(
  'LogicalExpression',
  {visitorKeys: ['left', 'right']},
);
