import {ParserInterface, ParserOptions} from './Parser';
import {Reporter} from '@romejs/cli-reporter';
import Parser from './Parser';

export {ParserInterface as FlagParser};

export function parse(
  reporter: Reporter,
  args: Array<string>,
  opts?: ParserOptions,
): ParserInterface {
  const parser = new Parser(reporter, opts === undefined ? {} : opts, args);
  return parser.getInterface();
}

export function parseArgv(
  opts: undefined | ParserOptions = {},
): ParserInterface {
  return parse(Reporter.fromProcess(), process.argv.slice(2), opts);
}
