import {Path} from '@romejs/js-compiler';
import isTypeNode from './isTypeNode';

export default function isInTypeAnnotation(path: Path): boolean {
  const parentTypeAnnotation = path.findAncestry(path => isTypeNode(path.node));
  return parentTypeAnnotation !== undefined;
}
