import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowBooleanLiteralTypeAnnotation(
  generator: Generator,
  node: t.FlowBooleanLiteralTypeAnnotationNode,
) {
  generator.word(node.value ? 'true' : 'false');
}
