import util = require('util');
import path = require('path');
import fs = require('fs');

const writeFile = util.promisify(fs.writeFile);
const mkdir = util.promisify(fs.mkdir);
const readFile = util.promisify(fs.readFile);
const exists = util.promisify(fs.exists);

const SNAPSHOTS_DIR = '__snapshots__';

export default class SnapshotManager {
  constructor(testFilename: string, forceUpdate: boolean) {
    this.testFilename = testFilename;
    this.snapshotFolder = path.join(path.dirname(testFilename), SNAPSHOTS_DIR);
    this.snapshotFilename = path.join(
      this.snapshotFolder,
      path.basename(testFilename) + '.snap',
    );
    this.forceUpdate = forceUpdate;

    this.snapshots = new Map();
  }

  testFilename: string;
  forceUpdate: boolean;
  snapshotFolder: string;
  snapshotFilename: string;
  snapshots: Map<string, string>;

  async load() {
    const {snapshotFilename} = this;
    if (!(await exists(snapshotFilename))) {
      return undefined;
    }

    // If we're force updating, pretend that no snapshots exist on disk
    if (this.forceUpdate) {
      return undefined;
    }

    const file = await readFile(snapshotFilename, 'utf8');
    const json = JSON.parse(file);

    for (const key in json) {
      this.snapshots.set(key, String(json[key]));
    }
  }

  async save() {
    // No point producing an empty snapshot file
    if (this.snapshots.size === 0) {
      return undefined;
    }

    const {snapshotFolder, snapshotFilename} = this;

    // Create snapshots directory if it doesn't exist
    if (!(await exists(snapshotFolder))) {
      await mkdir(snapshotFolder);
    }

    // Build the snapshot
    const json: Dict<string> = {};
    for (const [key, value] of this.snapshots) {
      json[key] = value;
    }
    const formatted = JSON.stringify(json, undefined, '  ');

    // Save the file
    await writeFile(snapshotFilename, formatted);
  }

  toSnapshotKey(testName: string, id: number | string): string {
    if (id === 0) {
      return testName;
    } else {
      return `${testName}: ${id}`;
    }
  }

  get(key: string): undefined | string {
    return this.snapshots.get(key);
  }

  set(key: string, value: string) {
    this.snapshots.set(key, value);
  }
}
