import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowFunctionTypeAnnotation(
  generator: Generator,
  node: t.FlowFunctionTypeAnnotationNode,
  parent: t.Node,
) {
  generator.print(node.typeParameters, node);
  generator.token('(');
  generator.printCommaList(node.params, node);
  generator.token(')');

  // this node type is overloaded, not sure why but it makes it EXTREMELY annoying
  if (
    parent.type === 'FlowObjectTypeCallProperty' ||
    parent.type === 'FlowDeclareFunction'
  ) {
    generator.token(':');
  } else {
    generator.space();
    generator.token('=>');
  }

  generator.space();
  generator.print(node.returnType, node);
}
