import {
  JSNodeBase,
  TSSignatureDeclarationBase,
  TSTypeAnnotationNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSFunctionTypeNode = JSNodeBase &
  TSSignatureDeclarationBase & {
    type: 'TSFunctionType';
    typeAnnotation: TSTypeAnnotationNode;
  };

export const tsFunctionType = createBuilder<TSFunctionTypeNode>(
  'TSFunctionType',
  {visitorKeys: ['typeAnnotation', 'parameters', 'typeParameters']},
);
