import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowQualifiedTypeIdentifier(
  generator: Generator,
  node: t.FlowQualifiedTypeIdentifierNode,
) {
  generator.print(node.qualification, node);
  generator.token('.');
  generator.print(node.id, node);
}
