import {Node} from '@romejs/js-ast';
import {BINDING_IDENTIFIER_KEYS} from './getBindingIdentifiers';

export default function isBinding(
  node: undefined | Node,
  parent: undefined | Node,
): boolean {
  if (node === undefined || parent === undefined) {
    return false;
  }

  const keys = BINDING_IDENTIFIER_KEYS.get(parent.type);
  if (keys === undefined) {
    return false;
  }

  for (const key of keys) {
    const val = parent[key];
    if (Array.isArray(val) && val.includes(node)) {
      return true;
    } else if (val === node) {
      return true;
    }
  }

  return false;
}
