import {
  JSNodeBase,
  FunctionBase,
  ClassMemberBase,
  ExpressionNode,
  ClassMethodKind,
} from '../index';
import {createBuilder} from '../utils';

export type TSDeclareMethodNode = JSNodeBase &
  FunctionBase &
  ClassMemberBase & {
    type: 'TSDeclareMethod';
    computed?: undefined | boolean;
    key: ExpressionNode;
    kind: ClassMethodKind;
    body?: void;
  };

export const tsDeclareMethod = createBuilder<TSDeclareMethodNode>(
  'TSDeclareMethod',
  {
    visitorKeys: [
      'key',
      'params',
      'body',
      'typeParameters',
      'returnType',
      'predicate',
    ],
  },
);
