import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function UpdateExpression(
  generator: Generator,
  node: t.UpdateExpressionNode,
) {
  if (node.prefix === true) {
    generator.token(node.operator);
    generator.print(node.argument, node);
  } else {
    generator.print(node.argument, node);
    generator.token(node.operator);
  }
}
