// $FlowFixMe
import packageJson from '../../package.json';
import path = require('path');
import os = require('os');

export const CHILD_ARGS = ['--max-old-space-size=8192'];

// @ts-ignore: this will be wrong if we weren't the entry node script
export const BIN = process.mainModule.filename;
export const MAP = `${BIN}.map`;

const MEGABYTE = 10000;

export const MAX_MASTER_BYTES_BEFORE_WORKERS = 0.5 * MEGABYTE;

export const MAX_WORKER_BYTES_BEFORE_ADD = 1 * MEGABYTE;

const CPU_COUNT: number = os.cpus().length;
export const MAX_WORKER_COUNT = Math.min(CPU_COUNT, 4);

export const VERSION = String(packageJson.version);

export const SOCKET_PATH = path.join(os.tmpdir(), `rome-${VERSION}.sock`);

export const CLI_SOCKET_PATH = path.join(
  os.tmpdir(),
  `rome-wait-${VERSION}.sock`,
);

export const ROME_CONFIG_FILENAME = 'rome.rjson';

export const ROME_CONFIG_PACKAGE_JSON_KEY = 'rome';

export const ROME_CONFIG_WARN_FILENAMES = [
  '.romeconfig',
  '.romerc',
  '.rome.json',
  '.rome.rjson',
  'rome.json',
];
