import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type YieldExpressionNode = JSNodeBase & {
  type: 'YieldExpression',
  delegate?: undefined | boolean,
  argument?: undefined | ExpressionNode,
};

export const yieldExpression = createBuilder<YieldExpressionNode>(
  'YieldExpression',
  {visitorKeys: ['argument']},
);
