import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type UnaryExpressionNode = JSNodeBase & {
  type: 'UnaryExpression',
  operator: UnaryOperator,
  prefix?: undefined | boolean,
  argument: ExpressionNode,
};

export type UnaryOperator =
  | '-'
  | '+'
  | '!'
  | '~'
  | 'typeof'
  | 'void'
  | 'delete'
  | 'throw';

export const unaryExpression = createBuilder<UnaryExpressionNode>(
  'UnaryExpression',
  {visitorKeys: ['argument']},
);
