import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import IntersectionT from '../../types/IntersectionT';

export function FlowIntersectionTypeAnnotation(
  node: n.FlowIntersectionTypeAnnotationNode,
  scope: Scope,
) {
  return new IntersectionT(
    scope,
    node,
    node.types.map(type => {
      return scope.evaluate(type);
    }),
  );
}
