import {Node} from '@romejs/js-ast';
import * as parens from './parentheses';

function isOrHasCallExpression(node: Node): boolean {
  if (node.type === 'CallExpression') {
    return true;
  }

  if (node.type === 'MemberExpression') {
    return (
      isOrHasCallExpression(node.object) ||
      (!node.computed && isOrHasCallExpression(node.property))
    );
  } else {
    return false;
  }
}

export function hasExtraLineBetween(
  a: undefined | Node,
  b: undefined | Node,
): boolean {
  return getLinesBetween(a, b) > 1;
}

export function getLinesBetween(
  a: undefined | Node,
  b: undefined | Node,
): number {
  return a && b && a.loc && b.loc
    ? Math.max(0, b.loc.start.line - a.loc.end.line)
    : 0;
}

export function isMultiLine(node: undefined | Node): boolean {
  return Boolean(
    node && node.loc ? node.loc.end.line > node.loc.start.line : true,
  );
}

export function needsParens(
  node: Node,
  parent: undefined | Node,
  printStack: Array<Node>,
): undefined | boolean {
  if (!parent) {
    return false;
  }

  if (parent.type === 'NewExpression' && parent.callee === node) {
    if (isOrHasCallExpression(node)) {
      return true;
    }
  }

  // @ts-ignore
  const fn = parens[node.type];
  return fn ? fn(node, parent, printStack) : undefined;
}
