import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import ObjIndexPropT from '../../types/ObjIndexPropT';

export function FlowObjectTypeIndexer(
  node: n.FlowObjectTypeIndexerNode,
  scope: Scope,
) {
  return new ObjIndexPropT(
    scope,
    node,
    scope.evaluate(node.key),
    scope.evaluate(node.value),
  );
}
