import {JSNodeBase, DirectiveLiteralNode} from '../index';
import {createBuilder} from '../utils';

export type DirectiveNode = JSNodeBase & {
  type: 'Directive',
  value: DirectiveLiteralNode,
};

export const directive = createBuilder<DirectiveNode>('Directive', {
  visitorKeys: ['value'],
});
