import {JSNodeBase, ObjectProperties} from '../index';
import {createBuilder} from '../utils';

export type ObjectExpressionNode = JSNodeBase & {
  type: 'ObjectExpression',
  properties: ObjectProperties,
};

export const objectExpression = createBuilder<ObjectExpressionNode>(
  'ObjectExpression',
  {visitorKeys: ['properties']},
);
