import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowTupleTypeAnnotation(
  generator: Generator,
  node: t.FlowTupleTypeAnnotationNode,
) {
  generator.token('[');
  generator.printCommaList(node.types, node);
  generator.token(']');
}
