import {
  JSNodeBase,
  IdentifierNode,
  FlowQualifiedTypeIdentifierNode,
  FlowTypeParameterInstantiationNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowGenericTypeAnnotationNode = JSNodeBase & {
  type: 'FlowGenericTypeAnnotation',
  id: IdentifierNode | FlowQualifiedTypeIdentifierNode,
  typeParameters?: undefined | FlowTypeParameterInstantiationNode,
};

export const flowGenericTypeAnnotation = createBuilder<FlowGenericTypeAnnotationNode>(
  'FlowGenericTypeAnnotation',
  {visitorKeys: ['id', 'typeParameters']},
);
