import {JSNodeBase, ExpressionNode, FlowTypeAnnotationNode} from '../index';
import {createBuilder} from '../utils';

export type FlowTypeCastExpressionNode = JSNodeBase & {
  type: 'FlowTypeCastExpression',
  expression: ExpressionNode,
  typeAnnotation: FlowTypeAnnotationNode,
};

export const flowTypeCastExpression = createBuilder<FlowTypeCastExpressionNode>(
  'FlowTypeCastExpression',
  {visitorKeys: ['expression', 'typeAnnotation']},
);
