import {Reporter} from '@romejs/cli-reporter';
import {
  Diagnostic,
  DiagnosticAdviceItem,
  DiagnosticAdviceItemLog,
  DiagnosticAdviceItemList,
  DiagnosticAdviceItemOrderedList,
  DiagnosticAdviceItemCode,
  DiagnosticAdviceItemFrame,
  DiagnosticAdviceItemInspect,
  DiagnosticAdviceItemDiff,
  DiagnosticAdviceItemAction,
  DiagnosticAdviceItemStacktrace,
} from '@romejs/diagnostics';
import {Position} from '@romejs/parser-core';
import {toLines} from './utils';
import {getDiagnosticHeader} from '@romejs/diagnostics';
import buildPatchCodeFrame from './buildPatchCodeFrame';
import buildMessageCodeFrame from './buildMessageCodeFrame';
import {escapeMarkup} from '@romejs/string-markup';
import {formatAnsi} from '@romejs/string-ansi';
import invariant from '@romejs/invariant';

type AdvicePrintOptions = {
  fileSources: Map<string, Array<string>>,
  reporter: Reporter,
  diagnostic: Diagnostic,
  cwd: string,
};

export default function printAdvice(
  item: DiagnosticAdviceItem,
  opts: AdvicePrintOptions,
): boolean {
  switch (item.type) {
    case 'log':
      return printLog(item, opts);

    case 'ordered-list':
      return printOrderedList(item, opts);

    case 'list':
      return printList(item, opts);

    case 'diff':
      return printDiff(item, opts);

    case 'code':
      return printCode(item, opts);

    case 'frame':
      return printFrame(item, opts);

    case 'stacktrace':
      return printStacktrace(item, opts);

    case 'action':
      return printAction(item, opts);

    case 'inspect':
      return printInspect(item, opts);
  }
}

function printInspect(
  item: DiagnosticAdviceItemInspect,
  opts: AdvicePrintOptions,
): boolean {
  const {reporter} = opts;
  reporter.indent();
  reporter.inspect(item.data);
  reporter.dedent();
  return false;
}

function printDiff(
  item: DiagnosticAdviceItemDiff,
  opts: AdvicePrintOptions,
): boolean {
  opts.reporter.log(escapeMarkup(buildPatchCodeFrame(item.diff)));
  return false;
}

function printList(
  item: DiagnosticAdviceItemList,
  opts: AdvicePrintOptions,
): boolean {
  if (item.list.length === 0) {
    return true;
  } else {
    opts.reporter.list(item.list);
    return false;
  }
}

function printOrderedList(
  item: DiagnosticAdviceItemOrderedList,
  opts: AdvicePrintOptions,
): boolean {
  if (item.list.length === 0) {
    return true;
  } else {
    opts.reporter.orderedList(item.list, item.reverse);
    return false;
  }
}

function printCode(
  item: DiagnosticAdviceItemCode,
  opts: AdvicePrintOptions,
): boolean {
  const {reporter} = opts;
  const {code} = item;
  reporter.indent();
  reporter.log(escapeMarkup(code));
  reporter.dedent();
  return false;
}

function printFrame(
  item: DiagnosticAdviceItemFrame,
  opts: AdvicePrintOptions,
): boolean {
  const {reporter} = opts;
  const {start, end, filename, code, marker} = item;

  let cleanMarker: string = '';
  if (marker !== undefined) {
    cleanMarker = formatAnsi.bold(cleanMessage(marker));
  }

  let lines: undefined | Array<string>;
  if (code !== undefined) {
    lines = toLines(code);
  } else if (filename !== undefined) {
    lines = opts.fileSources.get(filename);
  }
  if (lines === undefined) {
    lines = [];
  }

  const frame = buildMessageCodeFrame(lines, start, end, cleanMarker);
  if (frame.trim() === '') {
    return true;
  }

  reporter.log(escapeMarkup(frame));
  return false;
}

function printAction(
  item: DiagnosticAdviceItemAction,
  opts: AdvicePrintOptions,
): boolean {
  const {reporter} = opts;
  reporter.log(`<bold>${item.message}</bold>`);
  reporter.log('You have the following choices:');
  reporter.list(
    item.buttons.map(button => {
      return `${button.text}: \`${button.command}\``;
    }),
  );
  return false;
}

function printStacktrace(
  item: DiagnosticAdviceItemStacktrace,
  opts: AdvicePrintOptions,
): boolean {
  const {diagnostic} = opts;
  const {frames} = item;

  let shownCodeFrames = 0;

  const isFirstPart = diagnostic.advice[0] === item;
  if (!isFirstPart) {
    opts.reporter.info(item.title === undefined ? 'Stack trace' : item.title);
    opts.reporter.spacer();
  }

  for (let i = 0; i < frames.length; i++) {
    const {
      filename,
      object,
      suffix,
      property,
      prefix,
      line,
      column,
      language,
      code,
    } = frames[i];

    const logParts = [];

    // Add prefix
    if (prefix !== undefined) {
      logParts.push(formatAnsi.dim(escapeMarkup(prefix)));
    }

    // Build path
    const objParts = [];
    if (object !== undefined) {
      objParts.push(formatAnsi.magenta(escapeMarkup(object)));
    }
    if (property !== undefined) {
      objParts.push(formatAnsi.cyan(escapeMarkup(property)));
    }
    if (objParts.length > 0) {
      logParts.push(objParts.join('.'));
    }

    // Add suffix
    if (suffix !== undefined) {
      logParts.push(formatAnsi.green(escapeMarkup(suffix)));
    }

    // Add source
    if (filename !== undefined && line !== undefined && column !== undefined) {
      const header = getDiagnosticHeader({
        filename,
        start: {
          index: -1,
          line,
          column,
        },
        cwd: opts.cwd,
      });

      if (logParts.length === 0) {
        logParts.push(header);
      } else {
        logParts.push('(' + formatAnsi.dim(header) + ')');
      }
    }

    printAdvice(
      {
        type: 'log',
        category: 'none',
        message: `  ${String(i + 1)}. ${logParts.join(' ')}`,
        compact: true,
      },
      opts,
    );

    // Push on frame
    if (
      shownCodeFrames < 2 &&
      filename !== undefined &&
      line !== undefined &&
      column !== undefined
    ) {
      const pos: Position = {
        index: -1,
        line,
        column,
      };

      printAdvice(
        {
          type: 'frame',
          language,
          filename,
          marker: undefined,
          start: pos,
          end: pos,
          code,
        },
        opts,
      );
      shownCodeFrames++;
    }
  }

  return false;
}

function printLog(
  item: DiagnosticAdviceItemLog,
  opts: AdvicePrintOptions,
): boolean {
  const {reporter} = opts;
  const {message, category} = item;

  if (message !== undefined) {
    switch (category) {
      case 'none':
        reporter.log(message);
        break;

      case 'warn':
        reporter.warn(message);
        break;

      case 'info':
        reporter.info(message);
        break;

      case 'question':
        reporter.question(message);
        break;

      case 'error':
        reporter.error(message);
        break;

      default:
        invariant(false, 'Unknown message item log category %s', category);
    }
  }

  return item.compact;
}

function cleanMessage(msg: string): string {
  msg = msg.trim();
  if (msg.endsWith('.')) {
    msg = msg.slice(0, -1);
  }
  return msg;
}
