import {
  JSNodeBase,
  IdentifierNode,
  TSInterfaceBodyNode,
  TSTypeParameterDeclarationNode,
  TSExpressionWithTypeArgumentsNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSInterfaceDeclarationNode = JSNodeBase & {
  type: 'TSInterfaceDeclaration';
  id: IdentifierNode;
  body: TSInterfaceBodyNode;
  typeParameters?: undefined | TSTypeParameterDeclarationNode;
  extends?: undefined | Array<TSExpressionWithTypeArgumentsNode>;
};

export const tsInterfaceDeclaration = createBuilder<TSInterfaceDeclarationNode>(
  'TSInterfaceDeclaration',
  {visitorKeys: ['id', 'body', 'typeParameters', 'extends']},
);
