import {JSNodeBase, ExpressionNode, TSTypeNode} from '../index';
import {createBuilder} from '../utils';

export type TSTypeAssertionNode = JSNodeBase & {
  type: 'TSTypeAssertion';
  expression: ExpressionNode;
  typeAnnotation: TSTypeNode;
};

export const tsTypeAssertion = createBuilder<TSTypeAssertionNode>(
  'TSTypeAssertion',
  {visitorKeys: ['expression', 'typeAnnotation']},
);
