import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {printParams} from '../utils';

export function ArrowFunctionExpression(
  generator: Generator,
  node: t.ArrowFunctionExpressionNode,
) {
  if (node.async === true) {
    generator.word('async');
    generator.space();
  }

  const firstParam = node.params[0];

  if (
    node.params.length === 1 &&
    firstParam !== undefined &&
    firstParam.type === 'Identifier' &&
    !hasTypes(node, firstParam)
  ) {
    generator.print(firstParam, node);
  } else {
    printParams(generator, node);
  }

  generator.space();
  generator.token('=>');
  generator.space();

  generator.print(node.body, node);
}

function hasTypes(
  node: t.ArrowFunctionExpressionNode,
  param: t.PatternNode,
): boolean {
  return (
    node.typeParameters !== undefined ||
    node.returnType !== undefined ||
    param.typeAnnotation !== undefined ||
    param.optional === true ||
    (param.trailingComments !== undefined && param.trailingComments.length > 0)
  );
}
