import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import NumericT from '../../types/NumericT';

export function FlowNumberTypeAnnotation(
  node: n.FlowNumberTypeAnnotationNode,
  scope: Scope,
) {
  return new NumericT(scope, node);
}
