import {Node, IdentifierNode, FlowTypeParameterNode} from '@romejs/js-ast';

export const BINDING_IDENTIFIER_KEYS: Map<string, Array<string>> = new Map([
  ['FlowDeclareClass', ['id']],
  ['FlowDeclareFunction', ['id']],
  ['FlowDeclareModule', ['id']],
  ['FlowDeclareVariable', ['id']],
  ['FlowDeclareInterface', ['id']],
  ['FlowDeclareTypeAlias', ['id']],
  ['FlowDeclareOpaqueType', ['id']],
  ['FlowInterfaceDeclaration', ['id']],
  ['FlowTypeAlias', ['id']],
  ['FlowTypeParameterDeclaration', ['params']],
  ['FlowOpaqueType', ['id']],
  ['TSImportEqualsDeclaration', ['id']],
  ['TSTypeAliasDeclaration', ['id']],
  ['CatchClause', ['param']],
  ['LabeledStatement', ['label']],
  ['UnaryExpression', ['argument']],
  ['AssignmentExpression', ['left']],
  ['ExportDefaultDeclaration', ['declaration']],
  ['ExportNamedDeclaration', ['declaration']],
  ['ImportSpecifier', ['local']],
  ['ImportNamespaceSpecifier', ['local']],
  ['ImportDefaultSpecifier', ['local']],
  ['ImportDeclaration', ['specifiers']],
  ['FunctionDeclaration', ['id']],
  ['FunctionExpression', ['id']],
  ['ForInStatement', ['left']],
  ['ForOfStatement', ['left']],
  ['ClassDeclaration', ['id']],
  ['ClassExpression', ['id']],
  ['RestProperty', ['argument']],
  ['RestElement', ['argument']],
  ['UpdateExpression', ['argument']],
  ['ObjectProperty', ['value']],
  ['AssignmentPattern', ['left']],
  ['ArrayPattern', ['elements']],
  ['ObjectPattern', ['properties']],
  ['VariableDeclaration', ['declarations']],
  ['VariableDeclarator', ['id']],
]);

export default function getBindingIdentifiers(
  node: Node | Array<Node>,
): Array<FlowTypeParameterNode | IdentifierNode> {
  const ids: Array<Node> = [];
  let queue: Array<undefined | Node> = Array.isArray(node) ? node : [node];

  while (queue.length) {
    const node = queue.pop();
    if (node === undefined) {
      continue;
    }

    if (node.type === 'Identifier' || node.type === 'FlowTypeParameter') {
      ids.push(node);
      continue;
    }

    const keys: undefined | Array<string> = BINDING_IDENTIFIER_KEYS.get(
      node.type,
    );
    if (keys === undefined) {
      continue;
    }

    for (const key of keys) {
      const val = node[key];
      if (val === undefined) {
        continue;
      } else if (Array.isArray(val)) {
        queue = queue.concat(val);
      } else {
        queue.push(val);
      }
    }
  }

  return ids;
}
