import {
  JSNodeBase,
  IdentifierNode,
  FlowTypeParameterDeclarationNode,
  FlowNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowTypeAliasNode = JSNodeBase & {
  type: 'FlowTypeAlias',
  id: IdentifierNode,
  typeParameters?: undefined | FlowTypeParameterDeclarationNode,
  right: FlowNode,
};

export const flowTypeAlias = createBuilder<FlowTypeAliasNode>('FlowTypeAlias', {
  visitorKeys: ['id', 'typeParameters', 'right'],
});
