import {
  JSNodeBase,
  IdentifierNode,
  FlowTypeParameterInstantiationNode,
  FlowQualifiedTypeIdentifierNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowInterfaceExtendsNode = JSNodeBase & {
  type: 'FlowInterfaceExtends',
  id: IdentifierNode | FlowQualifiedTypeIdentifierNode,
  typeParameters?: undefined | FlowTypeParameterInstantiationNode,
};

export const flowInterfaceExtends = createBuilder<FlowInterfaceExtendsNode>(
  'FlowInterfaceExtends',
  {visitorKeys: ['id', 'typeParameters']},
);
