import {JSNodeBase, StatementNode} from '../index';
import {createBuilder} from '../utils';

export type TSModuleBlockNode = JSNodeBase & {
  type: 'TSModuleBlock';
  body: Array<StatementNode>;
};

export const tsModuleBlock = createBuilder<TSModuleBlockNode>('TSModuleBlock', {
  visitorKeys: ['body'],
});
