import {JSNodeBase, ExpressionNode, SuperNode} from '../index';
import {createBuilder} from '../utils';

export type OptionalMemberExpressionNode = JSNodeBase & {
  type: 'OptionalMemberExpression';
  object: ExpressionNode | SuperNode;
  property: ExpressionNode;
  computed?: undefined | boolean;
};

export const optionalMemberExpression = createBuilder<
  OptionalMemberExpressionNode
>('OptionalMemberExpression', {
  visitorKeys: ['object', 'property'],
});
