import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import ExhaustiveT from '../../types/ExhaustiveT';
import StringT from '../../types/StringT';

export function TemplateLiteral(node: n.TemplateLiteralNode, scope: Scope) {
  for (const expr of node.expressions) {
    new ExhaustiveT(
      scope,
      expr,
      scope.evaluate(expr),
      new StringT(scope, undefined),
    );
  }
  return new StringT(scope, node);
}
