import {ProgramNode} from '@romejs/js-ast';
import {UserOptions} from './options';
import {Token} from './tokenizer/index';
import {ParserSyntaxError} from '@romejs/parser-core';
import {types as tokTypes} from './tokenizer/types';
import Parser from './Parser';
import './tokenizer/context';

export {ParserSyntaxError};

export function parse(input: string, options: UserOptions): ProgramNode {
  return new Parser(options, input).parse();
}

export function tokenise(input: string, options: UserOptions): Array<Token> {
  const parser = new Parser({...options, tokens: true}, input);
  return parser.tokens;
}

export {Token};
export {tokTypes};
export {keywords as keywordTokTypes} from './tokenizer/types';
export {deriveSyntaxFromFilename} from './options';
