import {Node} from '@romejs/js-ast';
import Generator from '../Generator';
import * as t from '@romejs/js-ast';

export function buildForXStatementGenerator(op: 'of' | 'in') {
  return function(
    generator: Generator,
    node: t.ForInStatementNode | t.ForOfStatementNode,
  ) {
    generator.word('for');
    generator.space();
    if (op === 'of' && node.type === 'ForOfStatement' && node.await === true) {
      generator.word('await');
      generator.space();
    }
    generator.token('(');
    generator.print(node.left, node);
    generator.space();
    generator.word(op);
    generator.space();
    generator.print(node.right, node);
    generator.token(')');
    generator.printBlock(node);
  };
}

export function buildYieldAwaitGenerator(keyword: string) {
  return function(
    generator: Generator,
    node: t.YieldExpressionNode | t.AwaitExpressionNode,
  ) {
    generator.word(keyword);

    if (node.type === 'YieldExpression' && node.delegate === true) {
      generator.token('*');
    }

    if (node.argument) {
      generator.space();
      const terminatorState = generator.startTerminatorless();
      generator.print(node.argument, node);
      generator.endTerminatorless(terminatorState);
    }
  };
}

export function buildLabelStatementGenerator(prefix: string) {
  return function(
    generator: Generator,
    node:
      | t.ContinueStatementNode
      | t.ReturnStatementNode
      | t.BreakStatementNode
      | t.ThrowStatementNode,
  ) {
    generator.word(prefix);

    let arg: undefined | Node;
    if (
      (node.type === 'ContinueStatement' || node.type === 'BreakStatement') &&
      node.label !== undefined
    ) {
      arg = node.label;
    }
    if (
      (node.type === 'ThrowStatement' || node.type === 'ReturnStatement') &&
      node.argument !== undefined
    ) {
      arg = node.argument;
    }

    if (arg !== undefined) {
      generator.space();

      const terminatorState = generator.startTerminatorless();
      generator.print(arg, node);
      generator.endTerminatorless(terminatorState);
    }

    generator.semicolon();
  };
}

export function printParams(generator: Generator, node: t.FunctionNode) {
  generator.print(node.typeParameters, node);
  generator.token('(');
  generator.printCommaList(node.params, node, {
    iterator: node => {
      if (node.optional) {
        generator.token('?');
      }
      generator.print(node.typeAnnotation, node);
    },
  });
  generator.token(')');

  if (node.returnType) {
    generator.print(node.returnType, node);
  }

  if (node.predicate) {
    if (!node.returnType) {
      generator.token(':');
    }
    generator.space();
    generator.print(node.predicate, node);
  }
}

export function printMethod(
  generator: Generator,
  node: t.ClassMethodNode | t.ObjectMethodNode,
) {
  const kind = node.kind;
  const key = node.key;

  if (kind === 'method' && node.generator === true) {
    generator.token('*');
  }

  if (kind === 'get' || kind === 'set') {
    generator.word(kind);
    generator.space();
  }

  if (node.async === true) {
    generator.word('async');
    generator.space();
  }

  if (node.computed === true) {
    generator.token('[');
    generator.print(key, node);
    generator.token(']');
  } else {
    generator.print(key, node);
  }

  printParams(generator, node);
  generator.space();
  generator.print(node.body, node);
}
