import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowVariance(generator: Generator, node: t.FlowVarianceNode) {
  if (node.kind === 'plus') {
    generator.token('+');
  } else if (node.kind === 'minus') {
    generator.token('-');
  }
}
