import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXText(generator: Generator, node: t.JSXTextNode) {
  const raw = generator.getPossibleRaw(node);

  if (raw !== undefined) {
    generator.token(raw);
  } else {
    generator.token(node.value);
  }
}
