import {
  JSNodeBase,
  FlowTypeParameterDeclarationNode,
  FlowFunctionTypeParamNode,
  FlowFunctionTypeParamRestNode,
  FlowNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowFunctionTypeAnnotationNode = JSNodeBase & {
  type: 'FlowFunctionTypeAnnotation';
  typeParameters?: undefined | FlowTypeParameterDeclarationNode;
  params: Array<FlowFunctionTypeParamNode | FlowFunctionTypeParamRestNode>;
  returnType: FlowNode;
};

export const flowFunctionTypeAnnotation = createBuilder<
  FlowFunctionTypeAnnotationNode
>('FlowFunctionTypeAnnotation', {
  visitorKeys: ['typeParameters', 'params', 'rest', 'returnType'],
});
