import {Node} from '@romejs/js-ast';

export default function getNodeReferenceParts(node: Node): Array<string> {
  const parts: Array<string> = [];

  function add(node: Node): boolean {
    if (node.type === 'Identifier') {
      parts.push(node.name);
      return false;
    } else if (node.type === 'StringLiteral') {
      parts.push(node.value);
      return false;
    } else if (node.type === 'MemberExpression') {
      const stop = add(node.object);
      if (stop) {
        return true;
      }

      if (node.property.type === 'StringLiteral') {
        return add(node.property);
      } else if (node.computed === false) {
        return add(node.property);
      } else {
        return true;
      }
    } else {
      return true;
    }
  }

  add(node);

  return parts;
}
