import {JSNodeBase, IdentifierNode, FunctionBase} from '../index';
import {createBuilder, BOUND_FUNCTION_VISITOR_KEYS} from '../utils';

export type FunctionExpressionNode = JSNodeBase &
  FunctionBase & {
    type: 'FunctionExpression';
    id?: undefined | IdentifierNode;
  };

export const functionExpression = createBuilder<FunctionExpressionNode>(
  'FunctionExpression',
  {
    visitorKeys: BOUND_FUNCTION_VISITOR_KEYS,
  },
);
