import {Diagnostic} from '@romejs/diagnostics';
import stream = require('stream');

export type Stdout = stream.Writable;
export type Stdin = stream.Readable;
export type Package = {
  name: string;
  version?: string;
};

export type Tree = {
  name: string;
  children?: Trees;
  hint?: undefined | string;
  hidden?: boolean;
  color?: undefined | string;
};

export type Trees = Array<Tree>;

export type ReporterSpinner = {
  setText: (name: string) => void;
  render: () => void;
  resume: () => void;
  pause: () => void;
  end: () => void;
};

export type ReporterSelectOption = {
  name: string;
  value: string;
};

export type ReporterSpinnerGroup = {
  spinners: Array<ReporterSetSpinner>;
  setText: (text: string) => void;
  setCurrent: (current: number) => void;
  setTotal: (total: number) => void;
  tick: () => void;
  end: () => void;
};

export type ReporterSetSpinner = {
  clear: () => void;
  resume: () => void;
  pause: () => void;
  setTotal: (total: number) => void;
  setCurrent: (current: number) => void;
  setPrefix: (prefix: string) => void;
  setText: (msg: string) => void;
  tick: () => void;
  end: () => void;
};

export type ReporterJSON =
  | {
      type: 'DIAGNOSTIC';
      diagnostic: Diagnostic;
    }
  | {
      type: 'PROGRESS';
      current: number;
      total: number;
    }
  | {
      type: 'START';
    }
  | {
      type: 'END';
    };
