import {
  JSNodeBase,
  JSXIdentifierNode,
  JSXMemberExpressionNode,
  JSXNamespacedNameNode,
} from '../index';
import {createBuilder} from '../utils';

export type JSXClosingElementNode = JSNodeBase & {
  type: 'JSXClosingElement',
  name: JSXIdentifierNode | JSXMemberExpressionNode | JSXNamespacedNameNode,
};

export const jsxClosingElement = createBuilder<JSXClosingElementNode>(
  'JSXClosingElement',
  {visitorKeys: ['name']},
);
