import {ParserOptions} from '@romejs/parser-core';
import {unescapeString} from '@romejs/string-escape';
import {isAlpha, isEscaped, ParserCore} from '@romejs/parser-core';
import {
  WordToken,
  StringToken,
  Token,
  TagAttributes,
  TagNode,
  ChildNode,
  Children,
  TagName,
} from './types';

const tags: Map<string, Array<string>> = new Map();
tags.set('emphasis', []);
tags.set('number', []);
tags.set('hyperlink', ['target']);
tags.set('filelink', ['filename', 'column', 'line']);
tags.set('inverse', []);

//

function isStringValueChar(
  char: string,
  index: number,
  input: string,
): boolean {
  if (char === '"' && !isEscaped(index, input)) {
    return false;
  }

  return true;
}

function isTextChar(char: string, index: number, input: string): boolean {
  return char !== '<' || isEscaped(index, input);
}

type State = {
  inTagOpenHead: boolean;
};

class Parser extends ParserCore<Token, State> {
  constructor(opts: ParserOptions) {
    super(opts, '@romejs/string-markup', {inTagOpenHead: false});
  }

  tokenizeWithState(
    index: number,
    input: string,
    state: State,
  ): {token: Token; state: State} {
    const escaped = isEscaped(index, input);
    const char = input[index];

    if (!escaped && state.inTagOpenHead) {
      if (char === ' ') {
        return this.lookahead(index + 1);
      }

      if (char === '=') {
        return {
          state,
          token: this.finishToken('Equals'),
        };
      }

      if (char === '/') {
        return {
          state,
          token: this.finishToken('Slash'),
        };
      }

      if (isAlpha(char)) {
        const value = this.readInputFrom(index, isAlpha);
        return {
          state,
          token: this.finishValueToken(
            'Word',
            value,
            index + value.length,
          ) as WordToken,
        };
      }

      if (char === '"') {
        const value = this.readInputFrom(index + 1, isStringValueChar);
        const end = index + value.length + 2;

        if (input[end - 1] !== '"') {
          throw this.unexpected({
            message: 'Unclosed string',
            start: this.getPositionFromIndex(end),
          });
        }

        const unescaped = unescapeString(value);
        return {
          state,
          token: this.finishValueToken('String', unescaped, end) as StringToken,
        };
      }

      if (char === '>') {
        return {
          state: {
            inTagOpenHead: false,
          },
          token: this.finishToken('Greater'),
        };
      }
    }

    if (!escaped && char === '<') {
      return {
        state: {
          inTagOpenHead: true,
        },
        token: this.finishToken('Less'),
      };
    }

    // Keep eating text until we hit a <
    const value = this.readInputFrom(index, isTextChar);
    return {
      state,
      token: {
        type: 'Text',
        value: normalizeTextValue(value),
        start: index,
        end: index + value.length,
      },
    };
  }

  atTagEnd(): boolean {
    return this.matchToken('Less') && this.lookahead().token.type === 'Slash';
  }

  parseTag(): TagNode {
    const nameToken = this.expectToken('Word');
    if (nameToken.type !== 'Word') {
      throw new Error('Expected Word');
    }
    const rawName = nameToken.value;

    const allowedAttributes = tags.get(rawName);
    if (allowedAttributes === undefined) {
      throw this.unexpected({
        message: `Unknown tag name ${rawName}`,
        start: this.getPositionFromIndex(nameToken.start),
      });
    }

    const tagName: TagName = rawName as any;
    const attributes: TagAttributes = new Map();
    const children: Children = [];
    let selfClosing = false;

    // Parse attributes
    while (!this.matchToken('EOF') && !this.matchToken('Greater')) {
      const keyToken = this.getToken();

      let key;
      if (keyToken.type === 'Word') {
        key = keyToken.value;

        if (!allowedAttributes.includes(key)) {
          throw this.unexpected({
            message: `${key} is not a valid attribute name for <${tagName}>`,
          });
        }

        this.nextToken();

        this.expectToken('Equals');

        const valueToken = this.expectToken('String');
        if (valueToken.type !== 'String') {
          throw new Error('Expected String');
        }
        const value = valueToken.value;

        attributes.set(key, value);
      } else if (keyToken.type === 'Slash') {
        this.nextToken();
        selfClosing = true;
      } else {
        throw this.unexpected({
          message: 'Expected attribute name',
        });
      }
    }

    this.expectToken('Greater');

    // Verify closing tag
    if (!selfClosing) {
      // Build children
      while (!this.matchToken('EOF') && !this.atTagEnd()) {
        children.push(this.parseChild());
      }

      if (this.matchToken('EOF')) {
        throw this.unexpected({
          message: `Unclosed ${tagName} tag`,
        });
      } else {
        this.expectToken('Less');
        this.expectToken('Slash');

        const name = this.getToken();
        if (name.type === 'Word') {
          if (name.value !== tagName) {
            throw this.unexpected({
              message: `Expected to close ${tagName} but found ${name.value}`,
            });
          }

          this.nextToken();
        } else {
          throw this.unexpected({
            message: 'Expected closing tag name',
          });
        }

        this.expectToken('Greater');
      }
    }

    return {
      type: 'Tag',
      attributes,
      name: tagName,
      children,
    };
  }

  parseChild(): ChildNode {
    const token = this.getToken();
    this.nextToken();

    if (token.type === 'Text') {
      return {
        type: 'Text',
        value: token.value,
      };
    } else if (token.type === 'Less') {
      return this.parseTag();
    } else {
      throw this.unexpected({
        message: 'Unknown child start',
      });
    }
  }

  parse(): Children {
    const children: Children = [];
    while (!this.matchToken('EOF')) {
      children.push(this.parseChild());
    }
    return children;
  }
}

export function parse(input: string) {
  try {
    return new Parser({input}).parse();
  } catch (err) {
    throw err;
  }
}

function normalizeTextValue(str: string): string {
  return str.replace(/\\</g, '<');
}
