import Scope from '../Scope';
import * as t from '@romejs/js-ast';

export default function SwitchStatement(
  node: t.SwitchStatementNode,
  parent: t.Node,
  scope: Scope,
) {
  for (const child of node.cases) {
    scope.evaluate(child, node);
  }
}
