import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXSpreadAttribute(
  generator: Generator,
  node: t.JSXSpreadAttributeNode,
) {
  generator.token('{');
  generator.token('...');
  generator.print(node.argument, node);
  generator.token('}');
}
