import {NamedManifest} from '@romejs/codec-js-manifest';
import {PathPatterns} from '@romejs/match-path';
import {BundlerMode} from '../types/bundler';
import {VERSION} from '../constants';

import path = require('path');
import os = require('os');

export type Project = {
  id: number;
  dir: string;
  configLoc: string;
  config: ProjectConfig;
  packages: Map<string, ProjectPackage>;
  hasteMap: Map<string, string>;
  parent: undefined | Project;
};

export type ProjectPackage = {
  manifestFilename: string;
  manifest: NamedManifest;
  folder: string;
};

// This CANNOT contain non-JSON serialisable things as it is sent to workers
export type ProjectConfig = {
  name: string;
  memoryCacheCounter: number;
  configHashes: Array<string>;
  configDependencies: Array<string>;
  isolated: boolean;
  cache: {
    endpoint: undefined | string;
    folder: string;
  };
  resolver: {};
  bundler: {
    mode: BundlerMode;
  };
  haste: {
    enabled: boolean;
    ignore: PathPatterns;
  };
  lint: {
    enabled: boolean;
    globals: Array<string>;
    ignore: PathPatterns;
  };
  typeChecking: {
    enabled: boolean;
    libs: Array<string>;
  };
  format: {
    enabled: boolean;
  };
  dependencies: {
    enabled: boolean;
    folder: string;
  };
  testing: {
    enabled: boolean;
    folderName: string;
    ignore: PathPatterns;
  };
  files: {
    watchman: boolean;
    maxSize: number;
  };
};

export type PartialProjectConfig = {
  isolated?: boolean;
  configHashes: Array<string>;
  configDependencies: Array<string>;
  bundler: Partial<ProjectConfig['bundler']>;
  cache: Partial<ProjectConfig['cache']>;
  lint: Partial<ProjectConfig['lint']>;
  typeChecking: Partial<ProjectConfig['typeChecking']>;
  format: Partial<ProjectConfig['format']>;
  dependencies: Partial<ProjectConfig['dependencies']>;
  testing: Partial<ProjectConfig['testing']>;
  resolver: Partial<ProjectConfig['resolver']>;
  haste: Partial<ProjectConfig['haste']>;
  files: Partial<ProjectConfig['files']>;
};

export const DEFAULT_PROJECT_CONFIG: ProjectConfig = {
  name: 'unknown',
  isolated: false,
  memoryCacheCounter: 0,
  configHashes: [],
  configDependencies: [],

  cache: {
    endpoint: undefined,
    folder: path.join(os.tmpdir(), `rome-${VERSION}`),
  },

  bundler: {
    mode: 'flat',
  },
  resolver: {},

  typeChecking: {
    enabled: false,
    libs: [],
  },

  dependencies: {
    enabled: false,
    folder: path.join(os.userInfo().homedir, '.rome-dependencies'),
  },

  format: {
    enabled: false,
  },

  haste: {
    enabled: false,
    ignore: [],
  },

  lint: {
    enabled: false,
    ignore: [],
    globals: [],
  },

  testing: {
    enabled: false,
    folderName: '__tests__',
    ignore: [],
  },

  files: {
    watchman: false,
    // 40 megabytes
    maxSize: 4e7,
  },
};
