import {JSNodeBase, PatternNode, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type AssignmentExpressionNode = JSNodeBase & {
  type: 'AssignmentExpression',
  operator: AssignmentOperator,
  left: PatternNode | ExpressionNode,
  right: ExpressionNode,
};

export type AssignmentOperator =
  | '='
  | '+='
  | '-='
  | '*='
  | '/='
  | '%='
  | '<<='
  | '>>='
  | '>>>='
  | '|='
  | '^='
  | '&='
  | '??=';

export const assignmentExpression = createBuilder<AssignmentExpressionNode>(
  'AssignmentExpression',
  {visitorKeys: ['left', 'right']},
);
