import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXExpressionContainer(
  generator: Generator,
  node: t.JSXExpressionContainerNode,
) {
  generator.token('{');
  generator.print(node.expression, node);
  generator.token('}');
}
