import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import Hub from '../../Hub';

export function ExportAllDeclaration(
  node: n.ExportAllDeclarationNode,
  scope: Scope,
  {evaluator}: Hub,
) {
  evaluator.addExportAll(node.source.value);
}
