import {
  JSNodeBase,
  TSEntityNameNode,
  TSTypeParameterInstantiationNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSExpressionWithTypeArgumentsNode = JSNodeBase & {
  type: 'TSExpressionWithTypeArguments';
  expression: TSEntityNameNode;
  typeParameters?: undefined | TSTypeParameterInstantiationNode;
};

export const tsExpressionWithTypeArguments = createBuilder<
  TSExpressionWithTypeArgumentsNode
>('TSExpressionWithTypeArguments', {
  visitorKeys: ['expression', 'typeParameters'],
});
