import {Node} from '@romejs/js-ast';
import {Scope} from '../../scopes';
import E from './E';
import T from '../T';

export default class NotCallableE extends E {
  constructor(scope: Scope, originNode: undefined | Node, callee: T) {
    super(scope, originNode);
    this.callee = callee;
  }

  static type = 'NotCallableE';
  callee: T;

  getError() {
    return {
      message: `This type isn't callable`,
      lowerTarget: this.callee,
    };
  }
}
