import {
  JSNodeBase,
  IdentifierNode,
  FlowTypeParameterDeclarationNode,
  FlowNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowOpaqueTypeNode = JSNodeBase & {
  type: 'FlowOpaqueType',
  id: IdentifierNode,
  typeParameters?: undefined | FlowTypeParameterDeclarationNode,
  supertype?: undefined | FlowNode,
  impltype?: undefined | FlowNode,
};

export const flowOpaqueType = createBuilder<FlowOpaqueTypeNode>(
  'FlowOpaqueType',
  {visitorKeys: ['id', 'typeParameters', 'impltype', 'supertype']},
);
