import {Node} from '@romejs/js-ast';

export default function isUnaryLike(node: undefined | Node): boolean {
  if (node === undefined) {
    return false;
  }

  switch (node.type) {
    case 'UnaryExpression':
    case 'SpreadElement':
    case 'RestProperty':
    case 'SpreadProperty':
      return true;

    default:
      return false;
  }
}
