import {JSNodeBase, PatternNode, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type VariableDeclaratorNode = JSNodeBase & {
  type: 'VariableDeclarator',
  id: PatternNode,
  init?: undefined | ExpressionNode,
};

export const variableDeclarator = createBuilder<VariableDeclaratorNode>(
  'VariableDeclarator',
  {visitorKeys: ['id', 'init']},
);
