import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowObjectTypeProperty(
  generator: Generator,
  node: t.FlowObjectTypePropertyNode,
) {
  if (node.static === true) {
    generator.word('static');
    generator.space();
  }
  generator.print(node.variance, node);
  generator.print(node.key, node);
  if (node.optional === true) {
    generator.token('?');
  }
  generator.token(':');
  generator.space();
  generator.print(node.value, node);
}
