import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type ContinueStatementNode = JSNodeBase & {
  type: 'ContinueStatement',
  label?: undefined | IdentifierNode,
};

export const continueStatement = createBuilder<ContinueStatementNode>(
  'ContinueStatement',
  {visitorKeys: ['label']},
);
