import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type FlowDeclareVariableNode = JSNodeBase & {
  type: 'FlowDeclareVariable',
  id: IdentifierNode,
};

export const flowDeclareVariable = createBuilder<FlowDeclareVariableNode>(
  'FlowDeclareVariable',
  {visitorKeys: ['id']},
);
