import {JSNodeBase, BlockStatementNode} from '../index';
import {createBuilder} from '../utils';

export type DoExpressionNode = JSNodeBase & {
  type: 'DoExpression',
  body: BlockStatementNode,
};

export const doExpression = createBuilder<DoExpressionNode>('DoExpression', {
  visitorKeys: ['body'],
});
