import {
  JSNodeBase,
  VariableDeclarationNode,
  ExpressionNode,
  StatementNode,
} from '../index';
import {createBuilder} from '../utils';

export type ForOfStatementNode = JSNodeBase & {
  type: 'ForOfStatement',
  await?: undefined | boolean,
  left: VariableDeclarationNode | ExpressionNode,
  right: ExpressionNode,
  body: StatementNode,
};

export const forOfStatement = createBuilder<ForOfStatementNode>(
  'ForOfStatement',
  {visitorKeys: ['left', 'right', 'body']},
);
