import {PartialDiagnostics} from './types';
import {addOriginsToDiagnostics} from './derive';
import {getDiagnosticsFromError} from './errors';

type WrapResult<T> =
  | {
      foundDiagnostics: true,
      diagnostics: PartialDiagnostics,
      value: void,
    }
  | {
      foundDiagnostics: false,
      diagnostics: PartialDiagnostics,
      value: T,
    };

export function catchDiagnostics<T>(
  origin: string,
  callback: () => T,
): WrapResult<T> {
  try {
    const value = callback();

    return {
      foundDiagnostics: false,
      value,
      diagnostics: [],
    };
  } catch (err) {
    const diagnostics = getDiagnosticsFromError(err);

    if (diagnostics) {
      return {
        foundDiagnostics: true,
        value: undefined,
        diagnostics: addOriginsToDiagnostics([origin], diagnostics),
      };
    } else {
      throw err;
    }
  }
}

export async function catchDiagnosticsAsync<T>(
  origin: string,
  promise: () => Promise<T>,
): Promise<WrapResult<T>> {
  try {
    const value = await promise();

    return {
      foundDiagnostics: false,
      value,
      diagnostics: [],
    };
  } catch (err) {
    const diagnostics = getDiagnosticsFromError(err);

    if (diagnostics) {
      return {
        foundDiagnostics: true,
        value: undefined,
        diagnostics: addOriginsToDiagnostics([origin], diagnostics),
      };
    } else {
      throw err;
    }
  }
}
