import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowInterfaceExtends(
  generator: Generator,
  node:
    | t.FlowInterfaceExtendsNode
    | t.FlowGenericTypeAnnotationNode
    | t.FlowClassImplementsNode,
) {
  generator.print(node.id, node);
  generator.print(node.typeParameters, node);
}
