import {UnknownObject} from '@romejs/refinement';
import {SourceMap} from '@romejs/codec-source-map';
import {sourceMapManager} from '@romejs/v8';
import internalModule = require('module');
import path = require('path');
import vm = require('vm');

type ExecuteMainOptions = {
  filename: string;
  code: string;
  sourceMap: SourceMap;
  globals?: UnknownObject;
};

export default async function executeMain(
  opts: ExecuteMainOptions,
): Promise<{
  syntaxError: undefined | SyntaxError;
}> {
  const {filename, code, sourceMap, globals} = opts;

  // Create global context
  const sandbox: UnknownObject = {
    ...globals,

    process: {
      argv: [process.argv[0], filename],
      __proto__: process,
    },

    Buffer,
    clearImmediate,
    clearInterval,
    clearTimeout,
    setImmediate,
    setInterval,
    setTimeout,
    require: internalModule.createRequireFromPath(filename),
    console,
    __dirname: path.dirname(filename),
    __filename: filename,
  };
  sandbox.global = sandbox;
  const context = vm.createContext(sandbox);

  // Here we do some gymnastics to catch a syntax error to correctly identify it as being our fault
  let script: undefined | vm.Script = undefined;
  let syntaxError = undefined;
  try {
    script = new vm.Script(code, {
      filename,
    });
  } catch (err) {
    if (err instanceof SyntaxError) {
      syntaxError = err;
    } else {
      throw err;
    }
  }

  // Execute the script if there was no syntax error
  if (script !== undefined) {
    sourceMapManager.addSourceMap(filename, sourceMap);
    await script.runInContext(context);
  }

  return {syntaxError};
}
