import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowDeclareModuleExports(
  generator: Generator,
  node: t.FlowDeclareModuleExportsNode,
) {
  generator.word('declare');
  generator.space();
  generator.word('module');
  generator.token('.');
  generator.word('exports');
  generator.print(node.typeAnnotation, node);
}
