import {JSNodeBase, VariableDeclaratorNode} from '../index';
import {createBuilder} from '../utils';

export type VariableDeclarationKind = 'var' | 'let' | 'const';

export type VariableDeclarationNode = JSNodeBase & {
  type: 'VariableDeclaration',
  kind: VariableDeclarationKind,
  declarations: Array<VariableDeclaratorNode>,
};

export const variableDeclaration = createBuilder<VariableDeclarationNode>(
  'VariableDeclaration',
  {visitorKeys: ['declarations']},
);
