import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import ObjT from '../../types/ObjT';
import T from '../../types/T';

export function FlowObjectTypeAnnotation(
  node: n.FlowObjectTypeAnnotationNode,
  scope: Scope,
) {
  const props: Array<T> = [];
  const calls: Array<T> = [];

  for (const prop of node.properties) {
    props.push(scope.evaluate(prop));
  }

  return new ObjT(scope, node, {
    props,
    proto: scope.intrinsics.ObjectPrototype,
    calls,
  });
}
