import {
  JSNodeBase,
  IdentifierNode,
  FlowTypeParameterDeclarationNode,
  FlowNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowDeclareTypeAliasNode = JSNodeBase & {
  type: 'FlowDeclareTypeAlias',
  id: IdentifierNode,
  typeParameters?: undefined | FlowTypeParameterDeclarationNode,
  right: FlowNode,
};

export const flowDeclareTypeAlias = createBuilder<FlowDeclareTypeAliasNode>(
  'FlowDeclareTypeAlias',
  {visitorKeys: ['id', 'typeParameters', 'right']},
);
