import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import BooleanLiteralT from '../../types/BooleanLiteralT';

export function FlowBooleanLiteralTypeAnnotation(
  node: n.FlowBooleanLiteralTypeAnnotationNode,
  scope: Scope,
) {
  return new BooleanLiteralT(scope, node, node.value);
}
