import {Path, TypeBinding} from '@romejs/js-compiler';
import {Node, StatementNode, ExportSpecifier} from '@romejs/js-ast';
import {isFunctionNode, isTypeNode} from '@romejs/js-ast-utils';

export default {
  name: 'stripTypeAnnotations',
  enter(path: Path): Node | null {
    const {node} = path;

    // Clean class properties
    if (
      node.type === 'ClassProperty' &&
      (node.variance || node.typeAnnotation)
    ) {
      if (node.value) {
        // Remove type annotations
        return {
          ...node,
          type: 'ClassProperty',
          typeAnnotation: undefined,
          variance: undefined,
        };
      } else {
        // Type annotation without an initializer so just remove this proeprty
        return null;
      }
    }

    // Remove optional annotations
    if (node.type === 'AssignmentPattern' && node.left.optional) {
      return {
        ...node,
        left: {
          ...node.left,
          optional: undefined,
        },
      };
    }

    // Remove call type parameters
    if (node.type === 'CallExpression' || node.type === 'NewExpression') {
      if (node.typeArguments !== undefined) {
        return {
          ...node,
          typeArguments: undefined,
        };
      }
    }

    // Remove optional property from 'function params
    if (isFunctionNode(node)) {
      let hasOptional = false;
      for (const param of node.params) {
        if (param.optional) {
          hasOptional = true;
          break;
        }
      }
      if (hasOptional) {
        return {
          ...node,
          params: node.params.map(param => {
            return {
              ...param,
              optional: undefined,
            };
          }),
        };
      }
    }

    // Replace a type casts with their expression
    if (
      node.type === 'FlowTypeCastExpression' ||
      node.type === 'TSAsExpression'
    ) {
      return node.expression;
    }

    // Remove type annotation nodes
    if (isTypeNode(node)) {
      return null;
    }

    // Remove all export specifiers that refer to types
    if (node.type === 'Program') {
      // First collect all type export specifiers, we don't do any replacement
      // in place for perf if there are none
      let typeExportSpecifiers: Set<Node> = new Set();
      for (const bodyNode of node.body) {
        if (bodyNode.type === 'ExportNamedDeclaration') {
          for (const specifier of bodyNode.specifiers) {
            if (specifier.type === 'ExportSpecifier') {
              const localName = specifier.local.name;
              const binding = path.scope.getBinding(localName);
              if (binding !== undefined && binding instanceof TypeBinding) {
                typeExportSpecifiers.add(specifier);
              }
            }
          }
        }
      }
      if (typeExportSpecifiers.size === 0) {
        return node;
      }

      // Build a new body with all the export specifiers removed
      const body: Array<StatementNode> = [];
      for (const bodyNode of node.body) {
        if (
          bodyNode.type === 'ExportNamedDeclaration' &&
          bodyNode.specifiers !== undefined &&
          bodyNode.specifiers.length > 0
        ) {
          const specifiers: Array<ExportSpecifier> = [];

          for (const specifier of bodyNode.specifiers) {
            if (!typeExportSpecifiers.has(specifier)) {
              specifiers.push(specifier);
            }
          }

          // Remove entire declaration if there's no relevant exports
          if (specifiers.length === 0) {
            continue;
          }

          body.push({
            ...bodyNode,
            specifiers,
          });
        } else {
          body.push(bodyNode);
        }
      }
      return {
        ...node,
        body,
      };
    }

    return node;
  },
};
