import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type BinaryExpressionNode = JSNodeBase & {
  type: 'BinaryExpression',
  operator: BinaryOperator,
  left: ExpressionNode,
  right: ExpressionNode,
};

export type BinaryOperator =
  | '=='
  | '!='
  | '==='
  | '**'
  | '!=='
  | '<'
  | '<='
  | '>'
  | '>='
  | '<<'
  | '>>'
  | '>>>'
  | '+'
  | '-'
  | '*'
  | '/'
  | '%'
  | '|'
  | '^'
  | '&'
  | 'in'
  | 'instanceof';

export const binaryExpression = createBuilder<BinaryExpressionNode>(
  'BinaryExpression',
  {
    visitorKeys: ['left', 'right'],
  },
);
