import {
  JSNodeBase,
  PatternBase,
  ObjectPropertyNode,
  RestPropertyNode,
} from '../index';
import {createBuilder} from '../utils';

export type ObjectPatternNode = JSNodeBase &
  PatternBase & {
    type: 'ObjectPattern';
    properties: Array<ObjectPropertyNode | RestPropertyNode>;
  };

export const objectPattern = createBuilder<ObjectPatternNode>('ObjectPattern', {
  visitorKeys: ['properties', 'typeAnnotation'],
});
