import {
  JSNodeBase,
  ExpressionNode,
  TemplateLiteralNode,
  TypeArgumentsNode,
} from '../index';
import {createBuilder} from '../utils';

export type TaggedTemplateExpressionNode = JSNodeBase & {
  type: 'TaggedTemplateExpression',
  tag: ExpressionNode,
  quasi: TemplateLiteralNode,
  typeArguments?: undefined | TypeArgumentsNode,
};

export const taggedTemplateExpression = createBuilder<TaggedTemplateExpressionNode>(
  'TaggedTemplateExpression',
  {
    visitorKeys: ['tag', 'quasi'],
  },
);
