import {JSNodeBase, TSTypeNode} from '../index';
import {createBuilder} from '../utils';

export type TSTupleTypeNode = JSNodeBase & {
  type: 'TSTupleType';
  elementTypes: Array<TSTypeNode>;
};

export const tsTupleType = createBuilder<TSTupleTypeNode>('TSTupleType', {
  visitorKeys: ['elementTypes'],
});
