import {Worker, FileReference} from '@romejs/core';
import {ProgramNode} from '@romejs/js-ast';
import {PartialDiagnostics} from '@romejs/diagnostics';
import {
  TransformStageName,
  CompileResult,
  AnalyzeDependencyResult,
  CompilerOptions,
} from '@romejs/js-compiler';
import {LintResult} from './extensions';
import {
  PrefetchedModuleSignatures,
  WorkerParseOptions,
} from '../common/bridges/WorkerBridge';
import Logger from '../common/utils/Logger';
import {removeLoc} from '@romejs/js-ast-utils';
import {analyzeDependencies, compile} from '@romejs/js-compiler';
import {catchDiagnosticsAsync} from '@romejs/diagnostics';
import * as typeEngine from '@romejs/js-analysis';
import {getExtensionHandler} from './extensions';
import {program} from '@romejs/js-ast';
import diff from '@romejs/string-diff';

export default class WorkerAPI {
  constructor(worker: Worker) {
    this.worker = worker;
    this.logger = worker.logger;
  }

  worker: Worker;
  logger: Logger;

  async moduleSignature(ref: FileReference) {
    const {ast, projectConfig} = await this.worker.parse(ref);

    this.logger.info(`Generating export types:`, ref.real);

    return await typeEngine.getModuleSignature({
      ast,
      projectConfig,
      provider: await this.worker.getTypeCheckProvider(ref.project),
    });
  }

  async analyzeDependencies(
    ref: FileReference,
  ): Promise<AnalyzeDependencyResult> {
    const {ast, src, projectConfig} = await this.worker.parse(ref);
    this.logger.info(`Analysing:`, ref.real);
    return analyzeDependencies({ast, src, projectConfig, options: {}});
  }

  async compile(
    ref: FileReference,
    stage: TransformStageName,
    options: CompilerOptions,
  ): Promise<CompileResult> {
    const {ast, projectConfig, src} = await this.worker.parse(ref);
    this.logger.info(`Compiling:`, ref.real);
    return compile({
      ast,
      src,
      options,
      projectConfig,
      stage,
    });
  }

  async parse(
    ref: FileReference,
    opts: WorkerParseOptions,
  ): Promise<ProgramNode> {
    const {ast} = await this.worker.parse(ref, {
      sourceType: opts.sourceType,
      cache: false,
    });

    if (opts.compact) {
      return program.assert(removeLoc(ast));
    } else {
      return ast;
    }
  }

  async lint(
    ref: FileReference,
    prefetchedModuleSignatures: PrefetchedModuleSignatures,
    fix: boolean,
  ): Promise<PartialDiagnostics> {
    const projectConfig = this.worker.getProjectConfig(ref.project);
    this.logger.info(`Linting:`, ref.real);

    // Get the extension handler
    const extension = getExtensionHandler(ref.real);
    if (extension === undefined) {
      throw new Error('Expected extension to be valid');
    }

    const {lint} = extension;
    if (lint === undefined) {
      return [];
    }

    // Catch any diagnostics, in the case of syntax errors etc
    const res = await catchDiagnosticsAsync('Caught by WorkerAPI.lint', () =>
      lint({
        file: ref,
        projectConfig,
        prefetchedModuleSignatures,
        worker: this.worker,
      }),
    );

    // These are fatal diagnostics
    if (res.foundDiagnostics) {
      return res.diagnostics;
    }

    // These are normal diagnostics returned from the linter
    const {formatted, raw, diagnostics}: LintResult = res.value;

    // If the file has pending fixes
    const needsFix = formatted !== raw;

    // Autofix if necessary
    if (fix && needsFix) {
      // Save the file and evict it from the cache
      await this.worker.writeFile(ref.real, formatted);

      // Relint this file without fixing it, we do this to prevent false positive error messages
      return this.lint(ref, prefetchedModuleSignatures, false);
    }

    // If there's no pending fix then no need for diagnostics
    if (!needsFix) {
      return diagnostics;
    }

    // Add pending autofix diagnostic
    return [
      ...diagnostics,
      {
        category: 'lint/pendingFixes',
        filename: ref.uid,
        message: 'Pending lint fixes',
        advice: [
          {
            type: 'diff',
            diff: diff(raw, formatted),
          },
        ],
      },
    ];
  }
}
