import {
  JSNodeBase,
  FlowNode,
  FlowTypeAnnotationNode,
  FlowVarianceNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowTypeParameterNode = JSNodeBase & {
  type: 'FlowTypeParameter',
  name: string,
  bound?: undefined | FlowTypeAnnotationNode,
  default?: undefined | FlowNode,
  variance?: undefined | FlowVarianceNode,
};

export const flowTypeParameter = createBuilder<FlowTypeParameterNode>(
  'FlowTypeParameter',
  {
    visitorKeys: ['bound', 'default', 'variance'],
  },
);
