import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';

export function FlowTypeParameterDeclaration(
  node: n.FlowTypeParameterDeclarationNode,
  scope: Scope,
) {
  for (const param of node.params) {
    scope.evaluate(param);
  }
}
