import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type SpreadPropertyNode = JSNodeBase & {
  type: 'SpreadProperty',
  argument: ExpressionNode,
};

export const spreadProperty = createBuilder<SpreadPropertyNode>(
  'SpreadProperty',
  {visitorKeys: ['argument']},
);
