import {ImportDeclarationNode} from '@romejs/js-ast';
import {Scope} from '../../scopes';
import ImportT from '../../types/ImportT';

export function ImportDeclaration(node: ImportDeclarationNode, scope: Scope) {
  const source = node.source.value;

  for (const specifier of node.specifiers) {
    if (specifier.type === 'ImportSpecifier') {
      const localName = specifier.local.name;
      const importedName = specifier.imported.name;

      const open = new ImportT(scope, specifier, {
        importedName,
        source,
      });
      scope.addBinding(localName, open);
    } else if (specifier.type === 'ImportDefaultSpecifier') {
      const localName = specifier.local.name;
      const open = new ImportT(scope, specifier, {
        importedName: 'default',
        source,
      });
      scope.addBinding(localName, open);
    } else if (specifier.type === 'ImportNamespaceSpecifier') {
      const localName = specifier.local.name;
      const open = new ImportT(scope, specifier, {
        importedName: undefined,
        source,
      });
      scope.addBinding(localName, open);
    } else {
      // TODO error
    }
  }
}
