import {JSNodeBase, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowUnionTypeAnnotationNode = JSNodeBase & {
  type: 'FlowUnionTypeAnnotation',
  types: Array<FlowNode>,
};

export const flowUnionTypeAnnotation = createBuilder<FlowUnionTypeAnnotationNode>(
  'FlowUnionTypeAnnotation',
  {visitorKeys: ['types']},
);
