import {ModuleSignature, TypeCheckProvider} from '@romejs/js-analysis';
import {ProjectConfig} from '../common/types/project';
import {FileReference} from '@romejs/core';
import {
  WorkerProjects,
  PrefetchedModuleSignatures,
} from '../common/bridges/WorkerBridge';
import {ProgramNode, SourceType, Node} from '@romejs/js-ast';
import Logger from '../common/utils/Logger';
import WorkerBridge from '../common/bridges/WorkerBridge';
import * as parser from '@romejs/js-parser';
import {getExtensionHandler} from './extensions';
import {Profiler} from '@romejs/v8';
import WorkerAPI from './WorkerAPI';
import invariant from '@romejs/invariant';
import stream = require('stream');
import fs = require('fs');

export type ParseResult = {
  ast: ProgramNode;
  sourceType: SourceType;
  projectConfig: ProjectConfig;
  lastAccessed: number;
  src: string;
};

type FileCacheResult = {
  lastAccessed: number;
  src: string;
};

export default class Worker {
  constructor(bridge: WorkerBridge) {
    this.bridge = bridge;

    this.projects = new Map();
    this.fileCache = new Map();
    this.astCache = new Map();
    this.moduleSignatureCache = new Map();

    // fake stream to just emit data
    this.stdout = new stream.Writable({
      write: (chunk, encoding, callback) => {
        bridge.log.send(chunk.toString());
        callback();
      },
    });

    // logger instance which constructs the messages
    this.logger = new Logger('worker', bridge.log, {
      stderr: this.stdout,
      stdout: this.stdout,
    });

    //
    this.api = new WorkerAPI(this);
  }

  bridge: WorkerBridge;
  api: WorkerAPI;

  stdout: stream.Writable;
  logger: Logger;

  projects: Map<number, ProjectConfig>;
  astCache: Map<string, ParseResult>;
  fileCache: Map<string, FileCacheResult>;
  moduleSignatureCache: Map<string, ModuleSignature>;

  end() {
    // This will only actually be called when a Worker is created inside of the Master
    // Clear internal maps for memory, in case the Worker instance sticks around
    this.astCache.clear();
    this.fileCache.clear();
    this.projects.clear();
    this.moduleSignatureCache.clear();
  }

  init() {
    const bridge: WorkerBridge = this.bridge;

    bridge.endEvent.subscribe(() => {
      this.end();
    });

    let profiler: undefined | Profiler;
    bridge.profilingStart.subscribe(async () => {
      if (profiler !== undefined) {
        throw new Error('Expected no profiler to be running');
      }
      profiler = new Profiler();
      await profiler.startProfiling();
    });

    bridge.profilingStop.subscribe(async () => {
      if (profiler === undefined) {
        throw new Error('Expected a profiler to be running');
      }
      const workerProfile = await profiler.stopProfiling();
      profiler = undefined;
      return workerProfile;
    });

    bridge.compile.subscribe(payload => {
      return this.api.compile(payload.file, payload.stage, payload.options);
    });

    bridge.parse.subscribe(payload => {
      return this.api.parse(payload.file, payload.opts);
    });

    bridge.lint.subscribe(payload => {
      return this.api.lint(
        payload.file,
        payload.prefetchedModuleSignatures,
        payload.fix,
      );
    });

    bridge.analyzeDependencies.subscribe(payload => {
      return this.api.analyzeDependencies(payload.file);
    });

    bridge.evict.subscribe(payload => {
      this.evict(payload.filename);
      return undefined;
    });

    bridge.moduleSignature.subscribe(payload => {
      return this.api.moduleSignature(payload.file);
    });

    bridge.evictProject.subscribe(data => {
      return this.evictProject(data.id);
    });

    bridge.updateProjects.subscribe(payload => {
      return this.updateProjects(payload.projects);
    });

    bridge.status.subscribe(() => {
      return {
        astCacheSize: this.astCache.size,
        pid: process.pid,
        memoryUsage: process.memoryUsage(),
        uptime: process.uptime(),
      };
    });
  }

  async getTypeCheckProvider(
    projectId: number,
    prefetchedModuleSignatures: PrefetchedModuleSignatures = {},
  ): Promise<TypeCheckProvider> {
    const libs: Array<Node> = [];

    // TODO Figure out how to get the uids for the libraries, probably adding some additional stuff to ProjectConfig?
    /*
    const projectConfig = this.getProjectConfig(projectId);
    for (const filename of projectConfig.typeChecking.libs) {
      const {ast, err} = await this.parse(filename, uid, projectId);
      if (err) {
        throw err;
      } else {
        invariant(ast, 'expected ast');
        libs.push(ast);
      }
    }
    */

    const resolveGraph = async (
      key: string,
    ): Promise<undefined | ModuleSignature> => {
      const value = prefetchedModuleSignatures[key];
      if (value === undefined) {
        return undefined;
      }

      switch (value.type) {
        case 'RESOLVED':
          this.moduleSignatureCache.set(value.graph.filename, value.graph);
          return value.graph;

        case 'OWNED':
          return this.api.moduleSignature(value.file);

        case 'POINTER':
          return resolveGraph(value.key);

        case 'USE_CACHED':
          const cached = this.moduleSignatureCache.get(value.filename);
          invariant(
            cached !== undefined,
            'Master told us we have the export types for %s cached but we dont!',
            value.filename,
          );
          return cached;
      }
    };

    return {
      getExportTypes: async (
        origin: string,
        relative: string,
      ): Promise<undefined | ModuleSignature> => {
        return resolveGraph(`${origin}:${relative}`);
      },
      libs,
    };
  }

  async readFile(filename: string): Promise<string> {
    const cached: undefined | FileCacheResult = this.fileCache.get(filename);
    if (cached !== undefined) {
      this.fileCache.set(filename, {
        ...cached,
        lastAccessed: Date.now(),
      });
      return cached.src;
    }

    const src: string = await fs.promises.readFile(filename, 'utf8');
    this.fileCache.set(filename, {
      lastAccessed: Date.now(),
      src,
    });
    return src;
  }

  async parse(
    ref: FileReference,
    opts: {
      sourceType?: undefined | SourceType;
      cache?: boolean;
    } = {},
  ): Promise<ParseResult> {
    const {project, real, uid} = ref;
    const projectConfig = this.getProjectConfig(project);

    const sourceType =
      opts.sourceType === undefined ? 'module' : opts.sourceType;
    const cacheEnabled = opts.cache !== false;

    if (cacheEnabled) {
      // Update cachedFile access if it exists
      const cachedFile: undefined | FileCacheResult = this.fileCache.get(real);
      if (cachedFile) {
        this.fileCache.set(real, {
          ...cachedFile,
          lastAccessed: Date.now(),
        });
      }

      // Update the lastAccessed of the ast cache and return it, it will be evicted on
      // any file change
      const cachedResult: undefined | ParseResult = this.astCache.get(real);
      if (cachedResult && cachedResult.sourceType === sourceType) {
        this.astCache.set(real, {
          ...cachedResult,
          lastAccessed: Date.now(),
        });
        return cachedResult;
      }
    }

    this.logger.info(`Parsing:`, real);

    // Fetch and validate extension handler
    const extension = getExtensionHandler(real);
    if (extension === undefined) {
      throw new Error(`We don't have an extension handler for ${real}`);
    }
    if (extension.toJs === undefined) {
      throw new Error(`We don't know how to convert the file ${real} to js`);
    }

    // Fetch the file unless the extension has been defined as binary, it will perform it's own logic to read the file if necessary
    const src: string =
      extension.isBinary === true
        ? ''
        : await fs.promises.readFile(real, 'utf8');

    // Convert the file to JS
    const jsSrc = await extension.toJs(
      {
        file: ref,
        src,
      },
      this,
    );

    const ast = parser.parse(jsSrc, {
      filename: uid,
      sourceType,
    });

    const lastAccessed = Date.now();
    const res: ParseResult = {
      ast,
      lastAccessed,
      src: jsSrc,
      projectConfig,
      sourceType,
    };

    // put it in the cache
    if (cacheEnabled) {
      this.astCache.set(real, res);
      this.fileCache.set(real, {
        lastAccessed,
        src,
      });
    }

    return res;
  }

  getProjectConfig(id: number): ProjectConfig {
    const config = this.projects.get(id);
    if (config === undefined) {
      throw new Error(
        `Unknown project ${id}, known projects are ${this.projects.keys()}`,
      );
    }
    return config;
  }

  async writeFile(filename: string, content: string): Promise<void> {
    // Write the file out
    await fs.promises.writeFile(filename, content);

    // We just wrote the file but the server watcher hasn't had time to notify us
    this.evict(filename);
  }

  evict(filename: string) {
    this.astCache.delete(filename);
    this.fileCache.delete(filename);
    this.moduleSignatureCache.delete(filename);
  }

  evictProject(id: number) {
    this.projects.delete(id);
  }

  async updateProjects(projects: WorkerProjects) {
    this.projects.clear();
    for (const {config, id} of projects) {
      this.projects.set(id, config);
    }
  }
}
