import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXMemberExpression(
  generator: Generator,
  node: t.JSXMemberExpressionNode,
) {
  generator.print(node.object, node);
  generator.token('.');
  generator.print(node.property, node);
}
