import {JSNodeBase, IdentifierNode, ModuleKind} from '../index';
import {createBuilder} from '../utils';

export type ImportSpecifierNode = JSNodeBase & {
  type: 'ImportSpecifier',
  imported: IdentifierNode,
  local: IdentifierNode,
  importKind?: undefined | ModuleKind,
};

export const importSpecifier = createBuilder<ImportSpecifierNode>(
  'ImportSpecifier',
  {visitorKeys: ['local', 'imported']},
);
