import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXAttribute(generator: Generator, node: t.JSXAttributeNode) {
  generator.print(node.name, node);
  if (node.value) {
    generator.token('=');
    generator.print(node.value, node);
  }
}
