import {orderBySimilarity} from '@romejs/string-utils';

export default function findClosestStringMatch(
  key: string,
  matches: Array<string>,
): undefined | string {
  if (matches.length === 0) {
    return undefined;
  }

  if (matches.length === 1) {
    return matches[0];
  }

  const ratings = orderBySimilarity(key, matches);
  const bestMatch = ratings[0];
  // TODO set this to a sane percentage
  if (bestMatch.rating > 0) {
    return undefined;
  } else {
    return bestMatch.target;
  }
}
