import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';

export function FlowDeclareVariable(
  node: n.FlowDeclareVariableNode,
  scope: Scope,
) {
  const type = scope.evaluate(node.id.typeAnnotation);
  scope.addBinding(node.id.name, type);
  return type;
}
