import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {FlowInterfaceDeclaration} from './FlowInterfaceDeclaration';

export function FlowDeclareInterface(
  generator: Generator,
  node: t.FlowDeclareInterfaceNode,
) {
  generator.word('declare');
  generator.space();
  FlowInterfaceDeclaration(generator, node);
}
