import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';

export function FlowInterfaceDeclaration(
  node: n.FlowInterfaceDeclarationNode | n.FlowDeclareInterfaceNode,
  scope: Scope,
) {
  const typeScope = scope.fork();
  if (node.typeParameters) {
    typeScope.evaluate(node.typeParameters);
  }

  // TODO extends
  const body = typeScope.evaluate(node.body);
  scope.addBinding(node.id.name, body);
  return body;
}
