import {JSNodeBase, StringLiteralNode} from '../index';
import {createBuilder} from '../utils';

export type TSExternalModuleReferenceNode = JSNodeBase & {
  type: 'TSExternalModuleReference';
  expression: StringLiteralNode;
};

export const tsExternalModuleReference = createBuilder<
  TSExternalModuleReferenceNode
>('TSExternalModuleReference', {
  visitorKeys: ['expression'],
});
