import {
  JSNodeBase,
  TSTypeParameterDeclarationNode,
  IdentifierNode,
  TSTypeNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSTypeAliasDeclarationNode = JSNodeBase & {
  type: 'TSTypeAliasDeclaration';
  id: IdentifierNode;
  typeParameters?: undefined | TSTypeParameterDeclarationNode;
  typeAnnotation: TSTypeNode;
};

export const tsTypeAliasDeclaration = createBuilder<TSTypeAliasDeclarationNode>(
  'TSTypeAliasDeclaration',
  {visitorKeys: ['id', 'typeParameters', 'typeAnnotation']},
);
