import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowTypeAlias(
  generator: Generator,
  node: t.FlowTypeAliasNode | t.FlowDeclareTypeAliasNode,
) {
  generator.word('type');
  generator.space();
  generator.print(node.id, node);
  generator.print(node.typeParameters, node);
  generator.space();
  generator.token('=');
  generator.space();
  generator.print(node.right, node);
  generator.semicolon();
}
