import {
  JSNodeBase,
  TSEntityNameNode,
  ExpressionNode,
  TSTypeParameterInstantiationNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSImportTypeNode = JSNodeBase & {
  type: 'TSImportType';
  argument: ExpressionNode;
  typeParameters?: undefined | TSTypeParameterInstantiationNode;
  qualifier?: undefined | TSEntityNameNode;
};

export const tsImportType = createBuilder<TSImportTypeNode>('TSImportType', {
  visitorKeys: ['argument', 'typeParameters', 'qualifier'],
});
