import {JSNodeBase, PatternBase, ExpressionNode, PatternNode} from '../index';
import {createBuilder} from '../utils';

export type AssignmentPatternNode = JSNodeBase &
  PatternBase & {
    type: 'AssignmentPattern';
    left: PatternNode;
    right: ExpressionNode;
  };

export const assignmentPattern = createBuilder<AssignmentPatternNode>(
  'AssignmentPattern',
  {
    visitorKeys: ['left', 'right'],
  },
);
