import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function SwitchStatement(
  generator: Generator,
  node: t.SwitchStatementNode,
) {
  generator.word('switch');
  generator.space();
  generator.token('(');
  generator.print(node.discriminant, node);
  generator.token(')');
  generator.space();
  generator.token('{');

  generator.printStatementList(node.cases, node, {
    indent: true,
  });

  generator.token('}');
}
