import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type ExportSpecifierNode = JSNodeBase & {
  type: 'ExportSpecifier',
  exported: IdentifierNode,
  local: IdentifierNode,
};

export const exportSpecifier = createBuilder<ExportSpecifierNode>(
  'ExportSpecifier',
  {visitorKeys: ['local', 'exported']},
);
