import {
  JSNodeBase,
  FlowNode,
  IdentifierNode,
  FlowVarianceNode,
  StringLiteralNode,
  NumericLiteralNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowObjectTypePropertyKind = 'init' | 'get' | 'set';

export type FlowObjectTypePropertyKey =
  | IdentifierNode
  | StringLiteralNode
  | NumericLiteralNode;

export type FlowObjectTypePropertyNode = JSNodeBase & {
  type: 'FlowObjectTypeProperty';
  key: FlowObjectTypePropertyKey;
  value: FlowNode;
  kind: FlowObjectTypePropertyKind;
  static?: undefined | boolean;
  optional?: undefined | boolean;
  proto?: undefined | boolean;
  variance?: undefined | FlowVarianceNode;
};

export const flowObjectTypeProperty = createBuilder<FlowObjectTypePropertyNode>(
  'FlowObjectTypeProperty',
  {visitorKeys: ['key', 'value']},
);
