import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function ClassProperty(generator: Generator, node: t.ClassPropertyNode) {
  if (node.static === true) {
    generator.word('static');
    generator.space();
  }

  if (node.computed === true) {
    generator.token('[');
    generator.print(node.key, node);
    generator.token(']');
  } else {
    generator.print(node.variance, node);
    generator.print(node.key, node);
  }

  generator.print(node.typeAnnotation, node);
  if (node.value) {
    generator.space();
    generator.token('=');
    generator.space();
    generator.print(node.value, node);
  }
  generator.semicolon();
}
