import {JSNodeBase, PatternNode} from '../index';
import {createBuilder} from '../utils';

export type ArrayExpressionNode = JSNodeBase & {
  type: 'ArrayExpression';
  elements: Array<PatternNode>;
};

export const arrayExpression = createBuilder<ArrayExpressionNode>(
  'ArrayExpression',
  {visitorKeys: ['elements']},
);
