import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXNamespacedName(
  generator: Generator,
  node: t.JSXNamespacedNameNode,
) {
  generator.print(node.namespace, node);
  generator.token(':');
  generator.print(node.name, node);
}
