import {Consumer} from '@romejs/codec-json';
import {PathPatterns} from '@romejs/match-path';
import {parsePathPattern} from '@romejs/match-path';

export function arrayOfStrings(consumer: Consumer): Array<string> {
  if (consumer.exists()) {
    return consumer.asArray().map(item => item.asString());
  } else {
    return [];
  }
}

export function arrayOfPatterns(consumer: Consumer): PathPatterns {
  return consumer.asArray().map(item => {
    return parsePathPattern({
      input: item.asString(),
      offsetPosition: item.getInnerLocation().start,
    });
  });
}

export function mergeArrays<T>(
  a: undefined | Array<T>,
  b: undefined | Array<T>,
): undefined | Array<T> {
  if (a === undefined) {
    return a;
  }

  if (b === undefined) {
    return a;
  }

  return [...a, ...b];
}
