import {AnalyzeDependencyResult} from '@romejs/js-compiler';
import {ModuleSignature} from '@romejs/js-analysis';
import {ProgramNode, SourceType} from '@romejs/js-ast';
import {
  CompileResult,
  TransformStageName,
  CompilerOptions,
} from '@romejs/js-compiler';
import {Profile} from '@romejs/v8';
import {PartialDiagnostics} from '@romejs/diagnostics';
import {ProjectConfig} from '../types/project';
import {FileReference} from '@romejs/core';
import {DiagnosticError, DiagnosticsError} from '@romejs/diagnostics';
import {Bridge} from '@romejs/events';

export type WorkerProjects = Array<{
  id: number;
  config: ProjectConfig;
}>;

export type WorkerCompileResult = CompileResult;

export type WorkerAnalyzeDependencyResult = AnalyzeDependencyResult;

export type WorkerParseOptions = {
  compact: boolean;
  sourceType: SourceType;
};

export type WorkerStatus = {
  astCacheSize: number;
  memoryUsage: {
    rss: number;
    heapTotal: number;
    heapUsed: number;
    external: number;
  };
  pid: number;
  uptime: number;
};

export type PrefetchedModuleSignatures = {
  [key: string]:
    | {
        type: 'USE_CACHED';
        filename: string;
      }
    | {
        type: 'RESOLVED';
        graph: ModuleSignature;
      }
    | {
        type: 'OWNED';
        file: FileReference;
      }
    | {
        type: 'POINTER';
        key: string;
      };
};

export default class WorkerBridge extends Bridge {
  log = this.createEvent<string, void>({name: 'log'});
  evictProject = this.createEvent<{id: number}, void>({name: 'evictProject'});
  updateProjects = this.createEvent<{projects: WorkerProjects}, void>({
    name: 'updateProjects',
  });
  profilingStart = this.createEvent<void, void>({name: 'profiling.start'});
  profilingStop = this.createEvent<void, Profile>({name: 'profiling.stop'});
  status = this.createEvent<void, WorkerStatus>({name: 'status'});
  evict = this.createEvent<{filename: string}, void>({name: 'evict'});
  moduleSignature = this.createEvent<{file: FileReference}, ModuleSignature>({
    name: 'moduleSignature',
  });
  analyzeDependencies = this.createEvent<
    {file: FileReference},
    AnalyzeDependencyResult
  >({
    name: 'analyzeDependencies',
  });
  lint = this.createEvent<
    {
      file: FileReference;
      prefetchedModuleSignatures: PrefetchedModuleSignatures;
      fix: boolean;
    },
    PartialDiagnostics
  >({name: 'lint'});
  compile = this.createEvent<
    {
      file: FileReference;
      stage: TransformStageName;
      options: CompilerOptions;
    },
    CompileResult
  >({name: 'compile'});
  parse = this.createEvent<
    {
      file: FileReference;
      opts: WorkerParseOptions;
    },
    ProgramNode
  >({name: 'parse'});

  init() {
    this.addErrorTransport('DiagnosticError', {
      serialize(err: Error) {
        if (!(err instanceof DiagnosticError)) {
          throw new Error('Expected DiagnosticError');
        }

        return {
          message: err.message,
          diagnostic: err.diagnostic,
        };
      },
      hydrate(err, data) {
        return new DiagnosticError(String(err.message), data.diagnostic as any);
      },
    });

    this.addErrorTransport('DiagnosticsError', {
      serialize(err: Error) {
        if (!(err instanceof DiagnosticsError)) {
          throw new Error('Expected DiagnosticsError');
        }

        return {
          diagnostic: err.diagnostics,
        };
      },
      hydrate(err, data) {
        return new DiagnosticsError(
          String(err.message),
          data.diagnostics as any,
        );
      },
    });
  }
}
