import {
  JSNodeBase,
  ExpressionNode,
  FunctionBase,
  ClassMemberBase,
} from '../index';
import {createBuilder, METHOD_FUNCTION_VISITOR_KEYS} from '../utils';

export type ClassMethodNode = JSNodeBase &
  FunctionBase &
  ClassMemberBase & {
    type: 'ClassMethod';
    computed?: undefined | boolean;
    key: ExpressionNode;
    kind: ClassMethodKind;
  };

export type ClassMethodKind = 'constructor' | 'method' | 'get' | 'set';

export const classMethod = createBuilder<ClassMethodNode>('ClassMethod', {
  visitorKeys: METHOD_FUNCTION_VISITOR_KEYS,
});
