import {JSONParserOptions} from './types';
import Parser from './Parser';
import Consumer from './Consumer';

export {JSONParserMode, JSONParserOptions} from './types';
export {Consumer};

export function consumeMixed(value: unknown): Consumer {
  return new Consumer({
    filename: undefined,
    context: undefined,
    key: undefined,
    value,
    parent: undefined,
  });
}

export function consumeJSON(opts: JSONParserOptions): Consumer {
  const parser = new Parser(opts);
  const {value, context} = parser.parse();

  return new Consumer({
    filename: parser.filename,
    context: context,
    key: undefined,
    value,
    parent: undefined,
  });
}

export function stringifyJSON(value: unknown): string {
  const consumer = new Consumer({
    filename: undefined,
    context: undefined,
    key: undefined,
    value: value,
    parent: undefined,
  });
  return consumer.stringify();
}

export function parseJSON(opts: JSONParserOptions): unknown {
  const parser = new Parser(opts);
  return parser.parse().value;
}

export {default as messages} from './messages';
