import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type FlowQualifiedTypeIdentifierNode = JSNodeBase & {
  type: 'FlowQualifiedTypeIdentifier',
  id: IdentifierNode,
  qualification: IdentifierNode | FlowQualifiedTypeIdentifierNode,
};

export const flowQualifiedTypeIdentifier = createBuilder<FlowQualifiedTypeIdentifierNode>(
  'FlowQualifiedTypeIdentifier',
  {visitorKeys: ['id', 'qualification']},
);
