import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowDeclareExportAllDeclaration(
  generator: Generator,
  node: t.FlowDeclareExportAllDeclarationNode,
) {
  generator.word('declare');
  generator.space();
  generator.word('export');
  generator.space();
  generator.token('*');
  generator.space();
  generator.word('from');
  generator.space();
  generator.print(node.source, node);
  generator.semicolon();
}
