import Scope from './Scope';
import {
  LetBinding,
  ArgumentsBinding,
  REDUCE_SKIP_SUBTREE,
} from '@romejs/js-compiler';
import {getBindingIdentifiers, isFunctionNode} from '@romejs/js-ast-utils';
import * as t from '@romejs/js-ast';

export function addFunctionBindings(
  scope: Scope,
  node: t.FunctionNode,
  hasArguments: boolean = true,
) {
  // Add type parameters
  scope.evaluate(node.typeParameters);

  // Add parameters
  for (const param of node.params) {
    for (const id of getBindingIdentifiers(param)) {
      // TODO maybe add a `param` binding type?
      scope.addBinding(
        new LetBinding({
          node: id,
          name: id.name,
          scope,
        }),
      );
    }
  }

  // Add `arguments` binding
  if (hasArguments) {
    scope.addBinding(
      new ArgumentsBinding({
        name: 'arguments',
        node,
        scope,
      }),
    );
  }

  addVarBindings(scope, node);
}

export function addVarBindings(
  scope: Scope,
  topNode: t.FunctionNode | t.ProgramNode,
) {
  const {context} = scope.getRootScope();

  context.reduce(
    topNode,
    [
      {
        name: 'scopeVarFunc',
        enter: path => {
          const {node, parent} = path;

          if (isFunctionNode(node) && node !== topNode) {
            return REDUCE_SKIP_SUBTREE;
          }

          if (node.type === 'VariableDeclaration' && node.kind === 'var') {
            scope.evaluate(node, parent);
          }

          return node;
        },
      },
    ],
    {
      scope,
      noScopeCreation: true,
    },
  );
}
