import {Context, Path} from '@romejs/js-compiler';
import {
  Node,
  StatementNode,
  ClassMethodNode,
  ClassExpressionNode,
  ExpressionNode,
  PatternNode,
  ClassDeclarationNode,
} from '@romejs/js-ast';
import {Scope} from '@romejs/js-compiler';
import {
  restElement,
  classMethod,
  identifier,
  sequenceExpression,
  expressionStatement,
  callExpression,
  arrowFunctionExpression,
  blockStatement,
  returnStatement,
} from '@romejs/js-ast';
import {template, inheritLoc} from '@romejs/js-ast-utils';

function hasClassProps(
  node: ClassDeclarationNode | ClassExpressionNode,
): boolean {
  for (const bodyNode of node.body) {
    if (bodyNode.type === 'ClassProperty') {
      return true;
    }
  }

  return false;
}

function createConstructor(
  params: Array<PatternNode>,
  body: Array<StatementNode>,
) {
  return classMethod.create({
    kind: 'constructor',
    key: identifier.create({name: 'constructor'}),
    params,
    body: blockStatement.create({body}),
  });
}

function toExpressionStatements(
  expressions: Array<ExpressionNode>,
): Array<StatementNode> {
  return expressions.map(expr => {
    return expressionStatement.create({expression: expr});
  });
}

function isSuperCall(node: Node): boolean {
  return node.type === 'CallExpression' && node.callee.type === 'Super';
}

function transformClass(
  node: ClassDeclarationNode,
  scope: Scope,
  context: Context,
): {
  newClass: ClassDeclarationNode;
  className: string;
  declarations: Array<Node>;
} {
  const bodyReplacements: Array<StatementNode> = [];
  const constructorAssignments: Array<ExpressionNode> = [];
  const className: string =
    node.id === undefined ? scope.generateUid('class') : node.id.name;

  let constructor: undefined | ClassMethodNode = undefined;
  const filteredClassBody = [];
  for (const bodyNode of node.body) {
    if (bodyNode.type === 'ClassMethod' && bodyNode.kind === 'constructor') {
      constructor = bodyNode;
    } else if (bodyNode.type === 'ClassProperty') {
      if (bodyNode.value === undefined) {
        continue;
      }

      if (bodyNode.computed === true) {
        if (bodyNode.static === true) {
          bodyReplacements.push(
            template.statement`${className}[${bodyNode.key}] = ${
              bodyNode.value
            };`,
          );
        } else {
          constructorAssignments.push(
            template.expression`this[${bodyNode.key}] = ${bodyNode.value};`,
          );
        }
      } else {
        if (bodyNode.static === true) {
          bodyReplacements.push(
            template.statement`${className}.${bodyNode.key} = ${
              bodyNode.value
            };`,
          );
        } else {
          constructorAssignments.push(
            template.expression`this.${bodyNode.key} = ${bodyNode.value};`,
          );
        }
      }
    } else {
      filteredClassBody.push(bodyNode);
    }
  }

  if (constructorAssignments.length) {
    if (node.superClass !== undefined) {
      if (constructor) {
        const visited = new Set();

        // find super() and insert assignments
        const reducedConstructor = context.reduce(constructor, [
          {
            name: 'classPropertiesInjector',
            enter(path) {
              const {node} = path;

              if (visited.has(node)) {
                return node;
              }

              if (
                isSuperCall(node) &&
                path.getParent().type !== 'ExpressionStatement'
              ) {
                visited.add(node);

                // TODO retain proper value of super()
                return sequenceExpression.create({
                  expressions: [node, ...constructorAssignments],
                });
              }

              if (
                node.type === 'ExpressionStatement' &&
                isSuperCall(node.expression)
              ) {
                visited.add(node);

                return [
                  node,
                  ...toExpressionStatements(constructorAssignments),
                ] as Array<Node>;
              }

              return node;
            },
          },
        ]);
        constructor = classMethod.assert(reducedConstructor);
      } else {
        // create new constructor with a super() call and assignments
        constructor = createConstructor(
          [restElement.create({argument: identifier.create({name: 'args'})})],
          [
            template.statement`super(...args);`,
            ...toExpressionStatements(constructorAssignments),
          ],
        );
      }
    } else {
      if (constructor) {
        // add assignments to end of constructor
        constructor = {
          ...constructor,
          body: {
            ...constructor.body,
            body: [
              ...constructor.body.body,
              ...toExpressionStatements(constructorAssignments),
            ],
          },
        };
      } else {
        // create new constructor with just the assignments
        constructor = createConstructor(
          [],
          toExpressionStatements(constructorAssignments),
        );
      }
    }
  }

  if (constructor !== undefined) {
    filteredClassBody.unshift(constructor);
  }

  const newClass: ClassDeclarationNode = {
    ...node,
    id:
      node.id !== undefined && node.id.name === className
        ? node.id
        : identifier.create({
            name: className,
            loc: inheritLoc(node.id, className),
          }),
    body: filteredClassBody,
  };

  return {
    newClass,
    className,
    declarations: bodyReplacements,
  };
}

export default {
  name: 'classProperties',
  enter(path: Path) {
    const {node, scope, context} = path;

    // correctly replace an export class with the class node then append the declarations
    const isExport =
      node.type === 'ExportNamedDeclaration' ||
      node.type === 'ExportDefaultDeclaration';
    const isExportClass =
      isExport &&
      node.declaration !== undefined &&
      node.declaration.type === 'ClassDeclaration';
    if (isExportClass && hasClassProps(node.declaration)) {
      const {newClass, declarations} = transformClass(
        node.declaration,
        scope,
        context,
      );
      return [
        {
          ...node,
          declaration: newClass,
        },
        ...declarations,
      ] as Array<Node>;
    }

    // turn a class expression into an IIFE that returns a class declaration
    if (node.type === 'ClassExpression' && hasClassProps(node)) {
      const className =
        node.id === undefined ? scope.generateUid('class') : node.id.name;

      return callExpression.create({
        callee: arrowFunctionExpression.create({
          params: [],
          body: blockStatement.create({
            body: [
              {
                ...node,
                type: 'ClassDeclaration',
                id: identifier.create({name: className}),
              },
              returnStatement.create({
                argument: identifier.create({name: className}),
              }),
            ],
          }),
        }),
        arguments: [],
      });
    }

    if (node.type === 'ClassDeclaration' && hasClassProps(node)) {
      const {newClass, declarations} = transformClass(node, scope, context);
      return [newClass, ...declarations] as Array<Node>;
    }

    return node;
  },
};
