import {Node} from '@romejs/js-ast';
import {PartialDiagnosticAdvice} from '@romejs/diagnostics';
import {Scope} from '../../scopes';
import findClosestStringMatch from '../../utils/findClosestStringMatch';
import E from './E';

export default class UndeclaredVarE extends E {
  constructor(scope: Scope, originNode: undefined | Node, name: string) {
    super(scope, originNode);
    this.name = name;
  }

  static type = 'UndeclaredVarE';
  name: string;

  getError() {
    const possibleNames = this.scope.getBindingNames();
    const suggestion = findClosestStringMatch(this.name, possibleNames);

    let infoMessage = undefined;
    let infoList = undefined;

    if (suggestion === undefined) {
      infoMessage = 'This could be a module you forgot to import.';
      infoList = possibleNames;

      // TODO group bindings from 'this file at the top
      // TODO sort by ranking
    } else {
      infoMessage = `Did you mean '${suggestion}'?`;
    }

    const advice: PartialDiagnosticAdvice = [
      {
        type: 'log',
        category: 'info',
        message: infoMessage,
      },
    ];

    if (infoList !== undefined) {
      advice.push({
        type: 'list',
        list: infoList,
      });
    }

    return {
      message: `Undeclared variable '${this.name}'`,
      advice,
      lowerTarget: this,
    };
  }
}
