import {Stdout} from './types';
import {formatAnsi} from '@romejs/string-ansi';
import {humanizeNumber, humanizeTime} from '@romejs/string-utils';
import {clearLine, toStartOfLine} from './util';
import {Reporter} from '@romejs/cli-reporter';

type BoldRanges = Array<[number, number]>;
type SplitBar = Array<[number, string]>;

const BOUNCER_INTERVAL = 50;
const BOUNCER_WIDTH = 15;

export default class Progress {
  constructor(reporter: Reporter, onEnd?: () => void) {
    this.reporter = reporter;
    this.stdout = reporter.stdout;

    this.textStack = [];
    this.text = undefined;
    this.title = undefined;

    this.startTime = Date.now();
    this.lastRenderTime = Date.now();
    this.lastRenderCurrent = 0;

    this.open = true;
    this.current = 0;
    this.total = undefined;
    this.onEnd = onEnd;

    this.delay = 60;
    this.renderEvery = 0;
    clearLine(reporter.stdout);

    this.bounceStart = 0;
    this.startBouncer();

    this.queueRender();
  }

  reporter: Reporter;
  stdout: Stdout;
  open: boolean;
  delay: number;
  renderTimer: undefined | NodeJS.Timeout;

  bounceStart: number;
  bouncerTimer: undefined | NodeJS.Timeout;

  onEnd: undefined | (() => void);
  renderEvery: number;
  text: undefined | string;
  title: undefined | string;
  startTime: number;
  lastRenderCurrent: number;
  lastRenderTime: number;
  current: number;
  total: undefined | number;
  textStack: Array<string>;

  getElapsedTime(): number {
    return Date.now() - this.startTime;
  }

  startBouncer() {
    let direction = 'left-to-right';
    let start: number = 0;

    const tick = () => {
      // The point in which the bouncer will hit the end of the console
      const end = this.reporter.getColumns() - BOUNCER_WIDTH;

      // -->
      if (direction === 'left-to-right') {
        start++;

        if (start > end) {
          direction = 'right-to-left';
          start = end - 1;
        }
      }

      // <--
      if (direction === 'right-to-left') {
        start--;

        if (start <= 0) {
          direction = 'left-to-right';
          start = 0;
        }
      }

      this.bounceStart = start;
      this.bouncerTimer = setTimeout(tick, BOUNCER_INTERVAL);
      this.render();
    };

    // We don't call tick() here as we don't want to trigger a render
    this.bouncerTimer = setTimeout(tick, BOUNCER_INTERVAL);
  }

  setCurrent(current: number) {
    if (this.open === false) {
      return undefined;
    }

    this.current = current;

    // Schedule render
    if (this.renderTimer === undefined) {
      this.queueRender();
    }

    if (this.isRenderDue()) {
      this.render();
    }

    // Progress complete
    if (this.total !== undefined && this.current >= this.total) {
      this.end();
    }
  }

  setTotal(total: number) {
    this.total = total;
    this.renderEvery = Math.round(total / 100);
    this.endBouncer();
    this.queueRender();
  }

  setTitle(title: string) {
    this.title = title;
    this.queueRender();
  }

  setText(text: string) {
    this.assertOpen();
    this.text = text;
    this.queueRender();
  }

  pushText(text: string) {
    this.setText(text);
    this.textStack.push(text);
  }

  popText(text: string) {
    // Find
    const {textStack} = this;
    const index = textStack.indexOf(text);
    if (index === -1) {
      throw new Error(`No pushed text: ${text}`);
    }

    // Remove
    textStack.splice(index, 1);

    // Set last
    const last: undefined | string = textStack[textStack.length - 1];
    this.setText(last === undefined ? '' : last);
  }

  tick() {
    this.setCurrent(this.current + 1);
  }

  isOpen(): boolean {
    return this.open;
  }

  assertOpen() {
    if (this.open === false) {
      throw new Error('Progress bar has already been closed');
    }
  }

  queueRender() {
    if (this.open === false) {
      // Progress bar has been removed
      return undefined;
    }

    if (this.renderTimer !== undefined) {
      // Render already queued
      return undefined;
    }

    this.renderTimer = setTimeout(() => {
      this.render();
    }, this.delay);
  }

  endBouncer() {
    if (this.bouncerTimer !== undefined) {
      clearTimeout(this.bouncerTimer);
    }
    this.bouncerTimer = undefined;
  }

  endRender() {
    if (this.renderTimer !== undefined) {
      clearTimeout(this.renderTimer);
    }
    this.renderTimer = undefined;
  }

  end() {
    this.open = false;
    this.endBouncer();
    this.endRender();
    this.clear();

    if (this.onEnd !== undefined) {
      this.onEnd();
    }
  }

  clear() {
    clearLine(this.stdout);
  }

  // Ensure that we update the progress bar after a certain amount of ticks
  // This allows us to use the progress bar for sync work where the event loop is always blocked
  isRenderDue(): boolean {
    const isDue: boolean =
      this.current > this.lastRenderCurrent + this.renderEvery;
    if (isDue) {
      // We also make sure that we never force update more often than once a second
      // This is to ensure that the progress bar isn't negatively effecting performance
      const timeSinceLastRender: number = Date.now() - this.lastRenderTime;
      return timeSinceLastRender > 1000;
    } else {
      return false;
    }
  }

  isBoldCharacter(i: number, ranges: BoldRanges): boolean {
    for (const [start, end] of ranges) {
      if (start >= i && end <= i) {
        return true;
      }
    }

    return false;
  }

  splitCharacters(str: string, boldRanges: BoldRanges): SplitBar {
    return str.split('').map((char, i) => {
      if (this.isBoldCharacter(i, boldRanges)) {
        return [i, formatAnsi.bold(char)];
      } else {
        return [i, char];
      }
    });
  }

  buildProgressBouncer(bar: SplitBar): string {
    let start = this.bounceStart;
    let fullBar = '';
    for (const [i, char] of bar) {
      const isBounce = i >= start && i < start + BOUNCER_WIDTH;

      if (isBounce) {
        fullBar += formatAnsi.white(formatAnsi.bgYellow(char));
      } else {
        fullBar += char;
      }
    }
    return fullBar;
  }

  buildProgressBar(bar: SplitBar, total: number): string {
    const ratio = Math.min(Math.max(this.current / total, 0), 1);

    const completeLength = Math.round(this.reporter.getColumns() * ratio);
    let fullBar = '';
    for (const [i, char] of bar) {
      if (i < completeLength) {
        fullBar += formatAnsi.white(formatAnsi.bgGreen(char));
      } else {
        fullBar += char;
      }
    }
    return fullBar;
  }

  buildBar() {
    const {total, current, text, title} = this;

    // Text ranges that we should make bold
    const boldRanges: BoldRanges = [];

    // Text to prefix to the bar
    let prefix = ' ';
    if (title !== undefined) {
      prefix += title;

      // Only the title should be bold, not the subtext
      boldRanges.push([0, prefix.length - 1]);
    }
    if (text !== undefined) {
      // Separate a title and it's text with a colon
      if (title !== undefined) {
        prefix += ': ';
      }
      prefix += text;
    }
    prefix += ' ';

    // Text to put at the end of the bar
    let suffix = '';

    // ETA eg: 1m5s
    if (current > 0) {
      // Total time since the progress bar was created
      const elapsed = this.getElapsedTime();

      // How many milliseconds spent per total items
      const averagePer = current / elapsed;

      // How many items processed per second
      const perSecond = Math.round(averagePer * 1000);

      //
      suffix += `${humanizeNumber(perSecond)} op/s `;
      suffix += `elapsed ${humanizeTime(elapsed)} `;

      if (total !== undefined) {
        // How many items we have left
        const left = total - current;

        // Total estimated time left
        const eta = left * averagePer;
        suffix += `eta ${humanizeTime(eta)} `;
      }
    }

    // Counter eg: 5/100
    suffix += `${humanizeNumber(current)}`;
    if (total !== undefined) {
      suffix += `/${humanizeNumber(total)}`;
    }
    suffix += ' ';

    // Get the full width of the bar
    const width = this.reporter.getColumns();

    // The amount of spaces to put between the title and counter
    const spacerLength = Math.max(0, width - prefix.length - suffix.length);

    // Trim the prefix if it will overflow
    prefix = prefix.slice(0, width - suffix.length);

    // The full raw bar without any coloring
    const raw = prefix + ' '.repeat(spacerLength) + suffix;

    // Make sure the counter is bold
    boldRanges.push([raw.length - suffix.length, raw.length - 1]);

    // Split the raw bar into an array of formatted characters
    const chars = this.splitCharacters(raw, boldRanges);

    if (total === undefined) {
      return this.buildProgressBouncer(chars);
    } else {
      return this.buildProgressBar(chars, total);
    }
  }

  render() {
    this.assertOpen();
    this.endRender();

    this.lastRenderCurrent = this.current;
    this.lastRenderTime = Date.now();

    toStartOfLine(this.stdout);
    this.stdout.write(this.buildBar());
  }
}
