import {Reporter} from '@romejs/cli-reporter';
import {Event} from '@romejs/events';
import child_process = require('child_process');
import util = require('util');
import net = require('net');

const exec = util.promisify(child_process.exec);

export type WatchmanSubscriptionValue = {
  unilateral: true;
  subscription: string;
  root: string;
  files: Array<{
    exists: boolean;
    type: 'f' | 'd';
    size: number;
    name: string;
    mtime: number;
  }>;
  is_fresh_instance: boolean;
  version: string;
  since: string;
  clock: string;
};

export class WatchmanClient {
  constructor(socket: net.Socket, reporter: Reporter) {
    this.reporter = reporter;
    this.socket = socket;

    this.subscriptionCounter = 0;
    this.subscriptions = new Map();

    this.logEvent = new Event({name: 'WatchmanClient.log'});

    this.callbacks = [];
    this.listen();
  }

  callbacks: Array<{
    resolve: (data: unknown) => void;
    reject: (err: Error) => void;
  }>;

  subscriptions: Map<string, Event<WatchmanSubscriptionValue, void>>;
  subscriptionCounter: 0;

  reporter: Reporter;
  socket: net.Socket;
  logEvent: Event<void, void>;

  listen() {
    const {socket} = this;

    socket.on('error', function() {
      //
    });

    let buf: string = '';

    const checkBuffer = (offset: number) => {
      for (let i = offset; i < buf.length; i++) {
        const char = buf[i];

        if (char === '\n') {
          const message = buf.slice(0, i);
          this.processResponse(message);

          buf = buf.slice(i + 1);
          checkBuffer(0);
        }
      }
    };

    socket.on('data', function(chunk) {
      const offset = buf.length;
      buf += chunk.toString();
      checkBuffer(offset);
    });

    socket.on('end', () => {
      this.end();
    });
  }

  processResponse(str: string) {
    const obj = JSON.parse(str);

    if (typeof obj.warn === 'string') {
      this.reporter.warn(obj.warn);
    }

    if (typeof obj.subscription === 'string') {
      const event = this.subscriptions.get(obj.subscription);
      if (event === undefined) {
        this.reporter.warn(
          "Received a watchman subscription event for %s that we aren't listening for",
          obj.subscription,
        );
      } else {
        event.send(obj);
      }
      return undefined;
    }

    if (obj.log !== undefined) {
      // TODO
      return undefined;
    }

    if (obj.unilateral === true) {
      this.reporter.warn(
        "Received a watchman unilateral event that we don't support",
        obj,
      );
      return undefined;
    }

    const callback = this.callbacks.shift();
    if (callback === undefined) {
      throw new Error('Received message but no callback');
    }

    if (typeof obj.error === 'string') {
      callback.reject(new Error(obj.error));
    } else {
      callback.resolve(obj);
    }
  }

  async createSubscription(
    dir: string,
    opts: unknown,
  ): Promise<{
    root: string;
    event: Event<WatchmanSubscriptionValue, void>;
  }> {
    const name = `rome-${process.pid}.${String(this.subscriptionCounter++)}`;
    const event: Event<WatchmanSubscriptionValue, void> = new Event({
      name,
    });
    this.subscriptions.set(name, event);

    const res: any = await this.command(['watch-project', dir]);

    if (res == null || typeof res !== 'object') {
      throw new Error(
        'Malformed watchman watch-project response - not an object',
      );
    }

    if (typeof res.watch !== 'string') {
      throw new Error(
        'Malformed watchman watch-project response - non-string watch property',
      );
    }

    // Refine any filter to specifically files from the requested directory if we weren't the project root
    if (typeof res.relative_path === 'string') {
      opts = {
        ...opts,
        expression: ['allof', ['dirname', res.relative_path]],
      };
    }

    const root = res.watch;
    await this.command(['subscribe', root, name, opts]);

    return {
      root,
      event,
    };
  }

  async command(args: Array<unknown>): Promise<unknown> {
    return new Promise((resolve, reject) => {
      this.callbacks.push({resolve, reject});
      this.socket.write(JSON.stringify(args) + '\n');
    });
  }

  end() {
    for (const {reject} of this.callbacks) {
      reject(new Error('The watchman connection was closed'));
    }
    this.socket.end();
  }
}

export async function createWatchmanClient(
  reporter: Reporter,
): Promise<WatchmanClient> {
  let sockname: undefined | string;

  if (typeof process.env.WATCHMAN_SOCK === 'string') {
    sockname = process.env.WATCHMAN_SOCK;
  } else {
    // TODO add some error handling
    const {stdout} = await exec('watchman --no-pretty get-sockname');
    ({sockname} = JSON.parse(stdout));
  }

  return new Promise((resolve, reject) => {
    if (sockname === undefined) {
      throw new Error('Failed to find watchman socket path');
    }

    const socket = net.createConnection(sockname);

    socket.on('error', err => {
      reject(err);
    });

    socket.on('connect', () => {
      resolve(new WatchmanClient(socket, reporter));
    });
  });
}
