import {Scope} from '../../scopes';
import {ClassScope} from '../../scopes';
import * as n from '@romejs/js-ast';
import InstanceT from '../../types/InstanceT';
import ClassT from '../../types/ClassT';
import T from '../../types/T';
import OpenT from '../../types/OpenT';

export function ClassExpression(
  node: n.ClassExpressionNode | n.ClassDeclarationNode,
  scope: Scope,
) {
  const instances = [];
  const statics = [];

  //
  const classInstance = new OpenT(scope, node);
  const classId = new OpenT(scope, node);

  //
  const bodyScope = new ClassScope(
    {parentScope: scope},
    {
      instance: classInstance,
      static: classId,
    },
  );
  if (node.id) {
    bodyScope.addBinding(node.id.name, classId);
  }
  if (node.typeParameters) {
    bodyScope.evaluate(node.typeParameters);
  }

  let constructor: undefined | T = undefined;

  for (const bodyNode of node.body) {
    const type = bodyScope.evaluate(bodyNode);
    if (bodyNode.type === 'Unknown') {
      continue;
    }

    if (bodyNode.type === 'ClassMethod' && bodyNode.kind === 'constructor') {
      constructor = type;
    } else {
      if (bodyNode.type !== 'TSIndexSignature' && bodyNode.static === true) {
        statics.push(type);
      } else {
        instances.push(type);
      }
    }
  }

  //
  const classOrigin = node.id ? node.id : node;
  let type = new ClassT(scope, classOrigin, {
    constructor,
    instances,
    statics,
    extends: node.superClass ? scope.evaluate(node.superClass) : undefined,
  });
  if (node.id) {
    type.setHuman(node.id.name);
  }

  //
  classId.shouldMatch(type);

  //
  const instance = new InstanceT(scope, classOrigin, type, []);
  classInstance.shouldMatch(instance);

  return type;
}
