import {TransformExitResult} from '@romejs/js-compiler';
import {Node} from '@romejs/js-ast';
import invariant from '@romejs/invariant';

export default function assertSingleNode(result: TransformExitResult): Node {
  if (Array.isArray(result)) {
    invariant(
      result.length === 1,
      `Expected node list length of 1 but got ${result.length}`,
    );
    return result[0];
  } else if (result === undefined) {
    invariant(false, 'Expected node or node list but got null');
  } else {
    return result;
  }
}
