import {SourceMap} from '@romejs/codec-source-map';
import {V8Error, ErrorFrame} from '@romejs/v8';
import {SourceMapConsumer} from '@romejs/codec-source-map';
import {errors} from '@romejs/v8';

type ResolvedLocation = {
  found: boolean;
  filename: string;
  line: number;
  column: number;
  name: undefined | string;
};

let inited: boolean = false;
const maps: Map<string, SourceMapConsumer> = new Map();

function prepareStackTrace(err: Error, frames: Array<NodeJS.CallSite>) {
  try {
    const v8Err = errors.toV8Error(err);
    addErrorFrames(v8Err, frames);
    return buildStackString(v8Err);
  } catch (err2) {
    return `${err.name}: ${err.message}\n  Failed to generate stacktrace: ${
      err2.message
    }`;
  }
}

export function init() {
  if (!inited) {
    inited = true;
    Error.prepareStackTrace = prepareStackTrace;
  }
}

export function teardown() {
  // $FlowFixMe
  Error.prepareStackTrace = undefined;
}

function buildStackString(err: Error): string {
  const frames = errors.getFrames(err);
  const lines: Array<string> = [];

  lines.push(`${err.name}: ${err.message}`);

  for (const frame of frames) {
    const {
      resolvedLocation,
      methodName,
      functionName,
      typeName,
      isNative,
      isAsync,
      isEval,
      isConstructor,
      filename,
      lineNumber,
      columnNumber,
    } = frame;
    const parts: Array<string> = [];

    if (isAsync) {
      parts.push('await');
    }

    if (isEval) {
      parts.push('eval');
    }

    if (isConstructor) {
      parts.push('new');
    }

    let name = '<anonymous';
    if (functionName !== undefined) {
      name = functionName;
    }
    if (methodName !== undefined) {
      name = methodName;
    }
    if (typeName !== undefined) {
      parts.push(`${typeName}.${name}`);
    } else {
      parts.push(name);
    }

    if (isNative) {
      parts.push('native');
    } else if (
      filename !== undefined &&
      lineNumber !== undefined &&
      columnNumber !== undefined
    ) {
      parts.push(`(${filename}:${lineNumber}:${columnNumber})`);
    }

    if (resolvedLocation === false) {
      parts.push('generated source location');
    }

    lines.push('  at ' + parts.join(' '));
  }

  return lines.join('\n');
}

function noNull<T>(val: null | T): undefined | T {
  if (val === null) {
    return undefined;
  } else {
    return val;
  }
}

function addErrorFrames(err: V8Error, frames: Array<NodeJS.CallSite>) {
  if (err.__ROME_ERROR_FRAMES__) {
    return undefined;
  }

  let builtFrames = frames.map(
    (frameApi): ErrorFrame => {
      const filename = frameApi.getFileName();
      const lineNumber = frameApi.getLineNumber();
      const columnNumber = frameApi.getColumnNumber();

      const frame: ErrorFrame = {
        typeName: noNull(frameApi.getTypeName()),
        functionName: noNull(frameApi.getFunctionName()),
        methodName: noNull(frameApi.getMethodName()),

        isToplevel: frameApi.isToplevel(),
        isEval: frameApi.isEval(),
        isNative: frameApi.isNative(),
        isConstructor: frameApi.isConstructor(),

        // TODO frameApi.isAsync
        isAsync: false,

        resolvedLocation: true,

        filename: noNull(filename),
        lineNumber: noNull(lineNumber),

        // Rome expects 0-indexed columns, V8 provides 1-indexed
        columnNumber: columnNumber == null ? undefined : columnNumber - 1,
      };

      if (
        frame.filename !== undefined &&
        frame.lineNumber !== undefined &&
        frame.columnNumber !== undefined
      ) {
        const {found, line, column, filename, name} = resolveLocation(
          frame.filename,
          frame.lineNumber,
          frame.columnNumber,
        );

        return {
          ...frame,
          functionName:
            frame.functionName === undefined ? name : frame.functionName,
          methodName: frame.methodName === undefined ? name : frame.methodName,
          resolvedLocation: found,
          lineNumber: line,
          columnNumber: column,
          filename,
        };
      } else {
        return frame;
      }
    },
  );

  // This is a property that an error object can define that will remove that amount of frames
  // This is useful for removing levels of indirection, for example, an invariant error
  if (err.__ROME_POP_FRAMES__ !== undefined) {
    builtFrames = builtFrames.slice(err.__ROME_POP_FRAMES__);
  }

  err.__ROME_ERROR_FRAMES__ = builtFrames;
}

export function resolveLocation(
  filename: string,
  line: number,
  column: number,
): ResolvedLocation {
  const map = maps.get(filename);
  if (map === undefined) {
    return {
      found: true,
      filename,
      line,
      column,
      name: undefined,
    };
  }

  const resolved = map.approxOriginalPositionFor(line, column);
  if (resolved === undefined) {
    return {
      found: false,
      filename,
      line,
      column,
      name: undefined,
    };
  }

  return {
    found: true,
    filename: resolved.source,
    line: resolved.line,
    column: resolved.column,
    name: resolved.name,
  };
}

export function addSourceMap(filename: string, map: SourceMap) {
  init();
  maps.set(filename, new SourceMapConsumer(map));
}

export function getSourceMap(filename: string): undefined | SourceMapConsumer {
  return maps.get(filename);
}
