import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {isFor} from '@romejs/js-ast-utils';

export function VariableDeclaration(
  generator: Generator,
  node: t.VariableDeclarationNode,
  parent: t.Node,
) {
  generator.word(node.kind);
  generator.space();

  let hasInits = false;

  // don't add whitespace to loop heads
  if (!isFor(parent)) {
    for (const declar of node.declarations) {
      if (declar.init) {
        // has an init so let's split it up over multiple lines
        hasInits = true;
      }
    }
  }

  //
  // use a pretty separator when we aren't in compact mode, have initializers and don't have retainLines on
  // this will format declarations like:
  //
  //   let foo = "bar", bar = "foo";
  //
  // into
  //
  //   let foo = "bar",
  //       bar = "foo";
  //

  let separator = variableDeclarationNormal;
  if (hasInits) {
    separator =
      node.kind === 'const'
        ? constDeclarationIndent
        : variableDeclarationIndent;
  }

  //

  generator.printJoin(node.declarations, node, {separator});

  if (isFor(parent)) {
    // don't give semicolons to these nodes since they'll be inserted in the parent generator
    if (parent.left === node || parent.init === node) {
      return undefined;
    }
  }

  generator.semicolon();
}

function variableDeclarationNormal(generator: Generator) {
  generator.token(',');
  generator.space();
}

function variableDeclarationIndent(generator: Generator) {
  // "let " or "var " indentation.
  generator.token(',');
  generator.newline();
  if (generator.endsWith('\n')) {
    for (let i = 0; i < 4; i++) {
      generator.space();
    }
  }
}

function constDeclarationIndent(generator: Generator) {
  // "const " indentation.
  generator.token(',');
  generator.newline();
  if (generator.endsWith('\n')) {
    for (let i = 0; i < 6; i++) {
      generator.space();
    }
  }
}
