import {Node} from '@romejs/js-ast';
import {Scope} from '../scopes';
import T from './T';

export default class BlockT extends T {
  constructor(scope: Scope, originNode: undefined | Node, body: Array<T>) {
    super(scope, originNode);
    this.body = body;
  }

  static type = 'BlockT';

  body: Array<T>;

  reduce(): T {
    const body = [];
    let changed = false;

    for (const type of this.body) {
      const reduced = this.utils.reduce(type);
      body.push(reduced);
      if (reduced !== type) {
        changed = true;
      }
    }

    if (changed) {
      return new BlockT(this.scope, this.originNode, body);
    } else {
      return this;
    }
  }

  humanize(): string {
    return '{}';
  }
}
