import Scope from '../Scope';
import * as t from '@romejs/js-ast';
import FlowTypeAlias from './FlowTypeAlias';

export default function TSTypeAliasDeclaration(
  node: t.TSTypeAliasDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  return FlowTypeAlias(node, parent, scope);
}
