import {MasterRequest, Project} from '@romejs/core';
import {FlagParser} from '@romejs/cli-flags';
import {SourceMap} from '@romejs/codec-source-map';
import Bundler from '../bundler/Bundler';
import {commandCategories, createMasterCommand} from '../../commands';

// This will be dispatched to the client where it has a special case for `executeCode`
type RunResult = {
  type: 'executeCode';
  filename: string;
  code: string;
  map: SourceMap;
};

export default createMasterCommand({
  category: commandCategories.PROJECT_MANAGEMENT,
  description: 'TODO',

  parseCLIFlags(p: FlagParser) {
    p.allowUnknownArguments();
    return undefined;
  },

  async default(req: MasterRequest): Promise<RunResult> {
    const {master, args, flags} = req;

    if (args.length !== 1) {
      throw new Error('Expected one argument');
    }

    async function executeCode(loc: string): Promise<RunResult> {
      const bundler = Bundler.createFromMasterRequest(req);
      const {content, map} = await bundler.bundle(loc);
      return {
        type: 'executeCode',
        filename: loc,
        code: content,
        map,
      };
    }

    // Get the current project
    const project:
      | undefined
      | Project = await master.projectManager.findProject(flags.cwd);

    // check for absolute paths
    const target = args[0];
    const resolved = await master.resolver.resolveEntry({
      ...req.getResolverOptionsFromFlags(),
      origin: flags.cwd,
      source: target,
    });
    if (resolved !== undefined) {
      return executeCode(resolved);
    }

    // check for bin files in any manifests that belong to any projects
    if (project !== undefined) {
      for (const {manifest, folder} of project.packages.values()) {
        const relative = manifest.bin.get(target);
        if (relative === undefined) {
          continue;
        }

        const resolved = await master.resolver.resolveEntry({
          ...req.getResolverOptionsFromFlags(),
          origin: folder,
          platform: 'node',
          source: relative,
        });
        if (resolved === undefined) {
          throw new Error(
            `Failed to resolve relative path in found bin from '${folder} to ${relative}`,
          );
        }

        return executeCode(resolved);
      }
    }

    // TODO check node_modules/.bin

    // TODO check package.json scripts

    throw new Error(`Failed to find "${target}"`);
  },
});
