import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowObjectTypeCallProperty(
  generator: Generator,
  node: t.FlowObjectTypeCallPropertyNode,
) {
  if (node.static === true) {
    generator.word('static');
    generator.space();
  }
  generator.print(node.value, node);
}
