import {
  Context,
  TransformExitResult,
  PathOptions,
  TransformVisitors,
} from '@romejs/js-compiler';
import {HookConsume, HookRef} from '../api/createHook';
import {Node} from '@romejs/js-ast';
import {visitorKeys as allVisitorKeys} from '@romejs/js-ast';
import {isNodeLike} from '@romejs/js-ast-utils';
import {Path, REDUCE_SKIP_SUBTREE} from '@romejs/js-compiler';
import invariant from '@romejs/invariant';
import {HOOK, CONSUMER} from '../api/createHook';

const BAIL_EXIT: 'BAIL' = 'BAIL';
const KEEP_EXIT: 'KEEP' = 'KEEP';

/**
 * Check if a transform return value could be a hook
 */
function isHookLike(ret: TransformExitResult) {
  return ret != null && !Array.isArray(ret) && typeof ret !== 'string';
}

/**
 * Validate the return value of an enter or exit transform
 */
function validateTransformReturn(
  name: string,
  node: unknown,
  opts: PathOptions,
) {
  // If this function hits a string then it's invalid as we would have dealt with it before if it were a valid constant
  if (typeof node === 'string') {
    throw new Error(
      `Returned a string from transform ${name} that doesn't correspond to any reduce constant`,
    );
  }

  // Verify common mistake of forgetting to return something
  if (typeof node === 'undefined') {
    throw new Error(
      'Returned `undefined` from transform ' +
        name +
        '. If you meant to delete this node then use `return' +
        ' null`, otherwise if you want to keep it then use `return path.node;`',
    );
  }

  // Handle returning an array of nodes
  if (Array.isArray(node)) {
    // keyed nodes cannot be replaced with an array of nodes
    if (opts.noArrays === true) {
      throw new Error(
        `Cannot replace this keyed node with an array of nodes - originated from 'transform ${name}`,
      );
    }
    return undefined;
  }

  // Verify that it's a valid node
  if (isNodeLike(node) === false && node !== null) {
    throw new Error(
      `Expected a return value of \`null\` or a plain object with a \`type\` property - originated from 'transform ${name}`,
    );
  }
}

/**
 * Given a return value from 'a transform, determine if we should bail out.
 * Bailing out means returning the actual node and making the parent reduce
 * call handle it (if any).
 */
function shouldBailReduce(node: unknown): boolean {
  if (Array.isArray(node)) {
    // We just return the array of nodes, without transforming them
    // reduce() calls higher in the chain will splice this array and do it's
    // own transform call so when the transform is performed on the node it's
    // in it's correct place in the tree
    return true;
  }

  // This node is being removed, no point recursing into it
  if (node === null) {
    return true;
  }

  return false;
}

/**
 * Take a hook consume ref, get all the hooks, run them, and return
 * the first transformation.
 */
function consumeHook(hookConsume: HookConsume, path: Path) {
  const hookRefs = path.findHooks(hookConsume.id);
  const oldNode = path.node;

  if (hookRefs.length === 0) {
    throw new Error('No requested hooks found');
  }

  for (const hookRef of hookRefs) {
    const {node: newNode, state} = hookRef.hook.consume(
      path,
      hookRef.state,
      hookConsume.arg,
    );

    // Usually we do something with the return value of validate but consumeHook
    // will end up being validated again, but it wont throw that time
    // We do it here to get the correct transform name
    validateTransformReturn(hookRef.name, newNode, path.opts);

    // find some nicer way to do this than mutation
    // although maybe it's ok for performance?
    hookRef.state = state;

    // return the new node
    if (newNode !== oldNode) {
      return newNode;
    }
  }

  return oldNode;
}

/**
 * Run an exit handler. We will return a tuple marking whether we should bail
 * with the returned value.
 */
function runExit<State>(
  path: Path,
  name: string,
  callback: (node: Node, state: State) => TransformExitResult,
  state: State,
): [typeof BAIL_EXIT, Node] | [typeof KEEP_EXIT, Path] {
  // call transformer
  let transformedNode = callback(path, state);

  // allow consumers
  if (isHookLike(transformedNode)) {
    // exit handlers can't return a hook
    if (transformedNode.type === HOOK) {
      throw new Error(
        `Transform ${name} returned a hook. We have finished reducing the subtree.`,
      );
    }

    // handle hook consumers
    if (transformedNode.type === CONSUMER) {
      transformedNode = consumeHook(transformedNode, path);
    }
  }

  // validate the node
  validateTransformReturn(name, transformedNode, path.opts);

  // check if we need to bail out
  if (shouldBailReduce(transformedNode)) {
    return [BAIL_EXIT, transformedNode];
  }

  // create new path if node has been changed
  if (transformedNode !== path.node) {
    path = path.fork(transformedNode);
  }

  return [KEEP_EXIT, path];
}

export default function reduce(
  origNode: Node,
  visitors: TransformVisitors,
  context: Context,
  pathOpts: PathOptions = {},
): TransformExitResult {
  let hooks: Array<HookRef> = [];

  // Initialize first path
  let path: Path = new Path(origNode, context, pathOpts, hooks);

  // Perform enter transforms
  for (const visitor of visitors) {
    const {enter} = visitor;
    if (enter === undefined) {
      continue;
    }

    // Call transformer
    let transformedNode = enter(path);

    // When returning this symbol, it indicates we should skip the subtree
    if (transformedNode === REDUCE_SKIP_SUBTREE) {
      return origNode;
    }

    // Handle hooks
    if (isHookLike(transformedNode)) {
      // Handle consumers
      if (transformedNode.type === CONSUMER) {
        transformedNode = consumeHook(transformedNode, path);
      }

      // Handle hooks
      if (isHookLike(transformedNode) && transformedNode.type === HOOK) {
        // Set this path to the handle
        hooks.push(transformedNode);

        // Set the node to itself, we will check if the path has a handle at the end
        transformedNode = path.node;
      }
    }

    // Validate the return value
    validateTransformReturn(visitor.name, transformedNode, path.opts);

    // Check if we need to bail out. See the comment for shouldBailReduce on what that means
    if (shouldBailReduce(transformedNode)) {
      return transformedNode;
    }

    // Create new path if node has been changed
    if (transformedNode !== path.node) {
      path = path.fork(transformedNode);
    }
  }

  // Reduce the children
  let {node} = path;
  const visitorKeys = allVisitorKeys.get(node.type);
  if (visitorKeys !== undefined) {
    // Build the ancestry paths that we'll pass to each child path
    const ancestryPaths = pathOpts.ancestryPaths || [];
    let childAncestryPaths: Array<Path> = [path].concat(ancestryPaths);

    // Reduce the children
    for (const key of visitorKeys) {
      const oldVal = node[key];

      if (Array.isArray(oldVal)) {
        let children: Array<Node> = oldVal;

        // When removing items from the children array, we decrement this offset and subtract it
        // whenever looking up to get the correct position
        let childrenOffset = 0;

        // This needs to be calculated beforehand as the length of the array may change when removing
        // items
        let length = children.length;

        for (let i = 0; i < length; i++) {
          // Calculate the correct index that this children can be found at
          const correctedIndex = childrenOffset + i;

          // Get the child
          const child = children[correctedIndex];

          // An array may be mixed containg [null, Node] etc so check that it's actually a valid node
          if (isNodeLike(child)) {
            // Run transforms on this node
            const newChild = reduce(child, visitors, context, {
              noScopeCreation: pathOpts.noScopeCreation,
              parentScope: path.scope,
              ancestryPaths: childAncestryPaths,
              listKey: correctedIndex,
              nodeKey: key,
            });

            // If this item has been changed then...
            if (newChild !== child) {
              // Clone the children array
              children = children.slice();

              // Check if the item is to be deleted
              // `null` or an empty array are considered equivalent
              if (
                newChild === null ||
                (Array.isArray(newChild) && newChild.length === 0)
              ) {
                // Remove the item from the array
                children.splice(correctedIndex, 1);

                // Since the array now has one less item, change the offset so all
                // future indices will be correct
                childrenOffset--;
              } else if (Array.isArray(newChild)) {
                // Remove the previous, and add the new items to the array
                children.splice(correctedIndex, 1, ...newChild);

                // We increase the length of the array so that this loop covers
                // the newly inserted nodes
                // `childrenOffset` is not used here because that's just used to
                // skip elements
                length += newChild.length;

                // Revisit the current index, this is necessary as there's now a
                // new node at this position
                i--;
              } else {
                // Otherwise it's a valid node so set it
                children[correctedIndex] = newChild;

                // Revisit the current index, the node has changed and some
                // transforms may care about it
                i--;
              }

              // Mutate the original node - funky typing since Flow doesn't understand the mutation
              node = {...node, [key]: children} as Node;

              // Create a new node path
              path = path.fork(node);

              // And create a new ancestry array for subsequent children
              childAncestryPaths = [path].concat(ancestryPaths);
            }
          }
        }
      } else if (isNodeLike(oldVal)) {
        // Run transforms on this node
        let newVal = reduce(oldVal, visitors, context, {
          noScopeCreation: pathOpts.noScopeCreation,
          parentScope: path.scope,
          ancestryPaths: childAncestryPaths,
          noArrays: true,
          nodeKey: key,
        });

        // If this value has been changed then...
        if (newVal !== oldVal) {
          // When replacing a key value, we cannot replace it with an array
          invariant(
            Array.isArray(newVal) === false,
            'should not be an array as noArrays: true was passed to reduce()',
          );

          // If the node is deleted (as indicated by a value of `null`) then use `void` instead
          if (newVal === null) {
            newVal = undefined;
          }

          // Mutate the original object - funky typing since Flow doesn't understand the mutation
          node = {...node, [key]: newVal} as Node;

          // Create a new node path for it
          path = path.fork(node);

          // And create a new ancestry array for subsequent children
          childAncestryPaths = [path].concat(ancestryPaths);
        }
      } else {
        // not sure what this is...
        continue;
      }
    }
  }

  // Run all exit hooks
  for (const ref of hooks) {
    const {exit} = ref.hook;
    if (exit === undefined) {
      // A hook exit method is optional
      continue;
    }

    const res = runExit(path, ref.name, exit, ref.state);
    if (res[0] === BAIL_EXIT) {
      return res[1];
    } else {
      path = res[1];
    }
  }

  // Run exit transforms
  for (const visitor of visitors) {
    if (visitor.exit !== undefined) {
      const res = runExit(path, visitor.name, visitor.exit, undefined);
      if (res[0] === BAIL_EXIT) {
        return res[1];
      } else {
        path = res[1];
      }
    }
  }

  return path.node;
}
