import {JSNodeBase, PropertyNameBase, TSTypeAnnotationNode} from '../index';
import {createBuilder} from '../utils';

export type TSPropertySignatureNode = JSNodeBase &
  PropertyNameBase & {
    type: 'TSPropertySignature';
    optional?: undefined | boolean;
    readonly?: undefined | boolean;
    typeAnnotation?: undefined | TSTypeAnnotationNode;
  };

export const tsPropertySignature = createBuilder<TSPropertySignatureNode>(
  'TSPropertySignature',
  {
    visitorKeys: ['key', 'variance', 'typeAnnotation'],
  },
);
