import {MasterRequest} from '@romejs/core';
import {createMasterCommand} from '../../commands';
import {commandCategories} from '../../commands';

export default createMasterCommand({
  description: 'evict a file from the memory cache',
  category: commandCategories.INTERNAL,

  async default({master, args, reporter}: MasterRequest): Promise<void> {
    // TODO support relative and glob patterns
    const files =
      args.length === 0 ? master.fileManager.getAllOwnedFilenames() : args;

    for (const file of files) {
      await master.fileManager.evict(file);
      reporter.success(`Evicted ${file}`);
    }

    reporter.info(`Evicted ${String(files.length)} files`);
  },
});
