import Scope from '../Scope';
import * as t from '@romejs/js-ast';

export default function BlockStatement(
  node: t.BlockStatementNode,
  parent: t.Node,
  scope: Scope,
) {
  const newScope = scope.fork('block', node);
  for (const child of node.body) {
    newScope.evaluate(child, node);
  }
  return newScope;
}
