import {
  ModuleKind,
  Node,
  ProgramSyntax,
  ImportSpecifier,
  ExportSpecifier,
} from '@romejs/js-ast';
import {SourceLocation} from '@romejs/parser-core';
import {PartialDiagnostics} from '@romejs/diagnostics';
import {Path} from '@romejs/js-compiler';
import {CompileRequest} from '../types';
import {
  Context,
  Cache,
  Record,
  FunctionBinding,
  ImportBinding,
  TypeBinding,
} from '@romejs/js-compiler';
import {
  isFunctionNode,
  getBindingIdentifiers,
  doesNodeMatchPattern,
  isReferenced,
  isTypeNode,
} from '@romejs/js-ast-utils';
import transform from '../methods/transform';

const analyzeCache: Cache<AnalyzeDependencyResult> = new Cache();

export type AnalyzeModuleKind = 'type' | 'value';

export type AnalyzeModuleType = 'es' | 'cjs' | 'unknown';

export type AnalyzeDependencyName = {
  name: string;
  kind: AnalyzeModuleKind;
  loc: undefined | SourceLocation;
};

export type AnalyzeExport =
  | {
      type: 'local';
      loc: undefined | SourceLocation;
      kind: AnalyzeModuleKind;
      hoisted: boolean;
      name: string;
    }
  | {
      type: 'external';
      kind: AnalyzeModuleKind;
      loc: undefined | SourceLocation;
      imported: string;
      exported: string;
      source: string;
    }
  | {
      type: 'externalAll';
      loc: undefined | SourceLocation;
      kind: AnalyzeModuleKind;
      source: string;
    };

export type AnalyzeDependency = {
  names: Array<AnalyzeDependencyName>;
  async: boolean;
  kind: AnalyzeModuleKind;
  type: AnalyzeModuleType;
  loc: undefined | SourceLocation;
  all: boolean;
  optional: boolean;
  source: string;
};

export type AnalyzeDependencyImportUsageItem = {
  imported: string;
  local: string;
  source: string;
  loc: undefined | SourceLocation;
  kind: AnalyzeModuleKind;
};

export type AnalyzeDependencyImportFirstUsage = Array<
  AnalyzeDependencyImportUsageItem
>;

export type AnalyzeDependencyResult = {
  type: AnalyzeModuleType;
  syntax: Array<ProgramSyntax>;
  diagnostics: PartialDiagnostics;
  firstTopAwaitLocation: undefined | SourceLocation;
  importFirstUsage: AnalyzeDependencyImportFirstUsage;
  exports: Array<AnalyzeExport>;
  dependencies: Array<AnalyzeDependency>;
};

class ImportRecord extends Record {
  constructor(data: AnalyzeDependency) {
    super();
    this.data = data;
  }

  data: AnalyzeDependency;
}

class ExportRecord extends Record {
  constructor(data: AnalyzeExport) {
    super();
    this.data = data;
  }

  data: AnalyzeExport;
}

class CJSExportRecord extends Record {
  constructor(node: Node) {
    super();
    this.node = node;
  }

  node: Node;
}

class ESExportRecord extends Record {
  constructor(kind: AnalyzeModuleKind, node: Node) {
    super();
    this.node = node;
    this.kind = kind;
  }

  node: Node;
  kind: AnalyzeModuleKind;
}

class TopLevelAwaitRecord extends Record {
  constructor(loc: SourceLocation) {
    super();
    this.loc = loc;
  }

  loc: SourceLocation;
}

class ImportUsageRecord extends Record {
  constructor(inFunction: boolean, data: AnalyzeDependencyImportUsageItem) {
    super();
    this.inFunction = inFunction;
    this.data = data;
  }

  inFunction: boolean;
  data: AnalyzeDependencyImportUsageItem;
}

function isOptional(path: Path): boolean {
  for (const {node} of path.ancestryPaths) {
    if (node.type === 'TryStatement') {
      return true;
    }
  }

  return false;
}

function isTypeKind(kind: undefined | ModuleKind): boolean {
  return kind === 'type' || kind === 'typeof';
}

function getKind(kind: undefined | ModuleKind): AnalyzeModuleKind {
  return isTypeKind(kind) ? 'type' : 'value';
}

function getKindWithSpecifiers(
  rawKind: undefined | ModuleKind,
  specifierKinds: Array<AnalyzeModuleKind>,
): AnalyzeModuleKind {
  const kind: AnalyzeModuleKind = getKind(rawKind);
  if (isTypeKind(kind) || specifierKinds.length === 0) {
    return kind;
  }

  for (const specifierKind of specifierKinds) {
    if (specifierKind === 'value') {
      return 'value';
    }
  }
  return 'type';
}

const analyzeVisitor = function(path: Path) {
  const {node, scope, context} = path;

  // Handle require()
  if (node.type === 'CallExpression') {
    const {callee, arguments: args} = node;

    const isRequire: boolean =
      callee.type === 'Identifier' &&
      callee.name === 'require' &&
      path.scope.hasBinding('require') === false;
    const sourceArg = args[0];

    if (isRequire && args.length === 1 && sourceArg.type === 'StringLiteral') {
      context.record(
        new ImportRecord({
          type: 'cjs',
          kind: 'value',
          optional: isOptional(path),
          loc: node.loc,
          source: sourceArg.value,
          names: [],
          all: true,
          async: false,
        }),
      );
    }
  }

  // import('./bar');
  if (node.type === 'CallExpression' && node.callee.type === 'Import') {
    const args = node.arguments;
    const sourceArg = args[0];
    if (args.length === 1 && sourceArg.type === 'StringLiteral') {
      context.record(
        new ImportRecord({
          type: 'es',
          async: true,
          kind: 'value',
          names: [],
          loc: sourceArg.loc,
          source: sourceArg.value,
          optional: isOptional(path),
          all: true,
        }),
      );
    }
  }

  // Local bindings exports:
  // export const foo
  // export function foo() {}
  // export {};
  if (node.type === 'ExportNamedDeclaration' && node.source === undefined) {
    const hoisted =
      node.declaration !== undefined &&
      node.declaration.type === 'FunctionDeclaration';
    for (const {name, loc} of getBindingIdentifiers(node)) {
      context.record(
        new ExportRecord({
          type: 'local',
          hoisted,
          kind: getKind(node.exportKind),
          loc,
          name,
        }),
      );
    }

    if (node.specifiers !== undefined) {
      for (const specifier of node.specifiers) {
        const binding = scope.getBinding(specifier.local.name);

        let kind: AnalyzeModuleKind = getKind(
          specifier.exportKind || node.exportKind,
        );
        if (kind === 'value' && binding instanceof TypeBinding) {
          kind = 'type';
        }

        context.record(
          new ExportRecord({
            type: 'local',
            loc: specifier.loc,
            hoisted: binding instanceof FunctionBinding,
            kind,
            name: specifier.exported.name,
          }),
        );
      }
    }
  }

  // export default
  if (node.type === 'ExportDefaultDeclaration') {
    context.record(
      new ExportRecord({
        type: 'local',
        loc: node.declaration.loc,
        hoisted: node.declaration.type === 'FunctionDeclaration',
        kind: 'value',
        name: 'default',
      }),
    );
  }

  // External binding exports:
  // export {} from '';
  if (node.type === 'ExportNamedDeclaration') {
    const {source} = node;
    if (source !== undefined) {
      const specifiersKinds: Array<AnalyzeModuleKind> = [];
      const exportedNames: Array<AnalyzeDependencyName> = [];

      if (node.specifiers !== undefined) {
        for (const specifier of node.specifiers) {
          const kind = getKind(specifier.exportKind || node.exportKind);
          specifiersKinds.push(kind);

          exportedNames.push({
            name: specifier.local.name,
            kind,
            loc: specifier.loc,
          });

          context.record(
            new ExportRecord({
              type: 'external',
              kind,
              loc: specifier.loc,
              imported: specifier.local.name,
              exported: specifier.exported.name,
              source: source.value,
            }),
          );
        }
      }

      context.record(
        new ImportRecord({
          type: 'es',
          async: false,
          kind: getKindWithSpecifiers(node.exportKind, specifiersKinds),
          names: exportedNames,
          loc: source.loc,
          source: source.value,
          optional: isOptional(path),
          all: false,
        }),
      );
    }
  }

  // import A = require('B');
  if (
    node.type === 'TSImportEqualsDeclaration' &&
    node.moduleReference.type === 'TSExternalModuleReference'
  ) {
    context.record(
      new ImportRecord({
        type: 'cjs',
        kind: 'value',
        optional: isOptional(path),
        loc: node.loc,
        source: node.moduleReference.expression.value,
        names: [],
        all: true,
        async: false,
      }),
    );
  }

  // export * from '';
  if (node.type === 'ExportAllDeclaration') {
    context.record(
      new ImportRecord({
        type: 'es',
        async: false,
        kind: getKind(node.exportKind),
        optional: isOptional(path),
        loc: node.source.loc,
        names: [],
        source: node.source.value,
        all: true,
      }),
    );

    context.record(
      new ExportRecord({
        type: 'externalAll',
        loc: node.loc,
        kind: getKind(node.exportKind),
        source: node.source.value,
      }),
    );
  }

  if (
    node.type === 'ExportAllDeclaration' ||
    node.type === 'ExportDefaultDeclaration' ||
    node.type === 'ExportNamedDeclaration'
  ) {
    context.record(new ESExportRecord(getKind(node.exportKind), node));
  }

  // import {} from '';
  // import * as foo from '';
  if (node.type === 'ImportDeclaration') {
    let hasNamespaceSpecifier = false;
    const specifierKinds: Array<AnalyzeModuleKind> = [];
    const names: Array<AnalyzeDependencyName> = [];

    for (const specifier of node.specifiers) {
      if (specifier.type === 'ImportNamespaceSpecifier') {
        hasNamespaceSpecifier = true;
        break;
      }

      const kind: AnalyzeModuleKind = getKind(
        specifier.importKind || node.importKind,
      );
      specifierKinds.push(kind);

      if (specifier.type === 'ImportDefaultSpecifier') {
        names.push({
          kind,
          loc: specifier.loc,
          name: 'default',
        });
      }

      if (specifier.type === 'ImportSpecifier') {
        names.push({
          kind,
          loc: specifier.loc,
          name: specifier.imported.name,
        });
      }
    }

    context.record(
      new ImportRecord({
        type: 'es',
        async: false,
        kind: getKindWithSpecifiers(node.importKind, specifierKinds),
        loc: node.source.loc,
        optional: isOptional(path),
        source: node.source.value,
        all: hasNamespaceSpecifier,
        names,
      }),
    );
  }

  // module.exports or exports assignment
  if (node.type === 'AssignmentExpression') {
    const isModuleExports =
      path.scope.getBinding('module') === undefined &&
      doesNodeMatchPattern(node.left, 'module.exports', true);
    const isExports =
      path.scope.getBinding('exports') === undefined &&
      doesNodeMatchPattern(node.left, 'exports', true);

    if (isModuleExports || isExports) {
      context.record(new CJSExportRecord(node));
    }
  }

  // Detect top level await
  if (
    node.type === 'AwaitExpression' &&
    path.findAncestry(path => isFunctionNode(path.node)) === undefined
  ) {
    context.record(new TopLevelAwaitRecord(node.loc));
  }

  // Mark references to imports outside of functions
  if (node.type === 'Identifier' && isReferenced(path)) {
    const binding = path.scope.getBinding(node.name);
    if (binding !== undefined && binding instanceof ImportBinding) {
      const {meta} = binding;

      // We can skip this if it's referencing a namespace
      if (meta.type !== 'name') {
        return node;
      }

      const parentFunction = path.findAncestry(path =>
        isFunctionNode(path.node),
      );

      let kind: AnalyzeModuleKind = getKind(meta.kind);

      // We can reliably detect if we're in a type annotation by the two parent nodes
      // Inside of a type annotation AST it's all custom nodes
      // We need to test 2 levels high due to: (parser: Parser) => void
      // `Parser` here will be a property of the identifier param
      // We could recurse up all the parents but it's slow and wasteful
      if (
        isTypeNode(path.parent) ||
        (path.parentPath !== undefined && isTypeNode(path.parentPath.parent))
      ) {
        kind = 'type';
      }

      context.record(
        new ImportUsageRecord(parentFunction !== undefined, {
          kind,
          loc: node.loc,
          local: node.name,
          imported: meta.imported,
          source: meta.source,
        }),
      );
    }
  }

  return node;
};

export default async function analyzeDependencies(
  req: CompileRequest,
): Promise<AnalyzeDependencyResult> {
  const {ast, projectConfig} = req;

  const query = Cache.buildQuery(req);
  const cached: undefined | AnalyzeDependencyResult = analyzeCache.get(query);
  if (cached) {
    return cached;
  }

  const context = new Context({
    ast,
    projectConfig,
    origin: 'Analyzing dependencies',
  });
  const {ast: transformedAst} = await transform({...req, stage: 'pre'});
  context.reduce(transformedAst, [
    {name: 'analyzeDependencies', enter: analyzeVisitor},
  ]);

  //
  const importFirstUsage: AnalyzeDependencyImportFirstUsage = [];
  const seenImportFirstUsage: Set<string> = new Set();

  // Extract records
  const exports: Array<AnalyzeExport> = [];
  const dependenciesBySource: Map<string, AnalyzeDependency> = new Map();

  const esValueExports: Array<Node> = [];
  const cjsExports: Array<Node> = [];
  let firstTopAwaitLocation: undefined | SourceLocation;

  // Find the import sources that are only used as a type
  const sourcesUsedAsType: Set<string> = new Set();
  const sourcesUsedAsValue: Set<string> = new Set();
  for (const record of context.records) {
    if (record instanceof ExportRecord || record instanceof ImportUsageRecord) {
      if (record instanceof ExportRecord && record.data.type === 'local') {
        continue;
      }

      const {kind, source} = record.data;
      if (kind === 'type') {
        sourcesUsedAsType.add(source);
      } else {
        sourcesUsedAsValue.add(source);
      }
    }
  }
  for (const source of sourcesUsedAsValue) {
    sourcesUsedAsType.delete(source);
  }

  // Process rest of the records
  for (const record of context.records) {
    if (record instanceof ImportRecord) {
      let {data} = record;

      // If this source was only ever used as a type then convert us to a value
      if (data.kind === 'value' && sourcesUsedAsType.has(data.source)) {
        const names: Array<AnalyzeDependencyName> = [];

        for (const name of names) {
          names.push({
            ...name,
            kind: 'type',
          });
        }

        data = {...data, kind: 'type', names};
      }

      // If we have multiple import records for this file, then merge them together
      const existing = dependenciesBySource.get(data.source);
      if (existing === undefined) {
        dependenciesBySource.set(data.source, data);
      } else {
        let kind: AnalyzeModuleKind;
        if (data.kind === existing.kind) {
          kind = data.kind;
        } else {
          kind = 'value';
        }

        const combinedRecord: AnalyzeDependency = {
          type: data.type === 'es' && existing.type === 'es' ? 'es' : 'cjs',
          kind,
          optional: existing.optional && data.optional,
          async: existing.async || data.async,
          source: data.source,
          all: existing.all || data.all,
          names: [...existing.names, ...data.names],
          loc: existing.loc || data.loc,
        };

        // Map ordering is by insertion time, so in the case where the previous import was a type import
        // then we don't want to place our combined record in that position, it should be at the end.
        // Inserting a type import statement at the top of the file shouldn't change the execution order
        // if it was imported later
        if (existing.kind === 'type' && data.kind === 'value') {
          dependenciesBySource.delete(data.source);
        }

        dependenciesBySource.set(data.source, combinedRecord);
      }
    } else if (record instanceof ExportRecord) {
      exports.push(record.data);
    } else if (record instanceof CJSExportRecord) {
      cjsExports.push(record.node);
    } else if (record instanceof ESExportRecord) {
      if (record.kind === 'value') {
        esValueExports.push(record.node);
      }
    } else if (record instanceof TopLevelAwaitRecord) {
      if (firstTopAwaitLocation === undefined) {
        firstTopAwaitLocation = record.loc;
      }
    } else if (
      record instanceof ImportUsageRecord &&
      !record.inFunction &&
      record.data.kind === 'value'
    ) {
      // Track the first reference to a value import that's not in a function
      // This is used to detect module cycles
      const {data} = record;
      const key = `${data.source}:${data.imported}`;
      if (seenImportFirstUsage.has(key)) {
        continue;
      }

      seenImportFirstUsage.add(key);
      importFirstUsage.push(data);
    }
  }

  // Warn against mixing module type exports
  if (cjsExports.length > 0 && esValueExports.length > 0) {
    for (const node of cjsExports) {
      context.addNodeDiagnostic(node, {
        category: 'analyzeDependencies',
        message:
          'You cannot use CommonJS exports in a file that has also has ES module exports',
      });
    }
  }

  // Mark imports as types if they are only ever used in types

  // Build dependencies
  const dependencies: Array<AnalyzeDependency> = Array.from(
    dependenciesBySource.values(),
  );

  // Infer the module type
  let type: AnalyzeModuleType = 'unknown';
  if (cjsExports.length > 0) {
    type = 'cjs';
  } else if (esValueExports.length > 0) {
    type = 'es';
  }

  const res: AnalyzeDependencyResult = {
    type,
    firstTopAwaitLocation,
    exports,
    dependencies,
    importFirstUsage,
    syntax: ast.syntax,
    diagnostics: [...ast.diagnostics, ...context.diagnostics],
  };
  analyzeCache.set(query, res);
  return res;
}
