import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type JSXSpreadAttributeNode = JSNodeBase & {
  type: 'JSXSpreadAttribute',
  argument: ExpressionNode,
};

export const jsxSpreadAttribute = createBuilder<JSXSpreadAttributeNode>(
  'JSXSpreadAttribute',
  {visitorKeys: ['argument']},
);
