import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function ForStatement(generator: Generator, node: t.ForStatementNode) {
  generator.word('for');
  generator.space();
  generator.token('(');

  generator.inForStatementInitCounter++;
  generator.print(node.init, node);
  generator.inForStatementInitCounter--;
  generator.token(';');

  if (node.test) {
    generator.space();
    generator.print(node.test, node);
  }
  generator.token(';');

  if (node.update) {
    generator.space();
    generator.print(node.update, node);
  }

  generator.token(')');
  generator.printBlock(node);
}
