import {
  JSNodeBase,
  TemplateElementNode,
  ExpressionNode,
} from '../index';
import {createBuilder} from '../utils';

export type TemplateLiteralNode = JSNodeBase & {
  type: 'TemplateLiteral',
  quasis: Array<TemplateElementNode>,
  expressions: Array<ExpressionNode>,
};

export const templateLiteral = createBuilder<TemplateLiteralNode>(
  'TemplateLiteral',
  {visitorKeys: ['quasis', 'expressions']},
);
