import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXElement(generator: Generator, node: t.JSXElementNode) {
  const open = node.openingElement;
  generator.print(open, node);
  if (open.selfClosing === true) {
    return undefined;
  }

  generator.indent();
  for (const child of node.children) {
    generator.print(child, node);
  }
  generator.dedent();

  generator.print(node.closingElement, node);
}
