import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowObjectTypeIndexer(
  generator: Generator,
  node: t.FlowObjectTypeIndexerNode,
) {
  if (node.static === true) {
    generator.word('static');
    generator.space();
  }
  generator.print(node.variance, node);
  generator.token('[');
  generator.print(node.id, node);
  generator.token(':');
  generator.space();
  generator.print(node.key, node);
  generator.token(']');
  generator.token(':');
  generator.space();
  generator.print(node.value, node);
}
