import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function WithStatement(generator: Generator, node: t.WithStatementNode) {
  generator.word('with');
  generator.space();
  generator.token('(');
  generator.print(node.object, node);
  generator.token(')');
  generator.printBlock(node);
}
