import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function MetaProperty(generator: Generator, node: t.MetaPropertyNode) {
  generator.print(node.meta, node);
  generator.token('.');
  generator.print(node.property, node);
}
