import {JSNodeBase, JSXIdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type JSXNamespacedNameNode = JSNodeBase & {
  type: 'JSXNamespacedName',
  namespace: JSXIdentifierNode,
  name: JSXIdentifierNode,
};

export const jsxNamespacedName = createBuilder<JSXNamespacedNameNode>(
  'JSXNamespacedName',
  {visitorKeys: ['namespace', 'name']},
);
