import {
  JSNodeBase,
  SuperNode,
  ImportNode,
  ExpressionNode,
  SpreadElementNode,
  TypeArgumentsNode,
} from '../index';
import {createBuilder} from '../utils';

export type CallExpressionNode = JSNodeBase & {
  type: 'CallExpression';
  callee: ExpressionNode | SuperNode | ImportNode;
  arguments: Array<ExpressionNode | SpreadElementNode>;
  typeArguments?: undefined | TypeArgumentsNode;
};

export const callExpression = createBuilder<CallExpressionNode>(
  'CallExpression',
  {
    visitorKeys: ['callee', 'arguments', 'typeArguments'],
  },
);
