import Generator from '../../Generator';
import {escapeString} from '@romejs/string-escape';
import * as t from '@romejs/js-ast';

export function StringLiteral(
  generator: Generator,
  node:
    | t.StringLiteralNode
    | t.DirectiveLiteralNode
    | t.FlowStringLiteralTypeAnnotationNode,
  parent: t.Node,
) {
  const quotes = parent.type === 'JSXAttribute' ? '"' : "'";
  generator.token(
    escapeString(node.value, {
      quote: quotes,
    }),
  );
}
