import {SourceMap, Mapping} from './types';
import invariant from '@romejs/invariant';
import {decodeVLQ} from './base64';

type ParsedMappings = Map<string, Mapping>;

type ResolvedLocation = {
  source: string;
  line: number;
  column: number;
  name: undefined | string;
};

function getCacheKey(line: number, column: number): string {
  return `${String(line)}:${String(column)}`;
}

export default class SourceMapConsumer {
  constructor(map: SourceMap) {
    this.map = map;
    this.mappings = undefined;
  }

  map: SourceMap;
  mappings: undefined | ParsedMappings;

  static charIsMappingSeparator(str: string, index: number): boolean {
    const c = str.charAt(index);
    return c === ';' || c === ',';
  }

  static parseMappings(rawStr: string): ParsedMappings {
    const map: ParsedMappings = new Map();

    let generatedLine = 1;
    let previousGeneratedColumn = 0;
    let previousOriginalLine = 0;
    let previousOriginalColumn = 0;
    let previousSource = '';
    let previousName = '';
    let length = rawStr.length;
    let index: number = 0;
    let cachedSegments: Dict<Array<number>> = {};
    let value;

    while (index < length) {
      const char = rawStr[index];
      if (char === ';') {
        generatedLine++;
        index++;
        previousGeneratedColumn = 0;
      } else if (char === ',') {
        index++;
      } else {
        const mapping: Mapping = {
          generatedLine,
          generatedColumn: 0,
          source: undefined,
          originalLine: 0,
          originalColumn: 0,
          name: undefined,
        };

        // Because each offset is encoded relative to the previous one,
        // many segments often have the same encoding. We can exploit this
        // fact by caching the parsed variable length fields of each segment,
        // allowing us to avoid a second parse if we encounter the same
        // segment again.
        let end = index;
        for (; end < length; end++) {
          if (SourceMapConsumer.charIsMappingSeparator(rawStr, end)) {
            break;
          }
        }
        const str = rawStr.slice(index, end);

        let segment = cachedSegments[str];
        if (segment) {
          index += str.length;
        } else {
          segment = [];
          while (index < end) {
            [value, index] = decodeVLQ(rawStr, index);
            segment.push(value);
          }

          invariant(
            segment.length === 1 ||
              segment.length === 4 ||
              segment.length === 5,
            'Expected 1, 4 or 5 segment parts but got %s',
            segment.length,
          );

          cachedSegments[str] = segment;
        }

        // Generated column
        mapping.generatedColumn = previousGeneratedColumn + segment[0];
        previousGeneratedColumn = mapping.generatedColumn;

        if (segment.length > 1) {
          // Original source
          mapping.source = previousSource + segment[1];
          previousSource += segment[1];

          // Original line
          const newOriginalLine = previousOriginalLine + segment[2];
          previousOriginalLine = newOriginalLine;

          // Lines are stored 0-based
          mapping.originalLine = newOriginalLine + 1;

          // Original column
          const newOriginalColumn = previousOriginalColumn + segment[3];
          mapping.originalColumn = newOriginalColumn;
          previousOriginalColumn = newOriginalColumn;

          if (segment.length > 4) {
            // Original name
            mapping.name = previousName + segment[4];
            previousName += segment[4];
          }
        }

        map.set(
          getCacheKey(mapping.generatedLine, mapping.generatedColumn),
          mapping,
        );
      }
    }

    return map;
  }

  getMappings(): ParsedMappings {
    if (this.mappings === undefined) {
      const mappings = SourceMapConsumer.parseMappings(this.map.mappings);
      this.mappings = mappings;
      return mappings;
    } else {
      return this.mappings;
    }
  }

  approxOriginalPositionFor(
    line: number,
    column: number,
  ): undefined | ResolvedLocation {
    while (column >= 0) {
      const mapping = this.exactOriginalPositionFor(line, column);
      if (mapping === undefined) {
        column--;
        continue;
      } else {
        return mapping;
      }
    }

    return undefined;
  }

  exactOriginalPositionFor(
    line: number,
    column: number,
  ): undefined | ResolvedLocation {
    const key = getCacheKey(line, column);
    const mapping = this.getMappings().get(key);
    if (mapping === undefined) {
      return undefined;
    }

    return {
      // @ts-ignore: i'm too lazy to investigate this
      source: this.map.sources[mapping.source],
      line: mapping.originalLine,
      column: mapping.originalColumn,
      name: mapping.name,
    };
  }
}
