import {JSNodeBase, TSEntityNameNode, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type TSQualifiedNameNode = JSNodeBase & {
  type: 'TSQualifiedName';
  left: TSEntityNameNode;
  right: IdentifierNode;
};

export const tsQualifiedName = createBuilder<TSQualifiedNameNode>(
  'TSQualifiedName',
  {visitorKeys: ['left', 'right']},
);
