import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import UnionT from '../../types/UnionT';

export function FlowUnionTypeAnnotation(
  node: n.FlowUnionTypeAnnotationNode,
  scope: Scope,
) {
  return new UnionT(
    scope,
    node,
    node.types.map(type => {
      return scope.evaluate(type);
    }),
  );
}
