import invariant from '@romejs/invariant';

function compareTwoStrings(a: string, b: string): number {
  let result: undefined | number;

  result = calculateResultIfIdentical(a, b);
  if (result !== undefined) {
    return result;
  }

  result = calculateResultIfEitherIsEmpty(a, b);
  if (result !== undefined) {
    return result;
  }

  result = calculateResultIfBothAreSingleCharacter(a, b);
  if (result !== undefined) {
    return result;
  }

  const pairs1: Array<string> = wordLetterPairs(a.toUpperCase());
  const pairs2: Array<string> = wordLetterPairs(b.toUpperCase());
  const union = pairs1.length + pairs2.length;
  let intersection = 0;

  for (const pair1 of pairs1) {
    for (let i = 0; i < pairs2.length; i++) {
      const pair2 = pairs2[i];
      if (pair1 === pair2) {
        intersection++;
        pairs2.splice(i, 1);
        break;
      }
    }
  }

  return (2.0 * intersection) / union;
}

// Example: given the input string "foobar" returns ["fo", "oo", "ob", "ba", "ar"]
function letterPairs(str: string): Array<string> {
  const numPairs = str.length - 1;
  const pairs: Array<string> = [];
  for (let i = 0; i < numPairs; i++) {
    pairs[i] = str.substring(i, i + 2);
  }
  return pairs;
}

// Break the string into words and get the pairs from 'that
function wordLetterPairs(str: string): Array<string> {
  let pairs: Array<string> = [];

  const words = str.split(' ');
  for (const word of words) {
    pairs = pairs.concat(letterPairs(word));
  }

  return pairs;
}

function calculateResultIfIdentical(a: string, b: string): undefined | number {
  if (a.toUpperCase() == b.toUpperCase()) {
    return 1;
  }

  return undefined;
}

function calculateResultIfBothAreSingleCharacter(
  a: string,
  b: string,
): undefined | number {
  if (a.length == 1 && b.length == 1) {
    return 0;
  }
}

function calculateResultIfEitherIsEmpty(a: string, b: string): undefined | number {
  // if both are empty strings
  if (a.length == 0 && b.length == 0) {
    return 1;
  }

  // if only one is empty string
  if (a.length + b.length > 0 && a.length * b.length == 0) {
    return 0;
  }

  return undefined;
}

export type Rating = {
  target: string,
  rating: number,
};

export type Ratings = Array<Rating>;

export function orderBySimilarity(
  compareStr: string,
  targets: Array<string>,
): Ratings {
  invariant(targets.length > 0, 'At least 1 target string is required');

  // Calculate the rating for each target string
  const ratings: Ratings = targets.map(
    (target: string): Rating => {
      return {
        target,
        rating: compareTwoStrings(compareStr, target),
      };
    },
  );

  // Sort ratings, with the highest at the beginning
  const sortedRatings: Ratings = ratings.sort((a, b) => {
    return b.rating - a.rating;
  });

  return sortedRatings;
}
