import {JSNodeBase, ClassBase} from '../index';
import {createBuilder, CLASS_VISITOR_KEYS} from '../utils';

export type ClassExpressionNode = JSNodeBase &
  ClassBase & {
    type: 'ClassExpression';
  };

export const classExpression = createBuilder<ClassExpressionNode>(
  'ClassExpression',
  {visitorKeys: CLASS_VISITOR_KEYS},
);
