import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function RegExpLiteral(generator: Generator, node: t.RegExpLiteralNode) {
  const flags: Array<string> = [];

  if (node.global === true) {
    flags.push('g');
  }

  if (node.multiline === true) {
    flags.push('m');
  }

  if (node.sticky === true) {
    flags.push('y');
  }

  if (node.insensitive === true) {
    flags.push('i');
  }

  if (node.noDotNewline === true) {
    flags.push('s');
  }

  if (node.unicode === true) {
    flags.push('u');
  }

  generator.word(`/${node.pattern}/${flags.join('')}`);
}
