import {PartialDiagnostic, PartialDiagnostics} from '@romejs/diagnostics';
import {normalizePosition} from './normalize';
import {deriveDiagnosticFromError, mergeDiagnostics} from './derive';

/*import {
  printToString,
  buildMessageCodeFrame,
  toLines,
} from '@romejs/cli-diagnostics';
import {stripAnsi} from '@romejs/string-ansi';*/

// FIXME: SHIMS ONLY UNTIL I CAN FIGURE OUT HOW TO BREAK THE CYCLE
const printToString: any = () => '';
const buildMessageCodeFrame: any = () => '';
const toLines: any = () => [];
const stripAnsi: any = (str: string) => str;

function getCodeFrame(
  message: string,
  diagnostic: PartialDiagnostic,
): undefined | string {
  const {code} = diagnostic;
  if (typeof code !== 'string') {
    return undefined;
  }

  const start = normalizePosition(diagnostic.start);
  const end = normalizePosition(diagnostic.end);
  if (start === undefined || end === undefined) {
    return undefined;
  }

  const marker =
    diagnostic.message === message ? undefined : diagnostic.message;
  const lines = toLines(code);
  const codeFrame = buildMessageCodeFrame(lines, start, end, marker);
  if (codeFrame.trim() !== '') {
    return stripAnsi(codeFrame);
  }

  return undefined;
}

export class DiagnosticError extends Error {
  constructor(message: string, diagnostic: PartialDiagnostic) {
    // If possible, add on a code frame if we were provided a valid `code`, `start` and `end` property
    const codeFrame = getCodeFrame(message, diagnostic);
    if (codeFrame !== undefined) {
      message += '\n' + codeFrame;
    }

    super(message);
    this.name = 'DiagnosticError';

    // If we don't have a filename then this error wont target anywhere, so add a derived error diagnostic
    // to make it easier to track
    if (diagnostic.filename === undefined) {
      diagnostic = mergeDiagnostics(
        process.cwd(),
        deriveDiagnosticFromError({error: this}),
        diagnostic,
      );
    }

    this.diagnostic = diagnostic;
  }

  diagnostic: PartialDiagnostic;
}

export class DiagnosticsError extends Error {
  constructor(message: string, diagnostics: PartialDiagnostics) {
    super(
      message +
        '\n' +
        stripAnsi(
          printToString(diagnostics, {
            origins: ['Wrapped with DiagnosticsError'],
          }),
        ),
    );
    this.diagnostics = diagnostics;
    this.name = 'DiagnosticsError';

    if (diagnostics.length === 0) {
      throw new Error('DiagnosticsError constructed with no diagnostics');
    }
  }

  diagnostics: PartialDiagnostics;
}

export function getDiagnosticsFromError(
  err: Error,
): undefined | PartialDiagnostics {
  if (err instanceof DiagnosticError) {
    return [err.diagnostic];
  }

  if (err instanceof DiagnosticsError) {
    return err.diagnostics;
  }

  return undefined;
}
