import {ReporterOptions} from '@romejs/cli-reporter';
import {AnyEvent} from '@romejs/events';
import {Reporter} from '@romejs/cli-reporter';
import {formatAnsi} from '@romejs/string-ansi';

export default class Logger extends Reporter {
  constructor(name: string, event: AnyEvent, opts: ReporterOptions) {
    super({
      verbose: true,
      ...opts,
    });
    this._loggerName = name;
    this.event = event;
  }

  _loggerName: string;
  event: AnyEvent;

  isEnabled() {
    return this.event.hasSubscribers();
  }

  log(msg: string) {
    if (this.isEnabled()) {
      const prefix = formatAnsi.dim(`[${this._loggerName} ${process.pid}]`);
      super.log(`${prefix} ${msg}`);
    }
  }
}
