import {JSNodeBase, TSTypeNode, TSTypeParameterNode} from '../index';
import {createBuilder} from '../utils';

export type TSMappedTypeBoolean = undefined | boolean | '+' | '-';

export type TSMappedTypeNode = JSNodeBase & {
  type: 'TSMappedType';
  typeParameter: TSTypeParameterNode;
  typeAnnotation: TSTypeNode;
  optional?: TSMappedTypeBoolean;
  readonly?: TSMappedTypeBoolean;
};

export const tsMappedType = createBuilder<TSMappedTypeNode>('TSMappedType', {
  visitorKeys: ['typeParameter', 'typeAnnotation'],
});
