import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function TryStatement(generator: Generator, node: t.TryStatementNode) {
  generator.word('try');
  generator.space();
  generator.print(node.block, node);
  generator.space();
  generator.print(node.handler, node);

  if (node.finalizer) {
    generator.space();
    generator.word('finally');
    generator.space();
    generator.print(node.finalizer, node);
  }
}
