import DependencyGraph from './DependencyGraph';
import {
  AnalyzeDependencyResult,
  AnalyzeModuleType,
  AnalyzeDependency,
} from '@romejs/js-compiler';
import {Project} from '../../common/types/project';
import {DependencyOrder} from './DependencyOrderer';
import DependencyOrderer from './DependencyOrderer';

export type DependencyNodeOpts = {
  filename: string;
  all: boolean;
  async: boolean;
};

export default class DependencyNode {
  constructor(
    graph: DependencyGraph,
    id: string,
    filename: string,
    res: AnalyzeDependencyResult,
  ) {
    this.graph = graph;

    this.project = graph.master.projectManager.assertProjectExisting(filename);
    this.filename = filename;
    this.id = id;
    this.type = res.type;

    this.usedAsync = false;
    this.all = false;
    this.resolvedDependencies = new Map();
    this.dependencies = new Map();

    this.analyze = res;
  }

  analyze: AnalyzeDependencyResult;
  graph: DependencyGraph;
  resolvedDependencies: Map<string, string>;
  dependencies: Map<string, AnalyzeDependency>;
  type: AnalyzeModuleType;
  project: Project;
  filename: string;
  id: string;
  all: boolean;
  usedAsync: boolean;

  setUsedAsync(usedAsync: boolean) {
    this.usedAsync = usedAsync;
  }

  setAll(all: boolean) {
    this.all = all;
  }

  addDependency(relative: string, absolute: string, dep: AnalyzeDependency) {
    this.resolvedDependencies.set(relative, absolute);
    this.dependencies.set(absolute, dep);
  }

  getDependencyInfoFromAbsolute(filename: string): AnalyzeDependency {
    const dep = this.dependencies.get(filename);
    if (dep === undefined) {
      throw new Error('Expected dependency');
    }
    return dep;
  }

  getAbsoluteFromRelativeDependency(relative: string): string {
    const absolute = this.resolvedDependencies.get(relative);
    if (absolute === undefined) {
      throw new Error(`Expected dependency ${relative} in ${this.filename}`);
    }
    return absolute;
  }

  getAbsoluteDependencies(): Array<string> {
    return Array.from(this.resolvedDependencies.values());
  }

  getTransitiveDependencies(): Array<DependencyNode> {
    let queue: Array<DependencyNode> = [this];

    const nodes: Set<DependencyNode> = new Set();

    while (queue.length > 0) {
      const node = queue.shift();
      if (node === undefined) {
        throw new Error('Already validated queue.length');
      }

      nodes.add(node);

      for (const absolute of node.getAbsoluteDependencies()) {
        const node = this.graph.getNode(absolute);

        if (!nodes.has(node)) {
          queue.push(node);
        }
      }
    }

    return Array.from(nodes);
  }

  getDependencyOrder(): DependencyOrder {
    const orderer = new DependencyOrderer(this.graph);
    return orderer.order(this.filename);
  }
}
