import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowInterfaceDeclaration(
  generator: Generator,
  node: t.FlowInterfaceDeclarationNode | t.FlowDeclareInterfaceNode,
) {
  generator.word('interface');
  generator.space();
  _interfaceish(generator, node);
}

export function _interfaceish(
  generator: Generator,
  node:
    | t.FlowDeclareInterfaceNode
    | t.FlowInterfaceDeclarationNode
    | t.FlowDeclareClassNode,
) {
  generator.print(node.id, node);
  generator.print(node.typeParameters, node);

  if (node.extends.length > 0) {
    generator.space();
    generator.word('extends');
    generator.space();
    generator.printCommaList(node.extends, node);
  }

  if (node.mixins.length > 0) {
    generator.space();
    generator.word('mixins');
    generator.space();
    generator.printCommaList(node.mixins, node);
  }

  generator.space();
  generator.print(node.body, node);
}
