import {JSNodeBase, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowTypeofTypeAnnotationNode = JSNodeBase & {
  type: 'FlowTypeofTypeAnnotation',
  argument: FlowNode,
};

export const flowTypeofTypeAnnotation = createBuilder<FlowTypeofTypeAnnotationNode>(
  'FlowTypeofTypeAnnotation',
  {visitorKeys: ['argument']},
);
