import {Scope} from '../scopes';
import {HydrateData} from '../Evaluator';
import T from './T';
import OpenT from './OpenT';

export default class OpenIntrinsicT extends OpenT {
  constructor(scope: Scope, originNode: undefined | Node, name: string) {
    super(scope, originNode);
    this.name = name;
  }

  static type = 'OpenIntrinsicT';

  name: string;

  serialize(): HydrateData {
    return {
      name: this.name,
    };
  }

  static hydrate(scope: Scope, originNode: Node, data: HydrateData): T {
    return scope.intrinsics.get(String(data.name));
  }

  humanize(): string {
    return 'open intrinsic';
  }
}
