import {
  JSNodeBase,
  IdentifierNode,
  FlowTypeParameterDeclarationNode,
  FlowInterfaceExtendsNode,
  FlowObjectTypeAnnotationNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowDeclareInterfaceNode = JSNodeBase & {
  type: 'FlowDeclareInterface',
  id: IdentifierNode,
  typeParameters?: undefined | FlowTypeParameterDeclarationNode,
  extends: Array<FlowInterfaceExtendsNode>,
  implements: Array<FlowInterfaceExtendsNode>,
  mixins: Array<FlowInterfaceExtendsNode>,
  body: FlowObjectTypeAnnotationNode,
};

export const flowDeclareInterface = createBuilder<FlowDeclareInterfaceNode>(
  'FlowDeclareInterface',
  {visitorKeys: ['id', 'typeParameters', 'extends', 'body']},
);
