import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type ReturnStatementNode = JSNodeBase & {
  type: 'ReturnStatement',
  argument?: undefined | ExpressionNode,
};

export const returnStatement = createBuilder<ReturnStatementNode>(
  'ReturnStatement',
  {visitorKeys: ['argument']},
);
