import {Path} from '@romejs/js-compiler';
import {Node} from '@romejs/js-ast';
import {ImportBinding} from '@romejs/js-compiler';
import {
  identifier,
  exportNamedDeclaration,
  exportSpecifier,
  stringLiteral,
} from '@romejs/js-ast';

export default {
  name: 'optimizeExports',
  enter(path: Path): Node {
    const {node} = path;

    // turn `import {a} from 'b'; export {a}`; to `export {a} from 'b';`';
    if (
      node.type === 'ExportNamedDeclaration' &&
      node.exportKind === 'value' &&
      node.source === undefined &&
      node.declaration === undefined
    ) {
      const nodes = [];
      const specifiers = [];

      for (const specifier of node.specifiers) {
        if (specifier.type === 'ExportSpecifier') {
          const binding = path.scope.getBinding(specifier.local.name);
          if (
            binding !== undefined &&
            binding instanceof ImportBinding &&
            binding.meta.type === 'name'
          ) {
            nodes.push(
              exportNamedDeclaration.create({
                specifiers: [
                  exportSpecifier.create({
                    local: identifier.create({name: binding.meta.imported}),
                    exported: specifier.exported,
                  }),
                ],
                source: stringLiteral.create({value: binding.meta.source}),
              }),
            );
          } else {
            specifiers.push(specifier);
          }
        } else {
          // TODO ???
          specifiers.push(specifier);
        }
      }

      if (specifiers.length === node.specifiers.length && nodes.length === 0) {
        return node;
      }

      if (specifiers.length !== 0) {
        nodes.push(exportNamedDeclaration.create({specifiers}));
      }

      return nodes;
    }

    return node;
  },
};
