import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type TSNonNullExpressionNode = JSNodeBase & {
  type: 'TSNonNullExpression';
  expression: ExpressionNode;
};

export const tsNonNullExpression = createBuilder<TSNonNullExpressionNode>(
  'TSNonNullExpression',
  {visitorKeys: ['expression']},
);
