import {APIRequestFlags} from '../common/types/api';
import {
  MasterQueryResponse,
  MasterQueryRequest,
} from '../common/bridges/MasterBridge';
import {LocalCommand} from '../commands';
import API from './API';
import format from '@romejs/pretty-format';
import {consumeMixed} from '@romejs/codec-json';
import {stripAnsi} from '@romejs/string-ansi';
import {VERSION} from '../common/constants';
import {TarWriter} from '@romejs/codec-tar';
import {Reporter} from '@romejs/cli-reporter';
import {BridgeError} from '@romejs/events';
import {Trace, Profiler} from '@romejs/v8';
import {localCommands} from './commands';
import zlib = require('zlib');
import path = require('path');
import util = require('util');
import fs = require('fs');
import os = require('os');

const writeFile = util.promisify(fs.writeFile);

function getTimestamp(): string {
  return new Date().toISOString().replace(/[^0-9a-zA-Z]/g, '');
}

type APIRequestType = 'local' | 'master';

export type APIRequestOpts = {
  command: string;
  flags?: Partial<APIRequestFlags>;
  commandFlags?: unknown;
  args?: Array<string>;
  type?: APIRequestType;
  noData?: boolean;
};

export default class APIRequest {
  constructor(api: API, opts: APIRequestOpts) {
    this.api = api;
    this.reporter = api.reporter;
    this.command = opts.command;
    this.commandFlags =
      opts.commandFlags === undefined ? {} : opts.commandFlags;
    this.flags = {
      ...api.flags,
      ...opts.flags,
    };
    this.args = opts.args === undefined ? [] : opts.args;
    this.type = opts.type === undefined ? 'local' : opts.type;
    this.noData = opts.noData === undefined ? false : opts.noData;
  }

  reporter: Reporter;
  command: string;
  commandFlags: unknown;
  noData: boolean;
  flags: APIRequestFlags;
  args: Array<string>;
  type: APIRequestType;
  api: API;

  async init(): Promise<MasterQueryResponse> {
    try {
      if (this.flags.rage) {
        return await this.initWithRage();
      } else if (this.flags.profile) {
        return await this.initWithCPUProfile(false);
      } else {
        return await this.initCommand();
      }
    } catch (err) {
      return {
        error: {
          handled: false,
          name: err.name,
          message: err.message,
          stack: err.stack,
        },
      };
    }
  }

  async initCommand(): Promise<MasterQueryResponse> {
    const localCommand = localCommands.get(this.command);

    if (this.type === 'master' || localCommand === undefined) {
      return this.initFromMaster();
    } else {
      return this.initFromLocal(localCommand);
    }
  }

  async initWithRage(): Promise<MasterQueryResponse> {
    const timestamp = getTimestamp();
    const tmpdir = os.tmpdir();
    const profilePath = path.join(tmpdir, `RomeRageProfile-${timestamp}.json`);
    const logPath = path.join(tmpdir, `RomeRageLog-${timestamp}.txt`);

    // Overwrite flags so we collect logs and profiles
    this.flags = {
      ...this.flags,
      profilePath,
      logPath,
      logWorkers: true,
    };

    // TODO catch stdout and stderr
    let out = '';

    try {
      return await this.initWithCPUProfile(true);
    } finally {
      const {ragePath, cwd} = this.flags;
      const filename = path.resolve(
        cwd,
        ragePath === '' ? `rome-rage-${getTimestamp()}.tar.gz` : ragePath,
      );
      const stream = zlib.createGzip();
      stream.pipe(fs.createWriteStream(filename));

      const writer = new TarWriter(stream);

      writer.append({name: 'out-raw.txt'}, out);
      writer.append({name: 'out.txt'}, stripAnsi(out));

      const profile = await fs.promises.readFile(profilePath);
      writer.append({name: 'profile.json'}, profile);

      const log = await fs.promises.readFile(logPath);
      writer.append({name: 'log.txt'}, log);

      function indent(val: unknown): string {
        const str =
          typeof val === 'string' ? val : format(val, {compact: true});
        const lines = str.trim().split('\n');
        const indented = lines.join('\n  ');
        return '\n  ' + indented;
      }
      const env = [];
      env.push(`Command: ${indent(this.command)}`);
      env.push(`Command Flags: ${indent(this.commandFlags)}`);
      env.push(`Flags: ${indent(this.flags)}`);
      env.push(`Args: ${indent(this.args)}`);
      env.push(`PATH: ${indent(process.env.PATH)}`);
      env.push(`Rome version: ${indent(VERSION)}`);
      env.push(`Node version: ${indent(process.versions.node)}`);
      env.push(`Platform: ${indent(process.platform + ' ' + process.arch)}`);
      writer.append({name: 'environment.txt'}, env.join('\n\n') + '\n');

      // Don't do this if we never connected to the master
      const bridgeStatus = this.api.getBridge();
      if (bridgeStatus !== undefined) {
        const status = await this.api.query({
          flags: {
            silent: true,
          },
          command: 'status',
        });
        writer.append(
          {name: 'status.txt'},
          format(status.data, {compact: true}) + '\n',
        );
      }

      await writer.finalize();
      this.reporter.success('Rage archive written to', filename);
    }
  }

  async initFromLocal(
    localCommand: LocalCommand,
  ): Promise<MasterQueryResponse> {
    if (this.flags.silent !== true) {
      this.reporter.header(this.command);
    }

    const success = await localCommand.run(
      this,
      consumeMixed(this.commandFlags),
    );
    if (success) {
      if (this.flags.silent !== true) {
        this.reporter.footer();
      }
      return {};
    } else {
      return {
        error: {
          handled: true,
          name: 'Error',
          message: 'Command was not successful',
        },
      };
    }
  }

  async initFromMaster(): Promise<MasterQueryResponse> {
    try {
      const bridge = await this.api.findOrStartMaster();

      const request: MasterQueryRequest = {
        args: this.args,
        flags: this.flags,
        commandFlags: this.commandFlags,
        hasClearScreen: this.reporter.hasClearScreen,
        columns: this.reporter.getColumns(),
        command: this.command,
        noData: this.noData,
      };

      return await bridge.query.call(request);
    } catch (err) {
      if (err instanceof BridgeError) {
        return {
          killed: true,
          error: {
            handled: false,
            name: 'Error',
            message:
              'Server died while processing command. Results may be incomplete.',
            stack: undefined,
          },
        };
      } else {
        throw err;
      }
    }
  }

  async initWithCPUProfile(rage: boolean): Promise<MasterQueryResponse> {
    if (!rage) {
      this.reporter.info('Starting CPU profile...');
    }

    // Start server and start profiling
    const bridge = await this.api.findOrStartMaster();
    await bridge.profilingStart.call();

    // Start cli profiling
    let cliProfiler: undefined | Profiler;
    const bridgeStatus = this.api.getBridge();
    if (bridgeStatus === undefined || bridgeStatus.dedicated) {
      cliProfiler = new Profiler();
      await cliProfiler.startProfiling();
    }

    // Start a profile timer if one was specified
    const {profileTimeout} = this.flags;

    let hasProfiled: undefined | Promise<void>;
    let timeout: undefined | NodeJS.Timeout;
    if (profileTimeout > 0) {
      timeout = setTimeout(() => {
        hasProfiled = stopProfile();
      }, profileTimeout);
    }

    const stopProfile = async () => {
      // This is to prevent stopping the profile multiple times via the timeout and then at the end
      // It's a promise so that the final stopProfile call will block until the first has finished
      if (hasProfiled) {
        return hasProfiled;
      }

      // Stop the timeout if it hasn't been triggered
      if (timeout !== undefined) {
        clearTimeout(timeout);
      }

      //
      this.reporter.info(
        rage ? 'Creating rage archive...' : 'Fetching profiles...',
      );
      const trace = new Trace();

      // Add CLI profile if it exists
      if (cliProfiler !== undefined) {
        const cliProfile = await cliProfiler.stopProfiling();
        trace.addProfile('CLI', cliProfile);
      }

      // Fetch master and worker profiles
      const {
        master: masterProfile,
        workers: workerProfiles,
      } = await bridge.profilingStop.call();

      // Add master profile
      const masterProfileName =
        cliProfiler === undefined ? 'Master/CLI' : 'Master';
      trace.addProfile(masterProfileName, masterProfile);

      // Add worker profilers
      for (let i = 0; i < workerProfiles.length; i++) {
        trace.addProfile(`Worker ${String(i + 1)}`, workerProfiles[i]);
      }

      // Build filename
      const {cwd, profilePath} = this.flags;
      const filename = path.resolve(
        cwd,
        profilePath === '' ? `Profile-${getTimestamp()}.json` : profilePath,
      );

      // Build the trace and write it out
      const events = trace.build();
      await writeFile(filename, JSON.stringify(events, undefined, '  '));

      // Log
      if (!rage) {
        this.reporter.success(`Wrote CPU profile to "${filename}"`);
      }
    };

    try {
      return await this.initCommand();
    } finally {
      await stopProfile();
    }
  }
}
