import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {StringLiteral} from './StringLiteral';

export function DirectiveLiteral(
  generator: Generator,
  node: t.DirectiveLiteralNode,
  parent: t.Node,
) {
  StringLiteral(generator, node, parent);
}
