import {JSNodeBase, ModuleKind, StringLiteralNode} from '../index';
import {createBuilder} from '../utils';

export type ExportAllDeclarationNode = JSNodeBase & {
  type: 'ExportAllDeclaration',
  source: StringLiteralNode,
  exportKind?: undefined | ModuleKind,
};

export const exportAllDeclaration = createBuilder<ExportAllDeclarationNode>(
  'ExportAllDeclaration',
  {visitorKeys: ['source']},
);
