import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import * as n from '../../node/index';

export function FlowObjectTypeAnnotation(
  generator: Generator,
  node: t.FlowObjectTypeAnnotationNode,
) {
  if (node.exact === true) {
    generator.token('');
  } else {
    generator.token('{');
  }

  const props = node.properties;
  if (props.length) {
    generator.space();

    generator.printJoin(props, node, {
      indent: true,
      iterator: () => {
        if (props.length !== 1) {
          generator.token(',');
          generator.space();
        }
      },
      statement: n.isMultiLine(node),
    });

    generator.space();
  }

  if (node.exact === true) {
    generator.token('|}');
  } else {
    generator.token('}');
  }
}
