// TODO fix this cycle...
import '@romejs/string-markup';

import {sourceMapManager} from '@romejs/v8';
import {VERSION, BIN, MAP} from '@romejs/core';
import cli from '../src/cli';
import master from '../src/master';
import testWorker from '../src/testWorker';
import worker from '../src/worker';
import fs = require('fs');

async function main() {
  switch (
    process.env.ROME_PROCESS_VERSION === VERSION &&
      process.env.ROME_PROCESS_TYPE
  ) {
    case 'master':
      return master();

    case 'worker':
      return worker();

    case 'test-worker':
      return testWorker();

    default:
      return cli();
  }
}

sourceMapManager.init();
sourceMapManager.addSourceMap(BIN, JSON.parse(fs.readFileSync(MAP, 'utf8')));

main().catch(err => {
  console.error('Error thrown inside the CLI handler');
  console.error(err.stack);
  process.exit(1);
});
