import {JSNodeBase, ExpressionNode, JSXEmptyExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type JSXExpressionContainerNode = JSNodeBase & {
  type: 'JSXExpressionContainer',
  expression: ExpressionNode | JSXEmptyExpressionNode,
};

export const jsxExpressionContainer = createBuilder<JSXExpressionContainerNode>(
  'JSXExpressionContainer',
  {
    visitorKeys: ['expression'],
  },
);
