import {
  JSNodeBase,
  IdentifierNode,
  StringLiteralNode,
  TSModuleBlockNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSModuleDeclarationNode = JSNodeBase & {
  type: 'TSModuleDeclaration';
  id: IdentifierNode | StringLiteralNode;
  global?: undefined | boolean;
  body?: undefined | TSModuleBlockNode | TSModuleDeclarationNode;
};

export const tsModuleDeclaration = createBuilder<TSModuleDeclarationNode>(
  'TSModuleDeclaration',
  {visitorKeys: ['id', 'body']},
);
