import {Node, ExpressionNode, StatementNode} from '@romejs/js-ast';
import {Path} from '@romejs/js-compiler';
import {DEFAULT_PROJECT_CONFIG} from '@romejs/core';
import {program} from '@romejs/js-ast';
import removeLoc from './removeLoc';
import assertSingleNode from './assertSingleNode';
import {Context} from '@romejs/js-compiler';
import {identifier} from '@romejs/js-ast';
import {parse} from '@romejs/js-parser';
import invariant from '@romejs/invariant';

type BuiltTemplate = {
  ast: Node;
  placeholderPaths: Array<Array<string>>;
};

type TemplatePlaceholders = Dict<undefined | Array<string>>;

const templateCache: Map<TemplateStringsArray, BuiltTemplate> = new Map();

function getTemplate(strs: TemplateStringsArray): BuiltTemplate {
  const cached = templateCache.get(strs);
  if (cached) {
    return cached;
  }

  // calculate amount of placeholders to insert
  const pathCount = strs.length - 1;

  // create path ids
  let placeholders: TemplatePlaceholders = {};
  const placeholderIds: Array<string> = [];
  for (let i = 0; i < pathCount; i++) {
    const id = `__${String(i)}__`;
    placeholderIds.push(id);
    placeholders[id] = undefined;
  }

  // interpolate placeholders and original code
  let code = '';
  for (let i = 0; i < strs.length; i++) {
    // add original part of code
    code += strs[i];

    // add in placeholder
    const placeholder = placeholderIds[i];
    if (placeholder) {
      code += placeholder;
    }
  }

  // parse the interpolated code
  let ast = parse(code, {
    sourceType: 'template',
    filename: 'template',
  });

  // remove `loc` properties
  ast = assertSingleNode(removeLoc(ast));

  // traverse and find placeholders paths
  const collectPlaceholderPaths = (path: Path) => {
    const {node} = path;
    if (node.type === 'Identifier' && node.name in placeholders) {
      placeholders[node.name] = path.getPathKeys();
    }
    return node;
  };
  const context = new Context({ast, projectConfig: DEFAULT_PROJECT_CONFIG});
  context.reduce(ast, [
    {name: 'collectPlaceholderPaths', enter: collectPlaceholderPaths},
  ]);

  const placeholderPaths = [];
  for (const id in placeholders) {
    const path = placeholders[id];
    if (path) {
      placeholderPaths.push(path);
    } else {
      throw new Error(`Failed to find placeholder path for ${id}`);
    }
  }

  return {ast, placeholderPaths};
}

type TemplateSubstitions = Array<Node | string>;

export default function template(
  strs: TemplateStringsArray,
  ...substitutions: TemplateSubstitions
): Node {
  const {ast, placeholderPaths} = getTemplate(strs);

  // no substitutions so we can just return the ast!
  if (!substitutions.length) {
    return ast;
  }

  // this case should never be hit
  invariant(
    placeholderPaths.length === substitutions.length,
    'expected subtituions to be the same length as paths',
  );

  const newAst = {...ast};

  for (let i = 0; i < placeholderPaths.length; i++) {
    const paths = placeholderPaths[i];

    let substitute = substitutions[i];
    if (typeof substitute === 'string') {
      substitute = identifier.create({name: substitute});
    }

    let target = newAst;

    for (let i = 0; i < paths.length; i++) {
      const key = paths[i];
      const isLast = i === paths.length - 1;

      if (isLast) {
        target[key] = substitute;
      } else {
        let currTarget = target[key];
        if (Array.isArray(currTarget)) {
          currTarget = currTarget.slice();
        } else {
          currTarget = {...currTarget};
        }
        target[key] = currTarget;
        target = currTarget;
      }
    }
  }

  return newAst;
}

template.expression = (
  strs: TemplateStringsArray,
  ...substitutions: TemplateSubstitions
): ExpressionNode => {
  const first = template.statement(strs, ...substitutions);

  // ensure that the single statement is an ExpressionStatement
  invariant(
    first.type === 'ExpressionStatement',
    'Single statement should be an ExpressionStatement',
  );

  return first.expression;
};

template.statement = (
  strs: TemplateStringsArray,
  ...substitutions: TemplateSubstitions
): StatementNode => {
  // Parse the template, with caching
  const ast = program.assert(template(strs, ...substitutions));

  // Ensure that there's only a single statement in the Program body
  const body = ast.body;
  invariant(
    body.length === 1,
    "More than one statement isn't allowed for a template.",
  );
  return body[0];
};
