import {JSNodeBase, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowIntersectionTypeAnnotationNode = JSNodeBase & {
  type: 'FlowIntersectionTypeAnnotation',
  types: Array<FlowNode>,
};

export const flowIntersectionTypeAnnotation = createBuilder<FlowIntersectionTypeAnnotationNode>(
  'FlowIntersectionTypeAnnotation',
  {visitorKeys: ['types']},
);
