import Scope from '../Scope';
import {ImportBindingMeta} from '@romejs/js-compiler';
import {ImportBinding} from '@romejs/js-compiler';
import * as t from '@romejs/js-ast';
import {ModuleKind} from '@romejs/js-ast';

export default function ImportDeclaration(
  node: t.ImportDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  const source = node.source.value;

  for (const specifier of node.specifiers) {
    let kind: ModuleKind = specifier.importKind || node.importKind || 'value';
    let meta: undefined | ImportBindingMeta;

    if (specifier.type === 'ImportNamespaceSpecifier') {
      meta = {
        kind,
        type: 'namespace',
        source,
      };
    } else if (specifier.type === 'ImportDefaultSpecifier') {
      meta = {
        kind,
        type: 'name',
        imported: 'default',
        source,
      };
    } else if (specifier.type === 'ImportSpecifier') {
      meta = {
        kind,
        type: 'name',
        imported: specifier.imported.name,
        source,
      };
    }

    if (meta === undefined) {
      return undefined;
    }

    scope.addBinding(
      new ImportBinding(
        {
          node: specifier.local,
          name: specifier.local.name,
          scope,
        },
        meta,
      ),
    );
  }
}
