import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowObjectTypeSpreadProperty(
  generator: Generator,
  node: t.FlowObjectTypeSpreadPropertyNode,
) {
  generator.token('...');
  generator.print(node.argument, node);
}
