import {Path} from '@romejs/js-compiler';
import {markup} from '@romejs/string-markup';
import {isReferenced, isInTypeAnnotation} from '@romejs/js-ast-utils';

const NODE_VARIABLES = [
  'require',
  '__dirname',
  '__filename',
  'module',
  'exports',
  'babelHelpers',
];

const BROWSER_VARIABLES = [
  'fetch',
  'document',
  'window',
  'Worker',
  'cancelAnimationFrame',
  'requestAnimationFrame',
  'WebSocket',
  'alert',
  'Blob',
  'navigator',
  'Element',
  'Text',
  'Document',
  'performance',
];

export default {
  name: 'undeclaredVariables',
  enter(path: Path): Node {
    const {node, scope} = path;

    if (
      node.type === 'Identifier' &&
      isReferenced(path) &&
      !isInTypeAnnotation(path)
    ) {
      const {name} = node;
      const binding = scope.getBinding(name);

      const isDefined =
        binding !== undefined ||
        scope.getRootScope().isGlobal(name) ||
        // FIXME: This is such a bad way to determine global variables, we should store an array of variables
        name in global ||
        BROWSER_VARIABLES.includes(name) ||
        NODE_VARIABLES.includes(name);

      if (!isDefined) {
        path.context.addNodeDiagnostic(node, {
          category: 'lint/undeclaredVariables',
          message: markup`Undeclared variable <emphasis>${name}</emphasis>`,
        });
      }
    }

    return node;
  },
};
