import {
  GUTTER,
  CODE_FRAME_CONTEXT_LINES,
  CODE_FRAME_SELECTED_INDENT,
  CODE_FRAME_INDENT,
  MAX_CODE_FRAME_LINES,
  HALF_MAX_CODE_FRAME_LINES,
} from './constants';
import {Position} from '@romejs/parser-core';
import {cleanEquivalentString} from './utils';
import {leftPad, formatAnsi, stripAnsi} from '@romejs/string-ansi';

export default function buildMessageCodeFrame(
  allLines: Array<string>,
  start: Position,
  end: Position,
  maybeMarkerMessage: undefined | string,
): string {
  let markerMessage: string =
    maybeMarkerMessage === undefined ? '' : maybeMarkerMessage;

  const startLineIndex = start.line - 1;

  let endLineIndex = end.line - 1;
  let endLineCol = end.column;

  let markerOffset = end.column;
  let markerSize = 1;

  let contextStartIndex = Math.max(
    0,
    startLineIndex - CODE_FRAME_CONTEXT_LINES,
  );
  let contextEndIndex = Math.min(
    allLines.length - 1,
    endLineIndex + CODE_FRAME_CONTEXT_LINES,
  );

  let myLines: Array<
    | {
        gutter: string;
        line: string;
        lineNo: number;
        lineIndex: number;
      }
    | undefined
  > = [];
  for (let i = contextStartIndex; i <= contextEndIndex; i++) {
    let line: undefined | string = allLines[i];
    if (line === undefined) {
      continue;
    }

    // Ensure that the frame doesn't start with whitespace
    if (line.trim() === '' && myLines.length === 0 && i !== startLineIndex) {
      continue;
    }

    const shouldHighlight: boolean = i >= startLineIndex && i <= endLineIndex;

    if (shouldHighlight) {
      // Range to highlight
      let startCol = 0;
      let endCol = line.length;

      // First line in selection
      if (i === startLineIndex) {
        startCol = start.column;
      }

      // Last line in selection
      if (i === endLineIndex) {
        endCol = endLineCol;

        // Sometimes the end will be the same as the start if we just want to make a single place
        // and not perform highlighting
        if (endCol > startCol) {
          markerSize = endCol - startCol;
          markerOffset -= markerSize;
        }
      }
    }

    const lineNo = i + 1;
    let gutter = `${String(lineNo)}${GUTTER}`;

    if (shouldHighlight) {
      gutter = `${CODE_FRAME_SELECTED_INDENT}${gutter}`;
    } else {
      gutter = `${CODE_FRAME_INDENT}${gutter}`;
    }

    myLines.push({
      gutter,
      line,
      lineNo,
      lineIndex: i,
    });
  }

  // If there's no lines to target then return the normal marker
  if (myLines.length === 0 || end.line === -1 || start.line === -1) {
    return CODE_FRAME_INDENT + markerMessage;
  }

  // Cut out end for selections over the max
  const pruned = myLines.length > MAX_CODE_FRAME_LINES + 2;
  if (pruned) {
    const start = myLines.slice(0, HALF_MAX_CODE_FRAME_LINES);
    const end = myLines.slice(-HALF_MAX_CODE_FRAME_LINES);
    myLines = start.concat([undefined], end);
  }

  // Calculate max size of gutter, this is the maximum visible line plus the futter length plus the frame indent
  const lastLine = myLines[myLines.length - 1];
  if (lastLine === undefined) {
    throw new Error('Expected there to be a last line');
  }

  const maxVisibleLine = lastLine.lineNo;
  const maxGutterLength =
    String(maxVisibleLine).length + GUTTER.length + CODE_FRAME_INDENT.length;

  // If what the marker is highlighting equals the marker message then it's redundant so don't show the message
  if (markerMessage !== '' && start.line === end.line) {
    const text = stripAnsi(allLines[start.line - 1]).slice(
      start.column,
      end.column,
    );
    if (cleanEquivalentString(text) === cleanEquivalentString(markerMessage)) {
      markerMessage = '';
    }
  }

  // Calculate marker
  const pointerLength: number = Math.max(markerSize, 1);
  const pointer: string = formatAnsi.red(
    formatAnsi.bold('^'.repeat(pointerLength)),
  );
  const gutterIndent: string = ' '.repeat(maxGutterLength - GUTTER.length);
  const pointerIndent: string = ' '.repeat(markerOffset);
  const markerLine: string = `${gutterIndent}${formatAnsi.bold(
    GUTTER,
  )}${pointerIndent}${pointer} ${markerMessage}`;

  // Build up the line we display when source lines are omitted
  const omittedDots = '...';
  const omittedLine = leftPad(
    formatAnsi.bold(omittedDots) + GUTTER,
    maxGutterLength,
  );

  // Build the frame
  const result = [];
  for (const selection of myLines) {
    if (!selection) {
      result.push(omittedLine);
      continue;
    }

    const {gutter, line, lineIndex} = selection;
    result.push(formatAnsi.bold(leftPad(gutter, maxGutterLength)) + line);
    if (lineIndex === endLineIndex) {
      result.push(markerLine);
    }
  }

  const frame: string = result.join('\n');
  return frame;
}
