import {Reporter} from '@romejs/cli-reporter';
import {naturalCompare} from '@romejs/string-utils';
import {formatAnsi, rightPad} from '@romejs/string-ansi';
import invariant from '@romejs/invariant';

export class BreakCommand {}

type ArgDeclaration<Def> = {
  inputName?: string;
  description?: string;
  shorthand?: string;
  default?: Def;
  hidden?: boolean;
};

type CommandOptions = {
  category?: undefined | string;
  description?: undefined | string;
};

type CommandBoundOptions = CommandOptions & {
  callback: undefined | (() => void);
};

type ArgTypeDeclaration<Def> = ArgDeclaration<Def> & {
  type: 'string' | 'number' | 'boolean';
};

type ArgBoundDeclaration<Def> = ArgTypeDeclaration<Def> & {
  key: string;
  command: undefined | string;
};

export type ParserOptions = {
  examples?: Array<string>;
  usage?: string;
  description?: string;
  version?: string;
};

export default class Parser {
  constructor(reporter: Reporter, opts: ParserOptions, rawArgs: Array<string>) {
    this.reporter = reporter;
    this.opts = opts;

    this.declaredArguments = new Map();
    this.args = new Map();
    this._ = [];

    this.commands = new Map();
    this.ranCommands = [];
    this.currentCommand = undefined;
    this.ranAssert = false;
    this.errors = [];
    this._allowUnknownArguments = false;

    this.consumeRawArgs(rawArgs);

    if (opts.version !== undefined) {
      this.declareArgument('version', {
        type: 'boolean',
        description: 'show the version',
      });
    }

    this.declareArgument('help', {
      type: 'boolean',
      description: 'show this help screen',
      shorthand: 'h',
    });

    this.helpMode = this.getBooleanArg('help', false);
  }

  _allowUnknownArguments: boolean;

  reporter: Reporter;
  opts: ParserOptions;

  ranCommands: Array<string>;
  errors: Array<string>;
  args: Map<string, string>;
  declaredArguments: Map<string, ArgBoundDeclaration<unknown>>;
  commands: Map<string, CommandBoundOptions>;
  _: Array<string>;

  currentCommand: undefined | string;
  ranAssert: boolean;
  helpMode: boolean;

  looksLikeFlag(flag: undefined | string): boolean {
    return flag !== undefined && flag[0] === '-';
  }

  consumeRawArgs(rawArgs: Array<string>) {
    while (rawArgs.length > 0) {
      const arg: string = String(rawArgs.shift());

      // TODO error on duplicate flags

      if (arg === '--') {
        // We consider a -- by itself to halt parsing of args, the rest of the remaining args are added to _
        this._ = this._.concat(rawArgs);
        break;
      } else if (arg[0] === '-' && arg[1] === '-') {
        // Long argument like --foo or --bar

        // Clean the argument by stripping off the first two dashes
        const name = arg.slice(2);

        // If the next argument is a flag or we're at the end of the args then just set it to `true`
        if (rawArgs.length === 0 || this.looksLikeFlag(rawArgs[0])) {
          this.args.set(name, 'true');
        } else {
          // Otherwise, take that value
          this.args.set(name, String(rawArgs.shift()));
        }
      } else if (arg[0] === '-') {
        // Single argument like -x or -xyz

        // Strip off first dash and split into letters
        const names: Array<string> = arg.slice(1).split('');

        // Set all the flags to `true`
        for (const name of names) {
          this.args.set(name, 'true');
        }

        // If the next argument isn't a flag and we aren't at the end then take it as our value
        if (rawArgs.length > 0 && this.looksLikeFlag(rawArgs[0]) === false) {
          this.args.set(String(names.pop()), String(rawArgs.shift()));
        }
      } else {
        // Not a flag and hasn't been consumed already by a previous arg so it must be a file
        this._.push(arg);
      }
    }
  }

  addShorthand(frm: string, to: string) {
    const arg = this.args.get(frm);
    if (arg !== undefined) {
      this.args.set(to, arg);
    }
  }

  hasArg(name: string): boolean {
    return this.args.has(name) && this.args.get(name) !== undefined;
  }

  getArg(name: string, def?: unknown): unknown {
    const arg = this.args.get(name);

    if (arg !== undefined) {
      return arg;
    }

    if (def !== undefined) {
      return def;
    }

    // When in help mode, we have to return something
    if (this.helpMode) {
      return '';
    }

    // Produce an error to be displayed later
    this.error(`Argument ${name} is required`);
    return '';
  }

  declareArgument(name: string, decl: ArgTypeDeclaration<unknown>) {
    // Don't allow argument declaration after assert() as these declarations aren't observable to --help
    invariant(
      this.ranAssert === false,
      'Cannot declare an argument after calling assert()',
    );

    // Ensure it hasn't been declared more than once
    invariant(
      this.declaredArguments.has(name) === false,
      `Already declared argument ${name}`,
    );

    // Add shorthand if one specified
    if (decl.shorthand !== undefined) {
      invariant(
        decl.shorthand.length === 1,
        'Flag shorthand expected to be 1 character',
      );
      this.addShorthand(decl.shorthand, name);
    }

    // Create a key
    let argKey = name;
    // In helpMode and we're inside a command then namespace the arg to avoid collisions
    if (this.currentCommand !== undefined && this.helpMode === true) {
      argKey += ` [${this.currentCommand}]`;
    }

    // Declare argument
    this.declaredArguments.set(argKey, {
      key: name,
      command: this.currentCommand,
      ...decl,
    });
  }

  getStringArg(name: string, def?: unknown): string {
    const arg: unknown = this.getArg(name, def);
    return String(arg);
  }

  getBooleanArg(name: string, def?: unknown): boolean {
    if (this.args.get(`no-${name}`) === 'true') {
      return false;
    }

    const arg: unknown = this.getArg(name, def);
    return arg === true || arg === 'true';
  }

  getNumberArg(name: string, def?: unknown): number {
    const arg: unknown = this.getArg(name, def);
    return Number(arg);
  }

  getInterface(): ParserInterface {
    return new ParserInterface(this);
  }

  assert() {
    this.ranAssert = true;

    // Display any errors, unknown arguments, invalid etc
    if (this.errors.length > 0) {
      for (const msg of this.errors) {
        this.reporter.error(msg);
      }
      process.exit(1);
    }

    // Show help for --help
    if (this.helpMode) {
      if (this.currentCommand !== undefined) {
        // When inside commands, and we know we'll be producing help, bail out
        throw new BreakCommand();
      }

      this.showHelp();
      process.exit(1);
    }

    // Show help for --version
    if (this.getBooleanArg('version', false)) {
      this.reporter.log(String(this.opts.version));
      process.exit(0);
    }

    // We've parsed arguments like `--foo bar` as `{foo: 'bar}`
    // However, --foo may be a boolean flag, so `bar` needs to be correctly added to _
    for (const [key, value] of this.args) {
      const declared = this.declaredArguments.get(key);

      if (
        declared !== undefined &&
        declared.type === 'boolean' &&
        value !== 'true'
      ) {
        // TODO this isn't necessarily the correct position...
        this._.push(value);

        //
        this.args.set(key, 'true');
      }
    }

    if (this._allowUnknownArguments) {
      return undefined;
    }

    let hasUnknownArguments: boolean = false;
    for (const [rawKey] of this.args) {
      let key: string = rawKey;

      // We allow no- to set args to false, consider it equal
      if (key.startsWith('no-')) {
        key = key.slice(3);
      }

      if (this.declaredArguments.has(key) === false) {
        hasUnknownArguments = true;
        this.reporter.error(`Unknown argument ${key} specified`);
      }
    }
    if (hasUnknownArguments) {
      process.exit(1);
    }
  }

  error(msg: string) {
    if (this.ranAssert) {
      throw new Error('error() can only be called before assert()');
    }
    this.errors.push(msg);
  }

  logHelp(str: string) {
    this.reporter.log(`  ${str}`);
  }

  buildOptionsHelp(keys: Array<string>): Array<string> {
    const lines = [];

    const optionOutput: Array<{
      argName: string;
      arg: string;
      description: string;
    }> = [];
    let argColumnLength: number = 0;

    // Build up options, we need to do this to line up the columns correctly
    for (const key of keys) {
      const decl = this.declaredArguments.get(key);
      if (decl === undefined) {
        throw new Error('Expected argument declaration');
      }
      if (decl.hidden === true) {
        continue;
      }

      let argName = decl.key;
      let argCol = decl.key;

      // For booleans that default to `true`, show the --no- version as that'll be what users should use
      if (decl.type === 'boolean' && decl.default === true) {
        argCol = `--no-${argCol}`;
        argName = `no-${argName}`;
      } else {
        argCol = `--${argCol}`;
      }

      // Prepend shorthand if it has one
      if (decl.shorthand !== undefined) {
        argCol = `${argCol}, -${decl.shorthand}`;
      }

      // Add input specifier unless a boolean
      if (decl.type !== 'boolean') {
        const inputName =
          decl.inputName === undefined ? 'input' : decl.inputName;
        argCol += ` <${inputName}>`;
      }

      // Set arg col length if we'll be longer
      if (argColumnLength < argCol.length) {
        argColumnLength = argCol.length;
      }

      const descCol: string =
        decl.description === undefined
          ? 'no description found'
          : decl.description;

      optionOutput.push({
        argName,
        arg: argCol,
        description: descCol,
      });
    }

    // Sort options by argument name
    optionOutput.sort((a, b) => naturalCompare(a.argName, b.argName));

    // Output options
    for (const {arg, description} of optionOutput) {
      lines.push(
        `  ${formatAnsi.brightBlack(
          rightPad(arg, argColumnLength, ' '),
        )}  ${description}`,
      );
    }

    return lines;
  }

  showCommandsHelp(heading: string, rawCommandNames: Array<string>) {
    if (rawCommandNames.length === 0) {
      return undefined;
    }

    this.logHelp('');
    this.logHelp(formatAnsi.bold(heading));
    this.logHelp('');

    // Build output
    const commandNames: Array<string> = rawCommandNames.sort();
    const cmdOutput: Array<[string, string, Array<string>]> = [];
    let nameColumnLength: number = 0;
    for (const cmd of commandNames) {
      const opts = this.commands.get(cmd);
      if (opts === undefined) {
        throw new Error('Expected command options');
      }

      if (cmd[0] === '_') {
        continue;
      }

      // Set arg col length if we'll be longer
      if (nameColumnLength < cmd.length) {
        nameColumnLength = cmd.length;
      }

      // Find arguments that belong to this command
      const argKeys = [];
      for (const [key, decl] of this.declaredArguments) {
        if (decl.command === cmd) {
          argKeys.push(key);
        }
      }

      const desc =
        opts.description === undefined
          ? 'no description available'
          : opts.description;
      const optLines = this.buildOptionsHelp(argKeys);
      cmdOutput.push([cmd, desc, optLines]);
    }

    // Output, output...
    for (const [nameCol, descCol, optLines] of cmdOutput) {
      this.logHelp(
        `  ${formatAnsi.brightBlack(
          rightPad(nameCol, nameColumnLength, ' '),
        )}  ${descCol}`,
      );

      for (const line of optLines) {
        this.logHelp(`  ${line}`);
      }
    }
  }

  showHelp() {
    const {description, usage, examples} = this.opts;

    this.logHelp('');
    this.logHelp(
      `${formatAnsi.bold('Usage:')} ${usage === undefined ? '[flags]' : usage}`,
    );
    this.logHelp('');

    if (description !== undefined) {
      this.logHelp(description);
      this.logHelp('');
    }

    this.logHelp(formatAnsi.bold('Options'));
    this.logHelp('');

    // Show options not attached to any commands
    const lonerArgKeys = [];
    for (const [key, decl] of this.declaredArguments) {
      if (decl.command === undefined) {
        lonerArgKeys.push(key);
      }
    }
    for (const line of this.buildOptionsHelp(lonerArgKeys)) {
      this.logHelp(line);
    }

    // Sort commands into their appropriate categories for output
    const commandNames = new Set(this.commands.keys());
    const commandsByCategory: Map<string, Array<string>> = new Map();
    for (const name of commandNames) {
      const command = this.commands.get(name);
      if (command === undefined) {
        throw new Error('Expected command');
      }

      const {category} = command;
      if (category === undefined) {
        continue;
      }

      let categoryNames = commandsByCategory.get(category);
      if (categoryNames === undefined) {
        categoryNames = [];
        commandsByCategory.set(category, categoryNames);
      }
      categoryNames.push(name);

      commandNames.delete(name);
    }

    // Display commands by category
    const categoryNames = Array.from(commandsByCategory.keys()).sort();
    for (const category of categoryNames) {
      const commandNames = commandsByCategory.get(category);
      if (commandNames === undefined) {
        throw new Error('Expected command names');
      }
      this.showCommandsHelp(`${category} Commands`, commandNames);
    }

    // Display rest of the commands
    this.showCommandsHelp(
      commandsByCategory.size > 0 ? 'Other Commands' : 'Commands',
      Array.from(commandNames),
    );

    // Output examples
    if (examples !== undefined) {
      this.logHelp('');
      this.logHelp(formatAnsi.bold('Examples:'));
      this.logHelp('');

      for (const cmd of examples) {
        this.logHelp(`  $ ${cmd}`);
      }
    }

    this.logHelp('');
  }

  allowUnknownArguments() {
    if (this.helpMode) {
      return undefined;
    }

    this._allowUnknownArguments = true;
  }

  commandRequired() {
    if (this.ranCommands.length === 0) {
      this.reporter.error(
        'No command specified. Run --help to see available commands.',
      );
      process.exit(1);
    }
  }

  addCommand(
    cmd: string,
    opts: CommandOptions,
    callback?: undefined | (() => void),
  ) {
    invariant(
      this.currentCommand === undefined,
      "Nested commands aren't allowed",
    );

    // Define command
    this.commands.set(cmd, {...opts, callback});

    // Check if we should run this command
    const cmdParts = this.splitCommand(cmd);
    let shouldRun = true;
    let matched = true;
    for (let i = 0; i < cmdParts.length; i++) {
      const part = cmdParts[i];
      if (part !== this._[i]) {
        matched = false;
        shouldRun = false;
        break;
      }
    }

    this.processCommand(cmd, shouldRun, matched);
  }

  splitCommand(cmd: string): Array<string> {
    return cmd.split(' ');
  }

  processCommand(cmd: string, shouldRun: boolean, matched: boolean) {
    const opts = this.commands.get(cmd);
    if (opts === undefined) {
      throw new Error('Expected options');
    }

    const cmdParts = this.splitCommand(cmd);

    // If matched, properly set _
    const old_ = this._;
    if (matched) {
      // Remove the command from the start
      this._ = this._.slice(cmdParts.length);
    }

    // Always run command callbacks when in help mode, we wont execute these all the way anyway
    if (this.helpMode) {
      shouldRun = true;
    }

    // Run the command
    const {callback} = opts;
    if (shouldRun && callback !== undefined) {
      // Prepare
      this.ranAssert = false;
      this.currentCommand = cmd;
      this.ranCommands.push(cmd);

      // Run callback
      try {
        callback();
      } catch (err) {
        if (err instanceof BreakCommand) {
          // Silently ignore
        } else {
          throw err;
        }
      }

      // Ensure that assert() is always called in a callback
      invariant(
        // @ts-ignore: TS thinks that it is always `false` because we assigned it above...? wtf?
        this.ranAssert === true,
        'assert() was not called in command callback',
      );

      // Reset
      this.ranAssert = false;
      this.currentCommand = undefined;
    }

    // Reset _
    if (matched) {
      this._ = old_;
    }
  }
}

export class ParserInterface {
  constructor(parser: Parser) {
    this.parser = parser;
  }

  parser: Parser;

  assert() {
    this.parser.assert();
  }

  showHelp() {
    this.parser.showHelp();
  }

  getArgs(): Array<string> {
    return this.parser._;
  }

  allowUnknownArguments() {
    this.parser.allowUnknownArguments();
  }

  defaultCommand(callback: () => void) {
    if (this.parser.ranCommands.length === 0) {
      callback();
    }
  }

  error(msg: string) {
    this.parser.error(msg);
  }

  commandRequired() {
    this.parser.commandRequired();
  }

  command(
    cmd: string,
    callbackOrOpts: CommandOptions | (() => void),
    maybeCallback?: () => void,
  ) {
    const opts: CommandOptions =
      typeof callbackOrOpts === 'function' ? {} : callbackOrOpts;
    const callback: undefined | (() => void) =
      typeof callbackOrOpts === 'function' ? callbackOrOpts : maybeCallback;

    this.parser.addCommand(cmd, opts, callback);
  }

  optionalStringSet<ValidValue extends string>(
    name: string,
    validValues: Array<ValidValue>,
    decl: ArgDeclaration<ValidValue> = {},
  ): undefined | ValidValue {
    if (this.parser.hasArg(name)) {
      return this.stringSet(name, validValues, decl);
    } else {
      this.parser.declareArgument(name, {type: 'string', ...decl});
      return undefined;
    }
  }

  stringSet<ValidValue extends string>(
    name: string,
    validValues: Array<ValidValue>,
    decl: ArgDeclaration<ValidValue> = {},
  ): ValidValue {
    this.parser.declareArgument(name, {type: 'string', ...decl});

    if (this.parser.hasArg(name)) {
      // @ts-ignore: We are lying that this is a ValidValue but we'll only return it if it's actually valid
      const val: ValidValue = this.parser.getStringArg(name);
      if (validValues.includes(val)) {
        return val;
      } else {
        this.error(
          `Argument ${name} was given an invalid value of "${val}" - possible values are ${JSON.stringify(
            validValues,
          )}`,
        );
        return validValues[0];
      }
    } else if (decl.default !== undefined) {
      return decl.default;
    } else {
      this.error(`Argument ${name} is required`);
      return validValues[0];
    }
  }

  optionalString(
    name: string,
    decl: ArgDeclaration<void> = {},
  ): undefined | string {
    this.parser.declareArgument(name, {type: 'string', ...decl});
    if (this.parser.hasArg(name)) {
      return this.parser.getStringArg(name);
    } else {
      return undefined;
    }
  }

  string(name: string, decl: ArgDeclaration<string> = {}): string {
    this.parser.declareArgument(name, {type: 'string', ...decl});
    return this.parser.getStringArg(name, decl.default);
  }

  optionalNumber(
    name: string,
    decl: ArgDeclaration<void> = {},
  ): undefined | number {
    this.parser.declareArgument(name, {type: 'number', ...decl});
    if (this.parser.hasArg(name)) {
      return this.parser.getNumberArg(name);
    } else {
      return undefined;
    }
  }

  number(name: string, decl: ArgDeclaration<number> = {}): number {
    this.parser.declareArgument(name, {type: 'number', ...decl});
    return this.parser.getNumberArg(name, decl.default);
  }

  boolean(name: string, decl: ArgDeclaration<boolean> = {}): boolean {
    this.parser.declareArgument(name, {type: 'boolean', ...decl});
    return this.parser.getBooleanArg(name, decl.default);
  }
}
