import {ProgramNode} from '@romejs/js-ast';
import {PartialDiagnostics} from '@romejs/diagnostics';
import {CompileRequest} from '../types';
import {lintTransforms} from '../plugins/lint/index';
import {program} from '@romejs/js-ast';
import {Cache, Context} from '@romejs/js-compiler';
import generate from '@romejs/js-generator';

export type LintResult = {
  diagnostics: PartialDiagnostics,
  src: string,
  ast: ProgramNode,
};

const lintCache: Cache<LintResult> = new Cache();

export default async function lint(req: CompileRequest): Promise<LintResult> {
  const {ast, src, projectConfig} = req;

  const query = Cache.buildQuery(req);
  const cached = lintCache.get(query);
  if (cached) {
    return cached;
  }

  const context = new Context({
    ast,
    projectConfig,
    origin: 'Linting',
  });
  const newAst = program.assert(context.reduce(ast, lintTransforms));

  let formattedCode = src;
  if (projectConfig.format.enabled) {
    const generated = generate(newAst, {}, src);
    formattedCode = generated.getCode() + '\n';
  }

  const result: LintResult = {
    ast: newAst,
    diagnostics: [...ast.diagnostics, ...context.diagnostics],
    src: formattedCode,
  };
  lintCache.set(query, result);
  return result;
}
