import {ProjectConfig} from '../common/types/project';
import {FileReference} from '@romejs/core';
import {PrefetchedModuleSignatures} from '../common/bridges/WorkerBridge';
import Worker, {ParseResult} from './Worker';
import {PartialDiagnostics} from '@romejs/diagnostics';
import {JSONParserMode} from '@romejs/codec-json';
import * as compiler from '@romejs/js-compiler';
import {check as typeCheck} from '@romejs/js-analysis';
import {parseJSON, consumeJSON} from '@romejs/codec-json';
import invariant from '@romejs/invariant';

export type LintInfo = {
  file: FileReference;
  projectConfig: ProjectConfig;
  worker: Worker;
  prefetchedModuleSignatures: PrefetchedModuleSignatures;
};

export type LintResult = {
  formatted: string;
  raw: string;
  diagnostics: PartialDiagnostics;
};

type ExtensionHandler = {
  canAnalyze?: boolean;
  hasteMode?: 'ext' | 'noext';
  isBinary?: boolean;
  hasScale?: boolean;

  toJs?: (
    opts: {
      file: FileReference;
      src: string;
    },
    worker: Worker,
  ) => string | Promise<string>;

  lint?: (info: LintInfo) => Promise<LintResult>;
};

export function getExtensionName(filename: string): undefined | string {
  const [, ...parts] = filename.split('.');

  // try and get the highest match with the extension
  // eg. if we're given the filename `Foo.bar.js` we should check for the extension handler for `bar.js` then `js`
  for (let i = 0; i < parts.length; i++) {
    const ext = parts.slice(i).join('.');
    const extInfo = extensions.get(ext);
    if (extInfo !== undefined) {
      return ext;
    }
  }

  return undefined;
}

export function getExtensionHandler(
  filename: string,
): undefined | ExtensionHandler {
  const ext = getExtensionName(filename);

  if (ext !== undefined) {
    const extInfo = extensions.get(ext);
    invariant(extInfo !== undefined, 'expected extension info');
    return extInfo;
  }

  return undefined;
}

const textExt: ExtensionHandler = {
  toJs({src}) {
    const serial = JSON.stringify(src);
    return `export default ${serial};`;
  },
};

const binaryExt: ExtensionHandler = {
  hasScale: true,
  isBinary: true,

  async toJs() {
    return `export default '';`;
  },
};

// Extract the JSON type from 'a filename
// TODO remove this logic entirely, rome-json already infers the type from the extension
function getRJSONMode(filename: string): JSONParserMode {
  return filename.endsWith('.rjson') ? 'rjson' : 'json';
}

// If a JSON file exceeds 50KB then we will not autoformat it and will instead use the fast built-in JSON parser
// We want to use the rjson parser when possible because it has much more meaingful error messages and will point
// to the exact source location
function shouldDeoptJSON(mode: JSONParserMode, src: string): boolean {
  return mode === 'json' && src.length > 50000;
}

const jsonExt: ExtensionHandler = {
  hasteMode: 'noext',

  async lint(info: LintInfo): Promise<LintResult> {
    const {file: ref, projectConfig, worker} = info;
    const {uid, real} = ref;
    const src = await worker.readFile(real);

    const mode = getRJSONMode(real);
    let formatted: string = src;

    if (projectConfig.format.enabled) {
      if (shouldDeoptJSON(mode, src)) {
        // Fast path for big JSON files, see shouldDeoptJSON for info
        // We don't need to do anything here except make sure it's valid JSON
        parseJSON({
          mode,
          filename: ref.uid,
          input: src,
          locations: false,
        });
      } else {
        // Normal JSON will also be parsed by the RJSON parser, it will throw meaningful syntax errors though for non-JSON syntax
        const consumer = consumeJSON({input: src, filename: uid, mode});

        if (mode === 'json') {
          formatted = String(
            JSON.stringify(consumer.asMixed(), undefined, '  '),
          );
        } else {
          formatted = consumer.stringify() + '\n';
        }
      }
    }

    return {
      diagnostics: [],
      raw: src,
      formatted,
    };
  },

  toJs({file, src}) {
    // Parse the JSON to make sure it's valid
    const mode = getRJSONMode(file.real);
    const obj = parseJSON({
      mode,
      filename: file.uid,
      input: src,
      locations: false,
    });

    const rawJson = JSON.stringify(obj);
    const json: string = rawJson === undefined ? 'undefined' : rawJson;

    // TODO handle unicode newlines here
    return `export default ${json};`;
  },
};

const jsExt: ExtensionHandler = {
  canAnalyze: true,
  hasteMode: 'ext',

  toJs({src}): string {
    return src;
  },

  async lint(info: LintInfo): Promise<LintResult> {
    const {file: ref, projectConfig, prefetchedModuleSignatures, worker} = info;
    const {ast, src}: ParseResult = await worker.parse(ref);

    worker.logger.info(`Linting: `, ref.real);

    // Run the compiler in lint-mode which is where all the rules are actually ran
    const res = await compiler.lint({
      options: {},
      ast,
      projectConfig,
      src,
    });

    // Extract lint diagnostics
    let {diagnostics} = res;

    // Only enable typechecking if enabled in .romeconfig
    let typeCheckingEnabled = projectConfig.typeChecking.enabled === true;
    if (projectConfig.typeChecking.libs.includes(ref.real) === true) {
      // don't typecheck lib files
      typeCheckingEnabled = false;
    }

    // Run type checking if necessary
    if (typeCheckingEnabled) {
      const typeCheckProvider = await worker.getTypeCheckProvider(
        ref.project,
        prefetchedModuleSignatures,
      );
      const typeDiagnostics = await typeCheck({
        ast,
        filename: ref.uid,
        provider: typeCheckProvider,
        projectConfig,
      });
      diagnostics = [...diagnostics, ...typeDiagnostics];
    }

    return {
      diagnostics,
      raw: src,
      formatted: res.src,
    };
  },
};

const extensions: Map<string, ExtensionHandler> = new Map();

// setup text
extensions.set('html', textExt);
extensions.set('htm', textExt);
extensions.set('css', textExt);
extensions.set('txt', textExt);
extensions.set('md', textExt);

// setup images and videos
extensions.set('png', binaryExt);
extensions.set('jpg', binaryExt);
extensions.set('jpeg', binaryExt);
extensions.set('gif', binaryExt);
extensions.set('webm', binaryExt);
extensions.set('mp4', binaryExt);
extensions.set('m4v', binaryExt);
extensions.set('avi', binaryExt);
extensions.set('mkv', binaryExt);
extensions.set('mp3', binaryExt);

// setup JS
extensions.set('js', jsExt);
extensions.set('jsx', jsExt);
extensions.set('mjs', jsExt);
extensions.set('rjs', jsExt);
extensions.set('ts', jsExt);
extensions.set('tsx', jsExt);

// setup JSON
extensions.set('json', jsonExt);
extensions.set('rjson', jsonExt);

// Used when filtering files
export const JS_EXTENSIONS = ['js', 'mjs', 'rjs', 'ts', 'tsx', 'jsx'];

// These are extensions that be implicitly tried when a file is referenced
// This is mostly for compatibility with Node.js projects. This list should not
// be extended. Explicit extensions are required in the browser for as modules and
// should be required everywhere.
// TypeScript is unfortunately included here as it produces an error if you use an
// import source with ".ts"
export const IMPLICIT_JS_EXTENSIONS = ['js', 'js', 'json', 'ts', 'tsx'];
