import {
  Node,
  ExpressionNode,
  ObjectPropertyNode,
  JSXIdentifierNode,
  JSXNamespacedNameNode,
  JSXAttributeNode,
  ObjectProperties,
  MemberExpressionNode,
  ThisExpressionNode,
  StringLiteralNode,
  IdentifierNode,
} from '@romejs/js-ast';
import {
  objectExpression,
  objectProperty,
  stringLiteral,
  identifier,
  memberExpression,
  thisExpression,
  booleanLiteral,
  nullLiteral,
  callExpression,
  jsxIdentifier,
} from '@romejs/js-ast';
import {Path} from '@romejs/js-compiler';
import {
  template,
  isValidIdentifierName,
  isReferenced,
  inheritLoc,
} from '@romejs/js-ast-utils';

function isCompatTag(tagName: undefined | string): boolean {
  return tagName !== undefined ? /^[a-z]|-/.test(tagName) : false;
}

function convertJSXIdentifier(
  path: Path,
):
  | MemberExpressionNode
  | ThisExpressionNode
  | StringLiteralNode
  | JSXNamespacedNameNode
  | IdentifierNode {
  const {node} = path;

  if (node.type === 'JSXIdentifier') {
    if (node.name === 'this' && isReferenced(path)) {
      return thisExpression.create({});
    } else if (isValidIdentifierName(node.name)) {
      return identifier.create({
        name: node.name,
        loc: inheritLoc(node),
      });
    } else {
      return stringLiteral.create({value: node.name});
    }
  } else if (node.type === 'JSXMemberExpression') {
    return memberExpression.create({
      object: convertJSXIdentifier(path.getChildPath('object')),
      property: convertJSXIdentifier(path.getChildPath('property')),
    });
  } else if (node.type === 'JSXNamespacedName') {
    return node;
  } else {
    throw new Error(
      `Received a node of type ${
        node.type
      }, the only node types that should be in this position are JSXIdentifier and JSXMemberExpression`,
    );
  }
}

function convertAttributeValue(node: Node): Node {
  if (node.type === 'JSXExpressionContainer') {
    return node.expression;
  } else {
    return node;
  }
}

function extractName(node: JSXIdentifierNode | JSXNamespacedNameNode): string {
  if (node.type === 'JSXNamespacedName') {
    throw new Error('JSX is not XML blah blah blah');
  } else {
    return jsxIdentifier.assert(node).name;
  }
}

function convertAttribute(node: JSXAttributeNode): ObjectPropertyNode {
  let valueNode = convertAttributeValue(
    node.value || booleanLiteral.create({value: true}),
  );
  if (
    valueNode.type === 'StringLiteral' &&
    (!node.value || node.value.type !== 'JSXExpressionContainer')
  ) {
    valueNode = stringLiteral.create({
      value: valueNode.value.replace(/\n\s+/g, ' '),
    });
  }

  const name = extractName(node.name);
  let nameNode;
  if (isValidIdentifierName(name)) {
    nameNode = identifier.create({
      name,
      loc: inheritLoc(node),
    });
  } else {
    nameNode = stringLiteral.create({value: name});
  }

  return objectProperty.create({
    key: nameNode,
    value: valueNode,
  });
}

function pushProps(
  _props: ObjectProperties,
  objs: Array<ExpressionNode>,
): ObjectProperties {
  if (!_props.length) {
    return _props;
  }

  objs.push(objectExpression.create({properties: _props}));
  return [];
}

function buildOpeningElementAttributes(attribs: Array<Node>): ExpressionNode {
  let _props: ObjectProperties = [];
  const objs: Array<ExpressionNode> = [];

  while (attribs.length) {
    const prop = attribs.shift();
    if (prop.type === 'JSXSpreadAttribute') {
      _props = pushProps(_props, objs);
      objs.push(prop.argument);
    } else {
      _props.push(convertAttribute(prop));
    }
  }

  pushProps(_props, objs);

  let ret: ExpressionNode;
  if (objs.length === 1) {
    // only one object
    ret = objs[0];
  } else {
    // looks like we have multiple objects
    if (objs[0].type !== 'ObjectExpression') {
      objs.unshift(objectExpression.create({properties: []}));
    }

    // spread it
    ret = callExpression.create({
      callee: template.expression`Object.assign`,
      arguments: objs,
    });
  }

  return ret;
}

function cleanJSXElementLiteralChild(
  value: string,
): undefined | StringLiteralNode {
  const lines = value.split(/\r\n|\n|\r/);

  let lastNonEmptyLine = 0;

  for (let i = 0; i < lines.length; i++) {
    if (lines[i].match(/[^ \t]/)) {
      lastNonEmptyLine = i;
    }
  }

  let str = '';

  for (let i = 0; i < lines.length; i++) {
    const line = lines[i];

    const isFirstLine = i === 0;
    const isLastLine = i === lines.length - 1;
    const isLastNonEmptyLine = i === lastNonEmptyLine;

    // replace rendered whitespace tabs with spaces
    let trimmedLine = line.replace(/\t/g, ' ');

    // trim whitespace touching a newline
    if (!isFirstLine) {
      trimmedLine = trimmedLine.replace(/^[ ]+/, '');
    }

    // trim whitespace touching an endline
    if (!isLastLine) {
      trimmedLine = trimmedLine.replace(/[ ]+$/, '');
    }

    if (trimmedLine) {
      if (!isLastNonEmptyLine) {
        trimmedLine += ' ';
      }

      str += trimmedLine;
    }
  }

  if (str != '') {
    return stringLiteral.create({value: str});
  }
}

function buildChildren(children: Array<Node>): Array<ExpressionNode> {
  const elems = [];

  for (let child of children) {
    if (child.type === 'JSXText') {
      child = cleanJSXElementLiteralChild(child.value);
    }

    if (child === undefined) {
      continue;
    }

    if (child.type === 'JSXExpressionContainer') {
      child = child.expression;
    }

    if (child.type === 'JSXEmptyExpression') {
      continue;
    }

    if (child !== undefined) {
      elems.push(child);
    }
  }

  return elems;
}

export default {
  name: 'jsx',
  enter(path: Path): Node {
    const {node} = path;

    if (node.type === 'JSXElement') {
      let type = convertJSXIdentifier(
        path.getChildPath('openingElement').getChildPath('name'),
      );

      if (type.type === 'JSXNamespacedName') {
        /*context.addNodeDiagnostic(type, {
          category: 'compile/jsx',
          message: 'JSX is not XML',
        });*/
        // TODO better handle this
      }

      if (type.type === 'Identifier' && isCompatTag(type.name)) {
        type = stringLiteral.create({value: type.name});
      }

      let attribs = node.openingElement.attributes;
      if (attribs.length) {
        attribs = buildOpeningElementAttributes(attribs);
      } else {
        attribs = nullLiteral.create({});
      }

      return callExpression.create({
        callee: template.expression`React.createElement`,
        arguments: [type, attribs, ...buildChildren(node.children)],
      });
    }

    if (node.type === 'JSXFragment') {
      const type = template.expression`React.Fragment`;
      const attribs = template.expression`null`;
      return callExpression.create({
        callee: template.expression`React.createElement`,
        arguments: [type, attribs, ...buildChildren(node.children)],
      });
    }

    return node;
  },
};
