import {UnknownObject} from '@romejs/refinement';
import {Master} from '@romejs/core';
import {Reporter} from '@romejs/cli-reporter';
import {
  BundlerConfig,
  BundleResult,
  BundlerFiles,
  BundleRequestResult,
  BundlerMode,
} from '../../common/types/bundler';
import {MasterRequest} from '@romejs/core';
import {DiagnosticsProcessor} from '@romejs/diagnostics';
import {ResolverOptions} from '../Resolver';
import DependencyGraph from '../dependencies/DependencyGraph';
import BundleRequest from './BundleRequest';
import invariant from '@romejs/invariant';

export default class Bundler {
  constructor(
    req: MasterRequest,
    reporter: Reporter,
    config: BundlerConfig,
    resolverOpts: ResolverOptions,
  ) {
    this.config = config;
    this.master = req.master;
    this.reporter = reporter;
    this.request = req;

    this.entries = [];

    this.graph = new DependencyGraph(req.master, resolverOpts);

    this.diagnosticsProcessor = new DiagnosticsProcessor({
      origins: [],
    });

    this.resolverOpts = resolverOpts;
  }

  diagnosticsProcessor: DiagnosticsProcessor;
  graph: DependencyGraph;
  master: Master;
  request: MasterRequest;
  reporter: Reporter;
  entries: Array<string>;
  resolverOpts: ResolverOptions;
  config: BundlerConfig;

  static createFromMasterRequest(req: MasterRequest): Bundler {
    return new Bundler(
      req,
      req.reporter,
      req.getBundlerConfigFromFlags(),
      req.getResolverOptionsFromFlags(),
    );
  }

  async getResolvedEntry(unresolvedEntry: string): Promise<string> {
    const {cwd} = this.config;

    // Get file resolved as an entry
    const resolvedEntry = await this.master.resolver.resolveEntryAssert({
      ...this.resolverOpts,
      origin: cwd,
      source: unresolvedEntry,
    });

    if (resolvedEntry === undefined) {
      invariant(
        false,
        'Could not find entry file %s relative to %s',
        unresolvedEntry,
        cwd,
      );
    }

    return resolvedEntry;
  }

  createBundleRequest(resolvedEntry: string, mode: BundlerMode): BundleRequest {
    this.entries.push(resolvedEntry);
    return new BundleRequest(this, mode, resolvedEntry);
  }

  async bundle(unresolvedEntry: string): Promise<BundleResult> {
    const {reporter, master} = this;
    const {cwd} = this.config;
    const resolvedEntry = await this.getResolvedEntry(unresolvedEntry);

    // Figure out the bundler mode, if there was none passed to the BundlerConfig then we'll
    // use the project config bundler.mode
    let mode = this.config.mode;
    if (mode === undefined) {
      const project = master.projectManager.assertProjectExisting(
        resolvedEntry,
      );
      mode = project.config.bundler.mode;
    }

    // Now do the same resolver request but with a package
    const manifestRoot = master.resolver.resolve({
      ...this.resolverOpts,
      origin: cwd,
      type: 'package',
      source: unresolvedEntry,
    });
    let manifestRaw;
    let manifest;
    if (manifestRoot !== undefined) {
      const def = master.memoryFs.getManifestDefinition(manifestRoot);
      if (def !== undefined) {
        manifest = def.manifest;
        manifestRaw = def.raw;
      }
    }

    const createBundle = async (
      resolvedSegment: string,
    ): Promise<BundleRequestResult> => {
      reporter.info(
        `Bundling <emphasis><filelink filename="${resolvedSegment}" /></emphasis>`,
      );

      if (mode === undefined) {
        throw new Error(
          'This shouldnt be undefined after our refinement above...',
        );
      }

      const req = this.createBundleRequest(resolvedSegment, mode);
      const res = await req.bundle();
      this.diagnosticsProcessor.addDiagnostics(res.diagnostics);
      return res;
    };

    const res: BundleRequestResult = await createBundle(resolvedEntry);
    this.diagnosticsProcessor.maybeThrowDiagnosticsError();

    const files: BundlerFiles = new Map();
    files.set('index.js', res.content);
    files.set('index.js.map', JSON.stringify(res.map));

    //
    const bundleBuddyStats = this.graph.getBundleBuddyStats(this.entries);
    files.set('bundlebuddy.json', JSON.stringify(bundleBuddyStats, null, '  '));

    // TODO ensure that __dirname is relative to the project root

    if (
      manifestRaw !== undefined &&
      manifest !== undefined &&
      manifestRoot !== undefined
    ) {
      const newManifest: UnknownObject = {
        ...manifestRaw,
        main: 'index.js',
        browser: 'index.min.js',
      };

      // TODO Compile a index.d.ts

      // TODO copy manifest.files
      if (Array.isArray(manifest.files)) {
        for (const pattern of manifest.files) {
          if (typeof pattern !== 'string') {
            continue;
          }

          // TODO add all files that match this glob
        }
      }

      // Compile manifest.bin files
      const bin = manifest.bin;
      if (bin !== undefined) {
        const newBin: Dict<string> = {};
        newManifest.bin = newBin;

        for (const [binName, relative] of manifest.bin) {
          const absolute = await master.resolver.resolvePath({
            origin: manifestRoot,
            source: relative,
            ...this.resolverOpts,
          });

          if (absolute === undefined) {
            // TODO warn
            continue;
          }

          const binOut = `bin/${binName}`;
          const binIndex = `${binOut}/index.js`;
          newBin[binName] = binIndex;

          // Bundle
          const res = await createBundle(absolute);

          // Add the files to be output
          files.set(binIndex, res.content);
          files.set(`${binOut}/index.map`, JSON.stringify(res.map));
        }
      }

      // Add a package.json with updated values
      files.set('package.json', JSON.stringify(newManifest, undefined, '  '));
    }

    this.diagnosticsProcessor.maybeThrowDiagnosticsError();

    return {
      manifest,
      files,
      map: res.map,
      content: res.content,
    };
  }
}
