import {JSNodeBase, FunctionBase, PropertyNameBase} from '../index';
import {createBuilder, METHOD_FUNCTION_VISITOR_KEYS} from '../utils';

export type ObjectMethodKind = 'get' | 'set' | 'method';

export type ObjectMethodNode = JSNodeBase &
  FunctionBase &
  PropertyNameBase & {
    type: 'ObjectMethod';
    kind: ObjectMethodKind;
  };

export const objectMethod = createBuilder<ObjectMethodNode>('ObjectMethod', {
  visitorKeys: METHOD_FUNCTION_VISITOR_KEYS,
});
