import {JSNodeBase, ExpressionNode, SwitchCaseNode} from '../index';
import {createBuilder} from '../utils';

export type SwitchStatementNode = JSNodeBase & {
  type: 'SwitchStatement',
  discriminant: ExpressionNode,
  cases: Array<SwitchCaseNode>,
};

export const switchStatement = createBuilder<SwitchStatementNode>(
  'SwitchStatement',
  {visitorKeys: ['discriminant', 'cases']},
);
