import {JSNodeBase, ExpressionNode, StatementNode} from '../index';
import {createBuilder} from '../utils';

export type IfStatementNode = JSNodeBase & {
  type: 'IfStatement',
  test: ExpressionNode,
  consequent: StatementNode,
  alternate?: undefined | StatementNode,
};

export const ifStatement = createBuilder<IfStatementNode>('IfStatement', {
  visitorKeys: ['test', 'consequent', 'alternate'],
});
