import {MasterRequest} from '@romejs/core';
import {FlagParser} from '@romejs/cli-flags';
import {Consumer} from '@romejs/codec-json';
import {createMasterCommand} from '../../commands';
import Bundler from '../bundler/Bundler';
import {commandCategories} from '../../commands';

import path = require('path');
import fs = require('fs');

export default createMasterCommand({
  category: commandCategories.SOURCE_CODE,
  description: 'build a standalone js bundle for a package',

  parseCLIFlags(p: FlagParser) {
    return {
      dumpModuleOrder: p.boolean('dump-module-order', {
        description: '',
        default: false,
      }),
    };
  },

  async default(
    req: MasterRequest,
    flagConsumer: Consumer,
  ): Promise<{
    content: string;
    map: string;
  }> {
    const {args, flags, reporter} = req;

    if (args.length !== 2) {
      throw new Error('Expected two arguments');
    }

    const [entryFilename, outputFolder] = args;
    const bundler = Bundler.createFromMasterRequest(req);

    const {files: outFiles, content, map} = await bundler.bundle(entryFilename);

    const savedList = [];
    const dir = path.resolve(flags.cwd, outputFolder);
    for (const [filename, content] of outFiles) {
      const loc = path.join(dir, filename);
      savedList.push(`<filelink filename="${loc}">${filename}</filelink>`);
      await fs.promises.mkdir(path.dirname(loc), {recursive: true});
      await fs.promises.writeFile(loc, content);
    }

    reporter.success(`Saved bundle to folder <filelink filename="${dir}" />`);
    reporter.list(savedList);

    if (flagConsumer.getProperty('dumpModuleOrder').asBoolean(false)) {
      reporter.info(`Module order from root entry`);

      reporter.list(
        bundler.graph.getNode(bundler.entries[0]).getDependencyOrder().files,
      );
    }

    return {
      content,
      map: JSON.stringify(map),
    };
  },
});
