import {UnknownObject} from '@romejs/refinement';
import {
  EventOptions,
  BridgeMessage,
  BridgeErrorMessageValue,
  BridgeErrorResponseMessage,
  BridgeRequestMessage,
  BridgeSuccessResponseMessage,
  BridgeOptions,
} from './types';
import {PartialDiagnosticAdvice} from '@romejs/diagnostics';
import {ErrorFrames} from '@romejs/v8';
import {errors} from '@romejs/v8';
import BridgeError from './BridgeError';
import BridgeEvent from './BridgeEvent';
import Event from './Event';

class RPCError extends Error {
  constructor(opts: BridgeErrorMessageValue) {
    super(opts.message);

    this.stack = opts.stack;
    this.name = opts.name;
    this.__ROME_ERROR_ADVICE__ = opts.__ROME_ERROR_ADVICE__;
    this.__ROME_POP_FRAMES__ = opts.__ROME_POP_FRAMES__;
    this.__ROME_ERROR_FRAMES__ = opts.__ROME_ERROR_FRAMES__;
  }

  // TODO this is duplicated from 'V8Error but necessary due to cycles
  __ROME_ERROR_FRAMES__: undefined | ErrorFrames;
  __ROME_ERROR_ADVICE__: undefined | PartialDiagnosticAdvice;
  __ROME_POP_FRAMES__: undefined | number;
}

type ErrorTransport = {
  serialize: (err: Error) => UnknownObject;
  hydrate: (err: BridgeErrorMessageValue, obj: UnknownObject) => Error;
};

export default class Bridge {
  constructor(opts: BridgeOptions) {
    this.errorTransports = new Map();

    this.alive = true;
    this.opts = opts;

    this.messageIdCounter = 0;
    this.events = new Map();

    this.hasHandshook = false;
    this.handshakeEvent = new Event({name: 'Bridge.handshake'});
    this.endEvent = new Event({name: 'Bridge.end'});

    // A Set of event names that are being listened to on the other end
    // We track this to avoid sending over subscriptions that aren't needed
    this.listeners = new Set();

    this.heartbeatEvent = this.createEvent({name: 'Bridge.heartbeat'});
    this.heartbeatEvent.subscribe(() => {
      return undefined;
    });

    this.clear();
    this.init();
  }

  heartbeatEvent: BridgeEvent<void, void>;
  heartbeatTimeout: undefined | NodeJS.Timeout;

  handshakeEvent: Event<{first: boolean}, void>;
  hasHandshook: boolean;
  endEvent: Event<Error, void>;

  alive: boolean;

  messageIdCounter: number;
  listeners: Set<string>;
  events: Map<string, BridgeEvent<any, any>>;

  opts: BridgeOptions;
  errorTransports: Map<string, ErrorTransport>;

  monitorHeartbeat(
    timeout: number,
    onExceeded: () => undefined | Promise<void>,
  ) {
    this.heartbeatTimeout = setTimeout(async () => {
      try {
        await this.heartbeatEvent.call(undefined, timeout);
        this.monitorHeartbeat(timeout, onExceeded);
      } catch (err) {
        if (err instanceof BridgeError) {
          if (this.alive) {
            onExceeded();
          }
        } else {
          throw err;
        }
      }
    }, 1000);
  }

  async handshake(timeout?: number): Promise<void> {
    if (this.hasHandshook) {
      throw new Error('Already performed handshake');
    }

    // Send a handshake in case we were the first
    this.sendMessage({type: 'handshake', first: true});

    // Wait for a handshake from the other end
    const res = await this.handshakeEvent.wait(undefined, timeout);

    if (res.first) {
      // Send the handshake again, as it wouldn't have received the first
      this.sendMessage({type: 'handshake', first: false});
    }

    this.hasHandshook = true;
    this.sendSubscriptions();
  }

  sendSubscriptions() {
    if (!this.hasHandshook) {
      // If we haven't had the handshake then no point sending them. They'll be sent all at once after
      return undefined;
    }

    // Notify the other side of what we're currently subscribed to
    // We send over a list of all of our subscriptions every time
    // This is fine since we don't change subscriptions often and they aren't very large
    // If we have a lot of subscriptions, or are changing them a lot in the future then this could be optimized
    const names = [];
    for (const event of this.events.values()) {
      if (event.hasSubscriptions()) {
        names.push(event.name);
      }
    }
    this.sendMessage({
      type: 'subscriptions',
      names,
    });
  }

  receivedSubscriptions(names: Array<string>) {
    this.listeners = new Set(names);
  }

  init() {
    // This method can be overridden by subclasses, it allows you to add logic such as error serializers
  }

  clear() {
    for (const [, event] of this.events) {
      event.clear();
    }
  }

  getNextMessageId(): number {
    return ++this.messageIdCounter;
  }

  createEvent<Param, Ret>(opts: EventOptions): BridgeEvent<Param, Ret> {
    if (this.events.has(opts.name)) {
      throw new Error('Duplicate event');
    }

    const event = new BridgeEvent<Param, Ret>(opts, this);
    this.events.set(opts.name, event);
    return event;
  }

  //# Connection death

  assertAlive() {
    if (this.alive === false) {
      throw new Error('Bridge is dead');
    }
  }

  endWithError(err: Error) {
    if (this.alive === false) {
      return undefined;
    }

    this.alive = false;

    // Reject any pending requests
    for (const [, event] of this.events) {
      event.end(err);
    }
    this.clear();

    // Clear any currently processing heartbeat
    if (this.heartbeatTimeout !== undefined) {
      clearTimeout(this.heartbeatTimeout);
    }

    // Notify listeners
    this.endEvent.send(err);
  }

  end(message: string = 'Connection died') {
    this.endWithError(new BridgeError(message));
  }

  //# Error serialization

  buildError(value: BridgeErrorMessageValue, data: UnknownObject) {
    const transport = this.errorTransports.get(value.name);
    if (transport === undefined) {
      return new RPCError(value);
    } else {
      return transport.hydrate(value, data);
    }
  }

  buildErrorResponse(
    id: number,
    event: string,
    errRaw: unknown,
  ): BridgeErrorResponseMessage {
    // Just in case something that wasn't an Error was thrown
    const err = errRaw instanceof Error ? errRaw : new Error(String(errRaw));

    // Fetch some metadata for hydration
    const tranport = this.errorTransports.get(err.name);
    const metadata: UnknownObject =
      tranport === undefined ? {} : tranport.serialize(err);

    const v8Err = errors.toV8Error(err);
    return {
      id,
      event,
      type: 'response',
      responseStatus: 'error',
      value: {
        name: err.name,
        message: err.message,
        stack: err.stack,
        __ROME_ERROR_FRAMES__: v8Err.__ROME_ERROR_FRAMES__,
        __ROME_ERROR_ADVICE__: v8Err.__ROME_ERROR_ADVICE__,
        __ROME_POP_FRAMES__: v8Err.__ROME_POP_FRAMES__,
      },
      metadata,
    };
  }

  addErrorTransport(name: string, transport: ErrorTransport) {
    this.errorTransports.set(name, transport);
  }

  //# Message transmission

  sendMessage(msg: BridgeMessage) {
    // There's no try-catch gated around sendMessage because the call stack here will include some other error handler
    // We need to be specific for handleMessage because it could come from 'anywhere

    if (msg.type !== 'handshake' && !this.hasHandshook) {
      throw new Error(
        `Can't send a message before the handshake has been completed`,
      );
    }

    this.assertAlive();

    const {opts} = this;
    opts.sendMessage(msg);
    if (opts.onSendMessage !== undefined) {
      opts.onSendMessage(msg);
    }
  }

  handleJSONMessage(str: string) {
    try {
      const data = JSON.parse(str);
      this.handleMessage(data);
    } catch (err) {
      if (err instanceof SyntaxError) {
        this.endWithError(
          new BridgeError(`Error parsing message JSON: ${err.message}`),
        );
      } else {
        this.endWithError(err);
      }
    }
  }

  handleMessage(msg: BridgeMessage) {
    try {
      this.assertAlive();

      if (msg.type === 'handshake') {
        this.handshakeEvent.send({first: msg.first});
      }

      if (msg.type === 'subscriptions') {
        this.receivedSubscriptions(msg.names);
      }

      if (msg.type === 'request') {
        this.handleMessageRequest(msg);
      }

      if (msg.type === 'response') {
        this.handleMessageResponse(msg);
      }
    } catch (err) {
      this.endWithError(err);
    }
  }

  handleMessageResponse(
    data: BridgeSuccessResponseMessage | BridgeErrorResponseMessage,
  ) {
    const {id, event} = data;
    if (id === undefined) {
      throw new Error('Expected id');
    }
    if (event === undefined) {
      throw new Error('Expected event');
    }

    const eventHandler = this.events.get(event);
    if (eventHandler === undefined) {
      throw new Error('Unknown event');
    }

    eventHandler.dispatchResponse(id, data);
  }

  handleMessageRequest(data: BridgeRequestMessage) {
    const {id, event, param} = data;
    if (event === undefined) {
      throw new Error('Expected event in message request but received none');
    }

    const eventHandler = this.events.get(event);
    if (eventHandler === undefined) {
      throw new Error(`Unknown event ${event}`);
    }

    if (id === undefined) {
      // We don't need to do anything with the return value of this since
      // there's nothing on the other end to catch it
      eventHandler.dispatchRequest(param);
    } else {
      eventHandler
        .dispatchRequest(param)
        .then(value => {
          this.sendMessage({
            event,
            id,
            type: 'response',
            responseStatus: 'success',
            value,
          });
        })
        .catch(err => {
          this.sendMessage(this.buildErrorResponse(id, event, err));
        })
        .catch(err => this.endWithError(err));
    }
  }
}
