import {JSNodeBase, TSSignatureDeclarationOrIndexSignatureBase} from '../index';
import {createBuilder} from '../utils';

export type TSIndexSignatureNode = JSNodeBase &
  TSSignatureDeclarationOrIndexSignatureBase & {
    type: 'TSIndexSignature';
    readonly?: undefined | boolean;
  };

export const tsIndexSignature = createBuilder<TSIndexSignatureNode>(
  'TSIndexSignature',
  {visitorKeys: ['typeAnnotation', 'parameters']},
);
