import {
  JSNodeBase,
  SuperNode,
  ImportNode,
  ExpressionNode,
  SpreadElementNode,
  TypeArgumentsNode,
} from '../index';
import {createBuilder} from '../utils';

export type OptionalCallExpressionNode = JSNodeBase & {
  type: 'OptionalCallExpression';
  callee: ExpressionNode | SuperNode | ImportNode;
  arguments: Array<ExpressionNode | SpreadElementNode>;
  typeArguments?: undefined | TypeArgumentsNode;
};

export const optionalCallExpression = createBuilder<OptionalCallExpressionNode>(
  'OptionalCallExpression',
  {
    visitorKeys: ['callee', 'arguments'],
  },
);
