import Master from './Master';
import {Project, ProjectPackage, ProjectConfig} from '../common/types/project';
import {WorkerProjects} from '../common/bridges/WorkerBridge';
import {FileReference} from '@romejs/core';
import {WorkerContainer} from './WorkerManager';
import {DiagnosticsProcessor} from '@romejs/diagnostics';
import {loadCompleteProjectConfig} from './project/load';
import {
  ROME_CONFIG_FILENAME,
  ROME_CONFIG_PACKAGE_JSON_KEY,
  ROME_CONFIG_WARN_FILENAMES,
} from '../common/constants';
import {consumeJSON} from '@romejs/codec-json';
import walkDirectoryPath from '../common/utils/walkDirectoryPath';
import {parsePath, matchPathPatterns} from '@romejs/match-path';

import path = require('path');
import fs = require('fs');

type ProjectAddRequest = {
  dir: string;
  isPackage: boolean;
};

function cleanName(name: string): string {
  if (name[0] === '@') {
    return name.slice(1);
  } else {
    return name;
  }
}

export default class ProjectManager {
  constructor(master: Master) {
    this.master = master;

    this.isAddingProject = false;
    this.pendingAddProjects = [];

    this.projectIdCounter = 0;

    this.projectFolderToId = new Map();
    this.projectConfigDependenciesToIds = new Map();
    this.fileToProject = new Map();
    this.projects = new Map();

    // We maintain these maps so we can reverse any uids, and protect against collisions
    this.uidToFilename = new Map();
    this.filenameToUid = new Map();
  }

  master: Master;

  projectFolderToId: Map<string, number>;

  isAddingProject: boolean;
  pendingAddProjects: Array<{
    request: ProjectAddRequest;
    resolve: (project: Project) => void;
  }>;

  uidToFilename: Map<string, string>;
  filenameToUid: Map<string, string>;

  projects: Map<number, Project>;
  projectConfigDependenciesToIds: Map<string, Set<number>>;
  projectIdCounter: number;
  fileToProject: Map<string, number>;

  getFilenameFromUid(filename: string): undefined | string {
    return this.uidToFilename.get(filename);
  }

  getUid(filename: string): string {
    const existing = this.filenameToUid.get(filename);
    if (existing !== undefined) {
      return existing;
    }

    // Format of uids will be <PROJECT_NAME>/<PACKAGE_NAME>/<RELATIVE>
    const project = this.assertProjectExisting(filename);
    const parts: Array<string> = [];
    parts.push(project.config.name);

    if (this.isHasteDeclared(filename, project)) {
      const hasteName = this.master.memoryFs.getHasteName(filename);
      if (hasteName === undefined) {
        throw new Error(
          'isHasteDeclared returned true so this should always return a valid name',
        );
      }
      parts.push(hasteName);
    } else {
      // If we have a package then use it as the root and push it's name
      let root = project.dir;
      const pkg = this.getOwnedPackage(filename);
      if (pkg !== undefined) {
        parts.push(pkg.manifest.name);
        root = pkg.folder;

        // If the project name is "@romejs" and the package name is "@romejs/foo" then we'll have "@romejs/foo/foo"
        // which is pretty gross. So here we fix that up.
        if (cleanName(parts[1]).startsWith(cleanName(parts[0]))) {
          parts.shift();
        }
      }

      const relative = path.relative(root, filename);
      parts.push(relative);
    }

    const uid = cleanName(parts.join('/'));

    // Verify we didn't already generate this uid for another file
    const collided = this.uidToFilename.get(uid);
    if (collided !== undefined && collided !== filename) {
      throw new Error(
        `UID collision between ${filename} and ${collided}: ${uid}`,
      );
    }

    this.uidToFilename.set(uid, filename);
    this.filenameToUid.set(filename, uid);

    return uid;
  }

  getFileReference(filename: string): FileReference {
    const project = this.assertProjectExisting(filename);
    const uid = this.getUid(filename);
    return {
      uid,
      project: project.id,
      real: filename,
    };
  }

  // Get the closest package to a file
  getOwnedPackage(filename: string): undefined | ProjectPackage {
    // First, let's find the highest manifest so we can check the projects against it
    let expectedFolder;
    for (const dir of walkDirectoryPath(path.dirname(filename))) {
      if (this.master.memoryFs.getManifest(dir) !== undefined) {
        expectedFolder = dir;
        break;
      }
    }

    // Find a package from the projects that match this filename
    for (const project of this.getHierarchyFromFilename(filename)) {
      for (const pkg of project.packages.values()) {
        if (pkg.folder === expectedFolder) {
          return pkg;
        }
      }
    }

    return undefined;
  }

  async maybeEvictPossibleConfig(filename: string): Promise<boolean> {
    // check if this filename is a rome config dependency
    const projectIds = this.projectConfigDependenciesToIds.get(filename);
    if (projectIds === undefined) {
      return false;
    }

    for (const evictProjectId of projectIds) {
      // Fetch the project
      const project = this.projects.get(evictProjectId);
      if (project === undefined) {
        throw new Error(
          `Expected project of id ${evictProjectId} since it was declared in projectConfigLocsToId`,
        );
      }

      // Remove the config locs from 'our internal map that belong to this project
      for (const [configLoc, projectIds] of this
        .projectConfigDependenciesToIds) {
        if (projectIds.has(evictProjectId)) {
          projectIds.delete(evictProjectId);
        }

        if (projectIds.size === 0) {
          this.projectConfigDependenciesToIds.delete(configLoc);
        }
      }

      // Notify all workers that it should delete the project
      for (const {bridge} of this.master.workerManager.getWorkers()) {
        bridge.evictProject.send({id: evictProjectId});
      }

      // Delete the project from 'our internal map
      this.projects.delete(evictProjectId);
      this.projectFolderToId.delete(project.dir);

      // Evict all files that belong to this project and delete their project mapping
      const ownedFiles: Array<string> = [];
      for (const [filename, projectId] of this.fileToProject) {
        if (evictProjectId === projectId) {
          this.fileToProject.delete(filename);
          ownedFiles.push(filename);
        }
      }
      await Promise.all(
        ownedFiles.map(filename => this.master.fileManager.evict(filename)),
      );
    }

    return true;
  }

  getProjects(): Array<Project> {
    return Array.from(this.projects.values());
  }

  async queueAddProject(request: ProjectAddRequest): Promise<Project> {
    const projectFolder = request.dir;

    // Check if we've already loaded this project
    const maybeProject = this.findProjectExisting(projectFolder);
    if (maybeProject !== undefined) {
      return maybeProject;
    }

    // If we're currently adding a project then add it to the queue
    if (this.isAddingProject) {
      return new Promise(resolve => {
        this.pendingAddProjects.push({request, resolve});
      });
    }

    // First time loading this project
    this.isAddingProject = true;

    // fetch this project
    const mainProject = await this.addProject(request);
    const resolvedProjectsByDir: Map<string, Project> = new Map();
    resolvedProjectsByDir.set(projectFolder, mainProject);

    // Resolve all pending projects that were added while we were adding the current project
    const resolvedProjects: Array<{
      project: Project;
      resolve: (project: Project) => void;
    }> = [];
    for (const {request, resolve} of this.pendingAddProjects) {
      // Check if the project has already been resolved
      const existing = resolvedProjectsByDir.get(request.dir);
      if (existing !== undefined) {
        resolvedProjects.push({project: existing, resolve});
      } else {
        // It hasn't been resolved yet so let's add it
        const project = await this.addProject(request);
        resolvedProjects.push({project, resolve});
      }
    }

    // Resolve all promises
    for (const {project, resolve} of resolvedProjects) {
      resolve(project);
    }

    // Cleanup
    this.pendingAddProjects = [];
    this.isAddingProject = false;

    return mainProject;
  }

  addDependencyToProjectId(filename: string, projectId: number): void {
    const ids = this.projectConfigDependenciesToIds.get(filename);

    if (ids === undefined) {
      this.projectConfigDependenciesToIds.set(filename, new Set());
      return this.addDependencyToProjectId(filename, projectId);
    }

    ids.add(projectId);
  }

  async addProject(request: ProjectAddRequest): Promise<Project> {
    const projectFolder = request.dir;

    // get the configuration
    let configLoc = path.join(projectFolder, ROME_CONFIG_FILENAME);
    let subKey = undefined;
    if (request.isPackage) {
      configLoc = path.join(projectFolder, 'package.json');
      subKey = ROME_CONFIG_PACKAGE_JSON_KEY;
    }
    const config = loadCompleteProjectConfig(projectFolder, configLoc, subKey);

    // Make sure there's no project with the same `name` as us
    for (const project of this.projects.values()) {
      if (project.config.name === config.name) {
        throw new Error(
          `Conflicting project names. ${projectFolder} and ${project.dir}`,
        );
      }
    }

    // Declare the project
    const parentProject = this.findProjectExisting(path.dirname(projectFolder));
    const project: Project = {
      config,
      configLoc,
      dir: projectFolder,
      id: this.projectIdCounter++,
      packages: new Map(),
      hasteMap: new Map(),
      parent: parentProject,
    };
    this.projects.set(project.id, project);
    this.fileToProject.set(projectFolder, project.id);
    this.projectFolderToId.set(projectFolder, project.id);

    // Add all project config dependencies so changes invalidate the whole project
    this.addDependencyToProjectId(configLoc, project.id);
    for (const loc of config.configDependencies) {
      this.addDependencyToProjectId(loc, project.id);
    }

    // Notify other pieces of our creation
    this.master.cache.onNewProject(project);
    this.master.workerManager.onNewProject(project);

    // Start watching and crawl this project folder
    await this.master.memoryFs.watch(projectFolder, config);

    return project;
  }

  declarePackage(
    project: Project,
    opts: ProjectPackage,
    diagnostics: DiagnosticsProcessor,
  ) {
    const {manifestFilename, manifest} = opts;

    const {name} = manifest;
    if (name === undefined) {
      return undefined;
    }

    // Declare this package in all projects
    const projects = this.getHierarchyFromProject(project);

    // Check for collisions
    for (const project of projects) {
      // If there is no package then there's nothing to collide
      const existingPackage = project.packages.get(name);
      if (existingPackage === undefined) {
        continue;
      }

      diagnostics.addDiagnostic({
        category: 'projectManager',
        filename: manifestFilename,
        message: `Duplicate package name <emphasis>${name}</emphasis>`,
        advice: [
          {
            type: 'log',
            category: 'info',
            message: `Defined already by <filelink filename="${
              existingPackage.manifestFilename
            }" />`,
          },
        ],
      });
      return undefined;
    }

    // Set as a package
    for (const project of projects) {
      project.packages.set(name, opts);
    }
  }

  isHasteIgnored(loc: string, config: ProjectConfig): boolean {
    return matchPathPatterns(parsePath({input: loc}), config.haste.ignore);
  }

  isHasteDeclared(loc: string, project: Project): boolean {
    if (project.config.haste.enabled === false) {
      return false;
    }

    const hasteName = this.master.memoryFs.getHasteName(loc);
    if (hasteName === undefined) {
      return false;
    }

    return project.hasteMap.get(hasteName) === loc;
  }

  declareHaste(
    loc: string,
    hasteName: string,
    hasteLoc: string,
    diagnostics: DiagnosticsProcessor,
  ) {
    for (const project of this.getHierarchyFromFilename(loc)) {
      const {hasteMap, config} = project;

      if (config.haste.enabled === false) {
        break;
      }

      if (this.isHasteIgnored(loc, config)) {
        continue;
      }

      let existing = hasteMap.get(hasteName);
      if (existing !== undefined && existing !== hasteLoc) {
        // If both resolve to the same location then this isn't a collision and we should just ignore it
        if (
          hasteLoc ===
          this.master.resolver.resolvePath({
            origin: existing,
            source: existing,
          })
        ) {
          continue;
        }

        diagnostics.addDiagnostic({
          category: 'projectManager',
          filename: hasteLoc,
          message: `Found a haste collision for <emphasis>${hasteName}</emphasis>`,
          advice: [
            {
              type: 'log',
              category: 'info',
              message: `Defined already by <filelink filename="${existing}" />`,
            },
          ],
        });
        continue;
      }

      hasteMap.set(hasteName, hasteLoc);
    }
  }

  async notifyWorkersOfProjects(
    workers: Array<WorkerContainer>,
    projects?: Array<Project>,
  ): Promise<void> {
    if (projects === undefined) {
      projects = Array.from(this.projects.values());
    }

    const projectsSerial: WorkerProjects = [];
    for (const project of projects) {
      projectsSerial.push({
        config: project.config,
        id: project.id,
      });
    }

    await Promise.all(
      workers.map(async worker => {
        return worker.bridge.updateProjects.call({projects: projectsSerial});
      }),
    );
  }

  async assertProject(loc: string): Promise<Project> {
    const project = await this.findProject(loc);
    if (project) {
      // Continue searching for projects up the directory
      // We don't do this for isolated projects since it would be a waste, but there's no implications other than some unnecessary work if we did
      if (project.config.isolated === false) {
        await this.findProject(path.dirname(project.dir));
      }

      return project;
    } else {
      throw new Error(`Couldn't find a ${ROME_CONFIG_FILENAME} for ${loc}`);
    }
  }

  getHierarchyFromFilename(filename: string): Array<Project> {
    const project = this.findProjectExisting(filename);
    if (project === undefined) {
      return [];
    } else {
      return this.getHierarchyFromProject(project);
    }
  }

  getHierarchyFromProject(project: Project): Array<Project> {
    const projects: Array<Project> = [];

    let currProject: undefined | Project = project;
    while (currProject !== undefined) {
      projects.push(currProject);

      // Isolated projects shouldn't be considered to have any parents
      if (currProject.config.isolated) {
        break;
      }

      currProject = project.parent;
    }

    return projects;
  }

  assertProjectExisting(loc: string): Project {
    const project = this.findProjectExisting(loc);
    if (project === undefined) {
      throw new Error(`Expected existing project for ${loc}`);
    }
    return project;
  }

  findProjectExisting(cwd: string): undefined | Project {
    const tried = [];

    for (const dir of walkDirectoryPath(cwd)) {
      const cachedProjectId = this.fileToProject.get(dir);
      if (cachedProjectId === undefined) {
        tried.push(dir);
      } else {
        for (const dir of tried) {
          this.fileToProject.set(dir, cachedProjectId);
        }

        const project = this.projects.get(cachedProjectId);
        if (project === undefined) {
          throw new Error(
            'Expected project from project id found in fileToProject',
          );
        }
        return project;
      }
    }

    return undefined;
  }

  checkConfigFile(
    basename: string,
    loc: string,
    diagnostics: DiagnosticsProcessor,
  ) {
    if (ROME_CONFIG_WARN_FILENAMES.includes(basename)) {
      this.warnIncorrectConfigFile(loc, diagnostics);
    }
  }

  warnIncorrectConfigFile(loc: string, diagnostics: DiagnosticsProcessor) {
    diagnostics.addDiagnostic({
      category: 'projectManager',
      filename: loc,
      message: `Invalid rome config filename, <emphasis>${ROME_CONFIG_FILENAME}</emphasis> is the only valid filename`,
    });
  }

  async hasPackageJsonConfig(dir: string): Promise<boolean> {
    const packageLoc = path.join(dir, 'package.json');
    if (!(await this.master.exists(packageLoc))) {
      return false;
    }

    const json = await fs.promises.readFile(packageLoc, 'utf8');
    const obj = consumeJSON({
      input: json,
      filename: packageLoc,
    }).asUnknownObject();
    return (
      typeof obj === 'object' &&
      obj != null &&
      obj[ROME_CONFIG_PACKAGE_JSON_KEY] !== undefined
    );
  }

  async findProject(cwd: string): Promise<undefined | Project> {
    // Check if we have an existing project
    const syncProject = this.findProjectExisting(cwd);
    if (syncProject !== undefined) {
      return syncProject;
    }

    // If not then let's access the file system and try to find one
    for (const dir of walkDirectoryPath(cwd)) {
      const loc = path.join(dir, ROME_CONFIG_FILENAME);

      let hasProject = await this.master.exists(loc);
      let isPackage = false;

      if (hasProject === false && (await this.hasPackageJsonConfig(dir))) {
        hasProject = true;
        isPackage = true;
      }

      if (hasProject) {
        return this.queueAddProject({dir, isPackage});
      }
    }

    // If we didn't find a project config then check for incorrect config filenames
    for (const dir of walkDirectoryPath(cwd)) {
      for (const basename of ROME_CONFIG_WARN_FILENAMES) {
        const loc = path.join(dir, basename);

        if (await this.master.exists(loc)) {
          this.warnIncorrectConfigFile(
            loc,
            DiagnosticsProcessor.createImmediateThrower([
              'project-manager: Find project',
            ]),
          );
        }
      }
    }

    return undefined;
  }
}
