import {ReporterOptions} from './Reporter';
import Reporter from './Reporter';

import stream = require('stream');

type Streams = Set<stream.Writable>;

function createRedirectStream(streams: Streams): stream.Writable {
  return new stream.Writable({
    write: (chunk, encoding, callback) => {
      let remaining = streams.size;

      function done() {
        remaining--;

        if (remaining === 0) {
          callback();
        }
      }

      for (const stream of streams) {
        stream.write(chunk, encoding, done);
      }
    },
  });
}

export default class ReporterCollection extends Reporter {
  constructor(opts: ReporterOptions = {}) {
    super({
      ...opts,
      disabled: true,
    });

    this.reporters = new Set();
    this.stdouts = new Set();
    this.stderrs = new Set();

    this.stdout = createRedirectStream(this.stdouts);
    this.stderr = createRedirectStream(this.stderrs);
  }

  reporters: Set<Reporter>;
  stdouts: Streams;
  stderrs: Streams;

  isEnabled() {
    return this.reporters.size > 0;
  }

  addReporter(reporter: Reporter) {
    this.reporters.add(reporter);
    this.stdouts.add(reporter.stdout);
    this.stderrs.add(reporter.stderr);
  }

  deleteReporter(reporter: Reporter) {
    this.reporters.delete(reporter);
    this.stdouts.delete(reporter.stdout);
    this.stderrs.delete(reporter.stderr);
  }
}
