import {JSNodeBase, TSTypeNode} from '../index';
import {createBuilder} from '../utils';

export type TSTypeParameterInstantiationNode = JSNodeBase & {
  type: 'TSTypeParameterInstantiation',
  params: Array<TSTypeNode>,
};

export const tsTypeParameterInstantiation = createBuilder<TSTypeParameterInstantiationNode>(
  'TSTypeParameterInstantiation',
  {
    visitorKeys: ['params'],
  },
);
