import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import OpaqueT from '../../types/OpaqueT';

export function FlowDeclareOpaqueType(
  node: n.FlowDeclareOpaqueTypeNode,
  scope: Scope,
) {
  return new OpaqueT(scope, node.id, node.id.name);
}
