import {
  ClassMethodKind,
  JSNodeBase,
  FunctionBase,
  ClassMemberBase,
  ExpressionNode,
} from '../index';
import {createBuilder} from '../utils';

export type ClassPrivateMethodNode = JSNodeBase &
  FunctionBase &
  ClassMemberBase & {
    type: 'ClassPrivateMethod';
    key: ExpressionNode;
    kind: ClassMethodKind;
  };

export const classPrivateMethod = createBuilder<ClassPrivateMethodNode>(
  'ClassPrivateMethod',
);
