import {
  JSNodeBase,
  VariableDeclarationNode,
  ExpressionNode,
  StatementNode,
} from '../index';
import {createBuilder} from '../utils';

export type ForStatementNode = JSNodeBase & {
  type: 'ForStatement',
  init?: undefined | VariableDeclarationNode | ExpressionNode,
  test?: undefined | ExpressionNode,
  update?: undefined | ExpressionNode,
  body: StatementNode,
};

export const forStatement = createBuilder<ForStatementNode>('ForStatement', {
  visitorKeys: ['init', 'test', 'update', 'body'],
});
