import Scope from '../Scope';
import {addFunctionBindings} from '../utils';
import * as t from '@romejs/js-ast';

export default function ArrowFunctionExpression(
  node: t.ArrowFunctionExpressionNode,
  parent: t.Node,
  scope: Scope,
) {
  const newScope = scope.fork('function', node);
  addFunctionBindings(newScope, node, false);
  return newScope;
}
