import format from '@romejs/pretty-format';

// FIXME: SHIMS ONLY UNTIL I CAN FIGURE OUT HOW TO BREAK THE CYCLE
//import {escapeMarkup} from '@romejs/string-markup';
const escapeMarkup = (str: string): string => str;

class InvariantError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'InvariantError';
    this.__ROME_POP_FRAMES__ = 1;
  }

  __ROME_POP_FRAMES__: number;
}

export default function invariant(
  condition: boolean,
  rawMessage: string,
  ...args: Array<unknown>
) {
  if (!condition) {
    const message = interpolateMessage(rawMessage, args);
    throw new InvariantError(message);
  }
}

export function interpolateMessage(msg: string, args: Array<unknown>): string {
  let argIndex: number = 0;

  // replace %s in the message with each argument
  let interpolated: string = msg.replace(/%s/g, () => format(args[argIndex++]));

  // add on all other arguments to the end, separate with spaces
  if (argIndex < args.length) {
    interpolated += ' ';
    interpolated += args
      .slice(argIndex)
      .map(arg => escapeMarkup(format(arg)))
      .join(' ');
  }

  return interpolated;
}
