import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function ConditionalExpression(
  generator: Generator,
  node: t.ConditionalExpressionNode,
) {
  generator.print(node.test, node);
  generator.space();
  generator.token('?');
  generator.space();
  generator.print(node.consequent, node);
  generator.space();
  generator.token(':');
  generator.space();
  generator.print(node.alternate, node);
}
