import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import MaybeT from '../../types/MaybeT';

export function FlowNullableTypeAnnotation(
  node: n.FlowNullableTypeAnnotationNode,
  scope: Scope,
) {
  return new MaybeT(scope, node, scope.evaluate(node.typeAnnotation));
}
