import {MasterRequest} from '@romejs/core';
import {WorkerCompileResult} from '../../common/bridges/WorkerBridge';
import {createMasterCommand} from '../../commands';
import {commandCategories} from '../../commands';
import {DiagnosticsError} from '@romejs/diagnostics';

export default createMasterCommand({
  category: commandCategories.SOURCE_CODE,
  description: 'compile a single file',

  async default(req: MasterRequest): Promise<void> {
    const {master, args, flags, reporter} = req;
    const {stdout} = reporter;
    if (args.length !== 1) {
      throw new Error('Expected exactly one rgument.');
    }

    const filename = await master.resolver.resolveEntryAssert({
      ...req.getResolverOptionsFromFlags(),
      origin: flags.cwd,
      source: args[0],
    });
    const {code, diagnostics}: WorkerCompileResult = await master.api.compile(
      filename,
      'compile',
    );

    if (diagnostics.length > 0) {
      throw new DiagnosticsError('Compile diagnostics', diagnostics);
    }

    stdout.write(code);
  },
});
