import {JSNodeBase, FlowTypeParameterNode} from '../index';
import {createBuilder} from '../utils';

export type FlowTypeParameterDeclarationNode = JSNodeBase & {
  type: 'FlowTypeParameterDeclaration',
  params: Array<FlowTypeParameterNode>,
};

export const flowTypeParameterDeclaration = createBuilder<FlowTypeParameterDeclarationNode>(
  'FlowTypeParameterDeclaration',
  {visitorKeys: ['params']},
);
