import Scope from '../Scope';
import {ClassBinding} from '@romejs/js-compiler';
import * as t from '@romejs/js-ast';
import ClassExpression from '../pure-evaluators/ClassExpression';

export default function ClassDeclaration(
  node: t.ClassDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  if (node.id !== undefined) {
    scope.addBinding(
      new ClassBinding({
        name: node.id.name,
        node: node.id,
        scope,
      }),
    );
  }
  return ClassExpression(node, parent, scope);
}
