import {
  JSNodeBase,
  TSSignatureDeclarationBase,
  TSTypeAnnotationNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSConstructorTypeNode = JSNodeBase &
  TSSignatureDeclarationBase & {
    type: 'TSConstructorType';
    typeAnnotation: TSTypeAnnotationNode;
  };

export const tsConstructorType = createBuilder<TSConstructorTypeNode>(
  'TSConstructorType',
  {visitorKeys: ['typeAnnotation', 'parameters', 'typeParameters']},
);
