import * as t from '@romejs/js-ast';
import {
  isFor,
  isUnaryLike,
  isConditional,
  isBinary,
} from '@romejs/js-ast-utils';

const PRECEDENCE = {
  '||': 0,
  '&&': 1,
  '??': 1,
  '|': 2,
  '^': 3,
  '&': 4,
  '==': 5,
  '===': 5,
  '!=': 5,
  '!==': 5,
  '<': 6,
  '>': 6,
  '<=': 6,
  '>=': 6,
  in: 6,
  instanceof: 6,
  '>>': 7,
  '<<': 7,
  '>>>': 7,
  '+': 8,
  '-': 8,
  '*': 9,
  '/': 9,
  '%': 9,
  '**': 10,
};

export function FlowNullableTypeAnnotation(
  node: t.FlowNullableTypeAnnotationNode,
  parent: t.Node,
): boolean {
  return parent.type === 'FlowArrayTypeAnnotation';
}

export function UpdateExpression(
  node: t.UpdateExpressionNode,
  parent: t.Node,
): boolean {
  // (foo++).test()
  return parent.type === 'MemberExpression' && parent.object === node;
}

export function ObjectExpression(
  node: t.ObjectExpressionNode,
  parent: t.Node,
  printStack: Array<Node>,
): boolean {
  return isFirstInStatement(printStack, true, false);
}

export function DoExpression(
  node: t.DoExpressionNode,
  parent: t.Node,
  printStack: Array<Node>,
): boolean {
  return isFirstInStatement(printStack, false, false);
}

export function LogicalExpression(
  node: t.LogicalExpressionNode | t.BinaryExpressionNode,
  parent: t.Node,
): boolean {
  if (
    node.operator === '**' &&
    parent.type === 'BinaryExpression' &&
    parent.operator === '**'
  ) {
    return parent.left === node;
  }

  if (
    ((parent.type === 'CallExpression' || parent.type === 'NewExpression') &&
      parent.callee === node) ||
    isUnaryLike(parent) ||
    (parent.type === 'MemberExpression' && parent.object === node) ||
    parent.type === 'AwaitExpression'
  ) {
    return true;
  }

  if (isBinary(parent)) {
    const parentOp = parent.operator;
    // @ts-ignore
    const parentPos = PRECEDENCE[parentOp];

    const nodeOp = node.operator;
    const nodePos = PRECEDENCE[nodeOp];

    if (
      // Logical expressions with the same precedence don't need parens.
      (parentPos === nodePos &&
        parent.right === node &&
        parent.type !== 'LogicalExpression') ||
      parentPos > nodePos
    ) {
      return true;
    }
  }

  return false;
}

export function BinaryExpression(
  node: t.BinaryExpressionNode,
  parent: t.Node,
): boolean {
  // let i = (1 in []);
  // for ((1 in []);;);
  return (
    (node.operator === 'in' &&
      (parent.type === 'VariableDeclarator' || isFor(parent))) ||
    LogicalExpression(node, parent)
  );
}

export function SequenceExpression(
  node: t.SequenceExpressionNode,
  parent: t.Node,
): boolean {
  if (
    // Although parentheses wouldn"t hurt around sequence
    // expressions in the head of for loops, traditional style
    // dictates that e.g. i++, j++ should not be wrapped with
    // parentheses.
    parent.type === 'ForStatement' ||
    parent.type === 'ThrowStatement' ||
    parent.type === 'ReturnStatement' ||
    (parent.type === 'IfStatement' && parent.test === node) ||
    (parent.type === 'WhileStatement' && parent.test === node) ||
    (parent.type === 'ForInStatement' && parent.right === node) ||
    (parent.type === 'SwitchStatement' && parent.discriminant === node) ||
    (parent.type === 'ExpressionStatement' && parent.expression === node)
  ) {
    return false;
  }

  // Otherwise err on the side of overparenthesization, adding
  // explicit exceptions above if this proves overzealous.
  return true;
}

export function YieldExpression(
  node: t.YieldExpressionNode,
  parent: t.Node,
): boolean {
  return (
    isBinary(parent) ||
    isUnaryLike(parent) ||
    parent.type === 'CallExpression' ||
    parent.type === 'MemberExpression' ||
    parent.type === 'NewExpression' ||
    (parent.type === 'ConditionalExpression' && node === parent.test)
  );
}

export {YieldExpression as AwaitExpression};

export function ClassExpression(
  node: t.ClassExpressionNode,
  parent: t.Node,
  printStack: Array<Node>,
): boolean {
  return isFirstInStatement(printStack, false, true);
}

export function UnaryExpression(
  node:
    | t.UnaryExpressionNode
    | t.ArrowFunctionExpressionNode
    | t.AssignmentExpressionNode
    | t.ConditionalExpressionNode
    | t.SpreadElementNode
    | t.RestPropertyNode
    | t.SpreadPropertyNode,
  parent: t.Node,
): boolean {
  return (
    (parent.type === 'MemberExpression' && parent.object === node) ||
    (parent.type === 'CallExpression' && parent.callee === node) ||
    (parent.type === 'NewExpression' && parent.callee === node) ||
    (parent.type === 'BinaryExpression' &&
      parent.operator === '**' &&
      parent.left === node)
  );
}

export function SpreadElement(
  node: t.SpreadElementNode,
  parent: t.Node,
): boolean {
  return UnaryExpression(node, parent);
}

export function RestProperty(
  node: t.RestPropertyNode,
  parent: t.Node,
): boolean {
  return UnaryExpression(node, parent);
}

export function SpreadProperty(
  node: t.SpreadPropertyNode,
  parent: t.Node,
): boolean {
  return UnaryExpression(node, parent);
}

export function FunctionExpression(
  node: t.Node,
  parent: t.Node,
  printStack: Array<Node>,
): boolean {
  return isFirstInStatement(printStack, false, true);
}

export function ArrowFunctionExpression(
  node: t.ArrowFunctionExpressionNode,
  parent: t.Node,
): boolean {
  return (
    parent.type === 'ExportDeclaration' || ConditionalExpression(node, parent)
  );
}

export function ConditionalExpression(
  node:
    | t.ArrowFunctionExpressionNode
    | t.AssignmentExpressionNode
    | t.ConditionalExpressionNode,
  parent: t.Node,
): boolean {
  if (
    isUnaryLike(parent) ||
    isBinary(parent) ||
    (parent.type === 'ConditionalExpression' && parent.test === node) ||
    parent.type === 'AwaitExpression' ||
    parent.type === 'TaggedTemplateExpression'
  ) {
    return true;
  }

  return UnaryExpression(node, parent);
}

export function AssignmentExpression(
  node: t.AssignmentExpressionNode,
  parent: t.Node,
): boolean {
  if (node.left.type === 'ObjectPattern') {
    return true;
  } else {
    return ConditionalExpression(node, parent);
  }
}

export function FlowUnionTypeAnnotation(
  node: t.FlowUnionTypeAnnotationNode,
  parent: t.Node,
) {
  return (
    parent.type === 'FlowArrayTypeAnnotation' ||
    parent.type === 'FlowNullableTypeAnnotation' ||
    parent.type === 'FlowIntersectionTypeAnnotation' ||
    parent.type === 'FlowUnionTypeAnnotation'
  );
}

export {FlowUnionTypeAnnotation as FlowIntersectionTypeAnnotation};

export function FlowFunctionTypeAnnotation(
  node: t.FlowFunctionTypeAnnotationNode,
  parent: t.Node,
) {
  return (
    parent.type === 'FlowUnionTypeAnnotation' ||
    parent.type === 'FlowIntersectionTypeAnnotation'
  );
}

// Walk up the print stack to deterimine if our node can come first
// in statement.
function isFirstInStatement(
  printStack: Array<t.Node>,
  considerArrow: boolean,
  considerDefaultExports: boolean,
): boolean {
  let i = printStack.length - 1;
  let node = printStack[i];
  i--;
  let parent = printStack[i];
  while (i > 0) {
    if (
      (parent.type === 'ExpressionStatement' && parent.expression === node) ||
      parent.type === 'TaggedTemplateExpression' ||
      (considerDefaultExports &&
        (parent.type === 'ExportDefaultDeclaration' &&
          parent.declaration === node)) ||
      (considerArrow &&
        (parent.type === 'ArrowFunctionExpression' && parent.body === node))
    ) {
      return true;
    }

    if (
      (parent.type === 'CallExpression' && parent.callee === node) ||
      (parent.type === 'SequenceExpression' &&
        parent.expressions[0] === node) ||
      (parent.type === 'MemberExpression' && parent.object === node) ||
      (isConditional(parent) && parent.test === node) ||
      (isBinary(parent) && parent.left === node) ||
      (parent.type === 'AssignmentExpression' && parent.left === node)
    ) {
      node = parent;
      i--;
      parent = printStack[i];
    } else {
      return false;
    }
  }

  return false;
}
