import {JSNodeBase, TSTypeParameterNode} from '../index';
import {createBuilder} from '../utils';

export type TSInferTypeNode = JSNodeBase & {
  type: 'TSInferType';
  typeParameter: TSTypeParameterNode;
};

export const tsInferType = createBuilder<TSInferTypeNode>('TSInferType', {
  visitorKeys: ['typeParameter'],
});
