import {
  JSNodeBase,
  FlowNode,
  FlowObjectTypePropertyKey,
  FlowVarianceNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowObjectTypeIndexerNode = JSNodeBase & {
  type: 'FlowObjectTypeIndexer';
  id?: undefined | FlowObjectTypePropertyKey;
  key: FlowNode;
  value: FlowNode;
  static?: undefined | boolean;
  variance?: undefined | FlowVarianceNode;
};

export const flowObjectTypeIndexer = createBuilder<FlowObjectTypeIndexerNode>(
  'FlowObjectTypeIndexer',
  {visitorKeys: ['id', 'key', 'value']},
);
