import {TransformExitResult, TransformEnterResult} from '@romejs/js-compiler';
import {Path} from '@romejs/js-compiler';

type HookState = unknown;
type HookArg = unknown;

export type Hook<State extends HookState = void, Arg extends HookArg = void> = {
  name: string;
  defaultState?: State;
  consume: (
    path: Path,
    state: State,
    arg: undefined | Arg,
  ) => {
    node: TransformEnterResult;
    state: State;
  };
  exit?: (path: Path, state: State) => TransformExitResult;
};

export type HookWrapper<State extends HookState, Arg extends HookArg> = {
  consume: (arg?: Arg) => HookConsume;
  provide: (state?: Partial<State>) => HookRef;
};

export type HookConsume = {
  type: typeof CONSUMER;
  id: Symbol;
  arg: HookArg;
};

export type HookRef = {
  type: typeof HOOK;
  name: string;
  id: Symbol;
  state: HookState;
  hook: Hook<any, any>;
};

export const HOOK: 'HOOK' = 'HOOK';

export const CONSUMER: 'CONSUMER' = 'CONSUMER';

export default function createHook<
  State extends HookState,
  Arg extends HookArg
>(hook: Hook<State, Arg>): HookWrapper<State, Arg> {
  const id = Symbol();

  return {
    // $FlowFixMe: no idea why this is a type error
    provide(state?: Partial<State>): HookRef {
      return {
        type: HOOK,
        name: hook.name,
        id,
        state: {
          ...hook.defaultState,
          ...state,
        },
        hook,
      };
    },

    consume(arg?: Arg): HookConsume {
      return {type: CONSUMER, id, arg};
    },
  };
}
