// In this file, all methods are sychronous. This is pretty gross since the rest of Rome is async everything.
// This is required so we can integrate the project config code in third-party integrations with sync architectures.
// Project configs are initialized very infrequently anyway so we can live with the extremely minor perf hit.

import {Consumer} from '@romejs/codec-json';
import {ProjectConfig, PartialProjectConfig} from '../../common/types/project';
import {parsePathPattern} from '@romejs/match-path';
import {arrayOfStrings, arrayOfPatterns, mergeArrays} from './utils';
import {DEFAULT_PROJECT_CONFIG} from '../../common/types/project';
import {consumeJSON} from '@romejs/codec-json';

import crypto = require('crypto');
import path = require('path');
import fs = require('fs');

const WATCHMAN_CONFIG_FILENAME = '.watchmanconfig';

const IGNORE_FILENAMES = ['.gitignore', '.hgignore'];

// This is a counter so we can use it as a simple cache key part for in-memory caches
// Default config in ./types.js uses 0
let memoryCacheCounter = 1;

export function loadCompleteProjectConfig(
  projectFolder: string,
  configLoc: string,
  subKey?: undefined | string,
): ProjectConfig {
  const {partial, consumer} = loadPartialProjectConfig(
    projectFolder,
    configLoc,
    subKey,
  );

  // TODO Infer `name` from '`package.json`

  const config: ProjectConfig = {
    ...DEFAULT_PROJECT_CONFIG,
    memoryCacheCounter: memoryCacheCounter++,
    name: consumer.getProperty('name').asString(path.basename(projectFolder)),
    isolated:
      partial.isolated === undefined
        ? DEFAULT_PROJECT_CONFIG.isolated
        : partial.isolated,
    configDependencies: partial.configDependencies,
    configHashes: partial.configHashes,
    cache: {
      ...DEFAULT_PROJECT_CONFIG.cache,
      ...partial.cache,
    },
    bundler: {
      ...DEFAULT_PROJECT_CONFIG.bundler,
      ...partial.bundler,
    },
    lint: {
      ...DEFAULT_PROJECT_CONFIG.lint,
      ...partial.lint,
    },
    resolver: {
      ...DEFAULT_PROJECT_CONFIG.resolver,
      ...partial.resolver,
    },
    haste: {
      ...DEFAULT_PROJECT_CONFIG.haste,
      ...partial.haste,
    },
    typeChecking: {
      ...DEFAULT_PROJECT_CONFIG.typeChecking,
      ...partial.typeChecking,
    },
    format: {
      ...DEFAULT_PROJECT_CONFIG.format,
      ...partial.format,
    },
    dependencies: {
      ...DEFAULT_PROJECT_CONFIG.dependencies,
      ...partial.dependencies,
    },
    testing: {
      ...DEFAULT_PROJECT_CONFIG.testing,
      ...partial.testing,
    },
    files: {
      ...DEFAULT_PROJECT_CONFIG.files,
      ...partial.files,
    },
  };

  // Infer VCS ignore files as lint ignore rules
  // TODO Maybe we should have a config option to specify this as it may not be next to the project config
  for (const filename of IGNORE_FILENAMES) {
    const possibleLoc = path.join(projectFolder, filename);
    config.configDependencies.push(possibleLoc);

    if (fs.existsSync(possibleLoc)) {
      const file = fs.readFileSync(possibleLoc, 'utf8');
      const lines: Array<string> = file.split('\n');

      let index = 0;

      const patterns = lines.map((line, i) => {
        const pattern = parsePathPattern({
          input: line,
          filename: possibleLoc,
          offsetPosition: {
            index,
            line: i,
            column: 0,
          },
        });

        index += line.length;
        index++;

        return pattern;
      });

      config.lint.ignore = [...config.lint.ignore, ...patterns];
      // FIXME: Maybe these are useful in other places?
    }
  }

  // Set files.watchman=true when the file .watchmanconfig is present and no files.watchman config was set
  if (partial.files.watchman === undefined) {
    const watchmanConfigLoc = path.join(
      projectFolder,
      WATCHMAN_CONFIG_FILENAME,
    );
    config.configDependencies.push(watchmanConfigLoc);
    if (fs.existsSync(watchmanConfigLoc)) {
      config.files.watchman = true;
    }
  }

  return config;
}

function loadPartialProjectConfig(
  projectFolder: string,
  configLoc: string,
  subKey?: undefined | string,
): {
  partial: PartialProjectConfig;
  consumer: Consumer;
} {
  const configFile = fs.readFileSync(configLoc, 'utf8');

  let consumer = consumeJSON({
    filename: configLoc,
    input: configFile,
  });

  if (subKey !== undefined) {
    consumer = consumer.getProperty(subKey);
  }

  return {
    partial: normalizeProjectConfig(consumer, configFile, projectFolder),
    consumer,
  };
}

function normalizeProjectConfig(
  consumer: Consumer,
  input: string,
  projectFolder: string,
): PartialProjectConfig {
  // TODO error against unknown properties

  const hash = crypto
    .createHash('sha256')
    .update(input)
    .digest('hex');

  const config: PartialProjectConfig = {
    configHashes: [hash],
    configDependencies: [],
    bundler: {},
    cache: {},
    lint: {},
    haste: {},
    resolver: {},
    typeChecking: {},
    format: {},
    dependencies: {},
    testing: {},
    files: {},
  };

  if (consumer.hasProperty('isolated')) {
    config.isolated = consumer.getProperty('isolated').asBoolean();
  }

  const cache = consumer.getProperty('cache');
  if (cache.exists()) {
    if (cache.hasProperty('folder')) {
      config.cache.folder = cache.getProperty('folder').asString();
    }

    if (cache.hasProperty('endpoint')) {
      config.cache.endpoint = cache.getProperty('endpoint').asString();
    }
  }

  const resolver = consumer.getProperty('resolver');
  if (resolver.exists()) {
  }

  const bundler = consumer.getProperty('bundler');
  if (bundler.exists()) {
  }

  const haste = consumer.getProperty('haste');
  if (haste.exists()) {
    if (haste.hasProperty('enabled')) {
      config.haste.enabled = haste.getProperty('enabled').asBoolean();
    }

    if (haste.hasProperty('ignore')) {
      config.haste.ignore = arrayOfPatterns(haste.getProperty('ignore'));
    }
  }

  const typeChecking = consumer.getProperty('typeChecking');
  if (typeChecking.exists()) {
    if (typeChecking.hasProperty('enabled')) {
      config.typeChecking.enabled = typeChecking
        .getProperty('enabled')
        .asBoolean();
    }

    if (typeChecking.hasProperty('libs')) {
      config.typeChecking.libs = normalizeTypeCheckingLibs(
        projectFolder,
        typeChecking.getProperty('libs'),
      );
    }
  }

  const lint = consumer.getProperty('lint');
  if (lint.exists()) {
    if (lint.hasProperty('enabled')) {
      config.lint.enabled = lint.getProperty('enabled').asBoolean();
    }

    if (lint.hasProperty('ignore')) {
      config.lint.ignore = arrayOfPatterns(lint.getProperty('ignore'));
    }

    if (lint.hasProperty('globals')) {
      config.lint.globals = arrayOfStrings(lint.getProperty('globals'));
    }
  }

  const format = consumer.getProperty('format');
  if (format.exists()) {
    if (format.hasProperty('enabled')) {
      config.format.enabled = format.getProperty('enabled').asBoolean();
    }
  }

  const dependencies = consumer.getProperty('dependencies');
  if (dependencies.exists()) {
    if (dependencies.hasProperty('enabled')) {
      config.dependencies.enabled = dependencies
        .getProperty('enabled')
        .asBoolean();
    }

    if (dependencies.hasProperty('folder')) {
      config.dependencies.folder = path.resolve(
        projectFolder,
        dependencies.getProperty('folder').asString(),
      );
    }
  }

  const testing = consumer.getProperty('testing');
  if (testing.exists()) {
    if (testing.hasProperty('enabled')) {
      config.testing.enabled = testing.getProperty('enabled').asBoolean();
    }

    if (testing.hasProperty('folderName')) {
      config.testing.folderName = testing.getProperty('folderName').asString();
    }

    if (testing.hasProperty('ignore')) {
      config.testing.ignore = arrayOfPatterns(testing.getProperty('ignore'));
    }
  }

  const files = consumer.getProperty('files');
  if (files.exists()) {
    if (files.hasProperty('watchman')) {
      config.files.watchman = files.getProperty('watchman').asBoolean();
    }

    if (files.hasProperty('maxSize')) {
      config.files.maxSize = files.getProperty('maxSize').asNumber();
    }
  }

  const _extends = consumer.getProperty('extends');
  if (_extends.exists()) {
    return extendProjectConfig(projectFolder, _extends, config);
  }

  return config;
}

function normalizeTypeCheckingLibs(
  projectFolder: string,
  consumer: Consumer,
): Array<string> {
  const libFiles = [];

  // Normalize library folders
  const libFolders = arrayOfStrings(consumer).map(libFolder =>
    path.resolve(projectFolder, libFolder),
  );

  // Crawl library folders and add their files
  for (const folder of libFolders) {
    const files = fs.readdirSync(folder);
    for (const file of files) {
      const loc = path.join(folder, file);
      const stats = fs.statSync(loc);
      if (stats.isFile()) {
        libFiles.push(loc);
      } else if (stats.isDirectory()) {
        libFolders.push(loc);
      }
    }
  }

  return libFiles;
}

function extendProjectConfig(
  projectFolder: string,
  extendsConsumer: Consumer,
  config: PartialProjectConfig,
): PartialProjectConfig {
  const extendsRelative = extendsConsumer.asString();

  if (extendsRelative === 'parent') {
    // do some magic here
  }

  const extendsLoc = path.resolve(projectFolder, extendsRelative);
  const {partial: extendsObj} = loadPartialProjectConfig(
    path.dirname(extendsLoc),
    extendsLoc,
  );

  // Check for recursive config
  if (config.configDependencies.includes(extendsLoc)) {
    throw extendsConsumer.unexpected('Recursive config value');
  }

  const merged: PartialProjectConfig = {
    configDependencies: [
      extendsLoc,
      ...extendsObj.configDependencies,
      ...config.configDependencies,
    ],
    configHashes: [...extendsObj.configHashes, ...config.configHashes],
    cache: {
      ...extendsObj.cache,
      ...config.cache,
    },
    lint: {
      ...extendsObj.lint,
      ...config.lint,
    },
    bundler: {
      ...extendsObj.bundler,
      ...config.bundler,
    },
    resolver: {
      ...extendsObj.resolver,
      ...config.resolver,
    },
    haste: {
      ...extendsObj.haste,
      ...config.haste,
    },
    typeChecking: {
      ...extendsObj.typeChecking,
      ...config.typeChecking,
    },
    testing: {
      ...extendsObj.testing,
      ...config.testing,
    },
    format: {
      ...extendsObj.format,
      ...config.format,
    },
    dependencies: {
      ...extendsObj.dependencies,
      ...config.dependencies,
    },
    files: {
      ...extendsObj.files,
      ...config.files,
    },
  };

  const lintIgnore = mergeArrays(extendsObj.lint.ignore, config.lint.ignore);
  if (lintIgnore !== undefined) {
    merged.lint.ignore = lintIgnore;
  }

  const lintGlobals = mergeArrays(extendsObj.lint.globals, config.lint.globals);
  if (lintGlobals !== undefined) {
    merged.lint.globals = lintGlobals;
  }

  const hasteIgnore = mergeArrays(extendsObj.haste.ignore, config.haste.ignore);
  if (hasteIgnore !== undefined) {
    merged.haste.ignore = hasteIgnore;
  }

  const testingIgnore = mergeArrays(
    extendsObj.testing.ignore,
    config.testing.ignore,
  );
  if (testingIgnore !== undefined) {
    merged.testing.ignore = testingIgnore;
  }

  const typeCheckingLibs = mergeArrays(
    extendsObj.typeChecking.libs,
    config.typeChecking.libs,
  );
  if (typeCheckingLibs !== undefined) {
    merged.typeChecking.libs = typeCheckingLibs;
  }

  return merged;
}
