export type MasterCommand = {
  category: string;
  description: string;
  hasHeader?: boolean;
  getRequestFlags?: () => Partial<APIRequestFlags>;
  parseCLIFlags?: (p: FlagParser) => undefined | UnknownObject;
  default: (
    req: MasterRequest,
    commandFlags: Consumer,
  ) => undefined | Promise<unknown>;
};

export type LocalCommand = {
  category: string;
  description: string;
  parseCLIFlags?: (p: FlagParser) => undefined | UnknownObject;
  run: (api: APIRequest, commandFlags: Consumer) => Promise<boolean>;
};

export function createMasterCommand(cmd: MasterCommand): MasterCommand {
  return cmd;
}

export const commandCategories = {
  PROCESS_MANAGEMENT: 'Process Management',
  CODE_QUALITY: 'Code Quality',
  SOURCE_CODE: 'Source Code',
  PROJECT_MANAGEMENT: 'Project Management',
  SOURCE_CONTROL: 'Source Control',
  INTERNAL: 'Internal',
};
