import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function NumericLiteral(
  generator: Generator,
  node: t.NumericLiteralNode | t.FlowNumericLiteralTypeAnnotationNode,
) {
  const raw = generator.getPossibleRaw(node);
  const value = String(node.value);
  if (raw === undefined) {
    generator.number(value); // normalize
  } else {
    generator.number(raw);
  }
}
