import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type ExpressionStatementNode = JSNodeBase & {
  type: 'ExpressionStatement',
  expression: ExpressionNode,
};

export const expressionStatement = createBuilder<ExpressionStatementNode>(
  'ExpressionStatement',
  {visitorKeys: ['expression']},
);
