import {UnknownObject} from '@romejs/refinement';
import {Position} from '@romejs/parser-core';
import {
  Comments,
  StringToken,
  NumberToken,
  WordToken,
  LineCommentToken,
  BlockCommentToken,
  BracketOpenToken,
  BracketCloseToken,
  BraceOpenToken,
  BraceCloseToken,
  CommaToken,
  ColonToken,
  DotToken,
  PlusToken,
  MinusToken,
  Context,
  JSONParserResult,
  JSONParserOptions,
  JSONParserMode,
  RegexToken,
  ContextKey,
  Token,
} from './types';
import {DAYS_IN_A_MONTH, MONTH_NAMES} from './constants';
import {validateRegexFlags} from '@romejs/js-parser-utils';
import {unescapeString} from '@romejs/string-escape';
import messages from './messages';
import {ParserCore, isAlpha, isDigit, isEscaped} from '@romejs/parser-core';
import invariant from '@romejs/invariant';

// Words can't start with a digit
function isWordStartChar(char: string): boolean {
  return isAlpha(char) || char === '_' || char === '$';
}

// But a digit can appear inside of a word
function isWordChar(char: string): boolean {
  return isWordStartChar(char) || isDigit(char);
}

// Check if an input string is a valid word, this is used by the stringifier to
// determine if a property key should be quoted
export function isValidWord(word: string): boolean {
  if (word.length === 0 || isWordStartChar(word[0]) === false) {
    return false;
  }

  for (const char of word) {
    if (isWordChar(char) === false) {
      return false;
    }
  }

  return true;
}

// Used for Number token validation, allow underscore as a separatore
function isNumberChar(char: string): boolean {
  return isDigit(char) || char === '_';
}

// These are valid numbers
function isValidNumberWord(word: string): boolean {
  return word === 'NaN' || word === 'Infinity';
}

// Check if a character is a part of a regex pattern
function isRegexPatternChar(char: string, index: number, input: string) {
  if (char === '/' && !isEscaped(index, input)) {
    return false;
  } else {
    return true;
  }
}

// Check if a character is a part of a string, returning false for a newline or unescaped quote char
function isStringValueChar(
  char: string,
  index: number,
  input: string,
): boolean {
  if (char === '\n') {
    return false;
  }

  if (char === '"' && !isEscaped(index, input)) {
    return false;
  }

  return true;
}

function isntNewline(char: string): boolean {
  return char !== '\n';
}

function isntBlockCommentEnd(
  char: string,
  index: number,
  input: string,
): boolean {
  const nextChar = input[index + 1];
  return char !== '*' && nextChar !== '/';
}

// Turn a path into a string key we can use
export function toPathKey(parts: Array<string>) {
  // Right now this could conflict weirdly with properties with dots in them if they cause collisions
  // We have this method abstracted so we can make changes later if it's necessary (probably not worth it)
  return parts.join('.');
}

export default class Parser extends ParserCore<Token, void> {
  constructor({mode, locations, ...opts}: JSONParserOptions) {
    super(opts, '@romejs/codec-json');
    this.isRegularJSON = Parser.isRegularJSON(mode, opts.filename);
    this.locations = locations === undefined ? true : false;
    this.contextStack = [];
  }

  isRegularJSON: boolean;
  locations: boolean;
  contextStack: Array<Context>;

  static isRegularJSON(
    mode: undefined | JSONParserMode,
    filename: undefined | string,
  ): boolean {
    if (mode === 'json') {
      return true;
    }

    // If we were weren't given a mode then consider a file regular JSON unless it ends with .rjson
    if (mode === undefined && filename !== undefined) {
      return !filename.endsWith('.rjson');
    }

    return false;
  }

  tokenize(index: number, input: string) {
    const nextChar = input[index + 1];
    const char = input[index];

    // Line comment
    if (char === '/' && nextChar === '/') {
      const commentValueIndex = index + 2;
      const value = this.readInputFrom(commentValueIndex, isntNewline);
      // (comment content start + comment content length)
      return this.finishValueToken(
        'LineComment',
        value,
        commentValueIndex + value.length,
      ) as LineCommentToken;
    }

    // BlockComment
    if (char === '/' && nextChar === '*') {
      const commentValueIndex = index + 2;
      const value = this.readInputFrom(commentValueIndex, isntBlockCommentEnd);

      // (comment content start + comment content length + 2 characters for comment end)
      const endIndex = commentValueIndex + value.length + 2;

      // Ensure the comment is closed
      if (
        this.input[endIndex - 2] !== '*' ||
        this.input[endIndex - 1] !== '/'
      ) {
        throw this.unexpected({
          message: messages.UNCLOSED_BLOCK_COMMENT(),
          start: this.getPositionFromIndex(endIndex),
        });
      }

      return this.finishValueToken(
        'BlockComment',
        value,
        endIndex,
      ) as BlockCommentToken;
    }

    // Single character token starters
    switch (char) {
      case '"':
        const value = this.readInputFrom(index + 1, isStringValueChar);

        // Check for closed string (index is the current token index + string length + closing quote + 1 for the end char)
        const end = index + value.length + 2;
        if (input[end - 1] !== '"') {
          throw this.unexpected({
            message: messages.UNCLOSED_STRING(),
            start: this.getPositionFromIndex(end),
          });
        }

        // Don't allow newlines in JSON
        if (this.isRegularJSON) {
          for (let strIndex = 0; strIndex < value.length; strIndex++) {
            const char = value[strIndex];

            if (char === '\n') {
              throw this.unexpected({
                message: messages.STRING_NEWLINES_IN_JSON(),
                start: this.getPositionFromIndex(index + strIndex),
              });
            }
          }
        }

        // Unescape the string
        const unescaped = unescapeString(value, (message, strIndex) => {
          throw this.unexpected({
            message,
            start: this.getPositionFromIndex(index + strIndex),
          });
        });

        return this.finishValueToken('String', unescaped, end) as StringToken;

      case "'":
        throw this.unexpected({
          message: messages.SINGLE_QUOTE_USAGE(),
          start: this.getPositionFromIndex(index),
        });

      case ',':
        return this.finishToken('Comma') as CommaToken;

      case '.':
        return this.finishToken('Dot') as DotToken;

      case '+':
        return this.finishToken('Plus') as PlusToken;

      case '-':
        return this.finishToken('Minus') as MinusToken;

      case ':':
        return this.finishToken('Colon') as ColonToken;

      case '{':
        return this.finishToken('BraceOpen') as BraceOpenToken;

      case '}':
        return this.finishToken('BraceClose') as BraceCloseToken;

      case '[':
        return this.finishToken('BracketOpen') as BracketOpenToken;

      case ']':
        return this.finishToken('BracketClose') as BracketCloseToken;

      case '/': {
        const pattern = this.readInputFrom(index + 1, isRegexPatternChar);

        // (current token index + pattern length + one trailing slash + 1 for the char we end on)
        const endIndex = index + pattern.length + 2;

        // Make sure the regex was closed properly
        if (input[endIndex] !== '/') {
          throw this.unexpected({
            message: messages.UNCLOSED_REGEX(),
            start: this.getPositionFromIndex(endIndex - 1),
          });
        }

        return this.finishValueToken('Regex', pattern, endIndex) as RegexToken;
      }

      // Skip all whitespace characters
      case ' ':
      case '\t':
      case '\r':
      case '\n':
        return this.lookaheadToken(index + 1);
    }

    // Numbers
    if (isDigit(char)) {
      // Get the number
      const value = this.removeUnderscores(
        index,
        this.readInputFrom(index, isNumberChar),
      );
      const num = Number(value);

      // Perform some sanity validation
      invariant(!isNaN(num), 'Invalid number, internal error');

      return this.finishValueToken(
        'Number',
        num,
        index + value.length,
      ) as NumberToken;
    }

    // Word - boolean, undefined etc
    if (isWordStartChar(char)) {
      const value = this.readInputFrom(index, isWordChar);
      return this.finishValueToken(
        'Word',
        value,
        index + value.length,
      ) as WordToken;
    }

    // Unknown character
    return undefined;
  }

  // Remove underscores from 'a string, this is used for numeric separators eg. 100_000
  removeUnderscores(index: number, raw: string): string {
    let str = '';

    for (let i = 0; i < raw.length; i++) {
      const char = raw[i];

      if (char === '_') {
        // Don't allow separators in JSON
        if (this.isRegularJSON) {
          throw this.unexpected({
            message: messages.NUMERIC_SEPARATORS_IN_JSON(),
            start: this.getPositionFromIndex(index + 1),
          });
        }
      } else {
        str += char;
      }
    }

    return str;
  }

  eatComments(): Comments {
    const comments: Comments = [];

    while (true) {
      const token = this.getToken();

      if (token.type === 'LineComment') {
        comments.push({
          type: 'LineComment',
          value: token.value,
        });
      } else if (token.type === 'BlockComment') {
        comments.push({
          type: 'BlockComment',
          value: token.value,
        });
      } else {
        break;
      }

      // Comments aren't allowed in regular JSON mode
      if (this.isRegularJSON) {
        throw this.unexpected({message: messages.COMMENTS_IN_JSON()});
      }

      this.nextToken();
    }

    return comments;
  }

  getLastContext(): Context {
    const {contextStack} = this;

    const lastContext = contextStack[contextStack.length - 1];
    invariant(
      lastContext !== undefined,
      'Expected there to be a context but found none',
    );
    return lastContext;
  }

  createContext(key?: undefined | ContextKey) {
    // Create the context
    const start = this.getPosition();
    const context: Context = {
      originalValue: undefined,
      start,
      end: start,
      innerComments: [],
      comments: [],
      children: new Map(),
    };

    // If we were passed a key then we're a child of the parent context
    if (key !== undefined) {
      const parentContext = this.getLastContext();
      parentContext.children.set(key, context);
    }

    // Push the context
    this.contextStack.push(context);
  }

  provideContext(opts: {
    end?: Position;
    innerComments?: Comments;
    comments?: Comments;
    originalValue: unknown;
  }): Context {
    const lastContext = this.getLastContext();

    Object.assign(lastContext, {
      end: opts.end === undefined ? this.getPosition() : opts.end,
      originalValue: opts.originalValue,
      innerComments:
        opts.innerComments === undefined
          ? lastContext.innerComments
          : [...lastContext.innerComments, ...opts.innerComments],
      comments:
        opts.comments === undefined
          ? lastContext.comments
          : [...lastContext.comments, ...opts.comments],
    });

    return lastContext;
  }

  popContext() {
    return this.contextStack.pop();
  }

  parseObject(firstKey?: undefined | string): UnknownObject {
    const obj: UnknownObject = {};

    let innerComments: Comments = [];
    let isFirstProp = true;

    // These are comments that the next property should take in case the previous accidently took them
    let nextLeadingComments;

    do {
      // Eat all the comments that appeared before this property, it's the most common and natural place to put them,
      // and is where we'll print all comments for a property.
      let leadingComments = this.eatComments();

      // Take any leading comments that were left by the previous property
      if (nextLeadingComments !== undefined) {
        leadingComments = [...nextLeadingComments, ...leadingComments];
        nextLeadingComments = undefined;
      }

      // Throw a meaningful error for unquoted properties
      if (this.isRegularJSON && this.matchToken('Word')) {
        throw this.unexpected({
          message: messages.UNQUOTED_PROPERTY_KEY_IN_JSON(),
        });
      }

      // Throw a meainingful error for redundant commas
      if (this.matchToken('Comma')) {
        throw this.unexpected({
          message: messages.TRAILING_OBJECT_COMMA(),
        });
      }

      // If there's no property key indicator then delegate any comments we have to object
      const hasKey = isFirstProp && firstKey !== undefined;
      if (!hasKey && !this.matchToken('String') && !this.matchToken('Word')) {
        innerComments = [...innerComments, ...leadingComments];
        break;
      }

      // Parse the property key
      let key;
      if (isFirstProp && firstKey !== undefined) {
        // If this is the first property and we've been given a property key then use it instead
        key = firstKey;
      } else {
        key = this.parseStringOrWord();
      }
      this.expectToken('Colon');

      // Having comments before the value is a really weird place to put them, but we'll handle it
      // anyway to avoid throwing a parser error. When stringified, the comments will all be before
      // the property.
      const leadingValueComments = this.eatComments();
      this.createContext(key);

      // Parse the value.
      const value = this.parseExpression();
      const end = this.getPosition();

      // Eat the comments after the expression and associate the comments with them
      let trailingValueComments = this.eatComments();

      // If the next token isn't a comma or closing brace then we've just stolen
      // the leading comments of the next property
      if (!this.matchToken('Comma') && !this.matchToken('BraceClose')) {
        nextLeadingComments = trailingValueComments;
        trailingValueComments = [];
      }

      this.provideContext({
        end,
        originalValue: value,
        comments: [
          ...leadingComments,
          ...leadingValueComments,
          ...trailingValueComments,
        ],
      });
      this.popContext();

      // In RJSON mode throw an error on duplicate keys
      if (
        !this.isRegularJSON &&
        Object.prototype.hasOwnProperty.call(obj, key)
      ) {
        throw this.unexpected({message: 'Duplicate property'});
      }

      // Set the object correctly, accounting for JS weirdness
      if (key === '__proto__') {
        // Need to use defineProperty to avoid triggering the Object.prototype.__proto__ setter
        Object.defineProperty(obj, '__proto__', {
          value,
          configurable: true,
          writable: true,
          enumerable: true,
        });
      } else {
        obj[key] = value;
      }

      isFirstProp = false;
    } while (this.isPropertySeparator());

    // Take any loose leading comments
    if (nextLeadingComments !== undefined) {
      innerComments = [...innerComments, ...nextLeadingComments];
    }

    // Associate any collected comments with this object. These could be comments that were trailing a property,
    // or comments in a propertyless object.
    this.provideContext({
      originalValue: obj,
      innerComments,
    });

    // If we were passed a first key then this was an implicit object so there's no end token
    if (firstKey === undefined) {
      this.expectToken('BraceClose');
    }

    return obj;
  }

  parseRegExp(): RegExp {
    if (this.isRegularJSON) {
      throw this.unexpected({
        message: messages.REGEX_IN_JSON(),
      });
    }

    const token = this.expectToken('Regex');
    if (token.type !== 'Regex') {
      throw new Error('Impossible');
    }

    const pattern = token.value;
    let flags = '';

    const nextToken = this.getToken();
    if (nextToken.type === 'Word') {
      this.nextToken();
      flags = nextToken.value;

      validateRegexFlags(flags, (msg, index) => {
        throw this.unexpected({
          message: msg,
          start: this.getPositionFromIndex(nextToken.start + index),
        });
      });
    }

    // TODO catch invalid regex
    return new RegExp(pattern, flags);
  }

  parseArray(): Array<unknown> {
    this.expectToken('BracketOpen');

    const arr = [];
    let innerComments: Comments = [];
    let i = 0;

    do {
      // Eat all the comments before an element
      const leadingComments = this.eatComments();

      // Throw a meainingful error for redundant commas
      if (this.matchToken('Comma')) {
        throw this.unexpected({
          message: messages.TRAILING_ARRAY_COMMA(),
        });
      }

      // If we're at the end of the array then associate these comments with the array
      if (this.matchToken('BracketClose')) {
        innerComments = [...innerComments, ...leadingComments];
        break;
      }

      // Track the element position for comment attachment
      this.createContext(i);
      i++;

      // Parse the value
      const item = this.parseExpression();
      arr.push(item);
      const end = this.getPosition();

      // Trailing comments are really weird, but let's handle them just like object properties
      const trailingComments = this.eatComments();
      const comments = [...leadingComments, ...trailingComments];
      this.provideContext({originalValue: item, end, comments});
      this.popContext();

      // Have a meaningful error message when an object is incorrectly using brackets: ["foo": "bar"]
      if (this.matchToken('Colon')) {
        throw this.unexpected({message: messages.MISTAKEN_ARRAY_IDENTITY()});
      }
    } while (this.isPropertySeparator());

    this.expectToken('BracketClose');

    // Similar to objects, we associate comments in an array with the element after them, so we need to specifically handle loose comments
    this.provideContext({
      originalValue: arr,
      innerComments,
    });

    return arr;
  }

  // Check if the current token is a property separator and eat it if necessary
  isPropertySeparator(): boolean {
    const token = this.getToken();

    // Implicit commas are only allowed in rjson
    if (this.isRegularJSON) {
      if (token.type !== 'Comma') {
        return false;
      }
      // Make sure this isn't a trailing comma
      const lookahead = this.lookaheadToken();
      if (
        lookahead.type === 'BraceClose' ||
        lookahead.type === 'BracketClose'
      ) {
        throw this.unexpected({message: messages.TRAILING_COMMA_IN_JSON()});
      }

      this.nextToken();
      return true;
    }

    // Eat the token, don't care if we're in RJSON
    if (token.type === 'Comma') {
      this.nextToken();
    }

    // An object or array close is an instant failure
    // Doesn't matter what we're parsing since the subsequent tokens will be validated
    if (token.type === 'BraceClose' || token.type === 'BracketClose') {
      return false;
    }

    return true;
  }

  parseWord(
    isStart: boolean,
  ): undefined | number | boolean | null | UnknownObject {
    const token = this.expectToken('Word');
    if (token.type !== 'Word') {
      throw new Error('Impossible');
    }

    switch (token.value) {
      case 'true':
        return true;

      case 'false':
        return false;

      case 'null':
        return null;

      case 'undefined':
        if (this.isRegularJSON) {
          throw this.unexpected({message: messages.UNDEFINED_IN_JSON()});
        } else {
          return undefined;
        }
    }

    if (this.isRegularJSON) {
      throw this.unexpected({
        message: messages.NUMERIC_WORD_IN_JSON(token.value),
      });
    }

    // The following are not valid in RJSON
    switch (token.value) {
      case 'Infinity':
        return Infinity;

      case 'NaN':
        return NaN;

      default:
        if (isStart && this.matchToken('Colon')) {
          if (this.isRegularJSON) {
            throw this.unexpected({
              message: messages.IMPLICIT_OBJECT_IN_JSON(),
            });
          }

          return this.parseObject(token.value);
        } else {
          throw this.unexpected();
        }
    }
  }

  expectDateNumber(callback: (num: number) => void): number {
    const token = this.getToken();
    if (token.type !== 'Number') {
      throw this.unexpected({message: 'Expected a date number'});
    }
    callback(token.value);
    this.nextToken();

    // Important to note that the return value will ALWAYS be >=0
    return token.value;
  }

  parseDateOnly(year: number): {monthIndex: number; day: number} {
    // Year
    if (year < 1 || year > 9999) {
      throw this.unexpected({
        message: 'Year must be between 1-9999',
      });
    }
    this.nextToken();

    // Month
    const month = this.expectDateNumber(val => {
      if (val < 1 || val > 12) {
        throw this.unexpected({
          message: 'Month must be between 1-12',
        });
      }
    });
    const monthIndex = month - 1;
    this.expectToken('Minus');

    // Get information about the month
    const isLeapYear = year % 4 === 0;
    const monthName = MONTH_NAMES[monthIndex];
    let totalDays = DAYS_IN_A_MONTH[monthIndex];

    // February has 1 extra day in a leap year!
    if (month === 2 && isLeapYear) {
      totalDays++;
    }

    // Day
    const day = this.expectDateNumber(val => {
      if (val === 0 || val > totalDays) {
        throw this.unexpected({
          message: `Day for ${monthName} must be between 1-${String(
            totalDays,
          )}`,
        });
      }
    });

    return {monthIndex, day};
  }

  parseTime(): {
    hour: number;
    minute: number;
    second: number;
    millisecond: number;
  } {
    // Hour
    const hour = this.expectDateNumber(val => {
      if (val > 24) {
        throw this.unexpected({
          message: 'Hour must be between 0-24',
        });
      }
    });
    this.expectToken('Colon');

    // Minute
    const minute = this.expectDateNumber(val => {
      if (val > 59) {
        throw this.unexpected({
          message: 'Hour must be between 0-59',
        });
      }
    });
    this.expectToken('Colon');

    // Second
    const second = this.expectDateNumber(val => {
      if (val > 59) {
        throw this.unexpected({
          message: 'Seconds must be between 0-59',
        });
      }
    });

    // Milliseconds
    let millisecond = 0;
    if (this.eatToken('Dot')) {
      millisecond = this.expectDateNumber(val => {
        if (val > 999) {
          throw this.unexpected({
            message:
              'Milliseconds must only have 3 digits and be between 0-999',
          });
        }
      });
    }

    return {hour, minute, second, millisecond};
  }

  eatDateEnd() {
    // Optional date end, stringify will never produce this but we'll allow
    // it for parsing consistency with the simplified ISO format
    const token = this.getToken();
    if (token.type === 'Word' && token.value === 'Z') {
      this.nextToken();
    }
  }

  // ISO date format: 2019-03-14T19:47:14.230Z
  parseDate(year: number): Date {
    const {monthIndex, day} = this.parseDateOnly(year);

    // Date end
    const timeEndToken = this.getToken();
    if (timeEndToken.type === 'Word' && timeEndToken.value === 'T') {
      this.nextToken();
    } else {
      this.eatDateEnd();

      // If there's no T word token then this is a timeless date
      return new Date(year, monthIndex, day, 0, 0, 0, 0);
    }

    const {hour, minute, second, millisecond} = this.parseTime();
    this.eatDateEnd();
    return new Date(year, monthIndex, day, hour, minute, second, millisecond);
  }

  parseNumber(): Date | bigint | number {
    const isNegative = this.eatToken('Minus') !== undefined;
    let hasDecimal = false;
    let hasSciNotation = false;

    // Allow valid numeric words like NaN and Infinity
    const token = this.getToken();
    if (token.type === 'Word' && isValidNumberWord(token.value)) {
      const num = this.parseWord(false);
      if (typeof num !== 'number') {
        throw new Error('Impossible');
      }
      return num;
    }

    // Get a string of the current number that we'll parse later
    if (token.type !== 'Number') {
      throw new Error('Impossible');
    }
    let value: string = String(token.value);

    // ISO date starts with YYYY-
    if (this.eatToken('Minus')) {
      return this.parseDate(token.value);
    }

    // Skip the current number token
    this.nextToken();

    // Decimals
    if (this.eatToken('Dot')) {
      value += '.';

      const decimal = this.expectToken('Number');
      if (decimal.type !== 'Number') {
        throw new Error('Impossible');
      }
      value += String(decimal.value);
      hasDecimal = true;
    }

    // Scientific notation
    const nextToken = this.getToken();
    if (
      nextToken.type === 'Word' &&
      (nextToken.value === 'e' || nextToken.value === 'E')
    ) {
      value += 'e';

      // Operator
      const operator = this.nextToken();
      if (operator.type === 'Plus') {
        value += '+';
      } else if (operator.type === 'Minus') {
        value += '-';
      } else {
        throw this.unexpected();
      }

      // Factor
      this.nextToken();
      const factor = this.expectToken('Number');
      if (factor.type !== 'Number') {
        throw new Error('Impossible');
      }
      value += String(factor.value);

      hasSciNotation = true;
    }

    // BigInt
    const nextToken2 = this.getToken();
    if (nextToken2.type === 'Word' && nextToken2.value === 'n') {
      this.nextToken();

      if (hasSciNotation) {
        throw this.unexpected({
          message: messages.BIGINT_WITH_SCI_NOTATION(),
        });
      }

      if (hasDecimal) {
        throw this.unexpected({
          message: messages.BIGINT_WITH_DECIMAL(),
        });
      }

      if (typeof BigInt === 'undefined') {
        throw this.unexpected({
          message: messages.BIGINT_UNSUPPORTED_ENV(),
        });
      }

      let num = BigInt(value);
      if (isNegative) {
        num = -num;
      }
      return num;
    }

    // Turn the string into an actual number
    let num = Number(value);
    if (isNegative) {
      num = -num;
    }
    return num;
  }

  parseStringOrWord() {
    const token = this.getToken();

    switch (token.type) {
      case 'String':
        this.nextToken();
        return token.value;

      case 'Word':
        if (this.isRegularJSON) {
          throw this.unexpected({
            message: messages.PROPERTY_KEY_UNQUOTED_IN_JSON(),
          });
        } else {
          this.nextToken();
          return token.value;
        }

      default:
        throw this.unexpected();
    }
  }

  parseString(isStart: boolean): string | UnknownObject {
    const token = this.expectToken('String');
    if (token.type !== 'String') {
      throw new Error('Impossible');
    }

    if (isStart && this.nextToken().type === 'Colon') {
      if (this.isRegularJSON) {
        throw this.unexpected({message: messages.IMPLICIT_OBJECT_IN_JSON()});
      }

      return this.parseObject(token.value);
    } else {
      return token.value;
    }
  }

  parseExpression(isStart: boolean = false): unknown {
    const token = this.getToken();

    switch (token.type) {
      case 'String':
        return this.parseString(isStart);

      case 'Minus':
      case 'Number':
        return this.parseNumber();

      case 'Word':
        return this.parseWord(isStart);

      case 'Regex':
        return this.parseRegExp();

      case 'BracketOpen':
        return this.parseArray();

      case 'BraceOpen': {
        this.nextToken();
        return this.parseObject();
      }

      default:
        throw this.unexpected();
    }
  }

  parseEntry(): unknown {
    if (this.matchToken('EOF')) {
      if (this.isRegularJSON) {
        throw this.unexpected({message: messages.EMPTY_INPUT_IN_JSON()});
      } else {
        // If we're in RJSON mode then an empty input is an implicit object
        return {};
      }
    } else {
      return this.parseExpression(true);
    }
  }

  parse(): JSONParserResult {
    // If we're in regular JSON mode, try the native JSON.parse
    // On syntax errors we'll fall back to our parser which is slower, but produces more meaningful errors
    if (this.isRegularJSON && this.locations === false) {
      try {
        const value = JSON.parse(this.input);

        this.createContext();
        const context = this.provideContext({
          originalValue: value,
        });

        return {
          context,
          value,
        };
      } catch (err) {
        if (!(err instanceof SyntaxError)) {
          throw err;
        }
      }
    }

    // Eat all the leading comments or expression parsing will fail
    const leadingComments = this.eatComments();

    // Parse the input text, and ensure we don't have any leftover tokens
    this.createContext();
    const expr = this.parseEntry();
    const end = this.getPosition();

    // Eat trailing comments
    const trailingComments = this.eatComments();
    const context = this.provideContext({
      originalValue: expr,
      end,
      comments: [...leadingComments, ...trailingComments],
    });

    this.finalize();

    return {
      value: expr,
      context,
    };
  }
}
