import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowFunctionTypeParam(
  generator: Generator,
  node: t.FlowFunctionTypeParamNode,
) {
  if (node.name) {
    generator.print(node.name, node);
    if (node.optional === true) {
      generator.token('?');
    }
    generator.token(':');
    generator.space();
  }
  generator.print(node.typeAnnotation, node);
}
