import {Node} from '@romejs/js-ast';
import {HydrateData} from '../Evaluator';
import {Scope} from '../scopes';
import T from './T';

export default class OpaqueT extends T {
  constructor(scope: Scope, originNode: undefined | Node, name: string) {
    super(scope, originNode);
    this.name = name;
  }

  static type = 'OpaqueT';
  name: string;

  serialize(): HydrateData {
    return {name: this.name};
  }

  static hydrate(scope: Scope, originNode: Node, data: HydrateData): T {
    return new OpaqueT(scope, originNode, String(data.name));
  }

  humanize(): string {
    return `opaque ${this.name}`;
  }

  compatibleWith(otherType: T): boolean {
    return otherType === this;
  }
}
