import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function ExportNamespaceSpecifier(
  generator: Generator,
  node: t.ExportNamespaceSpecifierNode,
) {
  generator.token('*');
  generator.space();
  generator.word('as');
  generator.space();
  generator.print(node.exported, node);
}
