import {Node, StatementNode} from '@romejs/js-ast';

export default function isStatement(
  node: undefined | Node,
): node is StatementNode {
  if (node === undefined) {
    return false;
  }

  switch (node.type) {
    case 'BlockStatement':
    case 'BreakStatement':
    case 'ContinueStatement':
    case 'DebuggerStatement':
    case 'DoWhileStatement':
    case 'EmptyStatement':
    case 'ExpressionStatement':
    case 'ForInStatement':
    case 'ForStatement':
    case 'FunctionDeclaration':
    case 'IfStatement':
    case 'LabeledStatement':
    case 'ReturnStatement':
    case 'SwitchStatement':
    case 'ThrowStatement':
    case 'TryStatement':
    case 'VariableDeclaration':
    case 'WhileStatement':
    case 'WithStatement':
    case 'ClassDeclaration':
    case 'ExportAllDeclaration':
    case 'ExportDefaultDeclaration':
    case 'ExportNamedDeclaration':
    case 'ForOfStatement':
    case 'ImportDeclaration':
    case 'FlowDeclareClass':
    case 'FlowDeclareFunction':
    case 'FlowDeclareInterface':
    case 'FlowDeclareModule':
    case 'FlowDeclareModuleExports':
    case 'FlowDeclareTypeAlias':
    case 'FlowDeclareOpaqueType':
    case 'FlowDeclareVariable':
    case 'FlowDeclareExportDeclaration':
    case 'FlowInterfaceDeclaration':
    case 'FlowTypeAlias':
    case 'FlowOpaqueType':
    case 'TSTypeAliasDeclaration':
    case 'ForAwaitStatement':
      return true;

    default:
      return false;
  }
}
