import {MasterBridge, SOCKET_PATH, CLI_SOCKET_PATH} from '@romejs/core';
import {createBridgeFromSocket} from '@romejs/events';
import setProcessTitle from './utils/setProcessTitle';
import {Master, BIN} from '@romejs/core';
import net = require('net');
import fs = require('fs');

export default function master() {
  setProcessTitle('master');

  // Kill the server on changes to itself, useful for upgrades and development
  fs.watch(BIN, {recursive: true, persistent: false}, () => {
    process.exit();
  });

  const master = new Master(true);

  return master.init().then(() => {
    //
    const socketServer = net.createServer(function(socket) {
      const client = createBridgeFromSocket(MasterBridge, socket);
      master.attachToBridge(client);
    });

    fs.unlink(SOCKET_PATH, () => {
      socketServer.listen(SOCKET_PATH, () => {
        const socket = net.createConnection(CLI_SOCKET_PATH, () => {
          socket.end();
        });

        socket.on('error', err => {
          // Socket error occured, cli could have died before it caught us
          err;
          process.exit();
        });
      });
    });
  });
}
