import {JSNodeBase, TSSignatureDeclarationBase} from '../index';
import {createBuilder} from '../utils';

export type TSCallSignatureDeclarationNode = JSNodeBase &
  TSSignatureDeclarationBase & {
    type: 'TSCallSignatureDeclaration';
  };

export const tsCallSignatureDeclaration = createBuilder<
  TSCallSignatureDeclarationNode
>('TSCallSignatureDeclaration', {
  visitorKeys: ['typeAnnotation', 'parameters', 'typeParameters'],
});
