import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowTypeAnnotation(
  generator: Generator,
  node: t.FlowTypeAnnotationNode,
) {
  generator.token(':');
  generator.space();
  generator.print(node.typeAnnotation, node);
}
