import {FunctionNode} from '@romejs/js-ast';
import {Scope} from '../scopes';
import T from '../types/T';
import {FunctionScope} from '../scopes';
import executeAtom from './executeAtom';
import FunctionT from '../types/FunctionT';
import MaybeT from '../types/MaybeT';
import VoidT from '../types/VoidT';
import OpenT from '../types/OpenT';

export default function executeFunction(
  node: FunctionNode,
  scope: Scope,
  bindId: boolean,
  thisContext?: T,
): FunctionT {
  // build return type
  const returns = new OpenT(scope, node.returnType ? node.returnType : node);

  // type check the body
  const bodyScope = new FunctionScope(
    {
      parentScope: scope,
    },
    {
      thisContext: thisContext ? thisContext : new VoidT(scope, undefined),
      returnType: returns,
    },
  );
  if (node.typeParameters) {
    bodyScope.evaluate(node.typeParameters);
  }

  // build param types
  const params = [];
  let rest;
  for (let paramNode of node.params) {
    let optional = paramNode.optional === true;
    if (paramNode.type === 'AssignmentPattern') {
      optional = false;
      paramNode = paramNode.left;
    }

    let paramType;
    if (paramNode.typeAnnotation) {
      paramType = scope.evaluate(paramNode.typeAnnotation);
    } else {
      paramType = new OpenT(scope, paramNode);
    }

    if (optional) {
      paramType = new MaybeT(scope, paramNode, paramType);
    }

    if (paramNode.type === 'RestElement') {
      // TODO assert that this is an array
      rest = paramType;
    } else {
      params.push(paramType);
    }
  }

  for (let i = 0; i < node.params.length; i++) {
    executeAtom(node.params[i], params[i], scope);
  }
  const block = bodyScope.evaluate(node.body);

  // if no types have flowed into the return type then it'll return undefined
  if (returns.hasConnections() === false) {
    //const ret = new VoidT(scope, node);
    //returns.shouldMatch(ret);
  }

  if (node.returnType) {
    returns.shouldMatch(scope.evaluate(node.returnType));
  }

  // create the function
  const func = new FunctionT(scope, node, {params, rest, returns, body: block});
  return func;
}
