import {JSNodeBase, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowTypeAnnotationNode = JSNodeBase & {
  type: 'FlowTypeAnnotation',
  typeAnnotation: FlowNode,
};

export const flowTypeAnnotation = createBuilder<FlowTypeAnnotationNode>(
  'FlowTypeAnnotation',
  {visitorKeys: ['typeAnnotation']},
);
