import {
  JSNodeBase,
  FlowInterfaceExtendsNode,
  FlowObjectTypeAnnotationNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowInterfaceTypeAnnotationNode = JSNodeBase & {
  type: 'FlowInterfaceTypeAnnotation';
  extends: Array<FlowInterfaceExtendsNode>;
  body: FlowObjectTypeAnnotationNode;
};

export const flowInterfaceTypeAnnotation = createBuilder<
  FlowInterfaceTypeAnnotationNode
>('FlowInterfaceTypeAnnotation', {
  visitorKeys: ['body', 'extends'],
});
