import {JSNodeBase, IdentifierNode, FunctionBase} from '../index';
import {createBuilder, BOUND_FUNCTION_VISITOR_KEYS} from '../utils';

export type FunctionDeclarationNode = JSNodeBase &
  FunctionBase & {
    type: 'FunctionDeclaration';
    id: IdentifierNode;
  };

export const functionDeclaration = createBuilder<FunctionDeclarationNode>(
  'FunctionDeclaration',
  {
    visitorKeys: BOUND_FUNCTION_VISITOR_KEYS,
  },
);
