import {JSNodeBase, IdentifierNode, TSModuleReferenceNode} from '../index';
import {createBuilder} from '../utils';

export type TSImportEqualsDeclarationNode = JSNodeBase & {
  type: 'TSImportEqualsDeclaration';
  id: IdentifierNode;
  moduleReference: TSModuleReferenceNode;
};

export const tsImportEqualsDeclaration = createBuilder<
  TSImportEqualsDeclarationNode
>('TSImportEqualsDeclaration', {visitorKeys: ['id', 'moduleReference']});
