import {JSNodeBase, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowNullableTypeAnnotationNode = JSNodeBase & {
  type: 'FlowNullableTypeAnnotation',
  typeAnnotation: FlowNode,
};

export const flowNullableTypeAnnotation = createBuilder<FlowNullableTypeAnnotationNode>(
  'FlowNullableTypeAnnotation',
  {visitorKeys: ['typeAnnotation']},
);
