import {JSNodeBase, TSSignatureDeclarationBase} from '../index';
import {createBuilder} from '../utils';

export type TSConstructSignatureDeclarationNode = JSNodeBase &
  TSSignatureDeclarationBase & {
    type: 'TSConstructSignatureDeclaration';
  };

export const tsConstructSignatureDeclaration = createBuilder<
  TSConstructSignatureDeclarationNode
>('TSConstructSignatureDeclaration', {
  visitorKeys: ['typeAnnotation', 'parameters', 'typeParameters'],
});
