import {
  JSNodeBase,
  JSXIdentifierNode,
  JSXNamespacedNameNode,
} from '../index';
import {createBuilder} from '../utils';

export type JSXMemberExpressionNode = JSNodeBase & {
  type: 'JSXMemberExpression',
  object: JSXMemberExpressionNode | JSXIdentifierNode | JSXNamespacedNameNode,
  property: JSXIdentifierNode,
};

export const jsxMemberExpression = createBuilder<JSXMemberExpressionNode>(
  'JSXMemberExpression',
  {visitorKeys: ['object', 'property']},
);
