import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import NullT from '../../types/NullT';

export function FlowNullLiteralTypeAnnotation(
  node: n.FlowNullLiteralTypeAnnotationNode,
  scope: Scope,
) {
  return new NullT(scope, node);
}
