import {
  JSNodeBase,
  IdentifierNode,
  FlowTypeParameterInstantiationNode,
  FlowInterfaceExtendsNode,
  FlowObjectTypeAnnotationNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowDeclareClassNode = JSNodeBase & {
  type: 'FlowDeclareClass',
  id: IdentifierNode,
  typeParameters?: undefined | FlowTypeParameterInstantiationNode,
  extends: Array<FlowInterfaceExtendsNode>,
  implements: Array<FlowInterfaceExtendsNode>,
  mixins: Array<FlowInterfaceExtendsNode>,
  body: FlowObjectTypeAnnotationNode,
};

export const flowDeclareClass = createBuilder<FlowDeclareClassNode>(
  'FlowDeclareClass',
  {visitorKeys: ['id', 'typeParameters', 'extends', 'body']},
);
