import {IndexTracker} from '@romejs/js-parser-utils';
import {Position, SourceLocation} from '@romejs/parser-core';
import {types as tt} from '../tokenizer/types';
import {isStrictBindReservedWord} from '@romejs/js-parser-utils';
import {
  ExpressionNode,
  Node,
  PatternNode,
  SpreadElementNode,
  RestElementNode,
  AssignmentPatternNode,
  SpreadPropertyNode,
  ArrayPatternNode,
  TSAccessibility,
} from '@romejs/js-ast';
import Parser, {OpeningContext} from '../Parser';
import {
  parseMaybeAssign,
  parseIdentifier,
  parseObjectPattern,
  typeCastToParameter,
  parseTypeAnnotation,
  parseTSAccessModifier,
  hasTSModifier,
} from './index';
import {PartialDiagnosticAdvice} from '@romejs/diagnostics';

const VALID_REST_ARGUMENT_TYPES = ['Identifier', 'MemberExpression'];

// Convert existing expression atom to assignable pattern if possible.
export function toAssignable(
  parser: Parser,
  node: Node,
  isBinding: undefined | boolean,
  contextDescription: string,
): Node {
  if (node) {
    switch (node.type) {
      case 'Identifier':
      case 'ObjectPattern':
      case 'ArrayPattern':
      case 'AssignmentPattern':
      case 'RestProperty':
      case 'RestElement':
        break;

      case 'FlowTypeCastExpression':
        return toAssignable(
          parser,
          typeCastToParameter(parser, node),
          isBinding,
          contextDescription,
        );

      case 'TSParameterProperty':
        return toAssignable(parser, node, isBinding, contextDescription);

      case 'TSAsExpression':
      case 'TSNonNullExpression':
      case 'TSTypeAssertion':
        return parser.retainParenthesized(node, {
          ...node,
          expression: toAssignable(
            parser,
            node.expression,
            isBinding,
            contextDescription,
          ),
        });

      case 'ObjectExpression': {
        const props = [];
        for (let index = 0; index < node.properties.length; index++) {
          const prop = node.properties[index];
          const isLast = index === node.properties.length - 1;
          props.push(
            toAssignableObjectExpressionProp(parser, prop, isBinding, isLast),
          );
        }
        return parser.retainParenthesized(node, {
          type: 'ObjectPattern',
          loc: node.loc,
          properties: props,
        });
      }

      case 'ObjectProperty':
        return parser.retainParenthesized(node, {
          ...node,
          value: toAssignable(
            parser,
            node.value,
            isBinding,
            contextDescription,
          ),
        });

      case 'SpreadProperty': {
        checkToRestConversion(parser, node);

        return parser.retainParenthesized(node, {
          type: 'RestProperty',
          loc: node.loc,
          argument: toAssignable(
            parser,
            node.argument,
            isBinding,
            contextDescription,
          ),
        });
      }

      case 'SpreadElement': {
        checkToRestConversion(parser, node);

        return parser.retainParenthesized(node, {
          type: 'RestElement',
          loc: node.loc,
          argument: toAssignable(
            parser,
            node.argument,
            isBinding,
            contextDescription,
          ),
        });
      }

      case 'ArrayExpression':
        return parser.retainParenthesized(node, {
          type: 'ArrayPattern',
          loc: node.loc,
          elements: toAssignableList(
            parser,
            node.elements,
            isBinding,
            contextDescription,
          ),
        });

      case 'AssignmentExpression': {
        if (node.operator !== '=') {
          parser.addDiagnostic({
            loc: parser.getLoc(node.left),
            message:
              "Only '=' operator can be used for specifying default value.",
          });
        }

        return parser.retainParenthesized<AssignmentPatternNode>(node, {
          type: 'AssignmentPattern',
          left: node.left,
          right: node.right,
          loc: node.loc,
        });
      }

      default: {
        if (node.type === 'MemberExpression' && !isBinding) {
          return node;
        }

        const message = `Invalid left-hand side in ${contextDescription}`;
        parser.addDiagnostic({
          loc: node.loc,
          message,
        });
        return parser.createUnknownNode(contextDescription);
      }
    }
  }

  return node;
}

export function toAssignableObjectExpressionProp(
  parser: Parser,
  prop: Node,
  isBinding: undefined | boolean,
  isLast: boolean,
): Node {
  if (prop.type === 'ObjectMethod') {
    const error =
      prop.kind === 'get' || prop.kind === 'set'
        ? "Object pattern can't contain getter or setter"
        : "Object pattern can't contain methods";

    parser.addDiagnostic({
      loc: prop.key.loc,
      message: error,
    });

    return parser.createUnknownNode('object pattern with method');
  } else if (prop.type === 'RestElement') {
    if (!isLast) {
      raiseRestNotLast(parser, parser.getLoc(prop));
    }

    return parser.retainParenthesized(prop, {
      type: 'RestProperty',
      ...prop,
    });
  } else {
    return toAssignable(
      parser,
      prop,
      isBinding,
      'object destructuring pattern',
    );
  }
}

export function toAssignableList(
  parser: Parser,
  exprList: Array<undefined | ExpressionNode>,
  isBinding: undefined | boolean,
  contextDescription: string,
): Array<PatternNode> {
  const newList: Array<ExpressionNode> = [];

  // Turn type casts that we found in function parameter head into type annotated params
  for (let i = 0; i < exprList.length; i++) {
    const expr = exprList[i];
    if (expr === undefined) {
      continue;
    }

    if (
      expr.type === 'FlowTypeCastExpression' ||
      expr.type === 'TSTypeCastExpression'
    ) {
      exprList[i] = typeCastToParameter(parser, expr);
    }

    if (expr.type === 'TSAsExpression' || expr.type === 'TSTypeAssertion') {
      parser.addDiagnostic({
        loc: expr.loc,
        message: 'Unexpected type cast in parameter position',
      });
    }
  }

  // Validate last element
  let end = exprList.length;
  if (end > 0) {
    let last = exprList[end - 1];

    if (last !== undefined && last.type === 'RestElement') {
      newList[end - 1] = last;
      end--;
    }

    if (last !== undefined && last.type === 'SpreadElement') {
      const arg = toAssignable(
        parser,
        last.argument,
        isBinding,
        contextDescription,
      );

      newList[end - 1] = parser.retainParenthesized(last, {
        type: 'RestElement',
        argument: arg,
        loc: last.loc,
      });

      if (
        ![
          'Identifier',
          'MemberExpression',
          'ArrayPattern',
          'ObjectPattern',
        ].includes(arg.type)
      ) {
        parser.unexpectedToken(parser.getLoc(arg).start);
      }

      end--;
    }
  }

  for (let i = 0; i < end; i++) {
    const elt = exprList[i];
    if (elt === undefined) {
      continue;
    }

    if (elt.type === 'SpreadElement' || elt.type === 'RestElement') {
      raiseRestNotLast(parser, parser.getLoc(elt));
    }

    newList[i] = toAssignable(parser, elt, isBinding, contextDescription);
  }

  return newList;
}

// this is a list of nodes, from 'something like a call expression, we need to filter the
// type casts that we've found that are illegal in this context
export function toReferencedList(
  parser: Parser,
  exprList: Array<undefined | ExpressionNode>,
  isParenthesizedExpr?: boolean,
): Array<undefined | ExpressionNode> {
  for (let i = 0; i < exprList.length; i++) {
    const expr = exprList[i];
    if (
      expr &&
      expr.type === 'FlowTypeCastExpression' &&
      !parser.isParenthesized(expr) &&
      (exprList.length > 1 || !isParenthesizedExpr)
    ) {
      parser.addDiagnostic({
        loc: expr.typeAnnotation.loc,
        message:
          'The type cast expression is expected to be wrapped with parentheses',
      });
    }
  }

  return exprList;
}

export function toReferencedListDeep(
  parser: Parser,
  exprList: Array<undefined | ExpressionNode>,
  isParenthesizedExpr?: boolean,
): Array<undefined | ExpressionNode> {
  toReferencedList(parser, exprList, isParenthesizedExpr);

  for (const expr of exprList) {
    if (expr && expr.type === 'ArrayExpression') {
      toReferencedListDeep(parser, expr.elements);
    }
  }

  return exprList;
}

export function parseSpread(
  parser: Parser,
  refShorthandDefaultPos?: undefined | IndexTracker,
  refNeedsArrowPos?: undefined | IndexTracker,
): SpreadElementNode {
  const start = parser.getPosition();
  parser.next();

  const argument = parseMaybeAssign(
    parser,
    'spread argument',
    false,
    refShorthandDefaultPos,
    undefined,
    refNeedsArrowPos,
  );

  if (parser.state.commaAfterSpreadAt === -1 && parser.match(tt.comma)) {
    parser.state.commaAfterSpreadAt = parser.state.index;
  }

  return {
    loc: parser.finishLoc(start),
    type: 'SpreadElement',
    argument,
  };
}

export function parseRest(parser: Parser): RestElementNode {
  const start = parser.getPosition();
  parser.next();

  const argument = parsePattern(parser);
  return {
    loc: parser.finishLoc(start),
    type: 'RestElement',
    argument,
  };
}

// Parses lvalue (assignable) atom.
export function parsePattern(parser: Parser): PatternNode {
  switch (parser.state.tokenType) {
    case tt.bracketL:
      return parseArrayPattern(parser);

    case tt.braceL:
      return parseObjectPattern(parser);
  }

  return parseIdentifier(parser);
}

function parseArrayPattern(parser: Parser): ArrayPatternNode {
  const start = parser.getPosition();
  const openContext = parser.expectOpening(
    tt.bracketL,
    tt.bracketR,
    'array pattern',
  );
  const elements = parseBindingList(parser, openContext, true);
  return {
    loc: parser.finishLoc(start),
    type: 'ArrayPattern',
    elements,
  };
}

export function parseBindingList(
  parser: Parser,
  openContext: OpeningContext,
  allowEmpty: boolean = false,
  allowTSModifiers: boolean = false,
): Array<undefined | PatternNode> {
  const elts: Array<undefined | PatternNode> = [];
  let first = true;
  while (true) {
    if (parser.match(openContext.close) || parser.match(tt.eof)) {
      parser.expectClosing(openContext);
      break;
    }

    if (first) {
      first = false;
    } else {
      if (!parser.eat(tt.comma)) {
        parser.addDiagnostic({
          message: `Expected a comma to separate items in ${openContext.name}`,
        });
        break;
      }
    }

    if (allowEmpty && parser.match(tt.comma)) {
      elts.push(undefined);
    } else if (parser.match(openContext.close)) {
      parser.expectClosing(openContext);
      break;
    } else if (parser.match(tt.ellipsis)) {
      elts.push(parseAssignableListItemTypes(parser, parseRest(parser)));

      if (!hasCommaAfterRest(parser)) {
        parser.expectClosing(openContext);
        break;
      }
    } else {
      elts.push(parseAssignableListItem(parser, allowTSModifiers));
    }
  }
  return elts;
}

export function parseBindingListNonEmpty(
  parser: Parser,
  openContext: OpeningContext,
  allowTSModifiers?: boolean,
): Array<PatternNode> {
  const list = parseBindingList(parser, openContext, false, allowTSModifiers);
  // $FlowFixMe: Figure out how to correctly type this
  return list;
}

export function parseAssignableListItem(
  parser: Parser,
  allowTSModifiers: boolean,
): PatternNode {
  const start = parser.getPosition();

  let accessibility: undefined | TSAccessibility;
  let readonly = false;
  if (allowTSModifiers) {
    accessibility = parseTSAccessModifier(parser);
    readonly = hasTSModifier(parser, ['readonly']);
  }

  const left = parseAssignableListItemTypes(parser, parseMaybeDefault(parser));
  const elt = parseMaybeDefault(parser, start, left);

  if (accessibility !== undefined || readonly) {
    if (!parser.isSyntaxEnabled('ts')) {
      parser.addDiagnostic({
        message:
          'Accessibility and readonly syntax found but TS is not enabled',
      });
    }

    if (elt.type !== 'Identifier' && elt.type !== 'AssignmentPattern') {
      parser.addDiagnostic({
        start,
        message:
          'A parameter property may not be declared using a binding pattern.',
      });
    }

    return {
      loc: parser.finishLoc(start),
      type: 'TSParameterProperty',
      parameter: elt,
      accessibility,
      readonly,
    };
  }

  return elt;
}

export function parseAssignableListItemTypes(
  parser: Parser,
  param: PatternNode,
): PatternNode {
  let typeAnnotation;
  let optional;

  if (parser.eat(tt.question)) {
    if (param.type !== 'Identifier') {
      parser.addDiagnostic({
        loc: param.loc,
        message:
          'A binding pattern parameter cannot be optional in an implementation signature.',
      });
    }

    optional = true;
  }

  if (parser.match(tt.colon)) {
    typeAnnotation = parseTypeAnnotation(parser);
  }

  // $FlowFixMe
  return {
    ...param,
    // TODO update loc: parser.finishLoc(),
    optional,
    typeAnnotation,
  };
}

// Parses assignment pattern around given atom if possible.

export function parseMaybeDefault(
  parser: Parser,
  start: Position = parser.getPosition(),
  left: PatternNode = parsePattern(parser),
): PatternNode {
  let target: PatternNode;

  if (parser.eat(tt.eq)) {
    const right = parseMaybeAssign(parser, 'assignment pattern right');
    target = {
      loc: parser.finishLoc(start),
      type: 'AssignmentPattern',
      left,
      right,
    } as AssignmentPatternNode;
  } else {
    target = left;
  }

  if (
    target.type === 'AssignmentPattern' &&
    target.typeAnnotation &&
    parser.getLoc(target.right).start.index <
      parser.getLoc(target.typeAnnotation).start.index
  ) {
    parser.addDiagnostic({
      loc: target.typeAnnotation.loc,
      message:
        'Type annotations must come before default assignments, e.g. instead of `age = 25: number` use `age: number = 25`',
    });
  }

  return target;
}

const ALLOWED_PARENTHESIZED_LVAL_TYPES = [
  'Identifier',
  'MemberExpression',
  'TSAsExpression',
  'TSTypeAssertion',
];

// Verify that a node is an lval  something that can be assigned
// to.
export function checkLVal(
  parser: Parser,
  expr: ExpressionNode,
  maybeIsBinding: undefined | boolean,
  checkClashes: undefined | Map<string, Node>,
  contextDescription: string,
): void {
  const isBinding: boolean =
    maybeIsBinding === undefined ? false : maybeIsBinding;

  // Verify that nodes aren't parenthesized
  if (
    parser.isParenthesized(expr) &&
    !ALLOWED_PARENTHESIZED_LVAL_TYPES.includes(expr.type)
  ) {
    let adviceMsg;
    if (expr.type === 'ObjectPattern') {
      adviceMsg = 'Did you use `({a}) = 0` instead of `({a} = 0)`?';
    } else if (expr.type === 'ArrayPattern') {
      adviceMsg = 'Did you use `([a]) = 0` instead of `([a] = 0)`?';
    }
    const advice: PartialDiagnosticAdvice = [];
    if (adviceMsg !== undefined) {
      advice.push({
        type: 'log',
        category: 'info',
        message: adviceMsg,
      });
    }

    parser.addDiagnostic({
      message: 'Invalid parenthesized binding',
      advice,
      loc: expr.loc,
    });
  }

  switch (expr.type) {
    case 'FlowTypeCastExpression':
      // Allow 'typecasts' to appear on the left of assignment expressions,
      // because it may be in an arrow function.
      // e.g. `const f = (foo: number = 0) => foo;`
      // This will be validated later
      return undefined;

    case 'TSParameterProperty':
      checkLVal(
        parser,
        expr.parameter,
        isBinding,
        checkClashes,
        'parameter property',
      );
      return undefined;

    case 'TSAsExpression':
    case 'TSNonNullExpression':
    case 'TSTypeAssertion':
      checkLVal(
        parser,
        expr.expression,
        isBinding,
        checkClashes,
        contextDescription,
      );
      return undefined;

    case 'Identifier':
      if (
        parser.inScope('STRICT') &&
        isStrictBindReservedWord(expr.name, parser.inModule)
      ) {
        parser.addDiagnostic({
          loc: expr.loc,
          message: `${expr.name} is a reserved word`,
        });
      }

      if (checkClashes !== undefined) {
        const clash = checkClashes.get(expr.name);

        if (clash === undefined) {
          checkClashes.set(expr.name, expr);
        } else {
          const loc = parser.getLoc(clash);
          parser.addDiagnostic({
            loc: expr.loc,
            message: 'Argument name clash in strict mode',
            advice: [
              {
                type: 'log',
                category: 'info',
                message: 'Collides with this existing definition',
              },
              {
                type: 'frame',
                filename: parser.filename,
                start: loc.start,
                end: loc.end,
              },
            ],
          });
        }
      }
      break;

    case 'MemberExpression':
      if (isBinding) {
        parser.addDiagnostic({
          loc: expr.loc,
          message: 'Binding member expression',
        });
      }
      break;

    case 'ObjectPattern':
      for (let prop of expr.properties) {
        if (prop.type === 'ObjectProperty') {
          prop = prop.value;
        }
        checkLVal(
          parser,
          prop,
          isBinding,
          checkClashes,
          'object destructuring pattern',
        );
      }
      break;

    case 'ArrayPattern':
      for (const elem of expr.elements) {
        if (elem) {
          checkLVal(
            parser,
            elem,
            isBinding,
            checkClashes,
            'array destructuring pattern',
          );
        }
      }
      break;

    case 'AssignmentPattern':
      checkLVal(
        parser,
        expr.left,
        isBinding,
        checkClashes,
        'assignment pattern',
      );
      break;

    case 'RestElement':
      checkLVal(parser, expr.argument, isBinding, checkClashes, 'rest element');
      break;

    case 'RestProperty':
      checkLVal(
        parser,
        expr.argument,
        isBinding,
        checkClashes,
        'rest property',
      );
      break;

    default: {
      const messageParts = [
        isBinding ? 'Binding invalid' : 'Invalid',
        'left-hand side',
        contextDescription ? 'in ' + contextDescription : 'expression',
      ];
      parser.addDiagnostic({
        loc: expr.loc,
        message: messageParts.join(' '),
      });
    }
  }
}

export function checkToRestConversion(
  parser: Parser,
  node: SpreadPropertyNode,
): void {
  if (VALID_REST_ARGUMENT_TYPES.includes(node.argument.type) === false) {
    parser.addDiagnostic({
      loc: node.argument.loc,
      message: "Invalid rest operator's argument",
    });
  }
}

export function hasCommaAfterRest(parser: Parser): boolean {
  if (parser.match(tt.comma)) {
    raiseRestNotLast(parser);
    return true;
  }

  return false;
}

export function raiseRestNotLast(
  parser: Parser,
  loc?: SourceLocation,
  start?: Position,
) {
  parser.addDiagnostic({
    start,
    loc,
    message: `The rest element has to be the last element when destructuring`,
  });
}

export function checkCommaAfterRestFromSpread(parser: Parser): void {
  if (parser.state.commaAfterSpreadAt > -1) {
    raiseRestNotLast(
      parser,
      undefined,
      parser.getPositionFromIndex(parser.state.commaAfterSpreadAt),
    );
  }
}

export function isRestLike(node: Node): boolean {
  return (
    node.type === 'TSRestType' ||
    node.type === 'RestElement' ||
    node.type === 'FlowFunctionTypeParamRest'
  );
}
