import {
  RangeNode,
  AbsoluteVersionNode,
  UserVersion,
  UserRange,
} from './types';
import {SemverParserOptions} from './parse';
import {parseRange, parseVersion} from './parse';

export function normalizeUserVersion(
  ver: UserVersion,
  opts?: SemverParserOptions,
): AbsoluteVersionNode {
  if (typeof ver === 'string') {
    return parseVersion({...opts, input: ver});
  } else if (ver.type === 'AbsoluteVersion') {
    return ver;
  } else {
    throw new Error('Not a valid version: ' + ver.type);
  }
}

export function normalizeUserRange(
  range: UserRange,
  opts?: SemverParserOptions,
): RangeNode {
  if (typeof range === 'string') {
    return parseRange({...opts, input: range});
  } else {
    return range;
  }
}
