import {JSNodeBase, IdentifierNode, StatementNode} from '../index';
import {createBuilder} from '../utils';

export type LabeledStatementNode = JSNodeBase & {
  type: 'LabeledStatement',
  label: IdentifierNode,
  body: StatementNode,
};

export const labeledStatement = createBuilder<LabeledStatementNode>(
  'LabeledStatement',
  {visitorKeys: ['label', 'body']},
);
