import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function WhileStatement(
  generator: Generator,
  node: t.WhileStatementNode,
) {
  generator.word('while');
  generator.space();
  generator.token('(');
  generator.print(node.test, node);
  generator.token(')');
  generator.printBlock(node);
}
