import {SourceLocation} from '@romejs/parser-core';
import {Manifest} from '@romejs/codec-js-manifest';
import Master from './Master';
import {Platform} from '../common/types/platform';
import {Project} from '../common/types/project';
import {PLATFORM_ALIASES} from '../common/types/platform';
import {DiagnosticError} from '@romejs/diagnostics';
import walkDirectoryPath from '../common/utils/walkDirectoryPath';
import {
  getExtensionHandler,
  IMPLICIT_JS_EXTENSIONS,
} from '../worker/extensions';
import path = require('path');

const NODE_MODULES = 'node_modules';
const MOCKS_DIR = '__mocks__';

type ResolverQuery = ResolverOptions & {
  origin: string;
  source: string;
  type?: 'package' | 'folder' | 'file';
  entry?: boolean;
};

export type ResolverOptions = {
  mocks?: boolean;
  platform?: Platform;
  scale?: number;
};

function getPrefferedMain(manifest: Manifest): undefined | string {
  const {main, 'rome:main': romeMain, 'jsnext:main': jsnextMain} = manifest;

  if (romeMain !== undefined) {
    return romeMain;
  }

  if (jsnextMain !== undefined) {
    return jsnextMain;
  }

  return main;
}

export default class Resolver {
  constructor(master: Master) {
    this.master = master;
  }

  master: Master;

  async findProjectFromQuery(query: ResolverQuery) {
    // if we were passed an absolute path then we should find and add the project it belongs to
    if (path.isAbsolute(query.source)) {
      await this.master.projectManager.findProject(query.source);
    } else {
      await this.master.projectManager.findProject(query.origin);
      await this.master.projectManager.findProject(
        path.join(query.origin, query.source),
      );
    }
  }

  async resolveEntryAssert(
    query: ResolverQuery,
    loc?: SourceLocation,
  ): Promise<string> {
    await this.findProjectFromQuery(query);
    return this.resolveAssert({...query, entry: true}, loc);
  }

  async resolveEntry(query: ResolverQuery): Promise<undefined | string> {
    await this.findProjectFromQuery(query);
    return this.resolve({...query, entry: true});
  }

  resolveAssert(query: ResolverQuery, loc?: SourceLocation): string {
    const resolved = this.resolve(query);
    if (resolved !== undefined) {
      return resolved;
    }

    // TODO make a custom error if this resolution is case sensitive
    // TODO if we failed to resolve and we were passed a platform, but another platform matches then add it to the error

    const errMsg = `Failed to resolve ${query.source} from '${query.origin}`;

    if (loc === undefined) {
      throw new Error(errMsg);
    }

    throw new DiagnosticError(errMsg, {
      category: 'resolver/resolve',
      filename: loc.filename,
      start: loc.start,
      end: loc.end,
      message: `Failed to resolve <emphasis>${query.source}</emphasis>`,
    });
  }

  resolve(query: ResolverQuery): undefined | string {
    // Do some basic checks to determine if this is an absolute or relative path
    const {source} = query;
    if (source === '' || source[0] === '.' || path.isAbsolute(source)) {
      return this.resolvePath(query);
    }

    // Now resolve it as a module
    const resolved = this.resolveModule(query);

    // If we didn't resolve to a module, and we were asked to resolve relative, then do that
    if (resolved === undefined && query.entry === true) {
      return this.resolvePath(query);
    }

    return resolved;
  }

  *getFilenameVariants(query: ResolverQuery): Iterable<string> {
    const seen: Set<string> = new Set();
    for (const variant of this._getFilenameVariants(query, ['root'])) {
      if (seen.has(variant)) {
        continue;
      }

      seen.add(variant);
      yield variant;
    }
  }

  *_getFilenameVariants(
    query: ResolverQuery,
    callees: Array<string>,
  ): Iterable<string> {
    const {platform, source} = query;
    const extname = path.extname(source);
    const noextname = path.join(
      path.dirname(source),
      path.basename(source, extname),
    );

    yield source;

    //
    const extension = getExtensionHandler(source);
    let usesUnknownExtension = extension === undefined;

    // Check with appended extensions
    if (usesUnknownExtension) {
      for (const ext of IMPLICIT_JS_EXTENSIONS) {
        yield* this._getFilenameVariants(
          {
            ...query,
            source: source + '.' + ext,
          },
          [...callees, 'unknownext'],
        );
      }
      return undefined;
    }

    // Check with appended `platform`
    if (platform !== undefined && !callees.includes('platform')) {
      yield* this._getFilenameVariants(
        {
          ...query,
          source: `${noextname}.${platform}`,
        },
        [...callees, 'platform'],
      );

      // check if this platform has any subplatforms
      const platformAliases = PLATFORM_ALIASES[platform];
      if (platformAliases !== undefined) {
        for (const platform of platformAliases) {
          yield* this._getFilenameVariants(
            {
              ...query,
              source: `${noextname}.${platform}`,
            },
            [...callees, 'platform'],
          );
        }
      }
    }

    // check with appended `scale`
    if (
      extension !== undefined &&
      extension.hasScale === true &&
      !callees.includes('scale')
    ) {
      const scale = query.scale === undefined ? 3 : query.scale;
      for (let i = scale; i >= 1; i--) {
        yield* this._getFilenameVariants(
          {
            ...query,
            source: `${noextname}@${String(i)}x${extname}`,
          },
          [...callees, 'scale'],
        );
      }
    }
  }

  resolvePath(
    query: ResolverQuery,
    checkVariants: boolean = true,
  ): undefined | string {
    const {memoryFs} = this.master;
    const {origin, source} = query;

    // Resolve the path heiarchy
    const originFolder = memoryFs.isFile(origin)
      ? path.dirname(origin)
      : origin;
    const loc = path.resolve(originFolder, source);

    // check if this is an absolute filename
    // TODO maybe this should be after the variant checking
    if (memoryFs.isFile(loc)) {
      // If we're querying a package then we should never return a file
      if (query.type === 'package') {
        return undefined;
      }

      return loc;
    }

    // check variants
    if (checkVariants) {
      for (const variant of this.getFilenameVariants(query)) {
        if (variant === source) {
          continue;
        }

        const resolved = this.resolvePath({...query, source: variant}, false);
        if (resolved !== undefined) {
          return resolved;
        }
      }
    }

    // check if this is a folder
    if (memoryFs.isDirectory(loc)) {
      if (query.type === 'folder') {
        return loc;
      }

      // If this has a package.json then follow the `main` field
      const manifest = memoryFs.getManifest(loc);
      if (manifest !== undefined) {
        // If we're resolving a package then don't follow this
        if (query.type === 'package') {
          return loc;
        }

        if (manifest !== undefined) {
          const main = getPrefferedMain(manifest);
          if (main !== undefined) {
            return this.resolvePath({...query, origin: loc, source: main});
          }
        }
      }

      // check if it has an index.* file
      for (const ext of IMPLICIT_JS_EXTENSIONS) {
        const indexResolved = this.resolvePath({
          ...query,
          source: path.join(loc, 'index.' + ext),
        });
        if (indexResolved !== undefined) {
          return indexResolved;
        }
      }
    }

    return undefined;
  }

  resolvePackageFolder(
    query: ResolverQuery,
    moduleName: string,
  ): undefined | string {
    // Find the project
    const project = this.master.projectManager.findProjectExisting(
      query.origin,
    );
    if (project === undefined) {
      return undefined;
    }

    // Find the package
    const projects = this.master.projectManager.getHierarchyFromProject(
      project,
    );
    for (const project of projects) {
      const pkg = project.packages.get(moduleName);
      if (pkg !== undefined) {
        return pkg.folder;
      }
    }

    return undefined;
  }

  resolvePackage(
    query: ResolverQuery,
    moduleName: string,
    moduleNameParts: Array<string>,
  ): undefined | string {
    const packageDir = this.resolvePackageFolder(query, moduleName);

    if (packageDir === undefined) {
      return undefined;
    } else {
      return this.resolvePath({
        ...query,
        source: path.join(packageDir, ...moduleNameParts),
      });
    }
  }

  resolveMock(
    query: ResolverQuery,
    parentDirectories: Array<string>,
  ): undefined | string {
    const moduleName = query.source;
    for (const dir of parentDirectories) {
      const mocksDir = path.join(dir, MOCKS_DIR);

      // No use resolving against a directory that doesn't exist
      if (!this.master.memoryFs.exists(mocksDir)) {
        continue;
      }

      const resolved = this.resolve({
        ...query,
        source: path.join(mocksDir, moduleName),
      });
      if (resolved !== undefined) {
        return resolved;
      }
    }
    return undefined;
  }

  resolveHaste(
    query: ResolverQuery,
    project: Project,
    moduleName: string,
    moduleNameParts: Array<string>,
  ): undefined | string {
    // check for an entry for the direct name, in the case we're given something like react-relay/modern/ReactRelayQueryFetcher we'll resolve react-relay then resolve from 'it with the rest of the path
    const resolved = project.hasteMap.get(moduleName);
    if (resolved !== undefined) {
      return this.resolvePath({
        ...query,
        source: path.join(resolved, ...moduleNameParts),
      });
    }

    // check all filename variants, we use the full module path here so using the parts separately isn't necessary
    for (const filename of this.getFilenameVariants(query)) {
      const hasteName = this.master.memoryFs.getHasteName(filename);

      if (hasteName !== undefined) {
        const resolved = project.hasteMap.get(hasteName);
        if (resolved !== undefined) {
          return this.resolvePath({
            ...query,
            source: resolved,
          });
        }
      }
    }

    return undefined;
  }

  // Given a reference to a module, extract the module name and any trailing relative paths
  splitModuleName(moduleNameFull: string): [string, Array<string>] {
    // fetch the first part of the path as that's the module name
    // possible values of `moduleNameFull` could be `react` or `react/lib/whatever`
    const [moduleName, ...moduleNameParts] = moduleNameFull.split('/');

    // For scoped modules in the form of `@romejs/bar`, make sure we keep the `/bar` on the module name
    if (moduleName[0] === '@' && moduleNameParts.length > 0) {
      return [moduleName + '/' + moduleNameParts.shift(), moduleNameParts];
    }

    return [moduleName, moduleNameParts];
  }

  resolveModule(query: ResolverQuery): undefined | string {
    const {origin, source} = query;

    // get project for the origin
    const project = this.master.projectManager.findProjectExisting(origin);

    // get all the parent directories for when we crawl up
    const parentDirectories = Array.from(
      walkDirectoryPath(path.dirname(origin)),
    );

    // if mocks are enabled for this query then check all parent __mocks__ folder
    if (query.mocks === true) {
      const mockResolved = this.resolveMock(query, parentDirectories);
      if (mockResolved !== undefined) {
        return mockResolved;
      }
    }

    // Extract the module name and it's relative file parts
    const [moduleName, moduleNameParts] = this.splitModuleName(source);

    // check the haste map
    if (project !== undefined && project.hasteMap.size > 0) {
      const hasteResolved = this.resolveHaste(
        query,
        project,
        moduleName,
        moduleNameParts,
      );
      if (hasteResolved !== undefined) {
        return hasteResolved;
      }
    }

    // Check if it matches any of our project packages
    const packageResolved = this.resolvePackage(
      query,
      moduleName,
      moduleNameParts,
    );
    if (packageResolved !== undefined) {
      return packageResolved;
    }

    // Check all parent directories for packages and node_modules
    for (const dir of parentDirectories) {
      // check for node_modules/*
      const nodeModulesLoc = path.join(dir, NODE_MODULES, source);
      const nodeModulesResolved = this.resolvePath({
        ...query,
        source: nodeModulesLoc,
      });
      if (nodeModulesResolved !== undefined) {
        return nodeModulesResolved;
      }
    }

    return undefined;
  }
}
