import {JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type DirectiveLiteralNode = JSNodeBase & {
  type: 'DirectiveLiteral',
  value: string,
};

export const directiveLiteral = createBuilder<DirectiveLiteralNode>(
  'DirectiveLiteral',
);
