import {MasterRequest} from '@romejs/core';
import {createMasterCommand} from '../../commands';
import {commandCategories} from '../../commands';
import path = require('path');

export default createMasterCommand({
  category: commandCategories.SOURCE_CODE,
  description: 'resolve a file',

  async default(req: MasterRequest): Promise<void> {
    const {master, args, reporter, flags} = req;
    if (args.length !== 2 && args.length !== 1) {
      throw new Error('Expected one or two file arguments');
    }

    let origin = '';
    let relative = '';

    if (args.length === 2) {
      origin = path.resolve(flags.cwd, args[0]);
      relative = args[1];
    } else {
      origin = flags.cwd;
      relative = args[0];
    }

    const loc = await master.resolver.resolveEntryAssert({
      ...req.getResolverOptionsFromFlags(),
      origin,
      source: relative,
    });
    if (loc === undefined) {
      throw new Error(`Could not resolve ${relative} from '${origin}`);
    } else {
      reporter.log(loc);
    }
  },
});
