import {JSNodeBase, IdentifierNode, FunctionBase} from '../index';
import {createBuilder} from '../utils';

export type TSDeclareFunctionNode = JSNodeBase &
  FunctionBase & {
    type: 'TSDeclareFunction';
    id: IdentifierNode;
    body?: void;
  };

export const tsDeclareFunction = createBuilder<TSDeclareFunctionNode>(
  'TSDeclareFunction',
  {
    visitorKeys: ['id', 'params', 'typeParameters', 'returnType', 'predicate'],
  },
);
