import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import ObjPropT from '../../types/ObjPropT';
import ObjT from '../../types/ObjT';

import invariant from '@romejs/invariant';

export function ObjectExpression(node: n.ObjectExpressionNode, scope: Scope) {
  const props = [];

  for (const prop of node.properties) {
    if (prop.type === 'SpreadProperty') {
      // TODO
    } else if (prop.type === 'ObjectProperty') {
      if (prop.computed === true) {
        // TODO
      } else {
        const {key, value} = prop;

        let keyStr;
        if (key.type === 'Identifier') {
          keyStr = key.name;
        } else {
          // TODO
          continue;
        }

        invariant(keyStr !== undefined, 'expected keyStr');
        props.push(new ObjPropT(scope, prop, keyStr, scope.evaluate(value)));
      }
    } else {
      // TODO
    }
  }

  return new ObjT(scope, node, {
    calls: [],
    props,
    proto: scope.intrinsics.ObjectPrototype,
  });
}
