import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowDeclareVariable(
  generator: Generator,
  node: t.FlowDeclareVariableNode,
  parent: t.Node,
) {
  if (parent.type !== 'FlowDeclareExportDeclaration') {
    generator.word('declare');
    generator.space();
  }
  generator.word('var');
  generator.space();
  generator.print(node.id, node);
  generator.print(node.id.typeAnnotation, node);
  generator.semicolon();
}
