import {MasterRequest, Master} from '@romejs/core';
import {
  getDiagnosticsFromError,
  deriveDiagnosticFromError,
} from '@romejs/diagnostics';
import {removeSuffix, removePrefix} from '@romejs/string-utils';
import Bundler from '../bundler/Bundler';
import {WebSocketInterface, createKey} from '@romejs/codec-websocket';
import http = require('http');
import url = require('url');

type ConsoleLogMessageLevel = 'trace' | 'info' | 'warn' | 'log' | 'error';

type ConsoleLogMessage = {
  id: number;
  level: ConsoleLogMessageLevel;
  data: Array<unknown>;
};

export class WebServer {
  constructor(req: MasterRequest) {
    this.req = req;
    this.master = req.master;

    this.consoleLogBuffer = new Set();
    this.lastPrintedLogId = undefined;

    this.server = http.createServer((req, res) => {
      this.handleRequest(req, res);
    });
  }

  consoleLogBuffer: Set<ConsoleLogMessage>;
  lastPrintedLogId: undefined | number;

  req: MasterRequest;
  master: Master;
  server: http.Server;

  close() {
    this.server.close();
  }

  listen(port: number) {
    this.server.listen(port);
  }

  loadRawBody(req: http.IncomingMessage): Promise<string> {
    req.setEncoding('utf8');
    let rawBody = '';

    return new Promise(resolve => {
      req.on('data', chunk => {
        rawBody += chunk;
      });

      req.on('end', () => {
        resolve(rawBody);
      });
    });
  }

  async handleRequest(
    req: http.IncomingMessage,
    res: http.ServerResponse,
  ): Promise<void> {
    try {
      const rawBody = await this.loadRawBody(req);
      await this.handleRequestWithBody(req, rawBody, res);
      res.end();
    } catch (err) {
      res.writeHead(500, {'Content-Type': 'text/plain'});
      console.error(err.stack);

      let diagnostics = getDiagnosticsFromError(err);
      if (diagnostics === undefined) {
        diagnostics = [deriveDiagnosticFromError({error: err})];
      }

      // TODO Clear console, and write diagnostics
      res.end('Diagnostics available, see console');
    }
  }

  async handleRequestWithBody(
    req: http.IncomingMessage,
    body: string,
    res: http.ServerResponse,
  ): Promise<void> {
    const reqUrl = req.url;
    if (reqUrl === undefined) {
      throw new Error('req.url should not be undefined');
    }
    const parts = url.parse(reqUrl, true);
    console.log('Request for', req.url);

    switch (parts.pathname) {
      case '/log-to-console':
        return this.handleConsoleLog(req, body, res);

      case '/websocket':
        return this.handleWebsocket(req, res);

      case '/script.js':
        return this.handleFrontendScript(req, res);

      case '/':
        res.writeHead(200, {'Content-Type': 'text/html'});
        res.end(`
          <!doctype html>
          <html>
            <head>
              <meta charset="utf-8"/>
              <title>Rome</title>
            </head>
            <body>
              <div id="app"></div>
              <script src="/script.js"></script>
            </body>
          </html>
        `);
        break;

      default:
        if (
          parts.pathname !== undefined &&
          parts.pathname.endsWith('.bundle')
        ) {
          return this.handleBundleRequest(req, parts, res);
        }

        res.writeHead(404);
        res.end('Not found');
    }
  }

  printConsoleLog(msg: ConsoleLogMessage) {
    this.lastPrintedLogId = msg.id;

    // Print the actual log
    const {reporter} = this.req;
    switch (msg.level) {
      case 'info':
        reporter.info('');
        break;

      case 'warn':
        reporter.warn('');
        break;

      case 'error':
        reporter.error('');
        break;

      default:
        //case 'log':
        //case 'trace':
        reporter.log('');
        break;
    }

    // Print any trailing logs that we buffered up
    for (let bufMsg of this.consoleLogBuffer) {
      if (bufMsg.id === msg.id + 1) {
        this.consoleLogBuffer.delete(bufMsg);
        this.printConsoleLog(bufMsg);
        break;
      }
    }
  }

  async handleBundleRequest(
    req: http.IncomingMessage,
    parts: url.UrlWithParsedQuery,
    res: http.ServerResponse,
  ) {
    const pathname = removePrefix(
      removeSuffix(String(parts.pathname), '.bundle'),
      '/',
    );

    const bundler = Bundler.createFromMasterRequest(this.req);
    const bundle = await bundler.bundle(pathname);

    res.writeHead(200, {'Content-Type': 'application/javascript'});
    res.end(bundle.content);
  }

  async handleFrontendScript(
    req: http.IncomingMessage,
    res: http.ServerResponse,
  ) {
    res.writeHead(200, {'Content-Type': 'application/javascript'});
    res.end('');
  }

  handleConsoleLog(
    req: http.IncomingMessage,
    body: string,
    res: http.ServerResponse,
  ) {
    // TODO parse and validate this
    const msg: ConsoleLogMessage = JSON.parse(body);

    // If we have never printed a log
    // or the last logged id is higher than ours (a device refresh)
    // or we're the next log in the sequence
    // then instantly print and set us as the current log
    if (
      this.lastPrintedLogId === undefined ||
      this.lastPrintedLogId >= msg.id ||
      this.lastPrintedLogId === msg.id - 1
    ) {
      this.printConsoleLog(msg);
    } else {
      // Otherwise queue up this message and wait for the previous
      this.consoleLogBuffer.add(msg);
    }
  }

  handleWebsocket(req: http.IncomingMessage, res: http.ServerResponse) {
    const digest = createKey(String(req.headers['sec-websocket-key']));

    const headers = [
      'HTTP/1.1 101 Switching Protocols',
      'Upgrade: websocket',
      'Connection: Upgrade',
      'Sec-WebSocket-Protocol: rome',
      `Sec-WebSocket-Accept: ${digest}`,
      '',
    ];

    req.socket.write(headers.join('\r\n'));

    const socket = new WebSocketInterface('server', req.socket);
    socket;
    res;
  }
}
