import {
  ModuleKind,
  JSNodeBase,
  StringLiteralNode,
  ImportSpecifierNode,
  ImportDefaultSpecifierNode,
  ImportNamespaceSpecifierNode,
} from '../index';
import {createBuilder} from '../utils';

export type ImportSpecifier =
  | ImportSpecifierNode
  | ImportDefaultSpecifierNode
  | ImportNamespaceSpecifierNode;

export type ImportDeclarationNode = JSNodeBase & {
  type: 'ImportDeclaration';
  specifiers: Array<ImportSpecifier>;
  source: StringLiteralNode;
  importKind?: undefined | ModuleKind;
};

export const importDeclaration = createBuilder<ImportDeclarationNode>(
  'ImportDeclaration',
  {visitorKeys: ['specifiers', 'source']},
);
