import {ExpressionNode, JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type TSExportAssignmentNode = JSNodeBase & {
  type: 'TSExportAssignment';
  expression: ExpressionNode;
};

export const tsExportAssignment = createBuilder<TSExportAssignmentNode>(
  'TSExportAssignment',
  {visitorKeys: ['expression']},
);
