import {
  JSNodeBase,
  TSSignatureDeclarationBase,
  PropertyNameBase,
} from '../index';
import {createBuilder} from '../utils';

export type TSMethodSignatureNode = JSNodeBase &
  TSSignatureDeclarationBase &
  PropertyNameBase & {
    type: 'TSMethodSignature';
    optional?: undefined | boolean;
  };

export const tsMethodSignature = createBuilder<TSMethodSignatureNode>(
  'TSMethodSignature',
  {
    visitorKeys: [
      'key',
      'variance',
      'typeAnnotation',
      'parameters',
      'typeParameters',
    ],
  },
);
