import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type SequenceExpressionNode = JSNodeBase & {
  type: 'SequenceExpression',
  expressions: Array<ExpressionNode>,
};

export const sequenceExpression = createBuilder<SequenceExpressionNode>(
  'SequenceExpression',
  {visitorKeys: ['expressions']},
);
