import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type AwaitExpressionNode = JSNodeBase & {
  type: 'AwaitExpression',
  argument?: undefined | ExpressionNode,
};

export const awaitExpression = createBuilder<AwaitExpressionNode>(
  'AwaitExpression',
  {visitorKeys: ['argument']},
);
