import {JSNodeBase, ExpressionNode, StatementNode} from '../index';
import {createBuilder} from '../utils';

export type WhileStatementNode = JSNodeBase & {
  type: 'WhileStatement',
  test: ExpressionNode,
  body: StatementNode,
};

export const whileStatement = createBuilder<WhileStatementNode>(
  'WhileStatement',
  {visitorKeys: ['test', 'body']},
);
