import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function TaggedTemplateExpression(
  generator: Generator,
  node: t.TaggedTemplateExpressionNode,
) {
  generator.print(node.tag, node);
  generator.print(node.quasi, node);
}
