import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowDeclareModule(
  generator: Generator,
  node: t.FlowDeclareModuleNode,
) {
  generator.word('declare');
  generator.space();
  generator.word('module');
  generator.space();
  generator.print(node.id, node);
  generator.space();
  generator.print(node.body, node);
}
