import {JSNodeBase, IdentifierNode, ModuleKind} from '../index';
import {createBuilder} from '../utils';

export type ImportDefaultSpecifierNode = JSNodeBase & {
  type: 'ImportDefaultSpecifier';
  local: IdentifierNode;
  importKind?: undefined | ModuleKind;
};

export const importDefaultSpecifier = createBuilder<ImportDefaultSpecifierNode>(
  'ImportDefaultSpecifier',
  {visitorKeys: ['local']},
);
