import {
  JSNodeBase,
  ClassMemberBase,
  ExpressionNode,
  FlowTypeAnnotationNode,
} from '../index';
import {createBuilder} from '../utils';

export type ClassPropertyNode = JSNodeBase &
  ClassMemberBase & {
    type: 'ClassProperty';
    key: ExpressionNode;
    value?: undefined | ExpressionNode;
    typeAnnotation?: undefined | FlowTypeAnnotationNode;
    definite?: undefined | boolean;
  };

export const classProperty = createBuilder<ClassPropertyNode>('ClassProperty', {
  visitorKeys: ['key', 'value', 'typeAnnotation', 'decorators'],
});
