import Master from '../Master';
import {SourceLocation} from '@romejs/parser-core';
import {AnalyzeDependencyResult, AnalyzeModuleType} from '@romejs/js-compiler';
import {BundleBuddyStats} from '../../common/types/bundler';
import {DiagnosticsProcessor} from '@romejs/diagnostics';
import {ResolverOptions} from '../Resolver';
import WorkerQueue from '../WorkerQueue';
import {catchDiagnosticsAsync} from '@romejs/diagnostics';
import DependencyNode from './DependencyNode';
import {ReporterProgress} from '@romejs/cli-reporter';
import Locker from '../../common/utils/Locker';
import invariant from '@romejs/invariant';
import {ModuleKind} from '@romejs/js-ast';

type ResolvedImport = {
  node: DependencyNode;
  hoisted: boolean;
  name: string;
  kind: ModuleKind;
};

const BUILTINS = [
  'electron',
  'buffer',
  'child_process',
  'crypto',
  'dgram',
  'dns',
  'fs',
  'http',
  'https',
  'net',
  'os',
  'readline',
  'stream',
  'string_decoder',
  'tls',
  'tty',
  'zlib',
  'constants',
  'events',
  'url',
  'assert',
  'util',
  'path',
  'punycode',
  'querystring',
  'cluster',
  'console',
  'module',
  'process',
  'vm',
  'domain',
  'v8',
  'repl',
  'timers',
  'inspector',
];

type SeedQueueItem = {
  all: boolean;
  async: boolean;
  ancestry: Array<string>;
  type: AnalyzeModuleType;
  loc: undefined | SourceLocation;
};

export default class DependencyGraph {
  constructor(master: Master, resolverOpts: ResolverOptions) {
    this.master = master;
    this.nodes = new Map();
    this.resolverOpts = resolverOpts;

    this.seedQueue = new WorkerQueue(master);

    this.locker = new Locker();
  }

  resolverOpts: ResolverOptions;
  master: Master;
  nodes: Map<string, DependencyNode>;
  locker: Locker<string>;
  seedQueue: WorkerQueue<SeedQueueItem>;

  isExternal(source: string): boolean {
    return BUILTINS.includes(source);
  }

  getBundleBuddyStats(entries: Array<string>): BundleBuddyStats {
    const stats: BundleBuddyStats = [];

    for (const node of this.nodes.values()) {
      const source = node.id;

      for (const absoluteTarget of node.resolvedDependencies.values()) {
        const target = this.getNode(absoluteTarget).id;
        stats.push({
          target,
          source,
        });
      }
    }

    for (const absoluteEntry of entries) {
      const source = this.getNode(absoluteEntry).id;
      stats.push({
        source,
        target: undefined,
      });
    }

    return stats;
  }

  addNode(filename: string, res: AnalyzeDependencyResult) {
    const module = new DependencyNode(
      this,
      this.master.projectManager.getUid(filename),
      filename,
      res,
    );
    this.nodes.set(filename, module);
    return module;
  }

  getNode(filename: string): DependencyNode {
    const mod = this.nodes.get(filename);
    if (mod === undefined) {
      throw new Error(`No module found for ${filename}`);
    }
    return mod;
  }

  async seed(
    filename: string,
    diagnosticsProcessor: DiagnosticsProcessor,
    analyzeProgress?: ReporterProgress,
  ): Promise<DependencyNode> {
    // Add initial queue items
    const root = await this.resolve(
      filename,
      {
        all: true,
        async: false,
        ancestry: [],
      },
      diagnosticsProcessor,
      analyzeProgress,
    );

    this.seedQueue.addCallback(async (filename, item) => {
      const mod = await this.resolve(
        filename,
        {
          all: item.all,
          async: item.async,
          ancestry: item.ancestry,
        },
        diagnosticsProcessor,
        analyzeProgress,
      );
      mod;

      // Cannot import CJS as ES
      /*if (mod.type === 'es' && item.type === 'cjs') {
        this.diagnostics.addDiagnostic({
          ...item.loc,
          filename,
          message: 'You cannot import an ES module as a CommonJS module',
        });
      }

      // Cannot import an ES module as CJS
      if (mod.type === 'cjs' && item.type === 'es') {
        this.diagnostics.addDiagnostic({
          ...item.loc,
          filename,
          message: 'You cannot import a CommonJS module as an ES module',
        });
      }*/
    });

    await this.seedQueue.spin();

    return root;
  }

  async resolve(
    filename: string,
    opts: {
      all: boolean;
      async: boolean;
      ancestry: Array<string>;
    },
    diagnosticsProcessor: DiagnosticsProcessor,
    analyzeProgress?: ReporterProgress,
  ): Promise<DependencyNode> {
    const {async, all, ancestry} = opts;
    const {master} = this;

    if (async === true) {
      invariant(
        all === true,
        "When async is set on a module then all should be too but it wasn't...",
      );
    }

    // We have a lock here in case we hit `this.resolve` while we're waiting for the `analyzeDependencies` result
    const lock = await this.locker.getLock(filename);

    if (this.nodes.has(filename)) {
      const node = this.getNode(filename);

      if (all) {
        node.setAll(true);
      }

      if (async) {
        node.setUsedAsync(true);
      }

      lock.release();

      return node;
    }

    if (analyzeProgress !== undefined) {
      analyzeProgress.pushText(filename);
    }

    const res: AnalyzeDependencyResult = await master.api.analyzeDependencies(
      filename,
    );

    const node = this.addNode(filename, res);
    node.setAll(all);
    node.setUsedAsync(async);
    lock.release();

    const {dependencies, diagnostics} = res;

    if (diagnostics.length > 0) {
      diagnosticsProcessor.addDiagnostics(diagnostics);
    }

    // resolve full locations
    await Promise.all(
      dependencies.map(async dep => {
        const {source, optional} = dep;
        if (this.isExternal(source)) {
          return undefined;
        }

        const {foundDiagnostics, diagnostics} = await catchDiagnosticsAsync(
          'Caught by DependencyGraph.resolve',
          async () => {
            const absolute = await master.resolver.resolveAssert(
              {
                origin: filename,
                ...this.resolverOpts,
                source,
              },
              // TODO support non-loc
              dep.loc,
            );

            node.addDependency(source, absolute, dep);
          },
        );

        if (foundDiagnostics && !optional) {
          diagnosticsProcessor.addDiagnostics(diagnostics);
        }
      }),
    );

    // Queue our dependencies...
    const subAncestry = [...ancestry, filename];
    for (const filename of node.getAbsoluteDependencies()) {
      const dep = node.getDependencyInfoFromAbsolute(filename);
      this.seedQueue.pushQueue(filename, {
        all: dep.all,
        async: dep.async,
        type: dep.type,
        loc: dep.loc,
        ancestry: subAncestry,
      });
    }

    if (analyzeProgress !== undefined) {
      analyzeProgress.popText(filename);
      analyzeProgress.tick();
    }

    return node;
  }

  resolveImport(
    name: string,
    filename: string,
    ignoreDefault: boolean = false,
    ancestry: Array<string> = [],
  ): undefined | ResolvedImport {
    if (ancestry.includes(filename)) {
      return undefined;
    }

    const node = this.getNode(filename);
    const deps = node.resolvedDependencies;
    const subAncestry = [...ancestry, filename];

    // We always want to resolve exports from the bottom up
    for (const record of node.analyze.exports.reverse()) {
      // When resolving exportAll we never want to include the default export of those moduels
      if (
        record.type === 'local' &&
        record.name === 'default' &&
        ignoreDefault
      ) {
        continue;
      }

      if (record.type === 'local' && record.name === name) {
        return {
          node,
          kind: record.kind,
          hoisted: record.hoisted,
          name: record.name,
        };
      }

      if (record.type === 'external' && record.exported === name) {
        const absolute = deps.get(record.source);
        if (absolute === undefined) {
          throw new Error(
            `Expected source ${
              record.source
            } for file ${filename}, have ${deps}`,
          );
        }

        return this.resolveImport(
          record.imported,
          absolute,
          false,
          subAncestry,
        );
      }

      if (record.type === 'externalAll') {
        const absolute = deps.get(record.source);
        if (absolute === undefined) {
          throw new Error(
            `Expected source ${
              record.source
            } for file ${filename}, have ${deps}`,
          );
        }

        const maybeResolved = this.resolveImport(
          name,
          absolute,
          true,
          subAncestry,
        );
        if (maybeResolved !== undefined) {
          return maybeResolved;
        }
      }
    }

    return undefined;
  }
}
