import {Node} from '@romejs/js-ast';

export default function hasPotentialSideEffects(node: undefined | Node): boolean {
  if (node === undefined) {
    return false;
  }

  switch (node.type) {
    case 'ExportNamedDeclaration':
      if (node.source === undefined) {
        if (node.declaration === undefined) {
          return true;
        } else {
          return hasPotentialSideEffects(node.declaration);
        }
      } else {
        return true;
      }

    case 'FunctionDeclaration':
      return false;

    case 'ClassDeclaration':
      return node.superClass !== undefined;

    case 'Identifier':
      // TODO check if this is in scope
      return false;

    case 'VariableDeclaration':
      for (const declarator of node.declarations) {
        if (hasPotentialSideEffects(declarator)) {
          return true;
        }
      }
      return false;

    case 'VariableDeclarator':
      return (
        hasPotentialSideEffects(node.id) || hasPotentialSideEffects(node.init)
      );

    case 'AssignmentPattern':
      return hasPotentialSideEffects(node.right);

    case 'ObjectExpression':
    case 'ObjectPattern':
      for (const prop of node.properties) {
        if (hasPotentialSideEffects(prop)) {
          return true;
        }
      }
      return false;

    case 'ObjectProperty':
      if (node.computed == true && hasPotentialSideEffects(node.key)) {
        return true;
      } else {
        return hasPotentialSideEffects(node.value);
      }

    case 'ArrayPattern':
    case 'ArrayExpression':
      for (const elem of node.elements) {
        if (hasPotentialSideEffects(elem)) {
          return true;
        }
      }
      return false;

    case 'StringLiteral':
    case 'NumericLiteral':
    case 'BooleanLiteral':
    case 'NullLiteral':
      return false;
  }

  return true;
}
