import {
  JSNodeBase,
  FunctionDeclarationNode,
  ClassDeclarationNode,
  ExpressionNode,
} from '../index';
import {createBuilder} from '../utils';

export type ExportDefaultDeclarationNode = JSNodeBase & {
  type: 'ExportDefaultDeclaration';
  // TODO the `id` on these are optional
  declaration: FunctionDeclarationNode | ClassDeclarationNode | ExpressionNode;
};

export const exportDefaultDeclaration = createBuilder<
  ExportDefaultDeclarationNode
>('ExportDefaultDeclaration', {visitorKeys: ['declaration']});
