import {Scope} from '../../scopes';
import {ClassScope} from '../../scopes';
import * as n from '@romejs/js-ast';
import ObjPropT from '../../types/ObjPropT';
import executeFunction from '../../utils/executeFunction';
import invariant from '@romejs/invariant';

export function ClassMethod(node: n.ClassMethodNode, scope: Scope) {
  if (node.computed === true) {
    // TODO
    return undefined;
  }

  const classScope = scope.find(ClassScope);
  const thisContext =
    node.static === true ? classScope.meta.static : classScope.meta.instance;
  const func = executeFunction(node, scope, false, thisContext);

  invariant(node.key.type === 'Identifier', 'Expected only an identifier key');
  return new ObjPropT(scope, node, node.key.name, func);
}
