import {Node} from './index';
import invariant from '@romejs/invariant';

export const FUNCTION_VISITOR_KEYS = [
  'params',
  'body',
  'returnType',
  'typeParameters',
  'predicate',
  'decorators',
];
export const BOUND_FUNCTION_VISITOR_KEYS = ['id', ...FUNCTION_VISITOR_KEYS];
export const METHOD_FUNCTION_VISITOR_KEYS = ['key', ...FUNCTION_VISITOR_KEYS];

export const CLASS_VISITOR_KEYS = [
  'id',
  'body',
  'superClass',
  'mixins',
  'typeParameters',
  'superTypeParameters',
  'implements',
  'decorators',
];

export const visitorKeys: Map<string, Array<string>> = new Map();
export const nodeNames: Set<string> = new Set();

export function assertNodeTypeSet(names: Iterable<string>) {
  const set = new Set(names);

  const errors = [];

  // Verify that all names in `set` are valid
  for (const name of set) {
    if (name[0] === '_') {
      continue;
    }

    if (nodeNames.has(name) === false) {
      errors.push(`${name} is not a valid node`);
    }
  }

  // Verify all nodes
  for (const name of nodeNames) {
    if (set.has(name) === false) {
      errors.push(`${name} does not appear`);
    }
  }

  if (errors.length > 0) {
    throw new Error(errors.join('\n'));
  }
}

export function createBuilder<T extends Node>(
  type: string,
  opts: {
    visitorKeys?: undefined | Array<keyof T>;
  } = {},
): Builder<T> {
  nodeNames.add(type);

  if (opts.visitorKeys !== undefined) {
    visitorKeys.set(type, opts.visitorKeys);
  }

  return new Builder<T>(
    type,
    opts.visitorKeys === undefined ? [] : opts.visitorKeys,
  );
}

class Builder<T> {
  constructor(type: string, visitorKeys: Array<string>) {
    this.type = type;
    this.visitorKeys = visitorKeys;
  }

  type: string;
  visitorKeys: Array<string>;

  create(opts: Omit<T, 'type'>): T {
    // @ts-ignore
    return {
      ...opts,
      type: this.type,
    };
  }

  is(node: undefined | Node): boolean {
    return node !== undefined && node.type === this.type;
  }

  normalize(node: undefined | Node): undefined | T {
    if (this.is(node)) {
      return node;
    }
  }

  assert(node: undefined | Node): T {
    invariant(
      node !== undefined && node.type === this.type,
      `Expected ${this.type} Node`,
    );
    return node;
  }
}
