import {Node} from '@romejs/js-ast';
import {Path} from '@romejs/js-compiler';
import {Binding} from '@romejs/js-compiler';
import inheritLoc from './inheritLoc';
import {
  exportSpecifier,
  exportNamedDeclaration,
  identifier,
} from '@romejs/js-ast';
import isReferenced from './isReferenced';
import isBinding from './isBinding';
import getBindingIdentifiers from './getBindingIdentifiers';

export default function renameBindings(
  path: Path,
  mapping: Map<string, string>,
): Node {
  if (mapping.size === 0) {
    return path.node;
  }

  // get a list of the current bindings for this scope
  const currBindings: Map<string, undefined | Binding> = new Map();
  for (const [oldName] of mapping) {
    const binding = path.scope.getBinding(oldName);
    currBindings.set(oldName, binding);
  }

  // discover nodes to replace first without manipulating the AST as that will change the scope and binding objects
  const replaceNodes: Set<Node> = new Set();
  path.traverse('renameBindingsCollector', path => {
    const {node, scope} = path;
    if (node.type !== 'Identifier') {
      return;
    }

    const isRefId = isReferenced(path);
    const isBindingId = isBinding(node, path.parent);
    if (!isRefId && !isBindingId) {
      return;
    }

    const hasMatch =
      mapping.has(node.name) &&
      scope.getBinding(node.name) === currBindings.get(node.name);
    if (hasMatch) {
      replaceNodes.add(node);
    }
  });
  if (replaceNodes.size === 0) {
    return path.node;
  }

  //
  const replaced: Set<Node> = new Set();

  // replace the nodes
  const renamedNode = path.reduce({
    name: 'renameBindings',
    enter(path) {
      const {node} = path;

      // Retain the correct exported name for `export function` and `export class`
      if (
        node.type === 'ExportNamedDeclaration' &&
        node.declaration !== undefined &&
        replaceNodes.has(node.declaration.id)
      ) {
        replaced.add(node.declaration.id);

        const oldName = node.declaration.id.name;
        const newName = mapping.get(oldName);
        if (newName === undefined) {
          throw new Error('Expected new name');
        }

        return [
          node.declaration,
          exportNamedDeclaration.create({
            specifiers: [
              exportSpecifier.create({
                local: identifier.create({name: newName}),
                exported: identifier.create({name: oldName}),
              }),
            ],
          }),
        ] as Array<Node>;
      }

      // Retain the correct exported names for `export const`
      if (
        node.type === 'ExportNamedDeclaration' &&
        node.declaration !== undefined
      ) {
        const bindings = getBindingIdentifiers(node.declaration);
        let includesAny = false;
        for (const node of bindings) {
          if (replaceNodes.has(node)) {
            includesAny = true;
            break;
          }
        }

        if (includesAny) {
          return [
            node.declaration,
            exportNamedDeclaration.create({
              specifiers: bindings.map(node => {
                let local: undefined | string = node.name;
                if (replaceNodes.has(node)) {
                  local = mapping.get(local);
                  if (local === undefined) {
                    throw new Error('Expected local');
                  }
                  replaced.add(node);
                }
                return exportSpecifier.create({
                  local: identifier.create({name: local}),
                  exported: identifier.create({name: node.name}),
                });
              }),
            }),
          ] as Array<Node>;
        }
      }

      if (node.type === 'Identifier' && replaceNodes.has(node)) {
        const name = mapping.get(node.name);
        if (name === undefined) {
          throw new Error('Expected name');
        }
        replaced.add(node);
        return identifier.create({
          ...node,
          name,
          loc: inheritLoc(node, node.name),
        });
      }

      return node;
    },
  });

  //
  if (replaced.size !== replaceNodes.size) {
    throw new Error('Missed some bindings');
  }

  return renamedNode;
}
