import Scope from '../Scope';
import * as t from '@romejs/js-ast';

export default function ExportDefaultDeclaration(
  node: t.ExportDefaultDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  const {declaration} = node;
  const newScope = scope.evaluate(declaration, node);
  if (
    (declaration.type === 'ClassDeclaration' ||
      declaration.type === 'FunctionDeclaration') &&
    declaration.id !== undefined
  ) {
    newScope.getBindingAssert(declaration.id.name).setExported(true);
  }
  return newScope;
}
