import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type UpdateExpressionNode = JSNodeBase & {
  type: 'UpdateExpression',
  operator: UpdateOperator,
  argument: ExpressionNode,
  prefix?: undefined | boolean,
};

export type UpdateOperator = '++' | '--';

export const updateExpression = createBuilder<UpdateExpressionNode>(
  'UpdateExpression',
  {visitorKeys: ['argument']},
);
