import {Node} from '@romejs/js-ast';
import {Scope} from '@romejs/js-compiler';
import {
  Context,
  TransformVisitor,
  TransformVisitors,
} from '@romejs/js-compiler';
import {HookRef} from '../api/createHook';
import {PartialDiagnostic} from '@romejs/diagnostics';
import reduce from '../methods/reduce';
import invariant from '@romejs/invariant';

export type PathOptions = {
  ancestryPaths?: Array<Path>;
  nodeKey?: undefined | string;
  listKey?: undefined | number;
  parentScope?: undefined | Scope;
  scope?: undefined | Scope;
  noArrays?: boolean;
  noScopeCreation?: undefined | boolean;
};

type Handlers = Array<HookRef>;

export default class Path {
  constructor(
    node: Node,
    context: Context,
    opts: PathOptions,
    hooks: Handlers = [],
  ) {
    const ancestryPaths = opts.ancestryPaths || [];
    this.ancestryPaths = ancestryPaths;
    this.parentPath = ancestryPaths[0];

    this.node = node;
    this.parent = this.parentPath ? this.parentPath.node : undefined;
    this.context = context;

    const parentScope =
      opts.parentScope === undefined
        ? context.getRootScope()
        : opts.parentScope;

    let scope = opts.scope;
    if (scope === undefined) {
      if (opts.noScopeCreation === true) {
        scope = parentScope;
      } else {
        scope = parentScope.evaluate(node, this.parent, true);
      }
    }
    this.scope = scope;

    this.nodeKey = opts.nodeKey;
    this.listKey = opts.listKey;

    this.opts = opts;

    this.hooks = hooks;
  }

  context: Context;
  node: Node;
  parent: undefined | Node;
  scope: Scope;
  hooks: Handlers;
  opts: PathOptions;

  ancestryPaths: Array<Path>;
  parentPath: undefined | Path;

  nodeKey: undefined | string;
  listKey: undefined | number;

  findHooks(id: Symbol): Array<HookRef> {
    const handles = [];

    for (const {hooks} of this.ancestryPaths) {
      for (const hook of hooks) {
        if (hook.id === id) {
          handles.push(hook);
        }
      }
    }

    return handles;
  }

  findAncestry(callback: (path: Path) => boolean): undefined | Path {
    for (const path of this.ancestryPaths) {
      if (callback(path)) {
        return path;
      }
    }
  }

  getChildPath(key: string): Path {
    const node = this.node[key];
    invariant(
      node !== undefined,
      'Attempted to get child path for %s but no such node existed',
      key,
    );

    return new Path(node, this.context, {
      parentScope: this.scope,
      ancestryPaths: this.ancestryPaths.concat([this]),
      nodeKey: key,
    });
  }

  getChildPaths(key: string): Array<Path> {
    const nodes = this.node[key];

    invariant(
      nodes !== undefined,
      'Attempted to get child paths for %s but no such node existed',
      key,
    );

    invariant(
      Array.isArray(nodes),
      'Expected child nodes for %s to be an array',
      key,
    );

    const ancestryPaths = this.ancestryPaths.concat([this]);

    return nodes.map((node: Node, i: number) => {
      return new Path(node, this.context, {
        parentScope: this.scope,
        ancestryPaths,
        listKey: i,
        nodeKey: key,
      });
    });
  }

  getParent(): Node {
    const {parent} = this;
    if (parent === undefined) {
      throw new Error("Path doesn't have a parent");
    }
    return parent;
  }

  getParentPath(): Path {
    const {parentPath} = this;
    if (parentPath === undefined) {
      throw new Error("Path doesn't have a parent");
    }
    return parentPath;
  }

  getPathKeys(): Array<string> {
    const parts = [];

    let path: undefined | Path = this;
    while (path) {
      if (path.listKey !== undefined) {
        parts.push(String(path.listKey));
      }
      if (path.nodeKey !== undefined) {
        parts.push(path.nodeKey);
      }
      path = path.parentPath;
    }

    return parts.reverse();
  }

  fork(newNode: Node): Path {
    return new Path(newNode, this.context, this.getPathOptions(), this.hooks);
  }

  getPathOptions(): PathOptions {
    return {
      ...this.opts,
      parentScope:
        this.scope === undefined ? undefined : this.scope.parentScope,
    };
  }

  traverse(name: string, callback: (path: Path) => void) {
    this.reduce({
      name,
      enter(path: Path) {
        callback(path);
        return path.node;
      },
    });
  }

  reduce(visitors: TransformVisitor | TransformVisitors): Node {
    return reduce(
      this.node,
      Array.isArray(visitors) ? visitors : [visitors],
      this.context,
      this.getPathOptions(),
    );
  }

  addDiagnostic(opts: PartialDiagnostic) {
    return this.context.addNodeDiagnostic(this.node, opts);
  }
}
