import {MarkupFormatOptions} from '@romejs/string-markup';
import {
  ReporterJSON,
  ReporterSpinnerGroup,
  ReporterSpinner,
  Stdout,
} from './types';
import {markupToAnsi} from '@romejs/string-markup';
import {humanizeNumber, removeSuffix} from '@romejs/string-utils';
import {interpolateMessage} from '@romejs/invariant';
import {stripAnsi, splitAnsiLines} from '@romejs/string-ansi';
import Progress from './Progress';
import {clearLine, getColumns} from './util';
import Spinner from './Spinner';
import {formatAnsi, rightPad, escapes, hasAnsiColor} from '@romejs/string-ansi';
import spinnerGroup from './spinnerGroup';
import format from '@romejs/pretty-format';
import tty = require('tty');
import stream = require('stream');

export type ReporterOptions = {
  programName?: string;
  programVersion?: string;
  markupOptions?: undefined | MarkupFormatOptions;
  disabled?: boolean;
  isTTY?: boolean;
  verbose?: boolean;
  stdout?: Stdout;
  stderr?: Stdout;
  json?: boolean;
  silent?: boolean;
  startTime?: number;
};

const NOOP_SPINNER: ReporterSpinner = {
  resume() {},
  pause() {},
  render() {},
  setText() {},
  end() {},
};

const INDENT = '  ';

function indentMessage(msg: string, level: number): string {
  if (level === 0) {
    return msg;
  } else {
    const indent = INDENT.repeat(level);
    return indent + msg.replace(/\n/g, '\n' + indent);
  }
}

function createNoopStream(): stream.Writable {
  return new stream.Writable({
    write: (chunk, encoding, callback) => {
      callback();
    },
  });
}

export default class Reporter {
  constructor(opts: ReporterOptions = {}) {
    this.programName =
      opts.programName === undefined ? 'rome' : opts.programName;
    this.programVersion = opts.programVersion;

    // streams
    this.stdout = opts.stdout === undefined ? createNoopStream() : opts.stdout;
    this.stderr = opts.stderr === undefined ? createNoopStream() : opts.stderr;

    // booleans
    this.noProgress = process.env.CI === '1';
    this.isJSON = opts.json === true;
    this.isVerbose = Boolean(opts.verbose);
    this.isTTY =
      opts.isTTY === true ||
      (this.stdout instanceof tty.WriteStream && this.stdout.isTTY);

    // if we're silent then we don't output anything to stdout
    // we do however, output things like progress bars etc as they're over stderr
    if (opts.silent === true) {
      this.stdout = createNoopStream();
    }

    // internal
    this.startTime = opts.startTime === undefined ? Date.now() : opts.startTime;
    this.hasClearScreen = true;
    this.activeElements = new Set();
    this.indentLevel = 0;
    this.enabled = opts.disabled === true ? 0 : 1;
    this.markupOptions =
      opts.markupOptions === undefined ? {} : opts.markupOptions;
  }

  static fromProcess(opts: ReporterOptions = {}): Reporter {
    return new Reporter({
      stdout: process.stdout,
      stderr: process.stderr,
      ...opts,
      markupOptions: {
        cwd: process.cwd(),
        ...opts.markupOptions,
      },
    });
  }

  programName: string;
  programVersion: string | undefined;
  markupOptions: MarkupFormatOptions;

  // streams
  stdout: Stdout;
  stderr: Stdout;

  // booleans to control specific output
  isJSON: boolean;
  isTTY: boolean;
  noProgress: boolean;
  isVerbose: boolean;

  //
  enabled: number;

  // used to track run time
  startTime: number;

  // track whether we've output anything, we need this to avoid outputting multiple spacers etc
  hasClearScreen: boolean;

  // here we store active progress indicators so we can easily bail out and destroy them
  activeElements: Set<{
    end: () => void;
  }>;

  // the level of indentation we're currently at
  indentLevel: number;

  getColumns(): number {
    return getColumns(this.stdout);
  }

  //# Control

  isEnabled(): boolean {
    return this.enabled > 0;
  }

  enable(): () => void {
    let alreadyDisabled = false;

    this.enabled++;

    return () => {
      if (alreadyDisabled) {
        throw new Error('Already disabled Reporter');
      }

      this.enabled--;
      alreadyDisabled = true;
    };
  }

  //# JSON OUTPUT METHODS

  json(msg: ReporterJSON): boolean {
    if (this.isJSON) {
      const data = JSON.stringify(msg);
      this.stdout.write(`${String(data.length)}${data}`);
      return true;
    } else {
      return false;
    }
  }

  //# LIFECYCLE

  teardown() {
    for (const elem of this.activeElements) {
      elem.end();
    }
    this.activeElements.clear();
  }

  fork(opts: Partial<ReporterOptions> = {}) {
    return new Reporter({
      stdout: this.stdout,
      stderr: this.stderr,
      json: this.isJSON,
      isTTY: this.isTTY,
      verbose: this.isVerbose,
      markupOptions: this.markupOptions,
      ...opts,
    });
  }

  //# INDENTATION METHODS

  indent() {
    this.indentLevel++;
  }

  dedent() {
    this.indentLevel--;
  }

  //# INTERNAL

  prependEmoji(msg: string, emoji: string, fallback?: string): string {
    if (this.isTTY) {
      if (fallback === undefined) {
        return msg;
      } else {
        return `${fallback} ${msg}`;
      }
    } else {
      return `${emoji} ${msg}`;
    }
  }

  //# VISUALISATION

  table(head: Array<string>, rawBody: Array<Array<string | number>>) {
    // Format the head, just treat it like another row
    head = head.map(
      (field: string): string => formatAnsi.bold(formatAnsi.underline(field)),
    );

    // Humanize all number fields
    const rows: Array<Array<string>> = [head];
    for (const row of rawBody) {
      rows.push(
        row.map(field => {
          if (typeof field === 'number') {
            return humanizeNumber(field);
          } else {
            return field;
          }
        }),
      );
    }

    // Get column widths
    const cols: Array<number> = [];
    for (let i = 0; i < head.length; i++) {
      const widths = rows.map((row): number => stripAnsi(row[i]).length);
      cols[i] = Math.max(...widths);
    }

    // Format all rows
    const builtRows = rows.map(
      (row): string => {
        for (let i = 0; i < row.length; i++) {
          const field = row[i];
          const padding = cols[i] - stripAnsi(field).length;

          row[i] = field + ' '.repeat(padding);
        }
        return row.join(' ');
      },
    );

    this.log(builtRows.join('\n'));
  }

  verboseInspect(val: unknown) {
    if (this.isVerbose) {
      this.inspect(val);
    }
  }

  inspect(value: unknown) {
    if (typeof value !== 'number' && typeof value !== 'string') {
      value = format(value, {color: true});
    }

    this._log(String(value));
  }

  //# ESCAPE HATCHES

  clearLine(stdout: Stdout) {
    if (this.isTTY) {
      clearLine(stdout);
    }
  }

  write(msg: string) {
    if (this.isJSON || !this.isEnabled()) {
      return undefined;
    }

    this.hasClearScreen = false;
    this.clearLine(this.stdout);
    this.stdout.write(msg);
  }

  //# UTILITIES

  getTotalTime(): number {
    return Date.now() - this.startTime;
  }

  clear() {
    if (this.isJSON) {
      return undefined;
    }

    this.stdout.write(escapes.clearScreen);
    this.hasClearScreen = true;
  }

  //# SECTIONS

  heading(text: string) {
    this.log(formatAnsi.inverse(formatAnsi.bold(` ${text} `)));
    this.spacer();
  }

  header(command: string) {
    let msg = `${this.programName} ${command}`;
    if (this.programVersion !== undefined) {
      msg += ` v${this.programVersion}`;
    }
    this.log(formatAnsi.bold(msg));
  }

  footer() {
    const totalTime = (this.getTotalTime() / 1000).toFixed(2);
    const msg = `Done in ${totalTime}s.`;
    this.log(this.prependEmoji(msg, ''));
  }

  hr(text?: string) {
    this.spacer();

    if (this.hasClearScreen) {
      return undefined;
    }

    const len = this.getColumns();
    const prefix = text === undefined ? '' : `${text} `;
    this.log(prefix + ''.repeat(len - prefix.length));
    this.spacer();
  }

  step(current: number, total: number, msg: string) {
    if (msg.endsWith('?')) {
      msg = `${removeSuffix(msg, '?')}...?`;
    } else {
      msg += '...';
    }

    this.log(`${formatAnsi.dim(`[${current}/${total}]`)} ${msg}`);
  }

  spacer() {
    this.log('');
  }

  //# LOG

  markupify(str: string): string {
    return markupToAnsi(str, this.markupOptions);
  }

  log(msg: string) {
    const formatted = this.markupify(msg);
    this._log(formatted);
  }

  _log(msg: string) {
    this.write(indentMessage(msg, this.indentLevel) + '\n');
  }

  formatLogCategory(opts: {
    prefix: string;
    message: string;
    args: Array<unknown>;
  }): {inner: string; outer: string} {
    if (!this.isEnabled()) {
      return {outer: '', inner: ''};
    }

    const {message, args, prefix} = opts;

    let formatted: string = interpolateMessage(message, args);

    // Format with string-markup
    formatted = this.markupify(formatted);

    // Word wrap - we don't use getColumns() here as we don't want to line wrap for a non-tty
    if (this.stdout instanceof tty.WriteStream) {
      const width = this.stdout.columns;
      const allowedWidth =
        width - prefix.length - INDENT.length * this.indentLevel;
      if (stripAnsi(formatted).length > allowedWidth) {
        const lines = splitAnsiLines(formatted, allowedWidth);
        formatted = String(lines.shift());

        for (const line of lines) {
          formatted += '\n' + ' '.repeat(prefix.length) + line;
        }
      }
    }

    if (hasAnsiColor(message)) {
      return {inner: prefix, outer: formatted};
    } else {
      return {inner: prefix + formatted, outer: ''};
    }
  }

  success(msg: string, ...args: Array<unknown>) {
    const {outer, inner} = this.formatLogCategory({
      prefix: ' ',
      message: msg,
      args,
    });
    this._log(formatAnsi.green(inner) + outer);
  }

  error(msg: string, ...args: Array<unknown>) {
    this.clearLine(this.stderr);

    const {outer, inner} = this.formatLogCategory({
      message: msg,
      args,
      prefix: ' ',
    });
    const formatted = formatAnsi.red(inner) + outer;
    const indented = indentMessage(formatted, this.indentLevel);
    this.stderr.write(indented + '\n');
  }

  errorObj(err: Error) {
    this.error(err.stack || err.message || err.name || 'Unknown Error');
  }

  question(msg: string, ...args: Array<unknown>) {
    const {outer, inner} = this.formatLogCategory({
      prefix: ' ',
      message: msg,
      args,
    });
    this._log(formatAnsi.magenta(inner) + outer);
  }

  info(msg: string, ...args: Array<unknown>) {
    const {outer, inner} = this.formatLogCategory({
      prefix: ' ',
      message: msg,
      args,
    });
    this._log(formatAnsi.blue(inner) + outer);
  }

  warn(msg: string, ...args: Array<unknown>) {
    this.clearLine(this.stderr);
    const {outer, inner} = this.formatLogCategory({
      prefix: ' ',
      message: msg,
      args,
    });
    this.stderr.write(formatAnsi.yellow(inner) + outer + '\n');
  }

  verbose(msg: string, ...args: Array<unknown>) {
    if (this.isVerbose) {
      const {outer, inner} = this.formatLogCategory({
        prefix: ' ',
        message: msg,
        args,
      });
      this._log(formatAnsi.brightBlack(inner) + outer);
    }
  }

  command(command: string) {
    this._log(formatAnsi.dim(`$ ${command}`));
  }

  //# LISTS

  list(items: Iterable<string>) {
    for (const item of items) {
      this.log(`  ${formatAnsi.dim('-')} ${item}`);
    }
  }

  orderedList(items: Array<string> | Array<string>, reverse: boolean = false) {
    let numLen = String(items.length).length + 1;

    // Build an array of ordered indexes to list
    let nums: Array<number> = [];
    for (let i = 0; i < items.length; i++) {
      nums.push(i);
    }
    if (reverse) {
      nums = nums.reverse();
    }

    // Build items
    for (const i of nums) {
      const num: string = rightPad(String(i + 1) + '.', numLen);
      const val: string = items[i];
      this.log(`  ${formatAnsi.dim(num)} ${val}`);
    }
  }

  //# PROGRESS

  spinnerGroup(total: number, workers: number): ReporterSpinnerGroup {
    if (!this.isTTY || this.noProgress) {
      return {
        spinners: Array(workers).fill({
          clear() {},
          resume() {},
          tick() {},
          pause() {},
          setCurrent() {},
          setTotal() {},
          setPrefix() {},
          setText() {},
          end() {},
        }),
        setTotal() {},
        setCurrent() {},
        setText() {},
        tick() {},
        end() {},
      };
    }

    return spinnerGroup(this, total, workers);
  }

  spinner(): ReporterSpinner {
    if (!this.isTTY || this.noProgress) {
      return NOOP_SPINNER;
    }

    const spinner = new Spinner(this);
    spinner.start();

    const activity = {
      render() {
        spinner.render();
      },

      resume() {
        spinner.resume();
      },

      pause() {
        spinner.pause();
      },

      setText(name: string) {
        spinner.setText(name);
      },

      end: () => {
        this.activeElements.delete(activity);
        spinner.stop();
      },
    };
    this.activeElements.add(activity);
    return activity;
  }

  progress(): Progress {
    const bar = new Progress(this, () => {
      this.activeElements.delete(bar);
    });
    this.activeElements.add(bar);
    return bar;
  }
}
