import {JSNodeBase, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowTupleTypeAnnotationNode = JSNodeBase & {
  type: 'FlowTupleTypeAnnotation',
  types: Array<FlowNode>,
};

export const flowTupleTypeAnnotation = createBuilder<FlowTupleTypeAnnotationNode>(
  'FlowTupleTypeAnnotation',
  {visitorKeys: ['types']},
);
