import {APIRequestFlags} from '@romejs/core';
import {
  API,
  PLATFORMS,
  BUNDLER_MODES,
  DEFAULT_API_REQUEST_FLAGS,
  masterCommands,
  localCommands,
} from '@romejs/core';
import setProcessTitle from './utils/setProcessTitle';
import {parseArgv} from '@romejs/cli-flags';

export default async function cli() {
  setProcessTitle('cli');

  const p = parseArgv({
    usage: 'rome [command] [flags]',
  });

  let command = '';
  let flags: APIRequestFlags = {
    cwd: p.string('cwd', {
      description: 'Change the current working directory',
      default: process.cwd(),
      inputName: 'path',
    }),
    logs: p.boolean('logs', {
      description: 'Output master logs while your request is being processed',
      default: DEFAULT_API_REQUEST_FLAGS.logs,
    }),
    logWorkers: p.boolean('log-workers', {
      description: 'Output worker logs while your request is being processed',
      default: DEFAULT_API_REQUEST_FLAGS.logWorkers,
    }),
    logPath: p.string('log-path', {
      description: 'Write logs to file',
      default: DEFAULT_API_REQUEST_FLAGS.logPath,
      inputName: 'path',
    }),
    verbose: p.boolean('verbose', {
      description: 'Whether to enable verbose logs',
      default: DEFAULT_API_REQUEST_FLAGS.verbose,
    }),
    silent: p.boolean('silent', {
      description: "Don't output anything to stdout",
      default: DEFAULT_API_REQUEST_FLAGS.silent,
    }),
    color: p.boolean('color', {
      description:
        'Whether we should strip special ANSI control codes like color (disabled when under a non-TTY)',
      default: DEFAULT_API_REQUEST_FLAGS.color,
    }),
    json: p.boolean('json', {
      description: 'Output JSON instead of CLI output',
      default: DEFAULT_API_REQUEST_FLAGS.json,
    }),
    profile: p.boolean('profile', {
      description:
        'Collect a CPU profile of all processes involved while performing a request',
      default: DEFAULT_API_REQUEST_FLAGS.profile,
    }),
    profilePath: p.string('profile-path', {
      description: 'Path to write a profile to',
      default: DEFAULT_API_REQUEST_FLAGS.profilePath,
      inputName: 'path',
    }),
    profileTimeout: p.number('profile-timeout', {
      description:
        'Specify a timeout in milliseconds for stopping a CPU profile',
      default: DEFAULT_API_REQUEST_FLAGS.profileTimeout,
      inputName: 'ms',
    }),
    rage: p.boolean('rage', {
      description: 'Produce logs and a profile',
      default: DEFAULT_API_REQUEST_FLAGS.rage,
    }),
    ragePath: p.string('rage-path', {
      description: 'Path to write the rage archive to',
      default: DEFAULT_API_REQUEST_FLAGS.ragePath,
      inputName: 'path',
    }),
    watch: p.boolean('watch', {
      description: 'whether to stay alive and watch for file changes',
      default: DEFAULT_API_REQUEST_FLAGS.watch,
    }),
    fieri: p.boolean('fieri', {
      default: DEFAULT_API_REQUEST_FLAGS.fieri,
      hidden: true,
    }),
    focus: p.string('focus', {
      description:
        'if a filename, line number, column tuple is passed here then only diagnostics related to it wil be shown',
      default: DEFAULT_API_REQUEST_FLAGS.focus,
    }),
    grep: p.string('grep', {
      description: 'only show diagnostics that contain this string',
      default: DEFAULT_API_REQUEST_FLAGS.grep,
    }),
    maxDiagnostics: p.number('max-diagnostics', {
      description: '',
      default: DEFAULT_API_REQUEST_FLAGS.maxDiagnostics,
    }),
    verboseDiagnostics: p.boolean('verbose-diagnostics', {
      description: '',
      default: DEFAULT_API_REQUEST_FLAGS.verboseDiagnostics,
    }),
    showAllDiagnostics: p.boolean('show-all-diagnostics', {
      description: `show all diagnostics, default is to truncate`,
      default: DEFAULT_API_REQUEST_FLAGS.showAllDiagnostics,
    }),
    inverseGrep: p.boolean('inverse-grep', {
      description: 'flip grep matches',
      default: DEFAULT_API_REQUEST_FLAGS.inverseGrep,
    }),

    bundlerMode: p.optionalStringSet('bundler-mode', BUNDLER_MODES, {
      description: '',
      default: DEFAULT_API_REQUEST_FLAGS.bundlerMode,
    }),
    resolverPlatform: p.optionalStringSet('resolver-platform', PLATFORMS, {
      description: '',
    }),
    resolverScale: p.optionalNumber('resolver-scale', {
      description: '',
    }),
    resolverMocks: p.boolean('resolver-mocks', {
      description: '',
      default: DEFAULT_API_REQUEST_FLAGS.resolverMocks,
    }),
  };
  let commandFlags: unknown;
  let args: Array<string> = [];

  const commandNames = new Set([
    ...localCommands.keys(),
    ...masterCommands.keys(),
  ]);

  for (const cmd of commandNames) {
    const local = localCommands.get(cmd);
    const master = masterCommands.get(cmd);
    const opts = local || master;
    if (opts === undefined) {
      throw new Error('Impossible');
    }

    p.command(
      cmd,
      {
        category: opts.category,
        description: opts.description,
      },
      () => {
        if (master !== undefined && master.getRequestFlags !== undefined) {
          flags = {
            ...flags,
            ...master.getRequestFlags(),
          };
        }

        if (opts.parseCLIFlags !== undefined) {
          commandFlags = opts.parseCLIFlags(p);
        }

        args = p.getArgs();
        command = cmd;
        p.assert();
      },
    );
  }

  p.assert();
  p.commandRequired();

  const api = new API({
    flags,
    stdout: process.stdout,
    stderr: process.stderr,
  });

  const res = await api.query({
    command,
    commandFlags,
    args,
  });

  const {error} = res;
  if (error === undefined) {
    process.exit(0);
  } else {
    console.error('Unhandled CLI query error');
    if (!error.handled) {
      console.error(error.stack);
    }
    process.exit(1);
  }
}
