import Scope from '../Scope';
import * as t from '@romejs/js-ast';

export default function ForOfStatement(
  node: t.ForOfStatementNode,
  parent: t.Node,
  scope: Scope,
) {
  const newScope = scope.fork('loop', node);
  newScope.evaluate(node.left, node);
  return newScope;
}
