import {JSNodeBase, TSTypeNode} from '../index';
import {createBuilder} from '../utils';

export type TSTypeOperator = 'keyof' | 'unique' | 'readonly';

export type TSTypeOperatorNode = JSNodeBase & {
  type: 'TSTypeOperator';
  operator: 'keyof' | 'unique' | 'readonly';
  typeAnnotation: TSTypeNode;
};

export const tsTypeOperator = createBuilder<TSTypeOperatorNode>(
  'TSTypeOperator',
  {visitorKeys: ['typeAnnotation']},
);
