import {
  JSNodeBase,
  BlockStatementNode,
  ExpressionNode,
  FunctionBase,
} from '../index';
import {createBuilder, FUNCTION_VISITOR_KEYS} from '../utils';

export type ArrowFunctionExpressionNode = JSNodeBase &
  FunctionBase & {
    type: 'ArrowFunctionExpression';
    body: BlockStatementNode | ExpressionNode;
    generator?: void;
  };

export const arrowFunctionExpression = createBuilder<
  ArrowFunctionExpressionNode
>('ArrowFunctionExpression', {
  visitorKeys: FUNCTION_VISITOR_KEYS,
});
