import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowTypeCastExpression(
  generator: Generator,
  node: t.FlowTypeCastExpressionNode,
) {
  generator.token('(');
  generator.print(node.expression, node);
  generator.print(node.typeAnnotation, node);
  generator.token(')');
}
