import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXSpreadChild(
  generator: Generator,
  node: t.JSXSpreadChildNode,
) {
  generator.token('{');
  generator.token('...');
  generator.print(node.expression, node);
  generator.token('}');
}
