import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type JSXSpreadChildNode = JSNodeBase & {
  type: 'JSXSpreadChild',
  expression: ExpressionNode,
};

export const jsxSpreadChild = createBuilder<JSXSpreadChildNode>(
  'JSXSpreadChild',
  {visitorKeys: ['expression']},
);
