import {JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type BooleanLiteralNode = JSNodeBase & {
  type: 'BooleanLiteral',
  value: boolean,
};

export const booleanLiteral = createBuilder<BooleanLiteralNode>(
  'BooleanLiteral',
);
