import {JSNodeBase, IdentifierNode, TSEnumMemberNode} from '../index';
import {createBuilder} from '../utils';

export type TSEnumDeclarationNode = JSNodeBase & {
  type: 'TSEnumDeclaration';
  id: IdentifierNode;
  const?: undefined | boolean;
  members: Array<TSEnumMemberNode>;
};

export const tsEnumDeclaration = createBuilder<TSEnumDeclarationNode>(
  'TSEnumDeclaration',
  {visitorKeys: ['id', 'members']},
);
