import {JSNodeBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type SpreadElementNode = JSNodeBase & {
  type: 'SpreadElement',
  argument: ExpressionNode,
};

export const spreadElement = createBuilder<SpreadElementNode>('SpreadElement', {
  visitorKeys: ['argument'],
});
