import {
  JSNodeBase,
  StringLiteralNode,
  NumericLiteralNode,
  BooleanLiteralNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSLiteralTypeNode = JSNodeBase & {
  type: 'TSLiteralType';
  literal: StringLiteralNode | NumericLiteralNode | BooleanLiteralNode;
};

export const tsLiteralType = createBuilder<TSLiteralTypeNode>('TSLiteralType', {
  visitorKeys: ['literal'],
});
