import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {isStatement} from '@romejs/js-ast-utils';

export function FlowDeclareExportDeclaration(
  generator: Generator,
  node: t.FlowDeclareExportDeclarationNode,
) {
  generator.word('declare');
  generator.space();
  generator.word('export');
  generator.space();

  if (node.default === true) {
    generator.word('default');
    generator.space();
  }

  if (node.declaration) {
    const declar = node.declaration;
    generator.print(declar, node);
    if (!isStatement(declar)) {
      generator.semicolon();
    }
  } else {
    generator.token('{');
    if (node.specifiers !== undefined && node.specifiers.length > 0) {
      generator.space();
      generator.printCommaList(node.specifiers, node);
      generator.space();
    }
    generator.token('}');

    if (node.source) {
      generator.space();
      generator.word('from');
      generator.space();
      generator.print(node.source, node);
    }

    generator.semicolon();
  }
}
