import {DiagnosticsPrinterFlags} from '@romejs/cli-diagnostics';
import {Project} from '../common/types/project';
import {ResolverOptions} from './Resolver';
import {BundlerConfig} from '../common/types/bundler';
import {APIRequestFlags} from '../common/types/api';
import MasterBridge from '../common/bridges/MasterBridge';
import Master from './Master';
import {Reporter} from '@romejs/cli-reporter';
import {PathPatterns} from '@romejs/match-path';
import {Event} from '@romejs/events';
import {DiagnosticsPrinter} from '@romejs/cli-diagnostics';

import path = require('path');

type MasterRequestOptions = {
  args: Array<string>;
  flags: APIRequestFlags;
  master: Master;
  client: MasterBridge;
  reporter: Reporter;
  command: string;
};

export default class MasterRequest {
  constructor(opts: MasterRequestOptions) {
    this.args = opts.args;
    this.flags = opts.flags;
    this.master = opts.master;
    this.client = opts.client;
    this.reporter = opts.reporter;
    this.command = opts.command;
    this.teardownEvent = new Event({name: 'MasterRequest.teardown'});
  }

  command: string;
  args: Array<string>;
  flags: APIRequestFlags;
  master: Master;
  client: MasterBridge;
  reporter: Reporter;
  teardownEvent: Event<void, void>;

  teardown() {
    this.reporter.teardown();
    this.teardownEvent.send(undefined);
  }

  createDiagnosticsPrinter(origin: string): DiagnosticsPrinter {
    return new DiagnosticsPrinter({
      origins: [`master: ${this.command} command was dispatched`, origin],
      reporter: this.reporter,
      cwd: this.flags.cwd,
      flags: this.getDiagnosticsPrinterFlags(),
    });
  }

  getDiagnosticsPrinterFlags(): DiagnosticsPrinterFlags {
    return {
      grep: this.flags.grep,
      inverseGrep: this.flags.inverseGrep,
      focus: this.flags.focus,
      showAllDiagnostics: this.flags.showAllDiagnostics,
      verboseDiagnostics: this.flags.verboseDiagnostics,
      maxDiagnostics: this.flags.maxDiagnostics,
      fieri: this.flags.fieri,
    };
  }

  getResolverOptionsFromFlags(): ResolverOptions {
    return {
      platform: this.flags.resolverPlatform,
      scale: this.flags.resolverScale,
      mocks: this.flags.resolverMocks,
    };
  }

  getBundlerConfigFromFlags(): BundlerConfig {
    return {
      inlineSourceMap: false,
      mode: this.flags.bundlerMode,
      cwd: this.flags.cwd,
    };
  }

  async getFilesFromArgs(
    getIgnoreForProject: (project: Project) => PathPatterns,
  ): Promise<Array<string>> {
    const {master} = this;

    // Build up args, defaulting to the current project dir if none passed
    const args = [...this.args];
    if (args.length === 0) {
      const project = await master.projectManager.assertProject(this.flags.cwd);
      args.push(project.dir);
    }

    // Build up files
    let files: Array<string> = [];
    for (let arg of args) {
      // Normalize to an absolute path
      if (!path.isAbsolute(arg)) {
        arg = path.resolve(this.flags.cwd, arg);
      }

      const project = await master.projectManager.assertProject(arg);
      const projectIgnore: PathPatterns = getIgnoreForProject(project);

      const matches = master.memoryFs.glob(arg, {ignore: projectIgnore});
      files = files.concat(matches);
    }
    return files;
  }
}
