import {types as tt} from '../tokenizer/types';
import {Position} from '@romejs/parser-core';
import Parser from '../Parser';
import * as n from '@romejs/js-ast';
import {
  parseExpressionAtom,
  parseExpression,
  parseMaybeAssign,
  parseTSTypeArguments,
} from './index';

function isFragment(object: undefined | n.Node) {
  return object
    ? object.type === 'JSXOpeningFragment' ||
        object.type === 'JSXClosingFragment'
    : false;
}

// Transforms JSX element name to string.
function getQualifiedJSXName(node: undefined | n.Node): string {
  if (node === undefined) {
    return '';
  }

  if (node.type === 'JSXOpeningElement' || node.type === 'JSXClosingElement') {
    return getQualifiedJSXName(node.name);
  }

  if (node.type === 'JSXIdentifier') {
    node = n.jsxIdentifier.assert(node);
    return node.name;
  }

  if (node.type === 'JSXNamespacedName') {
    node = n.jsxNamespacedName.assert(node);
    return node.namespace.name + ':' + node.name.name;
  }

  if (node.type === 'JSXMemberExpression') {
    node = n.jsxMemberExpression.assert(node);
    return (
      getQualifiedJSXName(node.object) +
      '.' +
      getQualifiedJSXName(node.property)
    );
  }

  throw new Error('Node had unexpected type: ' + node.type);
}

// Parse next token as JSX identifier
function parseJSXIdentifier(parser: Parser): n.JSXIdentifierNode {
  const start = parser.getPosition();
  let name;
  if (parser.match(tt.jsxName)) {
    name = String(parser.state.tokenValue);
  } else if (parser.state.tokenType.keyword !== undefined) {
    name = parser.state.tokenType.keyword;
  } else {
    parser.addDiagnostic({
      message: 'Unknown JSX identifier token',
    });
    name = '';
  }
  parser.next();
  return {
    loc: parser.finishLoc(start),
    type: 'JSXIdentifier',
    name,
  };
}

// Parse namespaced identifier.
function parseJSXNamespacedName(
  parser: Parser,
): n.JSXIdentifierNode | n.JSXNamespacedNameNode {
  const start = parser.getPosition();

  const namespace = parseJSXIdentifier(parser);
  if (!parser.eat(tt.colon)) {
    return namespace;
  }

  const name = parseJSXIdentifier(parser);
  return {
    loc: parser.finishLoc(start),
    type: 'JSXNamespacedName',
    name,
    namespace,
  };
}

// Parses element name in any form - namespaced, member
// or single identifier.
function parseJSXElementName(
  parser: Parser,
): n.JSXIdentifierNode | n.JSXNamespacedNameNode | n.JSXMemberExpressionNode {
  const start = parser.getPosition();

  let node:
    | n.JSXIdentifierNode
    | n.JSXNamespacedNameNode
    | n.JSXMemberExpressionNode = parseJSXNamespacedName(parser);

  while (parser.eat(tt.dot)) {
    const property = parseJSXIdentifier(parser);
    node = {
      loc: parser.finishLoc(start),
      type: 'JSXMemberExpression',
      object: node,
      property,
    };
  }

  return node;
}

// Parses any type of JSX attribute value.
function parseJSXAttributeValue(
  parser: Parser,
): n.ExpressionNode | n.JSXExpressionContainerNode {
  let node;
  switch (parser.state.tokenType) {
    case tt.braceL:
      node = parseJSXExpressionContainer(parser);
      if (node.expression.type === 'JSXEmptyExpression') {
        parser.addDiagnostic({
          loc: node.loc,
          message:
            'JSX attributes must only be assigned a non-empty expression',
        });
      }
      return node;

    case tt.jsxTagStart:
    case tt.string:
      return parseExpressionAtom(parser, 'jsx attribute value');

    default:
      parser.addDiagnostic({
        message:
          'JSX value should be either an expression or a quoted JSX text',
      });
  }
}

// JSXEmptyExpression is unique type since it doesn't actually parse anything,
// and so it should start at the end of last read token (left brace) and finish
// at the beginning of the next one (right brace).
function parseJSXEmptyExpression(parser: Parser): n.JSXEmptyExpressionNode {
  return {
    loc: parser.finishLoc(parser.state.lastEndPos),
    type: 'JSXEmptyExpression',
  };
}

// Parse JSX spread child
function parseJSXSpreadChild(parser: Parser): n.JSXSpreadChildNode {
  const start = parser.getPosition();
  const openContext = parser.expectOpening(
    tt.braceL,
    tt.braceR,
    'jsx spread child',
  );
  parser.expect(tt.ellipsis);
  const expression = parseExpression(parser, 'jsx spread child expression');
  parser.expectClosing(openContext);

  return {
    loc: parser.finishLoc(start),
    type: 'JSXSpreadChild',
    expression,
  };
}

// Parses JSX expression enclosed into curly brackets.
function parseJSXExpressionContainer(
  parser: Parser,
): n.JSXExpressionContainerNode {
  const start = parser.getPosition();
  const openContext = parser.expectOpening(
    tt.braceL,
    tt.braceR,
    'jsx expression container',
  );
  let expression;
  if (parser.match(tt.braceR)) {
    expression = parseJSXEmptyExpression(parser);
  } else {
    expression = parseExpression(parser, 'jsx inner expression container');
  }
  parser.expectClosing(openContext);
  return {
    loc: parser.finishLoc(start),
    type: 'JSXExpressionContainer',
    expression,
  };
}

// Parses following JSX attribute name-value pair.
function parseJSXAttribute(
  parser: Parser,
): n.JSXSpreadAttributeNode | n.JSXAttributeNode {
  const start = parser.getPosition();

  if (parser.match(tt.braceL)) {
    const openContext = parser.expectOpening(
      tt.braceL,
      tt.braceR,
      'jsx attribute spread',
    );
    parser.expect(tt.ellipsis);
    const argument = parseMaybeAssign(parser, 'jsx attribute spread');
    parser.expectClosing(openContext);
    return {
      loc: parser.finishLoc(start),
      type: 'JSXSpreadAttribute',
      argument,
    };
  }

  const name = parseJSXNamespacedName(parser);
  const value = parser.eat(tt.eq) ? parseJSXAttributeValue(parser) : undefined;
  return {
    loc: parser.finishLoc(start),
    type: 'JSXAttribute',
    name,
    value,
  };
}

// Parses JSX opening tag starting after "<".
function parseJSXOpeningElementAt(
  parser: Parser,
  start: Position,
): n.JSXOpeningElementNode | n.JSXOpeningFragmentNode {
  if (parser.match(tt.jsxTagEnd)) {
    parser.expect(tt.jsxTagEnd);
    return {
      loc: parser.finishLoc(start),
      type: 'JSXOpeningFragment',
    };
  }

  const attributes = [];
  const name = parseJSXElementName(parser);

  let typeArguments;
  if (parser.isRelational('<')) {
    if (!parser.isSyntaxEnabled('ts')) {
      parser.addDiagnostic({
        message: 'JSX element type arguments are only allowed in TS',
      });
    }

    typeArguments = parseTSTypeArguments(parser);
  }

  // We need to check for isRelational('>') here as the above type arguments parsing can put the tokenizer
  // into an unusual state for: <foo<bar>></foo>
  while (
    !parser.match(tt.slash) &&
    !parser.match(tt.jsxTagEnd) &&
    !parser.atEOF()
  ) {
    attributes.push(parseJSXAttribute(parser));
  }
  const selfClosing = parser.eat(tt.slash);
  if (!parser.eat(tt.jsxTagEnd)) {
    parser.addDiagnostic({
      message: 'Unclosed JSX element open',
    });
  }
  return {
    loc: parser.finishLoc(start),
    type: 'JSXOpeningElement',
    typeArguments,
    name,
    attributes,
    selfClosing,
  };
}

// Parses JSX closing tag starting after "</".
function parseJSXClosingElementAt(
  parser: Parser,
  start: Position,
): n.JSXClosingElementNode | n.JSXClosingFragmentNode {
  if (parser.match(tt.jsxTagEnd)) {
    if (!parser.eat(tt.jsxTagEnd)) {
      parser.addDiagnostic({
        message: 'Unclosed JSX fragment close',
      });
    }

    return {
      loc: parser.finishLoc(start),
      type: 'JSXClosingFragment',
    };
  }

  const name = parseJSXElementName(parser);

  if (!parser.eat(tt.jsxTagEnd)) {
    parser.addDiagnostic({
      message: 'Unclosed JSX element close',
    });
  }

  return {
    loc: parser.finishLoc(start),
    type: 'JSXClosingElement',
    name,
  };
}

// Parses entire JSX element, including it"s opening tag
// (starting after "<"), attributes, contents and closing tag.
function parseJSXElementAt(
  parser: Parser,
  start: Position,
): n.JSXElementNode | n.JSXFragmentNode {
  const children = [];
  const openingElement = parseJSXOpeningElementAt(parser, start);

  let closingElement:
    | undefined
    | n.JSXClosingElementNode
    | n.JSXClosingFragmentNode = undefined;

  if (
    openingElement.type === 'JSXOpeningElement' &&
    openingElement.selfClosing !== true
  ) {
    contents: while (true) {
      switch (parser.state.tokenType) {
        case tt.jsxTagStart: {
          const start = parser.getPosition();
          parser.next();
          if (parser.eat(tt.slash)) {
            closingElement = parseJSXClosingElementAt(parser, start);
            break contents;
          }
          children.push(parseJSXElementAt(parser, start));
          break;
        }

        case tt.jsxText:
          children.push(parseExpressionAtom(parser, 'jsx text'));
          break;

        case tt.braceL:
          if (parser.lookaheadState().tokenType === tt.ellipsis) {
            children.push(parseJSXSpreadChild(parser));
          } else {
            children.push(parseJSXExpressionContainer(parser));
          }
          break;

        default:
          parser.addDiagnostic({
            message: 'Unknown JSX children start',
          });
          parser.next();
          break contents;
      }
    }

    // Fragment open, element close
    if (
      isFragment(openingElement) &&
      !isFragment(closingElement) &&
      closingElement !== undefined
    ) {
      parser.addDiagnostic({
        loc: parser.getLoc(closingElement),
        message: 'Expected corresponding JSX closing tag for <>',
      });
      closingElement = {
        type: 'JSXClosingFragment',
        loc:
          closingElement === undefined
            ? openingElement.loc
            : closingElement.loc,
      };
    }

    // Element open, fragment close
    if (
      !isFragment(openingElement) &&
      isFragment(closingElement) &&
      closingElement !== undefined
    ) {
      parser.addDiagnostic({
        loc: parser.getLoc(closingElement),
        message:
          'Expected corresponding JSX closing tag for <' +
          getQualifiedJSXName(n.jsxOpeningElement.assert(openingElement).name) +
          '>',
      });

      closingElement = {
        type: 'JSXClosingElement',
        loc: n.jsxClosingFragment.assert(closingElement).loc,
        name: n.jsxOpeningElement.assert(openingElement).name,
      } as n.JSXClosingElementNode;
    }

    // Element open, element close
    if (
      !isFragment(openingElement) &&
      !isFragment(closingElement) &&
      closingElement !== undefined
    ) {
      if (
        getQualifiedJSXName(closingElement) !==
        getQualifiedJSXName(openingElement)
      ) {
        parser.addDiagnostic({
          loc: parser.getLoc(closingElement),
          message:
            'Expected corresponding JSX closing tag for <' +
            getQualifiedJSXName(
              n.jsxOpeningElement.assert(openingElement).name,
            ) +
            '>',
        });

        closingElement = {
          type: 'JSXClosingElement',
          loc:
            closingElement === undefined
              ? openingElement.loc
              : closingElement.loc,
          name: n.jsxOpeningElement.assert(openingElement).name,
        } as n.JSXClosingElementNode;
      }
    }
  }

  if (isFragment(openingElement)) {
    checkAccidentalFragment(parser);
    return {
      loc: parser.finishLoc(start),
      type: 'JSXFragment',
      openingFragment: n.jsxOpeningFragment.assert(openingElement),
      closingFragment:
        closingElement === undefined
          ? undefined
          : n.jsxClosingFragment.assert(closingElement),
      children,
    } as n.JSXFragmentNode;
  } else {
    checkAccidentalFragment(parser);
    return {
      loc: parser.finishLoc(start),
      type: 'JSXElement',
      openingElement: n.jsxOpeningElement.assert(openingElement),
      closingElement:
        closingElement === undefined
          ? undefined
          : n.jsxClosingElement.assert(closingElement),
      children,
    } as n.JSXElementNode;
  }
}

function checkAccidentalFragment(parser: Parser) {
  if (parser.match(tt.relational) && parser.state.tokenValue === '<') {
    parser.addDiagnostic({
      message:
        'Adjacent JSX elements must be wrapped in an enclosing tag. ' +
        'Did you want a JSX fragment <>...</>?',
    });
  }
}

export function parseJSXText(parser: Parser): n.JSXTextNode {
  // No need to assert syntax here because we wont get that far as parseJSXElement would have already been called

  const start = parser.getPosition();
  const value = String(parser.state.tokenValue);
  parser.next();
  return {
    loc: parser.finishLoc(start),
    type: 'JSXText',
    value,
  };
}

// Parses entire JSX element from 'current position.
export function parseJSXElement(
  parser: Parser,
): n.JSXElementNode | n.JSXFragmentNode {
  // Only necessary here as this is the only JSX entry point
  parser.expectSyntaxEnabled('jsx');

  const start = parser.getPosition();
  parser.next();
  return parseJSXElementAt(parser, start);
}
