import {JSNodeBase, TSThisTypeNode, TSTypeAnnotationNode} from '../index';
import {createBuilder} from '../utils';

export type TSTypePredicateNode = JSNodeBase & {
  type: 'TSTypePredicate';
  parameterName: TSThisTypeNode;
  typeAnnotation: TSTypeAnnotationNode;
};

export const tsTypePredicate = createBuilder<TSTypePredicateNode>(
  'TSTypePredicate',
  {visitorKeys: ['parameterName', 'typeAnnotation']},
);
