import Scope from '../Scope';
import {TypeBinding} from '@romejs/js-compiler';
import {getBindingIdentifiers} from '@romejs/js-ast-utils';
import * as t from '@romejs/js-ast';

export default function FlowTypeParameterDeclaration(
  node: t.FlowTypeParameterDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  for (const id of getBindingIdentifiers(node)) {
    scope.addBinding(
      new TypeBinding({
        node: id,
        name: id.name,
        scope,
      }),
    );
  }
}
