import {JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type NumericLiteralNode = JSNodeBase & {
  type: 'NumericLiteral',
  value: number,
};

export const numericLiteral = createBuilder<NumericLiteralNode>(
  'NumericLiteral',
);
