import Scope from '../Scope';
import {FunctionBinding} from '@romejs/js-compiler';
import * as t from '@romejs/js-ast';
import {addFunctionBindings} from '../utils';

export default function FunctionDeclaration(
  node: t.FunctionDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  if (node.id !== undefined) {
    scope.addBinding(
      new FunctionBinding({
        node: node.id,
        name: node.id.name,
        scope,
      }),
    );
  }
  const newScope = scope.fork('function', node);
  addFunctionBindings(newScope, node);
  return newScope;
}
