import {SourceMap} from '@romejs/codec-source-map';
import {PartialDiagnostics} from '@romejs/diagnostics';
import {TestWorkerContainer, TestWorkerContainers} from './TestRunner';
import {CoverageCollector, sourceMapManager} from '@romejs/v8';
import {BridgeError} from '@romejs/events';
import {MasterRequest} from '@romejs/core';
import TestRunner from './TestRunner';

type TestSource = {
  code: string;
  sourceMap: SourceMap;
};

export type TestSources = Map<string, TestSource>;

export class TestWorkerUnresponsiveError extends BridgeError {
  constructor(message: string, frames: any) {
    super(message);
    this.__ROME_ERROR_FRAMES__ = frames;
  }

  __ROME_ERROR_FRAMES__: any;
}

export type RunTestOpts = {
  coverage: boolean;
  showAllCoverage: boolean;
  updateSnapshots: boolean;
  verboseDiagnostics: boolean;
};

export async function runTests(
  req: MasterRequest,
  opts: RunTestOpts,
  testFiles: TestSources,
  addDiagnostics: PartialDiagnostics,
) {
  const {flags} = req;

  const coverageCollector = new CoverageCollector();

  const runner = new TestRunner({
    addDiagnostics,
    showAllCoverage: opts.showAllCoverage,
    coverageCollector,
    sources: testFiles,
    request: req,
  });

  await runner.init();

  const workerContainers: TestWorkerContainers = runner.getWorkers();

  const testQueue: Array<[string, TestSource]> = Array.from(
    testFiles.entries(),
  );

  async function runWorker({bridge, process, inspector}: TestWorkerContainer) {
    if (inspector !== undefined && opts.coverage === true) {
      await inspector.call('Profiler.enable');
      await inspector.call('Profiler.startPreciseCoverage', {
        // Turning this on disables V8 optimizations https://v8.dev/blog/javascript-code-coverage#precise-coverage-(function-granularity)
        callCount: false,
        // Otherwise coverage will only have function granularity
        detailed: true,
      });
    }

    async function nextTest() {
      if (testQueue.length === 0) {
        return undefined;
      }

      const item = testQueue.pop();
      if (item === undefined) {
        throw new Error('testQueue.length was validated above');
      }
      const [filename, {code, sourceMap}] = item;
      coverageCollector.addSourceMap(filename, code, sourceMap);

      // Source map locations will always be resolved in the worker, but this is in case we need to resolve them in master in the case of an unresponsive worker
      // TODO remove this after test has ran
      sourceMapManager.addSourceMap(filename, sourceMap);

      await bridge.run.call({
        options: opts,
        filename,
        cwd: flags.cwd,
        code,
        sourceMap,
      });
      await nextTest();
    }

    try {
      await nextTest();
    } catch (err) {
      if (err instanceof BridgeError) {
        // When a worker dies, we automatically mark all it's currently running tests as failed
        // However, it will cause all the pending messages to throw an error which will bubble up here
        // We can safely ignore these since we've already handled it
      } else {
        throw err;
      }
    } finally {
      if (inspector !== undefined && opts.coverage) {
        if (inspector !== undefined && inspector.alive) {
          const profile: any = await inspector.call(
            'Profiler.takePreciseCoverage',
          );
          coverageCollector.addCoverage(profile.result);

          // Not really necessary but let's clean up anyway for completeness
          await inspector.call('Profiler.stopPreciseCoverage');
          await inspector.call('Profiler.disable');
        } else {
          // TODO log that we failed to fetch some coverage
        }
      }

      if (inspector !== undefined) {
        inspector.end();
      }

      process.kill();
    }
  }

  await Promise.all(workerContainers.map(container => runWorker(container)));

  runner.printTestResults();
}
