import Scope from '../Scope';
import * as t from '@romejs/js-ast';
import {ImportBinding} from '@romejs/js-compiler';

export default function TSImportEqualsDeclaration(
  node: t.TSImportEqualsDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  const {moduleReference, id} = node;

  if (moduleReference.type === 'TSExternalModuleReference') {
    scope.addBinding(
      new ImportBinding(
        {
          node: id,
          name: id.name,
          scope,
        },
        {
          type: 'namespace',
          source: moduleReference.expression.value,
        },
      ),
    );
  } else {
    // TODO
  }
}
