import {Path} from '@romejs/js-compiler';
import {
  Node,
  ObjectMemberNode,
  RestPropertyNode,
  ObjectPatternNode,
} from '@romejs/js-ast';
import {
  identifier,
  callExpression,
  objectPattern,
  variableDeclaration,
  variableDeclarator,
  objectExpression,
} from '@romejs/js-ast';
import {template} from '@romejs/js-ast-utils';

function last<T>(arr: Array<T>): undefined | T {
  return arr[arr.length - 1];
}

function hasSpreadProperty(props: Array<Node>): boolean {
  for (const prop of props) {
    if (prop.type === 'SpreadProperty') {
      return true;
    }
  }
  return false;
}

function getRestProperty(node: undefined | Node): undefined | RestPropertyNode {
  if (node === undefined) {
    return undefined;
  }

  switch (node.type) {
    case 'VariableDeclaration':
      for (const declarator of node.declarations) {
        const rest = getRestProperty(declarator);
        if (rest !== undefined) {
          return rest;
        }
      }
      return undefined;

    case 'VariableDeclarator':
      return getRestProperty(node.id);

    case 'RestProperty':
      return node;

    case 'ObjectPattern':
      const prop = getRestProperty(
        last((node as ObjectPatternNode).properties),
      );
      if (prop !== undefined) {
        return prop;
      }
  }
}

function transformSpreadProperty(path: Path) {
  const {node} = path;

  let props: Array<ObjectMemberNode> = [];
  const assignArgs = [];

  function pushProps() {
    if (props.length === 0 && assignArgs.length > 0) {
      return undefined;
    }

    assignArgs.push(objectExpression.create({properties: props}));

    props = [];
  }

  for (const prop of node.properties) {
    if (prop.type === 'SpreadProperty') {
      pushProps();
      assignArgs.push(prop.argument);
    } else {
      props.push(prop);
    }
  }

  pushProps();

  return callExpression.create({
    callee: template.expression`Object.assign`,
    arguments: assignArgs,
  });
}

function transformRestProperty(path: Path): Array<Node> {
  const {node} = path;

  const nodes = [];

  for (const declarator of node.declarations) {
    const restElem = getRestProperty(declarator);
    if (restElem !== undefined) {
      const uid = path.scope.generateUid();

      // push on the initial declaration so we can reference it later
      nodes.push(
        variableDeclaration.create({
          kind: node.kind,
          declarations: [
            variableDeclarator.create({
              id: identifier.create({
                name: uid,
              }),
              init: declarator.init,
            }),
          ],
        }),
      );

      // fetch all the previous prop names
      const removeProps = [];
      for (const prop of declarator.id.properties) {
        if (prop.type === 'ObjectProperty') {
          if (prop.computed) {
            // TODO complain about this, we don't support it
          } else {
            removeProps.push(prop.key.name);
          }
        }
      }

      // clone the init to the rest element
      const restName = restElem.argument.name;
      nodes.push(
        variableDeclaration.create({
          kind: node.kind,
          declarations: [
            variableDeclarator.create({
              id: identifier.create({name: restName}),
              init: template.expression`Object.assign({}, ${uid})`,
            }),
          ],
        }),
      );

      // `delete` the properties
      for (const name of removeProps) {
        nodes.push(template.statement`delete ${restName}.${name};`);
      }

      // push on the initial destructuring without the rest element
      nodes.push(
        variableDeclaration.create({
          kind: node.kind,
          declarations: [
            variableDeclarator.create({
              id: objectPattern.create({
                properties: declarator.id.properties.slice(0, -1),
              }),
              init: identifier.create({name: uid}),
            }),
          ],
        }),
      );
    } else {
      nodes.push(
        variableDeclaration.create({
          kind: node.kind,
          declarations: [declarator],
        }),
      );
    }
  }

  return nodes;
}

export default {
  name: 'objectSpread',
  enter(path: Path): Node {
    const {node} = path;

    if (
      node.type === 'VariableDeclaration' &&
      getRestProperty(node) !== undefined
    ) {
      return transformRestProperty(path);
    }

    if (
      node.type === 'ObjectExpression' &&
      hasSpreadProperty(node.properties)
    ) {
      return transformSpreadProperty(path);
    }

    return node;
  },
};
