import Scope from '../Scope';
import {LetBinding} from '@romejs/js-compiler';
import {addFunctionBindings} from '../utils';
import * as t from '@romejs/js-ast';

export default function FunctionExpression(
  node: t.FunctionExpressionNode,
  parent: t.Node,
  scope: Scope,
) {
  const newScope = scope.fork('function', node);
  if (node.id !== undefined) {
    newScope.addBinding(
      new LetBinding({
        node: node.id,
        name: node.id.name,
        scope,
      }),
    );
  }
  addFunctionBindings(newScope, node);
  return newScope;
}
