import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowTypeParameterInstantiation(
  generator: Generator,
  node:
    | t.FlowTypeParameterInstantiationNode
    | t.FlowTypeParameterDeclarationNode,
) {
  generator.token('<');
  generator.printCommaList(node.params, node);
  generator.token('>');
}
