import {JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type FlowVarianceKind = 'plus' | 'minus';

export type FlowVarianceNode = JSNodeBase & {
  type: 'FlowVariance';
  kind: FlowVarianceKind;
};

export const flowVariance = createBuilder<FlowVarianceNode>('FlowVariance');
