import {UnknownObject} from '@romejs/refinement';
import {Context, ContextKey} from './types';
import {Position, SourceLocation} from '@romejs/parser-core';
import {DiagnosticError} from '@romejs/diagnostics';
import {stringifyConsumer, stringifyComments} from './stringify';

export type ConsumerOptions = {
  filename: undefined | string;
  key: undefined | ContextKey;
  context: undefined | Context;
  value: unknown;
  parent: undefined | Consumer;
};

export class ConsumerMap extends Map<string, Consumer> {
  getStrict(key: string): Consumer {
    const val = this.get(key);
    if (val === undefined) {
      throw new Error(`Expected property ${key}`);
    }
    return val;
  }
}

const EMPTY_POSITION: Position = {
  line: 1,
  column: 0,
  index: 0,
};

const EMPTY_CONTEXT: Context = {
  start: EMPTY_POSITION,
  end: EMPTY_POSITION,
  comments: [],
  innerComments: [],
  originalValue: undefined,
  children: new Map(),
};

export default class Consumer {
  constructor(opts: ConsumerOptions) {
    this.filename = opts.filename;
    this.value = opts.value;
    this.parent = opts.parent;
    this.key = opts.key;

    if (opts.context === undefined) {
      this.context = {...EMPTY_CONTEXT};
      this.fromSource = false;
    } else {
      this.context = opts.context;
      this.fromSource = true;
    }
  }

  parent: undefined | Consumer;
  key: undefined | ContextKey;
  value: unknown;
  filename: undefined | string;
  context: Context;
  fromSource: boolean;

  getComments() {
    const {comments, innerComments} = this.context;
    return {
      outer: comments,
      inner: innerComments,
    };
  }

  getInnerLocation(): SourceLocation {
    const {originalValue} = this.context;

    const loc = this.getLocation();

    if (typeof originalValue === 'string') {
      return {
        ...loc,
        start: {
          index: loc.start.index + 1,
          line: loc.start.line,
          column: loc.start.column + 1,
        },
        end: {
          index: loc.end.index - 1,
          line: loc.end.line,
          column: loc.end.column - 1,
        },
      };
    } else {
      return loc;
    }
  }

  getLocation(): SourceLocation {
    const {start, end} = this.context;
    return {
      filename: this.filename,
      start,
      end,
    };
  }

  getPath(): string {
    const keys: Array<string> = [];

    let consumer: undefined | Consumer = this;
    do {
      if (consumer.key !== undefined) {
        keys.unshift(String(consumer.key));
      }
      consumer = consumer.parent;
    } while (consumer !== undefined);

    return keys.join('.');
  }

  unexpected(msg: string) {
    const {filename, fromSource, value, parent, key} = this;
    const {originalValue} = this.context;
    let {start, end} = this.context;

    // Add a descriptive path where the error occured
    // For an array, as indicated by a numeric key, then refer to it generally
    if (typeof key === 'number' && parent !== undefined) {
      msg += ` as an item for <emphasis>${parent.getPath()}</emphasis>`;
    } else {
      msg += ` at <emphasis>${this.getPath()}</emphasis>`;
    }

    // Make the errors more descriptive
    if (fromSource) {
      if (originalValue !== value) {
        msg += '. This value has been modified from the original source.';
      }
    } else {
      // Go up the consumer tree and take the position from the first consumer found in the source
      let consumer: undefined | Consumer = this;
      do {
        if (consumer.fromSource) {
          ({start, end} = consumer.context);
          break;
        }
        consumer = consumer.parent;
      } while (consumer !== undefined);

      // If consumer is undefined and we have no filename then we were not able to find a location,
      // in this case, just throw a normal error
      if (consumer === undefined && filename === undefined) {
        throw new Error(msg);
      }

      msg += `. This value was not found in the original source.`;
    }

    let locMsg = `(${start.line}: ${start.column})`;
    if (filename !== undefined) {
      // Not sure what scenario we would ever have position info but no filename...
      locMsg = `at ${filename} ${locMsg}`;
    }

    const errMsg = `Error occured while consuming JSON ${locMsg}: ${msg}`;

    throw new DiagnosticError(errMsg, {
      category: 'json',
      language: 'json',
      filename,
      message: msg,
      start,
      end: end,
    });
  }

  fork(key: ContextKey, value: unknown) {
    return new Consumer({
      filename: this.filename,
      context: this.context.children.get(key),
      value,
      parent: this,
      key,
    });
  }

  _normalizeValueForSet(value: unknown): unknown {
    if (value instanceof Set) {
      return Array.from(value);
    }

    if (value instanceof Map) {
      const obj: UnknownObject = {};
      for (const [key, val] of value) {
        obj[key] = val;
      }
      return obj;
    }

    return value;
  }

  getValue(def?: unknown): unknown {
    if (this.exists()) {
      return this.value;
    } else {
      return def;
    }
  }

  setValue(rawValue: unknown): this {
    const value = this._normalizeValueForSet(rawValue);
    this.value = value;

    // If we're at the root (as indicated by the lack of these properties) then go no where else
    const {parent, key} = this;
    if (parent === undefined || key === undefined) {
      return this;
    }

    // Validate the parent is an object
    const parentValue = parent.asMixed();
    if (parentValue === undefined || typeof parentValue !== 'object') {
      throw parent.unexpected('Attempted to set a property on a non-object');
    }

    // Mutate the parent
    const parentObj = parent.asUnknownObject();
    parentObj[String(key)] = value;
    parent.setValue(parentObj);

    return this;
  }

  renameProperty(before: string, after: string): this {
    const object = this.asUnknownObject();

    // Set the new value
    const value = object[before];
    object[before] = undefined;
    object[after] = value;

    // Replace the context
    const {children} = this.context;
    const beforeContext = children.get(before);
    children.delete(before);
    children.delete(after);
    if (beforeContext !== undefined) {
      children.set(after, beforeContext);
    }

    this.setValue(object);

    return this;
  }

  hasProperty(key: string): boolean {
    return this.getProperty(key).asMixed() !== undefined;
  }

  setProperty(key: string, value: unknown): Consumer {
    return this.getProperty(key).setValue(value);
  }

  getProperty(key: string): Consumer {
    const value = this.asUnknownObject();
    return this.fork(key, value[key]);
  }

  spliceArray(
    index: number,
    deleteCount: number,
    ...items: Array<unknown>
  ): this {
    const {children} = this.context;
    const arr = this.asPlainArray();

    // Remove all deleted elements context
    for (let i = 0; i < deleteCount; i++) {
      children.delete(index + i);
    }

    // Splice the actual array
    arr.splice(index, deleteCount, ...items);

    // Shift over all all the child contexts
    for (let i = arr.length - 1; i >= index + deleteCount; i--) {
      const context = children.get(i);
      if (context !== undefined) {
        children.set(i - deleteCount + items.length, context);
        children.delete(i);
      }
    }

    this.setValue(arr);

    return this;
  }

  pushArray(item: unknown): this {
    this.concatArray([item]);
    return this;
  }

  concatArray(items: Array<unknown>): this {
    const arr = this.asPlainArray();
    this.setValue(arr.concat(items));
    return this;
  }

  exists() {
    return this.value !== undefined;
  }

  mergeObjects(...consumers: Array<undefined | Consumer>): Consumer {
    // Clone value so we can put other objects onto it
    const newValue = {
      ...this.asUnknownObject(),
    };

    // Clone context so we can mutate it
    const newContext: Context = {
      ...this.context,
    };

    let hadConsumers = false;
    for (const consumer of consumers) {
      if (consumer === undefined) {
        continue;
      }
      hadConsumers = true;

      // Add all properties
      const obj = consumer.asUnknownObject();
      Object.assign(newValue, obj);

      // Add context entries
      for (const [key, entry] of consumer.context.children) {
        newContext.children.set(key, entry);
      }
    }

    // Optimization in case there were no consumers passed
    if (!hadConsumers) {
      return this;
    }

    return new Consumer({
      filename: this.filename,
      context: newContext,
      value: newValue,
      parent: this.parent,
      key: this.key,
    });
  }

  asUnknownObject(optional?: boolean): UnknownObject {
    if (optional === true && !this.exists()) {
      return {};
    }

    const {value} = this;
    if (value == null || typeof value !== 'object') {
      throw this.unexpected('Expected object');
    }
    // @ts-ignore
    return {...value};
  }

  asMap(optional?: boolean): ConsumerMap {
    const value = this.asUnknownObject(optional);
    const map = new ConsumerMap();
    for (const key in value) {
      map.set(key, this.fork(key, value[key]));
    }
    return map;
  }

  asSet(optional?: boolean): Set<Consumer> {
    const arr = this.asArray(optional);
    const setVals: Set<unknown> = new Set();
    const set: Set<Consumer> = new Set();

    for (let i = 0; i < arr.length; i++) {
      const consumer = arr[i];
      const value = consumer.asMixed();
      if (setVals.has(value)) {
        continue;
      }

      setVals.add(value);
      set.add(consumer);
    }

    return set;
  }

  asPlainArray(optional?: boolean): Array<unknown> {
    if (optional === true && !this.exists()) {
      return [];
    }

    const {value} = this;

    if (!Array.isArray(value)) {
      throw this.unexpected('Expected array');
    }

    return [...value];
  }

  asArray(optional?: boolean): Array<Consumer> {
    const arr = this.asPlainArray(optional);

    return arr.map((val, index) => {
      return this.fork(index, val);
    });
  }

  asDateOrVoid(def?: Date): undefined | Date {
    if (this.exists()) {
      return this.asDate(def);
    } else {
      return undefined;
    }
  }

  asDate(def?: Date): Date {
    const value = this.getValue(def);
    if (!(value instanceof Date)) {
      throw this.unexpected('Expected a date');
    }
    return value;
  }

  asBooleanOrVoid(def?: boolean): undefined | boolean {
    if (this.exists()) {
      return this.asBoolean(def);
    } else {
      return undefined;
    }
  }

  asBoolean(def?: boolean): boolean {
    const value = this.getValue(def);
    if (typeof value !== 'boolean') {
      throw this.unexpected('Expected a boolean');
    }
    return value;
  }

  asStringOrVoid(def?: string): undefined | string {
    if (this.exists()) {
      return this.asString(def);
    } else {
      return undefined;
    }
  }

  asString(def?: string): string {
    const value = this.getValue(def);
    if (typeof value !== 'string') {
      throw this.unexpected('Expected a string');
    }
    return value;
  }

  asBigIntOrVoid(def?: number | bigint): undefined | bigint {
    if (this.exists()) {
      return this.asBigInt(def);
    } else {
      return undefined;
    }
  }

  asBigInt(def?: number | bigint): bigint {
    const value = this.getValue(def);

    if (typeof value === 'number') {
      return BigInt(value);
    }

    if (typeof value === 'bigint') {
      return value;
    }

    throw this.unexpected('Expected a bigint');
  }

  asNumberOrVoid(def?: number): undefined | number {
    if (this.exists()) {
      return this.asNumber(def);
    } else {
      return undefined;
    }
  }

  asNumber(def?: number): number {
    const value = this.getValue(def);
    if (typeof value !== 'number') {
      throw this.unexpected('Expected a number');
    }
    return value;
  }

  asNull(): null {
    const {value} = this;
    if (value === null) {
      return value;
    } else {
      throw this.unexpected('Expected null');
    }
  }

  asMixed(): unknown {
    return this.value;
  }

  asAny(): any {
    return this.value;
  }

  stringify(): string {
    const val = stringifyConsumer(this, {
      isTopLevel: true,
      level: 0,
      stack: new Set(),
    });

    // stringifyConsumer doesn't add the outer comments of the current consumer
    const {outer: comments} = this.getComments();
    if (comments.length > 0) {
      return stringifyComments('', comments).join('\n') + '\n' + val;
    }

    return val;
  }
}
