import {JSNodeBase, PatternBase, PatternNode} from '../index';
import {createBuilder} from '../utils';

export type RestPropertyNode = JSNodeBase & PatternBase & {
  type: 'RestProperty',
  argument: PatternNode,
};

export const restProperty = createBuilder<RestPropertyNode>('RestProperty', {
  visitorKeys: ['argument'],
});
