import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowDeclaredPredicate(
  generator: Generator,
  node: t.FlowDeclaredPredicateNode,
) {
  generator.token('%checks');
  generator.space();
  generator.print(node.value, node);
}
