import Bundler from './Bundler';
import DependencyNode from '../dependencies/DependencyNode';
import {Mappings} from '@romejs/codec-source-map';
import {BundleRequestResult, BundlerMode} from '../../common/types/bundler';
import {DependencyOrder} from '../dependencies/DependencyOrderer';
import {
  CompileResult,
  BundleCompileOptions,
  BundleCompileResolvedImports,
} from '@romejs/js-compiler';
import {getPrefixedBundleNamespace} from '@romejs/js-compiler';
import {DiagnosticsProcessor} from '@romejs/diagnostics';
import {SourceMapGenerator} from '@romejs/codec-source-map';
import PRELUDE from './prelude';

export default class BundleRequest {
  constructor(bundler: Bundler, mode: BundlerMode, resolvedEntry: string) {
    this.bundler = bundler;

    this.mode = mode;

    this.resolvedEntry = resolvedEntry;
    this.resolvedEntryUid = bundler.master.projectManager.getUid(resolvedEntry);

    this.diagnosticsProcessor = new DiagnosticsProcessor({
      origins: [
        `bundler: Requested bundle for <filelink filename="${
          this.resolvedEntryUid
        }" />`,
      ],
    });
    this.compiles = new Map();

    this.sourceMap = new SourceMapGenerator({
      file: 'TODO-something',
    });
  }

  bundler: Bundler;
  resolvedEntry: string;
  resolvedEntryUid: string;
  diagnosticsProcessor: DiagnosticsProcessor;
  compiles: Map<string, CompileResult>;
  sourceMap: SourceMapGenerator;
  mode: BundlerMode;

  async stepAnalyze(): Promise<DependencyNode> {
    const {reporter, graph} = this.bundler;

    const analyzeProgress = reporter.progress();
    analyzeProgress.setTitle('Analysing');
    const node = await graph.seed(
      this.resolvedEntry,
      this.diagnosticsProcessor,
      analyzeProgress,
    );
    analyzeProgress.end();
    return node;
  }

  async stepCompile(files: Array<string>) {
    const {reporter, master} = this.bundler;

    const compilingSpinner = reporter.progress();
    compilingSpinner.setTotal(files.length);
    compilingSpinner.setText('Compiling');

    const groupedFiles = await master.fileManager.groupFilesByWorker(files);
    await Promise.all(
      groupedFiles.map(async files => {
        for (const filename of files) {
          await this.compile(filename);
          compilingSpinner.tick();
        }
      }),
    );
    compilingSpinner.end();
  }

  async compile(source: string) {
    const {graph, master} = this.bundler;

    const mod = graph.getNode(source);
    const deps = mod.resolvedDependencies;

    // Build a map of relative module sources to module id
    const relativeSourcesToModuleId: Dict<string> = {};
    for (const [relative, absolute] of mod.resolvedDependencies) {
      const moduleId = graph.getNode(absolute).id;
      relativeSourcesToModuleId[relative] = moduleId;
    }

    // Build up a map of any forwarded imports
    const resolvedImports: BundleCompileResolvedImports = {};

    // Go through all of our dependencies and check if they have any external exports to forward
    const parentModule = mod;
    const allowTypeImportsAsValue = mod.analyze.syntax.includes('ts');
    for (const absolute of deps.values()) {
      const mod = graph.getNode(absolute);

      // We can't follow CJS names
      if (mod.type === 'cjs') {
        continue;
      }

      const usedNames = parentModule.getDependencyInfoFromAbsolute(absolute)
        .names;

      // Try to resolve these exports
      for (const {name, kind, loc} of usedNames) {
        if (kind === 'type') {
          // Don't care about no type imports!
          continue;
        }

        const resolved = graph.resolveImport(name, absolute);

        // Unknown import
        if (resolved === undefined) {
          this.diagnosticsProcessor.addDiagnostic({
            category: 'bundler',
            ...loc,
            message: `Couldn't find export <emphasis>${name}</emphasis> in <emphasis><filelink filename="${
              mod.id
            }" /></emphasis>`,
          });
          continue;
        }

        // Flag imports of the wrong type
        if (
          !allowTypeImportsAsValue &&
          kind === 'value' &&
          (resolved.kind === 'type' || resolved.kind === 'typeof')
        ) {
          this.diagnosticsProcessor.addDiagnostic({
            category: 'bundler',
            ...loc,
            message: `The export <emphasis>${name}</emphasis> in <emphasis><filelink filename="${
              mod.id
            }" /></emphasis> was incorrectly imported as a <emphasis>${kind}</emphasis> when it's actually a <emphasis>${
              resolved.kind
            }</emphasis>`,
          });
        }

        if (resolved.node.id !== mod.id) {
          resolvedImports[`${mod.id}:${name}`] = {
            id: resolved.node.id,
            name: resolved.name,
          };
        }
      }
    }

    const opts: BundleCompileOptions = {
      mode: this.mode,
      moduleAll: mod.all,
      moduleType: mod.type,
      moduleId: mod.id,
      relativeSourcesToModuleId,
      resolvedImports,
    };

    const res: CompileResult = await master.api.compile(
      source,
      'compileForBundle',
      {bundle: opts},
    );

    if (res.diagnostics.length > 0) {
      this.diagnosticsProcessor.addDiagnostics(res.diagnostics);
    }

    this.compiles.set(source, res);
  }

  async stepCombine(order: DependencyOrder): Promise<BundleRequestResult> {
    const {files} = order;
    const {inlineSourceMap} = this.bundler.config;
    const {graph} = this.bundler;
    const {resolvedEntry, mode, sourceMap} = this;

    let content: string = '';
    let lineOffset: number = 0;

    function push(str: string) {
      str += '\n';
      content += str;
      for (let cha of str) {
        if (cha === '\n') {
          lineOffset++;
        }
      }
    }

    function addMappings(
      filename: string,
      sourceContent: string,
      mappings: Mappings,
    ) {
      sourceMap.setSourceContent(filename, sourceContent);
      for (const mapping of mappings) {
        sourceMap.addMapping({
          ...mapping,
          generatedLine: lineOffset + mapping.generatedLine,
        });
      }
    }

    // add on bootstrap
    if (order.firstTopAwaitLocations.length > 0) {
      if (mode === 'wrapper') {
        for (const loc of order.firstTopAwaitLocations) {
          this.diagnosticsProcessor.addDiagnostic({
            category: 'bundler/topLevelAwait',
            filename: loc.filename,
            start: loc.start,
            end: loc.end,
            message:
              "This module contains a top level await which isn't supported in wrapper mode",
          });
        }
      }

      push(`(async function() {`);
    } else {
      push(`(function() {`);
    }

    if (mode === 'flat') {
      push(`  'use strict';`);
    }

    push(PRELUDE);

    const declaredCJS: Set<DependencyNode> = new Set();
    const declareCJS = (module: DependencyNode) => {
      if (
        mode !== 'flat' ||
        module.type !== 'cjs' ||
        module.all === false ||
        declaredCJS.has(module)
      ) {
        return undefined;
      }

      declaredCJS.add(module);

      push(`  var ${getPrefixedBundleNamespace(module.id)} = {};`);
    };

    // add on files
    for (const source of files) {
      const module = graph.getNode(source);

      for (const filename of module.getAbsoluteDependencies()) {
        declareCJS(graph.getNode(filename));
      }

      const compileResult = this.compiles.get(source);
      if (compileResult === undefined) {
        throw new Error('Expected compile result');
      }

      // Only do this in flat mode, the module id will already be in the wrapper otherwise
      if (mode === 'flat') {
        push(`  // ${module.id}`);
      }

      declareCJS(module);

      addMappings(module.id, compileResult.src, compileResult.mappings);
      push(compileResult.code);

      if (module.usedAsync) {
        push(
          `  Rome.declareModule(${String(
            module.id,
          )}, ${getPrefixedBundleNamespace(module.id)});`,
        );
      }

      push('');
    }

    // push on initial entry require
    const entryModule = graph.getNode(resolvedEntry);
    if (mode === 'flat') {
      push(`  return ${getPrefixedBundleNamespace(entryModule.id)};`);
    } else {
      push(`  return Rome.require("${entryModule.id}");`);
    }

    // push footer
    push('})();');

    //
    if (inlineSourceMap === true) {
      const sourceMapComment = sourceMap.toComment();
      content += sourceMapComment;
    }

    return {
      diagnostics: this.diagnosticsProcessor.getPartialDiagnostics(),
      content,
      map: sourceMap.toJSON(),
    };
  }

  shouldAbort(): boolean {
    return this.diagnosticsProcessor.hasDiagnostics();
  }

  abort(): BundleRequestResult {
    return {
      map: this.sourceMap.toJSON(),
      content: '',
      diagnostics: this.diagnosticsProcessor.getPartialDiagnostics(),
    };
  }

  async bundle(): Promise<BundleRequestResult> {
    // Analyze dependencies
    const node: DependencyNode = await this.stepAnalyze();

    // Get a sorted list of files based on execution order
    const order = node.getDependencyOrder();
    this.diagnosticsProcessor.addDiagnostics(
      order.diagnostics,
      'bundler: Dependency order from entry file requested',
    );
    if (this.shouldAbort()) {
      return this.abort();
    }

    // Compile
    await this.stepCompile(order.files);
    if (this.shouldAbort()) {
      return this.abort();
    }

    // Combine
    return await this.stepCombine(order);
  }
}
