import {JSNodeBase, PatternNode, BlockStatementNode} from '../index';
import {createBuilder} from '../utils';

export type CatchClauseNode = JSNodeBase & {
  type: 'CatchClause',
  param: PatternNode,
  body: BlockStatementNode,
};

export const catchClause = createBuilder<CatchClauseNode>('CatchClause', {
  visitorKeys: ['param', 'body'],
});
