import {MasterRequest} from '@romejs/core';
import {FlagParser} from '@romejs/cli-flags';
import {Consumer} from '@romejs/codec-json';
import {DiagnosticsError} from '@romejs/diagnostics';
import {createMasterCommand} from '../../commands';
import {commandCategories} from '../../commands';

export default createMasterCommand({
  category: commandCategories.SOURCE_CODE,
  description: 'parse a single file and dump its ast',

  parseCLIFlags(p: FlagParser) {
    return {
      compact: p.boolean('compact', {
        description: 'remove locations and other noise from the output',
        default: true,
      }),
      sourceType: p.string('source-type', {
        description: '',
        default: 'module',
      }),
      showDespiteDiagnostics: p.boolean('show-despite-diagnostics', {
        description: '',
        default: false,
      }),
    };
  },

  async default(req: MasterRequest, commandFlags: Consumer): Promise<void> {
    const {args, master, reporter, flags} = req;
    if (args.length !== 1) {
      throw new Error('Expected exactly 1 argument.');
    }

    const filename = await master.resolver.resolveEntryAssert({
      ...req.getResolverOptionsFromFlags(),
      origin: flags.cwd,
      source: args[0],
    });

    const ast = await master.api.parse(filename, {
      compact: commandFlags.getProperty('compact').asBoolean(false),
      // @ts-ignore: validate this
      sourceType: commandFlags.getProperty('sourceType').asString('module'),
    });

    const showDespiteDiagnostics = commandFlags
      .getProperty('showDespiteDiagnostics')
      .asBoolean(false);
    const hasErrors = ast.diagnostics.length > 0;

    if (!hasErrors || showDespiteDiagnostics) {
      reporter.inspect(ast);
    }

    if (hasErrors) {
      throw new DiagnosticsError('Parsing failed', ast.diagnostics);
    }
  },
});
