import {JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type FlowNumericLiteralNode = JSNodeBase & {
  type: 'FlowNumericLiteral',
  value: number,
};

export const flowNumericLiteral = createBuilder<FlowNumericLiteralNode>(
  'FlowNumericLiteral',
);
