import {Scope} from '../../scopes';
import {PartialDiagnosticAdviceItem} from '@romejs/diagnostics';
import {Node} from '@romejs/js-ast';
import AnyT from '../AnyT';
import T from '../T';

export default class E extends T {
  static type = 'E';

  static hydrate(scope: Scope, originNode: Node): T {
    return new AnyT(scope, originNode);
  }

  humanize(): string {
    return this.getError().message;
  }

  getError(): {
    advice?: undefined | Array<PartialDiagnosticAdviceItem>;
    message: string;
    lowerTarget: T;
    upperTarget?: T;
  } {
    throw new Error('unimplemented');
  }

  compatibleWith(): boolean {
    return false;
  }
}
