import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowUnionTypeAnnotation(
  generator: Generator,
  node: t.FlowUnionTypeAnnotationNode,
) {
  generator.printJoin(node.types, node, {separator: orSeparator});
}

function orSeparator(generator: Generator) {
  generator.space();
  generator.token('|');
  generator.space();
}
