import Scope from '../Scope';
import {LetBinding, VarBinding, ConstBinding} from '@romejs/js-compiler';
import * as t from '@romejs/js-ast';
import {getBindingIdentifiers} from '@romejs/js-ast-utils';

export default function VariableDeclaration(
  node: t.VariableDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  for (const id of getBindingIdentifiers(node)) {
    switch (node.kind) {
      case 'let':
        scope.addBinding(
          new LetBinding({
            node: id,
            name: id.name,
            scope,
          }),
        );
        break;

      case 'const':
        scope.addBinding(
          new ConstBinding({
            node: id,
            name: id.name,
            scope,
          }),
        );
        break;

      case 'var':
        const funcScope =
          scope.getParentScope('function') ||
          scope.getParentScope('program') ||
          scope.getRootScope();
        funcScope.addBinding(
          new VarBinding({
            node: id,
            name: id.name,
            scope,
          }),
        );
        break;
    }
  }
}
