import {JSNodeBase, FlowNode} from '../index';
import {createBuilder} from '../utils';

export type FlowObjectTypeSpreadPropertyNode = JSNodeBase & {
  type: 'FlowObjectTypeSpreadProperty',
  argument: FlowNode,
};

export const flowObjectTypeSpreadProperty = createBuilder<FlowObjectTypeSpreadPropertyNode>(
  'FlowObjectTypeSpreadProperty',
  {visitorKeys: ['argument']},
);
