import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXClosingElement(
  generator: Generator,
  node: t.JSXClosingElementNode,
) {
  generator.token('</');
  generator.print(node.name, node);
  generator.token('>');
}
