import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import BooleanT from '../../types/BooleanT';

export function FlowBooleanTypeAnnotation(
  node: n.FlowBooleanTypeAnnotationNode,
  scope: Scope,
) {
  return new BooleanT(scope, node);
}
