import {types as tt, TokenType} from '../tokenizer/types';
import {Position, SourceLocation} from '@romejs/parser-core';
import {State} from '../tokenizer/state';
import Parser from '../Parser';
import * as n from '@romejs/js-ast';
import {
  isLetStart,
  parseImport,
  parseIdentifier,
  parseExpressionAtom,
  parseExpression,
  parseExport,
  parseFunctionParams,
  parseArrowHead,
  parseArrowExpression,
  createIdentifier,
  checkGetterSetterParamCount,
} from './index';
import invariant from '@romejs/invariant';

const primitiveTypes = [
  'any',
  'bool',
  'boolean',
  'empty',
  'false',
  'mixed',
  'null',
  'number',
  'bigint',
  'static',
  'string',
  'true',
  'typeof',
  'void',
  'interface',
  'extends',
  '_',
];

const exportSuggestions: Map<string, string> = new Map([
  ['const', 'declare export var'],
  ['let', 'declare export var'],
  ['type', 'export type'],
  ['interface', 'export interface'],
]);

function checkNotUnderscore(parser: Parser, id: n.IdentifierNode) {
  if (id.name === '_') {
    parser.addDiagnostic({
      loc: id.loc,
      message: '`_` is only allowed as a type argument to call or new',
    });
  }
}

function isEsModuleType(bodyElement: n.Node): boolean {
  return (
    bodyElement.type === 'DeclareExportAllDeclaration' ||
    (bodyElement.type === 'FlowDeclareExportDeclaration' &&
      (!bodyElement.declaration ||
        (bodyElement.declaration.type !== 'FlowTypeAlias' &&
          bodyElement.declaration.type !== 'FlowInterfaceDeclaration')))
  );
}

export function hasTypeImportKind(kind: undefined | n.ModuleKind): boolean {
  return kind === 'type' || kind === 'typeof';
}

export function isMaybeDefaultImport(state: State): boolean {
  return (
    (state.tokenType === tt.name || !!state.tokenType.keyword) &&
    state.tokenValue !== 'from'
  );
}

export function parseFlowTypeParameterInstantiationCallOrNew(
  parser: Parser,
): n.FlowTypeParameterInstantiationNode {
  const start = parser.getPosition();
  const params = [];

  parser.pushScope('TYPE', true);

  if (parser.expectRelational('<')) {
    while (!parser.isRelational('>')) {
      params.push(parseFlowTypeOrImplicitInstantiation(parser));

      if (!parser.isRelational('>') && !parser.expect(tt.comma)) {
        break;
      }
    }
    parser.expectRelational('>');
  }

  parser.popScope('TYPE');

  return {
    type: 'FlowTypeParameterInstantiation',
    loc: parser.finishLoc(start),
    params,
  };
}

function parseFlowTypeOrImplicitInstantiation(parser: Parser): n.FlowNode {
  if (parser.state.tokenType === tt.name && parser.state.tokenValue === '_') {
    const startPos = parser.state.startPos;
    const node = parseIdentifier(parser);
    return parseFlowGenericType(parser, startPos, node);
  } else {
    return parseFlowType(parser);
  }
}

function parseFlowTypeInitialiser(parser: Parser, tok?: TokenType): n.FlowNode {
  parser.pushScope('TYPE', true);
  parser.expect(tok || tt.colon);
  const type = parseFlowType(parser);
  parser.popScope('TYPE');
  return type;
}

function parseFlowPredicate(
  parser: Parser,
): n.FlowDeclaredPredicateNode | n.FlowInferredPredicateNode {
  const start = parser.getPosition();
  const moduloPos = parser.state.startPos;
  parser.expect(tt.modulo);
  const checksPos = parser.state.startPos;
  parser.expectContextual('checks');

  // Force '%' and 'checks' to be adjacent
  if (
    moduloPos.line !== checksPos.line ||
    moduloPos.column !== checksPos.column - 1
  ) {
    parser.addDiagnostic({
      start: moduloPos,
      message: 'Spaces between % and checks are not allowed here.',
    });
  }

  if (parser.match(tt.parenL)) {
    const openContext = parser.expectOpening(
      tt.parenL,
      tt.parenR,
      'flow declared predicate',
    );
    const value = parseExpression(parser, 'flow declared predicate');
    parser.expectClosing(openContext);
    return {
      loc: parser.finishLoc(start),
      type: 'FlowDeclaredPredicate',
      value,
    } as n.FlowDeclaredPredicateNode;
  } else {
    return {
      loc: parser.finishLoc(start),
      type: 'FlowInferredPredicate',
    } as n.FlowInferredPredicateNode;
  }
}

export function parseFlowTypeAndPredicateInitialiser(
  parser: Parser,
): [undefined | n.FlowNode, undefined | n.FlowPredicateNode] {
  parser.expectSyntaxEnabled('flow');

  parser.pushScope('TYPE', true);
  parser.expect(tt.colon);
  let type = undefined;
  let predicate = undefined;
  if (parser.match(tt.modulo)) {
    parser.popScope('TYPE');
    predicate = parseFlowPredicate(parser);
  } else {
    type = parseFlowType(parser);
    parser.popScope('TYPE');
    if (parser.match(tt.modulo)) {
      predicate = parseFlowPredicate(parser);
    }
  }
  return [type, predicate];
}

function parseFlowDeclareClass(
  parser: Parser,
  start: Position,
): n.FlowDeclareClassNode {
  parser.next();
  // @ts-ignore
  return {
    ...parseFlowInterfaceish(parser, true),
    loc: parser.finishLoc(start),
    type: 'FlowDeclareClass',
  };
}

function parseFlowDeclareFunction(
  parser: Parser,
  start: Position,
): n.FlowDeclareFunctionNode {
  parser.next();

  const id = parseIdentifier(parser);

  let typeParameters = undefined;
  if (parser.isRelational('<')) {
    typeParameters = parseFlowTypeParameterDeclaration(parser, true);
  }

  const openContext = parser.expectOpening(
    tt.parenL,
    tt.parenR,
    'flow function type params',
  );
  const params = parseFlowFunctionTypeParams(parser);
  parser.expectClosing(openContext);

  const [returnType, predicate] = parseFlowTypeAndPredicateInitialiser(parser);

  parser.semicolon();

  if (predicate !== undefined && predicate.type === 'FlowInferredPredicate') {
    parser.addDiagnostic({
      loc: predicate.loc,
      message:
        'Predicate function declarations need to declare a predicate expression',
    });
  }

  return {
    loc: parser.finishLoc(start),
    type: 'FlowDeclareFunction',
    id: {
      loc: parser.finishLoc(start),
      ...id,
      typeAnnotation: {
        loc: parser.finishLoc(start),
        type: 'FlowTypeAnnotation',
        typeAnnotation: {
          loc: parser.finishLoc(start),
          type: 'FlowFunctionTypeAnnotation',
          params,
          returnType,
          typeParameters,
        },
      },
    },
    predicate,
  };
}

export function parseFlowDeclare(
  parser: Parser,
  start: Position,
  insideModule: boolean = false,
): n.DeclareNode {
  parser.expectSyntaxEnabled('flow');

  if (parser.match(tt._class)) {
    return parseFlowDeclareClass(parser, start);
  }

  if (parser.match(tt._function)) {
    return parseFlowDeclareFunction(parser, start);
  }

  if (parser.match(tt._var)) {
    return parseFlowDeclareVariable(parser, start);
  }

  if (parser.isContextual('module')) {
    if (parser.lookaheadState().tokenType === tt.dot) {
      return parseFlowDeclareModuleExports(parser, start);
    } else {
      if (insideModule) {
        parser.addDiagnostic({
          message:
            '`declare module` cannot be used inside another `declare module`',
        });
      }
      return parseFlowDeclareModule(parser, start);
    }
  }

  if (parser.isContextual('type')) {
    return parseFlowDeclareTypeAlias(parser, start);
  }

  if (parser.isContextual('opaque')) {
    return parseFlowDeclareOpaqueType(parser, start);
  }

  if (parser.isContextual('interface')) {
    return parseFlowDeclareInterface(parser, start);
  }

  if (parser.match(tt._export)) {
    return parseFlowDeclareExportDeclaration(parser, start, insideModule);
  }

  parser.addDiagnostic({
    message: 'Unknown start to Flow declaration',
  });
  return parser.createUnknownNode('flow declaration', start);
}

function parseFlowDeclareVariable(
  parser: Parser,
  start: Position,
): n.FlowDeclareVariableNode {
  parser.next();
  const id = parseFlowTypeAnnotatableIdentifier(
    parser,
    /*allowPrimitiveOverride*/ true,
  );
  parser.semicolon();
  return {
    loc: parser.finishLoc(start),
    type: 'FlowDeclareVariable',
    id,
  };
}

function parseFlowDeclareModule(
  parser: Parser,
  start: Position,
): n.FlowDeclareModuleNode {
  // Eat `module` token
  parser.next();

  let id;
  if (parser.match(tt.string)) {
    id = parseExpressionAtom(parser, 'flow declare module id');
  } else {
    id = parseIdentifier(parser);
  }

  const bodyStart = parser.getPosition();
  const body = [];
  const openContext = parser.expectOpening(
    tt.braceL,
    tt.braceR,
    'flow declare module body',
  );

  while (!parser.match(tt.braceR)) {
    let bodyNodeStart = parser.getPosition();

    let bodyNode;
    if (parser.match(tt._import)) {
      const lookahead = parser.lookaheadState();
      if (
        lookahead.tokenValue !== 'type' &&
        lookahead.tokenValue !== 'typeof'
      ) {
        parser.addDiagnostic({
          message:
            'Imports within a `declare module` body must always be `import type` or `import typeof`',
        });
      }
      parser.next();
      bodyNode = parseImport(parser, bodyNodeStart);
    } else {
      if (
        !parser.expectContextual(
          'declare',
          'Only declares and type imports are allowed inside declare module',
        )
      ) {
        break;
      }

      bodyNode = parseFlowDeclare(parser, bodyNodeStart, true);
    }

    body.push(bodyNode);
  }

  parser.expectClosing(openContext);

  const bodyNode: n.BlockStatementNode = {
    loc: parser.finishLoc(bodyStart),
    type: 'BlockStatement',
    body,
  };

  let kind: undefined | 'commonjs' | 'es';
  let hasModuleExport = false;
  const errorMessage =
    'Found both `declare module.exports` and `declare export` in the same module. Modules can only have 1 since they are either an ES module or they are a CommonJS module';

  for (const bodyElement of body) {
    if (isEsModuleType(bodyElement)) {
      if (kind === 'commonjs') {
        parser.addDiagnostic({
          loc: bodyElement.loc,
          message: errorMessage,
        });
      }
      kind = 'es';
    } else if (bodyElement.type === 'FlowDeclareModuleExports') {
      if (hasModuleExport) {
        parser.addDiagnostic({
          loc: bodyElement.loc,
          message: 'Duplicate `declare module.exports` statement',
        });
      }

      if (kind === 'es') {
        parser.addDiagnostic({
          loc: bodyElement.loc,
          message: errorMessage,
        });
      }

      kind = 'commonjs';
      hasModuleExport = true;
    }
  }

  return {
    loc: parser.finishLoc(start),
    type: 'FlowDeclareModule',
    id,
    kind: kind === undefined ? 'commonjs' : kind,
    body: bodyNode,
  };
}

function parseFlowDeclareExportDeclaration(
  parser: Parser,
  start: Position,
  insideModule: undefined | boolean,
):
  | n.FlowDeclareExportDeclarationNode
  | n.FlowDeclareExportAllDeclarationNode
  | n.UnknownNode {
  parser.expect(tt._export);

  if (parser.eat(tt._default)) {
    let declaration;
    if (parser.match(tt._function) || parser.match(tt._class)) {
      // declare export default class ...
      // declare export default function ...
      declaration = parseFlowDeclare(parser, parser.getPosition());
    } else {
      // declare export default [type];
      declaration = parseFlowType(parser);
      parser.semicolon();
    }

    return {
      loc: parser.finishLoc(start),
      type: 'FlowDeclareExportDeclaration',
      declaration,
      default: true,
    };
  } else {
    if (
      parser.match(tt._const) ||
      isLetStart(parser) ||
      ((parser.isContextual('type') || parser.isContextual('interface')) &&
        !insideModule)
    ) {
      const label = String(parser.state.tokenValue);
      const suggestion = String(exportSuggestions.get(label));
      parser.addDiagnostic({
        message: `\`declare export ${label}\` is not supported. Use \`${suggestion}\` instead`,
      });
    }

    if (
      // declare export var ...
      parser.match(tt._var) ||
      // declare function ...
      parser.match(tt._function) ||
      // declare export class ...
      parser.match(tt._class) ||
      // declare export opaque ..
      parser.isContextual('opaque')
    ) {
      const declaration = parseFlowDeclare(parser, parser.getPosition());

      return {
        loc: parser.finishLoc(start),
        type: 'FlowDeclareExportDeclaration',
        declaration,
        default: false,
      };
    }

    if (
      // declare export * from '';
      parser.match(tt.star) ||
      // declare export {} ...
      parser.match(tt.braceL) ||
      // declare export interface ...
      parser.isContextual('interface') ||
      // declare export type ...
      parser.isContextual('type') ||
      // declare export opaque type ...
      parser.isContextual('opaque')
    ) {
      const node = parseExport(parser, start);

      if (node.type === 'ExportNamedDeclaration') {
        return {
          loc: parser.finishLoc(start),
          type: 'FlowDeclareExportDeclaration',
          default: false,
          declaration: node.declaration,
          specifiers: node.specifiers,
        };
      } else if (node.type === 'ExportAllDeclaration') {
        return {
          loc: parser.finishLoc(start),
          type: 'FlowDeclareExportAllDeclaration',
          source: node.source,
        } as n.FlowDeclareExportAllDeclarationNode;
      }
    }
  }

  parser.addDiagnostic({
    start,
    message: 'No valid start for Flow declare export declaration found',
  });
  return parser.createUnknownNode('flow declare export declaration', start);
}

function parseFlowDeclareModuleExports(
  parser: Parser,
  start: Position,
): n.FlowDeclareModuleExportsNode {
  parser.expectContextual('module');
  parser.expect(tt.dot);
  parser.expectContextual('exports');
  const typeAnnotation = parseFlowTypeAnnotation(parser);
  parser.semicolon();

  return {
    loc: parser.finishLoc(start),
    type: 'FlowDeclareModuleExports',
    typeAnnotation,
  };
}

function parseFlowDeclareTypeAlias(
  parser: Parser,
  start: Position,
): n.FlowDeclareTypeAliasNode {
  parser.next();
  return {
    ...parseFlowTypeAlias(parser, start),
    loc: parser.finishLoc(start),
    type: 'FlowDeclareTypeAlias',
  };
}

function parseFlowDeclareOpaqueType(
  parser: Parser,
  start: Position,
): n.FlowDeclareOpaqueTypeNode {
  parser.next();
  return {
    ...parseFlowOpaqueType(parser, start, true),
    loc: parser.finishLoc(start),
    type: 'FlowDeclareOpaqueType',
  };
}

function parseFlowDeclareInterface(
  parser: Parser,
  start: Position,
): n.FlowDeclareInterfaceNode {
  parser.next();
  return {
    ...parseFlowInterfaceish(parser),
    loc: parser.finishLoc(start),
    type: 'FlowDeclareInterface',
  };
}

// Interfaces
function parseFlowInterfaceish(
  parser: Parser,
  isClass: boolean = false,
): {
  body: n.FlowObjectTypeAnnotationNode;
  extends: Array<n.FlowInterfaceExtendsNode>;
  mixins: Array<n.FlowInterfaceExtendsNode>;
  implements: Array<n.FlowInterfaceExtendsNode>;
  id: n.IdentifierNode;
  typeParameters: undefined | n.FlowTypeParameterDeclarationNode;
} {
  const id = parseFlowRestrictedIdentifier(parser, /*liberal*/ !isClass);

  let typeParameters = undefined;
  if (parser.isRelational('<')) {
    typeParameters = parseFlowTypeParameterDeclaration(parser, true);
  }

  const _extends = [];
  const mixins = [];
  const _implements = [];

  if (parser.eat(tt._extends)) {
    do {
      _extends.push(parseFlowInterfaceExtends(parser));
    } while (!isClass && parser.eat(tt.comma));
  }

  if (parser.isContextual('mixins')) {
    parser.next();
    do {
      invariant(
        mixins !== undefined,
        'expected mixins to be present as it was set before in this method',
      );
      mixins.push(parseFlowInterfaceExtends(parser));
    } while (parser.eat(tt.comma));
  }

  if (parser.isContextual('implements')) {
    parser.next();
    do {
      _implements.push(parseFlowInterfaceExtends(parser));
    } while (parser.eat(tt.comma));
  }

  const body = parseFlowObjectType(parser, {
    allowStatic: isClass,
    allowExact: false,
    allowSpread: false,
    allowProto: isClass,
    allowInexact: false,
  });
  return {
    body,
    extends: _extends,
    mixins,
    id,
    typeParameters,
    implements: _implements,
  };
}

function parseFlowInterfaceType(
  parser: Parser,
): n.FlowInterfaceTypeAnnotationNode {
  const start = parser.getPosition();
  parser.expectContextual('interface');

  const _extends = [];
  if (parser.eat(tt._extends)) {
    do {
      _extends.push(parseFlowInterfaceExtends(parser));
    } while (parser.eat(tt.comma));
  }

  const body = parseFlowObjectType(parser, {
    allowStatic: false,
    allowExact: false,
    allowSpread: false,
    allowProto: false,
    allowInexact: false,
  });

  return {
    type: 'FlowInterfaceTypeAnnotation',
    loc: parser.finishLoc(start),
    extends: _extends,
    body,
  };
}

function parseFlowInterfaceExtends(parser: Parser): n.FlowInterfaceExtendsNode {
  const start = parser.getPosition();

  const id = parseFlowQualifiedTypeIdentifier(parser);
  let typeParameters = undefined;
  if (parser.isRelational('<')) {
    typeParameters = parseFlowTypeParameterInstantiation(parser);
  }

  return {
    loc: parser.finishLoc(start),
    type: 'FlowInterfaceExtends',
    id,
    typeParameters,
  };
}

export function parseFlowInterface(
  parser: Parser,
  start: Position,
): n.FlowInterfaceDeclarationNode {
  parser.expectSyntaxEnabled('flow');
  return {
    ...parseFlowInterfaceish(parser),
    loc: parser.finishLoc(start),
    type: 'FlowInterfaceDeclaration',
  };
}

export function checkReservedType(
  parser: Parser,
  word: string,
  loc: SourceLocation,
) {
  if (primitiveTypes.includes(word)) {
    parser.addDiagnostic({
      loc,
      message: `Cannot overwrite primitive type ${word}`,
    });
  }
}

export function parseFlowRestrictedIdentifier(
  parser: Parser,
  liberal?: boolean,
): n.IdentifierNode {
  checkReservedType(
    parser,
    String(parser.state.tokenValue),
    parser.finishLocAt(parser.state.startPos, parser.state.endPos),
  );
  return parseIdentifier(parser, liberal);
}

// Type aliases

export function parseFlowTypeAlias(
  parser: Parser,
  start: Position,
): n.FlowTypeAliasNode {
  parser.expectSyntaxEnabled('flow');

  const id = parseFlowRestrictedIdentifier(parser);
  let typeParameters;

  if (parser.isRelational('<')) {
    typeParameters = parseFlowTypeParameterDeclaration(parser, true);
  } else {
    typeParameters = undefined;
  }

  const right = parseFlowTypeInitialiser(parser, tt.eq);
  parser.semicolon();

  return {
    loc: parser.finishLoc(start),
    type: 'FlowTypeAlias',
    id,
    typeParameters,
    right,
  };
}

export function parseFlowOpaqueType(
  parser: Parser,
  start: Position,
  declare: boolean,
): n.FlowOpaqueTypeNode {
  parser.expectSyntaxEnabled('flow');
  parser.expectContextual('type');
  const id = parseFlowRestrictedIdentifier(parser, /*liberal*/ true);

  let typeParameters;
  if (parser.isRelational('<')) {
    typeParameters = parseFlowTypeParameterDeclaration(parser, true);
  } else {
    typeParameters = undefined;
  }

  // Parse the supertype
  let supertype = undefined;
  if (parser.match(tt.colon)) {
    supertype = parseFlowTypeInitialiser(parser, tt.colon);
  }

  let impltype = undefined;
  if (!declare) {
    impltype = parseFlowTypeInitialiser(parser, tt.eq);
  }
  parser.semicolon();

  return {
    loc: parser.finishLoc(start),
    type: 'FlowOpaqueType',
    id,
    typeParameters,
    supertype,
    impltype,
  };
}

function parseFlowTypeParameter(
  parser: Parser,
  allowDefault: boolean = true,
  requireDefault: boolean = false,
): n.FlowTypeParameterNode {
  const start = parser.getPosition();

  const variance = parseFlowVariance(parser);

  const ident = parseFlowTypeAnnotatableIdentifier(parser);
  const name = ident.name;
  const bound = ident.typeAnnotation;
  if (bound !== undefined && bound.type === 'TSTypeAnnotation') {
    throw new Error('Received a TS type annotation node in Flow mode');
  }

  let def;
  if (parser.match(tt.eq)) {
    if (!allowDefault) {
      parser.addDiagnostic({
        message: 'Default type parameters arent allowed here',
      });
    }

    parser.eat(tt.eq);
    def = parseFlowType(parser);
  } else if (requireDefault) {
    parser.addDiagnostic({
      message:
        'Type parameter declaration needs a default, since a preceding type parameter declaration has a default.',
    });
  }

  return {
    loc: parser.finishLoc(start),
    type: 'FlowTypeParameter',
    default: def,
    name,
    variance,
    bound,
  };
}

export function parseFlowTypeParameterDeclaration(
  parser: Parser,
  allowDefault: boolean,
): n.FlowTypeParameterDeclarationNode {
  parser.expectSyntaxEnabled('flow');

  const start = parser.getPosition();
  const params = [];

  parser.pushScope('TYPE', true);

  parser.expectRelational('<');

  let defaultRequired = false;

  do {
    const param = parseFlowTypeParameter(parser, allowDefault, defaultRequired);

    if (param.default) {
      defaultRequired = true;
    }

    params.push(param);

    if (!parser.isRelational('>') && !parser.expect(tt.comma)) {
      break;
    }
  } while (!parser.isRelational('>'));

  parser.expectRelational('>');

  parser.popScope('TYPE');

  return {
    loc: parser.finishLoc(start),
    type: 'FlowTypeParameterDeclaration',
    params,
  };
}

export function parseFlowTypeParameterInstantiation(
  parser: Parser,
): n.FlowTypeParameterInstantiationNode {
  parser.expectSyntaxEnabled('flow');

  const start = parser.getPosition();
  const params = [];

  parser.pushScope('TYPE', true);

  parser.expectRelational('<');
  const oldNoAnonFunctionType = parser.state.noAnonFunctionType;
  parser.state.noAnonFunctionType = false;

  while (!parser.isRelational('>')) {
    params.push(parseFlowType(parser));

    if (!parser.isRelational('>') && !parser.expect(tt.comma)) {
      break;
    }
  }

  parser.state.noAnonFunctionType = oldNoAnonFunctionType;
  parser.expectRelational('>');

  parser.popScope('TYPE');

  return {
    loc: parser.finishLoc(start),
    type: 'FlowTypeParameterInstantiation',
    params,
  };
}

function parseFlowObjectPropertyKey(
  parser: Parser,
): n.FlowObjectTypePropertyKey {
  if (parser.match(tt.num) || parser.match(tt.string)) {
    return parseExpressionAtom(parser, 'flow object property key');
  } else {
    return parseIdentifier(parser, true);
  }
}

function parseFlowObjectTypeIndexer(
  parser: Parser,
  start: Position,
  isStatic: boolean,
  variance: undefined | n.FlowVarianceNode,
): n.FlowObjectTypeIndexerNode {
  let id;
  let key;

  // Note: bracketL has already been consumed
  if (parser.lookaheadState().tokenType === tt.colon) {
    id = parseFlowObjectPropertyKey(parser);
    key = parseFlowTypeInitialiser(parser);
  } else {
    id = undefined;
    key = parseFlowType(parser);
  }
  parser.expect(tt.bracketR);
  const value = parseFlowTypeInitialiser(parser);

  return {
    loc: parser.finishLoc(start),
    type: 'FlowObjectTypeIndexer',
    static: isStatic,
    key,
    id,
    value,
    variance,
  };
}

function parseFlowObjectTypeMethodish(
  parser: Parser,
  start: Position,
): n.FlowFunctionTypeAnnotationNode {
  let typeParameters = undefined;

  if (parser.isRelational('<')) {
    typeParameters = parseFlowTypeParameterDeclaration(parser, false);
  }

  const openContext = parser.expectOpening(
    tt.parenL,
    tt.parenR,
    'function object method params',
  );
  const params = parseFlowFunctionTypeParams(parser);
  parser.expectClosing(openContext);
  const returnType = parseFlowTypeInitialiser(parser);

  return {
    loc: parser.finishLoc(start),
    type: 'FlowFunctionTypeAnnotation',
    params,
    typeParameters,
    returnType,
  };
}

function parseFlowObjectTypeCallProperty(
  parser: Parser,
  start: Position,
  isStatic: boolean,
): n.FlowObjectTypeCallPropertyNode {
  const valueNode = parser.getPosition();
  const value = parseFlowObjectTypeMethodish(parser, valueNode);
  return {
    loc: parser.finishLoc(start),
    type: 'FlowObjectTypeCallProperty',
    static: isStatic,
    value,
  };
}

function parseFlowObjectType(
  parser: Parser,
  opts: {
    allowStatic: boolean;
    allowExact: boolean;
    allowSpread: boolean;
    allowProto: boolean;
    allowInexact: boolean;
  },
): n.FlowObjectTypeAnnotationNode {
  const {allowExact, allowSpread, allowProto, allowInexact} = opts;
  let {allowStatic} = opts;

  parser.pushScope('TYPE', true);

  const start = parser.getPosition();

  const properties = [];

  let openContext;
  let exact;
  let inexact;
  if (allowExact && parser.match(tt.braceBarL)) {
    openContext = parser.expectOpening(
      tt.braceBarL,
      tt.braceBarR,
      'flow exact object',
    );
    exact = true;
  } else {
    openContext = parser.expectOpening(tt.braceL, tt.braceR, 'flow object');
    exact = false;
  }

  while (true) {
    if (parser.match(tt.eof) || parser.match(openContext.close)) {
      break;
    }

    let isStatic = false;
    let protoStart: undefined | Position = undefined;

    if (allowProto && parser.isContextual('proto')) {
      const lookahead = parser.lookaheadState();

      if (
        lookahead.tokenType !== tt.colon &&
        lookahead.tokenType !== tt.question
      ) {
        parser.next();
        protoStart = parser.state.startPos;
        allowStatic = false;
      }
    }

    if (allowStatic && parser.isContextual('static')) {
      const lookahead = parser.lookaheadState();

      // static is a valid identifier name
      if (
        lookahead.tokenType !== tt.colon &&
        lookahead.tokenType !== tt.question
      ) {
        parser.next();
        isStatic = true;
      }
    }

    const variance = parseFlowVariance(parser);

    if (parser.eat(tt.bracketL)) {
      if (protoStart !== undefined) {
        parser.unexpectedToken(protoStart);
      }

      if (parser.eat(tt.bracketL)) {
        if (variance) {
          parser.addDiagnostic({
            loc: variance.loc,
            message: 'Variance not allowed',
          });
        }

        properties.push(
          parseFlowObjectTypeInternalSlot(parser, start, isStatic),
        );
      } else {
        properties.push(
          parseFlowObjectTypeIndexer(parser, start, isStatic, variance),
        );
      }
    } else if (parser.match(tt.parenL) || parser.isRelational('<')) {
      if (protoStart !== undefined) {
        parser.unexpectedToken(protoStart);
      }

      if (variance) {
        parser.addDiagnostic({
          loc: variance.loc,
          message: 'Variance not allowed',
        });
      }

      properties.push(parseFlowObjectTypeCallProperty(parser, start, isStatic));
    } else {
      let kind: n.FlowObjectTypePropertyKind = 'init';

      if (parser.isContextual('get') || parser.isContextual('set')) {
        const lookahead = parser.lookaheadState();
        if (
          lookahead.tokenType === tt.name ||
          lookahead.tokenType === tt.string ||
          lookahead.tokenType === tt.num
        ) {
          const value = String(parser.state.tokenValue);
          if (value !== 'get' && value !== 'set') {
            throw new Error(
              'Expected get or set as we already validated it above',
            );
          }
          kind = value;
          parser.next();
        }
      }

      const propOrInexact = parseFlowObjectTypeProperty(parser, {
        start,
        isStatic,
        protoStart,
        variance,
        kind,
        allowSpread,
        allowInexact,
      });

      if (propOrInexact === undefined) {
        inexact = true;
      } else {
        properties.push(propOrInexact);
      }
    }

    flowObjectTypeSemicolon(parser);
  }

  parser.expectClosing(openContext);
  parser.popScope('TYPE');

  return {
    loc: parser.finishLoc(start),
    type: 'FlowObjectTypeAnnotation',
    properties,
    exact,
    inexact,
  };
}

function parseFlowObjectTypeProperty(
  parser: Parser,
  opts: {
    start: Position;
    protoStart: undefined | Position;
    isStatic: boolean;
    variance: undefined | n.FlowVarianceNode;
    kind: n.FlowObjectTypePropertyKind;
    allowSpread: boolean;
    allowInexact: boolean;
  },
):
  | undefined
  | n.FlowObjectTypePropertyNode
  | n.FlowObjectTypeSpreadPropertyNode {
  const {
    start,
    isStatic,
    protoStart,
    variance,
    kind,
    allowSpread,
    allowInexact,
  } = opts;

  if (parser.match(tt.ellipsis)) {
    if (!allowSpread) {
      parser.addDiagnostic({
        message:
          'Spread operator cannot appear in class or interface definitions',
      });
    }

    if (protoStart !== undefined) {
      parser.unexpectedToken(protoStart);
    }

    if (variance) {
      parser.addDiagnostic({
        loc: variance.loc,
        message: 'Spread properties cannot have variance',
      });
    }

    parser.expect(tt.ellipsis);

    const isInexactToken = parser.eat(tt.comma) || parser.eat(tt.semi);

    if (parser.match(tt.braceR)) {
      if (allowInexact) {
        return undefined;
      }

      parser.addDiagnostic({
        message:
          'Explicit inexact syntax is only allowed inside inexact objects',
      });
    }

    if (parser.match(tt.braceBarR)) {
      parser.addDiagnostic({
        message:
          'Explicit inexact syntax cannot appear inside an explicit exact object type',
      });
    }

    if (isInexactToken) {
      parser.addDiagnostic({
        message:
          'Explicit inexact syntax must appear at the end of an inexact object',
      });
    }

    const argument = parseFlowType(parser);
    return {
      loc: parser.finishLoc(start),
      type: 'FlowObjectTypeSpreadProperty',
      argument,
    };
  } else {
    const proto = protoStart !== undefined;
    const key = parseFlowObjectPropertyKey(parser);
    let value = undefined;
    let optional = false;

    if (parser.isRelational('<') || parser.match(tt.parenL)) {
      if (protoStart !== undefined) {
        parser.unexpectedToken(protoStart);
      }

      if (variance) {
        parser.addDiagnostic({
          loc: variance.loc,
          message: "Type methods can't have variance",
        });
      }

      value = parseFlowObjectTypeMethodish(parser, start);

      if (kind === 'get' || kind === 'set') {
        checkGetterSetterParamCount(parser, value, kind);
      }
    } else {
      if (kind !== 'init') {
        parser.unexpectedToken();
      }

      if (parser.eat(tt.question)) {
        optional = true;
      }
      value = parseFlowTypeInitialiser(parser);
    }

    return {
      loc: parser.finishLoc(start),
      type: 'FlowObjectTypeProperty',
      key,
      static: isStatic,
      kind,
      value,
      variance,
      optional,
      proto,
    };
  }
}

function flowObjectTypeSemicolon(parser: Parser): void {
  if (
    !parser.eat(tt.semi) &&
    !parser.eat(tt.comma) &&
    !parser.match(tt.braceR) &&
    !parser.match(tt.braceBarR)
  ) {
    parser.unexpectedToken();
  }
}

function parseFlowQualifiedTypeIdentifier(
  parser: Parser,
  start: Position = parser.getPosition(),
  id?: n.IdentifierNode,
): n.IdentifierNode | n.FlowQualifiedTypeIdentifierNode {
  let node: n.IdentifierNode | n.FlowQualifiedTypeIdentifierNode =
    id === undefined ? parseIdentifier(parser) : id;

  while (parser.eat(tt.dot)) {
    const id = parseIdentifier(parser);
    node = {
      loc: parser.finishLoc(start),
      type: 'FlowQualifiedTypeIdentifier',
      id,
      qualification: node,
    };
  }

  return node;
}

function parseFlowGenericType(
  parser: Parser,
  start: Position,
  _id: n.IdentifierNode,
): n.FlowGenericTypeAnnotationNode {
  let typeParameters = undefined;
  const id = parseFlowQualifiedTypeIdentifier(parser, start, _id);

  if (parser.isRelational('<')) {
    typeParameters = parseFlowTypeParameterInstantiation(parser);
  }

  return {
    loc: parser.finishLoc(start),
    type: 'FlowGenericTypeAnnotation',
    id,
    typeParameters,
  };
}

function parseFlowTypeofType(parser: Parser): n.FlowTypeofTypeAnnotationNode {
  const start = parser.getPosition();
  parser.expect(tt._typeof);
  const argument = parseFlowPrimaryType(parser);
  return {
    loc: parser.finishLoc(start),
    type: 'FlowTypeofTypeAnnotation',
    argument,
  };
}

function parseFlowTupleType(parser: Parser): n.FlowTupleTypeAnnotationNode {
  const start = parser.getPosition();
  const types = [];
  const openContext = parser.expectOpening(
    tt.bracketL,
    tt.bracketR,
    'flow tuple type',
  );
  // We allow trailing commas
  while (parser.state.index < parser.length && !parser.match(tt.bracketR)) {
    types.push(parseFlowType(parser));
    if (parser.match(tt.bracketR)) {
      break;
    }

    if (!parser.expect(tt.comma)) {
      break;
    }
  }
  parser.expectClosing(openContext);
  return {
    loc: parser.finishLoc(start),
    type: 'FlowTupleTypeAnnotation',
    types,
  };
}

function parseFlowFunctionTypeParam(
  parser: Parser,
): n.FlowFunctionTypeParamNode {
  let name = undefined;
  let optional = false;
  let typeAnnotation = undefined;
  const start = parser.getPosition();
  const lh = parser.lookaheadState();
  if (lh.tokenType === tt.colon || lh.tokenType === tt.question) {
    name = parseIdentifier(parser);
    if (parser.eat(tt.question)) {
      optional = true;
    }
    typeAnnotation = parseFlowTypeInitialiser(parser);
  } else {
    typeAnnotation = parseFlowType(parser);
  }
  return {
    loc: parser.finishLoc(start),
    type: 'FlowFunctionTypeParam',
    name,
    optional,
    typeAnnotation,
  };
}

function reinterpretTypeAsFlowFunctionTypeParam(
  parser: Parser,
  type: n.FlowNode,
): n.FlowFunctionTypeParamNode {
  return {
    loc: parser.finishLoc(parser.getLoc(type).start),
    type: 'FlowFunctionTypeParam',
    name: undefined,
    optional: false,
    typeAnnotation: type,
  };
}

function parseFlowFunctionTypeParams(
  parser: Parser,
): Array<n.FlowFunctionTypeParamNode | n.FlowFunctionTypeParamRestNode> {
  const params: Array<
    n.FlowFunctionTypeParamNode | n.FlowFunctionTypeParamRestNode
  > = [];

  while (!parser.match(tt.parenR) && !parser.match(tt.ellipsis)) {
    params.push(parseFlowFunctionTypeParam(parser));

    if (!parser.match(tt.parenR) && !parser.expect(tt.comma)) {
      break;
    }
  }

  if (parser.eat(tt.ellipsis)) {
    const param = parseFlowFunctionTypeParam(parser);
    params.push({
      type: 'FlowFunctionTypeParamRest',
      loc: param.loc,
      param,
    });
  }

  return params;
}

type FlowIdentTypeAnno =
  | n.FlowAnyTypeAnnotationNode
  | n.FlowVoidTypeAnnotationNode
  | n.FlowBooleanTypeAnnotationNode
  | n.FlowMixedTypeAnnotationNode
  | n.FlowEmptyTypeAnnotationNode
  | n.FlowNumberTypeAnnotationNode
  | n.FlowStringTypeAnnotationNode
  | n.FlowBigIntTypeAnnotationNode
  | n.FlowGenericTypeAnnotationNode;

function flowIdentToTypeAnnotation(
  parser: Parser,
  start: Position,
  id: n.IdentifierNode,
): FlowIdentTypeAnno {
  switch (id.name) {
    case 'any':
      return {
        loc: parser.finishLoc(start),
        type: 'FlowAnyTypeAnnotation',
      } as n.FlowAnyTypeAnnotationNode;

    case 'void':
      return {
        loc: parser.finishLoc(start),
        type: 'FlowVoidTypeAnnotation',
      } as n.FlowVoidTypeAnnotationNode;

    case 'bool':
    case 'boolean':
      return {
        loc: parser.finishLoc(start),
        type: 'FlowBooleanTypeAnnotation',
      } as n.FlowBooleanTypeAnnotationNode;

    case 'mixed':
      return {
        loc: parser.finishLoc(start),
        type: 'FlowMixedTypeAnnotation',
      } as n.FlowMixedTypeAnnotationNode;

    case 'empty':
      return {
        loc: parser.finishLoc(start),
        type: 'FlowEmptyTypeAnnotation',
      } as n.FlowEmptyTypeAnnotationNode;

    case 'number':
      return {
        loc: parser.finishLoc(start),
        type: 'FlowNumberTypeAnnotation',
      } as n.FlowNumberTypeAnnotationNode;

    case 'string':
      return {
        loc: parser.finishLoc(start),
        type: 'FlowStringTypeAnnotation',
      } as n.FlowStringTypeAnnotationNode;

    case 'bigint':
      return {
        loc: parser.finishLoc(start),
        type: 'FlowBigIntTypeAnnotation',
      } as n.FlowBigIntTypeAnnotationNode;

    default:
      checkNotUnderscore(parser, id);
      return parseFlowGenericType(parser, start, id);
  }
}

type FlowPrimaryType =
  | n.FlowFunctionTypeAnnotationNode
  | n.FlowTupleTypeAnnotationNode
  | n.FlowObjectTypeAnnotationNode
  | n.FlowTypeofTypeAnnotationNode
  | n.FlowIntersectionTypeAnnotationNode
  | n.FlowInterfaceTypeAnnotationNode
  | n.FlowUnionTypeAnnotationNode
  | n.FlowNullableTypeAnnotationNode
  | n.FlowIntersectionTypeAnnotationNode
  | n.FlowUnionTypeAnnotationNode
  | n.FlowTypeAnnotationNode
  | n.FlowArrayTypeAnnotationNode
  | n.UnknownNode
  | n.FlowIntersectionTypeAnnotationNode
  | n.FlowNullableTypeAnnotationNode
  | n.FlowTypeAnnotationNode
  | n.FlowUnionTypeAnnotationNode
  | n.FlowStringLiteralTypeAnnotationNode
  | n.FlowBooleanLiteralTypeAnnotationNode
  | n.FlowNumericLiteralTypeAnnotationNode
  | n.FlowNullLiteralTypeAnnotationNode
  | n.FlowThisTypeAnnotationNode
  | n.FlowExistsTypeAnnotationNode
  | n.IdentifierNode
  | FlowIdentTypeAnno;

// The parsing of types roughly parallels the parsing of expressions, and
// primary types are kind of like primary expressions...they're the
// primitives with which other types are constructed.
function parseFlowPrimaryType(parser: Parser): FlowPrimaryType {
  const start = parser.getPosition();
  let type;
  let isGroupedType = false;
  const oldNoAnonFunctionType = parser.state.noAnonFunctionType;

  switch (parser.state.tokenType) {
    case tt.name:
      if (parser.isContextual('interface')) {
        return parseFlowInterfaceType(parser);
      }

      return flowIdentToTypeAnnotation(parser, start, parseIdentifier(parser));

    case tt.braceL:
      return parseFlowObjectType(parser, {
        allowStatic: false,
        allowExact: false,
        allowSpread: true,
        allowProto: false,
        allowInexact: true,
      });

    case tt.braceBarL:
      return parseFlowObjectType(parser, {
        allowStatic: false,
        allowExact: true,
        allowSpread: true,
        allowProto: false,
        allowInexact: false,
      });

    case tt.bracketL:
      return parseFlowTupleType(parser);

    case tt.relational:
      if (parser.state.tokenValue === '<') {
        const typeParameters = parseFlowTypeParameterDeclaration(parser, false);
        const openContext = parser.expectOpening(
          tt.parenL,
          tt.parenR,
          'flow function params',
        );
        const params = parseFlowFunctionTypeParams(parser);
        parser.expectClosing(openContext);

        parser.expect(tt.arrow);

        const returnType = parseFlowType(parser);

        return {
          loc: parser.finishLoc(start),
          type: 'FlowFunctionTypeAnnotation',
          typeParameters,
          params,
          returnType,
        };
      }
      break;

    case tt.parenL: {
      const openContext = parser.expectOpening(
        tt.parenL,
        tt.parenR,
        'flow function params',
      );

      // Check to see if this is actually a grouped type
      if (!parser.match(tt.parenR) && !parser.match(tt.ellipsis)) {
        if (parser.match(tt.name)) {
          const token = parser.lookaheadState().tokenType;
          isGroupedType = token !== tt.question && token !== tt.colon;
        } else {
          isGroupedType = true;
        }
      }

      if (isGroupedType) {
        parser.state.noAnonFunctionType = false;
        type = parseFlowType(parser);
        parser.state.noAnonFunctionType = oldNoAnonFunctionType;

        // A `,` or a `) =>` means this is an anonymous function type
        if (
          parser.state.noAnonFunctionType ||
          !(
            parser.match(tt.comma) ||
            (parser.match(tt.parenR) &&
              parser.lookaheadState().tokenType === tt.arrow)
          )
        ) {
          parser.expectClosing(openContext);
          return type;
        } else {
          // Eat a comma if there is one
          parser.eat(tt.comma);
        }
      }

      let params;
      if (type) {
        const firstParam = reinterpretTypeAsFlowFunctionTypeParam(parser, type);
        params = parseFlowFunctionTypeParams(parser);
        params = [firstParam, ...params];
      } else {
        params = parseFlowFunctionTypeParams(parser);
      }

      parser.expectClosing(openContext);
      parser.expect(tt.arrow);

      const returnType = parseFlowType(parser);

      return {
        loc: parser.finishLoc(start),
        type: 'FlowFunctionTypeAnnotation',
        typeParameters: undefined,
        params,
        returnType,
      };
    }

    case tt.string: {
      const value = parser.state.tokenValue;
      parser.next();
      return {
        loc: parser.finishLoc(start),
        type: 'FlowStringLiteralTypeAnnotation',
        value,
      } as n.FlowStringLiteralTypeAnnotationNode;
    }

    case tt._true:
    case tt._false: {
      const value = parser.match(tt._true);
      parser.next();
      return {
        loc: parser.finishLoc(start),
        type: 'FlowBooleanLiteralTypeAnnotation',
        value,
      } as n.FlowBooleanLiteralTypeAnnotationNode;
    }

    case tt.plusMin: {
      if (parser.state.tokenValue === '-') {
        parser.next();
        if (!parser.match(tt.num)) {
          parser.addDiagnostic({
            message: `Unexpected token, expected "number"`,
          });
          parser.next();
          return {
            loc: parser.finishLoc(start),
            type: 'FlowNumericLiteralTypeAnnotation',
            value: NaN,
          } as n.FlowNumericLiteralTypeAnnotationNode;
        }

        const value = Number(parser.state.tokenValue);
        parser.next();
        return {
          loc: parser.finishLoc(start),
          type: 'FlowNumericLiteralTypeAnnotation',
          value: -value,
        } as n.FlowNumericLiteralTypeAnnotationNode;
      }
      break;
    }

    case tt.num: {
      const value = Number(parser.state.tokenValue);
      parser.next();
      return {
        loc: parser.finishLoc(start),
        type: 'FlowNumericLiteralTypeAnnotation',
        value,
      } as n.FlowNumericLiteralTypeAnnotationNode;
    }

    case tt._null:
      parser.next();
      return {
        type: 'FlowNullLiteralTypeAnnotation',
        loc: parser.finishLoc(start),
      } as n.FlowNullLiteralTypeAnnotationNode;

    case tt._this:
      parser.next();
      return {
        type: 'FlowThisTypeAnnotation',
        loc: parser.finishLoc(start),
      } as n.FlowThisTypeAnnotationNode;

    case tt.star:
      parser.next();
      return {
        type: 'FlowExistsTypeAnnotation',
        loc: parser.finishLoc(start),
      } as n.FlowExistsTypeAnnotationNode;

    default:
      if (parser.state.tokenType.keyword === 'typeof') {
        return parseFlowTypeofType(parser);
      } else if (parser.state.tokenType.keyword !== undefined) {
        const label = parser.state.tokenType.label;
        parser.next();
        return createIdentifier(parser, start, label);
      }
  }

  parser.addDiagnostic({
    message: 'Unknown flow primarty type start',
  });
  return parser.createUnknownNode('flow primary type', start);
}

function parseFlowPostfixType(parser: Parser): FlowPrimaryType {
  const startPos = parser.state.startPos;
  let type = parseFlowPrimaryType(parser);
  while (!parser.canInsertSemicolon() && parser.match(tt.bracketL)) {
    const elementType = type;
    parser.expect(tt.bracketL);
    parser.expect(tt.bracketR);
    type = {
      loc: parser.finishLoc(startPos),
      type: 'FlowArrayTypeAnnotation',
      elementType,
    } as n.FlowArrayTypeAnnotationNode;
  }
  return type;
}

function parseFlowPrefixType(parser: Parser): FlowPrimaryType {
  const start = parser.getPosition();
  if (parser.eat(tt.question)) {
    return {
      loc: parser.finishLoc(start),
      type: 'FlowNullableTypeAnnotation',
      typeAnnotation: parseFlowPrefixType(parser),
    };
  } else {
    return parseFlowPostfixType(parser);
  }
}

function parseFlowAnonFunctionWithoutParens(parser: Parser): FlowPrimaryType {
  const param = parseFlowPrefixType(parser);

  if (!parser.state.noAnonFunctionType && parser.eat(tt.arrow)) {
    const start = parser.getLoc(param).start;
    const params = [reinterpretTypeAsFlowFunctionTypeParam(parser, param)];
    const rest = undefined;
    const returnType = parseFlowType(parser);
    const typeParameters = undefined;
    return {
      loc: parser.finishLoc(start),
      type: 'FlowFunctionTypeAnnotation',
      params,
      rest,
      returnType,
      typeParameters,
    } as n.FlowFunctionTypeAnnotationNode;
  }

  return param;
}

function parseFlowIntersectionType(parser: Parser): FlowPrimaryType {
  const start = parser.getPosition();
  parser.eat(tt.bitwiseAND);

  const type = parseFlowAnonFunctionWithoutParens(parser);
  const types = [type];
  while (parser.eat(tt.bitwiseAND)) {
    types.push(parseFlowAnonFunctionWithoutParens(parser));
  }

  if (types.length === 1) {
    return type;
  } else {
    return {
      loc: parser.finishLoc(start),
      type: 'FlowIntersectionTypeAnnotation',
      types,
    };
  }
}

function parseFlowUnionType(parser: Parser): FlowPrimaryType {
  const start = parser.getPosition();
  parser.eat(tt.bitwiseOR);

  const type = parseFlowIntersectionType(parser);
  const types = [type];

  while (parser.eat(tt.bitwiseOR)) {
    types.push(parseFlowIntersectionType(parser));
  }

  if (parser.match(tt.logicalOR)) {
    parser.addDiagnostic({
      message: 'Unexpected ||, did you mean just |?',
    });
  }

  if (types.length === 1) {
    return type;
  } else {
    return {
      loc: parser.finishLoc(start),
      type: 'FlowUnionTypeAnnotation',
      types,
    } as n.FlowUnionTypeAnnotationNode;
  }
}

function parseFlowType(parser: Parser): FlowPrimaryType {
  parser.pushScope('TYPE', true);
  const type = parseFlowUnionType(parser);
  parser.popScope('TYPE');
  // Ensure that a brace after a function generic type annotation is a
  // statement, except in arrow functions (noAnonFunctionType)
  parser.state.exprAllowed =
    parser.state.exprAllowed || parser.state.noAnonFunctionType;
  return type;
}

export function parseFlowTypeAnnotation(
  parser: Parser,
): n.FlowTypeAnnotationNode {
  parser.expectSyntaxEnabled('flow');

  const start = parser.getPosition();
  const typeAnnotation = parseFlowTypeInitialiser(parser);
  return {
    loc: parser.finishLoc(start),
    type: 'FlowTypeAnnotation',
    typeAnnotation,
  };
}

function parseFlowTypeAnnotatableIdentifier(
  parser: Parser,
  allowPrimitiveOverride: boolean = false,
): n.IdentifierNode {
  const start = parser.getPosition();
  const ident = allowPrimitiveOverride
    ? parseIdentifier(parser)
    : parseFlowRestrictedIdentifier(parser);

  let typeAnnotation = undefined;
  if (parser.match(tt.colon)) {
    typeAnnotation = parseFlowTypeAnnotation(parser);
  }

  if (typeAnnotation === undefined) {
    return ident;
  } else {
    return {
      ...ident,
      loc: parser.finishLoc(start),
      typeAnnotation,
    };
  }
}

export function parseFlowClassImplemented(
  parser: Parser,
): Array<n.FlowClassImplementsNode> {
  const implemented: Array<n.FlowClassImplementsNode> = [];

  do {
    const start = parser.getPosition();
    const id = parseFlowRestrictedIdentifier(parser, /*liberal*/ true);

    let typeParameters;
    if (parser.isRelational('<')) {
      typeParameters = parseFlowTypeParameterInstantiation(parser);
    }

    implemented.push({
      loc: parser.finishLoc(start),
      type: 'FlowClassImplements',
      id,
      typeParameters,
    });
  } while (parser.eat(tt.comma));

  return implemented;
}

export function parseFlowVariance(
  parser: Parser,
): undefined | n.FlowVarianceNode {
  if (parser.match(tt.plusMin)) {
    parser.expectSyntaxEnabled('flow');
    const start = parser.getPosition();
    let kind: undefined | n.FlowVarianceKind;
    if (parser.state.tokenValue === '+') {
      kind = 'plus';
    } else {
      kind = 'minus';
    }
    parser.next();
    return {
      loc: parser.finishLoc(start),
      type: 'FlowVariance',
      kind,
    };
  }
}

export function parseAsyncArrowWithFlowTypeParameters(
  parser: Parser,
  startPos: Position,
): undefined | n.ArrowFunctionExpressionNode {
  const {params, typeParameters} = parseFunctionParams(parser);

  const {returnType, valid, predicate} = parseArrowHead(parser);
  if (!valid) {
    parser.addDiagnostic({
      message: 'Invalid async arrow with type parameters',
    });
    return undefined;
  }

  return {
    ...parseArrowExpression(parser, startPos, params, /* isAsync */ true),
    returnType,
    predicate,
    typeParameters,
  };
}

export function parseFlowObjectTypeInternalSlot(
  parser: Parser,
  start: Position,
  isStatic: boolean,
): n.FlowObjectTypeInternalSlotNode {
  // Note: both bracketL have already been consumed
  const id = parseFlowObjectPropertyKey(parser);
  parser.expect(tt.bracketR);
  parser.expect(tt.bracketR);

  let optional = false;
  let value;
  if (parser.isRelational('<') || parser.match(tt.parenL)) {
    value = parseFlowObjectTypeMethodish(parser, start);
  } else {
    optional = parser.eat(tt.question);
    value = parseFlowTypeInitialiser(parser);
  }

  return {
    type: 'FlowObjectTypeInternalSlot',
    optional,
    value,
    id,
    loc: parser.finishLoc(start),
  };
}
