import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function NewExpression(generator: Generator, node: t.NewExpressionNode) {
  generator.word('new');
  generator.space();
  generator.print(node.callee, node);
  generator.print(node.typeArguments, node);
  generator.token('(');
  generator.printCommaList(node.arguments, node);
  generator.token(')');
}
