import {JSNodeBase, PatternBase} from '../index';
import {createBuilder} from '../utils';

export type IdentifierNode = JSNodeBase &
  PatternBase & {
    type: 'Identifier';
    name: string;
    definite?: undefined | boolean;
  };

export const identifier = createBuilder<IdentifierNode>('Identifier', {
  visitorKeys: ['typeAnnotation'],
});
