import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {isStatement} from '@romejs/js-ast-utils';

export function ExportNamedDeclaration(
  generator: Generator,
  node: t.ExportNamedDeclarationNode,
) {
  generator.word('export');
  generator.space();
  _ExportDeclaration(generator, node);
}

export function _ExportDeclaration(
  generator: Generator,
  node: t.ExportNamedDeclarationNode | t.ExportDefaultDeclarationNode,
) {
  if (node.declaration) {
    const declar = node.declaration;
    generator.print(declar, node);
    if (!isStatement(declar)) {
      generator.semicolon();
    }
  } else {
    if (node.type !== 'ExportNamedDeclaration') {
      throw new Error('Expected ExportNamedDeclaration');
    }

    if (node.exportKind === 'type') {
      generator.word('type');
      generator.space();
    }

    if (node.specifiers === undefined) {
      throw new Error('Expected specifiers since there was no declaration');
    }

    const specifiers = node.specifiers.slice(0);

    // print "special" specifiers first
    let hasSpecial = false;
    while (true) {
      const first = specifiers[0];
      if (
        first !== undefined &&
        (first.type === 'ExportDefaultSpecifier' ||
          first.type === 'ExportNamespaceSpecifier')
      ) {
        hasSpecial = true;
        generator.print(specifiers.shift(), node);
        if (specifiers.length) {
          generator.token(',');
          generator.space();
        }
      } else {
        break;
      }
    }

    if (specifiers.length || (!specifiers.length && !hasSpecial)) {
      generator.token('{');
      if (specifiers.length) {
        generator.printCommaList(specifiers, node);
      }
      generator.token('}');
    }

    if (node.source) {
      generator.space();
      generator.word('from');
      generator.space();
      generator.print(node.source, node);
    }

    generator.semicolon();
  }
}
