import {JSNodeBase, TSTypeElementNode} from '../index';
import {createBuilder} from '../utils';

export type TSTypeLiteralNode = JSNodeBase & {
  type: 'TSTypeLiteral';
  members: Array<TSTypeElementNode>;
};

export const tsTypeLiteral = createBuilder<TSTypeLiteralNode>('TSTypeLiteral', {
  visitorKeys: ['members'],
});
