import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowTypeParameter(
  generator: Generator,
  node: t.FlowTypeParameterNode,
) {
  generator.print(node.variance, node);
  generator.word(node.name);

  if (node.bound) {
    generator.print(node.bound, node);
  }

  if (node.default) {
    generator.space();
    generator.token('=');
    generator.space();
    generator.print(node.default, node);
  }
}
