import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';

export function FlowDeclareFunction(
  node: n.FlowDeclareFunctionNode,
  scope: Scope,
) {
  return scope.addBinding(node.id.name, scope.evaluate(node.id.typeAnnotation));
}
