import {Node} from '@romejs/js-ast';
import {HydrateData} from '../Evaluator';
import {Scope} from '../scopes';
import T from './T';

export default class BooleanLiteralT extends T {
  constructor(scope: Scope, originNode: undefined | Node, value: boolean) {
    super(scope, originNode);
    this.value = value;
  }

  static type = 'BooleanLiteralT';

  value: boolean;

  serialize(): HydrateData {
    return {value: this.value};
  }

  static hydrate(scope: Scope, originNode: Node, data: HydrateData): T {
    return new BooleanLiteralT(scope, originNode, Boolean(data.value));
  }

  humanize(): string {
    if (this.value === true) {
      return 'true';
    } else {
      return 'false';
    }
  }

  compatibleWith(type: T): boolean {
    return type instanceof BooleanLiteralT && type.value === this.value;
  }
}
