import {
  MasterBridge,
  MasterQueryResponse,
  MasterQueryRequest,
} from '@romejs/core';
import {Diagnostics, PartialDiagnostics} from '@romejs/diagnostics';
import {Stats} from './MemoryFileSystem';
import {MasterCommand} from '../commands';
import {DiagnosticsPrinter} from '@romejs/cli-diagnostics';
import {consumeMixed} from '@romejs/codec-json';
import logsCommand from './commands/logs';
import {Event, EventSubscription} from '@romejs/events';
import {ReporterCollection} from '@romejs/cli-reporter';
import MasterRequest from './MasterRequest';
import {
  getDiagnosticsFromError,
  deriveDiagnosticFromError,
} from '@romejs/diagnostics';
import {JSON_END, JSON_START} from '@romejs/cli-reporter';
import {DiagnosticsProcessor} from '@romejs/diagnostics';
import ProjectManager from './ProjectManager';
import WorkerManager from './WorkerManager';
import Resolver from './Resolver';
import FileManager from './FileManager';
import Logger from '../common/utils/Logger';
import MemoryFileSystem from './MemoryFileSystem';
import Cache from './Cache';
import MasterAPI from './MasterAPI';
import {masterCommands} from './commands/index';
import {Reporter} from '@romejs/cli-reporter';
import {Profiler} from '@romejs/v8';
import {stripAnsi} from '@romejs/string-ansi';
import stream = require('stream');
import fs = require('fs');

const STDOUT_MAX_CHUNK_LENGTH = 100000;

export default class Master {
  constructor(dedicated: boolean) {
    this.profiling = false;
    this.fileChangeEvent = new Event({name: 'Master.fileChange'});
    this.dedicated = dedicated;

    // Logging
    this.logEvent = new Event({name: 'Master.log'});
    this.stdout = new stream.Writable({
      write: (
        chunk: string | Buffer,
        encoding: string,
        callback: () => void,
      ) => {
        this.logEvent.send(chunk.toString());
        callback();
      },
    });
    this.logger = new Logger('master', this.logEvent, {
      stderr: this.stdout,
      stdout: this.stdout,
    });
    this.connectedReporters = new ReporterCollection({});

    // Internal components
    this.memoryFs = new MemoryFileSystem(this);
    this.projectManager = new ProjectManager(this);
    this.workerManager = new WorkerManager(this);
    this.fileManager = new FileManager(this);
    this.resolver = new Resolver(this);
    this.cache = new Cache(this);
    this.api = new MasterAPI(this);

    this.memoryFs.deletedFileEvent.subscribe(({filename}) => {
      this.handleFileDelete(filename).catch(this.onFatalError);
    });

    this.memoryFs.changedFileEvent.subscribe(
      ({filename, oldStats, newStats}) => {
        this.handleFileChange(filename, oldStats, newStats).catch(
          this.onFatalError,
        );
      },
    );

    this.warnedCacheClients = new WeakSet();
  }

  fileChangeEvent: Event<string, void>;
  logEvent: Event<string, void>;

  profiling: boolean;
  dedicated: boolean;

  warnedCacheClients: WeakSet<MasterBridge>;
  memoryFs: MemoryFileSystem;
  resolver: Resolver;
  projectManager: ProjectManager;
  workerManager: WorkerManager;
  fileManager: FileManager;
  api: MasterAPI;
  cache: Cache;
  connectedReporters: ReporterCollection;
  stdout: stream.Writable;
  logger: Logger;

  onFatalError = (err: Error) => {
    const message = `Fatal error occured: ${err.stack}`;
    this.logger.info(message);
    this.connectedReporters.info(message);
    process.exit();
  };

  handleDisconnectedDiagnostics(diagnostics: PartialDiagnostics) {
    // TODO
    diagnostics;
  }

  createDisconnectedDiagnosticsProcessor(
    origins: Array<string>,
  ): DiagnosticsProcessor {
    return new DiagnosticsProcessor({
      onDiagnostics: (diags: PartialDiagnostics) => {
        this.handleDisconnectedDiagnostics(diags);
      },
      origins: [
        ...origins,
        'master: Created disconnected diagnostics collector',
      ],
    });
  }

  async init() {
    await this.workerManager.init();
  }

  end() {
    if (this.dedicated) {
      process.exit();
      return undefined;
    }

    // Otherwise this means we're running in another process, so we should clean up after ourselves
    // We should remove everything that has an external dependency like a socket or process

    this.workerManager.end();
    this.memoryFs.unwatchAll();
  }

  async attachToBridge(client: MasterBridge) {
    let profiler: undefined | Profiler;

    // If we aren't a dedicated process then we should only expect a single connection
    // and when that ends. End the Master.
    if (this.dedicated === false) {
      client.endEvent.subscribe(() => {
        this.end();
      });
    }

    client.profilingStart.subscribe(async () => {
      if (profiler !== undefined) {
        throw new Error('Expected no profiler to be running');
      }
      profiler = new Profiler();
      await profiler.startProfiling();
      this.profiling = true;
      for (const {bridge} of this.workerManager.getExternalWorkers()) {
        await bridge.profilingStart.call();
      }
    });

    client.profilingStop.subscribe(async () => {
      if (profiler === undefined) {
        throw new Error('Expected profiler to be running');
      }
      const masterProfile = await profiler.stopProfiling();
      profiler = undefined;
      this.profiling = false;

      const workerProfiles = [];
      for (const {bridge} of this.workerManager.getExternalWorkers()) {
        workerProfiles.push(await bridge.profilingStop.call());
      }

      return {
        master: masterProfile,
        workers: workerProfiles,
      };
    });

    client.query.subscribe(async request => {
      return await this.handleRequest(client, request);
    });

    client.endEvent.subscribe(() => {
      // TODO abort all running queries and clean up
    });

    await client.handshake();
  }

  async shouldIgnoreChange(loc: string): Promise<boolean> {
    // ignore .cache - babel-register will write to it regularly and it's very noisy
    if (loc.includes('node_modules/.cache')) {
      return true;
    }

    return false;
  }

  async handleFileDelete(loc: string) {
    if (await this.shouldIgnoreChange(loc)) {
      return undefined;
    }

    this.logger.info(`[Master] File delete:`, loc);

    // file no longer exists so disown it
    await this.fileManager.disown(loc);

    //
    this.fileChangeEvent.send(loc);
  }

  async handleFileChange(
    loc: string,
    oldStats: undefined | Stats,
    newStats: Stats,
  ) {
    if (await this.shouldIgnoreChange(loc)) {
      return undefined;
    }

    this.logger.info(`[Master] File change:`, loc);

    // Notify the worker manager
    await this.fileManager.onFileChange(loc, oldStats, newStats);

    //
    this.fileChangeEvent.send(loc);
  }

  async exists(loc: string): Promise<boolean> {
    const resolvedExistence: undefined | boolean = this.memoryFs.exists(loc);
    if (resolvedExistence === undefined) {
      try {
        await fs.promises.access(loc);
        return true;
      } catch (err) {
        return false;
      }
    } else {
      return resolvedExistence;
    }
  }

  async handleRequest(
    client: MasterBridge,
    query: MasterQueryRequest,
  ): Promise<MasterQueryResponse> {
    const {flags} = query;
    this.logger.info(`[Master] Handling CLI request:`, query);

    let req: undefined | MasterRequest;

    // Create a promise for the client dying so we can race it later
    let clientEndEvent: undefined | EventSubscription;
    const clientEndReject = new Promise((resolve, reject) => {
      clientEndEvent = client.endEvent.subscribe(err => {
        reject(err);
      });
    });
    if (clientEndEvent === undefined) {
      throw new Error('Expected clientEndEvent to have been initialized');
    }

    try {
      // Create reporter
      const stderr = new stream.Writable({
        write: (chunk, encoding, callback) => {
          client.stderr.send(chunk.toString());
          callback();
        },
      });

      // Create writable stream that sends data to the listening cli
      const stdout = new stream.Writable({
        write: (
          chunk: string | Buffer,
          encoding: string,
          callback: () => void,
        ) => {
          if (flags.silent === true) {
            callback();
            return undefined;
          }

          let str = chunk.toString();

          if (flags.color === false) {
            str = stripAnsi(str);
          }

          if (str.length < STDOUT_MAX_CHUNK_LENGTH) {
            client.stdout.send(str);
          } else {
            while (str.length > 0) {
              const chunk = str.slice(0, STDOUT_MAX_CHUNK_LENGTH);
              str = str.slice(STDOUT_MAX_CHUNK_LENGTH);
              client.stdout.send(chunk);
            }
          }

          callback();
        },
      });

      function setColumns(columns: number) {
        // @ts-ignore: just stubbing this in
        stderr.columns = columns;

        // @ts-ignore: just stubbing this in
        stdout.columns = columns;
      }

      setColumns(query.columns);

      client.setColumns.subscribe(columns => {
        setColumns(columns);
      });

      // Initialize the reporter
      const reporter = new Reporter({
        isTTY: true,
        stderr,
        stdout,
        verbose: query.flags.verbose,
        silent: query.flags.silent,
        json: query.flags.json,
        markupOptions: {
          cwd: flags.cwd,
          normalizeFilename: filename => {
            const uid = this.projectManager.getFilenameFromUid(filename);
            if (uid === undefined) {
              return filename;
            } else {
              return uid;
            }
          },
        },
      });

      // Output json start message
      reporter.json(JSON_START);

      // Inherit hasClearScreen from the rome-cli session
      reporter.hasClearScreen = query.hasClearScreen;

      // Add reporter to connected set, important logs may be output to these
      this.connectedReporters.addReporter(reporter);

      // Create master request wrapper which is just a bucket of objects
      req = new MasterRequest({
        command: query.command,
        master: this,
        reporter,
        args: query.args,
        client,
        flags,
      });

      // A type-safe wrapper for retreiving command flags
      const commandFlagsConsumer = consumeMixed(query.commandFlags);

      // An array of promises that we'll race, the only promise that will ever resolve will be the command one
      let promises: Array<Promise<unknown> | undefined> = [clientEndReject];

      // All these flags indicate a request for logs
      if (flags.logs || flags.logPath !== '' || flags.logWorkers) {
        promises.push(logsCommand.default(req, commandFlagsConsumer));
      }

      // Get command
      const commandOpts: undefined | MasterCommand = masterCommands.get(
        query.command,
      );
      if (commandOpts) {
        // Output header if command specified
        if (commandOpts.hasHeader === true) {
          reporter.header(query.command);
        }

        // Warn about disabled disk caching
        if (
          process.env.ROME_CACHE === '0' &&
          !this.warnedCacheClients.has(client)
        ) {
          reporter.warn(
            'Disk caching has been disabled by the <emphasis>ROME_CACHE</emphasis> environment variable',
          );
          this.warnedCacheClients.add(client);
        }

        const commandPromise = commandOpts.default(req, commandFlagsConsumer);
        promises.push(commandPromise);

        await Promise.race(promises);

        // Only the command promise should have won the race with a resolve
        const data = await commandPromise;
        if (query.noData) {
          return {};
        } else {
          return {data};
        }
      } else {
        throw new Error(`Unknown command ${String(query.command)}`);
      }
    } catch (err) {
      let diagnostics: undefined | Diagnostics;
      if (req !== undefined) {
        diagnostics = await this.handleRequestError(req, err);
      }

      if (diagnostics === undefined) {
        return {
          error: {
            handled: true,
            name: err.name,
            message: err.message,
            stack: err.stack,
          },
        };
      } else {
        return {diagnostics};
      }
    } finally {
      this.logger.info(`[Master] Replying to CLI request:`, query);

      // We no longer care if the client dies
      clientEndEvent.unsubscribe();

      if (req !== undefined) {
        // Output json end message
        req.reporter.json(JSON_END);

        // Teardown the reporter
        this.connectedReporters.deleteReporter(req.reporter);
        req.teardown();
      }
    }
  }

  async handleRequestError(
    req: MasterRequest,
    err: Error,
  ): Promise<undefined | Diagnostics> {
    // If we can derive diagnostics from the error then create a diagnostics printer
    const diagnostics = getDiagnosticsFromError(err);
    if (diagnostics !== undefined) {
      const printer = req.createDiagnosticsPrinter(
        'Derived diagnostics from thrown error',
      );
      printer.addDiagnostics(diagnostics);
      err = printer;
    }

    // Print it!
    if (err instanceof DiagnosticsPrinter) {
      const printer = err;
      await printer.print();
      printer.footer();
      return printer.getDiagnostics();
    }

    const printer = req.createDiagnosticsPrinter(
      'Error captured and converted into a diagnostic',
    );
    printer.addDiagnostic(
      deriveDiagnosticFromError({
        error: err,
        lastAdvice: [
          {
            type: 'log',
            category: 'info',
            message: 'This diagnostic was derived from an internal error',
          },
        ],
      }),
    );
    await printer.print();
    // We could probably return printer.getDiagnostics() but we just want to print to the console
    // We will still want to send the `error` property
    return undefined;
  }
}
