import {Consumer} from '@romejs/codec-json';
import {SemverVersionNode} from '@romejs/codec-semver';
import {SPDXExpressionNode} from '@romejs/codec-spdx-license';
import {normalizeDependencies} from './dependencies';
import * as spdx from '@romejs/codec-spdx-license';
import * as semver from '@romejs/codec-semver';
import {
  MString,
  MStringArray,
  MBoolean,
  ManifestMap,
  Manifest,
  NamedManifest,
  ManifestKind,
} from './types';

export * from './types';

export function getManifestKindFromBasename(
  filename: string,
): undefined | ManifestKind {
  if (filename === 'package.json') {
    return 'json';
  } else {
    return undefined;
  }
}

export function ifNamedManifest(manifest: Manifest): undefined | NamedManifest {
  const {name} = manifest;
  if (name !== undefined) {
    return {
      ...manifest,
      name,
    };
  } else {
    return undefined;
  }
}

function normalizeBoolean(consumer: Consumer, key: string): MBoolean {
  if (consumer.hasProperty(key)) {
    return consumer.getProperty(key).asBoolean();
  } else {
    return undefined;
  }
}

function normalizeString(consumer: Consumer, key: string): MString {
  if (consumer.hasProperty(key)) {
    return consumer.getProperty(key).asString();
  } else {
    return undefined;
  }
}

function normalizeStringArray(consumer: Consumer, key: string): MStringArray {
  const prop = consumer.getProperty(key);
  if (prop.exists()) {
    if (typeof prop.asMixed() === 'string') {
      return [prop.asString()];
    } else {
      return prop.asArray().map(item => item.asString());
    }
  } else {
    return undefined;
  }
}

function normalizeStringMap(consumer: Consumer, key: string): ManifestMap {
  const map: ManifestMap = new Map();

  if (!consumer.hasProperty(key)) {
    return map;
  }

  for (const [name, value] of consumer.getProperty(key).asMap()) {
    map.set(name, value.asString());
  }

  return map;
}

function normalizeBin(consumer: Consumer, name: MString): ManifestMap {
  const map: ManifestMap = new Map();
  if (!consumer.hasProperty('bin')) {
    return map;
  }

  // Allow a `bin` string
  const obj = consumer.getProperty('bin');
  if (typeof obj.asMixed() === 'string') {
    if (name === undefined) {
      obj.unexpected(
        'A string bin is only allowed if the manifest has a name property',
      );
    } else {
      map.set(name, obj.asString());
      return map;
    }
  }

  // Otherwise expect it to be an object
  return normalizeStringMap(consumer, 'bin');
}

function extractLicenseFromObjectConsumer(
  consumer: Consumer,
): [string, Consumer] {
  const prop = consumer.getProperty('type');
  const value = prop.asString();
  return [value, prop];
}

// These are all licenses I found that are wrong, we should eventually remove this as we update those deps
const INVALID_IGNORE_LICENSES = [
  'UNLICENSED',
  'none',
  'Facebook Platform License',
  'BSD',
  'MIT/X11',
  'Public Domain',
  'MIT License',
  'BSD-like',
];

function normalizeLicense(
  consumer: Consumer,
  loose: boolean,
): undefined | SPDXExpressionNode {
  if (!consumer.hasProperty('license')) {
    return undefined;
  }

  let licenseProp = consumer.getProperty('license');

  let licenseId;

  // Support some legacy ways of specifying licenses: https://docs.npmjs.com/files/package.json#license
  const raw = licenseProp.asMixed();
  if (loose && Array.isArray(raw)) {
    const licenseIds = licenseProp
      .asArray()
      .map(consumer => extractLicenseFromObjectConsumer(consumer)[0]);
    licenseId = `(${licenseIds.join(' OR ')})`;
  } else if (loose && typeof raw === 'object') {
    [licenseId, licenseProp] = extractLicenseFromObjectConsumer(licenseProp);
  } else {
    licenseId = licenseProp.asString();
  }

  // Allow referring to a custom license
  if (licenseId.startsWith('SEE LICENSE IN ')) {
    return undefined;
  }

  // Not valid licenses...
  if (INVALID_IGNORE_LICENSES.includes(licenseId)) {
    return undefined;
  }

  // Parse as a SPDX expression
  return spdx.parse({
    filename: consumer.filename,
    input: licenseId,
    offsetPosition: licenseProp.getInnerLocation().start,
  });
}

function normalizeVersion(
  consumer: Consumer,
  loose: boolean,
): undefined | SemverVersionNode {
  if (!consumer.hasProperty('version')) {
    return undefined;
  }

  const prop = consumer.getProperty('version');
  const rawVersion = prop.asString();

  // Used in some package.json templates
  if (rawVersion === 'VERSION_STRING') {
    return undefined;
  }

  const ast = semver.parseVersion({
    filename: consumer.filename,
    input: rawVersion,
    offsetPosition: prop.getInnerLocation().start,
    // Some node_modules have bogus versions, like being prefixed with a v like:
    // https://github.com/itinance/react-native-fs/commit/6232d4e392d5b52cca0792fdfe5903b7fb6b1c5c#diff-b9cfc7f2cdf78a7f4b91a753d10865a2R3
    loose,
  });
  return ast;
}

export function normalizeManifest(loc: string, consumer: Consumer): Manifest {
  const loose = loc.includes('node_modules');

  // npm allows bundleDependencies as an alias but let's just throw an error
  if (!loose && consumer.hasProperty('bundleDependencies')) {
    consumer
      .getProperty('bundleDependencies')
      .unexpected('bundleDependencies is a typo of bundledDependencies');
  }

  // FIXME: There's this ridiculous node module that includes it's tests... which deliberately includes an invalid package.json
  if (loc.includes('resolve/test/resolver/invalid_main')) {
    consumer.setValue({});
  }

  const name = normalizeString(consumer, 'name');

  return {
    // Allow refinement to access any unknown properties
    ...consumer.asUnknownObject(),

    name,
    version: normalizeVersion(consumer, loose),
    private: normalizeBoolean(consumer, 'private') === true,
    description: normalizeString(consumer, 'description'),
    license: normalizeLicense(consumer, loose),

    bin: normalizeBin(consumer, name),
    scripts: normalizeStringMap(consumer, 'scripts'),
    homepage: normalizeString(consumer, 'homepage'),
    engines: normalizeStringMap(consumer, 'engines'),

    files: normalizeStringArray(consumer, 'files'),
    keywords: normalizeStringArray(consumer, 'keywords'),
    cpu: normalizeStringArray(consumer, 'cpu'),
    os: normalizeStringArray(consumer, 'os'),

    browser: undefined, // normalizeString(consumer, 'browser'),
    main: normalizeString(consumer, 'main'),
    'rome:main': normalizeString(consumer, 'rome:main'),
    'jsnext:main': normalizeString(consumer, 'jsnext:main'),

    dependencies: normalizeDependencies(consumer, 'dependencies', loose),
    devDependencies: normalizeDependencies(consumer, 'devDependencies', loose),
    optionalDependencies: normalizeDependencies(
      consumer,
      'optionalDependencies',
      loose,
    ),
    peerDependencies: normalizeDependencies(
      consumer,
      'peerDependencies',
      loose,
    ),
    bundledDependencies: normalizeStringArray(consumer, 'bundledDependencies'),

    // TODO validate `author`
    // TODO validate `contributors`
    // TODO validate `maintainers`
    // TODO validate `repository`
    // TODO validate `bugs`
    // TODO validate `readme`
    // TODO validate `license`
    // TODO validate `noticeText`
    // TODO validate `directories`
  };
}
