import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function RestProperty(
  generator: Generator,
  node:
    | t.SpreadElementNode
    | t.RestPropertyNode
    | t.RestElementNode
    | t.SpreadPropertyNode,
) {
  generator.token('...');
  generator.print(node.argument, node);
}
