import path = require('path');

export default function walkDirectoryPath(dir: string): Iterable<string> {
  let nextDir: string = dir;

  const iterator: Iterator<string> = {
    next(): IteratorResult<string> {
      const currDir: string = nextDir;
      nextDir = path.dirname(currDir);

      return {
        value: currDir,
        done: nextDir === currDir,
      };
    },
  };

  return {
    [Symbol.iterator](): Iterator<string> {
      return iterator;
    },
  };
}
