import {
  ParserOptions,
  ComplexNode,
  BaseTokens,
  ValueToken,
  SimpleToken,
} from '@romejs/parser-core';
import {orderBySimilarity} from '@romejs/string-utils';
import {getLicense, licenseNames} from './index';
import {ParserCore, isAlpha, isDigit} from '@romejs/parser-core';

//# Tokens

type ParenOpenToken = SimpleToken<'ParenOpen'>;
type ParenCloseToken = SimpleToken<'ParenClose'>;
type PlusToken = SimpleToken<'Plus'>;
type AndToken = SimpleToken<'And'>;
type WithToken = SimpleToken<'With'>;
type OrToken = SimpleToken<'Or'>;
type WordToken = ValueToken<'Word', string>;

type Token =
  | BaseTokens
  | ParenOpenToken
  | ParenCloseToken
  | PlusToken
  | WithToken
  | AndToken
  | WordToken
  | OrToken;

//# Nodes

export type ExpressionNode = LicenseNode | AndNode | OrNode;

type AndNode = ComplexNode<
  'And',
  {
    left: ExpressionNode;
    right: ExpressionNode;
  }
>;

type OrNode = ComplexNode<
  'Or',
  {
    left: ExpressionNode;
    right: ExpressionNode;
  }
>;

type LicenseNode = ComplexNode<
  'License',
  {
    plus: boolean;
    id: string;
    exception: undefined | string;
  }
>;

function isWordChar(char: string) {
  return isAlpha(char) || isDigit(char) || char === '-' || char === '.';
}

class Parser extends ParserCore<Token, void> {
  constructor(opts: ParserOptions) {
    super(opts, 'spdx-license');
  }

  // For some reason Flow will throw an error without the type casts...
  tokenize(index: number, input: string): undefined | Token {
    const char = input[index];

    if (char === '+') {
      return this.finishToken('Plus') as PlusToken;
    }

    if (char === '(') {
      return this.finishToken('ParenOpen') as ParenOpenToken;
    }

    if (char === ')') {
      return this.finishToken('ParenClose') as ParenCloseToken;
    }

    // Skip spaces
    if (char === ' ') {
      return this.lookaheadToken(index + 1);
    }

    if (isAlpha(char)) {
      const value = this.readInputFrom(index, isWordChar);
      const end = index + value.length;

      if (value === 'AND') {
        return this.finishToken('And', end) as AndToken;
      } else if (value === 'OR') {
        return this.finishToken('Or', end) as OrToken;
      } else if (value === 'WITH') {
        return this.finishToken('With', end) as WithToken;
      } else {
        return this.finishValueToken('Word', value, end) as WordToken;
      }
    }

    // Unknown character
    return undefined;
  }

  parseLicense(token: WordToken): LicenseNode {
    const startPos = this.getPosition();

    // Validate id
    const id = token.value;
    const licenseInfo = getLicense(id);
    if (licenseInfo === undefined) {
      throw this.unexpected({
        message: `Unknown SPDX license <emphasis>${id}</emphasis>`,
        start: this.getPositionFromIndex(token.start),
        end: this.getPositionFromIndex(token.end),
        advice: [
          {
            type: 'log',
            category: 'info',
            message: `Did you mean <emphasis>${
              orderBySimilarity(id, licenseNames)[0].target
            }</emphasis>?`,
          },
        ],
      });
    }

    this.nextToken();

    // Is this a plus? (wtf is this)
    const plus = this.eatToken('Plus') !== undefined;

    // Get exception
    let exception;
    if (this.eatToken('With')) {
      const token = this.getToken();
      if (token.type === 'Word') {
        exception = token.value;
        this.nextToken();
      } else {
        throw this.unexpected({
          message: 'Only a license id can be on the right side of a WITH',
        });
      }
    }

    return {
      type: 'License',
      loc: this.finishLoc(startPos),
      id,
      exception,
      plus,
    } as LicenseNode;
  }

  parseExpression(): ExpressionNode {
    const startPos = this.getPosition();
    const startToken = this.getToken();

    let value;

    switch (startToken.type) {
      case 'ParenOpen':
        this.nextToken();
        value = this.parseExpression();
        this.expectToken('ParenClose');
        break;

      case 'Word':
        value = this.parseLicense(startToken);
        break;

      case 'Or':
      case 'And':
        throw this.unexpected({
          message: 'Can only use AND/OR in between an expression',
        });

      case 'Plus':
        throw this.unexpected({
          message: 'A plus can only come after a license id',
        });

      case 'ParenClose':
        throw this.unexpected({message: 'Nothing open to close'});

      case 'EOF':
        throw this.unexpected({message: 'Unexpected end of file'});

      default:
        throw this.unexpected();
    }

    // Parse and/or
    const nextToken = this.getToken();
    switch (nextToken.type) {
      case 'Or':
        this.nextToken();
        return {
          type: 'Or',
          loc: this.finishLoc(startPos),
          left: value,
          right: this.parseExpression(),
        } as OrNode;

      case 'And':
        this.nextToken();
        return {
          type: 'And',
          loc: this.finishLoc(startPos),
          left: value,
          right: this.parseExpression(),
        } as AndNode;

      default:
        return value;
    }
  }

  parse(): ExpressionNode {
    const expr = this.parseExpression();
    this.finalize();
    return expr;
  }
}

export default function parse(opts: ParserOptions): ExpressionNode {
  const parser = new Parser(opts);
  return parser.parse();
}
