import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXFragment(generator: Generator, node: t.JSXFragmentNode) {
  generator.print(node.openingFragment, node);

  generator.indent();
  for (const child of node.children) {
    generator.print(child, node);
  }
  generator.dedent();

  generator.print(node.closingFragment, node);
}
