import {
  JSNodeBase,
  FlowObjectTypePropertyNode,
  FlowObjectTypeSpreadPropertyNode,
  FlowObjectTypeIndexerNode,
  FlowObjectTypeCallPropertyNode,
  FlowObjectTypeInternalSlotNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowObjectTypeAnnotationNode = JSNodeBase & {
  type: 'FlowObjectTypeAnnotation';
  properties: Array<
    | FlowObjectTypePropertyNode
    | FlowObjectTypeSpreadPropertyNode
    | FlowObjectTypeIndexerNode
    | FlowObjectTypeCallPropertyNode
    | FlowObjectTypeInternalSlotNode
  >;
  exact?: undefined | boolean;
  inexact?: undefined | boolean;
};

export const flowObjectTypeAnnotation = createBuilder<
  FlowObjectTypeAnnotationNode
>('FlowObjectTypeAnnotation', {
  visitorKeys: ['properties'],
});
