import Generator from '../../Generator';
import * as n from '../../node/index';
import * as t from '@romejs/js-ast';

export function ObjectExpression(
  generator: Generator,
  node: t.ObjectExpressionNode | t.ObjectPatternNode,
) {
  const props = node.properties;

  generator.token('{');
  generator.printInnerComments(node);

  if (props.length) {
    generator.printCommaList(props, node, {
      indent: true,
      statement: n.isMultiLine(node),
    });
  }

  generator.token('}');
}
