import {
  JSNodeBase,
  FlowNode,
  ExportSpecifierNode,
  ExportNamespaceSpecifierNode,
  ExportDefaultSpecifierNode,
  StringLiteralNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowDeclareExportDeclarationNode = JSNodeBase & {
  type: 'FlowDeclareExportDeclaration';
  declaration?: undefined | FlowNode;
  specifiers?:
    | undefined
    | Array<
        | ExportSpecifierNode
        | ExportNamespaceSpecifierNode
        | ExportDefaultSpecifierNode
      >;
  source?: undefined | StringLiteralNode;
  default?: undefined | boolean;
};

export const flowDeclareExportDeclaration = createBuilder<
  FlowDeclareExportDeclarationNode
>('FlowDeclareExportDeclaration', {
  visitorKeys: ['declaration', 'specifiers', 'source'],
});
