import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowOpaqueType(
  generator: Generator,
  node: t.FlowOpaqueTypeNode,
) {
  generator.word('opaque');
  generator.space();
  generator.word('type');
  generator.space();
  generator.print(node.id, node);
  generator.print(node.typeParameters, node);
  if (node.supertype) {
    generator.token(':');
    generator.space();
    generator.print(node.supertype, node);
  }
  if (node.impltype) {
    generator.space();
    generator.token('=');
    generator.space();
    generator.print(node.impltype, node);
  }
  generator.semicolon();
}
