import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function AssignmentPattern(
  generator: Generator,
  node: t.AssignmentPatternNode,
) {
  generator.print(node.left, node);
  if (node.left.optional === true) {
    generator.token('?');
  }
  generator.print(node.left.typeAnnotation, node);
  generator.space();
  generator.token('=');
  generator.space();
  generator.print(node.right, node);
}
