import Master from './Master';
import {
  Manifest,
  ManifestKind,
  ManifestDefinition,
} from '@romejs/codec-js-manifest';
import {PathPatterns} from '@romejs/match-path';
import {ProjectConfig} from '../common/types/project';
import {
  DiagnosticsProcessor,
  getDiagnosticsFromError,
} from '@romejs/diagnostics';
import {Reporter} from '@romejs/cli-reporter';
import {createWatchmanClient} from '@romejs/codec-watchman';
import {Event} from '@romejs/events';
import {ROME_CONFIG_FILENAME} from '../common/constants';
import walkDirectoryPath from '../common/utils/walkDirectoryPath';
import {getExtensionHandler, getExtensionName} from '../worker/extensions';
import {consumeJSON} from '@romejs/codec-json';
import {
  ifNamedManifest,
  normalizeManifest,
  getManifestKindFromBasename,
} from '@romejs/codec-js-manifest';
import {humanizeNumber} from '@romejs/string-utils';
import invariant from '@romejs/invariant';
import {parsePath, matchPathPatterns} from '@romejs/match-path';
import {WatchmanSubscriptionValue} from '@romejs/codec-watchman';
import path = require('path');
import fs = require('fs');

const DEFAULT_DENYLIST = ['.hg', '.git'];

const PACKAGE_JSON = 'package.json';

// Whenever we're performing an operation on a set of files, always do these first as they may influence how the rest are processed
const PRIORITY_FILES = new Set([PACKAGE_JSON, ROME_CONFIG_FILENAME]);

type DeclareManifestOpts = {
  diagnostics: DiagnosticsProcessor;
  kind: ManifestKind;
  dirname: string;
  loc: string;
  hasteName: undefined | string;
  hasteLoc: string;
};

type CrawlOptions = {
  diagnostics: DiagnosticsProcessor;
  watchman?: boolean;
  tick?: (filename: string) => void;
};

export type StatsType = 'unknown' | 'directory' | 'file';

export type Stats = {
  size: number;
  mtime: number;
  type: StatsType;
};

export type WatcherClose = () => void;

export type HasteCollisionCallback = (
  hasteName: string,
  existing: string,
  filename: string,
) => void;

async function createRegularWatcher(
  memoryFs: MemoryFileSystem,
  diagnostics: DiagnosticsProcessor,
  projectFolder: string,
): Promise<WatcherClose> {
  const {logger} = memoryFs.master;

  // Create activity spinners for all connected reporters
  const activityPrefix = `Adding project ${projectFolder}`;
  const activity = memoryFs.master.connectedReporters.spinner();
  activity.setText(activityPrefix);

  // Handle file changes
  const watcher = fs.watch(
    projectFolder,
    {recursive: true, persistent: false},
    (eventType, filename) => {
      const loc = path.resolve(projectFolder, filename);

      memoryFs
        .stat(loc)
        .then(stats => {
          const diagnostics = memoryFs.master.createDisconnectedDiagnosticsProcessor(
            ['memory-fs: Processing fs.watch changes'],
          );
          if (stats.type === 'file') {
            memoryFs.handleFileChange(loc, stats, {diagnostics});
          } else if (stats.type === 'directory') {
            memoryFs.addDirectory(loc, stats, {diagnostics});
          }
        })
        .catch(err => {
          if (err.code === 'ENOENT') {
            memoryFs.handleDeletion(loc);
          } else {
            throw err;
          }
        });
    },
  );

  // Perform an initial crawl
  const stats = await memoryFs.stat(projectFolder);
  let totalFiles = 0;
  await memoryFs.addDirectory(projectFolder, stats, {
    diagnostics,
    tick(filename: string) {
      totalFiles++;

      activity.setText(
        `${activityPrefix}: #${humanizeNumber(totalFiles)}: ${filename}`,
      );
      activity.render();
    },
  });
  logger.info(
    `[MemoryFileSystem] Finished initial crawl for ${projectFolder} - added ${humanizeNumber(
      memoryFs.countFiles(projectFolder),
    )} files`,
  );
  activity.end();

  return () => {
    watcher.close();
  };
}

async function createWatchmanWatcher(
  memoryFs: MemoryFileSystem,
  diagnostics: DiagnosticsProcessor,
  projectFolder: string,
): Promise<WatcherClose> {
  try {
    const client = await createWatchmanClient(Reporter.fromProcess());

    const {event, root} = await client.createSubscription(projectFolder, {
      fields: ['mtime', 'name', 'size', 'type', 'exists'],
    });

    const initial: WatchmanSubscriptionValue = await event.wait();
    invariant(
      initial.is_fresh_instance === true,
      'expected this to be a fresh instance',
    );

    const processChanges = async (
      data: any,
      diagnostics: DiagnosticsProcessor,
    ) => {
      const dirs = [];
      const files = [];

      for (const file of data.files) {
        if (file.exists === false) {
          memoryFs.handleDeletion(path.join(root, file.name));
          continue;
        }

        if (file.type === 'f') {
          const basename = path.basename(file.name);

          if (PRIORITY_FILES.has(basename)) {
            files.unshift(file);
          } else {
            files.push(file);
          }
        } else if (file.type === 'd') {
          dirs.push(file);
        } else if (file.type === 'l') {
          // complain :(
        } else {
          // unsupported
        }
      }

      await Promise.all(
        dirs.map(async dir => {
          await memoryFs.addDirectory(
            path.join(root, dir.name),
            {
              size: dir.size,
              mtime: dir.mtime,
              type: 'directory',
            },
            {diagnostics, watchman: true},
          );
        }),
      );

      await Promise.all(
        files.map(async file => {
          const stats: Stats = {
            size: file.size,
            mtime: file.mtime,
            type: 'file',
          };

          const loc = path.join(root, file.name);
          if (memoryFs.files.has(loc)) {
            await memoryFs.handleFileChange(loc, stats, {
              diagnostics,
              watchman: true,
            });
          } else {
            await memoryFs.addFile(loc, stats, {diagnostics, watchman: true});
          }
        }),
      );
    };

    await processChanges(initial, diagnostics);

    event.subscribe((data: WatchmanSubscriptionValue) => {
      processChanges(
        data,
        memoryFs.master.createDisconnectedDiagnosticsProcessor([
          'memory-fs: Processing watchman changes',
        ]),
      );
    });

    return () => {
      // TODO close
    };
  } catch (err) {
    if (err.message.includes('RootResolveError')) {
      // Fallback to node processor
      memoryFs.master.connectedReporters.error(
        `Failed to use watchman: ${err.message}`,
      );
      return createRegularWatcher(memoryFs, diagnostics, projectFolder);
    } else {
      throw err;
    }
  }
}

export default class MemoryFileSystem {
  constructor(master: Master) {
    this.master = master;

    this.watchPromises = new Map();
    this.directoryListings = new Map();
    this.directories = new Map();
    this.files = new Map();
    this.manifests = new Map();
    this.watchers = new Map();

    this.changedFileEvent = new Event({name: 'MemoryFileSystem.changedFile'});
    this.deletedFileEvent = new Event({name: 'MemoryFileSystem.deletedFile'});
  }

  watchPromises: Map<string, Promise<WatcherClose>>;
  master: Master;
  directoryListings: Map<string, Set<string>>;
  directories: Map<string, Stats>;
  files: Map<string, Stats>;
  manifests: Map<string, ManifestDefinition>;
  watchers: Map<string, WatcherClose>;

  changedFileEvent: Event<
    {filename: string; oldStats: undefined | Stats; newStats: Stats},
    void
  >;
  deletedFileEvent: Event<{filename: string}, void>;

  unwatchAll() {
    for (const close of this.watchers.values()) {
      close();
    }
  }

  readdir(loc: string): Iterable<string> {
    const listing = this.directoryListings.get(loc);
    if (listing === undefined) {
      return [];
    } else {
      return listing;
    }
  }

  isDirectory(filename: string): boolean {
    return this.directories.has(filename);
  }

  isFile(filename: string): boolean {
    return this.files.has(filename);
  }

  getFiles(): Array<Stats> {
    return Array.from(this.files.values());
  }

  getManifestDefinition(dirname: string): undefined | ManifestDefinition {
    return this.manifests.get(dirname);
  }

  getManifest(dirname: string): undefined | Manifest {
    const def = this.getManifestDefinition(dirname);
    if (def === undefined) {
      return undefined;
    } else {
      return def.manifest;
    }
  }

  addFileToDirectoryListing(filename: string) {
    const dirname = path.dirname(filename);
    let listing = this.directoryListings.get(dirname);
    if (listing === undefined) {
      listing = new Set();
      this.directoryListings.set(dirname, listing);
    }
    listing.add(filename);
  }

  handleDeletion(loc: string) {
    // If a folder then evict all children
    const folderInfo = this.directories.get(loc);
    if (folderInfo !== undefined) {
      this.directories.delete(loc);

      const listing = this.directoryListings.get(loc);
      if (listing !== undefined) {
        this.directoryListings.delete(loc);
        for (const file of listing) {
          this.handleDeletion(file);
        }
      }
    }

    // Remove from 'all possible caches
    this.files.delete(loc);

    // Remove from 'haste maps
    this.handleDeletedHaste(loc);

    // If this is a manifest filename then clear it from 'any possible package and our internal module map
    const basename = path.basename(loc);
    if (getManifestKindFromBasename(basename) !== undefined) {
      this.handleDeletedManifest(loc);
    }

    // Remove from 'parent directory listing
    const dirname = path.dirname(loc);
    const parentListing = this.directoryListings.get(dirname);
    if (parentListing !== undefined) {
      parentListing.delete(loc);
    }

    this.deletedFileEvent.send({filename: loc});
  }

  handleDeletedHaste(loc: string) {
    const hasteName = this.getHasteName(loc);
    if (hasteName === undefined) {
      return undefined;
    }

    const projects = this.master.projectManager.getHierarchyFromFilename(loc);
    for (const {hasteMap} of projects) {
      const existing = hasteMap.get(hasteName);
      if (existing === loc) {
        hasteMap.delete(hasteName);
      }
    }
  }

  handleDeletedManifest(loc: string) {
    const folder = path.dirname(loc);
    const manifestDef = this.manifests.get(folder);
    if (manifestDef === undefined) {
      return undefined;
    }

    const {manifest} = manifestDef;

    // Clear from 'module map
    this.manifests.delete(folder);

    // Clear from 'project packages if named and in a project
    const namedManifest = ifNamedManifest(manifest);
    if (namedManifest === undefined || !this.isInsideProject(loc)) {
      return undefined;
    }

    for (const project of this.master.projectManager.getHierarchyFromFilename(
      loc,
    )) {
      // Get the project package with this name
      const pkg = project.packages.get(namedManifest.name);
      if (pkg === undefined) {
        continue;
      }

      // Verify that this project package refers to this manifest
      if (pkg.folder === folder) {
        project.packages.delete(namedManifest.name);
      }
    }
  }

  async handleFileChange(loc: string, stats: Stats, opts: CrawlOptions) {
    const oldStats: undefined | Stats = this.getFileStats(loc);
    const changed = await this.addFile(loc, stats, opts);
    if (changed) {
      const newStats: Stats = this.getFileStatsAssert(loc);
      this.changedFileEvent.send({filename: loc, oldStats, newStats});
    }
  }

  async watch(projectFolder: string, config: ProjectConfig): Promise<void> {
    const {logger} = this.master;

    // Defer if we're already currently initializing this project
    const cached = this.watchPromises.get(projectFolder);
    if (cached !== undefined) {
      await cached;
      return undefined;
    }

    // Check if we're already watching this folder
    if (this.watchers.has(projectFolder)) {
      return undefined;
    }

    // Check if we're already watching a parent directory
    for (const loc of this.watchers.keys()) {
      if ((projectFolder + path.sep).startsWith(loc)) {
        logger.info(
          `[MemoryFileSystem] Skipped crawl for ${projectFolder} because we're already watching a parent directory`,
        );
        return undefined;
      }
    }

    // Defer if we're initializing a parent folder
    for (const [loc, promise] of this.watchPromises) {
      if ((projectFolder + path.sep).startsWith(loc)) {
        await promise;
        return undefined;
      }
    }

    // Wait if we're initializing descendents
    for (const [loc, promise] of this.watchPromises) {
      if (loc.startsWith(projectFolder + path.sep)) {
        await promise;
      }
    }

    // New watch target
    logger.info(
      `[MemoryFileSystem] Adding new project folder ${projectFolder}`,
    );

    // Remove watchers that are descedents of this folder as this watcher will handle them
    for (const [loc, closeWatcher] of this.watchers) {
      if (loc.startsWith(projectFolder + path.sep)) {
        this.watchers.delete(loc);
        closeWatcher();
      }
    }

    const diagnostics = new DiagnosticsProcessor({
      origins: ['memory-fs: Crawl project folder'],
    });

    let promise;
    if (config.files.watchman) {
      logger.info(`[MemoryFileSystem] Watching ${projectFolder} with watchman`);
      promise = createWatchmanWatcher(this, diagnostics, projectFolder);
    } else {
      logger.info(`[MemoryFileSystem] Watching ${projectFolder} with fs.watch`);
      promise = createRegularWatcher(this, diagnostics, projectFolder);
    }
    this.watchPromises.set(projectFolder, promise);

    const watcherClose = await promise;
    this.watchers.set(projectFolder, watcherClose);
    this.watchPromises.delete(projectFolder);

    diagnostics.maybeThrowDiagnosticsError();
  }

  async stat(loc: string): Promise<Stats> {
    const stats = await fs.promises.stat(loc);

    let type: StatsType = 'unknown';
    if (stats.isDirectory()) {
      type = 'directory';
    } else if (stats.isFile()) {
      type = 'file';
    }

    return {
      type,
      size: stats.size,
      mtime: stats.mtimeMs,
    };
  }

  getMtime(filename: string) {
    const stats = this.getFileStats(filename);
    if (stats === undefined) {
      throw new Error(`File ${filename} not in database, cannot get mtime`);
    } else {
      return stats.mtime;
    }
  }

  getFileStats(filename: string): undefined | Stats {
    return this.files.get(filename);
  }

  getFileStatsAssert(filename: string): Stats {
    const stats = this.getFileStats(filename);
    if (stats === undefined) {
      throw new Error(`Expected file stats for ${filename}`);
    }
    return stats;
  }

  isIgnored(filename: string, type: 'directory' | 'file'): boolean {
    const project = this.master.projectManager.findProjectExisting(filename);
    if (project === undefined) {
      return false;
    }

    // If we're a file and don't have an extension handler so there's no reason for us to care about it
    if (type === 'file' && getExtensionHandler(filename) === undefined) {
      return true;
    }

    // Ensure we aren't in any of the default denylists
    const basename = path.basename(filename);
    if (DEFAULT_DENYLIST.includes(basename)) {
      return true;
    }

    return false;
  }

  isInsideProject(filename: string): boolean {
    return filename.includes('node_modules') === false;
  }

  isInsideHaste(filename: string): boolean {
    if (
      !this.isInsideProject(filename) ||
      filename.includes('__mocks__') ||
      filename.includes('__tests__') === true
    ) {
      return false;
    }

    // Check if we're inside a haste package, child files of a haste package shouldn't be added to the haste map
    for (const dir of walkDirectoryPath(path.dirname(filename))) {
      const packageLoc = path.join(dir, PACKAGE_JSON);
      if (filename === packageLoc) {
        // isInsideHaste will be called after we declare a haste package, all it's subfiles wont be inside the haste map but we should still be
        continue;
      }

      const module = this.getManifest(packageLoc);
      if (module !== undefined && module.haste_commonjs === true) {
        return false;
      }
    }

    return true;
  }

  getHasteName(loc: string): undefined | string {
    const extension = getExtensionHandler(loc);
    if (extension === undefined || extension.hasteMode === undefined) {
      return undefined;
    }

    const {hasteMode} = extension;

    let ext = getExtensionName(loc);
    invariant(
      ext !== undefined,
      'expected extension name for %s as we already validated that it has one',
      loc,
    );

    const basename = path.basename(loc);

    if (hasteMode === 'ext') {
      ext = '.' + ext; // we also want to remove the dot suffix from the haste name

      invariant(
        loc.endsWith(ext),
        'expected %s to end with %s as it was returned as the extension name',
        loc,
        ext,
      );

      return basename.slice(0, -ext.length);
    } else if (hasteMode === 'noext') {
      return basename;
    }

    return undefined;
  }

  // This is a wrapper around _declareManifest as it can produce diagnostics
  async declareManifest(
    opts: DeclareManifestOpts,
  ): Promise<undefined | string> {
    try {
      return await this._declareManifest(opts);
    } catch (err) {
      const diagnostics = getDiagnosticsFromError(err);

      if (diagnostics === undefined) {
        throw err;
      } else {
        opts.diagnostics.addDiagnostics(diagnostics);
      }
      return undefined;
    }
  }

  async _declareManifest({
    loc,
    kind,
    hasteName,
    diagnostics,
  }: DeclareManifestOpts): Promise<undefined | string> {
    // Fetch the manifest
    const manifestRaw = await fs.promises.readFile(loc, 'utf8');

    let consumer;
    if (kind === 'json') {
      consumer = consumeJSON({
        filename: loc,
        input: manifestRaw,
      });
    } else {
      throw new Error(`Unknown manifest kind`);
    }

    const manifest = normalizeManifest(loc, consumer);

    const folder = path.dirname(loc);
    this.manifests.set(folder, {
      raw: consumer.asUnknownObject(),
      manifest,
      kind,
    });

    //
    const namedManifest = ifNamedManifest(manifest);

    // Set haste name and haste location to the directory itself
    if (namedManifest !== undefined) {
      hasteName = namedManifest.name;
    }

    // If we aren't in node_modules then this is a project package
    if (namedManifest !== undefined && this.isInsideProject(loc)) {
      const {projectManager} = this.master;

      const project = projectManager.findProjectExisting(loc);
      if (project !== undefined) {
        projectManager.declarePackage(
          project,
          {
            manifestFilename: loc,
            folder,
            manifest: namedManifest,
          },
          diagnostics,
        );
      }
    }

    return hasteName;
  }

  glob(
    cwd: string,
    opts: {
      extensions?: Array<string>;
      ignore?: PathPatterns;
    } = {},
  ): Array<string> {
    const {extensions, ignore} = opts;

    const files: Array<string> = [];
    const ignoreParsed: PathPatterns = ignore === undefined ? [] : ignore;

    const cwdSegs = parsePath({input: cwd});
    let crawl = [cwd];

    while (crawl.length > 0) {
      const loc = crawl.pop();
      if (loc === undefined) {
        throw new Error('crawl.length already validated');
      }

      // Check if we should ignore this path
      if (matchPathPatterns(parsePath({input: loc}), ignoreParsed, cwdSegs)) {
        continue;
      }

      // Add if a matching file
      if (this.files.has(loc)) {
        // Remove the prefixed dot
        const ext = path.extname(loc).slice(1);
        if (extensions === undefined || extensions.includes(ext)) {
          files.push(loc);
        }
        continue;
      }

      // Crawl if we're a folder
      const listing = this.directoryListings.get(loc);
      if (listing !== undefined) {
        crawl = crawl.concat(Array.from(listing));
        continue;
      }

      // TODO maybe throw? not a file or folder, doesn't exist!
    }

    return files;
  }

  getAllFilesInFolder(folder: string): Array<string> {
    let files: Array<string> = [];

    const listing = this.directoryListings.get(folder);
    if (listing !== undefined) {
      for (const file of listing) {
        if (this.files.has(file)) {
          files.push(file);
        } else {
          files = files.concat(this.getAllFilesInFolder(file));
        }
      }
    }

    return files;
  }

  countFiles(folder: string): number {
    let count: number = 0;

    const listing = this.directoryListings.get(folder);
    if (listing !== undefined) {
      for (const file of listing) {
        count++;
        count += this.countFiles(file);
      }
    }

    return count;
  }

  async addDirectory(
    folder: string,
    stats: Stats,
    opts: CrawlOptions,
  ): Promise<boolean> {
    // Check if folder has been changed since we last saw it
    const oldFolderStats = this.directories.get(folder);
    if (oldFolderStats !== undefined && oldFolderStats.mtime === stats.mtime) {
      return false;
    }

    // Check if this folder has been ignored
    if (this.isIgnored(folder, 'directory')) {
      return false;
    }

    if (opts.tick !== undefined) {
      opts.tick(folder);
    }

    this.addFileToDirectoryListing(folder);
    this.directories.set(folder, stats);

    if (opts.watchman === true) {
      // TODO probably need to keep the file entries?
    } else {
      // If we aren't being called by watchman then we need to do the crawling ourselves

      // Crawl the folder
      const files: Set<string> = new Set(await fs.promises.readdir(folder));

      // Declare the file
      const declareItem = async (file: string) => {
        const loc = path.join(folder, file);
        const stats = await this.stat(loc);
        if (stats.type === 'file') {
          await this.addFile(loc, stats, opts);
        } else if (stats.type === 'directory') {
          await this.addDirectory(loc, stats, opts);
        }
      };

      // Give priority to package.json as we base some haste heuristics on it's entry
      for (const file of files) {
        if (PRIORITY_FILES.has(file)) {
          files.delete(file);
          await declareItem(file);
        }
      }

      // Add the rest of the items
      await Promise.all(Array.from(files, declareItem));
    }

    return true;
  }

  exists(loc: string): undefined | boolean {
    // if we have this in our cache then the file exists
    if (this.files.has(loc) || this.directories.has(loc)) {
      return true;
    }

    // if we're still performing an initial crawl of any path higher in the tree then we don't know if it exists yet
    for (const [projectFolder] of this.watchPromises) {
      if (loc.startsWith(projectFolder + path.sep)) {
        return undefined;
      }
    }

    // if we're watching the parent folder then we'd have it in our cache if it existed
    const parent = path.dirname(loc);
    if (this.directories.has(parent)) {
      return false;
    }

    return undefined;
  }

  async addFile(
    loc: string,
    stats: Stats,
    opts: CrawlOptions,
  ): Promise<boolean> {
    const {projectManager} = this.master;

    // check if file has been changed since we last saw it
    const oldFileInfo = this.files.get(loc);
    if (oldFileInfo !== undefined && oldFileInfo.mtime === stats.mtime) {
      return false;
    }

    // Check if this file has been ignored
    if (this.isIgnored(loc, 'file')) {
      return false;
    }

    if (opts.tick !== undefined) {
      opts.tick(loc);
    }

    this.files.set(loc, stats);
    this.addFileToDirectoryListing(loc);

    let hasteLoc = loc;
    let hasteName = this.getHasteName(loc);

    const basename = path.basename(loc);
    const dirname = path.dirname(loc);

    // Warn about potentially incorrect Rome config filenames
    projectManager.checkConfigFile(basename, loc, opts.diagnostics);

    // Add project if this is a config
    if (basename === ROME_CONFIG_FILENAME) {
      await projectManager.queueAddProject({dir: dirname, isPackage: false});
    }

    // If this is a package.json with a config property then declare it
    if (
      basename === PACKAGE_JSON &&
      (await projectManager.hasPackageJsonConfig(dirname))
    ) {
      await projectManager.queueAddProject({dir: dirname, isPackage: true});
    }

    // If this is a package.json then declare this module and setup the correct haste variables
    const manifestKind = getManifestKindFromBasename(basename);
    if (manifestKind !== undefined) {
      hasteName = await this.declareManifest({
        diagnostics: opts.diagnostics,
        kind: manifestKind,
        dirname,
        loc,
        hasteName,
        hasteLoc,
      });
      hasteLoc = dirname;
    }

    // Add to haste map
    if (hasteName !== undefined && this.isInsideHaste(loc)) {
      projectManager.declareHaste(loc, hasteName, hasteLoc, opts.diagnostics);
    }

    return true;
  }
}
