import {
  JSNodeBase,
  JSXIdentifierNode,
  JSXNamespacedNameNode,
  JSXElementNode,
  JSXFragmentNode,
  StringLiteralNode,
  JSXExpressionContainerNode,
} from '../index';
import {createBuilder} from '../utils';

export type JSXAttributeNode = JSNodeBase & {
  type: 'JSXAttribute',
  name: JSXIdentifierNode | JSXNamespacedNameNode,
  value?:
    | undefined
    | JSXElementNode
    | JSXFragmentNode
    | StringLiteralNode
    | JSXExpressionContainerNode,
};

export const jsxAttribute = createBuilder<JSXAttributeNode>('JSXAttribute', {
  visitorKeys: ['name', 'value'],
});
