import {ProjectConfig} from '@romejs/core';
import {
  CompilerOptions,
  TransformStageName,
  TransformStageFactories,
} from '../types';
import stripTypeAnnotations from './compile/stripTypeAnnotations';
import classProperties from './compile/transpile/classProperties';
import objectSpread from './compile/transpile/objectSpread';
import optimizeImports from './compile/validation/optimizeImports';
import optimizeExports from './compile/validation/optimizeExports';
import jsx from './compile/jsx';
import asyncImportTransform from './compileForBundle/asyncImportTransform';
import cjsRootTransform from './compileForBundle/cjsRootTransform';
import esToRefTransform from './compileForBundle/esToRefTransform';
import requireRewriteTransform from './compileForBundle/requireRewriteTransform';
import magicCJSTransform from './compileForBundle/magicCJSTransform';

export const stageOrder: Array<TransformStageName> = [
  'pre',
  'compile',
  'compileForBundle',
];

export const stageTransforms: TransformStageFactories = {
  pre: () => [optimizeImports, optimizeExports, jsx],
  compile: () => [objectSpread, classProperties],
  compileForBundle: (
    projectConfig: ProjectConfig,
    options: CompilerOptions,
  ) => {
    const opts = options.bundle;
    if (opts === undefined) {
      throw new Error('Expected bundle options for compileForBundle stage');
    }

    if (opts.mode === 'flat') {
      return [
        requireRewriteTransform,
        asyncImportTransform,
        opts.moduleType === 'cjs' ? cjsRootTransform : esToRefTransform,
        stripTypeAnnotations,
      ];
    } else {
      return [magicCJSTransform, stripTypeAnnotations];
    }
  },
};
