import {UnknownObject} from '@romejs/refinement';
import {PartialDiagnostics} from '@romejs/diagnostics';
import {SourceMap} from '@romejs/codec-source-map';

export type BundlerConfig = {
  inlineSourceMap: boolean;
  mode: undefined | BundlerMode;
  cwd: string;
};

export type BundlerMode = 'flat' | 'wrapper';

export const BUNDLER_MODES: Array<BundlerMode> = ['flat', 'wrapper'];

export type BundleRequestResult = {
  diagnostics: PartialDiagnostics;
  content: string;
  map: SourceMap;
};

export type BundleBuddyStats = Array<BundleBuddyGraphNode>;

export type BundleBuddyGraphNode = {
  source: string;
  target: string | undefined;
};

export type BundlerFiles = Map<string, string | Buffer>;

export type BundleResult = {
  map: SourceMap;
  content: string;
  manifest: undefined | UnknownObject;
  files: BundlerFiles;
};
