import {Path} from '@romejs/js-compiler';

export default function isReferenced(path: Path): boolean {
  const {node, parent} = path;
  if (parent === undefined) {
    return false;
  }

  switch (parent.type) {
    // yes: PARENT[NODE]
    // yes: NODE.child
    // no: parent.NODE
    case 'MemberExpression':
    case 'OptionalMemberExpression':
    case 'JSXMemberExpression':
      if (parent.property === node && parent.computed) {
        return true;
      } else if (parent.object === node) {
        return true;
      } else {
        return false;
      }

    // no: class F {name: foo.NODE;}
    // yes: class F {name: NODE.foo;}
    case 'FlowQualifiedTypeIdentifier':
      return parent.qualification === node;

    // no: new.NODE
    // no: NODE.target
    case 'MetaProperty':
      return false;

    // yes: { [NODE]: "" }
    // yes: { NODE }
    // no: { NODE: "" }
    case 'ObjectProperty':
      if (parent.key === node) {
        return Boolean(parent.computed);
      } else {
        return true;
      }

    case 'FlowObjectTypeProperty':
      return parent.value === node;

    // no: let NODE = init;
    // yes: let id = NODE;
    case 'VariableDeclarator':
      return parent.id !== node;

    // no: type T = (NODE: string) => void;
    // yes: type T = () => NODE;
    case 'FlowFunctionTypeAnnotation':
      return parent.returnType === node;

    // no: function NODE() {}
    // no: function foo(NODE) {}
    case 'ArrowFunctionExpression':
    case 'FunctionDeclaration':
    case 'FunctionExpression':
      return false;

    // yes: const foo = () => NODE;
    case 'ArrowFunctionExpression':
      return parent.body === node;

    // no: export { foo as NODE };
    // yes: export { NODE as foo };
    // no: export { NODE as foo } from '"foo";
    case 'ExportSpecifier': {
      if (path.parentPath !== undefined) {
        const exportDeclaration = path.parentPath.parent;
        if (
          exportDeclaration !== undefined &&
          exportDeclaration.type === 'ExportNamedDeclaration' &&
          exportDeclaration.source === undefined
        ) {
          return parent.local === node;
        }
      }

      return false;
    }

    // no: export NODE from '"foo";
    // no: export * as NODE from '"foo";
    case 'ExportNamespaceSpecifier':
    case 'ExportDefaultSpecifier':
      return false;

    // no: <div NODE="foo" />
    case 'JSXAttribute':
      return parent.name !== node;

    // no: class { NODE = value; }
    // yes: class { [NODE] = value; }
    // yes: class { key = NODE; }
    case 'ClassProperty':
      if (parent.key === node) {
        return parent.computed;
      } else {
        return parent.value === node;
      }

    // no: import NODE from '"foo";
    // no: import * as NODE from '"foo";
    // no: import { NODE as foo } from '"foo";
    // no: import { foo as NODE } from '"foo";
    // no: import NODE from '"bar";
    case 'ImportDefaultSpecifier':
    case 'ImportNamespaceSpecifier':
    case 'ImportSpecifier':
      return false;

    // no: class NODE {}
    case 'ClassDeclaration':
    case 'ClassExpression':
      return parent.id !== node;

    // yes: class { [NODE]() {} }
    case 'ClassMethod':
    case 'ObjectMethod':
      return parent.key === node && Boolean(parent.computed);

    // no: NODE: for (;;) {}
    case 'LabeledStatement':
      return false;

    // no: try {} catch (NODE) {}
    case 'CatchClause':
      return parent.param !== node;

    // no: type Foo = (...NODE) => void;
    case 'FlowFunctionTypeParam':
      return false;

    // no: function foo(...NODE) {}
    case 'RestElement':
      return false;

    // no: continue NODE;
    // no: break NODE;
    case 'BreakStatement':
    case 'ContinueStatement':
      return false;

    // no: [NODE = foo] = [];
    // yes: [foo = NODE] = [];
    case 'AssignmentPattern':
      return parent.right === node;

    // no: type Foo = Dict<void>;
    // yes: type Foo = Dict<void>;
    // yes: type Foo = Dict<NODE>;
    case 'FlowObjectTypeIndexer':
      return parent.id !== node;

    // no: [NODE] = [];
    // no: ({ NODE }) = [];
    case 'ObjectPattern':
    case 'ArrayPattern':
      return false;
  }

  return true;
}
