import Scope from '../Scope';
import {TypeBinding} from '@romejs/js-compiler';
import * as t from '@romejs/js-ast';

export default function FlowInterfaceDeclaration(
  node: t.FlowInterfaceDeclarationNode,
  parent: t.Node,
  scope: Scope,
) {
  scope.addBinding(
    new TypeBinding({
      node: node.id,
      name: node.id.name,
      scope,
    }),
  );
}
