import {
  JSNodeBase,
  IdentifierNode,
  FlowTypeParameterInstantiationNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowClassImplementsNode = JSNodeBase & {
  type: 'FlowClassImplements',
  id: IdentifierNode,
  typeParameters?: undefined | FlowTypeParameterInstantiationNode,
};

export const flowClassImplements = createBuilder<FlowClassImplementsNode>(
  'FlowClassImplements',
  {visitorKeys: ['id', 'typeParameters']},
);
