import {JSNodeBase} from '../index';
import {createBuilder} from '../utils';

export type JSXOpeningFragmentNode = JSNodeBase & {
  type: 'JSXOpeningFragment',
};

export const jsxOpeningFragment = createBuilder<JSXOpeningFragmentNode>(
  'JSXOpeningFragment',
  {visitorKeys: ['name', 'attributes']},
);
