import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {printParams} from '../utils';

export function FunctionExpression(
  generator: Generator,
  node: t.FunctionExpressionNode | t.FunctionDeclarationNode,
) {
  if (node.async === true) {
    generator.word('async');
    generator.space();
  }

  generator.word('function');

  if (node.generator === true) {
    generator.token('*');
  }

  if (node.id) {
    generator.space();
    generator.print(node.id, node);
  }

  printParams(generator, node);
  generator.space();
  generator.print(node.body, node);
}
