import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function FlowFunctionTypeParamRest(
  generator: Generator,
  node: t.FlowFunctionTypeParamRestNode,
) {
  generator.token('...');
  generator.print(node.param, node);
}
