import {JSNodeBase, PropertyNameBase, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type ObjectPropertyNode = JSNodeBase &
  PropertyNameBase & {
    type: 'ObjectProperty';
    shorthand?: undefined | boolean;
    value: ExpressionNode;
  };

export const objectProperty = createBuilder<ObjectPropertyNode>(
  'ObjectProperty',
  {
    visitorKeys: ['key', 'value', 'decorators'],
  },
);
