import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type PrivateNameNode = JSNodeBase & {
  type: 'PrivateName',
  id: IdentifierNode,
};

export const privateName = createBuilder<PrivateNameNode>('PrivateName', {
  visitorKeys: ['id'],
});
