import {
  JSNodeBase,
  JSXOpeningFragmentNode,
  JSXClosingFragmentNode,
  JSXTextNode,
  JSXExpressionContainerNode,
  JSXSpreadChildNode,
  JSXElementNode,
} from '../index';
import {createBuilder} from '../utils';

export type JSXFragmentNode = JSNodeBase & {
  type: 'JSXFragment';
  openingFragment?: undefined | JSXOpeningFragmentNode;
  closingFragment?: undefined | JSXClosingFragmentNode;
  children: Array<
    | JSXTextNode
    | JSXExpressionContainerNode
    | JSXSpreadChildNode
    | JSXElementNode
    | JSXFragmentNode
  >;
};

export const jsxFragment = createBuilder<JSXFragmentNode>('JSXFragment');
