import {Node} from '@romejs/js-ast';
import {Scope} from '../scopes';
import {HydrateTypeFactory, HydrateData} from '../Evaluator';
import {SerialTypeFactory} from './T';
import {HumanBuilder} from '../Utils';
import E from './errors/E';
import AnyT from './AnyT';
import T from './T';

class ENotExhaustive extends E {
  constructor(
    scope: Scope,
    originNode: undefined | Node,
    target: T,
    only: T,
    extraenous: Array<T>,
  ) {
    super(scope, originNode);
    this.target = target;
    this.only = only;
    this.extraenous = extraenous;
  }

  target: T;
  only: T;
  extraenous: Array<T>;

  static type = 'ENotExhaustive';

  getError() {
    let message = `Expected only a ${this.utils.humanize(
      this.only,
    )} but got ${this.utils.humanize(this.target)}`;
    //message += `but allows ${this.extraenous.map(type => this.utils.humanize(type)).join(' | ')}`;
    return {
      message,
      lowerTarget: this.target,
    };
  }
}

export default class ExhaustiveT extends T {
  constructor(scope: Scope, originNode: undefined | Node, target: T, only: T) {
    super(scope, originNode);
    this.target = target;
    this.only = only;
  }

  target: T;
  only: T;

  static type = 'ExhaustiveT';

  serialize(addType: SerialTypeFactory): HydrateData {
    return {
      target: addType(this.target),
      only: addType(this.only),
    };
  }

  static hydrate(
    scope: Scope,
    originNode: Node,
    data: HydrateData,
    getType: HydrateTypeFactory,
  ): T {
    return new ExhaustiveT(
      scope,
      originNode,
      getType(data.target),
      getType(data.only),
    );
  }

  reduce(): T {
    const target = this.utils.reduce(this.target);
    const only = this.utils.reduce(this.only);
    if (target instanceof AnyT || only instanceof AnyT) {
      return this.only;
    }

    const targetCandidates = this.utils.explodeUnion(target);
    const onlyCandidates = this.utils.explodeUnion(only);

    const extraneous = [];
    for (const possible of targetCandidates) {
      let compatible = false;

      for (const otherType of onlyCandidates) {
        if (this.utils.isCompatibleWith(possible, otherType)) {
          compatible = true;
        }
      }

      if (compatible === false) {
        extraneous.push(possible);
      }
    }

    if (extraneous.length === 0) {
      return target;
    } else {
      return new ENotExhaustive(
        this.scope,
        this.originNode,
        this.target,
        this.only,
        extraneous,
      );
    }
  }

  humanize(builder: HumanBuilder): string {
    return `exhaustive ${builder.humanize(
      this.target,
    )} should only match ${builder.humanize(this.target)}`;
  }
}
