import {Children, TagAttributes} from './types';
import {parse} from './parse';
import {humanizeNumber} from '@romejs/string-utils';
import {formatAnsi} from '@romejs/string-ansi';

import path = require('path');

type FormatReduceCallback = (
  name: string,
  attributes: TagAttributes,
  value: string,
) => string;

export type MarkupFormatFilenameNormalizer = (filename: string) => string;

export type MarkupFormatOptions = {
  normalizeFilename?: undefined | MarkupFormatFilenameNormalizer,
  cwd?: undefined | string,
};

function formatReduceFromInput(
  input: string,
  callback: FormatReduceCallback,
): string {
  return formatReduceFromChildren(parse(input), callback);
}

function formatReduceFromChildren(
  children: Children,
  callback: FormatReduceCallback,
): string {
  let buff = '';
  for (const child of children) {
    if (child.type === 'Text') {
      buff += child.value;
    } else if (child.type === 'Tag') {
      buff += callback(
        child.name,
        child.attributes,
        formatReduceFromChildren(child.children, callback),
      );
    } else {
      throw new Error('Unknown child node type');
    }
  }
  return buff;
}

export function markupToAnsi(
  input: string,
  opts: MarkupFormatOptions = {},
): string {
  return formatReduceFromInput(input, (tag, attributes, value) => {
    switch (tag) {
      case 'hyperlink':
        return formatAnsi.hyperlink(value, attributes.get('target') || value);

      case 'filelink': {
        // Normalize filename
        let filename = attributes.get('filename') || '';
        let origFilename = filename;
        if (opts.normalizeFilename !== undefined) {
          filename = opts.normalizeFilename(filename);
        }

        let text = value;

        // Default text to a humanized version of the filename
        if (text === '') {
          text = humanizeMarkupFilename([filename, origFilename], opts);

          const line = attributes.get('line');
          if (line !== undefined) {
            text += `:${line}`;

            const column = attributes.get('column');
            if (column !== undefined) {
              text += `:${column}`;
            }
          }
        }

        return formatAnsi.fileHyperlink(text, filename);
      }

      case 'inverse':
        return formatAnsi.inverse(` ${value} `);

      case 'emphasis':
        return formatAnsi.bold(value);

      case 'number':
        return humanizeNumber(Number(value));

      default:
        throw new Error('Unknown tag name ' + tag);
    }
  });
}

export function humanizeMarkupFilename(
  filenames: Array<string>,
  opts: MarkupFormatOptions = {},
): string {
  if (filenames.length === 0) {
    return 'unknown';
  }

  const names: Array<string> = [];

  for (const filename of filenames) {
    // Push on the absolute filename since it may be shorter!
    names.push(filename);

    // Get a path relative to HOME
    const home = process.env.HOME;
    if (home != null && filename.startsWith(home + path.sep)) {
      // Path starts with the home directory, so let's trim it off
      const tildify = filename.slice(home.length);

      // Add tilde and push it as a possible name
      names.push(path.join('~', tildify));
    }

    // Get a path relative to the cwd
    if (opts.cwd !== undefined) {
      names.push(path.relative(opts.cwd, filename));
    }
  }

  // Get the shortest name
  return names.sort((a, b) => a.length - b.length)[0];
}
