import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import {IfStatement} from './IfStatement';

export function ConditionalExpression(
  node: n.ConditionalExpressionNode,
  scope: Scope,
) {
  return IfStatement(node, scope);
}
