import {
  JSNodeBase,
  StringLiteralNode,
  IdentifierNode,
  ExpressionNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSEnumMemberNode = JSNodeBase & {
  type: 'TSEnumMember';
  id: StringLiteralNode | IdentifierNode;
  initializer: ExpressionNode;
};

export const tsEnumMember = createBuilder<TSEnumMemberNode>('TSEnumMember', {
  visitorKeys: ['id', 'initializer'],
});
