import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import AnyT from '../../types/AnyT';

export function FlowTypeParameter(node: n.FlowTypeParameterNode, scope: Scope) {
  const type = new AnyT(scope, node);
  scope.addBinding(node.name, type);
  return type;
}
