import {Consumer} from '@romejs/codec-json';
import {SemverRangeNode} from '@romejs/codec-semver';
import * as semver from '@romejs/codec-semver';

import path = require('path');

type Pattern =
  | HostedGitPattern
  | TarballPattern
  | SemverPattern
  | GitPattern
  | FilePattern
  | TagPattern;

export type ManifestDependencies = Map<string, Pattern>;

function explodeHashUrl(
  pattern: string,
  consumer: Consumer,
): {url: string; hash: string | undefined} {
  const parts = pattern.split('#');

  if (parts.length > 2) {
    throw consumer.unexpected('Too many hashes');
  }

  return {
    hash: parts[1],
    url: parts[0],
  };
}

function removePrefix(prefix: string, value: string): string {
  if (value.startsWith(prefix)) {
    return value.slice(prefix.length);
  } else {
    return value;
  }
}

//# HOSTED GIT

type HostedGitHost = 'bitbucket' | 'github' | 'gist' | 'gitlab';

type HostedGitPattern = {
  type: 'hosted-git';
  host: HostedGitHost;
  user: string;
  repo: string;
  commitish: undefined | string;
};

const GITHUB_SHORTHAND = /^[^:@%/\s.-][^:@%/\s]*[/][^:@\s/%]+(?:#.*)?$/;

const HOSTED_GIT_PREFIXES: Array<HostedGitHost> = [
  'bitbucket',
  'github',
  'gist',
  'gitlab',
];

function parseHostedGit(
  host: HostedGitHost,
  pattern: string,
  consumer: Consumer,
): HostedGitPattern {
  // Extract and trim hash
  let commitish: undefined | string;
  if (pattern.includes('#')) {
    const hashIndex = pattern.indexOf('#');
    commitish = pattern.slice(hashIndex + 1);
    pattern = pattern.slice(0, hashIndex - 1);
  }

  const parts = pattern.split('/');
  if (parts.length > 2) {
    throw consumer.unexpected('Expected only 2 parts');
  }

  const user = parts[0];
  if (user === undefined) {
    throw consumer.unexpected('We are missing a user!');
  }

  const repo = parts[1];
  if (repo === undefined) {
    throw consumer.unexpected('We are missing a repo!');
  }

  return {
    type: 'hosted-git',
    host,
    user,
    repo,
    commitish,
  };
}

//# REGULAR GIT

type GitPattern = {
  type: 'git';
  url: string;
  hash: undefined | string;
};

const GIT_PATTERN_MATCHERS = [
  /^git:/,
  /^git\+.+:/,
  /^ssh:/,
  /^https?:.+\.git$/,
  /^https?:.+\.git#.+/,
];

function parseGit(pattern: string, consumer: Consumer): GitPattern {
  return {
    type: 'git',
    ...explodeHashUrl(pattern, consumer),
  };
}

//# TARBALL

type TarballPattern = {
  type: 'tarball';
  url: string;
  hash: undefined | string;
};

function parseTarball(pattern: string, consumer: Consumer): TarballPattern {
  return {
    type: 'tarball',
    ...explodeHashUrl(pattern, consumer),
  };
}

//# SEMVER

type SemverPattern = {
  type: 'semver';
  range: SemverRangeNode;
};

function parseSemver(
  pattern: string,
  consumer: Consumer,
  loose: boolean,
): SemverPattern {
  const ast = semver.parseRange({
    loose,
    filename: consumer.filename,
    input: pattern,
    offsetPosition: consumer.getInnerLocation().start,
  });

  return {
    type: 'semver',
    range: ast,
  };
}

//# FILE

const FILE_PREFIX_REGEX = /^\.{1,2}\//;

type FilePattern = {
  type: 'file';
  path: string;
};

function parseFile(pattern: string): FilePattern {
  return {
    type: 'file',
    path: removePrefix('file:', pattern),
  };
}

//# TAG

// This regex will likely need to be refined, not sure what the allowable characters of a tag are
const TAG_REGEX = /^[a-z]+$/g;

type TagPattern = {
  type: 'tag';
  tag: string;
};

function parseTag(pattern: string): TagPattern {
  return {
    type: 'tag',
    tag: pattern,
  };
}

//#

export function parseDependencyPattern(
  consumer: Consumer,
  loose: boolean,
): Pattern {
  const pattern = consumer.asString();

  for (const host of HOSTED_GIT_PREFIXES) {
    const prefix = `${host}:`;
    if (pattern.startsWith(prefix)) {
      return parseHostedGit(host, removePrefix(prefix, pattern), consumer);
    }
  }

  for (const matcher of GIT_PATTERN_MATCHERS) {
    if (matcher.test(pattern)) {
      return parseGit(pattern, consumer);
    }
  }

  if (GITHUB_SHORTHAND.test(pattern)) {
    return parseHostedGit('github', pattern, consumer);
  }

  if (pattern.startsWith('http://') || pattern.startsWith('https://')) {
    return parseTarball(pattern, consumer);
  }

  if (
    FILE_PREFIX_REGEX.test(pattern) ||
    path.isAbsolute(pattern) ||
    pattern.startsWith('file:')
  ) {
    return parseFile(pattern);
  }

  if (pattern.match(TAG_REGEX)) {
    return parseTag(pattern);
  }

  return parseSemver(pattern, consumer, loose);
}

export function normalizeDependencies(
  consumer: Consumer,
  key: string,
  loose: boolean,
): ManifestDependencies {
  const map: ManifestDependencies = new Map();

  if (!consumer.hasProperty(key)) {
    return map;
  }

  for (const [name, value] of consumer.getProperty(key).asMap()) {
    // TODO validate name

    map.set(name, parseDependencyPattern(value, loose));
  }

  return map;
}
