import {Scope} from '../../scopes';
import * as n from '@romejs/js-ast';
import NumericLiteralT from '../../types/NumericLiteralT';

export function FlowNumericLiteralTypeAnnotation(
  node: n.FlowNumericLiteralTypeAnnotationNode,
  scope: Scope,
) {
  return new NumericLiteralT(scope, node, node.value);
}
