import {JSNodeBase, ExpressionNode, StatementNode} from '../index';
import {createBuilder} from '../utils';

export type SwitchCaseNode = JSNodeBase & {
  type: 'SwitchCase',
  test?: undefined | ExpressionNode,
  consequent: Array<StatementNode>,
};

export const switchCase = createBuilder<SwitchCaseNode>('SwitchCase', {
  visitorKeys: ['test', 'consequent'],
});
