import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function JSXOpeningElement(
  generator: Generator,
  node: t.JSXOpeningElementNode,
) {
  generator.token('<');
  generator.print(node.name, node);

  if (node.attributes.length > 0) {
    generator.space();
    generator.printJoin(node.attributes, node, {separator: spaceSeparator});
  }

  if (node.selfClosing === true) {
    generator.space();
    generator.token('/>');
  } else {
    generator.token('>');
  }
}

function spaceSeparator(generator: Generator) {
  generator.space();
}
