import {Node} from '@romejs/js-ast';
import {SerialTypeFactory} from './T';
import {HydrateTypeFactory, HydrateData} from '../Evaluator';
import {HumanBuilder} from '../Utils';
import {Scope} from '../scopes';
import T from './T';

export default class ObjIndexPropT extends T {
  constructor(scope: Scope, originNode: undefined | Node, key: T, value: T) {
    super(scope, originNode);
    this.key = key;
    this.value = value;
  }

  static type = 'ObjIndexPropT';

  key: T;
  value: T;

  serialize(addType: SerialTypeFactory): HydrateData {
    return {
      key: addType(this.key),
      value: addType(this.value),
    };
  }

  static hydrate(
    scope: Scope,
    originNode: Node,
    data: HydrateData,
    getType: HydrateTypeFactory,
  ): T {
    return new ObjIndexPropT(
      scope,
      originNode,
      getType(data.key),
      getType(data.value),
    );
  }

  humanize(builder: HumanBuilder): string {
    return `[${builder.humanize(this.key)}]: ${builder.humanize(this.value)}`;
  }
}
