import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function DoWhileStatement(
  generator: Generator,
  node: t.DoWhileStatementNode,
) {
  generator.word('do');
  generator.space();
  generator.print(node.body, node);
  generator.space();
  generator.word('while');
  generator.space();
  generator.token('(');
  generator.print(node.test, node);
  generator.token(')');
  generator.semicolon();
}
