import {
  WorkerCompileResult,
  WorkerAnalyzeDependencyResult,
} from '../common/bridges/WorkerBridge';
import {ModuleSignature} from '@romejs/js-analysis';
import {Project} from '../common/types/project';
import Master from './Master';
import {DEFAULT_PROJECT_CONFIG} from '../common/types/project';
import {VERSION} from '../common/constants';
import path = require('path');
import fs = require('fs');

type CacheEntry = {
  filename: string;
  version: string;
  configHash: string;
  projectDir: string;
  mtime: number;
  compile: {
    [key: string]: WorkerCompileResult;
  };
  analyzeDependencies: undefined | WorkerAnalyzeDependencyResult;
  moduleSignature: undefined | ModuleSignature;
};

export default class Cache {
  constructor(master: Master) {
    this.master = master;
    this.loadedEntries = new Map();
    this.disabled = process.env.ROME_CACHE === '0';
  }

  disabled: boolean;
  loadedEntries: Map<string, CacheEntry>;
  master: Master;

  async onNewProject(project: Project) {
    if (this.disabled) {
      return undefined;
    }

    const {memoryFs} = this.master;
    const folder = project.config.cache.folder;

    // $FlowFixMe: this is fine
    await fs.promises.mkdir(folder, {recursive: true});
    await memoryFs.watch(folder, DEFAULT_PROJECT_CONFIG);
  }

  async createEntry(filename: string): Promise<CacheEntry> {
    const {projectManager, memoryFs} = this.master;

    const project: Project = await projectManager.assertProject(filename);

    const entry: CacheEntry = {
      filename,
      version: VERSION,
      projectDir: project.dir,
      configHash: project.config.configHashes.join(';'),
      mtime: memoryFs.getMtime(filename),
      compile: {},
      analyzeDependencies: undefined,
      moduleSignature: undefined,
    };

    return entry;
  }

  getCacheFilename(filename: string): string {
    const project = this.master.projectManager.assertProjectExisting(filename);
    const uid = this.master.projectManager.getUid(filename);
    return path.join(project.config.cache.folder, uid);
  }

  async evict(filename: string) {
    const cacheFilename = this.getCacheFilename(filename);
    await fs.promises.unlink(cacheFilename);
    this.loadedEntries.delete(filename);
  }

  async get(filename: string): Promise<CacheEntry> {
    // TODO use projectConfig.cache.endpoint

    const loaded = this.loadedEntries.get(filename);
    if (loaded !== undefined) {
      return loaded;
    }

    const defaultEntry = await this.createEntry(filename);
    if (this.disabled) {
      return defaultEntry;
    }

    const {memoryFs} = this.master;
    const cacheFilename = this.getCacheFilename(filename);
    const entry = await this._get(cacheFilename, defaultEntry);

    if (entry === defaultEntry && memoryFs.exists(cacheFilename)) {
      // Some incompatibility has been detected so clear it from 'disk
      await this.evict(filename);
    }

    this.loadedEntries.set(filename, entry);

    return entry;
  }

  async _get(cacheFilename: string, defaultEntry: CacheEntry) {
    const {memoryFs} = this.master;

    if (!memoryFs.exists(cacheFilename)) {
      return defaultEntry;
    }

    try {
      const json = await fs.promises.readFile(cacheFilename, 'utf8');
      const obj = JSON.parse(json);

      if (obj.version !== defaultEntry.version) {
        // outdated cache file
        return defaultEntry;
      }

      if (obj.configHash !== defaultEntry.configHash) {
        // project config has been changed since this was last updated
        return defaultEntry;
      }

      if (obj.mtime !== defaultEntry.mtime) {
        // file has been changed
        return defaultEntry;
      }

      if (obj.filename !== defaultEntry.filename) {
        // cache file was placed in wrong location
        return defaultEntry;
      }

      return {...defaultEntry, ...obj};
    } catch (err) {
      // TODO add some heuristic to only catch json and cache permission errors
      return defaultEntry;
    }
  }

  async write(filename: string, entry: CacheEntry) {
    // TODO should batch these and write during idle time
    const cacheFilename = this.getCacheFilename(filename);
    this.loadedEntries.set(filename, entry);

    if (this.disabled) {
      return undefined;
    }

    // $FlowFixMe: this is fine
    await fs.promises.mkdir(path.dirname(cacheFilename), {recursive: true});
    await fs.promises.writeFile(
      cacheFilename,
      JSON.stringify(entry, null, '  '),
    );
  }
}
