import {JSNodeBase, IdentifierNode} from '../index';
import {createBuilder} from '../utils';

export type ExportDefaultSpecifierNode = JSNodeBase & {
  type: 'ExportDefaultSpecifier',
  exported: IdentifierNode,
};

export const exportDefaultSpecifier = createBuilder<ExportDefaultSpecifierNode>(
  'ExportDefaultSpecifier',
  {visitorKeys: ['exported']},
);
