import Generator from '../../Generator';
import * as t from '@romejs/js-ast';
import {printMethod} from '../utils';

export function ClassMethod(generator: Generator, node: t.ClassMethodNode) {
  if (node.static === true) {
    generator.word('static');
    generator.space();
  }

  printMethod(generator, node);
}
