import {EventSubscription} from '@romejs/events';
import {MasterRequest} from '@romejs/core';
import {commandCategories} from '../../commands';
import {createMasterCommand} from '../../commands';
import {stripAnsi} from '@romejs/string-ansi';

import path = require('path');
import fs = require('fs');

export default createMasterCommand({
  category: commandCategories.PROCESS_MANAGEMENT,
  description: 'listen and stream daemon logs',

  async default(req: MasterRequest): Promise<void> {
    const {master, reporter, flags} = req;
    const {stdout} = reporter;
    return new Promise(() => {
      // If we were passed a --log-file then write logs there
      let fileout: undefined | fs.WriteStream;
      if (flags.logPath !== '') {
        fileout = fs.createWriteStream(path.resolve(flags.cwd, flags.logPath));

        req.teardownEvent.subscribe(() => {
          if (fileout !== undefined) {
            fileout.end();
          }
        });
      }

      function onLog(chunk: string) {
        if (fileout === undefined) {
          stdout.write(chunk);
        } else {
          fileout.write(stripAnsi(chunk));
        }
      }

      const subscriptions: Array<EventSubscription> = [];
      subscriptions.push(master.logEvent.subscribe(onLog));

      // Listen to worker logs when passed --worker-logs
      if (flags.logWorkers) {
        // Add on existing workers if there are any
        for (const {bridge} of master.workerManager.getWorkers()) {
          subscriptions.push(bridge.log.subscribe(onLog));
        }

        // Listen for logs for any workers that start later
        subscriptions.push(
          master.workerManager.workerStartEvent.subscribe(worker => {
            subscriptions.push(worker.log.subscribe(onLog));
          }),
        );
      }

      req.teardownEvent.subscribe(() => {
        for (const listener of subscriptions) {
          listener.unsubscribe();
        }
      });
    });
  },
});
