import {
  JSNodeBase,
  IdentifierNode,
  FlowDeclaredPredicateNode,
  FlowInferredPredicateNode,
} from '../index';
import {createBuilder} from '../utils';

export type FlowDeclareFunctionNode = JSNodeBase & {
  type: 'FlowDeclareFunction',
  id: IdentifierNode,
  predicate?: undefined | FlowDeclaredPredicateNode | FlowInferredPredicateNode,
};

export const flowDeclareFunction = createBuilder<FlowDeclareFunctionNode>(
  'FlowDeclareFunction',
  {visitorKeys: ['id']},
);
