import {
  JSNodeBase,
  TSEntityNameNode,
  TSTypeParameterInstantiationNode,
} from '../index';
import {createBuilder} from '../utils';

export type TSTypeReferenceNode = JSNodeBase & {
  type: 'TSTypeReference';
  typeName: TSEntityNameNode;
  typeParameters?: undefined | TSTypeParameterInstantiationNode;
};

export const tsTypeReference = createBuilder<TSTypeReferenceNode>(
  'TSTypeReference',
  {visitorKeys: ['typeName', 'typeParameters']},
);
