import {JSNodeBase, StatementNode, ExpressionNode} from '../index';
import {createBuilder} from '../utils';

export type DoWhileStatementNode = JSNodeBase & {
  type: 'DoWhileStatement',
  body: StatementNode,
  test: ExpressionNode,
};

export const doWhileStatement = createBuilder<DoWhileStatementNode>(
  'DoWhileStatement',
  {visitorKeys: ['test', 'body']},
);
