import {Node} from '@romejs/js-ast';
import {DEFAULT_PROJECT_CONFIG} from '@romejs/core';
import {Context, Path} from '@romejs/js-compiler';

const removeLocTransform = [
  {
    name: 'removeLocTransform',
    enter(path: Path) {
      const {node} = path;
      if (node.loc === undefined) {
        return node;
      } else {
        const newNode = {...node};
        delete newNode.loc;

        // Also remove any `undefined` properties
        for (const key in newNode) {
          if (newNode[key] === undefined) {
            delete newNode[key];
          }
        }

        return newNode;
      }
    },
  },
];

export default function removeLoc(ast: Node) {
  const context = new Context({ast, projectConfig: DEFAULT_PROJECT_CONFIG});
  return context.reduce(ast, removeLocTransform);
}
