import {JSNodeBase, TSTypeNode} from '../index';
import {createBuilder} from '../utils';

export type TSTypeParameterNode = JSNodeBase & {
  type: 'TSTypeParameter';
  name: string;
  default?: undefined | TSTypeNode;
  constraint?: undefined | TSTypeNode;
};

export const tsTypeParameter = createBuilder<TSTypeParameterNode>(
  'TSTypeParameter',
  {visitorKeys: ['default', 'constraint']},
);
