import {
  JSNodeBase,
  VariableDeclarationNode,
  ExpressionNode,
  StatementNode,
} from '../index';
import {createBuilder} from '../utils';

export type ForInStatementNode = JSNodeBase & {
  type: 'ForInStatement',
  left: VariableDeclarationNode | ExpressionNode,
  right: ExpressionNode,
  body: StatementNode,
};

export const forInStatement = createBuilder<ForInStatementNode>(
  'ForInStatement',
  {visitorKeys: ['left', 'right', 'body']},
);
