import Generator from '../../Generator';
import * as t from '@romejs/js-ast';

export function LabeledStatement(
  generator: Generator,
  node: t.LabeledStatementNode,
) {
  generator.print(node.label, node);
  generator.token(':');
  generator.space();
  generator.print(node.body, node);
}
