import {Node} from '@romejs/js-ast';
import {Path} from '@romejs/js-compiler';
import {template, doesNodeMatchPattern} from '@romejs/js-ast-utils';
import {arrayExpression, stringLiteral} from '@romejs/js-ast';
import {getOptions, getModuleId} from './_utils';

export default {
  name: 'asyncImportTransform',
  enter(path: Path): Node {
    const {node, context} = path;
    const opts = getOptions(context);

    if (node.type !== 'CallExpression') {
      return node;
    }

    // desugar import('source') to Rome.import(moduleId)
    if (node.callee.type === 'Import') {
      const id = stringLiteral.create({
        value: getModuleId(node.arguments[0].value, opts),
      });
      return template.expression`Rome.import(${id})`;
    }

    // desugar requireLazy/requireWeak('source', callback) to Bootloader.loadModules(['source'], callback);
    if (
      node.callee.type === 'Identifier' &&
      (node.callee.name === 'requireLazy' || node.callee.name === 'requireWeak')
    ) {
      return template.expression`Bootloader.loadModules([${
        node.arguments[0]
      }], ${node.arguments[1]});`;
    }

    // desugar Bootloader.loadModules([...modules], callback) to Promise.all([Rome.import(moduleId)]).then(callback);
    if (doesNodeMatchPattern(node.callee, 'Bootloader.loadModules')) {
      const [sources, callback] = node.arguments;

      if (sources.type !== 'ArrayExpression') {
        // analyzeDependencies would have thrown an error for this
        return node;
      }

      const promises = [];
      for (const source of sources.elements) {
        if (source.type !== 'StringLiteral') {
          // analyzeDependencies would have thrown an error for this
          continue;
        }

        const id = stringLiteral.create({
          value: getModuleId(source.value, opts),
        });
        promises.push(template.expression`Rome.import(${id})`);
      }

      return template.expression`Promise.all(${arrayExpression.create({
        elements: promises,
      })}).then(${callback});`;
    }

    return node;
  },
};
