import Reporter from './Reporter';
import {ReporterSpinnerGroup, ReporterSetSpinner} from './types';
import Spinner from './Spinner';
import {formatAnsi, leftPad, rightPad} from '@romejs/string-ansi';
import {humanizeNumber} from '@romejs/string-utils';
import readline = require('readline');

export default function spinnerGroup(
  reporter: Reporter,
  total: number,
  workers: number,
): ReporterSpinnerGroup {
  const spinners: Array<ReporterSetSpinner> = [];

  // Save hasClearScreen so we can check it later
  let hasClearScreen = reporter.hasClearScreen;

  // Create lines for worker spinners
  for (let i = 1; i < workers; i++) {
    reporter.stderr.write('\n');
  }

  // Create line for progress bar
  reporter.stderr.write('\n');
  const progress = reporter.progress();
  progress.setTotal(total);

  // The largest total/current padding of all the activity sets so we can properly align the progress indicator
  let largestCurrentPad = 0;
  let largestTotalPad = 0;
  const updatePrefixes: Array<() => void> = [];
  function updateAllPrefixes() {
    for (const updatePrefix of updatePrefixes) {
      updatePrefix();
    }
  }

  // Set hasClearScreen to true if it was previously true after an activity set has ran, it will be removed from the screen, resulting in a cleared screen
  if (hasClearScreen) {
    reporter.hasClearScreen = true;
  }

  for (let i = 0; i < workers; i++) {
    const spinner = new Spinner(reporter, i + 1);
    spinner.start();

    let prefix: undefined | string = undefined;
    let total = 0;
    let current = 0;
    const updatePrefix = () => {
      const currentStr: string = current === 0 ? '-' : humanizeNumber(current);
      const totalStr: string = total === 0 ? '-' : humanizeNumber(total);
      const fullStr: string = `${leftPad(
        currentStr,
        largestCurrentPad,
      )}/${rightPad(totalStr, largestTotalPad)}`;
      const brackets: string = `[${fullStr}]`;
      spinner.setPrefix(`${formatAnsi.dim(brackets)} `);
    };
    updatePrefixes.push(updatePrefix);
    const clear = () => {
      prefix = undefined;
      current = 0;
      updatePrefix();
      spinner.setText('Waiting...');
    };
    clear();

    spinners.unshift({
      clear,

      tick() {
        current++;
        updatePrefix();
      },

      setCurrent(newCurrent: number) {
        current = newCurrent;

        const ourCurrentPad = humanizeNumber(current).length;
        if (ourCurrentPad > largestCurrentPad) {
          largestCurrentPad = ourCurrentPad;
          updateAllPrefixes();
        } else {
          updatePrefix();
        }
      },

      setTotal(newTotal: number) {
        total = newTotal;

        const ourTotalPad = humanizeNumber(total).length;
        if (ourTotalPad > largestTotalPad) {
          largestTotalPad = ourTotalPad;
          updateAllPrefixes();
        } else {
          updatePrefix();
        }
      },

      setPrefix(_prefix: string) {
        prefix = _prefix;
        spinner.setText(prefix);
        updatePrefix();
      },

      setText(msg: string) {
        if (prefix !== undefined) {
          msg = `${prefix}: ${msg}`;
        }
        spinner.setText(msg);
      },

      resume() {
        spinner.resume();
      },

      pause() {
        spinner.pause();
      },

      end() {
        spinner.stop();
      },
    });
  }

  const spinnerGroup: ReporterSpinnerGroup = {
    setText(text: string) {
      progress.setText(text);
    },

    setCurrent(current: number) {
      progress.setCurrent(current);
    },

    setTotal(total: number) {
      progress.setTotal(total);
    },

    tick() {
      progress.tick();
    },

    end: () => {
      reporter.activeElements.delete(spinnerGroup);
      progress.end();
      for (const spinner of spinners) {
        spinner.end();
      }
      readline.moveCursor(reporter.stderr, 0, -workers);
    },

    spinners,
  };
  reporter.activeElements.add(spinnerGroup);
  return spinnerGroup;
}
