// Like Array#filter, but returns a tuple [ acceptedElements, discardedElements ]
function partition<T>(
  list: Array<T>,
  test: (item: T, index: number, list: Array<T>) => undefined | boolean,
): [Array<T>, Array<T>] {
  const accepted: Array<T> = [];
  const rejected: Array<T> = [];
  for (let i = 0; i < list.length; i++) {
    const item = list[i];
    if (test(item, i, list)) {
      accepted.push(item);
    } else {
      rejected.push(item);
    }
  }
  return [accepted, rejected];
}

// Check if property name clashes with already added.
// Object/class getters and setters are not allowed to clash 
// either with each other or with an init property  and in
// strict mode, init properties are also not allowed to be repeated.
export function checkPropClash(
  parser: Parser,
  prop: n.ObjectMemberNode,
  props: Set<string>,
): void {
  if (prop.computed || prop.kind || parser.getExtra(prop, 'shorthand')) {
    return undefined;
  }

  const key = prop.key;
  // It is either an Identifier or a String/NumericLiteral
  const name = key.type === 'Identifier' ? key.name : String(key.value);

  if (name === '__proto__') {
    if (props.has('proto')) {
      parser.addDiagnostic({
        message: 'Redefinition of __proto__ property',
        loc: key.loc,
      });
    } else {
      props.add('proto');
    }
  }
}

// ### Expression parsing

// These nest, from the most general expression type at the top to
// 'atomic', nondivisible expression types at the bottom. Most of
// the functions will simply let the function (s) below them parse,
// and, *if* the syntactic construct they handle is present, wrap
// the AST node that the inner parser gave them in another node.

// Parse a full expression. The optional arguments are used to
// forbid the `in` operator (in for loops initialization expressions)
// and provide reference for storing '=' operator inside shorthand
// property assignment in contexts where both object expression
// and object pattern might appear (so it's possible to raise
// delayed syntax error at correct position).
export function parseExpression(
  parser: Parser,
  context: ExpressionContext,
  noIn?: boolean,
  refShorthandDefaultPos?: IndexTracker,
): n.ExpressionNode {
  const startPos = parser.state.startPos;
  const expr = parseMaybeAssign(parser, context, noIn, refShorthandDefaultPos);
  if (parser.match(tt.comma)) {
    const expressions = [expr];
    while (parser.eat(tt.comma)) {
      expressions.push(
        parseMaybeAssign(parser, context, noIn, refShorthandDefaultPos),
      );
    }
    toReferencedList(parser, expressions);
    return {
      loc: parser.finishLoc(startPos),
      type: 'SequenceExpression',
      expressions,
    };
  }
  return expr;
}

// Parse an assignment expression. This includes applications of
// operators like `+=`.

// We need to support type parameter declarations for arrow functions. This
// is tricky. There are three situations we need to handle
//
// 1. This is either JSX or an arrow function. We'll try JSX first. If that
//    fails, we'll try an arrow function. If that fails, we'll throw the JSX
//    error.
// 2. This is an arrow function. We'll parse the type parameter declaration,
//    parse the rest, make sure the rest is an arrow function, and go from
//    there
// 3. This is neither. Just call the super method
export function parseMaybeAssign(
  parser: Parser,
  context: ExpressionContext,
  noIn?: undefined | boolean,
  refShorthandDefaultPos?: undefined | IndexTracker,
  afterLeftParse?: MaybeAssignAfterParse,
  refNeedsArrowPos?: undefined | IndexTracker,
): n.ExpressionNode {
  const branches = parser.createBranch<n.ExpressionNode>();

  // Try parsing as JSX
  if (
    (parser.isRelational('<') || parser.match(tt.jsxTagStart)) &&
    parser.shouldTokenizeJSX()
  ) {
    branches.add(
      () => {
        return _parseMaybeAssign(
          parser,
          context,
          noIn,
          refShorthandDefaultPos,
          afterLeftParse,
          refNeedsArrowPos,
        );
      },
      {diagnosticsPriority: 1},
    );

    // Remove `tc.j_expr` and `tc.j_oTag` from 'context added
    // by parsing `jsxTagStart` to stop the JSX plugin from
    // messing with the tokens
    const cLength = parser.state.context.length;
    if (parser.state.context[cLength - 1] === tc.jsxOpenTag) {
      parser.state.context.length -= 2;
    }
    finishToken(parser, tt.relational, '<');
  }

  // Try parsing as an arrow function with type parameters
  if (parser.isRelational('<')) {
    branches.add(() => {
      const start = parser.getPosition();
      const typeParameters = parseTypeParameters(parser);
      const arrowExpression = forwardNoArrowParamsConversionAt(
        parser,
        start,
        () =>
          _parseMaybeAssign(
            parser,
            context,
            noIn,
            refShorthandDefaultPos,
            afterLeftParse,
            refNeedsArrowPos,
          ),
      );
      parser.resetStartLocationFromNode(arrowExpression, typeParameters);

      if (arrowExpression.type === 'ArrowFunctionExpression') {
        return {
          ...arrowExpression,
          typeParameters,
        };
      } else {
        parser.addDiagnostic({
          loc: typeParameters.loc,
          message:
            'Expected an arrow function after this type parameter declaration',
        });
        return parser.createUnknownNode('type params without arrow function');
      }
    });
  }

  branches.add(() => {
    return _parseMaybeAssign(
      parser,
      context,
      noIn,
      refShorthandDefaultPos,
      afterLeftParse,
      refNeedsArrowPos,
    );
  });

  // Pick the branch with the least amount of errors
  return branches.pick();
}

type MaybeAssignAfterParse = (
  parser: Parser,
  left: n.ExpressionNode,
  startPos: Position,
) => n.ExpressionNode;

function _parseMaybeAssign(
  parser: Parser,
  context: ExpressionContext,
  noIn?: undefined | boolean,
  refShorthandDefaultPos?: undefined | IndexTracker,
  afterLeftParse?: MaybeAssignAfterParse,
  refNeedsArrowPos?: undefined | IndexTracker,
): n.ExpressionNode {
  const startPos = parser.state.startPos;

  if (parser.isContextual('yield')) {
    if (parser.inScope('GENERATOR')) {
      let left = parseYield(parser, noIn);
      if (afterLeftParse) {
        left = afterLeftParse(parser, left, startPos);
      }
      return left;
    } else {
      // The tokenizer will assume an expression is allowed after
      // `yield`, but this isn't that kind of yield
      parser.state.exprAllowed = false;
    }
  }

  const oldCommaAfterSpreadAt = parser.state.commaAfterSpreadAt;
  parser.state.commaAfterSpreadAt = -1;

  let failOnShorthandAssign;
  if (refShorthandDefaultPos) {
    failOnShorthandAssign = false;
  } else {
    refShorthandDefaultPos = {index: 0};
    failOnShorthandAssign = true;
  }

  if (parser.match(tt.parenL) || parser.match(tt.name)) {
    parser.state.potentialArrowAt = parser.state.startPos.index;
  }

  let left = parseMaybeConditional(
    parser,
    context,
    noIn,
    refShorthandDefaultPos,
    refNeedsArrowPos,
  );
  if (afterLeftParse) {
    left = afterLeftParse(parser, left, startPos);
  }

  if (parser.state.tokenType.isAssign) {
    const operator = String(parser.state.tokenValue);
    left = parser.match(tt.eq)
      ? toAssignable(parser, left, undefined, 'assignment expression')
      : left;

    // reset because shorthand default was used correctly
    refShorthandDefaultPos.index = 0;

    checkLVal(parser, left, undefined, undefined, 'assignment expression');

    if (left.type === 'ArrayPattern' || left.type === 'ObjectPattern') {
      checkCommaAfterRestFromSpread(parser);
    }

    parser.state.commaAfterSpreadAt = oldCommaAfterSpreadAt;

    parser.next();
    const right = parseMaybeAssign(parser, 'assignment right', noIn);
    return {
      loc: parser.finishLoc(startPos),
      type: 'AssignmentExpression',
      // $FlowFixMe
      operator,
      left,
      right,
    } as n.AssignmentExpressionNode;
  } else if (failOnShorthandAssign && refShorthandDefaultPos.index > 0) {
    parser.unexpectedToken(
      parser.getPositionFromIndex(refShorthandDefaultPos.index),
    );
  }

  parser.state.commaAfterSpreadAt = oldCommaAfterSpreadAt;

  return left;
}

export function parseMaybeConditional(
  parser: Parser,
  context: ExpressionContext,
  noIn: undefined | boolean,
  refShorthandDefaultPos: IndexTracker,
  refNeedsArrowPos?: undefined | IndexTracker,
): n.ExpressionNode {
  const startPos = parser.state.startPos;
  const potentialArrowAt = parser.state.potentialArrowAt;
  const expr = parseExpressionOps(
    parser,
    context,
    noIn,
    refShorthandDefaultPos,
  );

  if (
    expr.type === 'ArrowFunctionExpression' &&
    parser.getLoc(expr).start.index === potentialArrowAt
  ) {
    return expr;
  }

  if (refShorthandDefaultPos && refShorthandDefaultPos.index > 0) {
    return expr;
  }

  return parseConditional(parser, expr, noIn, startPos, refNeedsArrowPos);
}

export function tryParseConditionalConsequent(
  parser: Parser,
): {
  consequent: n.ExpressionNode;
  failed: boolean;
} {
  const brancher = parser.createBranch<{
    consequent: n.ExpressionNode;
    failed: boolean;
  }>();

  brancher.add(() => {
    parser.state.noArrowParamsConversionAt.push(parser.state.startPos.index);
    const consequent = parseMaybeAssign(parser, 'conditional consequent');
    parser.state.noArrowParamsConversionAt.pop();
    return {
      consequent,
      failed: !parser.match(tt.colon),
    };
  });

  return brancher.pick();
}

// Given an expression, walks throught its arrow functions whose body is
// an expression and throught conditional expressions. It returns every
// function which has been parsed with a return type but could have been
// parenthesized expressions.
// These functions are separated into two arrays: one containing the ones
// whose parameters can be converted to assignable lists, one containing the
// others.
function getArrowLikeExpressions(
  parser: Parser,
  node: n.Node,
  disallowInvalid: boolean = false,
): [
  Array<n.ArrowFunctionExpressionNode>,
  Array<n.ArrowFunctionExpressionNode>
] {
  const stack = [node];
  const arrows: Array<n.ArrowFunctionExpressionNode> = [];

  while (stack.length > 0) {
    const node = stack.pop();
    if (node.type === 'ArrowFunctionExpression') {
      if (node.typeParameters || !node.returnType) {
        // This is an arrow expression without ambiguity, so check its parameters
        toAssignableList(
          parser,
          // node.params is Array<Expression> instead of Array<Pattern> because it
          // has not been converted yet.
          node.params,
          true,
          'arrow function parameters',
        );

        // force the parameters to be checked
        checkFunctionNameAndParams(parser, node, true, true);
      } else {
        arrows.push(node);
      }
      stack.push(node.body);
    } else if (node.type === 'ConditionalExpression') {
      stack.push(node.consequent);
      stack.push(node.alternate);
    }
  }

  if (disallowInvalid) {
    for (let i = 0; i < arrows.length; i++) {
      toAssignableList(parser, node.params, true, 'arrow function parameters');
    }
    return [arrows, []];
  }

  return partition(arrows, node => {
    const branches = parser.createBranch<void>();

    branches.add(
      () => {
        toAssignableList(
          parser,
          node.params,
          true,
          'arrow function parameters',
        );
      },
      {maxNewDiagnostics: 0},
    );

    return branches.hasBranch();
  });
}

export function parseConditional(
  parser: Parser,
  expr: n.ExpressionNode,
  noIn: undefined | boolean,
  startPos: Position,
  refNeedsArrowPos?: undefined | IndexTracker,
): n.ExpressionNode {
  if (!parser.match(tt.question)) {
    return expr;
  }

  // This is to handle a case like this: const foo = (foo?: bar) => {};
  // We'll be called due to the `?`, and we should mark ourselves as an
  // expected arrow function if parsing as a regular conditional fails
  if (refNeedsArrowPos) {
    const branch = parser.createBranch();

    branch.add(() => _parseConditional(parser, expr, noIn, startPos), {
      maxNewDiagnostics: 0,
    });

    if (branch.hasBranch()) {
      return branch.pick();
    } else {
      refNeedsArrowPos.index = parser.state.startPos.index;
      return expr;
    }
  }

  parser.expect(tt.question);
  const originalNoArrowAt = parser.state.noArrowAt;
  const origState = parser.cloneState();
  let {consequent, failed} = tryParseConditionalConsequent(parser);
  let [valid, invalid] = getArrowLikeExpressions(parser, consequent);

  if (failed || invalid.length > 0) {
    const noArrowAt = [...originalNoArrowAt];

    if (invalid.length > 0) {
      parser.state = origState;
      parser.state.noArrowAt = noArrowAt;

      for (let i = 0; i < invalid.length; i++) {
        noArrowAt.push(parser.getLoc(invalid[i]).start.index);
      }

      ({consequent, failed} = tryParseConditionalConsequent(parser));
      [valid, invalid] = getArrowLikeExpressions(parser, consequent);
    }

    if (failed && valid.length > 1) {
      // if there are two or more possible correct ways of parsing, throw an
      // error.
      // e.g.   Source: a ? (b): c => (d): e => f
      //      Result 1: a ? b : (c => ((d): e => f))
      //      Result 2: a ? ((b): c => d) : (e => f)
      parser.addDiagnostic({
        message:
          'Ambiguous expression: wrap the arrow functions in parentheses to disambiguate.',
      });
    }

    if (failed && valid.length === 1) {
      parser.state = origState;
      parser.state.noArrowAt = noArrowAt.concat(
        parser.getLoc(valid[0]).start.index,
      );
      ({consequent, failed} = tryParseConditionalConsequent(parser));
    }

    getArrowLikeExpressions(parser, consequent, true);
  }

  parser.state.noArrowAt = originalNoArrowAt;

  if (!parser.eat(tt.colon)) {
    parser.addDiagnostic({
      message: 'Missing conditional expression consequent separator',
    });
  }

  const alternate = forwardNoArrowParamsConversionAt(parser, startPos, () =>
    parseMaybeAssign(
      parser,
      'conditional alternate',
      noIn,
      undefined,
      undefined,
      undefined,
    ),
  );

  return {
    loc: parser.finishLoc(startPos),
    type: 'ConditionalExpression',
    test: expr,
    consequent,
    alternate,
  };
}

export function forwardNoArrowParamsConversionAt<T>(
  parser: Parser,
  start: Position,
  parse: () => T,
): T {
  if (parser.state.noArrowParamsConversionAt.indexOf(start.index) !== -1) {
    let result: T;
    parser.state.noArrowParamsConversionAt.push(parser.state.startPos.index);
    result = parse();
    parser.state.noArrowParamsConversionAt.pop();
    return result;
  } else {
    return parse();
  }
}

function _parseConditional(
  parser: Parser,
  expr: n.ExpressionNode,
  noIn: undefined | boolean,
  startPos: Position,
): n.ExpressionNode {
  if (parser.eat(tt.question)) {
    const test = expr;
    const consequent = parseMaybeAssign(parser, 'conditional consequent');
    parser.expect(tt.colon);
    const alternate = parseMaybeAssign(parser, 'conditional alternate', noIn);
    return {
      loc: parser.finishLoc(startPos),
      type: 'ConditionalExpression',
      test,
      consequent,
      alternate,
    };
  }
  return expr;
}

export function parseExpressionOps(
  parser: Parser,
  context: ExpressionContext,
  noIn: undefined | boolean,
  refShorthandDefaultPos: IndexTracker,
): n.ExpressionNode {
  const startPos = parser.state.startPos;
  const potentialArrowAt = parser.state.potentialArrowAt;
  const expr = parseMaybeUnary(parser, context, refShorthandDefaultPos);

  if (
    expr.type === 'ArrowFunctionExpression' &&
    parser.getLoc(expr).start.index === potentialArrowAt
  ) {
    return expr;
  }
  if (refShorthandDefaultPos && refShorthandDefaultPos.index > 0) {
    return expr;
  }

  return parseExpressionOp(parser, context, expr, startPos, -1, noIn);
}

// Parse binary operators with the operator precedence parsing
// algorithm. `left` is the left-hand side of the operator.
// `minPrec` provides context that allows the function to stop and
// defer further parser to one of its callers when it encounters an
// operator that has a lower precedence than the set it is parsing.

export function parseExpressionOp(
  parser: Parser,
  context: ExpressionContext,
  left: n.ExpressionNode,
  leftStartPos: Position,
  minPrec: number,
  noIn: boolean = false,
): n.ExpressionNode {
  if (
    tt._in.getBinop() > minPrec &&
    !parser.hasPrecedingLineBreak() &&
    parser.isContextual('as')
  ) {
    const _const = tryTSNextParseConstantContext(parser);

    let typeAnnotation;
    if (_const) {
      tsCheckLiteralForConstantContext(parser, left);
      typeAnnotation = _const;
    } else {
      typeAnnotation = tsNextThenParseType(parser);
    }

    const node = {
      type: 'TSAsExpression',
      loc: parser.finishLoc(leftStartPos),
      typeAnnotation,
      expression: left,
    } as n.TSAsExpressionNode;

    return parseExpressionOp(
      parser,
      context,
      node,
      leftStartPos,
      minPrec,
      noIn,
    );
  }

  const prec = parser.state.tokenType.binop;
  if (prec !== undefined && (!noIn || !parser.match(tt._in))) {
    if (prec > minPrec) {
      const operator = String(parser.state.tokenValue);

      if (
        operator === '**' &&
        left.type === 'UnaryExpression' &&
        !parser.isParenthesized(left)
      ) {
        parser.addDiagnostic({
          loc: left.argument.loc,
          message:
            'Illegal expression. Wrap left hand side or entire exponentiation in parentheses.',
        });
      }

      const op = parser.state.tokenType;
      parser.next();

      const startPos = parser.state.startPos;

      const right = parseExpressionOp(
        parser,
        context,
        parseMaybeUnary(parser, context),
        startPos,
        op.rightAssociative ? prec - 1 : prec,
        noIn,
      );

      let node: n.LogicalExpressionNode | n.BinaryExpressionNode;
      if (operator === '||' || operator === '&&' || operator === '??') {
        node = {
          loc: parser.finishLoc(leftStartPos),
          type: 'LogicalExpression',
          left,
          right,
          operator,
        } as n.LogicalExpressionNode;
      } else {
        node = {
          loc: parser.finishLoc(leftStartPos),
          type: 'BinaryExpression',
          left,
          right,
          operator,
        } as n.BinaryExpressionNode;
      }

      return parseExpressionOp(
        parser,
        context,
        node,
        leftStartPos,
        minPrec,
        noIn,
      );
    }
  }

  return left;
}

// Parse unary operators, both prefix and postfix.
export function parseMaybeUnary(
  parser: Parser,
  context: ExpressionContext,
  refShorthandDefaultPos?: IndexTracker,
): n.ExpressionNode {
  if (
    parser.isSyntaxEnabled('ts') &&
    !parser.isSyntaxEnabled('jsx') &&
    parser.isRelational('<')
  ) {
    return parseTSTypeAssertion(parser);
  }

  if (parser.isContextual('await') && parser.inScope('ASYNC')) {
    return parseAwait(parser);
  }

  if (parser.state.tokenType.prefix) {
    const start = parser.getPosition();
    const update = parser.match(tt.incDec);
    const operator = String(parser.state.tokenValue);
    const prefix = true;

    parser.next();

    const argument = parseMaybeUnary(parser, context);

    if (refShorthandDefaultPos && refShorthandDefaultPos.index > 0) {
      parser.unexpectedToken(
        parser.getPositionFromIndex(refShorthandDefaultPos.index),
      );
    }

    if (update) {
      checkLVal(parser, argument, undefined, undefined, 'prefix operation');
    } else if (parser.inScope('STRICT') && operator === 'delete') {
      if (argument.type === 'Identifier') {
        parser.addDiagnostic({
          loc: argument.loc,
          message: 'Deleting local variable in strict mode',
        });
      } else if (
        argument.type === 'MemberExpression' &&
        argument.property.type === 'PrivateName'
      ) {
        parser.addDiagnostic({
          loc: argument.property.loc,
          message: 'Deleting a private field is not allowed',
        });
      }
    }

    let node: n.UpdateExpressionNode | n.UnaryExpressionNode;
    if (update) {
      invariant(
        operator === '++' || operator === '--',
        'Expected ++/-- operator only for UpdateExpression',
      );
      node = {
        loc: parser.finishLoc(start),
        type: 'UpdateExpression',
        argument,
        operator,
        prefix,
      } as n.UpdateExpressionNode;
    } else {
      invariant(
        operator !== '++' && operator !== '--',
        'BinaryExpression cannot have ++/-- operator',
      );
      node = {
        loc: parser.finishLoc(start),
        type: 'UnaryExpression',
        argument,
        // $FlowFixMe
        operator,
        prefix,
      } as n.UnaryExpressionNode;
    }

    return node;
  }

  const startPos = parser.state.startPos;

  let expr = parseExpressionWithPossibleSubscripts(
    parser,
    context,
    refShorthandDefaultPos,
  );
  if (refShorthandDefaultPos && refShorthandDefaultPos.index > 0) {
    return expr;
  }

  while (parser.state.tokenType.postfix && !parser.canInsertSemicolon()) {
    const operator = parser.state.tokenValue;
    checkLVal(parser, expr, undefined, undefined, 'postfix operation');
    parser.next();
    expr = {
      loc: parser.finishLoc(startPos),
      type: 'UpdateExpression',
      operator,
      prefix: false,
      argument: expr,
    };
  }

  return expr;
}

// Parse call, dot, and `[]`-subscript expressions.
export function parseExpressionWithPossibleSubscripts(
  parser: Parser,
  context: ExpressionContext,
  refShorthandDefaultPos?: IndexTracker,
): n.ExpressionNode {
  const startPos = parser.state.startPos;
  const potentialArrowAt = parser.state.potentialArrowAt;
  const expr = parseExpressionAtom(parser, context, refShorthandDefaultPos);

  if (
    expr.type === 'ArrowFunctionExpression' &&
    parser.getLoc(expr).start.index === potentialArrowAt
  ) {
    return expr;
  }

  if (refShorthandDefaultPos && refShorthandDefaultPos.index > 0) {
    return expr;
  }

  return parseSubscripts(parser, expr, startPos);
}

export function parseSubscripts(
  parser: Parser,
  base: n.ExpressionNode,
  startPos: Position,
  noCalls?: undefined | boolean,
): n.ExpressionNode {
  const maybeAsyncArrow = atPossibleAsync(parser, base);

  if (
    base.type === 'Identifier' &&
    base.name === 'async' &&
    parser.state.noArrowAt.includes(startPos.index)
  ) {
    const openContext = parser.expectOpening(
      tt.parenL,
      tt.parenR,
      'call arguments',
    );
    const callee = base;
    const args = parseCallExpressionArguments(
      parser,
      callee,
      openContext,
      false,
    );
    base = {
      type: 'CallExpression',
      loc: parser.finishLoc(startPos),
      callee,
      arguments: args,
    };
  } else if (
    base.type === 'Identifier' &&
    base.name === 'async' &&
    parser.isRelational('<')
  ) {
    const branch = parser.createBranch<n.ExpressionNode>();
    branch.add(() => parseAsyncArrowWithFlowTypeParameters(parser, startPos));
    branch.add(() =>
      parseExpressionSubscriptsRecursively(
        parser,
        base,
        startPos,
        noCalls,
        maybeAsyncArrow,
      ),
    );
    return branch.pick();
  }

  return parseExpressionSubscriptsRecursively(
    parser,
    base,
    startPos,
    noCalls,
    maybeAsyncArrow,
  );
}

function parseExpressionSubscriptsRecursively(
  parser: Parser,
  base: n.ExpressionNode,
  startPos: Position,
  noCalls: undefined | boolean,
  maybeAsyncArrow: boolean,
): n.ExpressionNode {
  const state: ParseSubscriptState = {
    optionalChainMember: false,
    stop: false,
  };
  do {
    base = parseExpressionSubscript(
      parser,
      base,
      startPos,
      noCalls,
      state,
      maybeAsyncArrow,
    );
  } while (!state.stop);
  return base;
}

type ParseSubscriptState = {
  stop: boolean;
  optionalChainMember: boolean;
};

export function parseExpressionSubscript(
  parser: Parser,
  base: n.ExpressionNode,
  startPos: Position,
  noCalls: boolean = false,
  state: ParseSubscriptState,
  maybeAsyncArrow: boolean,
): n.ExpressionNode {
  if (!parser.hasPrecedingLineBreak() && parser.match(tt.bang)) {
    parser.state.exprAllowed = false;
    parser.next();

    return {
      type: 'TSNonNullExpression',
      loc: parser.finishLoc(startPos),
      expression: base,
    } as n.TSNonNullExpressionNode;
  }

  if (parser.match(tt.questionDot)) {
    state.optionalChainMember = true;

    if (noCalls && parser.lookaheadState().tokenType == tt.parenL) {
      state.stop = true;
      return base;
    }

    parser.next();

    // eg: o.m?.<T>(e);
    if (parser.isRelational('<')) {
      if (noCalls) {
        state.stop = true;
        return base;
      }

      const callee = base;
      const typeArguments = parseTypeCallArguments(parser);
      const openContext = parser.expectOpening(
        tt.parenL,
        tt.parenR,
        'call arguments',
      );
      const args = parseCallExpressionArguments(
        parser,
        callee,
        openContext,
        false,
      );
      return {
        loc: parser.finishLoc(startPos),
        type: 'OptionalCallExpression',
        arguments: args,
        callee,
        typeArguments,
      } as n.OptionalCallExpressionNode;
    }

    if (parser.match(tt.bracketL)) {
      const openContext = parser.expectOpening(
        tt.bracketL,
        tt.bracketR,
        'computed property',
      );
      const object = base;
      const property = parseExpression(
        parser,
        'optional member expression property',
      );
      const computed = true;
      parser.expectClosing(openContext);
      return {
        loc: parser.finishLoc(startPos),
        type: 'OptionalMemberExpression',
        object,
        property,
        computed,
      } as n.OptionalMemberExpressionNode;
    }

    if (parser.match(tt.parenL)) {
      const openContext = parser.expectOpening(
        tt.parenL,
        tt.parenR,
        'call arguments',
      );
      const callee = base;
      const args = parseCallExpressionArguments(
        parser,
        callee,
        openContext,
        maybeAsyncArrow,
      );

      return {
        loc: parser.finishLoc(startPos),
        type: 'OptionalCallExpression',
        callee,
        arguments: args,
      } as n.OptionalCallExpressionNode;
    }

    const object = base;
    const property = parseIdentifier(parser, true);
    const computed = false;
    return {
      loc: parser.finishLoc(startPos),
      type: 'OptionalMemberExpression',
      object,
      property,
      computed,
    } as n.OptionalMemberExpressionNode;
  }

  if (parser.eat(tt.dot)) {
    const object = base;
    const property = parseMaybePrivateName(parser);
    const computed = false;

    return {
      loc: parser.finishLoc(startPos),
      type: 'MemberExpression',
      object,
      property,
      computed,
    } as n.MemberExpressionNode;
  }

  if (parser.match(tt.bracketL)) {
    const openContext = parser.expectOpening(
      tt.bracketL,
      tt.bracketR,
      'computed property',
    );
    const object = base;
    const property = parseExpression(
      parser,
      'member expression computed property',
    );
    const computed = true;
    parser.expectClosing(openContext);

    return {
      loc: parser.finishLoc(startPos),
      type: 'MemberExpression',
      object,
      property,
      computed,
    } as n.MemberExpressionNode;
  }

  // Supports: foo<Foo>(); and foo<Foo>``;
  if (parser.isRelational('<') && isTypeSystemEnabled(parser)) {
    const possibleCallExpression = parser.tryBranch(() => {
      const typeArguments = parseTypeCallArguments(parser);

      if (!noCalls && parser.match(tt.parenL)) {
        const openContext = parser.expectOpening(
          tt.parenL,
          tt.parenR,
          'call arguments',
        );
        const args = parseCallExpressionArguments(
          parser,
          base,
          openContext,
          false,
        );
        return {
          loc: parser.finishLoc(startPos),
          type: 'CallExpression',
          arguments: args,
          callee: base,
          typeArguments,
        } as n.CallExpressionNode;
      }

      if (parser.match(tt.backQuote)) {
        return parseTaggedTemplateExpression(
          parser,
          startPos,
          base,
          state,
          typeArguments,
        );
      }
    });

    if (possibleCallExpression !== undefined) {
      return possibleCallExpression;
    }
  }

  if (!noCalls && parser.match(tt.parenL)) {
    const oldMaybeInArrowParameters = parser.state.maybeInArrowParameters;
    const oldYieldPos = parser.state.yieldPos;
    const oldAwaitPos = parser.state.awaitPos;
    parser.state.maybeInArrowParameters = true;
    parser.state.yieldPos = 0;
    parser.state.awaitPos = 0;

    const openContext = parser.expectOpening(
      tt.parenL,
      tt.parenR,
      'call arguments',
    );
    const callee = base;

    const oldCommaAfterSpreadAt = parser.state.commaAfterSpreadAt;
    parser.state.commaAfterSpreadAt = -1;

    const args = parseCallExpressionArguments(
      parser,
      callee,
      openContext,
      maybeAsyncArrow,
    );

    if (maybeAsyncArrow && shouldParseAsyncArrow(parser)) {
      state.stop = true;

      checkCommaAfterRestFromSpread(parser);

      const node = parseAsyncArrowFromCallExpression(parser, startPos, args);
      checkYieldAwaitInDefaultParams(parser);
      parser.state.yieldPos = oldYieldPos;
      parser.state.awaitPos = oldAwaitPos;
      return node;
    } else {
      toReferencedListDeep(parser, args);

      // We keep the old value if it isn't null, for cases like
      //   (x = async(yield)) => {}
      parser.state.yieldPos = oldYieldPos || parser.state.yieldPos;
      parser.state.awaitPos = oldAwaitPos || parser.state.awaitPos;
    }

    parser.state.maybeInArrowParameters = oldMaybeInArrowParameters;
    parser.state.commaAfterSpreadAt = oldCommaAfterSpreadAt;

    validateCallExpression(parser, callee, args);

    return {
      type: 'CallExpression',
      loc: parser.finishLoc(startPos),
      callee,
      arguments: args,
    } as n.CallExpressionNode;
  }

  if (parser.match(tt.backQuote)) {
    return parseTaggedTemplateExpression(parser, startPos, base, state);
  }

  state.stop = true;
  return base;
}

export function parseTaggedTemplateExpression(
  parser: Parser,
  startPos: Position,
  tag: n.ExpressionNode,
  state: ParseSubscriptState,
  typeArguments?: n.TypeArgumentsNode,
): n.TaggedTemplateExpressionNode {
  if (state.optionalChainMember) {
    parser.addDiagnostic({
      message: 'Tagged Template Literals are not allowed in optionalChain',
    });
  }

  const quasi = parseTemplate(parser, true);
  return {
    loc: parser.finishLoc(startPos),
    type: 'TaggedTemplateExpression',
    tag,
    quasi,
    typeArguments,
  };
}

export function checkYieldAwaitInDefaultParams(parser: Parser) {
  if (
    parser.state.yieldPos > 0 &&
    (parser.state.awaitPos === 0 ||
      parser.state.yieldPos < parser.state.awaitPos)
  ) {
    parser.addDiagnostic({
      index: parser.state.yieldPos,
      message: 'Yield cannot be used as name inside a generator function',
    });
  }

  if (parser.state.awaitPos > 0) {
    parser.addDiagnostic({
      index: parser.state.awaitPos,
      message: 'Await cannot be used as name inside an async function',
    });
  }
}

export function atPossibleAsync(
  parser: Parser,
  base: n.ExpressionNode,
): boolean {
  const loc = parser.getLoc(base);
  return (
    base.type === 'Identifier' &&
    base.name === 'async' &&
    parser.state.lastEndPos.index === loc.end.index &&
    !parser.canInsertSemicolon() &&
    parser.getRawInput(loc.start.index, loc.end.index) === 'async'
  );
}

export function validateCallExpression(
  parser: Parser,
  callee: n.Node,
  args: Array<n.Node>,
): void {
  if (callee.type === 'Import') {
    if (args.length !== 1) {
      parser.addDiagnostic({
        loc: callee.loc,
        message: 'import() requires exactly one argument',
      });
    }

    const importArg = args[0];
    if (importArg && importArg.type === 'SpreadElement') {
      parser.addDiagnostic({
        loc: importArg.loc,
        message: 'Spread is not allowed in import()',
      });
    }
  }
}

export function parseCallExpressionArguments(
  parser: Parser,
  callee: n.Node,
  openContext: OpeningContext,
  possibleAsyncArrow: boolean,
  refTrailingCommaPos?: IndexTracker,
): Array<n.ExpressionNode> {
  const elts = [];
  let innerParenStart;
  let first = true;

  while (true) {
    if (parser.match(openContext.close) || parser.match(tt.eof)) {
      parser.expectClosing(openContext);
      break;
    }

    if (first) {
      first = false;
    } else {
      if (!parser.expect(tt.comma)) {
        break;
      }

      if (parser.eat(openContext.close)) {
        if (callee.type === 'Import') {
          parser.addDiagnostic({
            start: parser.state.lastStartPos,
            end: parser.state.lastEndPos,
            message:
              'Trailing comma is disallowed inside import(...) arguments',
          });
        }

        break;
      }
    }

    // we need to make sure that if this is an async arrow functions, that we don't allow inner parens inside the params
    if (parser.match(tt.parenL) && !innerParenStart) {
      innerParenStart = parser.state.startPos;
    }

    const elt = parseExpressionListItem(
      parser,
      'call expression argument',
      false,
      possibleAsyncArrow ? {index: 0} : undefined,
      possibleAsyncArrow ? {index: 0} : undefined,
      possibleAsyncArrow ? refTrailingCommaPos : undefined,
    );
    invariant(elt !== undefined, 'expected element');
    elts.push(elt);
  }

  // we found an async arrow function so let's not allow any inner parens
  if (
    possibleAsyncArrow &&
    innerParenStart !== undefined &&
    shouldParseAsyncArrow(parser)
  ) {
    parser.addDiagnostic({
      start: innerParenStart,
      message: 'Inner paren inside of an async arrow function params',
    });
  }

  return elts;
}

export function shouldParseAsyncArrow(parser: Parser): boolean {
  return (
    parser.match(tt.colon) ||
    (parser.match(tt.arrow) && !parser.canInsertSemicolon())
  );
}

export function parseAsyncArrowFromCallExpression(
  parser: Parser,
  start: Position,
  args: Array<n.Node>,
): n.ArrowFunctionExpressionNode {
  let returnType;

  if (parser.match(tt.colon)) {
    const oldNoAnonFunctionType = parser.state.noAnonFunctionType;
    parser.state.noAnonFunctionType = true;
    returnType = parseTypeAnnotation(parser);
    parser.state.noAnonFunctionType = oldNoAnonFunctionType;
  }

  const oldYield = parser.state.yieldInPossibleArrowParameters;
  parser.state.yieldInPossibleArrowParameters = undefined;
  parser.expect(tt.arrow);
  const node = parseArrowExpression(parser, start, args, true);
  parser.state.yieldInPossibleArrowParameters = oldYield;
  return {
    ...node,
    returnType,
  };
}

// Parse a no-call expression (like argument of `new` or `::` operators).
export function parseNoCallExpr(
  parser: Parser,
  context: ExpressionContext,
): n.ExpressionNode {
  const startPos = parser.state.startPos;
  return parseSubscripts(
    parser,
    parseExpressionAtom(parser, context),
    startPos,
    true,
  );
}

type ExpressionContext =
  | 'await argument'
  | 'export default declaration'
  | 'export from'
  | 'import source'
  | 'return argument'
  | 'switch discriminant'
  | 'case test'
  | 'throw argument'
  | 'flow object property key'
  | 'flow declare module id'
  | 'flow declared predicate'
  | 'class private property'
  | 'class property value'
  | 'assignment right'
  | 'class heritage'
  | 'new callee'
  | 'var init'
  | 'for right'
  | 'for update'
  | 'for test'
  | 'for init'
  | 'with object'
  | 'while test'
  | 'do test'
  | 'if test'
  | 'conditional consequent'
  | 'conditional alternate'
  | 'class private property value'
  | 'statement expression'
  | 'class private property value'
  | 'optional member expression property'
  | 'member expression computed property'
  | 'call expression argument'
  | 'new expression argument'
  | 'template expression value'
  | 'object property value'
  | 'property name'
  | 'function body'
  | 'yield argument'
  | 'array element'
  | 'spread argument'
  | 'assignment pattern right'
  | 'ts export assignment'
  | 'ts external module reference expression'
  | 'ts enum member initializer'
  | 'ts enum member id'
  | 'ts type assertion'
  | 'ts literal type'
  | 'ts import argument'
  | 'jsx inner expression container'
  | 'jsx attribute value'
  | 'jsx spread child expression'
  | 'jsx attribute spread'
  | 'jsx text';

// Parse an atomic expression  either a single token that is an
// expression, an expression started by a keyword like `function` or
// `new`, or an expression wrapped in punctuation like `()`, `[]`,
// or `{}`.
export function parseExpressionAtom(
  parser: Parser,
  context: ExpressionContext,
  refShorthandDefaultPos?: undefined | IndexTracker,
): n.ExpressionNode {
  // If a division operator appears in an expression position, the
  // tokenizer got confused, and we force it to read a regexp instead.
  if (parser.state.tokenType === tt.slash) {
    readRegexp(parser);
  }

  const canBeArrow =
    parser.state.potentialArrowAt === parser.state.startPos.index;

  // We don't want to match <! as it's the start of a HTML comment
  if (
    parser.isRelational('<') &&
    parser.input.charCodeAt(parser.state.index) !== charCodes.exclamationMark
  ) {
    // In case we encounter an lt token here it will always be the start of
    // jsx as the lt sign is not allowed in places that expect an expression
    finishToken(parser, tt.jsxTagStart);
    return parseJSXElement(parser);
  }

  switch (parser.state.tokenType) {
    case tt.jsxText:
      return parseJSXText(parser);

    case tt.jsxTagStart:
      return parseJSXElement(parser);

    case tt._super:
      return parseSuper(parser);

    case tt._import:
      return parseImportOrMetaProperty(parser);

    case tt._this: {
      const start = parser.getPosition();
      parser.next();
      return {
        type: 'ThisExpression',
        loc: parser.finishLoc(start),
      };
    }

    case tt.name: {
      const start = parser.getPosition();
      const containsEsc = parser.state.escapePosition !== undefined;
      const id = parseIdentifier(parser);

      if (
        !containsEsc &&
        id.name === 'async' &&
        parser.match(tt._function) &&
        !parser.canInsertSemicolon()
      ) {
        parser.next();
        return parseFunction(parser, {
          start,
          isStatement: false,
          isAsync: true,
        });
      }

      if (
        canBeArrow &&
        !containsEsc &&
        id.name === 'async' &&
        parser.match(tt.name)
      ) {
        const oldYield = parser.state.yieldInPossibleArrowParameters;
        parser.state.yieldInPossibleArrowParameters = undefined;
        const params = [parseIdentifier(parser)];
        parser.expect(tt.arrow);
        // let foo = bar => {};
        const node = parseArrowExpression(parser, start, params, true);
        parser.state.yieldInPossibleArrowParameters = oldYield;
        return node;
      }

      if (canBeArrow && !parser.canInsertSemicolon() && parser.eat(tt.arrow)) {
        const oldYield = parser.state.yieldInPossibleArrowParameters;
        parser.state.yieldInPossibleArrowParameters = undefined;
        const node = parseArrowExpression(parser, start, [id]);
        parser.state.yieldInPossibleArrowParameters = oldYield;
        return node;
      }

      return id;
    }

    case tt._do:
      return parseDoExpression(parser);

    case tt.regexp:
      return parseRegExpLiteral(parser);

    case tt.num:
      return parseNumericLiteral(parser);

    case tt.bigint:
      return parseBigIntLiteral(parser);

    case tt.string:
      return parseStringLiteral(parser);

    case tt._null:
      return parseNullLiteral(parser);

    case tt._true:
    case tt._false:
      return parseBooleanLiteral(parser);

    case tt.parenL:
      return parseParenAndDistinguishExpression(parser, context, canBeArrow);

    case tt.bracketL:
      return parseArrayExpression(parser, refShorthandDefaultPos);

    case tt.braceL:
      return parseObjectExpression(parser, refShorthandDefaultPos);

    case tt._function:
      return parseFunctionExpressionOrMetaProperty(parser);

    case tt._class: {
      const start = parser.getPosition();
      return parseClass(parser, start, false);
    }

    case tt._new:
      return parseNew(parser);

    case tt.backQuote:
      return parseTemplate(parser, false);

    case tt.doubleColon: {
      const start = parser.getPosition();
      parser.next();
      const object = undefined;
      const callee = parseNoCallExpr(parser, context);

      if (callee.type === 'MemberExpression') {
        return {
          loc: parser.finishLoc(start),
          type: 'BindExpression',
          object,
          callee,
        };
      } else {
        parser.addDiagnostic({
          loc: callee.loc,
          message: 'Binding should be performed on object property.',
        });
        return parser.createUnknownNode(
          'bind expression non-member callee',
          start,
        );
      }
    }

    default: {
      const start = parser.getPosition();
      parser.addDiagnostic({
        message: `Unknown start to an ${context}`,
      });
      parser.next();
      return parser.createUnknownNode(context, start);
    }
  }
}

export function parseBooleanLiteral(parser: Parser): n.BooleanLiteralNode {
  const start = parser.getPosition();
  const value = parser.match(tt._true);
  parser.next();
  return {
    loc: parser.finishLoc(start),
    type: 'BooleanLiteral',
    value,
  };
}

export function parseMaybePrivateName(
  parser: Parser,
): n.PrivateNameNode | n.IdentifierNode {
  const isPrivate = parser.match(tt.hash);

  if (isPrivate) {
    const start = parser.getPosition();
    parser.next();
    parser.assertNoSpace('Unexpected space between # and identifier');
    const id = parseIdentifier(parser, true);
    return {
      loc: parser.finishLoc(start),
      type: 'PrivateName',
      id,
    };
  } else {
    return parseIdentifier(parser, true);
  }
}

export function parseFunctionExpressionOrMetaProperty(
  parser: Parser,
): n.FunctionExpressionNode | n.MetaPropertyNode {
  const start = parser.getPosition();
  parser.next();

  // We do not do parseIdentifier here because when parseFunctionExpressionOrMetaProperty
  // is called we already know that the current token is a "name" with the value "function"
  // This will improve perf a tiny little bit as we do not do validation but more importantly
  // here is that parseIdentifier will remove an item from the expression stack
  // if "function" or "class" is parsed as identifier (in objects e.g.), which should not happen here.
  const meta = createIdentifier(parser, start, 'function');

  if (parser.inScope('GENERATOR') && parser.eat(tt.dot)) {
    return parseMetaProperty(parser, start, meta, 'sent');
  }

  const node = parseFunction(parser, {
    start,
    isStatement: false,
  });

  if (node.type !== 'FunctionExpression') {
    throw new Error('Expected parseFunction to return a FunctionExpression');
  }

  return node;
}

export function parseMetaProperty(
  parser: Parser,
  start: Position,
  meta: n.IdentifierNode,
  propertyName: string,
): n.MetaPropertyNode {
  if (
    meta.name === 'function' &&
    propertyName === 'sent' &&
    !parser.isContextual(propertyName)
  ) {
    // They didn't actually say `function.sent`, just `function.`, so a simple error would be less confusing.
    parser.unexpectedToken();
  }

  const escapePosition = parser.state.escapePosition;
  const property = parseIdentifier(parser, true);

  if (property.name === propertyName) {
    parser.banUnicodeEscape(escapePosition, propertyName);
  } else {
    parser.addDiagnostic({
      loc: property.loc,
      message: `The only valid meta property for ${meta.name} is ${
        meta.name
      }.${propertyName}`,
    });
  }

  return {
    loc: parser.finishLoc(start),
    type: 'MetaProperty',
    meta,
    property,
  };
}

export function parseImportMetaProperty(parser: Parser): n.MetaPropertyNode {
  const start = parser.getPosition();
  const id = parseIdentifier(parser, true);
  parser.expect(tt.dot);
  const node = parseMetaProperty(parser, start, id, 'meta');

  if (!parser.inModule) {
    parser.addDiagnostic({
      loc: node.loc,
      message: `import.meta may appear only with 'sourceType: "module"'`,
    });
  }

  return node;
}

export function parseParenExpression(
  parser: Parser,
  context: ExpressionContext,
): n.ExpressionNode {
  const openContext = parser.expectOpening(tt.parenL, tt.parenR, context);
  const val = parseExpression(parser, context);
  parser.expectClosing(openContext);
  return val;
}

export function parseParenAndDistinguishExpression(
  parser: Parser,
  context: ExpressionContext,
  canBeArrow: boolean,
): n.ExpressionNode {
  if (parser.state.noArrowAt.includes(parser.state.startPos.index)) {
    canBeArrow = false;
  }

  const startPos = parser.state.startPos;

  let val;
  const openContext = parser.expectOpening(
    tt.parenL,
    tt.parenR,
    'paren expression',
  );

  const oldMaybeInArrowParameters = parser.state.maybeInArrowParameters;
  const oldYieldPos = parser.state.yieldPos;
  const oldAwaitPos = parser.state.awaitPos;
  const oldYield = parser.state.yieldInPossibleArrowParameters;
  parser.state.maybeInArrowParameters = true;
  parser.state.yieldInPossibleArrowParameters = undefined;
  parser.state.yieldPos = 0;
  parser.state.awaitPos = 0;

  const innerStart = parser.getPosition();
  let exprList: Array<ExpressionNode> = [];
  const refShorthandDefaultPos: IndexTracker = {index: 0};
  const refNeedsArrowPos: IndexTracker = {index: 0};
  let first = true;
  let spreadStart;
  let optionalCommaStart;

  while (!parser.match(tt.parenR)) {
    if (first) {
      first = false;
    } else {
      if (
        !parser.expect(
          tt.comma,
          refNeedsArrowPos.index === 0
            ? undefined
            : parser.getPositionFromIndex(refNeedsArrowPos.index),
        )
      ) {
        break;
      }

      if (parser.match(tt.parenR)) {
        optionalCommaStart = parser.state.startPos;
        break;
      }
    }

    if (parser.match(tt.ellipsis)) {
      const spreadNodeStartPos = parser.state.startPos;
      spreadStart = parser.state.startPos;
      exprList.push(
        parseParenItem(parser, parseRest(parser), spreadNodeStartPos),
      );

      if (
        parser.match(tt.comma) &&
        parser.lookaheadState().tokenType === tt.parenR
      ) {
        raiseRestNotLast(parser);
        parser.eat(tt.comma);
      }
    } else {
      exprList.push(
        parseMaybeAssign(
          parser,
          context,
          false,
          refShorthandDefaultPos,
          parseParenItem,
          refNeedsArrowPos,
        ),
      );
    }
  }

  const innerEnd = parser.getPosition();
  parser.expectClosing(openContext);

  parser.state.maybeInArrowParameters = oldMaybeInArrowParameters;

  const arrowStart = startPos;
  if (canBeArrow && shouldParseArrow(parser)) {
    const {valid, returnType, predicate} = parseArrowHead(parser);

    if (valid) {
      checkYieldAwaitInDefaultParams(parser);
      parser.state.yieldPos = oldYieldPos;
      parser.state.awaitPos = oldAwaitPos;

      for (const param of exprList) {
        if (parser.isParenthesized(param)) {
          parser.addDiagnostic({
            loc: param.loc,
            message: "Function parameters can't be parenthesized",
          });
        }
      }

      const arrow = parseArrowExpression(parser, arrowStart, exprList);
      parser.state.yieldInPossibleArrowParameters = oldYield;
      return {
        ...arrow,
        predicate,
        returnType,
      };
    }
  }

  parser.state.yieldInPossibleArrowParameters = oldYield;

  // We keep the old value if it isn't null, for cases like
  //   (x = (yield)) => {}
  parser.state.yieldPos = oldYieldPos || parser.state.yieldPos;
  parser.state.awaitPos = oldAwaitPos || parser.state.awaitPos;

  if (exprList.length === 0) {
    parser.addDiagnostic({
      start: innerStart,
      end: innerEnd,
      message: 'Parenthesized expression didnt contain anything',
    });

    exprList.push({
      type: 'Noop',
      loc: parser.finishLocAt(innerStart, innerEnd),
    });
  }

  if (optionalCommaStart !== undefined) {
    parser.unexpectedToken(optionalCommaStart);
  }

  if (spreadStart !== undefined) {
    parser.unexpectedToken(spreadStart);
  }

  if (refShorthandDefaultPos.index > 0) {
    parser.unexpectedToken(
      parser.getPositionFromIndex(refShorthandDefaultPos.index),
    );
  }

  if (refNeedsArrowPos.index > 0) {
    parser.unexpectedToken(parser.getPositionFromIndex(refNeedsArrowPos.index));
  }

  exprList = toReferencedListDeep(
    parser,
    exprList,
    /* isParenthesizedExpr */ true,
  );

  if (exprList.length > 1) {
    const expressions = toReferencedList(parser, exprList);
    val = {
      loc: parser.finishLocAt(innerStart, innerEnd),
      type: 'SequenceExpression',
      expressions,
    };
  } else {
    val = exprList[0];
  }

  parser.addParenthesized(val);

  return val;
}

export function shouldParseArrow(parser: Parser): boolean {
  return parser.match(tt.colon) || !parser.canInsertSemicolon();
}

export function parseArrowHead(
  parser: Parser,
): {
  valid: boolean;
  predicate: undefined | n.FlowPredicateNode;
  returnType: undefined | n.FlowTypeAnnotationNode;
} {
  if (parser.match(tt.colon)) {
    const oldNoAnonFunctionType = parser.state.noAnonFunctionType;
    parser.state.noAnonFunctionType = true;

    const branch = parser.createBranch<
      undefined | TypeAnnotationAndPredicate
    >();

    branch.add(() => {
      const res = parseTypeAnnotationAndPredicate(parser);

      if (parser.canInsertSemicolon()) {
        parser.addDiagnostic({
          message: 'No semicolon insertion expected',
        });
      }

      if (parser.eat(tt.arrow)) {
        return res;
      }
    });

    if (branch.hasBranchResult()) {
      const typeInfo = branch.pick();
      parser.state.noAnonFunctionType = oldNoAnonFunctionType;

      if (typeInfo === undefined) {
        throw new Error(
          'hasBranchResult call above should have refined this condition',
        );
      }

      return {
        valid: true,
        predicate: typeInfo[1],
        returnType: typeInfo[0],
      };
    } else {
      parser.state.noAnonFunctionType = oldNoAnonFunctionType;
      return {
        valid: false,
        predicate: undefined,
        returnType: undefined,
      };
    }
  } else {
    return {
      valid: parser.eat(tt.arrow),
      predicate: undefined,
      returnType: undefined,
    };
  }
}

export function parseParenItem(
  parser: Parser,
  node: n.Node,
  startPos: Position,
): n.ExpressionNode {
  let optional = undefined;
  if (parser.eat(tt.question)) {
    optional = true;
  }

  if (parser.match(tt.colon)) {
    // TODO When converting to a referenced list, throw on FlowTypeCastExpression in TS mode
    const typeAnnotation = parseTypeAnnotation(parser);
    return {
      loc: parser.finishLoc(startPos),
      type: 'FlowTypeCastExpression',
      expression: node,
      typeAnnotation,
      optional,
    };
  }

  if (optional) {
    return {
      ...node,
      optional,
    };
  } else {
    return node;
  }
}

// New's precedence is slightly tricky. It must allow its argument to
// be a `[]` or dot subscript expression, but not a call  at least,
// not without wrapping it in parentheses. Thus, it uses the noCalls
// argument to parseSubscripts to prevent it from 'consuming the
// argument list.
export function parseNew(
  parser: Parser,
): n.NewExpressionNode | n.MetaPropertyNode {
  const start = parser.getPosition();
  const meta = parseIdentifier(parser, true);

  if (parser.eat(tt.dot)) {
    const metaProp = parseMetaProperty(parser, start, meta, 'target');

    if (
      !parser.inScope('NON_ARROW_FUNCTION') &&
      !parser.inScope('CLASS_PROPERTY')
    ) {
      parser.addDiagnostic({
        loc: metaProp.loc,
        message: 'new.target can only be used in functions or class properties',
      });
    }

    return metaProp;
  }

  const callee = parseNoCallExpr(parser, 'new callee');

  if (callee.type === 'Import') {
    parser.addDiagnostic({
      loc: callee.loc,
      message: 'Cannot use new with import(...)',
    });
  }

  const optionalMember = getFirstOptionalChainMember(callee);
  if (optionalMember !== undefined) {
    const memberLoc = parser.getLoc(optionalMember);

    parser.addDiagnostic({
      message: 'constructors in/after an Optional Chain are not allowed',
      advice: [
        {
          type: 'log',
          category: 'info',
          message: 'Optional chain member responsible',
        },
        {
          type: 'frame',
          filename: parser.filename,
          start: memberLoc.start,
          end: memberLoc.end,
        },
      ],
    });
  }

  if (parser.eat(tt.questionDot)) {
    parser.addDiagnostic({
      message: 'constructors in/after an Optional Chain are not allowed',
    });
  }

  let optional = undefined;
  if (parser.eat(tt.questionDot)) {
    optional = true;
  }

  let typeArguments = undefined;
  if (isTypeSystemEnabled(parser) && parser.isRelational('<')) {
    typeArguments = parser.tryBranch(parseTypeCallArguments);
  }

  let args = [];
  if (parser.match(tt.parenL)) {
    const openContext = parser.expectOpening(
      tt.parenL,
      tt.parenR,
      'new argument',
    );
    args = parseExpressionList(parser, 'new expression argument', openContext);
    toReferencedList(parser, args);
  } else if (parser.isSyntaxEnabled('ts') && typeArguments !== undefined) {
    parser.addDiagnostic({
      message:
        'In TypeScript, a new expression with type arguments must have parens',
    });
  }

  return {
    loc: parser.finishLoc(start),
    type: 'NewExpression',
    callee,
    typeArguments,
    arguments: args,
    optional,
  };
}

function getFirstOptionalChainMember(node: n.Node): undefined | n.Node {
  if (
    node.type === 'OptionalMemberExpression' ||
    node.type === 'OptionalCallExpression'
  ) {
    return node;
  }

  if (node.type === 'MemberExpression' && node.computed !== true) {
    return getFirstOptionalChainMember(node.object);
  }
}

// Parse template expression.
export function parseTemplateElement(
  parser: Parser,
  isTagged: boolean,
): n.TemplateElementNode {
  const start = parser.getPosition();
  const tokenValue = parser.state.tokenValue;

  if (tokenValue === undefined) {
    if (isTagged) {
      parser.state.invalidTemplateEscapePosition = undefined;
    } else {
      // TODO: fix this weird || 0
      parser.addDiagnostic({
        index: parser.state.invalidTemplateEscapePosition || 0,
        message: 'Invalid escape sequence in template',
      });
    }
  }

  const raw = parser
    .getRawInput(parser.state.startPos.index, parser.state.endPos.index)
    .replace(/\r\n?/g, '\n');
  const cooked = tokenValue === undefined ? raw : String(tokenValue);

  parser.next();
  const tail = parser.match(tt.backQuote);
  return {
    loc: parser.finishLoc(start),
    type: 'TemplateElement',
    raw,
    cooked,
    tail,
  };
}

export function parseTemplate(
  parser: Parser,
  isTagged: boolean,
): n.TemplateLiteralNode {
  const start = parser.getPosition();
  const openContext = parser.expectOpening(
    tt.backQuote,
    tt.backQuote,
    'template literal',
  );
  const expressions = [];
  let curElt = parseTemplateElement(parser, isTagged);
  const quasis = [curElt];

  while (true) {
    if (parser.match(tt.eof) || curElt.tail === true) {
      break;
    }

    const exprPpenContext = parser.expectOpening(
      tt.dollarBraceL,
      tt.braceR,
      'template expression value',
    );
    expressions.push(parseExpression(parser, 'template expression value'));
    parser.expectClosing(exprPpenContext);

    curElt = parseTemplateElement(parser, isTagged);
    quasis.push(curElt);
  }

  parser.expectClosing(openContext);

  return {
    loc: parser.finishLoc(start),
    type: 'TemplateLiteral',
    expressions,
    quasis,
  };
}

export function parseObjectExpression(
  parser: Parser,
  refShorthandDefaultPos?: undefined | IndexTracker,
): n.ObjectExpressionNode {
  const propHash: Set<string> = new Set();
  let first = true;

  const start = parser.getPosition();
  const properties = [];

  const openContext = parser.expectOpening(tt.braceL, tt.braceR, 'object');

  while (true) {
    if (parser.match(tt.braceR) || parser.match(tt.eof)) {
      parser.expectClosing(openContext);
      break;
    }

    if (first) {
      first = false;
    } else {
      if (!parser.expect(tt.comma)) {
        break;
      }

      if (parser.eat(tt.braceR)) {
        break;
      }
    }

    if (parser.match(tt.ellipsis)) {
      const prop: n.SpreadPropertyNode = {
        ...parseSpread(parser),
        type: 'SpreadProperty',
      };
      properties.push(prop);
      continue;
    }

    const start = parser.getPosition();
    let isGenerator = parser.eat(tt.star);
    let isAsync = false;

    let key;
    let computed;
    let variance;
    let escapePosition;

    if (parser.isContextual('async')) {
      if (isGenerator) {
        parser.unexpectedToken();
      }

      const asyncId = parseIdentifier(parser);
      if (
        parser.match(tt.colon) ||
        parser.match(tt.parenL) ||
        parser.match(tt.braceR) ||
        parser.match(tt.eq) ||
        parser.match(tt.comma)
      ) {
        key = asyncId;
        computed = false;
      } else {
        if (parser.hasPrecedingLineBreak()) {
          parser.addDiagnostic({
            message:
              "There shouldn't be any newlines between async and the rest of the function",
          });
        }

        isAsync = true;
        if (parser.match(tt.star)) {
          parser.next();
          isGenerator = true;
        }
        escapePosition = parser.state.escapePosition;
        ({computed, key, variance} = parsePropertyName(parser));
      }
    } else {
      escapePosition = parser.state.escapePosition;
      ({computed, key, variance} = parsePropertyName(parser));
    }

    const prop = parseObjectPropertyValue(parser, {
      name: {
        computed,
        key,
        variance,
      },
      start,
      isGenerator,
      isAsync,
      isPattern: false,
      refShorthandDefaultPos,
      escapePosition,
    });
    if (prop.type === 'Unknown') {
      continue;
    }

    checkPropClash(parser, prop, propHash);
    properties.push(prop);
  }

  return {
    loc: parser.finishLoc(start),
    type: 'ObjectExpression',
    properties,
  };
}

export function parseObjectPattern(
  parser: Parser,
  refShorthandDefaultPos?: undefined | IndexTracker,
): n.ObjectPatternNode {
  const propHash: Set<string> = new Set();
  let first = true;

  const start = parser.getPosition();
  const properties: Array<n.ObjectPropertyNode | n.RestPropertyNode> = [];

  const openContext = parser.expectOpening(
    tt.braceL,
    tt.braceR,
    'object pattern',
  );

  let firstRestLocation = undefined;

  while (true) {
    if (parser.match(tt.eof) || parser.match(tt.braceR)) {
      break;
    }

    if (first) {
      first = false;
    } else {
      parser.expect(tt.comma);

      if (parser.match(tt.eof) || parser.match(tt.braceR)) {
        break;
      }
    }

    let isGenerator = false;
    let isAsync = false;
    let start = parser.getPosition();

    if (parser.eat(tt.ellipsis)) {
      const argument = parseIdentifier(parser);

      const prop: n.RestPropertyNode = {
        type: 'RestProperty',
        loc: parser.finishLoc(start),
        argument,
      };

      properties.push(toAssignable(parser, prop, true, 'object pattern'));

      if (firstRestLocation !== undefined) {
        parser.addDiagnostic({
          loc: prop.loc,
          message: 'Cannot have multiple rest elements when destructuring',
        });
      }

      if (parser.match(tt.braceR) || parser.match(tt.eof)) {
        break;
      }

      if (
        parser.match(tt.comma) &&
        parser.lookaheadState().tokenType === tt.braceR
      ) {
        parser.addDiagnostic({
          message: 'A trailing comma is not permitted after the rest element',
        });
        parser.eat(tt.comma);
        break;
      } else {
        firstRestLocation = prop.loc;
        continue;
      }
    }

    start = parser.getPosition();

    const {computed, key, variance} = parsePropertyName(parser);

    const prop = parseObjectPropertyValue(parser, {
      name: {
        computed,
        key,
        variance,
      },
      start,
      isGenerator,
      isAsync,
      isPattern: true,
      refShorthandDefaultPos,
      escapePosition: undefined,
    });

    if (prop.type === 'Unknown') {
      continue;
    }

    checkPropClash(parser, prop, propHash);

    if (prop.type !== 'ObjectProperty') {
      throw new Error(
        `Should have already been validated but got ${prop.type}`,
      );
    }

    properties.push(prop);
  }

  parser.expectClosing(openContext);

  if (firstRestLocation !== undefined) {
    raiseRestNotLast(parser, firstRestLocation);
  }

  return {
    loc: parser.finishLoc(start),
    type: 'ObjectPattern',
    properties,
  };
}

export function isGetterOrSetterMethod(
  parser: Parser,
  name: PropertyNameBase,
  isPattern: boolean,
): boolean {
  return (
    !isPattern &&
    name.computed !== true &&
    name.key.type === 'Identifier' &&
    (name.key.name === 'get' || name.key.name === 'set') &&
    (parser.match(tt.string) ||
      // get "string"() {}
      parser.match(tt.num) ||
      // get 1() {}
      parser.match(tt.bracketL) ||
      // get ["string"]() {}
      parser.match(tt.name) ||
      // get foo() {}
      !!parser.state.tokenType.keyword) // get debugger() {}
  );
}

// get methods aren't allowed to have any parameters
// set methods must have exactly 1 parameter
export function checkGetterSetterParamCount(
  parser: Parser,
  method:
    | n.FlowFunctionTypeAnnotationNode
    | n.ObjectMethodNode
    | n.ClassMethodNode
    | n.ClassPrivateMethodNode
    | n.TSDeclareFunctionNode
    | n.TSDeclareMethodNode,
  kind: string,
): void {
  const paramCount = getGetterSetterExpectedParamCount(parser, method, kind);

  if (method.params.length !== paramCount) {
    if (kind === 'get') {
      parser.addDiagnostic({
        loc: method.loc,
        message: 'getter should have no params',
      });
    } else {
      parser.addDiagnostic({
        loc: method.loc,
        message: 'setter should have exactly one param',
      });
    }
  }

  if (kind === 'set') {
    const lastParam = method.params[method.params.length - 1];
    if (lastParam !== undefined && isRestLike(lastParam)) {
      parser.addDiagnostic({
        loc: lastParam.loc,
        message: 'setter function argument must not be a rest parameter',
      });
    }
  }
}

export function getGetterSetterExpectedParamCount(
  parser: Parser,
  method:
    | n.FlowFunctionTypeAnnotationNode
    | n.ObjectMethodNode
    | n.ClassMethodNode
    | n.ClassPrivateMethodNode
    | n.TSDeclareFunctionNode
    | n.TSDeclareMethodNode,
  kind: string,
): number {
  const baseCount = kind === 'get' ? 0 : 1;

  // TypeScript allows a `this` parameter as a type
  const firstParam = method.params[0];
  const hasContextParam =
    firstParam !== undefined &&
    firstParam.type === 'Identifier' &&
    firstParam.name === 'this';

  return hasContextParam ? baseCount + 1 : baseCount;
}

type ParseObjectMethodOpts = {
  name: PropertyNameBase;
  start: Position;
  isGenerator: boolean;
  isAsync: boolean;
  isPattern: boolean;
  escapePosition: undefined | number;
};

export function parseObjectMethod(
  parser: Parser,
  {
    name,
    start,
    isGenerator,
    isAsync,
    isPattern,
    escapePosition,
  }: ParseObjectMethodOpts,
): undefined | n.ObjectMethodNode {
  if (isAsync || isGenerator || parser.match(tt.parenL)) {
    if (isPattern) {
      parser.addDiagnostic({
        message: "Object methods aren't allowed in object patterns",
      });
    }

    const partial = parseMethod(parser, {
      kind: 'method',
      isClass: false,
      isGenerator,
      isAsync,
      isConstructor: false,
    });
    invariant(partial.body !== undefined, 'Expected body');

    return {
      ...partial,
      loc: parser.finishLoc(start),
      ...name,
      type: 'ObjectMethod',
      kind: 'method',
    };
  }

  if (isGetterOrSetterMethod(parser, name, isPattern)) {
    if (isAsync) {
      parser.addDiagnostic({
        message: "An object setter/getter can't be async",
      });
    }

    if (isGenerator) {
      parser.addDiagnostic({
        message: "An object setter/getter can't be a generator",
      });
    }

    const kind = name.key.name;
    invariant(
      kind === 'get' || kind === 'set',
      'name should be get or set as we already validated it as such',
    );
    parser.banUnicodeEscape(escapePosition, kind);

    const methodName = parsePropertyName(parser);

    const partial = parseMethod(parser, {
      kind,
      isClass: false,
      isGenerator: false,
      isAsync: false,
      isConstructor: false,
    });
    invariant(partial.body !== undefined, 'Expected body');

    const method: n.ObjectMethodNode = {
      ...partial,
      loc: parser.finishLoc(start),
      ...methodName,
      type: 'ObjectMethod',
      kind,
    };
    checkGetterSetterParamCount(parser, method, method.kind);
    return method;
  }
}

export function parseObjectProperty(
  parser: Parser,
  name: PropertyNameBase,
  start: Position,
  isPattern: boolean,
  refShorthandDefaultPos: undefined | IndexTracker,
): undefined | n.ObjectPropertyNode {
  if (parser.eat(tt.colon)) {
    const value = isPattern
      ? parseMaybeDefault(parser)
      : parseMaybeAssign(
          parser,
          'object property value',
          false,
          refShorthandDefaultPos,
        );

    return {
      loc: parser.finishLoc(start),
      ...name,
      type: 'ObjectProperty',
      value,
    };
  }

  if (name.computed !== true && name.key.type === 'Identifier') {
    checkReservedWord(
      parser,
      name.key.name,
      parser.getLoc(name.key),
      true,
      true,
    );

    let shorthand = false;
    let value;
    if (isPattern) {
      value = parseMaybeDefault(parser, start, parser.cloneNode(name.key));
    } else if (parser.match(tt.eq) && refShorthandDefaultPos) {
      if (refShorthandDefaultPos.index === 0) {
        refShorthandDefaultPos.index = parser.state.startPos.index;
      }
      value = parseMaybeDefault(parser, start, parser.cloneNode(name.key));
    } else {
      value = parser.cloneNode(name.key);
      shorthand = true;
    }

    const node: n.ObjectPropertyNode = {
      loc: parser.finishLoc(start),
      ...name,
      type: 'ObjectProperty',
      value,
    };

    if (shorthand) {
      parser.addExtra(node, 'shorthand', shorthand);
    }

    return node;
  }
}

type ParseObjectPropValueOpts = {
  name: PropertyNameBase;
  start: Position;
  isGenerator: boolean;
  isAsync: boolean;
  isPattern: boolean;
  refShorthandDefaultPos: undefined | IndexTracker;
  escapePosition: undefined | number;
};

export function parseObjectPropertyValue(
  parser: Parser,
  {
    name,
    start,
    isGenerator,
    isAsync,
    isPattern,
    refShorthandDefaultPos,
    escapePosition,
  }: ParseObjectPropValueOpts,
): n.ObjectMethodNode | n.ObjectPropertyNode | n.UnknownNode {
  if (name.variance !== undefined) {
    parser.addDiagnostic({
      loc: name.variance.loc,
      message: 'variance not allowed here',
    });
  }

  // parse type parameters for object method shorthand
  let typeParameters = maybeParseTypeParameters(parser);
  if (typeParameters !== undefined && !parser.match(tt.parenL)) {
    parser.unexpectedToken();
  }

  let node:
    | undefined
    | n.ObjectMethodNode
    | n.ObjectPropertyNode
    | n.UnknownNode =
    parseObjectMethod(parser, {
      name,
      start,
      isGenerator,
      isAsync,
      isPattern,
      escapePosition,
    }) ||
    parseObjectProperty(parser, name, start, isPattern, refShorthandDefaultPos);

  if (node === undefined) {
    parser.unexpectedToken();
    node = parser.createUnknownNode('object property value', start);
  }

  // Add typeParameters if we found them
  if (typeParameters) {
    // @ts-ignore: Unactionable error
    return {...node, typeParameters};
  } else {
    return node;
  }
}

export function parsePropertyName(parser: Parser): PropertyNameBase {
  const variance = parseFlowVariance(parser);

  let key;
  let computed = false;

  if (parser.match(tt.bracketL)) {
    const openContext = parser.expectOpening(
      tt.bracketL,
      tt.bracketR,
      'property name',
    );
    computed = true;
    key = parseMaybeAssign(parser, 'property name');
    parser.expectClosing(openContext);
  } else {
    parser.pushScope('PROPERTY_NAME', true);

    // We check if it's valid for it to be a private name when we push it.
    key =
      parser.match(tt.num) || parser.match(tt.string)
        ? parseExpressionAtom(parser, 'property name')
        : parseMaybePrivateName(parser);

    if (key.type !== 'PrivateName') {
      // ClassPrivateProperty is never computed, so we don't assign in that case.
      computed = false;
    }

    parser.popScope('PROPERTY_NAME');
  }

  return {key, variance, computed};
}

// Parse object or class method.
export function parseMethod(
  parser: Parser,
  opts: {
    kind: n.ClassMethodKind | n.ObjectMethodKind;
    isGenerator: boolean;
    isAsync: boolean;
    isConstructor: boolean;
    isClass: boolean;
  },
): n.FunctionBase & {
  body: undefined | n.BlockStatementNode | n.ExpressionNode;
} {
  const {kind, isClass, isGenerator, isAsync, isConstructor} = opts;

  const oldYieldPos = parser.state.yieldPos;
  const oldAwaitPos = parser.state.awaitPos;
  parser.pushScope('FUNCTION', true);
  parser.pushScope('NON_ARROW_FUNCTION');
  parser.pushScope('METHOD', kind);
  parser.pushScope('GENERATOR', isGenerator);
  parser.state.yieldPos = 0;
  parser.state.awaitPos = 0;

  const allowTSModifiers = isConstructor;
  const {typeParameters, params} = parseFunctionParams(
    parser,
    kind,
    allowTSModifiers,
  );
  const {body, returnType} = parseFunctionBodyAndFinish(parser, {
    allowBodiless: isClass,
    isArrowFunction: false,
    isAsync,
    isGenerator,
    isMethod: true,
    id: undefined,
    params,
    start: parser.getPosition(),
  });

  parser.popScope('METHOD');
  parser.popScope('GENERATOR');
  parser.popScope('FUNCTION');
  parser.popScope('NON_ARROW_FUNCTION');
  parser.state.yieldPos = oldYieldPos;
  parser.state.awaitPos = oldAwaitPos;

  return {
    generator: isGenerator,
    async: isAsync,
    typeParameters,
    params,
    body,
    returnType,
  };
}

// Parse arrow function expression.
// If the parameters are provided, they will be converted to an assignable list.
export function parseArrowExpression(
  parser: Parser,
  start: Position,
  params: Array<n.ExpressionNode>,
  isAsync: boolean = false,
): n.ArrowFunctionExpressionNode {
  // if we got there, it's no more "yield in possible arrow parameters";
  // it's just "yield in arrow parameters"
  if (parser.state.yieldInPossibleArrowParameters) {
    parser.addDiagnostic({
      start: parser.state.yieldInPossibleArrowParameters,
      message: 'Yield cannot be used as name inside a generator function',
    });
  }

  parser.pushScope('FUNCTION', true);

  params = normalizeArrowFunctionParameters(parser, start, params);

  const oldYieldPos = parser.state.yieldPos;
  const oldAwaitPos = parser.state.awaitPos;
  const oldMaybeInArrowParameters = parser.state.maybeInArrowParameters;
  parser.pushScope('GENERATOR', false);
  parser.state.maybeInArrowParameters = false;
  parser.state.yieldPos = 0;
  parser.state.awaitPos = 0;

  const body = parseFunctionBody(parser, {
    allowBodiless: false,
    isArrowFunction: true,
    isMethod: false,
    isAsync,
    isGenerator: false,
    params,
    id: undefined,
    start,
  });

  parser.popScope('GENERATOR');
  parser.popScope('FUNCTION');
  parser.state.maybeInArrowParameters = oldMaybeInArrowParameters;
  parser.state.yieldPos = oldYieldPos;
  parser.state.awaitPos = oldAwaitPos;

  return {
    loc: parser.finishLoc(start),
    type: 'ArrowFunctionExpression',
    params,
    async: isAsync,
    body,
  };
}

export function normalizeArrowFunctionParameters(
  parser: Parser,
  start: Position,
  params: Array<n.ExpressionNode>,
): Array<n.PatternNode> {
  if (parser.state.noArrowParamsConversionAt.indexOf(start.index) !== -1) {
    return params;
  } else {
    return toAssignableList(parser, params, true, 'arrow function parameters');
  }
}

export function isStrictBody(parser: Parser, body: n.Node): boolean {
  const isBlockStatement = body.type === 'BlockStatement';

  if (isBlockStatement && body.directives !== undefined) {
    for (const directive of body.directives) {
      if (directive.value.value === 'use strict') {
        return true;
      }
    }
  }

  return false;
}

type FunctionBodyParseOpts = {
  allowBodiless: boolean;
  isArrowFunction: boolean;
  isAsync: boolean;
  isGenerator: boolean;
  isMethod: boolean;
  id: undefined | n.IdentifierNode;
  params: Array<n.PatternNode>;
  start: Position;
};

export function parseFunctionBodyAndFinish(
  parser: Parser,
  opts: FunctionBodyParseOpts,
): {
  returnType: undefined | n.FlowTypeAnnotationNode;
  predicate: undefined | n.FlowPredicateNode;
  body: undefined | n.BlockStatementNode | n.ExpressionNode;
} {
  let returnType = undefined;
  let predicate;

  // For arrow functions, `parseArrow` handles the return type itself.
  if (!opts.isArrowFunction && parser.match(tt.colon)) {
    [returnType, predicate] = parseTypeAnnotationAndPredicate(parser);
  }

  if (
    opts.allowBodiless &&
    !parser.match(tt.braceL) &&
    parser.isLineTerminator()
  ) {
    return {
      predicate,
      returnType,
      body: undefined,
    };
  }

  const body = parseFunctionBody(parser, opts);
  return {body, predicate, returnType};
}

export function parseFunctionBody(
  parser: Parser,
  opts: FunctionBodyParseOpts,
): n.BlockStatementNode | n.ExpressionNode {
  if (opts.isArrowFunction) {
    return forwardNoArrowParamsConversionAt(parser, opts.start, () =>
      _parseFunctionBody(parser, opts),
    );
  } else {
    return _parseFunctionBody(parser, opts);
  }
}

// Parse function body and check parameters.
function _parseFunctionBody(
  parser: Parser,
  opts: FunctionBodyParseOpts,
): n.BlockStatementNode | n.ExpressionNode {
  const {isArrowFunction, isAsync, isGenerator} = opts;

  const isExpression = isArrowFunction && !parser.match(tt.braceL);

  parser.pushScope('PARAMETERS', false);
  parser.pushScope('ASYNC', isAsync);

  let body;
  if (isExpression) {
    body = parseMaybeAssign(parser, 'function body');
  } else {
    // Start a new scope with regard to labels and the `inGenerator`
    // flag (restore them to their old value afterwards).
    const oldLabels = parser.state.labels;
    parser.pushScope('GENERATOR', isGenerator);

    parser.state.labels = [];

    body = parseBlock(parser, true);

    parser.popScope('GENERATOR');

    parser.state.labels = oldLabels;
  }

  parser.popScope('ASYNC');
  checkFunctionNameAndParams(parser, opts, body);
  parser.popScope('PARAMETERS');

  let firstDirective;
  if (
    !isSimpleParamList(opts.params) &&
    body.type === 'BlockStatement' &&
    body.directives !== undefined
  ) {
    firstDirective = body.directives[0];
  }

  if (
    firstDirective !== undefined &&
    firstDirective.value.value === 'use strict'
  ) {
    parser.addDiagnostic({
      loc: firstDirective.loc,
      message:
        "Illegal 'use strict' directive in function with non-simple parameter list",
    });
  }

  return body;
}

export function checkFunctionNameAndParams(
  parser: Parser,
  opts: FunctionBodyParseOpts,
  body: n.ExpressionNode | n.BlockStatementNode,
  force?: boolean,
): void {
  const {isArrowFunction, isMethod, id, params, start} = opts;

  if (
    isArrowFunction &&
    force !== true &&
    parser.state.noArrowParamsConversionAt.indexOf(start.index) !== -1
  ) {
    return undefined;
  }

  // If this is a strict mode function, verify that argument names
  // are not repeated, and it does not try to bind the words `eval`
  const _isStrictBody = isStrictBody(parser, body);
  const isStrict = parser.inScope('STRICT') || _isStrictBody;

  const isSimpleParams = isSimpleParamList(params);
  const shouldCheckLVal: boolean =
    isStrict || isArrowFunction || isMethod || !isSimpleParams;

  parser.pushScope('STRICT', isStrict);

  if (shouldCheckLVal) {
    const clashes: Map<string, n.Node> = new Map();

    if (id !== undefined) {
      checkLVal(parser, id, true, undefined, 'function name');
    }

    for (const param of params) {
      if (_isStrictBody && param.type !== 'Identifier') {
        parser.addDiagnostic({
          loc: param.loc,
          message: 'Non-simple parameter in strict mode',
        });
      }
      checkLVal(parser, param, true, clashes, 'function parameter list');
    }
  }

  parser.popScope('STRICT');
}

function isSimpleParamList(params: Array<n.Node>): boolean {
  for (const param of params) {
    if (param.type !== 'Identifier') {
      return false;
    }
  }
  return true;
}

// Parses a comma-separated list of expressions, and returns them as
// an array. `close` is the token type that ends the list, and
// `allowEmpty` can be turned on to allow subsequent commas with
// nothing in between them to be parsed as `null` (which is needed
// for array literals).

export function parseExpressionList(
  parser: Parser,
  context: ExpressionContext,
  openContext: OpeningContext,
  allowEmpty?: boolean,
  refShorthandDefaultPos?: undefined | IndexTracker,
): Array<undefined | n.ExpressionNode> {
  const elts = [];
  let first = true;

  while (true) {
    if (parser.match(openContext.close) || parser.match(tt.eof)) {
      break;
    }

    if (first) {
      first = false;
    } else {
      parser.expect(tt.comma);

      if (parser.match(openContext.close)) {
        break;
      }
    }

    elts.push(
      parseExpressionListItem(
        parser,
        context,
        allowEmpty,
        refShorthandDefaultPos,
      ),
    );
  }

  parser.expectClosing(openContext);

  return elts;
}

export function parseExpressionListItem(
  parser: Parser,
  context: ExpressionContext,
  maybeAllowEmpty?: undefined | boolean,
  refShorthandDefaultPos?: undefined | IndexTracker,
  refNeedsArrowPos?: undefined | IndexTracker,
  refTrailingCommaPos?: IndexTracker,
): undefined | n.ExpressionNode {
  const allowEmpty = Boolean(maybeAllowEmpty);
  const start = parser.getPosition();

  let elt;
  if (allowEmpty && parser.match(tt.comma)) {
    elt = undefined;
  } else if (parser.match(tt.ellipsis)) {
    const spreadNodeStart = parser.state.startPos;

    elt = parseParenItem(
      parser,
      parseSpread(parser, refShorthandDefaultPos, refNeedsArrowPos),
      spreadNodeStart,
    );

    if (refTrailingCommaPos && parser.match(tt.comma)) {
      refTrailingCommaPos.index = parser.state.startPos.index;
    }
  } else {
    elt = parseMaybeAssign(
      parser,
      context,
      false,
      refShorthandDefaultPos,
      parseParenItem,
      refNeedsArrowPos,
    );
  }

  // parse an item inside a expression list eg. `(NODE, NODE)` where NODE represents
  // the position where this function is called
  if (parser.match(tt.colon)) {
    const expression = elt;
    const typeAnnotation = parseTypeAnnotation(parser);
    return {
      loc: parser.finishLoc(start),
      type: 'FlowTypeCastExpression',
      expression,
      typeAnnotation,
      _exprListItem: true,
    };
  } else {
    return elt;
  }
}

// Parse the next token as an identifier. If `liberal` is true (used
// when parsing properties), it will also convert keywords into
// identifiers.

export function parseIdentifier(
  parser: Parser,
  liberal?: boolean,
): n.IdentifierNode {
  const start = parser.getPosition();
  const name = parseIdentifierName(parser, liberal);
  return createIdentifier(parser, start, name);
}

export function createIdentifier(
  parser: Parser,
  start: Position,
  name: string,
): n.IdentifierNode {
  return {
    type: 'Identifier',
    name,
    loc: parser.finishLoc(start),
  };
}

export function parseIdentifierName(
  parser: Parser,
  liberal: boolean = false,
): string {
  const loc = parser.finishLocAt(parser.state.startPos, parser.state.endPos);

  if (!liberal) {
    checkReservedWord(
      parser,
      String(parser.state.tokenValue),
      loc,
      !!parser.state.tokenType.keyword,
      false,
    );
  }

  let name: string;

  if (parser.match(tt.name)) {
    name = String(parser.state.tokenValue);
  } else if (parser.state.tokenType.keyword !== undefined) {
    name = parser.state.tokenType.keyword;

    // `class` and `function` keywords push new context into this.context.
    // But there is no chance to pop the context if the keyword is consumed
    // as an identifier such as a property name.
    // If the previous token is a dot, this does not apply because the
    // context-managing code already ignored the keyword
    if (
      (name === 'class' || name === 'function') &&
      (parser.state.lastEndPos.index !== parser.state.lastStartPos.index + 1 ||
        parser.input.charCodeAt(parser.state.lastStartPos.index) !==
          charCodes.dot)
    ) {
      parser.state.context.pop();
    }
  } else {
    parser.addDiagnostic({
      message: 'Expected an identifier here',
    });
    name = '';
  }

  if (!liberal) {
    checkReservedWord(
      parser,
      name,
      loc,
      parser.state.tokenType.keyword !== undefined,
      false,
    );
  }

  parser.next();
  return name;
}

export function checkReservedWord(
  parser: Parser,
  word: string,
  loc: SourceLocation,
  checkKeywords: boolean,
  isBinding: boolean,
): void {
  if (parser.isSyntaxEnabled('ts')) {
    // TypeScript support in Babel disables reserved word checking...
    // This is mostly because TS allows reserved words in certain scenarios
    // TODO we should just allow those rather than relying on this hack
    return undefined;
  }

  if (parser.inScope('GENERATOR') && word === 'yield') {
    parser.addDiagnostic({
      loc,
      message: "Can not use 'yield' as identifier inside a generator",
    });
  }

  if (parser.inScope('ASYNC') && word === 'await') {
    parser.addDiagnostic({
      loc,
      message: "Can not use keyword 'await' outside an async function",
    });
  }

  if (parser.inScope('CLASS_PROPERTY') && word === 'arguments') {
    parser.addDiagnostic({
      loc,
      message: "'arguments' is not allowed in class field initializer",
    });
  }

  if (checkKeywords && isKeyword(word)) {
    parser.addDiagnostic({
      loc,
      message: `Unexpected keyword '${word}'`,
    });
  }

  let isReserved = false;
  if (parser.inScope('STRICT')) {
    if (isBinding) {
      isReserved = isStrictBindReservedWord(word, parser.inModule);
    } else {
      isReserved = isStrictReservedWord(word, parser.inModule);
    }
  } else {
    isReserved = isReservedWord(word, parser.inModule);
  }

  if (isReserved) {
    if (!parser.inScope('ASYNC') && word === 'await') {
      parser.addDiagnostic({
        loc,
        message: "Can not use keyword 'await' outside an async function",
      });
    } else {
      parser.addDiagnostic({
        loc,
        message: `${word} is a reserved word`,
      });
    }
  }
}

// Parses await expression inside async function.

export function parseAwait(parser: Parser): n.AwaitExpressionNode {
  if (!parser.state.awaitPos) {
    parser.state.awaitPos = parser.state.index;
  }

  if (!parser.inScope('ASYNC')) {
    parser.addDiagnostic({
      message: "Can't use await outside of an async function",
    });
  }

  const start = parser.getPosition();
  parser.next();

  if (parser.inScope('PARAMETERS')) {
    parser.addDiagnostic({
      message: 'await is not allowed in async function parameters',
    });
  }

  if (parser.eat(tt.star)) {
    parser.addDiagnostic({
      start,
      message:
        'await* has been removed from the async functions proposal. Use Promise.all() instead.',
    });
  }

  const argument = parseMaybeUnary(parser, 'await argument');
  return {
    type: 'AwaitExpression',
    loc: parser.finishLoc(start),
    argument,
  };
}

// Parses yield expression inside generator.

export function parseYield(
  parser: Parser,
  noIn?: undefined | boolean,
): n.YieldExpressionNode {
  if (!parser.state.yieldPos) {
    parser.state.yieldPos = parser.state.index;
  }

  const start = parser.getPosition();

  if (parser.inScope('PARAMETERS')) {
    parser.addDiagnostic({
      start,
      message: 'yield is not allowed in generator parameters',
    });
  }

  if (
    parser.state.maybeInArrowParameters &&
    // We only set yieldInPossibleArrowParameters if we haven't already
    // found a possible invalid YieldExpression.
    parser.state.yieldInPossibleArrowParameters === undefined
  ) {
    parser.state.yieldInPossibleArrowParameters = start;
  }

  parser.next();

  let delegate;
  let argument;
  if (
    parser.match(tt.semi) ||
    (!parser.match(tt.star) && !parser.state.tokenType.startsExpr) ||
    parser.canInsertSemicolon()
  ) {
    delegate = false;
    argument = null;
  } else {
    delegate = parser.eat(tt.star);
    argument = parseMaybeAssign(parser, 'yield argument', noIn);
  }

  return {
    type: 'YieldExpression',
    loc: parser.finishLoc(start),
    delegate,
    argument,
  };
}

function parseNullLiteral(parser: Parser): n.NullLiteralNode {
  const start = parser.getPosition();
  parser.next();
  return {
    type: 'NullLiteral',
    loc: parser.finishLoc(start),
  };
}

export function parseStringLiteral(parser: Parser): n.StringLiteralNode {
  const start = parser.getPosition();
  const value = String(parser.state.tokenValue);
  parser.next();
  return {
    loc: parser.finishLoc(start),
    type: 'StringLiteral',
    value,
  };
}

function parseBigIntLiteral(parser: Parser): n.BigIntLiteralNode {
  const start = parser.getPosition();
  const value = String(parser.state.tokenValue);
  parser.next();
  return {
    loc: parser.finishLoc(start),
    type: 'BigIntLiteral',
    value,
  };
}

function parseNumericLiteral(parser: Parser): n.NumericLiteralNode {
  const start = parser.getPosition();
  const value = Number(parser.state.tokenValue);
  parser.next();
  return {
    loc: parser.finishLoc(start),
    type: 'NumericLiteral',
    value,
  };
}

function parseRegExpLiteral(parser: Parser): n.RegExpLiteralNode {
  const start = parser.getPosition();
  const value = parser.state.tokenValue;
  if (!(value instanceof RegExpTokenValue)) {
    throw new Error('Expected regex token value');
  }
  parser.next();

  const {flags} = value;
  return {
    loc: parser.finishLoc(start),
    type: 'RegExpLiteral',
    pattern: value.pattern,
    global: flags.has('g'),
    multiline: flags.has('m'),
    sticky: flags.has('y'),
    insensitive: flags.has('i'),
    noDotNewline: flags.has('s'),
    unicode: flags.has('u'),
  };
}

function parseImportOrMetaProperty(
  parser: Parser,
): n.ImportNode | n.MetaPropertyNode {
  if (parser.lookaheadState().tokenType === tt.dot) {
    return parseImportMetaProperty(parser);
  }

  const start = parser.getPosition();
  parser.next();
  if (!parser.match(tt.parenL)) {
    parser.unexpectedToken(undefined, tt.parenL);
  }
  return {
    type: 'Import',
    loc: parser.finishLoc(start),
  };
}

function parseSuper(parser: Parser): n.SuperNode {
  if (
    !parser.inScope('METHOD') &&
    !parser.inScope('CLASS_PROPERTY') &&
    parser.sourceType !== 'template'
  ) {
    parser.addDiagnostic({
      message: 'super is only allowed in object methods and classes',
    });
  }

  const start = parser.getPosition();
  parser.next();

  if (
    !parser.match(tt.parenL) &&
    !parser.match(tt.bracketL) &&
    !parser.match(tt.dot)
  ) {
    parser.addDiagnostic({
      message: 'Invalid super suffix operator',
    });
  }

  const loc = parser.finishLoc(start);

  if (
    parser.match(tt.parenL) &&
    (parser.getLastScope('METHOD') !== 'constructor' ||
      parser.getLastScope('CLASS') !== 'derived') &&
    parser.sourceType !== 'template'
  ) {
    parser.addDiagnostic({
      loc,
      message: 'super() is only valid inside a class constructor of a subclass',
      advice: [
        {
          type: 'log',
          category: 'info',
          message:
            "Maybe a typo in the method name ('constructor') or not extending another class?",
        },
      ],
    });
  }

  return {
    type: 'Super',
    loc,
  };
}

function parseDoExpression(parser: Parser): n.DoExpressionNode {
  const start = parser.getPosition();
  parser.next();
  const oldLabels = parser.state.labels;
  parser.state.labels = [];
  parser.pushScope('FUNCTION', false);
  const body = parseBlock(parser, false);
  parser.popScope('FUNCTION');
  parser.state.labels = oldLabels;
  return {
    type: 'DoExpression',
    body,
    loc: parser.finishLoc(start),
  };
}

function parseArrayExpression(
  parser: Parser,
  refShorthandDefaultPos?: IndexTracker,
): n.ArrayExpressionNode {
  const start = parser.getPosition();
  const openContext = parser.expectOpening(tt.bracketL, tt.bracketR, 'array');

  let elements = parseExpressionList(
    parser,
    'array element',
    openContext,
    true,
    refShorthandDefaultPos,
  );

  if (!parser.state.maybeInArrowParameters) {
    // This could be an array pattern:
    //   ([a: string, b: string]) => {}
    // In this case, we don't have to call toReferencedList. We will
    // call it, if needed, when we are sure that it is a parenthesized
    // expression by calling toReferencedListDeep.
    elements = toReferencedList(parser, elements);
  }

  return {
    loc: parser.finishLoc(start),
    type: 'ArrayExpression',
    elements,
  };
}
