import {
  JSNodeBase,
  VariableDeclarationNode,
  FunctionDeclarationNode,
  ClassDeclarationNode,
  ExportSpecifierNode,
  StringLiteralNode,
  ModuleKind,
  ExportNamespaceSpecifierNode,
  ExportDefaultSpecifierNode,
  TSModuleDeclarationNode,
  TSEnumDeclarationNode,
  FlowInterfaceDeclarationNode,
  TSTypeAliasDeclarationNode,
  TSInterfaceDeclarationNode,
  TSDeclareFunctionNode,
  FlowOpaqueTypeNode,
  FlowTypeAliasNode,
} from '../index';
import {createBuilder} from '../utils';

export type ExportSpecifier =
  | ExportSpecifierNode
  | ExportNamespaceSpecifierNode
  | ExportDefaultSpecifierNode;

export type ExportNamedDeclarationNode = JSNodeBase & {
  type: 'ExportNamedDeclaration',
  declaration?:
    | undefined
    | VariableDeclarationNode
    | FunctionDeclarationNode
    | ClassDeclarationNode
    | TSModuleDeclarationNode
    | TSEnumDeclarationNode
    | FlowInterfaceDeclarationNode
    | TSTypeAliasDeclarationNode
    | TSInterfaceDeclarationNode
    | TSDeclareFunctionNode
    | FlowOpaqueTypeNode
    | FlowTypeAliasNode,
  specifiers?: undefined | Array<ExportSpecifier>,
  source?: undefined | StringLiteralNode,
  exportKind?: undefined | ModuleKind,
};

export const exportNamedDeclaration = createBuilder<ExportNamedDeclarationNode>(
  'ExportNamedDeclaration',
  {
    visitorKeys: ['declaration', 'specifiers', 'source'],
  },
);
